import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import _capitalize from 'lodash/capitalize';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import isNull from 'lodash/isNull';
import styles from './sn-uib-instance-name-popover.scss';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const INSTANCE_NAME_POPOVER_CLOSED =
	'SN_UIB_INSTANCE_NAME_POPOVER#POPOVER_CLOSED';
export const INSTANCE_NAME_POPOVER_VALUES_SAVED =
	'SN_UIB_INSTANCE_NAME_POPOVER#SAVE_CLICKED';

const getValidationMessage = (input, type) => {
	const inputLabel = input === 'id' ? 'ID' : 'label';
	switch (type) {
		case 'duplicate':
			return t('This {0} is already in use.', inputLabel);
		case 'alphanumeric':
			/* prettier-ignore */
			return t('Invalid ID. Supported characters are: alphanumeric and underscore.');
		default:
			return t('Invalid {0}.', inputLabel);
	}
};

const view = (state) => {
	const {
		invalidValues,
		properties: { context, id, instanceName, category }
	} = state;
	const messages = Object.entries(invalidValues).reduce(
		(acc, [input, type]) => {
			const message = getValidationMessage(input, type);
			acc[input] = [
				{
					content: message,
					status: 'critical',
					icon: 'circle-exclamation-outline'
				}
			];
			return acc;
		},
		{}
	);
	const idEditingDisabled = category === 'viewport' || category === 'modal';

	return (
		<div className="instance-name-popover-container">
			<div className="header">
				<span className="header-text">
					{_capitalize(t('{0} details', context))}
				</span>
				<now-button-iconic bare variant="tertiary" icon="close-fill" />
			</div>
			<div className="body">
				<now-input
					label={t('{0} label', _capitalize(context))}
					value={instanceName}
					invalid={!!invalidValues[instanceName]}
					manage-invalid={true}
					messages={messages.instanceName || []}
					append-to-payload={{ context: 'instanceName' }}
				/>
				<now-input
					label={t('{0} ID', _capitalize(context))}
					value={id}
					disabled={idEditingDisabled}
					invalid={!!invalidValues[id]}
					manage-invalid={true}
					messages={messages.id || []}
					append-to-payload={{ context: 'id' }}
				/>
			</div>
			<div className="footer">
				<now-button
					label={t('Apply')}
					variant="primary"
					disabled={!isEmpty(invalidValues)}
					append-to-payload={{ context: 'save' }}
				/>
				<now-button
					label={t('Cancel')}
					append-to-payload={{ context: 'cancel' }}
				/>
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-instance-name-popover', {
	view,
	styles,
	initialState: {
		initialValues: {},
		invalidValues: {},
		pendingValues: {}
	},
	properties: {
		context: {
			schema: { type: 'string' },
			default: ''
		},
		id: {
			schema: { type: 'string' },
			default: ''
		},
		instanceName: {
			schema: { type: 'string' },
			default: ''
		},
		instanceNameMap: {
			schema: { type: 'object' },
			default: {}
		},
		category: {
			schema: { type: 'string' },
			default: ''
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ properties, updateState }) => {
			updateState({
				initialValues: {
					id: properties.id,
					instanceName: properties.instanceName
				}
			});
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			updateState
		}) => {
			const { name, value } = action.payload;
			if (name === 'id' || name === 'instanceName') {
				updateState({
					initialValues: {
						...state.initialValues,
						[name]: value
					}
				});
			}
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			batchShorthandUpdateState(
				{ invalidValues: [], pendingValues: {} },
				updateState
			);
			dispatch(INSTANCE_NAME_POPOVER_CLOSED);
		},
		'NOW_BUTTON#CLICKED': ({ action, dispatch, state, updateState }) => {
			action.stopPropagation();
			if (action.payload.context === 'cancel') {
				action.stopPropagation();
				batchShorthandUpdateState(
					{ invalidValues: [], pendingValues: {} },
					updateState
				);
				dispatch(INSTANCE_NAME_POPOVER_CLOSED);
			} else if (action.payload.context === 'save') {
				const { pendingValues, initialValues } = state;
				const changes = Object.entries(pendingValues).reduce((acc, val) => {
					const [propName, propValue] = val;
					if (initialValues[propName] !== propValue) {
						acc[propName] = propValue;
					}
					return acc;
				}, {});

				!isEmpty(changes) &&
					dispatch(INSTANCE_NAME_POPOVER_VALUES_SAVED, {
						previousId: initialValues.id,
						changes
					});
				updateState({ pendingValues: {} });
				dispatch(INSTANCE_NAME_POPOVER_CLOSED);
			}
		},
		'NOW_INPUT#INPUT': ({ action, properties, state, updateState }) => {
			action.stopPropagation();
			const { context, fieldValue } = action.payload;
			const { initialValues, invalidValues, pendingValues } = state;
			const newInvalidValues = cloneDeep(invalidValues);
			if (context === 'id') {
				if (!isNull(fieldValue.match(/[^\w]/g))) {
					// If the current value contains non-alphanumeric characters, it is invalid
					newInvalidValues.id = 'alphanumeric';
				} else if (
					fieldValue in properties.instanceNameMap &&
					initialValues.id !== fieldValue
				) {
					// If the current value is equal to another instance ID on the page, it is invalid
					newInvalidValues.id = 'duplicate';
				} else {
					delete newInvalidValues.id;
				}
			} else if (context === 'instanceName') {
				if (
					Object.values(properties.instanceNameMap).includes(fieldValue) &&
					initialValues.instanceName !== fieldValue
				) {
					// If the current value is equal to another instance name on the page, it is invalid
					newInvalidValues.instanceName = 'duplicate';
				} else {
					delete newInvalidValues.instanceName;
				}
			}

			batchShorthandUpdateState(
				{
					invalidValues: newInvalidValues,
					pendingValues: { ...pendingValues, [context]: fieldValue }
				},
				updateState
			);
		}
	}
});
