import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';

export default function ({ isEmptyState, parentInfo }) {
	if (!isEmpty(parentInfo) && parentInfo.screenName) {
		return (
			<now-button
				class={{
					'stage-screen-back-button': true,
					'empty-state': isEmptyState
				}}
				size="sm"
				append-to-payload={{ context: 'backButton' }}>
				<now-icon icon="arrow-left-outline" />
				<span className="back-button-label">
					{t('Back to {0}', parentInfo.screenName)}
				</span>
			</now-button>
		);
	} else {
		return null;
	}
}
