import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import 'sn-devx-components';
import { t } from 'sn-translate';
import get from 'lodash/get';
import isEqual from 'lodash/isEqual';
import focusBehavior from '@servicenow/behavior-focus';
import styles from './sn-uib-element-configuration-panel.scss';
import '@servicenow/now-tabs';
import '../sn-uib-style-configuration-pane';
import { componentInterfaceSchema } from './schemas';
import { filterTemplates } from '../helpers/filterTemplates';
import { DYNAMIC_PROPERTY_INPUT_FOCUSED } from '../sn-uib-dynamic-property-input';
import { INSTANCE_NAME_EDITOR_IS_HIDDEN_TOGGLED } from '../sn-uib-instance-name-editor';
import { IS_HIDDEN_PROPERTY_TEST_VALUE_CHANGED } from '../sn-uib-is-hidden-property-input';
import { IS_HIDDEN } from '../sn-uib-properties-configuration-pane/constants';
import {
	extractReferencesFromProperties,
	getValuesFromEventMappingHandlers
} from '../sn-uib-properties-configuration-pane/utils';
import { memoizeDeepN } from 'seismic-memo-utils';

const PROPERTIES = 'properties';
const STYLE = 'style';
const EVENTS = 'events';
const tabLookup = {
	[PROPERTIES]: true,
	[STYLE]: true,
	[EVENTS]: true
};

const getIsHiddenValue = (currentProperties) => {
	const currentElementIsHiddenProperty = currentProperties.find(
		(prop) => prop.id === IS_HIDDEN
	);
	if (currentElementIsHiddenProperty) {
		return currentElementIsHiddenProperty.value;
	}
	return '';
};

const UIB_ELEMENT_CONFIGURATION_PANEL_ELEMENT_INSERTED =
	'SN_UIB_ELEMENT_CONFIGURATION_PANEL#ELEMENT_INSERTED';

const SN_UIB_ELEMENT_CONFIGURATION_PANEL_BINDINGS_REQUESTED =
	'SN_UIB_ELEMENT_CONFIGURATION_PANEL_BINDINGS_REQUESTED';

const ELEMENT_CONFIGURATION_PANEL_ELEMENT_CHANGED =
	'SN_UIB_ELEMENT_CONFIGURATION_PANEL#ELEMENT_CHANGED';

const propertiesTab = {
	id: PROPERTIES,
	icon: 'gear-outline',
	label: t('Config')
};

const layoutTab = {
	id: STYLE,
	icon: 'chart-treemap-outline',
	label: t('Layout')
};

const styleTab = {
	id: STYLE,
	icon: 'palette-outline',
	label: t('Styles')
};

const eventTab = {
	id: EVENTS,
	icon: 'lightning-outline',
	label: t('Events')
};

const allTabsWithLayout = [propertiesTab, layoutTab, eventTab];
const allTabsWithStyle = [propertiesTab, styleTab, eventTab];
const propsAndStyleTab = [propertiesTab, styleTab];
const propsAndLayoutTab = [propertiesTab, layoutTab];
const propsAndEventTab = [propertiesTab, eventTab];
const propsOnly = [propertiesTab];

const tabsAppendToPayload = {
	id: 'element-configuration-tabs'
};

const panelAppendToPayload = {
	panel: 'element-configuration'
};

const panelCollapseTooltip = {
	expanded: t('Hide configuration panel'),
	collapsed: t('Show configuration panel')
};

const getTabs = (
	stylingPropertiesEnabled,
	eventMappingEnabled,
	elementLayout,
	enabledElementTabs,
	enableStotTabs
) => {
	let tabs = [];
	if (stylingPropertiesEnabled && eventMappingEnabled) {
		tabs =
			get(elementLayout, 'type') === 'container'
				? allTabsWithLayout
				: allTabsWithStyle;
	} else if (stylingPropertiesEnabled) {
		tabs =
			get(elementLayout, 'type') === 'container'
				? propsAndLayoutTab
				: propsAndStyleTab;
	} else if (eventMappingEnabled) {
		tabs = propsAndEventTab;
	} else {
		tabs = propsOnly;
	}
	return tabs.map((tab) => {
		switch (tab.id) {
			case PROPERTIES:
			case EVENTS:
				return { ...tab, disabled: !enabledElementTabs };
			case STYLE:
				return { ...tab, disabled: !enabledElementTabs && !enableStotTabs };
			default:
				return tab;
		}
	});
};

const isHiddenPropertyEmpty = (currentElementProperties = []) => {
	const isHiddenProperty =
		currentElementProperties.find((prop) => prop.id === IS_HIDDEN) || null;
	return (
		isHiddenProperty &&
		(isHiddenProperty.value === '@' || isHiddenProperty === '')
	);
};

const renderTabbedContent = (currentTab, state, properties) => {
	const {
		properties: currentProperties,
		element,
		tables,
		error,
		referenceDisplayValues,
		referenceResults,
		referenceSearching,
		tableFieldValues,
		c3PropertyConfig,
		elementConfigurationPanelExpanded,
		disabled,
		elementStyles,
		externalConfigurationButtons,
		eventMappings,
		events,
		eventHandlers,
		eventMappingHandlers,
		dataBindingEnabled,
		elementLayout,
		selectedSlotName,
		selectedSlotParent,
		selectedSlotStyles,
		interfaceConfigurationComponent,
		layoutTemplates,
		bindings,
		container,
		ancestorEventHandlers,
		appConfig,
		currentTriggeringElements,
		editInstanceNameInHeader,
		clientTransformScriptTemplates,
		viewportVariantsEnabled,
		selectedComponentTag,
		lastAppliedTemplateId,
		dynamicTabsEnabled,
		isPageDefinitionEventsDisabled,
		isPageDefinitionLayoutDisabled,
		isMacroponentReadOnly
	} = properties;
	const { standardTemplates, customizedTemplates, isHiddenInputOpen } = state;
	const elementId = get(element, 'id', null);
	const filteredHandlers = [...eventHandlers, ...ancestorEventHandlers].filter(
		(handler) => handler.relay === false
	);

	switch (currentTab) {
		case PROPERTIES:
			return (
				<sn-uib-properties-configuration-pane
					properties={currentProperties}
					element={element}
					tables={tables}
					error={error}
					elementLayout={elementLayout}
					layoutTemplates={layoutTemplates}
					referenceDisplayValues={referenceDisplayValues}
					referenceResults={referenceResults}
					referenceSearching={referenceSearching}
					tableFieldValues={tableFieldValues}
					c3PropertyConfig={c3PropertyConfig}
					elementConfigurationPanelExpanded={elementConfigurationPanelExpanded}
					disabled={disabled}
					externalConfigurationButtons={externalConfigurationButtons}
					component-id={`properties-configuration-pane-${
						element ? element.id || 'id' : ''
					}`}
					selectedComponentTag={selectedComponentTag}
					dataBindingEnabled={dataBindingEnabled}
					dynamicTabsEnabled={dynamicTabsEnabled}
					interfaceConfigurationComponent={interfaceConfigurationComponent}
					bindings={bindings}
					container={container}
					app-config={appConfig}
					instance-name-editor-enabled={editInstanceNameInHeader}
					scriptTemplate={clientTransformScriptTemplates.ELEMENT}
					viewportVariantsEnabled={viewportVariantsEnabled}
					is-hidden-input-open={isHiddenInputOpen}
					is-macroponent-read-only={isMacroponentReadOnly}
				/>
			);
		case STYLE:
			return (
				<sn-uib-style-configuration-pane
					element-styles={elementStyles}
					element={element}
					element-layout={elementLayout}
					selected-slot-name={selectedSlotName}
					selected-slot-parent={selectedSlotParent}
					selected-slot-styles={selectedSlotStyles}
					layout-templates={layoutTemplates}
					standard-templates={standardTemplates}
					customized-templates={customizedTemplates}
					disabled={isPageDefinitionLayoutDisabled}
					lastAppliedTemplateId={lastAppliedTemplateId}
					is-page-definition-layout-disabled={isPageDefinitionLayoutDisabled}
				/>
			);
		case EVENTS:
			return (
				<sn-uib-event-configuration-pane
					eventMappings={eventMappings}
					events={events}
					eventHandlers={filteredHandlers}
					eventMappingHandlers={eventMappingHandlers}
					disabled={disabled || isPageDefinitionEventsDisabled}
					append-to-payload={{ elementId }}
					component-id={`event-configuration-pane-${
						element ? element.id || 'id' : ''
					}`}
					interfaceConfigurationComponent={interfaceConfigurationComponent}
					bindings={bindings}
					elementId={element ? element.id : ''}
					ancestor-event-handlers={ancestorEventHandlers}
					app-config={appConfig}
					current-triggering-elements={currentTriggeringElements}
					scriptTemplate={clientTransformScriptTemplates.EVENT}
				/>
			);
	}
};

const getFilterTemplates = memoizeDeepN(function (layoutTemplates) {
	return filterTemplates(layoutTemplates);
});

const view = (state, { dispatch }) => {
	const { properties, currentIsHiddenValue, isHiddenInputOpen } = state;
	const {
		elementConfigurationPanelExpanded,
		showTabs,
		element,
		elementInstanceNameMap,
		elementLayout,
		disabled,
		editInstanceNameInHeader,
		dataBrokersEnabled,
		editorOpened,
		currentTab,
		selectedSlotName,
		stylingPropertiesEnabled,
		eventMappingEnabled,
		selectedComponentTag,
		componentDescription,
		hideComponentPropertyBindingsEnabled,
		isMacroponentReadOnly
	} = properties;
	const elementId = get(element, 'id', '');
	return (
		<sn-uib-collapsible-panel
			id="collapsible-element-configuration-panel"
			class={{ expanded: elementConfigurationPanelExpanded }}
			expanded={elementConfigurationPanelExpanded}
			append-to-payload={panelAppendToPayload}
			direction="right"
			heading={
				selectedSlotName
					? t('{0} styles', selectedSlotName)
					: t('Configuration')
			}
			heading-icon="sliders-outline"
			collapseTooltip={panelCollapseTooltip}
			isEmptyState={'@uib-app/isEmptyState'}
			heading-renderer={
				editInstanceNameInHeader
					? {
							render: !selectedSlotName && elementId ? true : false,
							tagName: 'sn-uib-instance-name-editor',
							context: 'component',
							disabled,
							id: elementId,
							instanceName: get(element, 'displayName', ''),
							instanceNameMap: elementInstanceNameMap,
							category: get(element, 'componentCategory', ''),
							editorOpened: editorOpened,
							isHiddenInputOpen:
								isHiddenInputOpen || isMacroponentReadOnly || disabled,
							currentIsHiddenValue,
							selectedComponentTag: selectedComponentTag,
							componentDescription: componentDescription,
							hideComponentPropertyBindingsEnabled,
							isMacroponentReadOnly
					  }
					: {}
			}>
			{showTabs && !selectedSlotName ? (
				<now-tabs
					fixed-width
					selected-item={currentTab}
					append-to-payload={tabsAppendToPayload}
					items={getTabs(
						stylingPropertiesEnabled,
						eventMappingEnabled,
						elementLayout,
						!!element,
						!!selectedSlotName
					)}
				/>
			) : null}
			<div
				class={{
					'scrollable-pane-container': true,
					'full-length': dataBrokersEnabled === false,
					'no-tabs': !(showTabs && !selectedSlotName)
				}}
				hook-insert={({ elm }) => {
					dispatch(UIB_ELEMENT_CONFIGURATION_PANEL_ELEMENT_INSERTED, elm);
				}}>
				{renderTabbedContent(currentTab, state, properties)}
			</div>
		</sn-uib-collapsible-panel>
	);
};

export default createCustomElement('sn-uib-element-configuration-panel', {
	view,
	behaviors: [focusBehavior],
	properties: {
		editorOpened: { default: false },
		elementConfigurationPanelExpanded: { default: true, selectable: true },
		elementLayout: { default: {} },
		elementStyles: { default: {} },
		selectedSlotName: { default: null },
		selectedSlotParent: { default: null },
		selectedSlotStyles: { default: {} },
		layoutTemplates: { default: [] },
		properties: { default: [] },
		element: { default: null },
		tables: { default: [], selectable: true },
		icons: { default: [], selectable: true },
		error: { default: false },
		referenceDisplayValues: { default: null, selectable: true },
		referenceResults: { default: null, selectable: true },
		referenceSearching: { default: null, selectable: true },
		tableFieldValues: { default: null, selectable: true },
		c3PropertyConfig: { default: null, selectable: true },
		disabled: { default: false },
		externalConfigurationButtons: { default: null },
		dynamicTabsEnabled: { default: true },
		componentDescription: { default: '' },
		eventMappings: {
			default: []
		},
		events: {
			default: []
		},
		eventHandlers: {
			default: []
		},
		eventMappingHandlers: {
			default: {}
		},
		dataBindingEnabled: {
			default: true,
			selectable: true
		},
		scriptedPropertiesEnabled: {
			default: true,
			selectable: true
		},
		stylingPropertiesEnabled: {
			default: true
		},
		eventMappingEnabled: {
			default: true
		},
		showTabs: {
			default: true,
			computed({
				properties: { eventMappingEnabled, stylingPropertiesEnabled }
			}) {
				return eventMappingEnabled || stylingPropertiesEnabled;
			}
		},
		currentTab: {
			default: PROPERTIES
		},
		lastAppliedTemplateId: {
			computed({ properties: { elementLayout } }) {
				return get(elementLayout, 'layout.default.templateId', null);
			}
		},
		interfaceConfigurationComponent: {
			/**
			 *  the interface name and its corresponding component tag
			 * @typedef componentInterface
			 * @property {string} interfaceName
			 * @property {string}  componentId
			 */

			/**
			 *  a map containing component interfaces
			 * @typedef {Object.<string, componentInterface>} componentInterfaceMap
			 */

			/**
			 * @type {componentInterfaceMap}
			 */
			default: {},
			schema: { type: 'object', schema: componentInterfaceSchema }
		},
		appConfig: {
			/**
			 * @typedef AppConfig
			 * @property {Object[]} appRoutes
			 * @property {Object[]} bindings
			 * @property {Object} dataBrokerInstanceNameMap
			 * @property {Element[]} modalElements
			 * @property {string} elementId
			 * @property {boolean} disabled
			 */
			/**
			 * a config passed in from emotive-builder that contains relevant data
			 * @type {AppConfig}
			 */
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * The properties editor container that individual property inputs should be constrained within.
		 * @type {HTMLElement}
		 */
		container: {
			default: null
		},
		/**
		 * Ancestor event handlers to be used for payload templates in the dispatched events modal
		 */
		ancestorEventHandlers: {
			default: [],
			schema: { type: 'array' }
		},
		/**
		 * Array of elements with event mapping handlers that trigger the currently selected element
		 */
		currentTriggeringElements: {
			default: [],
			schema: { type: 'array' }
		},
		/**
		 * A map of element IDs to their instance names
		 */
		elementInstanceNameMap: {
			schema: { type: 'object' },
			default: {}
		},

		/**
		 * Whether data broker panel is enabled or not
		 */
		dataBrokersEnabled: {
			schema: { type: 'boolean' },
			default: false
		},
		/**
		 * Used to determine if the instance name editor should appear
		 * in the header of the configuration panel, or as a field in the pane
		 */
		editInstanceNameInHeader: {
			schema: { type: 'boolean' },
			default: true
		},
		/**
		 * client transform script template for element and event
		 */
		clientTransformScriptTemplates: {
			default: {}
		},
		/**
		 * the tagName of the current selected component
		 */
		selectedComponentTag: { default: '' },
		/**
		 * Can viewport variants be created?
		 */
		viewportVariantsEnabled: {
			default: false
		},
		/**
		 * Can event mappings be used?
		 */
		isPageDefinitionEventsDisabled: {
			default: false
		},
		isPageDefinitionLayoutDisabled: {
			default: false
		},
		hideComponentPropertyBindingsEnabled: {
			default: false
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	initialState: {
		customizedTemplates: [],
		standardTemplates: [],
		isHiddenInputOpen: false,
		currentIsHiddenValue: ''
	},
	transformState: (state) => {
		const { layoutTemplates, properties: currentProperties } = state.properties;
		const { standardTemplates, customizedTemplates } = getFilterTemplates(
			layoutTemplates
		);
		const currentIsHiddenValue = getIsHiddenValue(currentProperties);

		return {
			...state,
			customizedTemplates,
			standardTemplates,
			currentIsHiddenValue
		};
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ dispatch, properties }) => {
			const {
				properties: currentElementProperties = [],
				eventMappingHandlers = {}
			} = properties;
			const currentEventMappingHandlerValues =
				getValuesFromEventMappingHandlers(eventMappingHandlers) || [];

			const references = [
				...(extractReferencesFromProperties(currentEventMappingHandlerValues) ||
					[]),
				...(extractReferencesFromProperties(currentElementProperties) || [])
			];
			references.length &&
				dispatch(ELEMENT_CONFIGURATION_PANEL_ELEMENT_CHANGED, { references });
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			updateState,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const { name, previousValue, value } = action.payload;
			if (name === 'element' && value && !isEqual(value, previousValue)) {
				const {
					properties: currentElementProperties = [],
					eventMappingHandlers = {}
				} = properties;
				dispatch(IS_HIDDEN_PROPERTY_TEST_VALUE_CHANGED, {
					elementId: value.id,
					value: 'none'
				});
				updateState({
					isHiddenInputOpen: !isHiddenPropertyEmpty(currentElementProperties)
				});

				const currentEventMappingHandlerValues =
					getValuesFromEventMappingHandlers(eventMappingHandlers) || [];
				const references = [
					...(extractReferencesFromProperties(
						currentEventMappingHandlerValues
					) || []),
					...(extractReferencesFromProperties(currentElementProperties) || [])
				];
				references.length &&
					dispatch(ELEMENT_CONFIGURATION_PANEL_ELEMENT_CHANGED, { references });
			}
		},
		['SN_UIB_LAYOUT_CONTEXT_MENU#RESET_LAYOUT']: ({
			action,
			dispatch,
			properties
		}) => {
			const { context } = action.payload;
			const { lastAppliedTemplateId, element } = properties;
			if (context === 'layout') {
				dispatch('SN_UIB_ELEMENT_CONFIGURATION_PANEL#RESET_LAYOUT', {
					elementId: element.id,
					layoutTemplateId: lastAppliedTemplateId
				});
			}
		},
		['NOW_TABS#SELECTED_ITEM_SET']: ({ action, dispatch }) => {
			if (action.payload.id !== 'element-configuration-tabs') {
				return;
			}
			const {
				payload: { value }
			} = action;
			if (tabLookup[value]) {
				dispatch('SN_UIB_CONFIG_PANEL#TAB_SELECTED', { name: value });
			}
		},
		[UIB_ELEMENT_CONFIGURATION_PANEL_ELEMENT_INSERTED]: ({
			updateProperties,
			action
		}) => {
			action.stopPropagation();
			updateProperties({ container: action.payload });
		},
		[DYNAMIC_PROPERTY_INPUT_FOCUSED]: ({ dispatch, action, properties }) => {
			const { element } = properties;
			dispatch(SN_UIB_ELEMENT_CONFIGURATION_PANEL_BINDINGS_REQUESTED, {
				element: element.id,
				...action.payload
			});
		},
		[INSTANCE_NAME_EDITOR_IS_HIDDEN_TOGGLED]: ({
			action,
			properties,
			state,
			updateState,
			dispatch
		}) => {
			action.stopPropagation();
			if (properties.currentTab === PROPERTIES && state.isHiddenInputOpen) {
				updateState({ isHiddenInputOpen: false });
			} else {
				dispatch('NOW_TABS#SELECTED_ITEM_SET', {
					id: 'element-configuration-tabs',
					value: PROPERTIES
				});
				updateState({ isHiddenInputOpen: true });
			}
		}
	},
	styles
});
