import _camelCase from 'lodash/camelCase';
import { t } from 'sn-translate';

export const validateString = (value, input = '') => {
	const validationRegex = /(?:^sub$|^params$|[^\w-])/g;
	let matches, camelCase;
	switch (input) {
		default:
			matches = value.match(validationRegex);
			break;
		case 'parameter':
			camelCase = _camelCase(value);
			matches = value.match(validationRegex) || value !== camelCase;
			break;
	}

	return {
		invalid: Boolean(matches),
		keyword:
			Array.isArray(matches) &&
			matches.filter((match) => match === 'sub' || match === 'params')[0],
		suggestion: camelCase
	};
};

export const validationErrorMessage = (value, input, checkForMatches = {}) => {
	let errorMessage;
	const alreadyUsed =
		Object.values(checkForMatches).filter(
			({ name, label }) => name === value || label === value
		).length > 0;
	const { invalid, keyword, suggestion } = validateString(value, input);
	if (alreadyUsed) {
		errorMessage = (
			<span>
				<strong> {value} </strong>
				{
					/* prettier-ignore */
					t('has already been used for another required parameter/optional parameter. Please use a different name.')
				}
			</span>
		);
	} else if (keyword) {
		errorMessage = (
			<span>
				{t('Invalid {0} name. The following keyword is not allowed: ', input)}
				<strong> {keyword} </strong>
			</span>
		);
	} else {
		switch (input) {
			case 'parameter':
				errorMessage = (
					<span>
						{
							/* prettier-ignore */
							t('Invalid {0} name. Name must use alphanumeric characters in camelCase.', input)
						}
						{suggestion ? <strong> Suggestion: {suggestion} </strong> : null}
					</span>
				);
				break;
			default:
				errorMessage = (
					<span>
						{t('Invalid {0} name. Supported characters are: ', input)}
						<strong> alphanumeric, dash </strong>
						and
						<strong> underscore.</strong>
					</span>
				);
		}
	}

	return !(invalid || alreadyUsed) || !value
		? []
		: [
				{
					content: errorMessage,
					status: 'critical',
					icon: 'circle-exclamation-outline'
				}
		  ];
};
