import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-alert';
import '@servicenow/now-icon';
import '@servicenow/now-pill';
import styles from './sn-uib-page-properties-configuration-pane.scss';
import { t } from 'sn-translate';
import _difference from 'lodash/difference';
import _kebabCase from 'lodash/kebabCase';
import '../sn-uib-property-description-tooltip';
import focusBehavior from '@servicenow/behavior-focus';
import EmptyState from '../partials/empty-state'; //eslint-disable-line no-unused-vars
// eslint-disable-next-line no-unused-vars
import PropertyFormView from '../partials/sn-uib-property-form-view';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import { PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED } from '../sn-uib-properties-editor-panel';
import isEmpty from 'lodash/isEmpty';

export const SN_UIB_PAGE_PROPERTIES_CONFIGURATION_PANE_EDIT_CLICKED =
	'SN_UIB_PAGE_PROPERTIES_CONFIGURATION_PANE#EDIT_CLICKED';
export const PAGE_PROP_CREATED = 'PAGE_CONFIG_PANEL#PAGE_PROP_CREATED';
export const PAGE_PROP_CHANGED = 'PAGE_CONFIG_PANEL#PAGE_PROP_CHANGED';

const appendToSyncPayload = { category: 'add-props' };
const appendToButtonPayload = { category: 'edit-props' };
const appendToDocumentationPayload = {
	category: 'page-template-documentation'
};
const propertiesHeader = t('Properties');
const editLabel = t('Edit');
/* prettier-ignore */
const parametersInfo = t('Page properties that are mapped to optional URL parameters. They receive their value from the URL, so a value cannot be set here. If you wish to set a test value, you can do so in the header.');
/* prettier-ignore */
const fieldsInfo = t('Page properties that are mapped to required URL parameters. They receive their value from the URL, so a value cannot be set here. If you wish to set a test value, you can do so in the header.');

const renderAlert = (outOfSyncFields, usingSharedPageDefinition, disabled) => {
	let content;
	if (!outOfSyncFields) {
		return null;
	}
	if (!isEmpty(outOfSyncFields)) {
		if (usingSharedPageDefinition) {
			content = (
				<div
					style={{
						'font-size': '14px'
					}}>
					<div>
						<now-icon size="sm" icon="circle-exclamation-outline" />

						<strong> {t('Map parameters')}</strong>
					</div>
					<p>
						{' '}
						{
							/* prettier-ignore */
							t('Refer to documentation to manually map the required and optional parameters to its URL.')
						}
					</p>
					<now-button
						style={{
							width: '100%'
						}}
						label={t('Go to documentation')}
						icon="open-link-outline"
						variant="tertiary"
						size="sm"
						append-to-payload={appendToDocumentationPayload}
					/>
				</div>
			);
		} else {
			content = (
				<div
					style={{
						'font-size': '14px'
					}}>
					<div>
						<now-icon size="sm" icon="circle-exclamation-outline" />

						<strong> {t('Required parameters are out of sync')}</strong>
					</div>
					<p>
						{' '}
						{
							/* prettier-ignore */
							t('Sync the required parameters to make your page work properly: ')
						}
						<strong>{outOfSyncFields.join(', ')}. </strong>
					</p>
					<now-button
						style={{
							width: '100%'
						}}
						label={t('Sync')}
						variant="tertiary"
						size="sm"
						disabled={disabled}
						append-to-payload={appendToSyncPayload}
					/>
				</div>
			);
		}
	}
	return (
		<now-alert
			status={usingSharedPageDefinition ? 'info' : 'critical'}
			content={{
				type: 'jsx',
				value: content
			}}
		/>
	);
};

const view = (state) => {
	const {
		pageProperties,
		dataBindingEnabled,
		bindings,
		disabled,
		outOfSyncFields,
		usingSharedPageDefinition
	} = state.properties;

	const { invalidProps } = state;

	const inheritedProps = pageProperties.filter(({ inherited }) => inherited);
	const fieldProps = pageProperties.filter(({ isField }) => isField);
	const paramProps = pageProperties.filter(({ isParam }) => isParam);
	const addedProps = _difference(pageProperties, [
		...inheritedProps,
		...fieldProps,
		...paramProps
	]);
	const orderedProps = [...addedProps, ...inheritedProps];

	return (
		<div className="scrollable-pane-container">
			<section className="add-page-property">
				{propertiesHeader}
				<now-button-bare
					icon-start="gear-outline"
					label={editLabel}
					size="sm"
					append-to-payload={appendToButtonPayload}
					variant="tertiary"
					disabled={disabled}
				/>
			</section>
			{renderAlert(outOfSyncFields, usingSharedPageDefinition, disabled)}
			{pageProperties.length === 0 ? (
				<EmptyState
					headerMessage={t('There are no page properties defined.')}
					description={t('Add one by clicking the button above.')}
				/>
			) : (
				<div>
					{orderedProps.map((prop) => (
						<PropertyFormView
							{...prop}
							dataBindingEnabled={dataBindingEnabled}
							bindings={bindings}
							disabled={disabled}
							invalidProps={invalidProps}
						/>
					))}
					{paramProps.length > 0 ? (
						<div className="pill-section">
							<div className="sub-header">
								{t('Optional parameters')}
								<sn-uib-property-description-tooltip
									description={parametersInfo}
									hide-text-link
								/>
							</div>
							{paramProps.map(({ name }) => (
								<now-pill label={_kebabCase(name)} size="sm" manage-selected />
							))}
						</div>
					) : null}
					{fieldProps.length > 0 ? (
						<div className="pill-section">
							<div className="sub-header">
								{t('Required parameters')}
								<sn-uib-property-description-tooltip
									description={fieldsInfo}
									hide-text-link
								/>
							</div>
							{fieldProps.map(({ name }) => (
								<now-pill label={name} size="sm" manage-selected />
							))}
						</div>
					) : null}
				</div>
			)}
		</div>
	);
};

createCustomElement('sn-uib-page-properties-configuration-pane', {
	view,
	properties: {
		pageProperties: {
			default: []
		},
		pageTemplates: {
			default: []
		},
		disabled: {
			default: false
		},
		dataBindingEnabled: {
			default: true,
			selectable: true
		},
		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		outOfSyncFields: {
			default: null
		},
		usingSharedPageDefinition: {
			default: false
		}
	},
	initialState: {
		invalidProps: {
			default: {}
		}
	},
	behaviors: [focusBehavior],
	actionHandlers: {
		'NOW_BUTTON_BARE#CLICKED': ({ dispatch, action }) => {
			const { payload = {} } = action;
			const { category } = payload;
			if (category === 'edit-props') {
				dispatch(SN_UIB_PAGE_PROPERTIES_CONFIGURATION_PANE_EDIT_CLICKED);
			}
		},
		'NOW_BUTTON#CLICKED': ({ dispatch, action, properties }) => {
			action.stopPropagation();
			const { outOfSyncFields } = properties;
			const { category } = action.payload;

			switch (category) {
				case 'add-props':
					outOfSyncFields.forEach((name) =>
						dispatch(PAGE_PROP_CREATED, { name, label: name })
					);
					break;
				case 'page-template-documentation':
					dispatch(
						'SN_UIB_PAGE_PROPERTIES_CONFIGURATION_PANE#DOCUMENTATION_LINK_CLICKED',
						{
							resourceId: 'uib-page-templates'
						}
					);
					break;
			}
		},
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: ({
			action,
			dispatch,
			properties: { pageProperties }
		}) => {
			action.stopPropagation();

			const prop = pageProperties.find(
				(p) => action.payload.propertyId === `${p.id}-value`
			);
			if (!prop) {
				return;
			}

			dispatch(PAGE_PROP_CHANGED, {
				...prop,
				value: action.payload.isStatic ? '@' : null,
				pagePropertyId: prop.id
			});
		},
		[PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED]: ({
			action,
			dispatch,
			properties: { pageProperties }
		}) => {
			action.stopPropagation();

			const { context } = action.payload;

			const prop = pageProperties.find(
				(p) =>
					action.payload.id ===
					`${p.id}${
						context === 'page-property-test-value' ? '-test' : ''
					}-value`
			);
			if (!prop) {
				return;
			}
			switch (context) {
				case 'page-property-value':
					dispatch(PAGE_PROP_CHANGED, {
						...prop,
						value: action.payload.value,
						pagePropertyId: prop.id
					});
					break;
			}
		}
	},
	styles
});
