import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-highlighted-value';
import get from 'lodash/get';
import '../sn-uib-property-input-data-link-toggle/sn-uib-property-input-data-link-toggle';
import '../sn-uib-property-description-tooltip';
import { t } from 'sn-translate';
import styles from './sn-uib-properties-editor-panel.scss';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';

export const PROPERTY_WRAPPER_STATIC_MODE_TOGGLED =
	'PROPERTY_WRAPPER#STATIC_MODE_TOGGLED';

const renderDataConfigIndicator = (isScripted, isDataBound) => {
	const hideIndicator = !isScripted && !isDataBound;
	const label = isScripted ? t('Script') : t('Data');
	const icon = isScripted ? 'document-code-outline' : 'database-outline';
	const color = isScripted ? 'purple' : 'info';
	const dataConfigIconClasses = {
		'data-config-indicator': true,
		'is-scripted': isScripted
	};
	if (hideIndicator) {
		return null;
	}
	return (
		<div class={dataConfigIconClasses}>
			<now-highlighted-value label={label} icon={icon} color={color} />
		</div>
	);
};

const view = (state, { updateState }) => {
	const { editingScript, isHovered } = state;
	const { child, isFirstElement } = state.properties;
	const {
		disabled,
		value,
		isScripted,
		scriptTemplate,
		id,
		alwaysBound,
		dataBindingEnabled,
		scriptingEnabled,
		container
	} = get(child, 'data.props', {});
	const currentContainer = get(container, 'current', null);
	const isDataBound = typeof value === 'string' && value.startsWith('@');
	const isStatic = !isScripted && !isDataBound;
	const isBooleanInput = child.sel === 'sn-uib-boolean-property-input';
	const isPageProperty =
		currentContainer && currentContainer.className === 'property-form-view';
	const hideDataBindButton =
		!alwaysBound && !dataBindingEnabled && !isDataBound;
	const staticOnly = !scriptingEnabled && hideDataBindButton;
	const hideConfigButtons =
		staticOnly ||
		(alwaysBound && !scriptingEnabled) ||
		isPageProperty ||
		disabled;

	const containerClasses = {
		'property-type-input-container': true,
		'is-hovered': isHovered,
		'is-data-bound': isDataBound,
		'is-scripted': isScripted,
		'is-static': isStatic,
		'is-boolean': isBooleanInput,
		'hide-config-buttons': hideConfigButtons,
		'is-editingScript': editingScript
	};
	const asideClasses = {
		'property-type-input-selection-state': true,
		'is-hovered': isHovered,
		'is-data-bound': isDataBound,
		'is-scripted': isScripted,
		'is-static': isStatic,
		'is-boolean': isBooleanInput,
		'hide-config-buttons': hideConfigButtons
	};

	const statefulButtonClasses = {
		'is-static': isStatic
	};

	const dynamicConfigButtonClasses = {
		'sn-uib-dynamic-config-buttons': true,
		'all-buttons-shown': scriptingEnabled && !hideDataBindButton,
		'two-buttons-shown':
			(scriptingEnabled && hideDataBindButton) ||
			(!scriptingEnabled && !hideDataBindButton) ||
			(alwaysBound && scriptingEnabled),
		'first-element-dynamic-config-buttons': isFirstElement
	};

	return (
		<article
			tabIndex={0}
			class={containerClasses}
			on-mouseenter={() => updateState({ isHovered: true })}
			on-mouseleave={() => updateState({ isHovered: false })}
			on-focus={() => updateState({ isHovered: true })}>
			<aside class={asideClasses} />
			<div className="property-type-input-child-container">
				<slot name="content" />
			</div>
			{editingScript || (isHovered && !disabled && !hideConfigButtons) ? (
				<div class={dynamicConfigButtonClasses}>
					{!alwaysBound ? (
						<div className="pencil-icon">
							<now-button-stateful
								class={statefulButtonClasses}
								manageSelected
								selected={isStatic}
								className="sn-uib-property-input-default-config-icon"
								variant="tertiary"
								icon="pencil-outline"
								tooltipContext={t('Static input')}
								disabled={disabled}
								bare
								append-to-payload={{ context: 'set-static' }}
							/>
						</div>
					) : null}
					{hideDataBindButton ? null : (
						<sn-uib-property-input-data-link-toggle
							selected={isDataBound}
							isStatic={true}
							propertyId={id}
							disabled={disabled}
						/>
					)}
					{!scriptingEnabled ? null : (
						<sn-uib-property-input-script-link-toggle
							selected={isScripted}
							editingScript={editingScript}
							current-script={
								isScripted ? value || scriptTemplate : scriptTemplate
							}
							propertyId={id}
							disabled={disabled}
							append-to-payload={{ propertyId: id }}
							clientScriptApiCompletions="@uxf/clientScriptApiCompletions"
						/>
					)}
				</div>
			) : (
				renderDataConfigIndicator(isScripted, isDataBound)
			)}
		</article>
	);
};

export default createCustomElement('sn-uib-property-config-wrapper', {
	view,
	styles,
	initialState: {
		editingScript: false,
		isHovered: false
	},
	properties: {
		child: {
			default: null
		},
		isFirstElement: {
			default: false
		}
	},
	eventHandlers: [
		{
			events: ['blur'],
			effect({
				updateState,
				action: {
					payload: { event }
				}
			}) {
				const nodeName = get(event, ['path', 0, 'nodeName']);
				if (!nodeName) {
					return;
				}
				if (nodeName === 'ARTICLE' || nodeName === 'BUTTON') {
					updateState({ isHovered: false });
				}
			}
		}
	],
	actionHandlers: {
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		},
		'NOW_BUTTON_STATEFUL#SELECTED_SET': ({ action, properties, dispatch }) => {
			const context = get(action, 'payload.context', '');
			if (context !== 'set-static') {
				return;
			}
			const { isScripted, id, value } = get(properties, 'child.data.props', {});
			const isDataBound =
				value && typeof value === 'string' && value.startsWith('@');

			action.stopPropagation();

			if (isScripted || isDataBound) {
				dispatch(PROPERTY_WRAPPER_STATIC_MODE_TOGGLED, { propertyId: id });
			}
		}
	}
});
