import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

const view = ({ componentId, properties }) => {
	return <span style={{ display: 'none' }} />;
};

export default createCustomElement('sn-uib-dnd-node-provider', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		active: {
			default: false,
			selectable: true
		},
		activeDirection: {
			default: '',
			selectable: true
		}
	}
});
