import { Fragment } from '@servicenow/ui-renderer-snabbdom';

function onNextFrame(callback, duration = 0) {
	return new Promise((resolve) => {
		setTimeout(function () {
			requestAnimationFrame(() => {
				callback();
				resolve(true);
			});
		}, duration);
	});
}

const applyHooks = (object, className, enter = 0, exit = 0, enterDelay = 0) => {
	const { data = {} } = object;
	const { hooks = {} } = data;
	const {
		insert = () => {},
		remove = (_, cb) => {
			cb();
		}
	} = hooks;
	return {
		...object,
		data: {
			...data,
			hook: {
				insert: (vnode) => {
					const { elm } = vnode;
					elm.classList.add(`${className}-enter`);
					onNextFrame(() => {
						elm.classList.add(`${className}-entering`);

						onNextFrame(() => {
							elm.classList.add(`${className}-entered`);
							elm.classList.remove(`${className}-entering`);
							insert(vnode);
						}, enter);
					}, enterDelay);
				},
				remove: (vnode, cb) => {
					const { elm } = vnode;
					elm.classList.remove(`${className}-enter`);
					elm.classList.remove(`${className}-entered`);
					elm.classList.add(`${className}-exit`);

					onNextFrame(() => {
						elm.classList.add(`${className}-exiting`);
						onNextFrame(() => {
							elm.classList.add(`${className}-exited`);
							elm.classList.remove(`${className}-exiting`);
							onNextFrame(() => remove(vnode, cb));
						}, exit);
					});
				}
			}
		}
	};
};
const Transition = ({ className, enter, exit, enterDelay }, ...children) => {
	return (
		<Fragment>
			{children.map((arr) => {
				return arr.map((obj) =>
					applyHooks(obj, className, enter, exit, enterDelay)
				);
			})}
		</Fragment>
	);
};

export { Transition, onNextFrame };
