import { t } from 'sn-translate';
import get from 'lodash/get';

export const getTabs = () => {
	const tabs = [
		{
			id: 'pages',
			icon: 'document',
			label: t('Pages')
		}
	];
	return tabs;
};

export const addSlotPlaceholders = (elements, disabled) => {
	elements.children &&
		elements.children.forEach((element) => addPlaceholder(element, disabled));
	return elements;
};

const addPlaceholder = (element, disabled) => {
	const { children, slotName, id, elementId } = element;
	const isSlot = !get(element, 'component.id');
	const isChildless = children && !children.length;
	const placeholder = {
		id: `${id}-placeholder`,
		elementId: `${id}-placeholder`,
		identifierProps: {
			type: 'icon',
			icon: 'plus-outline'
		},
		actionable: true,
		children: [],
		component: { id: 'placeholder' },
		label: t('Add component'),
		isModal: false,
		isModalDescendant: false,
		isNamedSlot: false,
		isRoot: false,
		parentElementId: elementId,
		parentSlotName: slotName,
		selectedSlotParentComponent: slotName,
		slotName: null,
		variant: 'placeholder',
		disabled
	};

	if (isSlot && isChildless) {
		children.push(placeholder);
	} else if (children && children.length) {
		children.forEach((element) => addPlaceholder(element));
	}
};
