import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-flat-list-item.scss';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import {
	FLAT_LIST_ITEM_CLICKED,
	FLAT_LIST_ITEM_POPOVER_TRIGGERED,
	FLAT_LIST_ITEM_POPOVER_CLOSED
} from './actions';

import { SN_UIB_PAGE_MANAGEMENT_POPOVER_ACTION_CLICKED } from '../sn-uib-page-management-popover';

const renderActions = (itemActionPopoverOpened, listType, actions, item) => {
	if (actions.length > 0) {
		return (
			<Fragment>
				<div className="actions now-align -block-center">
					<now-popover
						key={item.id}
						hide-tail
						manage-opened
						opened={itemActionPopoverOpened}
						on-click={(e) => e.stopPropagation()}
						append-to-payload={{ context: 'item-actions' }}>
						<now-button-iconic
							bare
							className="item-action-button"
							icon="ellipsis-v-outline"
							size="sm"
							hide-padding={true}
							slot="trigger"
							append-to-payload={{ button: 'itemAction' }}
							variant="tertiary"
							config-aria={{
								'aria-label': t('{0} options', listType)
							}}
						/>
						<sn-uib-page-management-popover
							actions={actions}
							append-to-payload={{ item }}
							slot="content"
						/>
					</now-popover>
				</div>
			</Fragment>
		);
	}
};

const renderOptionalPopover = (
	popoverContent,
	popoverButtonLabel,
	popoverButtonIcon,
	optionalPopoverOpened
) => {
	return (
		<now-popover
			manage-opened
			opened={optionalPopoverOpened}
			positions={[{ target: 'center-end', content: 'center-start' }]}
			append-to-payload={{ context: 'optional-popover' }}>
			<now-button
				slot="trigger"
				on-click={(e) => e.stopPropagation()}
				label={popoverButtonLabel}
				icon={popoverButtonIcon}
				size="sm"
				append-to-payload={{ context: 'popover' }}
				variant="tertiary"
				bare
			/>
			<sn-uib-flat-list-item-optional-popover
				slot="content"
				content={popoverContent}
			/>
		</now-popover>
	);
};

const view = (state, { dispatch }) => {
	const { itemActionPopoverOpened, optionalPopoverOpened, properties } = state;
	const {
		label,
		secondaryLabel,
		actions,
		selected,
		listType,
		item,
		displayPopover,
		popoverContent,
		popoverButtonLabel,
		popoverButtonIcon,
		actionsDisabled
	} = properties;
	return (
		<div
			class={{
				'now-align': true,
				wrapper: true,
				selected,
				'flat-list-item': true,
				'height-with-secondary-label': secondaryLabel
			}}
			on-click={() => dispatch(FLAT_LIST_ITEM_CLICKED)}>
			<div className="icon now-align -block-start">
				<now-icon icon="diamond-outline" size="sm" />
			</div>
			<div className="label-container">
				<div className="label">{label}</div>
				{secondaryLabel ? (
					<div className="secondary-label">{secondaryLabel}</div>
				) : null}
			</div>
			{displayPopover ? (
				<div className="popover-container">
					{renderOptionalPopover(
						popoverContent,
						popoverButtonLabel,
						popoverButtonIcon,
						optionalPopoverOpened
					)}
				</div>
			) : null}
			{!actionsDisabled
				? renderActions(itemActionPopoverOpened, listType, actions, item)
				: null}
		</div>
	);
};

createCustomElement('sn-uib-flat-list-item-optional-popover', {
	view: (state) => {
		return <Fragment>{state.properties.content}</Fragment>;
	},
	properties: {
		content: {
			default: null
		}
	}
});

createCustomElement('sn-uib-flat-list-item', {
	view,
	properties: {
		label: {
			default: null
		},
		secondaryLabel: {
			default: null
		},
		id: {
			default: null
		},
		actions: {
			default: null
		},
		disabled: {
			default: null
		},
		selected: {
			default: false
		},
		item: {
			default: null
		},
		listType: {
			default: ''
		},
		displayPopover: {
			schema: {
				type: 'boolean'
			},
			default: false
		},
		popoverContent: {
			default: null
		},
		popoverButtonLabel: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		popoverButtonIcon: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		actionsDisabled: {
			default: false
		}
	},
	styles,
	initialState: {
		itemActionPopoverOpened: false,
		optionalPopoverOpened: false
	},
	actionHandlers: {
		[FLAT_LIST_ITEM_CLICKED]: ({ action }) => {
			if (action.payload.button === 'itemAction') {
				action.stopPropagation();
			}
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action }) => {
			action.stopPropagation();
		},
		['NOW_BUTTON#CLICKED']: ({
			action,
			dispatch,
			properties,
			state,
			updateState
		}) => {
			action.stopPropagation();
			if (action.payload.context === 'popover') {
				const { item } = properties;
				dispatch(FLAT_LIST_ITEM_POPOVER_TRIGGERED, { item });
				updateState({
					optionalPopoverOpened: !state.optionalPopoverOpened
				});
			}
		},
		'NOW_POPOVER#OPENED_SET': ({ action, updateState }) => {
			action.stopPropagation();
			if (action.payload.context === 'item-actions') {
				updateState({
					itemActionPopoverOpened: action.payload.value
				});
			} else if (action.payload.context === 'optional-popover') {
				updateState({
					optionalPopoverOpened: action.payload.value
				});
			}
		},
		[SN_UIB_PAGE_MANAGEMENT_POPOVER_ACTION_CLICKED]: ({ updateState }) => {
			updateState({
				itemActionPopoverOpened: false
			});
		},
		[FLAT_LIST_ITEM_POPOVER_CLOSED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ optionalPopoverOpened: false });
		}
	}
});
