export const filterElements = (elements, search, property) => {
	const searchExpression = new RegExp(search.trim(), 'i');
	return elements
		.map((element) => filterElementsHelper(element, searchExpression, property))
		.filter((element) => Object.keys(element).length > 0);
};

const filterElementsHelper = (child, search, property) => {
	let newChildren;
	const { [property]: label = '', children } = child;
	if (label.search(search) > -1) {
		return child;
	}

	if (children) {
		newChildren = children
			.map((child) => filterElementsHelper(child, search, property))
			.filter((child) => Object.keys(child).length > 0);
		if (newChildren.length > 0) {
			return { ...child, children: newChildren };
		}
	}
	return {};
};

export const expandAllItems = (elements, paths = [], currentPath = []) => {
	elements.forEach(({ children, isModal, id }) => {
		if (children) {
			if (!isModal) {
				paths.push([...currentPath, id]);
				expandAllItems(children, paths, [...currentPath, id]);
			} else if (isModal) {
				paths.push(['modals', id]);
				expandAllItems(children, paths, ['modals', id]);
			}
		}
	});
	return paths;
};
