import { t } from 'sn-translate';
import _startCase from 'lodash/startCase';
import { removePrefixes } from '../helpers/removePrefixes';

/**
 * Takes the bindings and a chosen event and adds the payloadShape expected values to the bindings.
 * @param {Array.<Object>} bindings - Array of available bindings
 * @param {Object} selectedEvent - Event selected from the dropdown panel to be mapped
 * @returns {Array.<Object>} New bindings array with the old bindings and additional payload bindings.
 */
export const addPayloadBindings = (bindings, selectedEvent) => {
	return selectedEvent && selectedEvent.payloadShape
		? [
				...bindings,
				...selectedEvent.payloadShape.map(({ name, value, defaultValue }) => ({
					name,
					value: value || defaultValue,
					label: `@payload.${name}`
				}))
		  ]
		: bindings;
};

const types = {
	EVENT: t('Page-level event handlers'),
	CLIENT_SCRIPT: t('Scripts'),
	DATABROKER_OP: t('Data broker operations'),
	DECLARATIVE_ACTION: t('Declarative actions')
};

const filterHandlers = (handlers, isGlobal) => {
	return handlers.reduce(
		([globalHandlers, otherHandlers], handler) => {
			return isGlobal(handler)
				? [[...globalHandlers, handler], otherHandlers]
				: [globalHandlers, [...otherHandlers, handler]];
		},
		[[], []]
	);
};

export const sortHandlersByType = (handlers = []) => {
	const transformedHandlers = handlers.map((handler = {}) => {
		const { label = '', type = '' } = handler;
		const [handlerName = '', sourceName] = label
			.split('(')
			.map((str) => str.slice(0, str.length - 1));
		let newLabel = handlerName;
		if (type === 'DATABROKER_OP') {
			const name = removePrefixes(handlerName || '') || '';
			const casedName = _startCase(name.toLowerCase()) || '';
			newLabel = (handlerName || '').replace(name, casedName);
		}
		return { ...handler, label: newLabel, sourceName };
	});
	const [globalHandlers = [], remainingHandlers = []] = filterHandlers(
		transformedHandlers,
		(handler) => handler.type === 'EVENT' && handler.ancestor === true
	);

	const sorted = Object.keys(types).reduce((acc, type) => {
		const filteredChildren = remainingHandlers.filter(
			(handler) => handler.type === type
		);
		if (type === 'DATABROKER_OP') {
			const dataBrokerHandlers = filteredChildren.reduce(
				(items, handler = {}) => {
					if (!items[handler.sourceName]) {
						items[handler.sourceName] = [handler];
					} else {
						items[handler.sourceName] = [...items[handler.sourceName], handler];
					}
					return items;
				},
				{}
			);

			acc.push(dataBrokerHandlers);
		} else {
			if (filteredChildren.length > 0) {
				acc.push({ [types[type]]: filteredChildren });
			}
		}
		return acc;
	}, []);

	if (globalHandlers.length > 0) {
		return [{ [t('Inherited event handlers')]: globalHandlers }, ...sorted];
	} else {
		return [...sorted];
	}
};
