const sizeClass = (size) => {
	switch (size) {
		case 'sm':
			return 'small';
		case 'md':
			return 'medium';
		default:
			return '';
	}
};

export default ({
	headerMessage,
	description,
	icon = 'sliders-fill',
	size = 'lg',
	hideBorder = false,
	hidePadding = false
}) => (
	<div
		className={`empty-state
		${sizeClass(size)}
		${hideBorder ? 'hide-border' : ''}
		${hidePadding ? 'hide-padding' : ''}`}>
		<figure>
			<div className="icon-container">
				<now-icon icon={icon} size="lg" />
			</div>
			<figcaption>
				<span className="empty-state-headline">{headerMessage}</span>
				<span className="empty-state-description">{description}</span>
			</figcaption>
		</figure>
	</div>
);
