import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { onNextFrame } from '../helpers/Transition';
import { RESIZE_ENDED, RESIZE_STARTED } from '../sn-uib-resize-handle';
import strategyManager from '../helpers/strategyManager';
import focusStrategy from '../helpers/focusStrategy';
import get from 'lodash/get';
const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	focusTargetId
} = DevxKeyFocusBehavior;

const keyboardNavKeys = [
	'ArrowUp',
	'ArrowDown',
	'ArrowRight',
	'ArrowLeft',
	'Enter',
	'Tab',
	'Space'
];
/**
 * @typedef KeyboardHandlerObject
 * @property {keyof GlobalEventHandlersEventMap} events
 */

/** @type {KeyboardHandlerObject & EventListenerObject} */
const KEYBOARD_HANDLER = {
	events: ['mousedown', 'keydown'],
	/**
	 * @param {HTMLElement} element
	 */
	addElement(element) {
		const { events } = this;

		for (const eventType of events) {
			element.addEventListener(eventType, this, true);
		}
	},

	/**
	 * @param {HTMLElement} element
	 */
	removeElement(element) {
		const { events } = this;

		for (const eventType of events) {
			element.removeEventListener(eventType, this, true);
		}
	},

	handleEvent(event) {
		const { currentTarget: element, type, key } = event;
		if (
			type === 'keydown' &&
			keyboardNavKeys.includes(key) &&
			element.isKeyboardNav !== true
		) {
			element.isKeyboardNav = true;
		} else if (type === 'mousedown' && element.isKeyboardNav !== false) {
			element.isKeyboardNav = false;
		}
	}
};

const setFocusOnNextFrame = (dispatch, id) => {
	if (!dispatch || !id) {
		return;
	}
	onNextFrame(() => focusTargetId(dispatch, id));
};

const focusDropEntryPoint = (coeffects) => {
	const {
		updateProperties,
		dispatch,
		state: {
			properties: { dragEntryId }
		}
	} = coeffects;

	if (!dragEntryId) {
		updateProperties({
			focusNextEntrypoint: true
		});
	} else {
		setFocusOnNextFrame(dispatch, dragEntryId);
	}
};

const clearDragSession = (coeffects) => {
	const { updateProperties } = coeffects;

	updateProperties({
		focusNextEntrypoint: false
	});
};

const view = ({ properties: { isDragging } }, dispatch) => {
	const stageRoot = strategyManager.get('stage', 'ref');

	return (
		<span
			on-focusin={({ target }) => {
				const current = get(stageRoot, 'current', null);

				if (!isDragging || !current) {
					return;
				}

				const isNotInTheStage = !(
					current.contains(target) || current.getRootNode().host === target
				);
				if (isNotInTheStage) {
					dispatch.updateProperties({
						focusNextEntrypoint: false
					});

					dispatch('KB_NAV_PROVIDER#INVALID_DROP');
				}
			}}>
			<slot />
		</span>
	);
};

export default createCustomElement('sn-uib-kb-nav-provider', {
	view,
	properties: {
		dragEntryId: {
			default: '',
			selectable: true
		},
		focusNextEntrypoint: {
			default: false
		},
		elementRoot: {
			default: false
		},
		pageTree: {
			default: {}
		},
		isDragging: {
			default: false
		},
		isResizing: {
			default: false,
			selectable: true
		},
		selectedElementId: {
			default: null
		},
		overrideElementFocus: {
			default: false
		},
		isKeyboardNav: {
			default: false,
			selectable: true
		}
	},
	behaviors: [keyFocusBehavior],

	actionHandlers: {
		['DEVX_KEY_FOCUS_BEHAVIOR#TARGET_REF_FOCUSED']: () => {
			focusStrategy.deactivate();
		},
		[RESIZE_ENDED]: ({ updateProperties }) => {
			updateProperties({ isResizing: false });
		},
		[RESIZE_STARTED]: ({ updateProperties }) => {
			updateProperties({ isResizing: true });
		},
		[actionTypes.COMPONENT_CONNECTED]: ({ action }) => {
			KEYBOARD_HANDLER.addElement(action.payload.host);
		},
		[actionTypes.COMPONENT_DISCONNECTED]: ({ action }) => {
			KEYBOARD_HANDLER.removeElement(action.payload.host);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			dispatch
		}) => {
			const { name, value } = action.payload;
			const { overrideElementFocus } = properties;
			if (name !== 'selectedElementId' || !value) {
				return;
			}

			if (!overrideElementFocus) {
				focusStrategy.init(dispatch, `${value}_ROOT`);
			}
		},
		'UIB_STAGE#ELEMENT_ROOT_FOCUS_CREATED': (coeffects) => {
			const { updateProperties, action } = coeffects;

			const { id } = action.payload;

			updateProperties({
				elementRoot: id
			});
		},
		'UIB_STAGE#DND_ENTRYPOINT_CREATED': (coeffects) => {
			const {
				updateProperties,
				dispatch,
				action,
				state: {
					properties: { focusNextEntrypoint }
				}
			} = coeffects;

			const { id } = action.payload;

			if (focusNextEntrypoint) {
				updateProperties({
					dragEntryId: id,
					focusNextEntrypoint: false
				});
				setFocusOnNextFrame(dispatch, id);
			} else {
				updateProperties({
					dragEntryId: id
				});
			}
		},
		'UIB_RESIZE_HANDLE#RESIZED': (coeffects) => {
			const {
				action: {
					payload: { resizer }
				},
				dispatch
			} = coeffects;
			setFocusOnNextFrame(dispatch, resizer);
		},
		'DRAG_LEAF#START_KEYBOARD_DRAG': focusDropEntryPoint,
		'UIB_DRAGGABLE_BEHAVIOR#DRAG_END': clearDragSession,
		'UIB_DND_DROPZONE#INVALID_DROP': clearDragSession,
		'UIB_DRAGGABLE_BEHAVIOR#INVALID_DROP': clearDragSession,
		'UIB_PAGE_MANAGEMENT#FOCUS_DND_ENTRYPOINT': focusDropEntryPoint
	}
});
