import { attachInteract, removeInteract } from './utils';

const mountedEffect = (coeffects) => {
	const { dispatch, action } = coeffects;
	const { el, data = {} } = action.payload;
	attachInteract({ el, dispatch, data });
};

const unmountedEffect = (coeffects) => {
	const { action } = coeffects;
	const { el } = action.payload;
	removeInteract({ el });
};

const droppedEffect = (coeffects) => {};

const draggedOverEffect = (coeffects) => {};

const dragLeftEffect = (coeffects) => {};

const dragEnteredEffect = (coeffects) => {};

export {
	mountedEffect,
	unmountedEffect,
	droppedEffect,
	draggedOverEffect,
	dragLeftEffect,
	dragEnteredEffect
};
