import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
export const config = {
	name: 'sn-uib-dynamic-property-input',
	properties: {
		/**
		 * Boolean for whether input should be focused on render
		 * @type {string}
		 */
		autofocus: { default: false },
		/**
		 * Boolean to determine when to dispatch value update
		 * @type {boolean}
		 */
		dispatchOnBlur: { default: false },
		/**
		 * Label to be displayed above the input field.
		 * @type {string}
		 */
		label: {},
		/**
		 * The string value given to the property from the parent.
		 * @type {string}
		 */
		value: { reflect: true },
		/**
		 * Whether the input should be disabled
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * The ID of the property
		 * @type {string}
		 */
		id: { default: '' },
		/**
		 * Whether the property is statically or dynamically configured
		 * @type {boolean}
		 */
		isStatic: { default: true },
		/**
		 * Whether to prevent static configuration (if the property's typeMeta
		 * value is a selectable property address)
		 * @type {boolean}
		 */
		alwaysBound: { default: false },
		/**
		 * Whether dynamic configuration is enabled.
		 * @type {boolean}
		 */
		dataBindingEnabled: {
			default: true
		},
		/**
		 * An object containing a pattern to pass in to a RegExp in order to
		 * validate string values and a message to display if invalid
		 * @type {object}
		 */
		validation: {
			default: {}
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * Error state code for property
		 */
		error: {
			schema: {
				type: ['object', 'null']
			},
			default: null
		},
		/**
		 * A short description of what the property does or how it is used
		 * @type {string}
		 */
		description: {},
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {},
		/**
		 * Whether to show the script button
		 */
		scriptingEnabled: {
			default: false
		},
		/**
		 * The template for scripts to use
		 */
		scriptTemplate: {
			default: ''
		},
		/**
		 * Translatable for page properties
		 */
		translatable: {
			default: false
		},
		/**
		 * To set the max width on pop up panel dropdown, which would be able to
		 * show dot walked resolved value
		 */
		longestResolvedSuggestedValue: {
			computed({ suggestedResolvedBindings }) {
				// Find the longest suggested resolved value
				const newSuggestedResolvedBindings = cloneDeep(
					suggestedResolvedBindings
				);
				if (isEmpty(suggestedResolvedBindings)) {
					return;
				}
				const sortedArray = newSuggestedResolvedBindings.sort(
					(a, b) => b.label.length - a.label.length
				);
				return sortedArray[0].label;
			}
		}
	}
};
