import { createCustomElement } from '@servicenow/ui-core';
import '../sn-uib-layout-drop-branch';
import '../sn-uib-layout-drop-leaf';
import '../sn-uib-layout-drag-leaf';

const getDropTag = (type) => {
	switch (type) {
		case 'leaf':
			return 'sn-uib-layout-drop-leaf';
		case 'col':
		case 'row':
		default:
			return 'sn-uib-layout-drop-branch';
	}
};

const getDragTag = (type) => {
	switch (type) {
		case 'leaf':
			return 'div';
		case 'col':
		case 'row':
		default:
			return 'div';
	}
};
const view = (state) => {
	const { isDragging, type, parentId, keyboardDrag, id } = state.properties;
	const Wrapper = isDragging ? getDropTag(type) : getDragTag(type); // eslint-disable-line no-unused-vars

	const keymaps = state.properties.keymaps || {};
	const wrapperProps =
		isDragging && keymaps
			? {
					isActive: `@${id}_dnd_node/active`,
					activeDirection: `@${id}_dnd_node/activeDirection`,
					id,
					entrypoint: keymaps.entrypoint,
					keymaps,
					keyboardDrag,
					additionalData: {
						parentId
					},
					type,
					style: { width: '100%' }
			  }
			: { style: { width: '100%' } };

	return (
		<Wrapper {...wrapperProps}>
			<slot />
		</Wrapper>
	);
};

export default createCustomElement('sn-uib-element-dnd-wrapper', {
	view,
	properties: {
		isDragging: {
			default: false
		},
		id: {
			default: ''
		},
		detachedElement: {
			default: null
		},
		parentId: {
			default: ''
		},
		childId: {
			default: null
		},
		parentElementId: {
			default: null
		},
		nextId: {
			default: null
		},
		keymaps: {
			default: {}
		},
		previousId: {
			default: null
		},
		keyboardDrag: {
			default: false
		},
		type: {
			default: 'blank'
		}
	}
});
