import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-popover';
import '@servicenow/now-input';
import styles from './sn-uib-address-bar.scss';
import omit from 'lodash/omit';
import clone from 'lodash/clone';
import { FIELDS, PARAMS, validationRegex } from './sn-uib-address-bar';

/* prettier-ignore */
const invalidMessage = t('Test values are restricted to alphanumeric characters and select special characters which are ~ - . _');

const tvInput = (type, disabled, invalidTestValues) => ({ pageProperty }) => {
	return (
		<now-input
			name={pageProperty.name}
			label={pageProperty.name}
			value={pageProperty.testValue || ''}
			append-to-payload={{ type, pageProperty }}
			disabled={disabled}
			messages={
				invalidTestValues[pageProperty.id]
					? [
							{
								status: 'critical',
								content: invalidMessage
							}
					  ]
					: []
			}
		/>
	);
};

createCustomElement('url-test-values', {
	view: (state) => {
		const {
			properties: {
				fields,
				params,
				applications,
				userScope,
				pageSysPolicy,
				supportScope,
				pageScope,
				disabled
			},
			invalidTestValues
		} = state;
		return (
			<div className="url-test-values">
				<div className="content help-text">
					<h3>Test values</h3>
					<p>
						{
							/* prettier-ignore */
							t('Preview how components, scripts, and data resources will render with test values. This applies to anything you create in UI Builder. For example, you can use the table "incident" as a test value to populate actual data into a page.')
						}
					</p>
					{/*
						TODO: enable open link(s) for field/param editing
						<now-button-bare size="sm">Edit page configuration</now-button-bare>
					*/}
				</div>
				<div className="transaction-alert">
					<sn-uib-transaction-alert
						size="sm"
						applications={applications}
						userScope={userScope}
						requestScopeId={pageScope.id}
						requestScopeName={pageScope.label}
						recordProtectionPolicy={pageSysPolicy}
						supportScope={supportScope}
						requestItemType={'Page'}
					/>
				</div>
				{fields.length ? (
					<div className="content field-inputs">
						<h4>Required parameters</h4>
						{fields.map(tvInput(FIELDS, disabled, invalidTestValues))}
					</div>
				) : null}
				{params.length ? (
					<div className="content param-inputs">
						<h4>Optional parameters</h4>
						{params.map(tvInput(PARAMS, disabled, invalidTestValues))}
					</div>
				) : null}
				<now-button-iconic bare icon="close-fill" />
			</div>
		);
	},
	properties: {
		fields: {
			default: []
		},
		params: {
			default: []
		},
		pageSysPolicy: {
			default: ''
		},
		applications: {
			default: []
		},
		userScope: {
			default: null
		},
		supportScope: {
			default: false
		},
		pageScope: {
			default: null
		},
		disabled: {
			default: false
		}
	},
	initialState: {
		invalidTestValues: {}
	},
	actionHandlers: {
		['NOW_INPUT#INPUT']: ({ action, dispatch, updateState, state }) => {
			action.stopPropagation();
			const { invalidTestValues } = state;
			const { fieldValue: testValue, type, pageProperty } = action.payload;
			const pagePropertyId = pageProperty.id;
			const newInvalidTestValues = clone(invalidTestValues);
			if (!testValue.match(validationRegex)) {
				newInvalidTestValues[pagePropertyId] = true;
				if (!invalidTestValues[pagePropertyId]) {
					updateState({
						invalidTestValues: newInvalidTestValues
					});
				}
			} else {
				if (invalidTestValues[pagePropertyId]) {
					updateState({
						invalidTestValues: omit(invalidTestValues, pagePropertyId)
					});
				}
			}
			dispatch('URL_TEST_VALUE#INPUT', {
				testValue,
				type,
				pagePropertyId
			});
		},
		['NOW_INPUT#VALUE_SET']: ({ action, dispatch }) => {
			action.stopPropagation();
			const { pageProperty, value } = action.payload;
			const pagePropertyId = pageProperty.id;
			dispatch('URL_TEST_VALUE#CHANGE_COMPLETE', {
				...pageProperty,
				pagePropertyId,
				testValue: value
			});
		},
		['NOW_BUTTON_ICONIC#CLICKED']: ({ dispatch }) => {
			dispatch('URL_TEST_VALUE#CLOSE_ICON_CLICKED');
		}
	},
	styles
});
