import { createCustomElement } from '@servicenow/ui-core';
import mediaQueryBehavior from '@servicenow/behavior-media-query';
import styles from './sn-uib-component-interstitial.scss';

const view = ({ properties, behaviors }) => {
	const { content } = properties;
	const { mediaQuery } = behaviors;
	const fullscreen = mediaQuery.smallQuery;

	return (
		<div
			class={{
				'sn-uib-component-interstitial-container': true,
				'is-not-fullscreen': !fullscreen
			}}>
			<div
				class={{
					'sn-uib-component-interstitial': true
				}}>
				<div
					class={{
						'sn-uib-component-interstitial-body': true
					}}>
					<slot>
						<p className="now-m-block--0">{content}</p>
					</slot>
				</div>
			</div>
			<div className="sn-uib-component-interstitial-overlay" />
		</div>
	);
};

export default createCustomElement('sn-uib-component-interstitial', {
	view,
	properties: {
		content: {}
	},
	behaviors: [
		{
			behavior: mediaQueryBehavior,
			options: {
				smallQuery: '(max-width: 27em)'
			}
		}
	],
	styles
});
