import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-dropdown';
import styles from './sn-uib-modal-picker-config.scss';
import { HANDLER_VALUE_CHANGED } from '../sn-uib-event-mapping-handler-values-config';
import '../sn-uib-choice-property-input';
import '../sn-uib-dynamic-property-input';
import { get } from 'lodash';
/**
 * @typedef DropdownItem
 * @property {string} id
 * @property {string} label
 */

/**
 * @typedef Element
 * @property {string} id
 * @property {string} instanceName
 */

/**
 *
 * @param {Element[]} modalElements
 * @return {DropdownItem[]} modal choices
 */
const getModalChoices = (modalElements) =>
	modalElements
		.filter(
			(element) =>
				get(element, 'component.tag', null) !== 'now-uxf-modal-viewport'
		)
		.map((element) => {
			return {
				id: `[component-id$='${element.id}']`,
				label: element.instanceName
			};
		});

const isCustomOrViewportModal = (modalElements, elementId) =>
	!!modalElements.find((element) => {
		const tag = get(element, 'component.tag', null);
		return (
			(tag === 'now-uxf-page-container' || tag === 'now-uxf-modal-viewport') &&
			`[component-id$='${element.id}']` === elementId
		);
	});

const view = ({ properties }) => {
	const {
		appConfig: { modalElements = [], bindings, disabled },
		modalId,
		container,
		description
	} = properties;

	const label = t('Choose a modal dialog');
	const items = getModalChoices(modalElements);
	const placeholder = isEmpty(items)
		? 'No modals have been created'
		: 'Select a modal';
	const selectedItem = modalId || undefined;

	return (
		<div className="modal-picker-container">
			{modalId && modalId.startsWith('@') ? (
				<sn-uib-dynamic-property-input
					description={description}
					bindings={bindings}
					value={modalId}
					label={label}
					isStatic={false}
					disabled={disabled}
				/>
			) : (
				<sn-uib-choice-property-input
					description={description}
					choices={items}
					container={container}
					value={selectedItem}
					label={label}
					placeholder={placeholder}
					disabled={disabled}
				/>
			)}
		</div>
	);
};

createCustomElement('sn-uib-modal-picker-config', {
	view,
	styles,
	properties: {
		/**
		 * @typedef AppConfig
		 * @property {Object[]} appRoutes
		 * @property {Object[]} bindings
		 * @property {Object} dataBrokerInstanceNameMap
		 * @property {Element[]} modalElements
		 * @property {string} elementId
		 * @property {boolean} disabled
		 */
		/**
		 * a config passed in from emotive-builder that contains relevant data
		 * @type {AppConfig}
		 */
		appConfig: {
			default: null
		},
		/**
		 *  the modal id, eg [component-id$='ckaczuobccaaaaaaggddhggbqhkuccjcm']
		 */
		modalId: {
			default: undefined
		},
		/**
		 * The properties editor container that the now-dropdown-panel should be constrained within
		 * @type {HTMLElement}
		 */
		container: {
			default: '@sn-uib-element-configuration-panel/container'
		},
		/**
		 * Description tooltip
		 * @type {string}
		 */
		description: {
			default: null
		}
	},
	actionHandlers: {
		['SN_UIB_CHOICE_PROPERTY_INPUT#VALUE_CHANGED']: ({
			action,
			dispatch,
			properties
		}) => {
			const {
				payload: { value }
			} = action;
			action.stopPropagation();

			//look at modal type and set bare to true if custom modal
			dispatch(HANDLER_VALUE_CHANGED, {
				value: isCustomOrViewportModal(
					properties.appConfig.modalElements,
					value
				),
				handlerValueId: 'bare'
			});
			dispatch(HANDLER_VALUE_CHANGED, {
				value: value,
				handlerValueId: 'modalId'
			});
		},
		['SN_UIB_PROPERTY_INPUT#PROPERTY_BINDING_BUTTON_CLICKED']: ({
			action,
			dispatch
		}) => {
			dispatch(HANDLER_VALUE_CHANGED, {
				value: action.payload.isStatic ? '@' : '',
				handlerValueId: 'modalId'
			});
		},
		['SN_UIB_PROPERTY_INPUT#PROPERTY_BINDING_SET']: ({ action, dispatch }) => {
			const {
				payload: { value }
			} = action;
			action.stopPropagation();

			dispatch(HANDLER_VALUE_CHANGED, {
				value: value,
				handlerValueId: 'modalId'
			});
		}
	}
});
