import focusBehavior from '@servicenow/behavior-focus';
import { createCustomElement } from '@servicenow/ui-core';
import 'sn-ui-builder-icons';
import '../sn-uib-data-broker-editor';
import {
	DATA_BROKER_EDITOR_PROPERTY_CHANGED,
	DATA_BROKER_EDITOR_EVENT_MAPPING_ADDED,
	DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_ADDED,
	DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_DELETED,
	DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_CHANGED
} from '../sn-uib-data-broker-editor/actionHandlers';
import {
	DATA_BROKER_NAVIGATOR_ADD_DATA_BROKER_BUTTON_CLICKED,
	DATA_BROKER_NAVIGATOR_DATA_BROKER_SELECTED,
	DATA_BROKER_NAVIGATOR_DELETE_DATA_BROKER_BUTTON_CLICKED,
	DATA_BROKER_NAVIGATOR_OPEN_DATA_BROKER_DEF_CLICKED
} from '../sn-uib-data-broker-navigator';
import '../sn-uib-data-broker-preview';
import * as snUIBDynamicPropertyInput from '../sn-uib-dynamic-property-input';
import styles from './sn-uib-data-broker-configuration-panel.scss';

export const DATA_BROKER_CONFIG_PANEL_DELETE_BUTTON_CLICKED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#DELETE_BUTTON_CLICKED';
export const DATA_BROKER_CONFIG_PANEL_ADD_BUTTON_CLICKED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#ADD_BUTTON_CLICKED';
export const DATA_BROKER_CONFIG_PANEL_DATA_BROKER_SELECTED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#DATA_BROKER_SELECTED';
export const DATA_BROKER_CONFIG_PANEL_PROPERTY_CHANGED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#PROPERTY_CHANGED';
export const DATA_BROKER_CONFIG_PANEL_CONTEXT_BINDINGS_REQUESTED =
	'DATA_BROKER_CONFIG_PANEL#CONTEXT_BINDINGS_REQUESTED';
export const DATA_BROKER_CONFIG_PANEL_OPEN_DATA_BROKER_DEF_CLICKED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#OPEN_DATA_BROKER_DEF_CLICKED';
const DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_ADDED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_ADDED';
const DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_HANDLER_ADDED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_HANDLER_ADDED';
const DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_HANDLER_DELETED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_HANDLER_DELETED';
const DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_HANDLER_CHANGED =
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_HANDLER_CHANGED';

const view = (state) => {
	const {
		dataBrokerInstances,
		selectedDataBroker,
		disabled,
		currentSelectedProperties,
		currentSelectablePropPreview,
		currentSelectablePropExecutionTime,
		selectedDataBrokerId,
		parentDataBrokerInstances,
		selectedDataBrokerIsAncestor,
		bindings,
		dataBrokerInstanceNameMap,
		dataBrokerDefinitions,
		eventHandlers,
		eventMappings,
		eventMappingHandlers,
		appConfig,
		interfaceConfigurationComponent,
		clientTransformScriptTemplate,
		isMacroponentReadOnly
	} = state.properties;

	return (
		<div className="sn-uib-data-broker-configuration-panel-container">
			<div className="body now-align">
				<sn-uib-data-broker-navigator
					dataBrokerInstances={dataBrokerInstances}
					disabled={disabled}
					selectedDataBroker={selectedDataBroker}
					parentDataBrokerInstances={parentDataBrokerInstances}
					selectedDataBrokerIsAncestor={selectedDataBrokerIsAncestor}
					dataBrokerDefinitions={dataBrokerDefinitions}
					isMacroponentReadOnly={isMacroponentReadOnly}
				/>
				<sn-uib-data-broker-editor
					selectedDataBroker={selectedDataBroker}
					disabled={disabled}
					currentSelectedProperties={currentSelectedProperties}
					selectedDataBrokerIsAncestor={selectedDataBrokerIsAncestor}
					bindings={bindings}
					dataBrokerInstanceNameMap={dataBrokerInstanceNameMap}
					eventHandlers={eventHandlers}
					eventMappings={eventMappings}
					eventMappingHandlers={eventMappingHandlers}
					appConfig={appConfig}
					interfaceConfigurationComponent={interfaceConfigurationComponent}
					append-to-payload={{ dataBrokerId: selectedDataBrokerId }}
					clientTransformScriptTemplate={clientTransformScriptTemplate}
					output={currentSelectablePropPreview}
				/>
				<sn-uib-data-broker-preview
					component-id={`data-broker-preview-pane-${selectedDataBrokerId}`}
					output={currentSelectablePropPreview}
					executionTime={currentSelectablePropExecutionTime}
				/>
			</div>
		</div>
	);
};

createCustomElement('sn-uib-data-broker-configuration-panel', {
	initialState: {
		expanded: false
	},
	properties: {
		/**
		 * the list of data broker definitions
		 * @type {array}
		 */
		dataBrokerDefinitions: {
			default: null
		},
		/**
		 * the list of data broker instances and their properties
		 * @type {array}
		 */
		dataBrokerInstances: {
			default: null
		},
		/**
		 * the ID of the data broker instance that is currently being configured
		 * @type {string}
		 */
		selectedDataBrokerId: {
			default: null
		},
		/** the data broker instance that is currently being configured
		 * @type {object}
		 */
		selectedDataBroker: {
			computed({
				properties: {
					dataBrokerInstances,
					parentDataBrokerInstances,
					selectedDataBrokerId
				}
			}) {
				return [...dataBrokerInstances, ...parentDataBrokerInstances].find(
					(dataBroker) => dataBroker.id === selectedDataBrokerId
				);
			}
		},
		/**
		 * Whether the component is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * The array of properties for the currently selected data broker instance
		 * @type {array}
		 */
		currentSelectedProperties: {
			default: []
		},
		currentSelectablePropPreview: { default: '' },
		currentSelectablePropExecutionTime: { default: '' },
		referenceDisplayValues: { default: null, selectable: true },
		referenceResults: { default: null, selectable: true },
		referenceSearching: { default: null, selectable: true },
		tables: { default: null, selectable: true },
		tableFieldValues: { default: null, selectable: true },
		c3PropertyConfig: { default: null, selectable: true },
		/**
		 * whether the panel is expanded
		 * @type {boolean}
		 */
		expanded: {
			default: false
		},
		/**
		 * the list of parent data broker instances and their properties
		 * @type {array}
		 */
		parentDataBrokerInstances: {
			default: []
		},
		/**
		 * whether the currently selected data broker instance is an ancestor broker
		 * @type {boolean}
		 */
		selectedDataBrokerIsAncestor: {
			default: false
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * A map of data broker instance IDs to their instance names
		 */
		dataBrokerInstanceNameMap: {
			schema: { type: 'object' },
			default: {}
		},
		eventHandlers: {
			default: []
		},
		eventMappings: {
			default: []
		},
		eventMappingHandlers: {
			default: null
		},
		appConfig: {
			default: null
		},
		interfaceConfigurationComponent: {
			default: null
		},
		clientTransformScriptTemplate: {
			default: null
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	actionHandlers: {
		[DATA_BROKER_NAVIGATOR_ADD_DATA_BROKER_BUTTON_CLICKED]: ({
			action,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const { dataBrokerDefinitions } = properties;
			dispatch(DATA_BROKER_CONFIG_PANEL_ADD_BUTTON_CLICKED, {
				dataBrokerDefinitions
			});
		},
		[DATA_BROKER_NAVIGATOR_DATA_BROKER_SELECTED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(DATA_BROKER_CONFIG_PANEL_DATA_BROKER_SELECTED, action.payload);
		},
		[DATA_BROKER_EDITOR_PROPERTY_CHANGED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(DATA_BROKER_CONFIG_PANEL_PROPERTY_CHANGED, action.payload);
		},
		[DATA_BROKER_NAVIGATOR_DELETE_DATA_BROKER_BUTTON_CLICKED]: ({
			action,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const {
				selectedDataBroker: { instanceName, id }
			} = properties;
			dispatch(DATA_BROKER_CONFIG_PANEL_DELETE_BUTTON_CLICKED, {
				selectedDataBroker: { name: instanceName, id }
			});
		},
		[snUIBDynamicPropertyInput.DYNAMIC_PROPERTY_INPUT_FOCUSED]: ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			const { id: propertyId } = action.payload;
			const {
				selectedDataBroker: { instanceName, id }
			} = properties;
			dispatch(DATA_BROKER_CONFIG_PANEL_CONTEXT_BINDINGS_REQUESTED, {
				selectedDataBroker: { name: instanceName, id },
				propertyId
			});
		},
		[DATA_BROKER_NAVIGATOR_OPEN_DATA_BROKER_DEF_CLICKED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			dispatch(
				DATA_BROKER_CONFIG_PANEL_OPEN_DATA_BROKER_DEF_CLICKED,
				action.payload
			);
		},
		[DATA_BROKER_EDITOR_EVENT_MAPPING_ADDED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_ADDED, action.payload);
		},
		[DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_ADDED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			dispatch(
				DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_HANDLER_ADDED,
				action.payload
			);
		},
		[DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_DELETED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			dispatch(
				DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_HANDLER_DELETED,
				action.payload
			);
		},
		[DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_CHANGED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			dispatch(
				DATA_BROKER_CONFIG_PANEL_EVENT_MAPPING_HANDLER_CHANGED,
				action.payload
			);
		}
	},
	styles,
	view,
	behaviors: [focusBehavior]
});
