import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './sn-uib-scripts-configuration-panel.scss';
import '../sn-uib-shelf-pane';
import EmptyState from '../partials/empty-state';
import { t } from 'sn-translate';

import '@servicenow/now-code-editor';

import { SCRIPTS_PICKER_SCRIPT_SELECTED } from '../sn-uib-scripts-picker';
import {
	SCRIPTS_DETAILS_SCRIPT_NAME_CHANGED,
	SCRIPTS_DETAILS_TRIGGERING_ELEMENT_OPEN,
	SCRIPTS_DETAILS_TRIGGERING_ELEMENT_DELETE,
	SCRIPTS_DETAILS_SCRIPT_INCLUDES_CHANGED
} from '../sn-uib-scripts-details';
import { LINTING_CONFIG } from '../constants';

export const SCRIPTS_CONFIG_PANEL_SCRIPT_CREATED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_CREATED';
export const SCRIPTS_CONFIG_PANEL_SCRIPT_DELETED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_DELETED';
export const SCRIPTS_CONFIG_PANEL_SCRIPT_NAME_CHANGED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_NAME_CHANGED';
export const SCRIPTS_CONFIG_PANEL_SCRIPT_CODE_CHANGED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_CODE_CHANGED';
export const SCRIPTS_CONFIG_PANEL_TRASH_REQUESTED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#TRASH_REQUESTED';
export const SCRIPTS_CONFIG_PANEL_ADD_REQUESTED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#ADD_REQUESTED';
export const SCRIPTS_CONFIG_PANEL_TRIGGERING_ELEMENT_DELETE =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#TRIGGERING_ELEMENT_DELETE';
export const SCRIPTS_CONFIG_PANEL_TRIGGERING_ELEMENT_SELECTED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#TRIGGERING_ELEMENT_SELECTED';
export const SCRIPTS_CONFIG_PANEL_SCRIPT_INCLUDES_CHANGED =
	'SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_INCLUDES_CHANGED';

const DELETE = 'delete';
const ADD = 'add';

const view = (state) => {
	const {
		selectedScriptId,
		properties,
		selectedScript,
		triggeringElements
	} = state;

	const {
		scripts,
		disabled,
		isScriptsBusy,
		clientScriptApiCompletions,
		isMacroponentReadOnly
	} = properties;

	return (
		<div className="scripts-config-panel-container">
			<sn-uib-shelf-pane
				id="script-picker"
				headerLabel={t('Page scripts')}
				headerButtons={[
					{
						id: DELETE,
						icon: 'trash-fill',
						clickActionType: SCRIPTS_CONFIG_PANEL_TRASH_REQUESTED
					},
					{
						label: t('Add'),
						id: ADD,
						icon: 'plus-fill',
						clickActionType: SCRIPTS_CONFIG_PANEL_ADD_REQUESTED
					}
				]}
				disabledButtons={
					disabled || isScriptsBusy
						? [ADD, DELETE]
						: selectedScriptId
						? []
						: [DELETE]
				}>
				<sn-uib-scripts-picker
					slot="content"
					scripts={scripts}
					selectedScriptId={selectedScriptId}
				/>
			</sn-uib-shelf-pane>
			<sn-uib-shelf-pane id="script-details" headerLabel={t('Details')}>
				{!selectedScript ? (
					<div slot="content">
						<EmptyState
							headerMessage={t('No configuration options.')}
							description={t('Select a script to view configuration options.')}
						/>
					</div>
				) : (
					<sn-uib-scripts-details
						slot="content"
						script={selectedScript}
						triggering-elements={triggeringElements}
						disabled={disabled}
					/>
				)}
			</sn-uib-shelf-pane>
			{selectedScript ? (
				<now-code-editor
					component-id={`nce-${selectedScript.id}`}
					append-to-payload={{
						name: selectedScript.name,
						id: selectedScript.id
					}}
					autocomplete-suggestions={{
						variant: 'replace',
						disabled: false,
						suggestions: [clientScriptApiCompletions.clientScript]
					}}
					disable-debugging
					disable-context-menu
					language="javascript"
					content={selectedScript.script}
					readOnly={isMacroponentReadOnly || disabled}
					linting={LINTING_CONFIG}
				/>
			) : null}
		</div>
	);
};

export default createCustomElement('sn-uib-scripts-configuration-panel', {
	view,
	styles,
	initialState: {
		selectedScriptId: null
	},
	transformState: (state) => {
		const { properties } = state;

		return {
			...state,
			selectedScript: properties.scripts.find(
				(script) => script.id === state.selectedScriptId
			),
			triggeringElements: state.selectedScriptId
				? properties.eventHandlerElementMap[state.selectedScriptId]
				: null
		};
	},
	properties: {
		scripts: {
			schema: {
				type: 'array'
			},
			default: []
		},
		isScriptsBusy: {
			default: false
		},
		eventHandlerElementMap: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		disabled: {
			default: false
		},
		clientScriptApiCompletions: {
			default: {
				clientScript: '',
				clientTransformScript: ''
			}
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ properties, updateState }) => {
			const { scripts = [] } = properties;
			const scriptToSelect = scripts[0] ? scripts[0].id : '';
			updateState({ selectedScriptId: scriptToSelect });
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name, value, previousValue }
			},
			updateState,
			state
		}) => {
			if (name === 'scripts' && value.length !== previousValue.length) {
				if (value.length < previousValue.length) {
					const prevIdx = previousValue.findIndex(
						(script) => script.id === state.selectedScriptId
					);
					const newSelectIndex =
						prevIdx >= value.length ? prevIdx - 1 : prevIdx;
					updateState({
						selectedScriptId: value.length ? value[newSelectIndex].id : null
					});
					return;
				}
				updateState({
					selectedScriptId: value.length ? value[0].id : null
				});
			}
		},
		[SCRIPTS_CONFIG_PANEL_ADD_REQUESTED]: ({ dispatch }) =>
			dispatch(SCRIPTS_CONFIG_PANEL_SCRIPT_CREATED),
		[SCRIPTS_CONFIG_PANEL_TRASH_REQUESTED]: ({
			state,
			properties,
			dispatch
		}) => {
			dispatch(SCRIPTS_CONFIG_PANEL_SCRIPT_DELETED, {
				id: state.selectedScriptId,
				name: (
					properties.scripts.find(
						(script) => script.id === state.selectedScriptId
					) || {}
				).name
			});
		},
		[SCRIPTS_PICKER_SCRIPT_SELECTED]: ({ action, updateState }) => {
			const {
				payload: { id }
			} = action;
			updateState({
				selectedScriptId: id
			});
		},
		[SCRIPTS_DETAILS_SCRIPT_NAME_CHANGED]: ({ action, dispatch }) => {
			dispatch(SCRIPTS_CONFIG_PANEL_SCRIPT_NAME_CHANGED, action.payload);
		},
		[SCRIPTS_DETAILS_SCRIPT_INCLUDES_CHANGED]: ({ action, dispatch }) => {
			dispatch(SCRIPTS_CONFIG_PANEL_SCRIPT_INCLUDES_CHANGED, action.payload);
		},
		'NOW_CODE_EDITOR#CONTENT_CHANGED': ({ action, dispatch }) => {
			const script = action.payload.text;
			const name = action.payload.name;
			const id = action.payload.id;

			dispatch(SCRIPTS_CONFIG_PANEL_SCRIPT_CODE_CHANGED, {
				code: script,
				id,
				name
			});
		},
		[SCRIPTS_DETAILS_TRIGGERING_ELEMENT_OPEN]: ({ action, dispatch }) => {
			dispatch(
				SCRIPTS_CONFIG_PANEL_TRIGGERING_ELEMENT_SELECTED,
				action.payload
			);
		},
		[SCRIPTS_DETAILS_TRIGGERING_ELEMENT_DELETE]: ({ action, dispatch }) => {
			dispatch(SCRIPTS_CONFIG_PANEL_TRIGGERING_ELEMENT_DELETE, action.payload);
		}
	}
});
