import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from './sn-uib-data-broker-definitions.scss';
import _get from 'lodash/get';
import _sortBy from 'lodash/sortBy';
import _debounce from 'lodash/debounce';
import './sn-uib-db-applications';
import './sn-uib-db-definitions';
import './sn-uib-db-details';
import { CREATE_DB_POPOVER_TYPE_SELECTED } from './sn-uib-create-db-popover';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';
import SearchBox from '../partials/search-box'; // eslint-disable-line no-unused-vars

export const DATA_BROKER_DEFINITION_SELECTED =
	'SN_UIB_DATA_BROKER_DEFINITIONS#DEFINITION_SELECTED';
export const CREATE_NEW_DATA_BROKER_DEFINITION_CLICKED =
	'SN_UIB_DATA_BROKER_DEFINITIONS#CREATE_NEW_CLICKED';
const CREATE_DB_POPOVER_TOGGLED =
	'SN_UIB_DATA_BROKER_DEFINITIONS#CREATE_POPOVER_TOGGLED';

const CATEGORIES = {
	read: t('Server data'),
	write: t('Operations'),
	transform: t('Transform'),
	client: t('Client data')
};

const categoryFilters = {
	read: (dbdef) =>
		!dbdef.simpleMutationBroker &&
		dbdef.type !== 'TRANSFORM' &&
		dbdef.type !== 'CLIENT_STATE',
	write: (dbdef) => dbdef.simpleMutationBroker,
	client: (dbdef) => dbdef.type === 'CLIENT_STATE',
	transform: (dbdef) => dbdef.type === 'TRANSFORM'
};

const sortDefinitionsByCategory = (definitions) => {
	const sorted = Object.entries(CATEGORIES).reduce((acc, [id, label]) => {
		const filteredChildren = definitions
			.filter(categoryFilters[id])
			.reduce((acc, cur) => {
				acc.push({ id: cur.sysId, label: cur.name, icon: 'database-outline' });
				return acc;
			}, []);
		const alphabetizedChildren = _sortBy(filteredChildren, 'label');
		if (alphabetizedChildren.length > 0) {
			acc.push({ id, label, children: alphabetizedChildren });
		}
		return acc;
	}, []);
	return sorted;
};

const transformState = (state) => {
	const {
		searchTerm,
		selectedApplication,
		selectedDefinition,
		properties: { dataBrokerDefinitions = [] }
	} = state;

	let filteredDefinitions = _sortBy([...dataBrokerDefinitions], ['scope']);
	if (searchTerm) {
		const search = searchTerm.toUpperCase().trim();
		filteredDefinitions = filteredDefinitions.filter(({ name }) =>
			name.toUpperCase().includes(search)
		);
	}

	const applications = filteredDefinitions.reduce((acc, def) => {
		const scope = def.scope || null;
		if (scope && !acc[scope]) {
			acc[scope] = { id: scope, label: scope, icon: 'window-outline' };
		}
		return acc;
	}, {});

	const selectedAppOrFirstListed =
		selectedApplication && applications[selectedApplication]
			? selectedApplication
			: _get(filteredDefinitions, '[0].scope');

	filteredDefinitions = filteredDefinitions.filter(
		({ scope }) => scope === selectedAppOrFirstListed
	);

	const isSelectedListed = filteredDefinitions.find(
		({ sysId }) => sysId === _get(selectedDefinition, 'sysId')
	);

	return {
		...state,
		applications: _sortBy(Object.values(applications), ['id']),
		selectedApplication: selectedAppOrFirstListed,
		currentDefinitions: sortDefinitionsByCategory(filteredDefinitions),
		selectedDefinition: isSelectedListed ? selectedDefinition : null
	};
};

const view = (state, { dispatch }) => {
	const {
		searchTerm,
		currentDefinitions = [],
		selectedDefinition,
		applications = [],
		selectedApplication,
		popoverOpened = false
	} = state;
	return (
		<div className="data-broker-definitions-container">
			<div className="data-broker-definitions-search">
				<SearchBox
					placeholder={t('Search data resources')}
					value={searchTerm}
					manageValue={true}
					dispatch={dispatch}
				/>
			</div>
			<div className="data-broker-definitions-wrapper">
				<sn-uib-shelf-pane
					id="application-picker"
					headerLabel={t('Applications')}>
					<sn-uib-db-applications
						applications={applications}
						selectedItems={[[selectedApplication]]}
						slot="content"
					/>
				</sn-uib-shelf-pane>
				<sn-uib-shelf-pane
					id="definition-picker"
					headerLabel={t('Data resources')}
					headerButtons={[
						{
							label: t('New'),
							id: 'add',
							icon: 'plus-fill',
							triggerPopover: true,
							popoverOpened,
							popoverActionType: CREATE_DB_POPOVER_TOGGLED,
							TagName: 'sn-uib-create-db-popover'
						}
					]}>
					<sn-uib-db-definitions
						dataBrokerDefinitions={currentDefinitions}
						searchTerm={searchTerm}
						key={selectedApplication}
						slot="content"
					/>
				</sn-uib-shelf-pane>
				<sn-uib-shelf-pane
					id="definition-details"
					headerLabel={_get(selectedDefinition, 'name') || t('Overview')}>
					<div slot="content" className="data-broker-definitions-details">
						<sn-uib-db-details selectedDefinition={selectedDefinition} />
						<div className="data-broker-definitions-actions">
							<now-button
								variant="tertiary"
								size="sm"
								label={t('Cancel')}
								append-to-payload={{ context: 'cancel' }}
							/>
							<now-button
								variant="primary"
								size="sm"
								label={t('Add')}
								append-to-payload={{ context: 'add' }}
								disabled={!selectedDefinition}
							/>
						</div>
					</div>
				</sn-uib-shelf-pane>
			</div>
		</div>
	);
};

createCustomElement('sn-uib-data-broker-definitions', {
	initialState: {
		searchTerm: '',
		applications: [],
		selectedApplication: null,
		currentDefinitions: [],
		selectedDefinition: null,
		popoverOpened: false
	},
	transformState,
	properties: {
		/**
		 * the list of data broker definitions
		 * @type {array}
		 */
		dataBrokerDefinitions: {
			default: null
		},
		/**
		 * Whether the add data broker popover is currently being displayed
		 * @type {boolean}
		 */
		displayed: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const {
				payload: { name, value, previousValue }
			} = action;
			if (name === 'displayed' && value === false && previousValue === true) {
				// When the popover is closed, reset to initial state
				batchShorthandUpdateState(
					{
						searchTerm: '',
						selectedApplication: null,
						selectedDefinition: null,
						popoverOpened: false
					},
					updateState
				);
			}
		},
		['NOW_INPUT#INPUT']: _debounce(({ action, updateState }) => {
			action.stopPropagation();
			const { fieldValue: searchTerm } = action.payload;
			updateState({ searchTerm });
		}, 250),
		['NOW_BUTTON_ICONIC#CLICKED']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ searchTerm: '' });
		},
		['SN_UIB_DB_APPLICATIONS#APPLICATION_SELECTED']: ({
			action,
			updateState
		}) => {
			batchShorthandUpdateState(
				{
					selectedApplication: action.payload.application,
					selectedDefinition: null
				},
				updateState
			);
		},
		['SN_UIB_DB_DEFINITIONS#DEFINITION_SELECTED']: ({
			action,
			properties,
			updateState
		}) => {
			const selected = properties.dataBrokerDefinitions.find(
				(db) => db.sysId === action.payload.definition
			);
			selected && updateState({ selectedDefinition: selected });
		},
		[CREATE_DB_POPOVER_TOGGLED]: ({ action, updateState }) => {
			updateState({ popoverOpened: action.payload.opened });
		},
		[CREATE_DB_POPOVER_TYPE_SELECTED]: ({ action, updateState, dispatch }) => {
			updateState({ popoverOpened: false });
			dispatch(CREATE_NEW_DATA_BROKER_DEFINITION_CLICKED, action.payload);
		},
		'NOW_BUTTON#CLICKED': ({ action, state, dispatch }) => {
			action.stopPropagation();
			if (action.payload.context === 'add') {
				dispatch(DATA_BROKER_DEFINITION_SELECTED, {
					...state.selectedDefinition
				});
			}
			dispatch('SN_UIB_DATA_BROKER_NAVIGATOR#TOGGLE_POPOVER', {
				value: false
			});
		}
	},
	styles,
	view
});
