import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import './sn-uib-default-node-info';
import { visitEveryNode, getRelativeIds } from './utils';
import get from 'lodash/get';

const view = ({ properties: { nodeInfo } }) => {
	return (
		<div>
			{Object.keys(nodeInfo || {}).map((key) => {
				const props = nodeInfo[key];
				return (
					<sn-uib-default-node-info
						{...props}
						nodeId={key}
						component-id={`${key}_information`}
					/>
				);
			})}
		</div>
	);
};
export default createCustomElement('sn-uib-default-page-info', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		pageTree: {
			default: {}
		},
		elementTree: {
			default: {}
		},
		nodeInfo: {
			computed({ properties: { pageTree, elementTree } }) {
				if (!pageTree || !pageTree.root) {
					return {};
				}
				const root = pageTree ? pageTree.root : {};
				let mappedKeys = visitEveryNode(root, ({ id }) => {
					return { [id]: getRelativeIds(id, root) };
				});

				const modals = get(elementTree, 'children', []).filter(
					(el) => el.isModal
				);

				modals.forEach((modal) => {
					const modalRoot = get(modal, 'layoutTree.root', {});

					const modalKeys = visitEveryNode(modalRoot, ({ id }) => {
						return { [id]: getRelativeIds(id, modalRoot) };
					});
					mappedKeys = {
						...mappedKeys,
						...modalKeys
					};
				});

				return mappedKeys;
			}
		}
	}
});
