export const config = {
	name: 'sn-uib-reference-property-input',
	properties: {
		/**
		 * The id of the property.
		 * @type {string}
		 */
		id: {
			default: ''
		},
		/**
		 * The label to be displayed above the input field.
		 * @type {string}
		 */
		label: {
			default: ''
		},
		/**
		 * The value given to the property from the parent.
		 * @type {string}
		 * @example '973qg097hg3g9uhqejwrfopjhqeg' {sysId}
		 */
		value: {
			default: null
		},
		/**
		 * The table which is being referenced, to pass in to the GQL query.
		 * @type {string}
		 */
		table: {
			default: null
		},
		/**
		 * The reference results passed in from the data provider, associated with a table.
		 * @type {object}
		 * @example {pa_widget: [{id: '123', label: 'Open Incidents'}, {id: '456', label: 'Closed Incidents'}]}
		 */
		referenceResults: {
			default: null
		},
		/**
		 * The cache of displayValues available to get this reference input's
		 * displayValue for currently selected sysId
		 * @type {object}
		 * @example { 'p9q3745fgp9unaefv': 'My email doesn't work!' } { sysId: displayValue, ... }
		 */
		referenceDisplayValues: {
			default: null
		},
		/**
		 * The id of this reference input to be used to namespace reference results
		 * @type {string}
		 * @example 'abc123'
		 */
		referenceId: {
			default: null
		},
		/**
		 * The hash of reference Ids denoting which reference input is currently searching
		 * @type {object}
		 * @example { '123abc': true, '234cde': false }
		 */
		referenceSearching: {
			default: null
		},
		/**
		 * The computed displayValue for the currently selected record in the reference input
		 * @type {string}
		 * @example 'My email doesn't work!'
		 */
		displayValue: {
			computed({ properties: { referenceDisplayValues, value } }) {
				if (!referenceDisplayValues) {
					return null;
				}
				return typeof referenceDisplayValues[value] === 'string'
					? referenceDisplayValues[value]
					: null;
			}
		},
		/**
		 * The items to be passed in to the typeahead picker, after checking that the
		 * reference results correspond to the current property's table reference.
		 * @type {object[]}
		 * @example [{id: '123', label: 'Open Incidents'}, {id: '456', label: 'Closed Incidents'}]
		 */
		items: {
			computed(state) {
				const {
					value,
					table,
					displayValue,
					referenceResults,
					referenceId
				} = state.properties;
				if (value && (!referenceResults || !referenceResults[referenceId])) {
					// if an initial value is passed down from UXF and the GQL query hasn't been
					// dispatched yet for the current table, put that value in the items array for the typeahead
					return [{ id: value, name: value, label: displayValue }];
				} else if (!table || !referenceResults) {
					return [];
				} else {
					return referenceResults[referenceId] instanceof Array
						? referenceResults[referenceId]
						: [];
				}
			}
		},
		/**
		 * Whether the input should be disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * Whether dynamic configuration is enabled.
		 * @type {boolean}
		 */
		dataBindingEnabled: {
			default: true
		},
		/**
		 * An error message
		 * @type {string}
		 */
		errorMsg: {
			default: null
		},
		/**
		 * the value to default to when the property is in an empty state, eg. input has ""
		 * @type {*}
		 */
		emptyStateBehavior: {
			default: { value: '' }
		},
		/**
		 * The container that the reference input devx-typeahead-panel should be constrained within.
		 * @type {HTMLElement}
		 */
		container: {
			default: null
		},
		/**
		 * Whether the property value is scripted
		 */
		isScripted: {
			default: false
		},
		/**
		 * Template for client transform scripts
		 */
		scriptTemplate: {
			default: ''
		},
		/**
		 * Whether client transform script is enabled.
		 * @type {boolean}
		 */
		scriptingEnabled: {
			default: true
		},
		/**
		 * A short description of what the property does or how it is used
		 * @type {string}
		 */
		description: {},
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {
			default: ''
		}
	}
};
