const TAB_SP = '    ';
const parse = (text, fallback) => {
	let value = fallback;
	try {
		value = JSON.parse(text);
	} catch (e) {
		//
	}
	return value;
};

const inlineCSSWarnings = (theCSS = '') => {
	if (/{[^{]*{/.test(theCSS)) {
		return [
			{
				message: 'Only global(*) css block will be considered. ',
				tooltip:
					'Please be advised all other css block will be discarded once you finish editing.'
			}
		];
	}
	return [];
};

const cssElementBlockToJson = (css) => {
	let inlineCSS = [];
	const theCss = css
		.replace(/}/g, ' } ')
		.replace(/[\s,]\*[,\s][^{]+{/g, ' * {');
	const matches = ` ${theCss} `.matchAll(/[\s,]\*\s*\{([^}{}]+)\}/g);
	if (matches) {
		inlineCSS = [...matches].map((m) => m[1]);
	}
	return cssStringToObject(inlineCSS.join('\n'));
};

const objectToCssElementBlock = (obj) => {
	return `* {
${objectToCssString(obj)}
}`;
};

const objectToCssString = (obj) => {
	const kvs = Object.keys(obj).map((key) => `${TAB_SP}${key}: ${obj[key]};`);
	kvs.push(TAB_SP);
	return kvs.join('\n');
};

const cssStringToObject = (css) => {
	/**
	 * Fun regex:
	 * (?:^|;) -- This is making sure the pattern either starts with the beginning of the string or the previous line has a semicolon
	 * \s* -- covering possible whitespace
	 * (\w[\w-]+) -- Looking for rules that start with a word character and then consists of word characters and '-'. This is captured to use as the rule
	 * \s* -- possible whitespace
	 * : -- a colon!
	 * \s* -- possible whitespace
	 * (?:\([^)]+\)|[^;])+) -- Capturing the rule's value. Anything that is in a parentheses or (the pipe character) anything that's not a semicolon
	 * \s* -- possible whitespace
	 * (?:;|$) making sure the end of the 'rule' is either the end of the input or a semicolon.
	 */

	const rgx = /(?:^|;)\s*(\w[\w-]+)\s*:\s*((?:\([^)]+\)|[^;])+)\s*(?:;|$)/gm;
	const cssObj = {};
	let result = rgx.exec(css);
	while (result) {
		const value = result[2].trim();
		if (value && !/^\s*$/.test(value)) {
			cssObj[result[1].trim()] = value;
		}
		result = rgx.exec(css);
	}

	return cssObj;
};
const IgnoredWarnings = ['Do not use empty rulesets'];
const combineErrors = (warnings, nce_reported_errors) => {
	const err_warn = nce_reported_errors.reduce(
		({ warning, error }, err) => {
			if (err.severity === 'warning') {
				if (!IgnoredWarnings.find((m) => m === err.message)) {
					warning.add(err.message);
				}
			} else {
				error.add(err.message);
			}
			return { error, warning };
		},
		{ warning: new Set(), error: new Set() }
	);
	return [
		...warnings.map((problem) => ({
			severity: 'warning',
			problem: problem.message,
			tooltip: problem.tooltip
		})),
		...[...err_warn.error].map((problem) => ({
			severity: 'error',
			problem
		})),
		...[...err_warn.warning].map((problem) => ({
			severity: 'warning',
			problem
		}))
	];
};

export {
	parse,
	objectToCssString,
	cssStringToObject,
	objectToCssElementBlock,
	cssElementBlockToJson,
	inlineCSSWarnings,
	combineErrors
};
