import get from 'lodash/get';
import {
	extractReferencesFromProperties,
	getValuesFromEventMappingHandlers
} from '../../sn-uib-properties-configuration-pane/utils';
import { EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETE_REQUESTED } from '../../sn-uib-event-configuration-pane';

export const DATA_BROKER_EDITOR_PROPERTY_CHANGED =
	'SN_UIB_DATA_BROKER_EDITOR#PROPERTY_CHANGED';
const DATA_BROKER_EDITOR_REFERENCE_PROPERTY_SEARCHED =
	'SN_UIB_DATA_BROKER_EDITOR#REFERENCE_SEARCHED';
const DATA_BROKER_EDITOR_DATA_BROKER_CHANGED =
	'SN_UIB_DATA_BROKER_EDITOR#DATA_BROKER_CHANGED';
const DATA_BROKER_EDITOR_PROPERTY_BINDING_TOGGLED =
	'SN_UIB_DATA_BROKER_EDITOR#PROPERTY_BINDING_TOGGLED';
const DATA_BROKER_EDITOR_FIELD_PROPERTY_TABLE_SELECTED =
	'SN_UIB_DATA_BROKER_EDITOR#FIELD_PROPERTY_TABLE_SELECTED';
export const DATA_BROKER_EDITOR_EVALUATION_MODE_CHANGED =
	'SN_UIB_DATA_BROKER_EDITOR#EVALUATION_MODE_CHANGED';
export const DATA_BROKER_EDITOR_EVENT_MAPPING_ADDED =
	'SN_UIB_DATA_BROKER_EDITOR#EVENT_MAPPING_ADDED';
export const DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_ADDED =
	'SN_UIB_DATA_BROKER_EDITOR#EVENT_MAPPING_HANDLER_ADDED';
export const DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_DELETED =
	'SN_UIB_DATA_BROKER_EDITOR#EVENT_MAPPING_HANDLER_DELETED';
export const DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_CHANGED =
	'SN_UIB_DATA_BROKER_EDITOR#EVENT_MAPPING_HANDLER_CHANGED';
const DATA_BROKER_EDITOR_REFERENCE_HANDLER_VALUE_SEARCHED =
	'SN_UIB_DATA_BROKER_EDITOR#HANDLER_VALUE_REFERENCE_SEARCHED';

export const dataBrokerInstancePropertyUpdated = ({
	action,
	dispatch,
	state: { properties }
}) => {
	action.stopPropagation();

	const context = get(action, 'payload.context', null);
	context === 'dataBrokers' &&
		dispatch(DATA_BROKER_EDITOR_PROPERTY_CHANGED, {
			propertyId: action.payload.id,
			value: action.payload.value,
			dataBrokerId: properties.selectedDataBroker.id,
			noDebounce: action.payload.noDebounce
		});
};

export const dataBrokerPropertyBindingToggled = ({
	action,
	properties,
	dispatch
}) => {
	action.stopPropagation();

	dispatch(DATA_BROKER_EDITOR_PROPERTY_BINDING_TOGGLED, {
		value: !action.payload.isStatic,
		propertyId: action.payload.propertyId,
		dataBrokerId: properties.selectedDataBroker.id
	});
};

export const dataBrokerReferencePropertySearched = ({ action, dispatch }) => {
	action.stopPropagation();
	dispatch(DATA_BROKER_EDITOR_REFERENCE_PROPERTY_SEARCHED, action.payload);
};

export const dataBrokerEditorComponentPropertyChanged = ({
	properties: { currentSelectedProperties = [], eventMappingHandlers = {} },
	action: {
		payload: { value, previousValue, name }
	},
	dispatch
}) => {
	const newValueisDifferent =
		previousValue === null || !value || previousValue.id !== value.id;
	if (name === 'selectedDataBroker' && newValueisDifferent) {
		const currentEventMappingHandlerValues =
			getValuesFromEventMappingHandlers(eventMappingHandlers) || [];
		const references = [
			...(extractReferencesFromProperties(currentEventMappingHandlerValues) ||
				[]),
			...(extractReferencesFromProperties(currentSelectedProperties) || [])
		];
		references.length &&
			dispatch(DATA_BROKER_EDITOR_DATA_BROKER_CHANGED, { references });
	}
};

export const dataBrokerFieldPropertyTableSelected = ({
	action,
	dispatch,
	state: { properties }
}) => {
	action.stopPropagation();

	dispatch(DATA_BROKER_EDITOR_FIELD_PROPERTY_TABLE_SELECTED, {
		table: action.payload.value,
		propertyId: action.payload.id,
		dataBrokerId: properties.selectedDataBroker.id
	});
};

export const dataBrokerInstanceEvaluationModeChanged = ({
	action,
	dispatch
}) => {
	action.stopPropagation();
	const { value } = action.payload || {};
	dispatch(DATA_BROKER_EDITOR_EVALUATION_MODE_CHANGED, { value });
};

export const dataBrokerEventMappingAdded = ({ action, dispatch }) => {
	action.stopPropagation();
	dispatch(DATA_BROKER_EDITOR_EVENT_MAPPING_ADDED, action.payload);
};

export const dataBrokerEventMappingHandlerAdded = ({ action, dispatch }) => {
	action.stopPropagation();
	dispatch(DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_ADDED, action.payload);
};

export const dataBrokerEventMappingHandlerDeleteRequested = ({
	action,
	dispatch
}) => {
	action.stopPropagation();
	dispatch(
		EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETE_REQUESTED,
		action.payload
	);
};

export const dataBrokerEventMappingHandlerDeleted = ({ action, dispatch }) => {
	action.stopPropagation();
	action.payload.context === 'data-broker' &&
		dispatch(DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_DELETED, action.payload);
};

export const dataBrokerEventMappingHandlerChanged = ({ action, dispatch }) => {
	action.stopPropagation();
	dispatch(DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_CHANGED, action.payload);
};

export const dataBrokerEventMappingHandlerReferencePropertySearched = ({
	action,
	dispatch
}) => {
	action.stopPropagation();
	action.payload.context === 'data-broker' &&
		dispatch(
			DATA_BROKER_EDITOR_REFERENCE_HANDLER_VALUE_SEARCHED,
			action.payload
		);
};
