import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

const view = ({ properties }) => {
	return (
		<div
			style={{ display: 'none' }}
			innerHTML={JSON.stringify(properties, null, 2).replace(/\n/g, '<br/>')}
		/>
	);
};
export default createCustomElement('sn-uib-row-col-node-info', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		rootElement: {
			default: {}
		},
		pageTree: {
			default: {}
		},
		element: {
			default: {},
			selectable: true
		},
		drag: {
			default: {},
			selectable: true
		},
		emptySlot: {
			default: {},
			selectable: true
		},
		type: {
			default: '',
			selectable: true
		},
		nodeId: {
			default: null
		},
		resize: {
			default: {},
			selectable: true
		},
		elementRef: {
			default: {
				current: null
			},
			selectable: true
		},
		rank: {
			default: 'BASIC',
			selectable: true
		},
		wrapperProps: {
			default: {},
			selectable: true
		}
	}
});
