import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-popover';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-collapse';
import styles from '../sn-uib-layout-template-panel/sn-uib-layout-template-panel.scss';
import '../sn-uib-layout-template-panel';

const popoverView = ({
	properties: {
		elementId,
		layoutTemplates,
		standardTemplates,
		customizedTemplates,
		templateType,
		disabled
	},
	popoverOpened
}) => {
	return disabled ? (
		<div /> //TODO find out and remove this readonly
	) : (
		<now-popover
			positions={[
				{ target: 'bottom-end', content: 'top-end' },
				{ target: 'bottom-start', content: 'top-start' }
			]}
			manageOpened="true"
			opened={popoverOpened}
			hideTail="true">
			<sn-uib-layout-template-panel
				slot="content"
				elementId={elementId}
				templateType={templateType}
				standardTemplates={standardTemplates}
				customizedTemplates={customizedTemplates}
				layoutTemplates={layoutTemplates}
				disabled={disabled}
			/>
		</now-popover>
	);
};

export default createCustomElement('sn-uib-layout-template-popover', {
	view: popoverView,
	actionHandlers: {
		'TEMPLATE_POPOVER#SET_OPENED': ({ action, updateState }) => {
			updateState({
				popoverOpened: action.payload.value
			});
		},
		'NOW_POPOVER#OPENED_SET': ({ action, updateState }) => {
			updateState({
				popoverOpened: action.payload.value
			});
		}
	},
	properties: {
		elementId: { default: null },
		layoutTemplates: { default: [] },
		standardTemplates: { default: [] },
		customizedTemplates: { default: [] },

		templateType: { default: null },
		disabled: { default: false }
	},
	initialState: {
		popoverOpened: false
	},
	styles
});
