import { actionTypes } from '@servicenow/ui-core';
import {
	getMessage,
	applicableProtectionPolicies,
	mixedScopes,
	stringValuesFromProperties
} from './utils';
import {
	SN_UIB_TRANSACTION_ALERT_TRANSACTION_IN_SCOPE,
	SN_UIB_TRANSACTION_ALERT_REQUEST_STARTED,
	SN_UIB_TRANSACTION_ALERT_UNLOCK_TO_OVERRIDE_CLICKED,
	SN_UIB_TRANSACTION_ALERT_COPY_TO_SAME_SCOPE_CLICKED,
	SN_UIB_TRANSACTION_ALERT_COPY_CLICKED,
	SN_UIB_TRANSACTION_ALERT_ACTION_REQUESTED,
	SN_UIB_TRANSACTION_ALERT_CANCEL_CLICKED,
	SN_UIB_TRANSACTION_ALERT_EDIT_TOGGLED
} from './actions';

export const dispatchTransactionNecessaryValue = ({
	state,
	dispatch,
	updateState,
	properties
}) => {
	const {
		requestScopeId,
		requestPageDefinitionScopeId,
		isSharedPageDef
	} = properties;

	const stringValues = stringValuesFromProperties(properties);

	const isEditing = state.isEditing;
	const message = getMessage(properties, stringValues, isEditing);
	const { recordProtected, pageDefProtected } = applicableProtectionPolicies(
		properties
	);

	if (!message) {
		updateState &&
			updateState({
				operation: 'set',
				path: 'message',
				value: null
			});
		return;
	}

	updateState &&
		updateState({
			operation: 'set',
			path: 'message',
			value: message
		});

	/**
	 * WHEN NOT EDITING:
	 * If the row has a message.transactionMessage then the record AND the page def are readonly
	 * If the row has a message.ppMessage then the record AND/OR page def are readonly based on their PP
	 * The above will also be both read only if the scopes of the two records are different
	 *
	 * WHEN EDITING:
	 * Use readonlyBehavior
	 */
	const createReadonlyBehaviorFromMessage = (message) => {
		const hasMixedScopes = mixedScopes(
			requestScopeId,
			requestPageDefinitionScopeId
		);
		const everythingReadonly = {
			isRecordReadonly: true,
			isPageDefReadonly: true
		};
		if (message.transactionMessage) {
			return everythingReadonly;
		} else if (message.ppMessageBody || message.ppMessageTitle) {
			return hasMixedScopes || isSharedPageDef
				? everythingReadonly
				: {
						isRecordReadonly:
							recordProtected === 'read' || recordProtected === 'protected',
						isPageDefReadonly:
							pageDefProtected === 'read' || pageDefProtected === 'protected'
				  };
		} else {
			return {
				isRecordReadonly: false,
				isPageDefReadonly: false
			};
		}
	};

	const readonlyBehavior = isEditing
		? message.options.readonlyBehavior
		: createReadonlyBehaviorFromMessage(message.message);

	const isNecessaryTransactionInProgress = !readonlyBehavior.isRecordReadonly;

	/*
	 *    A transaction is necessary when the user is either not in the same scope and/or domain
	 *    as the record. A tranasction can be performed that places the user in the same scope
	 *    and/or domain.  If this transaction in currently in progress, we rely on readonly
	 *    behavior that comes from the spreadsheet.  If it is not in progress, the record is
	 *    not editable at all (and neither is the page definition if the record is a screen record).
	 *
	 *    If no transaction is necessary, then we rely on the spreadsheet once again, as it factors
	 *    in things like protection policy when this is relevant(in foreign scope (or global) and
	 *    not a MAINT user).
	 */

	dispatch(SN_UIB_TRANSACTION_ALERT_TRANSACTION_IN_SCOPE, {
		isNecessaryTransactionInProgress,
		canDeleteRecord: message.options.canDeleteRecord,
		...readonlyBehavior,
		...message.options.canCopyRecord,
		message
	});
};

const actionRequested = ({ action, updateState, dispatch, properties }) => {
	const { context } = action.payload;
	const actionMap = {
		edit: () => {
			updateState({
				operation: 'set',
				path: 'isEditing',
				value: true
			});
			dispatch(SN_UIB_TRANSACTION_ALERT_EDIT_TOGGLED);
		},
		override: () => {
			dispatch(SN_UIB_TRANSACTION_ALERT_UNLOCK_TO_OVERRIDE_CLICKED);
		},
		cancel: () => {
			const { allowCancelTransactionInProgress } = properties;

			if (allowCancelTransactionInProgress) {
				updateState({
					operation: 'set',
					path: 'isEditing',
					value: false
				});

				dispatch(SN_UIB_TRANSACTION_ALERT_EDIT_TOGGLED);
			}

			dispatch(SN_UIB_TRANSACTION_ALERT_CANCEL_CLICKED);
		},
		copy: () => {
			dispatch(SN_UIB_TRANSACTION_ALERT_COPY_CLICKED);
		},
		copyToSameScope: () => {
			dispatch(SN_UIB_TRANSACTION_ALERT_COPY_TO_SAME_SCOPE_CLICKED);
		}
	};
	actionMap[context]();
};

const componentBootstrapped = ({
	state,
	dispatch,
	updateState,
	properties
}) => {
	dispatch(SN_UIB_TRANSACTION_ALERT_REQUEST_STARTED);
	dispatchTransactionNecessaryValue({
		state,
		dispatch,
		updateState,
		properties
	});
};

export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: componentBootstrapped,
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: dispatchTransactionNecessaryValue,
	['NOW_BUTTON#CLICKED']: {
		effect: actionRequested,
		stopPropagation: true
	},
	[SN_UIB_TRANSACTION_ALERT_ACTION_REQUESTED]: {
		effect: actionRequested,
		stopPropagation: true
	},
	[SN_UIB_TRANSACTION_ALERT_EDIT_TOGGLED]: dispatchTransactionNecessaryValue
};
