import { t } from 'sn-translate';

export const handleEscapeKeyPressed = (e, dispatch, entrypoint) => {
	const { key } = e;
	if (key === 'Escape' && entrypoint) {
		dispatch('UIB_DND_DROPZONE#INVALID_DROP');
		dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
			/* prettier-ignore */
			ariaLiveText: t('You are no longer in insert mode, please select a component to add on a stage')
		});
	}
};
