import '@servicenow/now-code-editor';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import cuid from 'cuid';
import isEmpty from 'lodash/isEmpty';
import isNull from 'lodash/isNull';
import { t } from 'sn-translate';
import styles from './sn-uib-data-broker-preview.scss';
import '../sn-uib-property-description-tooltip';

const renderOutputPreviewByKey = (stringifiedOutputs, currentOutputKey) => {
	return (
		<now-code-editor
			disable-full-screen
			key={cuid()} // temporary fix for now-code-editor content refresh issue
			component-id={`nce-${currentOutputKey}`}
			language="json"
			content={stringifiedOutputs[currentOutputKey]}
			readOnly={true}
		/>
	);
};

const checkDisableTab = (value) => {
	switch (typeof value) {
		case 'boolean':
			return false;
		case 'string':
			return value === '';
		case 'number':
			return false;
		case 'object':
			return isEmpty(value) || isNull(value);
		default:
			return true;
	}
};

const circularReferenceReplacer = (cache) => (key, value) => {
	if (typeof value === 'object' && value !== null) {
		// Duplicate reference found, discard key
		if (cache.includes(value)) {
			return;
		}
		cache.push(value);
	}
	return value;
};

const generateTabItems = (output) => {
	const outputKeys = Object.keys(output);
	return outputKeys.map((outputKey) => ({
		id: outputKey,
		label: outputKey,
		disabled: checkDisableTab(output[outputKey])
	}));
};

const formatExecutionTime = (executionTime) =>
	t('Last execution took {0} sec', executionTime / 1000);

const view = ({
	currentOutputKey,
	properties: { output, stringified, executionTime }
}) => {
	const tabItems = generateTabItems(output);
	return (
		<div className="sn-uib-data-broker-preview-container">
			<div className="data-broker-preview-header">
				<div>
					<span className="header-text">
						{
							/* prettier-ignore */
							t('Preview {0}', executionTime ? ` (${formatExecutionTime(executionTime)})` : '')
						}
					</span>
					<sn-uib-property-description-tooltip
						description={t('Preview of the data resource payload')}
					/>
				</div>
			</div>
			{!isEmpty(output) ? (
				<div className="data-broker-preview-body-wrapper">
					<now-tabs items={tabItems} />
					<section className="data-broker-preview-body">
						{renderOutputPreviewByKey(stringified, currentOutputKey)}
					</section>
				</div>
			) : null}
		</div>
	);
};

export default createCustomElement('sn-uib-data-broker-preview', {
	view,
	properties: {
		/**
		 * The current data state of the data broker in object form
		 * @type {object}
		 */
		output: { default: {} },
		executionTime: { default: null },
		stringified: {
			computed: ({ properties: { output } }) => {
				return Object.entries(output).reduce((acc, [outputKey, outputData]) => {
					let value;
					const valueCache = [];
					switch (typeof outputData) {
						case 'number':
						case 'boolean':
							value = outputData.toString();
							break;
						case 'string':
							value = outputData;
							break;
						case 'object':
							try {
								value = JSON.stringify(
									outputData,
									circularReferenceReplacer(valueCache),
									2
								);
							} catch (e) {
								(() => null)();
							}
							break;
						default:
							value = '';
					}
					return {
						...acc,
						[outputKey]: value
					};
				}, {});
			}
		}
	},
	transformState: (state) => {
		const {
			properties: { output },
			currentOutputKey
		} = state;
		if (!currentOutputKey) {
			return {
				...state,
				currentOutputKey: Object.keys(output)[0]
			};
		}
	},
	initialState: {
		currentOutputKey: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			state,
			action,
			updateState
		}) => {
			action.stopPropagation();
			const { name, value } = action.payload;
			if (name === 'output' && !isEmpty(value)) {
				let outputKeys = [],
					currentOutputKeyIsInOutput = true;
				try {
					outputKeys = Object.keys(value);
					currentOutputKeyIsInOutput = outputKeys.includes(
						state.currentOutputKey
					);
				} catch (_) {
					// do nothing
				}

				!currentOutputKeyIsInOutput &&
					updateState({
						currentOutputKey: outputKeys.length ? outputKeys[0] : null
					});
			}
		},
		['NOW_TABS#SELECTED_ITEM_SET']: ({ updateState, action }) => {
			action.stopPropagation();
			updateState({ currentOutputKey: action.payload.value });
		}
	},
	styles
});
