const flattenPage = (pages) => {
	return Array.isArray(pages)
		? pages.flatMap((page) => [page, ...flattenPage(page.children)])
		: [];
};

export const findSelectedItem = (memoHash, pages, selectedPage, parentInfo) => {
	if (!selectedPage) {
		return [];
	}

	const flatPages = flattenPage(pages);
	const selectedItem = flatPages.find((p) => p.id === selectedPage);

	if (!selectedItem) {
		return [];
	}

	return flatPages.filter((p) => {
		return (
			p.parentId === selectedItem.parentId &&
			p.parentElementId === parentInfo.compositionElementId
		);
	});
};
