import get from 'lodash/get';
import styles from './sn-uib-event-mapping-configuration.scss';
import { t } from 'sn-translate';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-content-tree';
import '@servicenow/now-popover';

import {
	EVENT_MAPPING_HANDLER_CONFIG_ADD_CLICKED,
	EVENT_MAPPING_HANDLER_CONFIG_CANCEL_CLICKED
} from '../sn-uib-event-mapping-handler-config';
import { addPayloadBindings } from './utils';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const EVENT_MAPPING_CONFIG_MAPPING_ADDED =
	'EVENT_MAPPING_CONFIG#MAPPING_ADDED';
export const EVENT_MAPPING_CONFIG_HANDLER_ADDED =
	'EVENT_MAPPING_CONFIG#EVENT_MAPPING_HANDLER_CREATED';
export const EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED =
	'EVENT_MAPPING_CONFIG#EVENT_MAPPING_HANDLER_DELETE_REQUESTED';
export const EVENT_MAPPING_CONFIG_HANDLER_DELETED =
	'EVENT_MAPPING_CONFIG#EVENT_MAPPING_HANDLER_DELETED';
const EVENT_MAPPING_HANDLER_SETTINGS_CLICKED =
	'EVENT_MAPPING_HANDLER_CONFIG#HANDLER_SETTINGS_CLICKED';
const EVENT_MAPPING_HANDLER_TRASH_CLICKED =
	'EVENT_MAPPING_HANDLER_CONFIG#HANDLER_TRASH_CLICKED';

const findEventHandler = (
	eventHandlers,
	handlerTypeId,
	handlerName,
	sourceId
) => {
	const hasSourceId = !!sourceId;
	return eventHandlers.find((eventHandler) => {
		const eventHandlerTypeSplit = eventHandler.id.split(':');
		const [definitionId, instanceId, ...name] = eventHandlerTypeSplit;
		if (!hasSourceId) {
			return definitionId === handlerTypeId;
		}
		return (
			definitionId === handlerTypeId &&
			instanceId === sourceId &&
			name.join('_') === handlerName
		);
	});
};

const view = (state) => {
	const {
		expandedEvents = [],
		properties: {
			appConfig,
			bindings,
			context,
			disabled,
			elementId,
			enableScripting,
			event,
			eventHandlers,
			eventMapping,
			handlerItems,
			interfaceConfigurationComponent,
			scriptTemplate,
			searchTerm = ''
		}
	} = state;

	const fullBindings = addPayloadBindings(bindings, event);
	const appConfigWithPayloadBindings = { ...appConfig, bindings: fullBindings };
	const isExpanded = expandedEvents.some((eventArr) =>
		eventArr.includes(event.id)
	);
	const treeClasses = { 'event-tree': true, expanded: isExpanded, disabled };

	return (
		<div className="event-mappings">
			<now-content-tree
				class={treeClasses}
				size="sm"
				items={[{ ...event, children: handlerItems }]}
				expandedItems={isExpanded ? [[event.id]] : []}
				manageExpandedItems={true}
				selectedItems={[]}
				manageSelectedItems={true}
				searchTerm={searchTerm}
				overflow="wrap"
				slot="trigger"
				triggerIcon="caret"
			/>
			<now-popover
				opened={state.popoverOpen}
				manageOpened={true}
				positionTarget={state.popoverTarget}
				positions={[
					{ target: 'center-start', content: 'center-end' },
					{ target: 'center-end', content: 'center-start' },
					{ target: 'top-center', content: 'bottom-center' }
				]}
				key={state.popoverKey ? state.popoverKey : undefined}
				append-to-payload={{
					target: state.popoverTarget,
					context: 'event-mapping-config'
				}}>
				<sn-uib-event-mapping-handler-config
					className="sn-uib-event-mapping-handler-config"
					key={state.popoverKey ? state.popoverKey : undefined}
					component-id={`handler-config_${state.popoverKey}`}
					clientTransformScript={state.clientTransformScript}
					scriptTemplate={scriptTemplate}
					event={event}
					eventHandlers={eventHandlers}
					handlerTypeId={state.handlerTypeId}
					handlerValues={state.handlerValues}
					interfaceConfigurationComponent={interfaceConfigurationComponent}
					bindings={fullBindings}
					elementId={elementId}
					appConfig={appConfigWithPayloadBindings}
					slot="content"
					appendToPayload={{
						context,
						handlerId: state.popoverKey,
						eventMappingId: eventMapping ? eventMapping.id : undefined
					}}
					enableScripting={enableScripting}
				/>
			</now-popover>
		</div>
	);
};

export default createCustomElement('sn-uib-event-mapping-configuration', {
	view,
	styles,
	behaviors: [focusBehavior],
	initialState: {
		expandedEvents: [],
		popoverOpen: false,
		popoverTarget: null,
		popoverKey: null,
		handlerTypeId: null,
		handlerValues: [],
		clientTransformScript: ''
	},
	transformState(state) {
		const {
			popoverKey = '',
			properties: { eventMappingHandlers = [] }
		} = state;
		const selectedHandler =
			eventMappingHandlers.find(
				(handler) => handler.handlerId === popoverKey
			) || {};
		const sourceAndOperationName =
			selectedHandler.handlerType &&
			selectedHandler.handlerType === 'DATABROKER_OP'
				? `:${selectedHandler.sourceId}:${selectedHandler.handlerName}`
				: '';

		return {
			...state,
			handlerTypeId: selectedHandler.handlerTypeId
				? `${selectedHandler.handlerTypeId}${sourceAndOperationName}`
				: null,
			handlerValues: selectedHandler.values || [],
			clientTransformScript: selectedHandler.clientTransformScript
		};
	},
	properties: {
		/**
		 * The actual mapping object itself.
		 * @type {object}
		 */
		eventMapping: {
			default: null
		},
		/**
		 * The source event for the selected event mapping.
		 * @type {object[]}
		 */
		event: {
			default: null
		},
		/**
		 * A list of available handlers to bind the event mapping to.
		 * @type {object[]}
		 */
		eventHandlers: {
			default: []
		},
		/**
		 * A list of handlers (targets) for the selected event mapping.
		 * @type {object[]}
		 */
		eventMappingHandlers: {
			default: []
		},
		/**
		 * Whether the controls should be disabled.
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * A map containing component interfaces
		 * @type {Object.<string, componentInterface>}
		 */
		interfaceConfigurationComponent: {
			default: {}
		},
		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * elementId the element id
		 * @type {string}
		 */
		elementId: {
			default: null
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * The search term in the input at the top of the event pane.
		 */
		searchTerm: {
			default: '',
			schema: { type: 'string' }
		},
		/**
		 * The default value (placeholder) for scipt
		 */
		scriptTemplate: {
			default: ''
		},
		/**
		 * Option to enable scriptable payload, defaults to true
		 */
		enableScripting: {
			default: true
		},
		/**
		 * The context that this event mapping configuration is in.
		 */
		context: {
			default: 'element',
			schema: {
				type: 'string',
				enum: ['element', 'screen', 'page', 'data-broker']
			}
		},
		/**
		 * The event mapping handlers for the current event mapping (to be passed into the content tree).
		 */
		handlerItems: {
			default: [],
			schema: { type: 'array' },
			computed: ({
				properties: {
					disabled = false,
					eventHandlers = [],
					eventMappingHandlers = []
				}
			}) => {
				const items = eventMappingHandlers.reduce((items, handler = {}) => {
					const matchingHandler =
						findEventHandler(
							eventHandlers,
							handler.handlerTypeId,
							handler.handlerName,
							handler.sourceId
						) || {};

					// don't show the handler in the tree until a target is selected
					if (matchingHandler.label) {
						items.push({
							id: handler.handlerId,
							label: matchingHandler.label,
							handlerTypeId: matchingHandler.id || handler.handlerTypeId,
							actions: disabled
								? []
								: [
										{
											icon: 'sliders-fill',
											label: t('Configure'),
											clickActionType: EVENT_MAPPING_HANDLER_SETTINGS_CLICKED
										},
										{
											icon: 'trash-fill',
											label: t('Delete'),
											clickActionType: EVENT_MAPPING_HANDLER_TRASH_CLICKED
										}
								  ]
						});
					}
					return items;
				}, []);
				const createNewHandlerItem = {
					id: 'add',
					label: t('Add a new event handler'),
					icon: 'plus-fill',
					actionable: !disabled
				};
				return [...items, createNewHandlerItem];
			}
		},
		/**
		 * Whether the hoisted c3 modal is currently opened (to determine popover open/close behavior).
		 * @type {boolean}
		 */
		c3ModalOpened: {
			default: '@uib-app/showC3Modal'
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ state, updateState }) => {
			const isExpanded = get(state, 'properties.event.expanded', false);
			const eventId = get(state, 'properties.event.id', '');
			if (isExpanded && eventId) {
				updateState({
					expandedEvents: [[eventId]]
				});
			}
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name, value = [], previousValue = [] }
			},
			updateState,
			host
		}) => {
			if (
				name === 'eventMappingHandlers' &&
				value.length === previousValue.length + 1
			) {
				const emptyEventMappingHandler =
					value.find((handler) => !handler.handlerTypeId) || null;
				if (emptyEventMappingHandler) {
					const popoverTarget = host.shadowRoot
						.querySelector('now-content-tree')
						.shadowRoot.querySelector('li#add');
					batchShorthandUpdateState(
						{
							popoverOpen: true,
							popoverTarget,
							popoverKey: emptyEventMappingHandler.handlerId
						},
						updateState
					);
				}
			}
		},
		['NOW_CONTENT_TREE#EXPANDED_ITEMS_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ expandedEvents: action.payload.value });
		},
		['NOW_POPOVER#OPENED_SET']: ({
			action,
			dispatch,
			properties,
			updateState
		}) => {
			action.stopPropagation();
			const { context, value } = action.payload || {};
			const {
				c3ModalOpened = false,
				eventMappingHandlers = [],
				handlerItems = []
			} = properties;
			if (
				context === 'event-mapping-config' &&
				value === false &&
				c3ModalOpened === false
			) {
				// If OPENED_SET fires with a value of false *and* the registered outside
				// click doesn't come from within the hoisted c3 modal, close the popover.
				batchShorthandUpdateState(
					{
						popoverOpen: false,
						popoverTarget: null,
						popoverKey: null
					},
					updateState
				);
				if (eventMappingHandlers.length === handlerItems.length) {
					// If the popover is blurred while the user is in the process of configuring an unconfigured event
					// handler, we want to treat that the same way as clicking "Cancel" and delete the handler.
					const emptyHandler =
						eventMappingHandlers.find(
							(handler) => handler.handlerTypeId === null
						) || null;
					emptyHandler &&
						dispatch(EVENT_MAPPING_CONFIG_HANDLER_DELETED, {
							handlerId: emptyHandler.handlerId
						});
				}
			}
		},
		[EVENT_MAPPING_HANDLER_SETTINGS_CLICKED]: ({ action, updateState }) => {
			const { item = {} } = action.payload;
			const popoverTarget = action.payload.domTarget.parentNode;
			batchShorthandUpdateState(
				{
					popoverOpen: true,
					popoverTarget,
					popoverKey: item.id
				},
				updateState
			);
		},
		[EVENT_MAPPING_HANDLER_TRASH_CLICKED]: ({
			action,
			dispatch,
			updateState
		}) => {
			const {
				item: { id, label }
			} = action.payload;
			dispatch(EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED, {
				handler: { id, name: label }
			});
			batchShorthandUpdateState(
				{
					popoverOpen: false,
					popoverTarget: null,
					popoverKey: null
				},
				updateState
			);
		},
		['NOW_CONTENT_TREE#ACTIONABLE_ITEM_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			if (properties.eventMapping && properties.eventMapping.id) {
				dispatch(EVENT_MAPPING_CONFIG_HANDLER_ADDED);
			} else {
				dispatch(EVENT_MAPPING_CONFIG_MAPPING_ADDED);
			}
		},
		[EVENT_MAPPING_HANDLER_CONFIG_ADD_CLICKED]: ({ updateState }) => {
			batchShorthandUpdateState(
				{
					popoverOpen: false,
					popoverTarget: null,
					popoverKey: null
				},
				updateState
			);
		},
		[EVENT_MAPPING_HANDLER_CONFIG_CANCEL_CLICKED]: ({
			action,
			dispatch,
			updateState
		}) => {
			if (action.payload.delete === true) {
				dispatch(EVENT_MAPPING_CONFIG_HANDLER_DELETED, action.payload);
			}
			batchShorthandUpdateState(
				{
					popoverOpen: false,
					popoverTarget: null,
					popoverKey: null
				},
				updateState
			);
		}
	}
});
