import get from 'lodash/get';
export const ROW_COLUMN_LAYOUT = 'rowColumn';
export const GRID_LAYOUT = 'grid';
export const FLEX_LAYOUT = 'flex';

import '../sn-uib-default-adapter';
export const layoutType = (layout) => {
	return get(layout, 'default.type');
};

const layoutLookup = {
	[ROW_COLUMN_LAYOUT]: 'sn-uib-row-col-adapter',
	[GRID_LAYOUT]: 'sn-uib-grid-adapter',
	[FLEX_LAYOUT]: 'sn-uib-default-adapter',
	default: 'sn-uib-default-adapter'
};
const layoutAdapterSelector = (layout) => {
	const type = layoutType(layout);
	return get(layoutLookup, type, 'sn-uib-default-adapter');
};

export default layoutAdapterSelector;
