import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import get from 'lodash/get';
import '@servicenow/now-typeahead';
import '@servicenow/now-button';
import '@servicenow/now-dot-walk';
import styles from './sn-uib-field-list-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import '../sn-uib-property-description-tooltip';

// Dispatched actions
export const FIELD_LIST_PROPERTY_TABLE_SELECTED =
	'FIELD_LIST_PROPERTY_INPUT#TABLE_SELECTED';
export const FIELD_LIST_PROPERTY_FIELD_SELECTED =
	'FIELD_LIST_PROPERTY_INPUT#FIELD_SELECTED';
export const DRAGGABLE_LIST_INSERTED =
	'FIELD_LIST_PROPERTY_INPUT#DRAGGABLE_LIST_INSERTED';
export const FIELD_LIST_PROPERTY_FIELD_DELETED =
	'FIELD_LIST_PROPERTY_INPUT#FIELD_DELETED';

const processDraggableItems = (fieldList) => {
	if (!fieldList) {
		return [];
	}
	return fieldList.split(',').map((field, i) => ({
		id: field,
		label: field,
		order: i + 1,
		isDraggable: true,
		actions: [
			{
				icon: 'pencil-outline',
				id: `update-${field}`,
				label: t('Update field')
			},
			{
				icon: 'close-outline',
				id: `delete-${field}`,
				label: t('Delete field')
			}
		]
	}));
};

const renderUnorderedFieldPicker = (
	state,
	table,
	fields,
	disabled,
	selectedFields,
	description
) => (
	<now-typeahead-multi
		component-id={`${state.componentId}_${table}`}
		items={fields}
		disabled={disabled}
		search="contains"
		selectedItems={selectedFields || []}
		manageSelectedItems
		helperContent={
			!description
				? /* prettier-ignore */
				  t('Search for a field and click or use your keyboard arrow keys to select')
				: null
		}
		style={{ width: '100%' }}
	/>
);

const renderOrderedFieldPicker = (
	state,
	table,
	fields,
	disabled,
	draggableItems,
	description,
	dispatch
) => (
	<Fragment>
		<div className="field-list-property-add-button-wrapper">
			<now-button-bare
				label={t('Add')}
				size="sm"
				disabled={disabled}
				variant="tertiary"
				append-to-payload={{ context: 'dotwalk' }}
				icon-start="plus-outline"
				helper-content={
					description || t('Select fields from the field chooser')
				}
			/>
		</div>
		<div
			className="fields-draggable-list-container"
			hook-insert={() => dispatch(DRAGGABLE_LIST_INSERTED)}>
			{draggableItems && draggableItems.length ? (
				<sn-uib-draggable-list
					style={{ width: '100%' }}
					items={draggableItems}
				/>
			) : (
				<div className="no-items-message-container">
					<now-icon icon="sliders-outline" />
					<span className="no-items-message-heading">
						{t('There are no selected fields')}
					</span>
					<div className="no-items-message">
						{t('Please selected fields in the dropdown above')}
					</div>
				</div>
			)}
		</div>
		<now-modal
			opened={state.dotwalkModalOpen}
			header-label={t('Choose a field')}
			size="lg"
			footer-actions={[
				{
					label: t('OK'),
					clickActionType: 'FIELD_LIST_PROPERTY#DOTWALK_COMPLETE',
					variant: 'primary'
				},
				{
					label: t('Cancel'),
					clickActionType: 'FIELD_LIST_PROPERTY#DOTWALK_CANCEL',
					variant: 'secondary'
				}
			]}>
			<div className="dot-walk-container">
				<now-dot-walk-connected
					dot-walk-path={state.currentSelectedField}
					table-name={table}
					include-template-metadata={true}
				/>
			</div>
		</now-modal>
	</Fragment>
);

const view = (state, dispatch) => {
	const {
		label,
		table,
		tableFieldValues,
		fields,
		selectedFields,
		disabled,
		description,
		selectedComponentTag,
		variant
	} = state.properties;
	const showOrdered = variant === 'ordered';

	const { draggableItems } = state;

	return (
		<Fragment className="sn-uib-property-input">
			<label className="sn-uib-property-input-label">
				{label}
				{!description ? null : (
					<sn-uib-property-description-tooltip
						description={description}
						selectedComponentTag={selectedComponentTag}
					/>
				)}
			</label>
			{showOrdered
				? renderOrderedFieldPicker(
						state,
						table,
						fields,
						disabled,
						draggableItems,
						description,
						dispatch
				  )
				: renderUnorderedFieldPicker(
						state,
						table,
						fields,
						disabled,
						selectedFields,
						description
				  )}
		</Fragment>
	);
};

createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		draggableItems: [],
		dotwalkModalOpen: false,
		currentSelectedField: null,
		lastSelectedField: null,
		pendingFields: []
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({
			properties,
			dispatch,
			updateState
		}) => {
			const { table, tableFieldValues, variant, value } = properties;
			if (
				variant === 'unordered' &&
				table &&
				(!tableFieldValues || !tableFieldValues[table])
			) {
				dispatch(FIELD_LIST_PROPERTY_TABLE_SELECTED, { value: table });
			}

			updateState({
				draggableItems: processDraggableItems(value),
				pendingFields: typeof value === 'string' ? value.split(',') : []
			});
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			dispatch
		}) => {
			const { variant } = properties;
			const propertyChanged = get(action, 'payload.name', '');
			const table = get(action, 'payload.value', '');
			if (propertyChanged === 'table' && variant === 'unordered' && table) {
				dispatch(FIELD_LIST_PROPERTY_TABLE_SELECTED, { value: table });
			}
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({
				dotwalkModalOpen: true
			});
		},
		['NOW_MODAL#OPENED_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			const { value } = action.payload;
			!value &&
				updateState({
					dotwalkModalOpen: false,
					lastSelectedField: null,
					currentSelectedField: null
				});
		},
		['NOW_DOT_WALK_CONNECTED#PATH_UPDATED']: ({ action, updateState }) => {
			const { path } = action.payload;
			const fieldValue = path.map((pathItem) => pathItem.field).join('.');
			updateState({
				lastSelectedField: fieldValue
			});
		},
		['FIELD_LIST_PROPERTY#DOTWALK_COMPLETE']: ({
			action,
			state,
			properties,
			dispatch,
			updateState
		}) => {
			let newPendingFields;
			if (!state.currentSelectedField) {
				newPendingFields = state.lastSelectedField
					? [...state.pendingFields, state.lastSelectedField]
					: state.pendingFields;
			} else {
				newPendingFields = state.pendingFields.map((pendingField) => {
					if (pendingField === state.currentSelectedField) {
						return state.lastSelectedField;
					}
					return pendingField;
				});
			}

			const newFieldList = newPendingFields.join(',');
			const newDraggableItems = processDraggableItems(newFieldList);
			dispatch(FIELD_LIST_PROPERTY_FIELD_SELECTED, {
				value: newFieldList
			});
			updateState({
				dotwalkModalOpen: false,
				draggableItems: newDraggableItems,
				pendingFields: newPendingFields,
				lastSelectedField: null,
				currentSelectedField: null
			});
		},
		['FIELD_LIST_PROPERTY#DOTWALK_CANCEL']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({
				currentSelectedField: null,
				lastSelectedField: null,
				dotwalkModalOpen: false
			});
		},
		['SN_UIB_DRAGGABLE_LIST#ITEM_MOVED']: ({
			action,
			properties,
			state,
			updateState,
			dispatch
		}) => {
			const { item, dragIndex } = action.payload;
			const spliceddraggableItems = [
				...state.draggableItems
					.filter((field) => field.id !== item.id)
					.map((field) => field.id)
			];
			spliceddraggableItems.splice(dragIndex - 1, 0, item.id);
			const newFieldList = spliceddraggableItems.join(',');
			const newDraggableItems = processDraggableItems(newFieldList);

			updateState({
				draggableItems: newDraggableItems,
				pendingFields: spliceddraggableItems
			});

			dispatch(FIELD_LIST_PROPERTY_FIELD_SELECTED, {
				value: newFieldList
			});
		},
		'NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET': ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			const { value } = action.payload;
			const { emptyStateBehavior } = properties;

			const values = value.reduce((acc, val) => [...acc, val.id], []);
			const finalValue = values.join(',');
			const valueToEmit =
				finalValue === '' ? emptyStateBehavior.value : finalValue;

			dispatch(FIELD_LIST_PROPERTY_FIELD_SELECTED, { value: valueToEmit });
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		},
		['SN_UIB_PILLAR_WRAPPER#ACTION_CLICKED']: ({
			state,
			action,
			properties,
			updateState,
			dispatch
		}) => {
			if (action.payload.actionId.match(/delete/)) {
				const newFieldList = [
					...state.draggableItems.filter(
						(field) => field.id !== action.payload.id
					)
				]
					.map((field) => field.id)
					.join(',');
				const newDraggableItems = processDraggableItems(newFieldList);
				updateState({
					draggableItems: newDraggableItems,
					pendingFields: newFieldList ? newFieldList.split(',') : [],
					lastSelectedField: null
				});

				dispatch(FIELD_LIST_PROPERTY_FIELD_SELECTED, {
					value: newFieldList
				});
			} else if (action.payload.actionId.match(/update/)) {
				updateState({
					dotwalkModalOpen: true,
					currentSelectedField: action.payload.id
				});
			}
		}
	},
	styles
});
