import { createCustomElement } from '@servicenow/ui-core';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import '@servicenow/now-icon';
import { t } from 'sn-translate';
import styles from './sn-uib-element.scss';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import focusStrategy from '../helpers/focusStrategy';
import '../sn-uib-no-interact-wrapper';
import { elementClassFactory } from '../sn-uib-element-controls/sn-uib-element-controls';

/* prettier-ignore */
export const ELEMENT_TOOLTIP_MESSAGE = t('Use tab and shift-tab to navigate between selected elements on the stage area.  Once selected, press Enter to interact with selected element');
export const elementRefMap = new Map();

const { DevxKeyFocusBehavior: keyFocusBehavior } = DevxKeyFocusBehavior;

/**
 *
 * @param {Object} param0 Object params
 * @param {Boolean} param0.dispatch dispatch Seismic framework
 * @param {Boolean} param0.classes classes of the element
 * @returns {JSX} renderable UI button
 */
const getContentManagementPill = ({ dispatch, classes }) => {
	return (
		<div
			class={{
				...classes.container,
				'edit-text': true
			}}
			on-click={(e) => {
				e.stopPropagation();
				dispatch('SN_UIB_ISOLATED_EDITING');
			}}>
			<span className="iso-edit-text">{t('Manage content')}</span>
		</div>
	);
};

const view = (state, { updateProperties, dispatch }) => {
	const { properties } = state;
	const classes = elementClassFactory(state);
	const elementId = properties.element.id;
	const {
		resizeInfo,
		focused,
		disabled,
		elementRef,
		elementType,
		elementWrapper,
		wrapperProps,
		isDragging,
		rank,
		allowIsolatedEditing
	} = properties;
	const slotRef = createRef();
	const componentLabel = properties.element.displayName
		? `${properties.element.displayName} Component`
		: 'Component';
	const componentAriaLabel = isDragging
		? t('{0}', componentLabel)
		: t('{0}, press enter to access controls', componentLabel);

	elementRefMap.set(elementId, {
		ref: elementRef,
		displayName: properties.element.displayName,
		resizeInfo
	});

	const refId = `${elementId}_ROOT`;
	if (focusStrategy.awaiting(refId)) {
		focusStrategy.fire(refId);
	}

	const contentManagementButton = allowIsolatedEditing
		? getContentManagementPill({
				dispatch,
				classes
		  })
		: null;

	const Wrapper = //eslint-disable-line no-unused-vars
		typeof elementWrapper === 'function' ? elementWrapper : Fragment;
	return (
		<Fragment>
			<div
				class={{
					...classes.container,
					'isolated-editing': allowIsolatedEditing
				}}
				tabindex="0"
				aria-label={componentAriaLabel}
				role="button"
				aria-describedby="sn-uib-element-tooltip"
				on-focus={(event) => {
					event.stopPropagation();
					dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
						type: 'element',
						elementId,
						elementInstanceName: properties.element.displayName,
						component: properties.element.component
					});
					if (focusStrategy.isActive()) {
						focusStrategy.deactivate();
					}
					/**
					 * This is to refresh the element after detaching/moving a component via keyboard
					 * It seems the renders that would refresh it happen when it is still 'detached'.
					 * This causes the component to have incorrect resize handles if the valid sides changed with the new drop.
					 * Setting a fresh property seems to work. Otherwise you have to change focus to get the correct render.
					 */

					updateProperties({ focusRefresh: {} });
				}}
				on-dblclick={() => {
					if (allowIsolatedEditing) {
						dispatch('SN_UIB_ISOLATED_EDITING');
					}
				}}
				hook-insert={() => {
					if (rank === 'ENTRYPOINT') {
						dispatch('UIB_STAGE#ELEMENT_ROOT_FOCUS_CREATED', {
							id: `${elementId}_ROOT`
						});
					}
				}}
				ref={elementRef}>
				{contentManagementButton}
				<div
					id="sn-uib-element-tooltip"
					className="sn-uib-element-aria-message">
					{ELEMENT_TOOLTIP_MESSAGE}
				</div>
				<Wrapper
					{...wrapperProps}
					focused={focused}
					disabled={disabled}
					slotRef={slotRef}
					elementId={elementId}
				/>
				<section ref={slotRef} id="uib-element-slot" class={classes.slot}>
					<sn-uib-no-interact-wrapper stop-events={elementType === 'leaf'}>
						<slot />
					</sn-uib-no-interact-wrapper>
				</section>
			</div>
		</Fragment>
	);
};

const transformState = (state) => {
	return {
		...state,
		confirm: state.properties.confirm
	};
};

export default createCustomElement('sn-uib-element', {
	view,
	transformState,
	properties: {
		allowIsolatedEditing: {
			default: false
		},
		elementMap: {
			default: {}
		},
		focusRefresh: {
			default: {}
		},
		elementType: {
			default: 'leaf'
		},
		/**
		 * The element object that describes the currently
		 * mounted element
		 * @type {object}
		 * @example - {id: '12345', displayValue: 'Form component'}
		 */
		element: { default: null },
		resizeInfo: { default: {} },
		/**
		 * The focused flag used to denote that the mounted
		 * element is the currently selected element on UIB
		 * stage and is in focus
		 * @type {boolean}
		 */
		focused: { default: false },
		elementWrapper: {
			default() {
				return null;
			}
		},
		isWorkspace: {
			default: false
		},
		wrapperProps: {
			default: {}
		},
		rank: {
			default: null
		},
		elementRef: {
			default: {
				current: null
			}
		},
		/**
		 * The hovered flag used to denote that the mounted
		 * element is currently being moused over on UIB
		 * stage
		 */
		hovered: { default: false },
		keymaps: { default: {} },

		/**
		 * Whether the element is read only
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * Whether or not the element is confirming deletion
		 * @type {boolean}
		 */
		confirm: { default: false },
		/**
		 * Whether or not the user is currently in drag mode
		 * @type {boolean}
		 */
		isDragging: { default: '@dnd-provider/isDragging' },
		selectedSlotParentComponent: { default: null }
	},
	initialState: {
		confirm: false
	},
	dispatches: {
		/**
		 * Dispatched when configuration button is clicked
		 * in element controls tab
		 * @type {object}
		 * @public
		 * @example - {element: {id: '12345', displayValue: 'Form component'}}
		 */
		'SN_UIB_ELEMENT#CONFIG_BUTTON_CLICKED': {},
		/**
		 * Dispatched when trash button is initially clicked in
		 * element controls tab
		 * @type {void}
		 * @private
		 */
		'SN_UIB_ELEMENT#TRASH_BUTTON_CLICKED': {},
		/**
		 * Dispatched when confirm button is clicked on the
		 * deletion dialog in element controls tab
		 * @type {object}
		 * @public
		 * @example {elementId: '12345'}
		 */
		'SN_UIB_ELEMENT#TRASH_CONFIRM_BUTTON_CLICKED': {},
		/**
		 * Dispatched when cancel button is clicked on the
		 * deletion dialog in element controls tab
		 * @type {void}
		 * @private
		 */
		'SN_UIB_ELEMENT#TRASH_CANCEL_BUTTON_CLICKED': {}
	},
	behaviors: [keyFocusBehavior],
	styles,
	actionHandlers: {
		['SN_UIB_ISOLATED_EDITING']: ({
			action: { stopPropagation },
			properties: { element },
			dispatch
		}) => {
			stopPropagation();
			const elementId = element.id;
			if (elementRefMap.has(elementId)) {
				const elementRef = elementRefMap.get(elementId).ref;
				const { width } = elementRef
					? elementRef.current.getBoundingClientRect()
					: {};
				dispatch('SN_UIB_VIEWPORT#ISOLATED_EDIT', {
					elementId,
					width
				});
			}
		}
	}
});
