import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-is-hidden-property-input.scss';
import { t } from 'sn-translate';
import '../sn-uib-properties-editor-panel';
import '@servicenow/now-select';

export const IS_HIDDEN_PROPERTY_TEST_VALUE_CHANGED =
	'SN_UIB_IS_HIDDEN_PROPERTY_INPUT#TEST_VALUE_CHANGED';

const view = (state) => {
	const { properties } = state;
	const { bindings, hiddenProperty, scriptTemplate } = properties;
	return (
		<div className="is-hidden-property-input-wrapper">
			<h3 className="is-hidden-heading">{t('Component visibility')}</h3>
			<sn-uib-properties-editor-panel
				properties={hiddenProperty ? [hiddenProperty] : []}
				dataBindingEnabled={true}
				bindings={bindings}
				scriptingEnabled={true}
				append-to-payload={{ context: 'element' }}
				script-template={scriptTemplate}
			/>
			<div className="test-value-wrapper">
				<now-select
					label={t('Test value')}
					selected-item={'none'}
					size="sm"
					/* prettier-ignore */
					helper-content={t('Option to apply a test value to the hidden component')}
					items={[
						{
							id: 'true',
							label: t('True')
						},
						{
							id: 'false',
							label: t('False')
						},
						{
							id: 'none',
							label: t('None')
						}
					]}
				/>
			</div>
		</div>
	);
};

createCustomElement('sn-uib-is-hidden-property-input', {
	view,
	initialState: {
		editingScript: false
	},
	properties: {
		elementId: {
			default: null
		},
		bindings: {
			schema: { type: 'array' },
			default: []
		},
		hiddenProperty: {
			schema: { type: 'object' },
			default: null
		},
		scriptTemplate: {
			default: ''
		}
	},
	actionHandlers: {
		['NOW_SELECT#SELECTED_ITEM_SET']: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			dispatch(IS_HIDDEN_PROPERTY_TEST_VALUE_CHANGED, {
				value: action.payload.value,
				elementId: properties.elementId
			});
		}
	},
	styles
});
