import get from 'lodash/get';
import interact from 'interactjs';
import { EXPAND, SHRINK, RESIZE_ENDED, RESIZE_STARTED } from './helpers';
import strategyManager from '../helpers/strategyManager';

const shouldResize = ({
	width = 0,
	sections = 0,
	clientX = 0,
	handleX = 0
}) => {
	if ([width, handleX, clientX].some((num) => !num || num <= 0)) {
		return false;
	}
	const bufferArea = (width / sections) * 0.5;
	const distance = Math.abs(handleX - clientX);
	if (distance > bufferArea) {
		return true;
	}
	return false;
};

const setNewSize = (
	side = '',
	dir = '',
	dispatch,
	dragId,
	maxSize,
	size,
	drags
) => {
	const lookup = {
		LEFT: {
			LEFT: EXPAND,
			RIGHT: SHRINK
		},
		RIGHT: {
			LEFT: SHRINK,
			RIGHT: EXPAND
		}
	};
	const [SIDE, DIR] = [side.toUpperCase(), dir.toUpperCase()];
	const direction = get(lookup, `${SIDE}.${DIR}`, '');

	if (direction === EXPAND && maxSize <= size) {
		return false;
	}
	if (direction === SHRINK && 1 >= size) {
		return false;
	}
	const { layoutInfo, resizeActionName } = drags.get(dragId);

	dispatch(resizeActionName, {
		side,
		direction,
		id: layoutInfo.id,
		elementId: layoutInfo.owner
	});
	dispatch('UIB_RESIZE_HANDLE#RESIZED', {
		resizer: dragId
	});
	drags.set(dragId, { ...drags.get(dragId), awaitingUpdate: true });
};
const getArgsForResizeCheck = (handle, sections, clientX, wrapperRect) => {
	const { width } = wrapperRect;
	const { left, width: handleWidth } = handle.getBoundingClientRect();
	const handleX = left + handleWidth / 2;
	return {
		width,
		clientX,
		handleX,
		sections
	};
};

const getSide = (clientX, el) => {
	const { left, right } = el.getBoundingClientRect();

	if (clientX < left) {
		return 'LEFT';
	}
	if (clientX > right) {
		return 'RIGHT';
	}
	return 'MIDDLE';
};
const movingEvent = ({ edge, dragId, elementId, drags, clientX }) => {
	const size = drags.get(dragId).size;
	const maxSize = drags.get(dragId).maxSize;
	const el = drags.get(dragId).el;
	/**
	 * In addition to the normal drag map I also needed to sync my `wrapper` ref (which is the element we compare if we should resize).
	 * Again this would only really happen when dragging quickly. It might have something to do with data-provider props arriving before the render?
	 */

	const wrapper = strategyManager.get(elementId, 'root');
	const dispatch = drags.get(dragId).dispatch;
	const awaitingUpdate = drags.get(dragId).awaitingUpdate;

	if (!wrapper.current) {
		return;
	}
	const rect = wrapper.current.getBoundingClientRect();

	/**
	 * With Seismic 18 they have been batching events and updates which caused the multiple `dragmove` events that passed a resize check
	 * to fire multiple resizes in one `update` which causes the resizing to sort of "chase" the mouse movements.
	 * `awaitingUpdate` is a flag that lets the resize function to know if we fired a resize but have yet to get new `layoutInfo`
	 * which is what I currently use as the signifier that the layout has been updated. This is set in `setNewSize`
	 * Note: if porting this behavior or refactoring. Try to use a state machine to define this behavior.
	 */

	if (
		!awaitingUpdate &&
		shouldResize(getArgsForResizeCheck(el, size, clientX, rect))
	) {
		setNewSize(
			edge,
			getSide(clientX, el),
			dispatch,
			dragId,
			maxSize,
			size,
			drags
		);
	}
};
const dragmove = ({ edge, elementId, drags, dragId }) => (event) => {
	const { target, clientX, clientY } = event;

	const centerY = clientY - 50;
	const centerX = clientX - 50;

	// keep the dragged position in the data-x/data-y attribute;
	const x = centerX;
	const y = centerY;
	event.stopPropagation();
	event.stopImmediatePropagation();

	// translate the element
	target.style.webkitTransform = target.style.transform = ` translate(${x}px, ${y}px)`;
	target.setAttribute('data-x', x);
	target.setAttribute('data-y', y);

	return movingEvent({ edge, dragId, elementId, drags, clientX });
};
const DRAG_SIZE = 1000;
const attachDrag = ({ edge, elementId, drags, dragId }) => {
	const el = drags.get(dragId).el;

	interact(el)
		.draggable(
			{ manualStart: true },
			{
				inertia: true,
				autoScroll: true,
				hold: 25
			}
		)
		.on('move', (event) => {
			const interaction = event.interaction;
			// if the pointer was moved while being held down
			// and an interaction hasn't started yet
			if (interaction.pointerIsDown && !interaction.interacting()) {
				const clone = document.createElement('sn-uib-drag-wrapper');
				clone.setAttribute('tag', 'div');
				clone.setAttribute('data', JSON.stringify({}));
				clone.setAttribute('cursor', 'ew-resize');

				const { clientX, clientY } = event;

				const centerY = clientY - DRAG_SIZE / 2;
				const centerX = clientX - DRAG_SIZE / 2;

				// update the posiion attributes
				clone.setAttribute('data-x', centerX);
				clone.setAttribute('data-y', centerY);

				/**
				 * Making the width and height relatively large so there isn't as much of a possibility of the mouse outrunnning our `dragmove`
				 * update which can happen with smaller sizes on slower computers.
				 */
				clone.setAttribute(
					'style',
					`
					transform:  translate(${centerX}px, ${centerY}px);
					cursor: ew-resize;
					user-select: none;
					touch-action: none;
					position: absolute;
					top: 0;
					left: 0;
					width: ${DRAG_SIZE}px;
					height:${DRAG_SIZE}px;
					-webkit-user-select: none;
				`
				);
				document.body.appendChild(clone);
				interaction.start({ name: 'drag' }, event.interactable, clone);
				const dispatch = drags.get(dragId).dispatch;

				dispatch(RESIZE_STARTED);
			}
		})
		.on('dragmove', dragmove({ edge, elementId, drags, dragId }))
		.on('dragend', ({ target }) => {
			const dispatch = drags.get(dragId).dispatch;

			dispatch(RESIZE_ENDED);
			target.parentNode.removeChild(target);
		});
};

export { attachDrag };
