import { createCustomElement } from '@servicenow/ui-core'; //eslint-disable-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import strategyManager from '../helpers/strategyManager';
import get from 'lodash/get';
import { LEFT, RIGHT } from '../sn-uib-resize-handle';

const nextElementId = (nextElement) => {
	return nextElement
		? `${nextElement}_ROOT`
		: 'quick-link-element-configuration-panel';
};
//eslint-disable-next-line no-unused-vars
const ResizeHandles = ({ id, wrapperRef, nextElement, ...rest }) => {
	const left = get(rest, 'resizeInfo.left');
	const right = get(rest, 'resizeInfo.right');
	const resizeActionName = 'SN_UXF#ELEMENT_RESIZED';
	strategyManager.set(id, 'root', wrapperRef);

	const leftResize = left ? (
		<sn-uib-resize-handle
			element-id={id}
			layoutInfo={left}
			side={LEFT}
			keymaps={{
				Escape: `${id}_ROOT`,
				Tab: right ? `${id}_RESIZE_RIGHT` : nextElementId(nextElement),
				ShiftTab: `${id}_TRASH`
			}}
			resizeActionName={resizeActionName}
			wrapperRef={wrapperRef}
		/>
	) : null;
	const rightResize = right ? (
		<sn-uib-resize-handle
			element-id={id}
			layoutInfo={right}
			keymaps={{
				Escape: `${id}_ROOT`,
				Tab: nextElementId(nextElement),
				ShiftTab: left ? `${id}_RESIZE_LEFT` : `${id}_TRASH`
			}}
			side={RIGHT}
			resizeActionName={resizeActionName}
			wrapperRef={wrapperRef}
		/>
	) : null;
	return (
		<Fragment>
			{leftResize}
			{rightResize}
		</Fragment>
	);
};
export const ElementWrapper = (props, children) => {
	const {
		focused,
		disabled,
		slotRef,
		resizeInfo,
		nextElement,
		elementId
	} = props;

	return (
		<Fragment>
			{focused && !disabled ? (
				<ResizeHandles
					wrapperRef={slotRef}
					resizeInfo={resizeInfo}
					nextElement={nextElement}
					id={elementId}
				/>
			) : null}
			{children}
		</Fragment>
	);
};
