import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-json-property-input.scss';
import { config } from './component-config';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import '../sn-uib-property-input-data-link-toggle';
import '../sn-uib-property-description-tooltip';
import '@servicenow/now-button';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import {
	SN_UIB_CODE_EDITOR_MODAL_TEXT_UPDATED,
	SN_UIB_CODE_EDITOR_MODAL_CLOSED
} from '../sn-uib-code-editor-modal';

import '@servicenow/now-textarea';

import attempt from 'lodash/attempt';
import isError from 'lodash/isError';
import isEmpty from 'lodash/isEmpty';
const isValidJSON = (text) => {
	return isEmpty(text) || !isError(attempt(JSON.parse.bind(null, text)));
};

// Dispatched action types
export const JSON_PROPERTY_VALUE_CHANGED =
	'SN_UIB_JSON_PROPERTY_INPUT#VALUE_CHANGED';
const OPEN_EDITOR_CLICKED = 'OPEN_EDITOR#CLICKED';
const dispatchWithEmptyBehaviour = ({ value, dispatch, properties }) => {
	const valueToEmit =
		value === '' ? properties.emptyStateBehavior.value : value;
	dispatch(JSON_PROPERTY_VALUE_CHANGED, { value: valueToEmit });
};
const view = ({ properties, openJSONEditor }, { dispatch, updateState }) => {
	const { description, selectedComponentTag, inline } = properties;
	return (
		<Fragment>
			<div className="sn-uib-json-property-input-container">
				<div
					id="json-property-input-textarea"
					className="sn-uib-json-property-input-edit-area">
					<div
						on-click={(e) => {
							e.stopPropagation();
							dispatch(OPEN_EDITOR_CLICKED);
						}}>
						<now-button size="sm" class="edit-button-nce">
							{t('Edit')}
						</now-button>
					</div>
					<now-textarea
						class={{ disabled: properties.disabled }}
						disabled={properties.disabled}
						value={properties.value}
						resize="none"
						label={properties.label}
						helper-content={
							!description ? null : (
								<sn-uib-property-tooltip-content
									description={description}
									selectedComponentTag={selectedComponentTag}
									hideTextLink={false}
								/>
							)
						}
					/>
				</div>
			</div>
			<sn-uib-code-editor-modal
				show-modal={openJSONEditor}
				internal-value={properties.value}
				read-only={properties.disabled}
				editor-type="json"
				editor-header={properties.label}
				validator={isValidJSON}
				inline={inline}
			/>
		</Fragment>
	);
};

createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: { editingScript: false },
	actionHandlers: {
		['NOW_BUTTON#CLICKED']: {
			stopPropagation: true
		},
		['NOW_TEXTAREA#INPUT']: ({ action, dispatch, properties }) => {
			const { fieldValue: value } = action.payload;
			dispatchWithEmptyBehaviour({ value, properties, dispatch });
		},
		[OPEN_EDITOR_CLICKED]: {
			effect: ({ updateState }) => {
				updateState({ openJSONEditor: true });
			},
			stopPropagation: true
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		},
		[SN_UIB_CODE_EDITOR_MODAL_CLOSED]: ({ updateState }) => {
			updateState({
				openJSONEditor: false
			});
		},
		[SN_UIB_CODE_EDITOR_MODAL_TEXT_UPDATED]: ({
			properties,
			action,
			updateState,
			dispatch
		}) => {
			const { value } = action.payload;
			dispatchWithEmptyBehaviour({ value, properties, dispatch });
			updateState({
				openJSONEditor: false
			});
		}
	},
	styles
});
