import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom, { createRef } from '@servicenow/ui-renderer-snabbdom';
import styles from './_sn-uib-read-only-element-wrapper.scss';
import get from 'lodash/get';
import { elementRefMap } from '../sn-uib-element-controls-placement';

const view = (state) => {
	const { elmHeightStyle, elmWidthStyle, wrapperRef } = state;
	const { id, parentNodeId } = state.properties;

	const style = { ...elmHeightStyle, ...elmWidthStyle };

	elementRefMap.set(id, wrapperRef, parentNodeId);

	return (
		<div ref={wrapperRef} className="container" style={style}>
			<slot />
		</div>
	);
};

export default createCustomElement('sn-uib-read-only-element-wrapper', {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		wrapperRef: createRef()
	},

	properties: {
		id: {
			default: null
		},
		elementStyles: {
			default: []
		},
		parentNodeId: {
			default: null
		},
		slotName: {
			default: null
		}
	},
	transformState: (state) => {
		const { elementStyles, id } = state.properties;
		const style = get(elementStyles, id, {});
		const height = get(style, 'height');
		if (height) {
			return { ...state, elmHeightStyle: { height, 'overflow-y': 'auto' } };
		}
		const width = get(style, 'width');
		if (width) {
			return { ...state, elmWidthStyle: { width, 'overflow-x': 'auto' } };
		}
		return state;
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ updateState }) => {
			updateState({
				wrapperRef: createRef()
			});
		}
	},
	styles
});
