export const filterTemplates = (templates = []) => {
	const standardTemplates = [];
	const customizedTemplates = [];

	templates.map((template) => {
		if (template.customized) {
			customizedTemplates.push(template);
		} else {
			standardTemplates.push(template);
		}
	});
	return {
		standardTemplates,
		customizedTemplates
	};
};
