import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-uib-drag-wrapper.scss';

const cursorLookup = (id) => {
	if (id) {
		return 'copy';
	}
	return 'move';
};

const getDataFromString = (data) => {
	try {
		return JSON.parse(data);
	} catch (e) {
		return {};
	}
};

const view = ({ properties }) => {
	const { tag, data, activeId } = properties;
	const Comp = tag;

	return (
		<div style={{ position: 'relative', width: '100%', height: '100%' }}>
			<div
				className={`wrapper ${cursorLookup(activeId)}`}
				style={{ position: 'absolute', width: '100%' }}>
				<div className="overlay" />

				<Comp {...getDataFromString(data)} />
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-drag-wrapper', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		status: {
			default: 'default'
		},
		activeId: {
			default: ''
		},
		tag: {
			default: 'div'
		},
		data: {
			default: {},
			schema: {
				type: 'string'
			}
		}
	},
	styles
});
