import '@servicenow/now-button';
import '@servicenow/now-pill';
import '@servicenow/now-typeahead';

export default function CreateFlowNavigator({
	sectionLabel,
	items,
	pills,
	buttonLabels,
	buttonStates
}) {
	if (!items) {
		return null;
	}

	return (
		<div>
			<div className="section-label"> {sectionLabel} </div>
			{items.map((item) => (
				<div className="container">
					<div className="row">
						<div className="text-portion">
							<div className="row-label">{item.label}</div>
							<div className="row-sublabel">{item.description}</div>
						</div>
						<now-button
							disabled={buttonStates[item.id]}
							label={buttonLabels[item.id]}
							append-to-payload={{ id: item.id }}
							variant="secondary"
							size="sm"
						/>
					</div>
					{pills && pills[item.id] ? (
						<div className="items-row">
							{pills[item.id].map((label) => (
								<now-pill size="sm" label={label} />
							))}
						</div>
					) : null}
				</div>
			))}
		</div>
	);
}
