import {
	createCustomElement,
	actionTypes,
	unstableResolvePropertySelectorValue
} from '@servicenow/ui-core';
import { t } from 'sn-translate';

import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { config } from './component-config';
import debounce from 'lodash/debounce';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import styles from './sn-uib-dynamic-property-input.scss';
import '@servicenow/now-typeahead';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import { getErrorMessage, isValidValue } from '../sn-uib-string-property-input';
import '../sn-uib-property-description-tooltip';
import {
	getItemListDropdownSize,
	updateDataBindingWithResolvedValues,
	getValuesFromResolvedInputDataSource,
	isValueAlreadyPresentInSuggestedBindings
} from './utils.js';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const PROPERTY_BINDING_BINDING_SET =
	'SN_UIB_PROPERTY_INPUT#PROPERTY_BINDING_SET';
export const DYNAMIC_PROPERTY_INPUT_FOCUSED = 'DYNAMIC_PROPERTY_INPUT#FOCUSED';
export const INTERNAL_RESET_OPEN_ON_FOCUS =
	'DYNAMIC_PROPERTY_INPUT#RESET_OPEN_ON_FOCUS';
export const INTERNAL_FOCUS = 'DYNAMIC_PROPERTY_INPUT#FOCUS';
export const INTERNAL_BLUR = 'DYNAMIC_PROPERTY_INPUT#BLUR';

const view = (
	{
		openOnFocus,
		isValid,
		internalValue,
		bindings,
		suggestedResolvedBindings,
		typeaheadDropdownSize,
		properties: {
			disabled,
			label,
			validation = {},
			error,
			autofocus,
			description,
			selectedComponentTag,
			translatable,
			longestResolvedSuggestedValue
		}
	},
	{ dispatch, updateState }
) => {
	const inputValue =
		internalValue && internalValue.startsWith('@')
			? `\\${internalValue}`
			: internalValue;
	const errorMsg = getErrorMessage(isValid, validation.message, error);
	const updatedBindings = isEmpty(suggestedResolvedBindings)
		? bindings
		: [...bindings, ...suggestedResolvedBindings];
	const internalValueSpanRef = createRef();
	const typeaheadRef = createRef();
	return (
		<Fragment>
			<span className="hidden-span" ref={internalValueSpanRef}>
				{longestResolvedSuggestedValue}
			</span>
			<div className="input-container" ref={typeaheadRef}>
				<label className="sn-uib-property-input-label">
					{label}
					{!description ? null : (
						<sn-uib-property-description-tooltip
							description={description}
							selectedComponentTag={selectedComponentTag}
						/>
					)}
					{!translatable ? null : (
						<sn-uib-property-description-tooltip
							description={t('Translatable turned on')}
							icon={'globe-outline'}
						/>
					)}
				</label>
				<now-typeahead
					value={inputValue}
					disabled={disabled}
					items={updatedBindings}
					search={'contains'}
					autofocus={autofocus}
					itemsListConstrain={{
						minWidth: 'target',
						...typeaheadDropdownSize
					}}
					messages={
						errorMsg
							? [
									{
										status: 'critical',
										content: errorMsg,
										icon: 'circle-exclamation-outline'
									}
							  ]
							: []
					}
					invalid={!isValid}
					manage-invalid
					manage-value
					on-keyup={() =>
						debounce(
							() =>
								getItemListDropdownSize(
									internalValueSpanRef,
									updateState,
									typeaheadDropdownSize
								),
							200
						)()
					}
					// REALLY UGLY focus listener but needed to open typeahead when empty dynamic
					// input field renders for the first time.
					on-focus={(event) => {
						getItemListDropdownSize(
							internalValueSpanRef,
							updateState,
							typeaheadDropdownSize
						);
						if (openOnFocus && autofocus) {
							const input = event.composedPath()[0];
							if (input) {
								input.click();
							}
							dispatch(INTERNAL_RESET_OPEN_ON_FOCUS);
						} else {
							dispatch(INTERNAL_FOCUS);
						}
					}}
					on-blur={() => dispatch(INTERNAL_BLUR)}
				/>
			</div>
		</Fragment>
	);
};

createCustomElement('sn-uib-dynamic-property-input', {
	view,
	styles,
	properties: config.properties,
	initialState: {
		openOnFocus: true,
		isValid: true,
		internalValue: '@',
		hasFocus: false,
		bindings: [],
		editingScript: false,
		resolvedInputDataSource: null,
		baseDataSource: '',
		suggestedResolvedBindings: [],
		typeaheadDropdownSize: {},
		isInputValuePresentOnLoad: false
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties, updateState }) => {
			const internalValue = get(properties, 'value', '') || '';
			const bindings = get(properties, 'bindings', []);
			dispatch(DYNAMIC_PROPERTY_INPUT_FOCUSED);
			const valueWithoutDataBindingChar = (internalValue || '').replace(
				'@',
				''
			);
			batchShorthandUpdateState(
				{
					internalValue,
					isInputValuePresentOnLoad: !isEmpty(valueWithoutDataBindingChar),
					bindings: bindings.map((item, idx) => ({
						...item,
						id: `item-${idx}`
					}))
				},
				updateState
			);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ updateState, action, state }) => {
			const { value, name } = action.payload;
			const { hasFocus } = state;
			if (name === 'value' && !hasFocus) {
				updateState({
					internalValue: value
				});
			}
			if (name === 'bindings') {
				updateState({
					bindings: value.map((item, idx) => ({ ...item, id: `item-${idx}` }))
				});
			}
		},
		[INTERNAL_RESET_OPEN_ON_FOCUS]: ({ updateState, action }) => {
			action.stopPropagation();
			batchShorthandUpdateState(
				{
					openOnFocus: false,
					hasFocus: true
				},
				updateState
			);
		},
		[INTERNAL_FOCUS]: ({ updateState, action }) => {
			action.stopPropagation();
			updateState({
				hasFocus: true
			});
		},
		[INTERNAL_BLUR]: ({ updateState, action, properties, state, dispatch }) => {
			const { dispatchOnBlur, id } = properties;
			const { internalValue: value, isValid } = state;
			action.stopPropagation();
			updateState({
				hasFocus: false
			});
			if (dispatchOnBlur && isValid) {
				dispatch(PROPERTY_BINDING_BINDING_SET, { value, id });
			}
		},
		['NOW_TYPEAHEAD#VALUE_SET']: ({
			updateState,
			action,
			properties,
			state,
			dispatch
		}) => {
			action.stopPropagation();
			const { id, validation = {}, bindings, dispatchOnBlur } = properties;
			const {
				suggestedResolvedBindings,
				resolvedInputDataSource,
				baseDataSource,
				isInputValuePresentOnLoad
			} = state;
			const { value } = action.payload;
			const isValid = isValidValue(value, validation.pattern);

			// Get the base data broker object value from the already existing bindings which would be resolved
			// and used to create other items in the updated bindings.
			const baseDataSourceObject = bindings.find((elem) =>
				value.includes(elem.label)
			);
			let suggestedDataBindings = [...suggestedResolvedBindings];

			if (baseDataSourceObject && isValid) {
				let resolvedInputValue;
				const isBaseDataSourceChanged =
					baseDataSourceObject.label !== baseDataSource;

				if (isBaseDataSourceChanged) {
					// resolve the props only if the base binding object changes
					if (baseDataSourceObject.bindingType === 'appChromeSettings') {
						// in this case, the base data source object is context.app
						resolvedInputValue = get(baseDataSourceObject, 'value', '');
					} else {
						// in this case, the base data source object is a data broker
						const resolvedBinding = get(
							baseDataSourceObject,
							'resolvedBinding',
							''
						);
						resolvedInputValue = unstableResolvePropertySelectorValue(
							resolvedBinding
						);
					}

					batchShorthandUpdateState(
						{
							baseDataSource: baseDataSourceObject.label,
							resolvedInputDataSource: resolvedInputValue
						},
						updateState
					);
				}

				if (!isBaseDataSourceChanged || isInputValuePresentOnLoad) {
					// We don't have to resolve the values again as the base data source didn't change,
					// so we can just get the values from state and process it.
					// Also, if the value is already present in the data source when page loads
					// The resolved object should find and return the appropriate value for the property present.
					resolvedInputValue = getValuesFromResolvedInputDataSource({
						value,
						baseDataSourceItem: baseDataSourceObject,
						resolvedInputDataSource:
							resolvedInputDataSource || resolvedInputValue
					});
				}

				if (
					!isValueAlreadyPresentInSuggestedBindings(
						value,
						suggestedDataBindings,
						resolvedInputValue
					)
				) {
					if (Array.isArray(resolvedInputValue)) {
						suggestedDataBindings = updateDataBindingWithResolvedValues(
							resolvedInputValue,
							value,
							baseDataSourceObject,
							true
						);
					} else if (
						typeof resolvedInputValue === 'object' &&
						resolvedInputValue !== null
					) {
						const resolvedInputValueKeys = Object.keys(resolvedInputValue);
						suggestedDataBindings = updateDataBindingWithResolvedValues(
							resolvedInputValueKeys,
							value,
							baseDataSourceObject
						);
					}
					updateState({
						suggestedResolvedBindings: suggestedDataBindings
					});
				}
			}

			batchShorthandUpdateState(
				{
					isValid,
					internalValue: value
				},
				updateState
			);

			if (isValid && !dispatchOnBlur) {
				debounce(
					() => dispatch(PROPERTY_BINDING_BINDING_SET, { value, id }),
					400
				)();
			}

			if (value === '') {
				dispatch(PROPERTY_INPUT_BINDING_BUTTON_CLICKED, {
					propertyId: id,
					isStatic: false
				});
			}
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	}
});
