import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import styles from './sn-uib-no-interact-wrapper.scss';

const Overlay = (props, ...children) => {
	return <div className="overlay">{children}</div>;
};
const view = ({ properties }) => {
	const { stopEvents } = properties;

	const Wrapper = stopEvents ? Overlay : Fragment;
	return (
		<div className="no-interact">
			<slot tabIndex="-1" />
			<Wrapper />
		</div>
	);
};

createCustomElement('sn-uib-no-interact-wrapper', {
	view,
	styles,
	properties: {
		targetRef: { default: null },
		stopEvents: {
			default: false
		}
	},
	eventHandlers: [
		{
			events: ['focusin'],
			effect({ action }) {
				action.payload.host.blur();
			}
		}
	]
});
