import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-scripts-picker.scss';
import '@servicenow/now-content-tree';
import { t } from 'sn-translate';
import EmptyState from '../partials/empty-state';

export const SCRIPTS_PICKER_SCRIPT_SELECTED =
	'SN_UIB_SCRIPTS_PICKER#SCRIPT_SELECTED';

const transformScripts = (scripts) => {
	return scripts.map(({ id, name }) => ({ id, label: name }));
};

const view = ({ properties: { scripts, selectedScriptId } }) => {
	if (!scripts || !scripts.length) {
		return (
			<EmptyState description={t('No page scripts have been added yet.')} />
		);
	} else {
		return (
			<div className="scripts-picker-container">
				<now-content-tree
					items={transformScripts(scripts)}
					selected-items={[[selectedScriptId]]}
					manage-selected-items={true}
				/>
			</div>
		);
	}
};

createCustomElement('sn-uib-scripts-picker', {
	view,
	properties: {
		scripts: {
			schema: {
				type: 'array'
			},
			default: []
		},
		selectedScriptId: {
			schema: {
				anyOf: [{ type: 'string' }, { type: 'null' }]
			},
			default: null
		}
	},
	actionHandlers: {
		['NOW_CONTENT_TREE#ITEM_CLICKED']: ({
			action: {
				payload: { item }
			},
			dispatch
		}) => {
			dispatch(SCRIPTS_PICKER_SCRIPT_SELECTED, { id: item.id });
		}
	},
	styles
});
