import { createCustomElement } from '@servicenow/ui-core';
import get from 'lodash/get';

import {
	droppableBehavior,
	DROP_BEHAVIOR_MOUNTED,
	DROP_BEHAVIOR_UNMOUNTED,
	DROP_BEHAVIOR_DROPPED,
	DROP_BEHAVIOR_DRAGGED_OVER,
	DROP_BEHAVIOR_DRAG_LEFT
} from '../behaviors/drop-behavior';
import styles from './_sn-uib-dnd-element-droppable.scss';
import '@servicenow/now-icon';
import getStyleClassNames from '../helpers/layoutDirection';

/* eslint-disable-next-line no-unused-vars */
const DropSection = ({
	dispatch,
	direction,
	dropId,
	elementId,
	isActiveDrag,
	classes = {}
}) => {
	return (
		<div
			class={{
				'drop-section': true,
				[direction]: true,
				active: isActiveDrag,
				...classes
			}}
			hook-insert={({ elm }) => {
				dispatch(DROP_BEHAVIOR_MOUNTED, {
					el: elm,
					data: {
						dropId,
						elementId,
						direction
					}
				});
			}}
			hook-remove={({ elm }) => dispatch(DROP_BEHAVIOR_UNMOUNTED, { el: elm })}>
			<div class={{ 'active-wrapper': true, ...classes, [direction]: true }}>
				<div class={{ 'active-border': true, [direction]: true }} />
			</div>
		</div>
	);
};

const view = (state, { dispatch, updateState }) => {
	const {
		properties: {
			id,
			isDragging,
			activeId,
			slotStyles,
			isHovered,
			selected,
			parentElement,
			slotName
		}
	} = state;
	const classes = getStyleClassNames(slotStyles);
	return (
		<div class={{ 'droppable-wrapper': true }}>
			<div
				class={{
					'drop-section-wrapper': true,
					selected: !!selected,
					hovered: !!isHovered,
					...classes
				}}>
				<DropSection
					key="left"
					direction="left"
					updateState={updateState}
					isActiveDrag={isDragging && activeId === `${id}_left`}
					dropId={`${id}_left`}
					elementId={id}
					parentElement={parentElement}
					slotName={slotName}
					classes={classes}
					dispatch={dispatch}
					state={state}
				/>
				<DropSection
					key="right"
					direction="right"
					dropId={`${id}_right`}
					parentElement={parentElement}
					slotName={slotName}
					updateState={updateState}
					elementId={id}
					isActiveDrag={isDragging && activeId === `${id}_right`}
					classes={classes}
					dispatch={dispatch}
					state={state}
				/>
			</div>
			<slot />
		</div>
	);
};

export default createCustomElement('sn-uib-dnd-element-droppable', {
	view,
	properties: {
		id: {
			default: null
		},

		addNextToElementId: {
			default: null
		},
		directionToAdd: {
			default: null
		},
		slotStyles: {
			default: {}
		},
		slotName: {
			default: null
		},
		parentElement: {
			default: null
		},
		isHovered: {
			default: false
		},
		selected: {
			default: false
		},

		disabled: {
			default: false
		},
		activeDirection: {
			default: null
		},
		keyboardDrag: {
			default: null
		},
		isDragging: {
			default: false
		},
		activeId: {
			default: null
		},
		parentId: {
			default: null
		}
	},
	behaviors: [
		{
			behavior: droppableBehavior,
			options: {}
		}
	],

	actionHandlers: {
		[DROP_BEHAVIOR_DRAGGED_OVER]: (coeffects) => {
			const {
				action: { payload },
				dispatch
			} = coeffects;
			const { data = {} } = payload;
			const { dropId } = data;

			dispatch('UIB_DND_DROPLEAF#HOVER', {
				id: dropId
			});
		},
		[DROP_BEHAVIOR_DRAG_LEFT]: (coeffects) => {
			const { dispatch } = coeffects;
			setTimeout(() => dispatch('UIB_DND_DROPZONE#CLEAR_ACTIVE_DROPZONE'), 50);
		},
		[DROP_BEHAVIOR_DROPPED]: (coeffects) => {
			const {
				dispatch,
				action: { payload },
				properties: { id }
			} = coeffects;
			const { action } = coeffects;

			action.stopPropagation();

			const { dataTransfer, data = {} } = payload;
			const { direction = null } = data;
			const elementId = data.elementId || id;

			dispatch('UIB_DND_DROPZONE#DROPPED', {
				dataTransfer: dataTransfer,
				elementId,
				direction
			});
		}
	},
	styles
});
