import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { onNextFrame } from '../helpers/Transition';
import get from 'lodash/get';
import { t } from 'sn-translate';
import styles from './sn-uib-workspace-element-controls.scss';

// Actions dispatched
export const ELEMENT_CONFIG_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#CONFIG_BUTTON_CLICKED';
export const ELEMENT_TRASH_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#TRASH_BUTTON_CLICKED';
export const ELEMENT_TRASH_CONFIRM_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#TRASH_CONFIRM_BUTTON_CLICKED';
export const ELEMENT_TRASH_CANCEL_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#TRASH_CANCEL_BUTTON_CLICKED';
export const ELEMENT_FIT_DETECTED = 'SN_UIB_ELEMENT_CONTROLS#FIT_DETECTED';

// Button icons
const CONFIG_ICON = 'sliders-outline';
const TRASH_ICON = 'trash-outline';
const CONFIRM_ICON = 'check-outline';
const CANCEL_ICON = 'close-outline';

// CSS Classes
const ROOT_CONTAINER_BASE_CLASS = 'uib-element-container';
const CONTROLS_CONTAINER_BASE_CLASS =
	'uib-workspace-element-controls-container';
const ELEMENT_SLOT_CONTAINER_BASE_CLASS = 'uib-element-slot-container';

const { createFocusRef } = DevxKeyFocusBehavior;

export const elementClassFactory = function ({
	properties: { focused, hovered, disabled, confirm }
}) {
	const classes = {
		container: {
			[ROOT_CONTAINER_BASE_CLASS]: true
		},
		controls: {
			[CONTROLS_CONTAINER_BASE_CLASS]: true
		},
		slot: {
			[ELEMENT_SLOT_CONTAINER_BASE_CLASS]: true
		}
	};
	Object.keys(classes).forEach((key) => {
		classes[key] = {
			...classes[key],
			'--focused': focused,
			'--hovered': !focused && hovered,
			'--confirm': confirm,
			'--disabled': disabled
		};
	});
	return classes;
};

// Renderers

const renderConfigButton = (properties, dispatch) => {
	return (
		<button
			aria-label={t('Open element configuration')}
			class={{
				'uib-workspace-element-controls-button': true,
				'uib-workspace-element-controls-button__open-config-button': true
			}}
			tabindex="-1"
			ref={createFocusRef(`${properties.element.id}_CONFIG`, {
				Escape: `${properties.element.id}_ROOT`,
				Tab: `${properties.element.id}_TRASH`,
				ShiftTab: `${properties.element.id}_MOVE`
			})}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(ELEMENT_CONFIG_BUTTON_CLICKED, {
					element: properties.element
				});
			}}>
			<now-icon icon={CONFIG_ICON} />
		</button>
	);
};

const renderTrashButton = (properties, dispatch) => {
	return (
		<button
			aria-label={t('Delete component')}
			class={{
				'uib-workspace-element-controls-button': true,
				'uib-workspace-element-controls-button__delete-button': true
			}}
			disabled={properties.disabled}
			tabindex="-1"
			ref={createFocusRef(`${properties.element.id}_TRASH`, {
				ShiftTab: `${properties.element.id}_CONFIG`,
				Escape: `${properties.element.id}_ROOT`,
				Tab: tabId(
					properties.resizeInfo,
					properties.keymaps,
					properties.element.id
				)
			})}
			on-click={(e) => {
				e.stopPropagation();
				if (properties.disabled) {
					return;
				}

				dispatch(ELEMENT_TRASH_BUTTON_CLICKED, {
					elementId: properties.element.id
				});
			}}>
			<now-icon icon={TRASH_ICON} />
		</button>
	);
};

const renderConfirmButton = (properties, dispatch) => {
	return (
		<button
			aria-label={t('Confirm delete component')}
			class={{
				'uib-workspace-element-controls-button': true,
				'uib-workspace-element-controls-button__confirm-delete-button': true
			}}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(ELEMENT_TRASH_CONFIRM_BUTTON_CLICKED, {
					elementId: properties.element.id
				});
				dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
					ariaLiveText: t('Element deleted from the workspace')
				});
			}}>
			<now-icon icon={CONFIRM_ICON} />
		</button>
	);
};

const renderCancelButton = (dispatch) => {
	return (
		<button
			aria-label={t('Cancel delete component')}
			class={{
				'uib-workspace-element-controls-button': true,
				'uib-workspace-element-controls-button__cancel-delete-button': true
			}}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(ELEMENT_TRASH_CANCEL_BUTTON_CLICKED);
			}}>
			<now-icon icon={CANCEL_ICON} />
		</button>
	);
};

const renderButton = (type, properties, dispatch) => {
	switch (type) {
		case 'config':
			return renderConfigButton(properties, dispatch);
		case 'trash':
			return renderTrashButton(properties, dispatch);
		case 'confirm':
			return renderConfirmButton(properties, dispatch);
		case 'cancel':
			return renderCancelButton(dispatch);
		default:
			return null;
	}
};

const renderGrip = (properties, dispatch) => {
	const dragProps = {
		id: properties.element.id,
		key: properties.element.id,
		element: {
			displayName: properties.element.displayName,
			icon: properties.element.icon || 'building-fill'
		},
		disabled: properties.disabled
	};

	const DragWrapper = properties.disabled // eslint-disable-line no-unused-vars
		? Fragment
		: 'sn-uib-layout-drag-leaf';

	return (
		<DragWrapper {...dragProps}>
			<span
				className="uib-workspace-element-controls-drag-grip"
				tabindex="-1"
				ref={createFocusRef(`${properties.element.id}_MOVE`, {
					Escape: `${properties.element.id}_ROOT`,
					Tab: `${properties.element.id}_CONFIG`
				})}
				on-keydown={(e) => {
					if (properties.disabled) {
						return;
					}

					const { key, defaultPrevented } = e;
					if (key !== 'Enter' || defaultPrevented) {
						return;
					}
					e.preventDefault();
					dispatch('DRAG_LEAF#START_KEYBOARD_DRAG', {
						id: properties.element.id
					});
					onNextFrame(() => {
						dispatch('UIB_PAGE_MANAGEMENT#FOCUS_DND_ENTRYPOINT');
					});
				}}
				aria-label={t('Move Element Button')}
				className="uib-workspace-element-controls-drag-grip"
			/>
		</DragWrapper>
	);
};

const tabId = (resizeInfo, keymaps, id) => {
	let target = id;
	const { nextElement } = keymaps || {};
	const left = get(resizeInfo, 'left', null);
	const right = get(resizeInfo, 'right', null);
	if (left) {
		target += '_RESIZE_LEFT';
	} else if (right) {
		target += '_RESIZE_RIGHT';
	} else {
		target = nextElement
			? `${nextElement}_ROOT`
			: 'quick-link-element-configuration-panel';
	}
	return target;
};

const view = (state, { dispatch }) => {
	const classes = elementClassFactory(state);
	const { properties, fitLastElement } = state;
	const { confirm } = properties;
	return (
		<article
			id="uib-workspace-element-controls"
			on-mouseover={(e) => e.stopPropagation()}
			class={{
				...classes.controls,
				'--fit': fitLastElement
			}}>
			{renderGrip(properties, dispatch)}
			<span className="uib-workspace-element-controls-instance-name">
				{confirm ? t('Delete component?') : properties.element.displayName}
			</span>
			<span className="uib-workspace-element-controls-buttons">
				<span className="uib-workspace-element-controls-divider" />
				{confirm
					? renderButton('confirm', properties, dispatch)
					: renderButton('config', properties, dispatch)}
				<span className="uib-workspace-element-controls-divider" />
				{confirm
					? renderButton('cancel', properties, dispatch)
					: renderButton('trash', properties, dispatch)}
			</span>
		</article>
	);
};

export default createCustomElement('sn-uib-workspace-element-controls', {
	view,
	properties: {
		focusRefresh: {
			default: {}
		},
		/**
		 * The element object that describes the currently
		 * mounted element
		 * @type {object}
		 * @example - {id: '12345', displayValue: 'Form component'}
		 */
		element: { default: null },
		/**
		 * The focused flag used to denote that the mounted
		 * element is the currently selected element on UIB
		 * stage and is in focus
		 * @type {boolean}
		 */
		focused: { default: false },

		/**
		 * The hovered flag used to denote that the mounted
		 * element is currently being moused over on UIB
		 * stage
		 */
		hovered: { default: false },
		keymaps: { default: {} },

		/**
		 * Whether the element belongs to a page that has a read-only state, eg.
		 * should not allow changing any values for that page
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * Whether or not the element is being delete confirmed
		 * @type {boolean}
		 */
		confirm: { default: false },
		/**
		 * The current state of the resize handles on the related element
		 * @type {object}
		 */
		resizeInfo: { default: null }
	},
	initialState: {
		fitLastElement: false
	},
	actionHandlers: {
		[ELEMENT_TRASH_BUTTON_CLICKED]: ({ properties, action, updateState }) => {
			if (action.payload.elementId === properties.element.id) {
				updateState({ confirm: true });
			}
		},
		[ELEMENT_TRASH_CONFIRM_BUTTON_CLICKED]: ({ updateState }) => {
			updateState({ confirm: false });
		},
		[ELEMENT_TRASH_CANCEL_BUTTON_CLICKED]: ({ updateState }) => {
			updateState({ confirm: false });
		}
	},
	styles
});
