import { createCustomElement } from '@servicenow/ui-core';
import '../sn-uib-address-bar';
import { SnUIBMatchMediaBehavior } from '../behaviors/SnUIBMatchMediaBehavior';

import focusBehavior from '@servicenow/behavior-focus';

import {
	SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE,
	SN_UIB_PAGE_CLOSE_MENU,
	nowButtonClickedHandler,
	nowDropdownItemClickedHandler,
	snUIBPageHeaderUpdateMediaSizeHandler,
	uibContentTreeItemSelectedHandler,
	uibContentTreeSecondaryActionHandler,
	nowPopoverOpenedSetHandler,
	snUIBCloseMenuHandler
} from './actions';

import {
	SnUIBPageHeaderActions, // eslint-disable-line no-unused-vars
	SnUIBPageHeaderMenu // eslint-disable-line no-unused-vars
} from './partials';

import styles from './sn-uib-page-header.scss';

function SnUIBPageHeader(state, { updateState }) {
	const { mediaSize } = state;
	const {
		appType,
		appScope,
		appName,
		pageName,
		pageStatus,
		parentInfo,
		menuItems,
		headerActions,
		builderType,
		runtimeUrl,
		urlTestValueChanged,
		displayScopePicker,
		applications,
		userScope,
		isPagesLoading,
		domainTree,
		currentDomain,
		displayDomainPicker,
		displayExpandDomain,
		isDomainExpanded,
		openedMenu,
		isEmptyState,
		isVariantSelected,
		pageSysPolicy,
		pageScope
	} = state.properties;
	const isRuntimeUrlPresent = runtimeUrl && runtimeUrl.absolute;

	return (
		<div className="sn-uib-page-header--container">
			<header
				class={{
					'sn-uib-page-header': true,
					'empty-state': isEmptyState,
					'--isolation-mode': !isRuntimeUrlPresent
				}}>
				<SnUIBPageHeaderMenu
					classes={{
						'sn-uib-page-header-menu': true,
						'--isolation-mode': !isRuntimeUrlPresent
					}}
					appName={appName}
					menuItems={menuItems}
					builderType={builderType}
					openedMenu={openedMenu}
					parentInfo={parentInfo}
					isEmptyState={isEmptyState}
					updateState={updateState}
					state={state}
				/>

				<sn-uib-address-bar
					class={{
						'sn-uib-address-bar': true,
						'--runtime-url': isRuntimeUrlPresent
					}}
					runtimeUrl={runtimeUrl}
					url-test-value-changed={urlTestValueChanged}
					is-variant-selected={isVariantSelected}
					page-name={pageName}
					page-sys-policy={pageSysPolicy}
					applications={applications}
					user-scope={userScope}
					support-scope={displayScopePicker}
					page-scope={pageScope}
				/>

				<SnUIBPageHeaderActions
					classes={{
						'sn-uib-page-header-actions': true,
						'header-item': true,
						'--isolation-mode': !isRuntimeUrlPresent
					}}
					mediaSize={mediaSize}
					appScope={appScope}
					pageStatus={pageStatus}
					appType={appType}
					headerActions={headerActions}
					displayScopePicker={displayScopePicker}
					applications={applications}
					userScope={userScope}
					isPagesLoading={isPagesLoading}
					domainTree={domainTree}
					currentDomain={currentDomain}
					displayDomainPicker={displayDomainPicker}
					displayExpandDomain={displayExpandDomain}
					isDomainExpanded={isDomainExpanded}
				/>
			</header>
		</div>
	);
}

createCustomElement('sn-uib-page-header', {
	view: SnUIBPageHeader,

	properties: {
		appType: {},

		/**
		 * This property represents the application scope of the current app.
		 * Known aliases are `sysparm_transaction_scope` or app id.
		 * This property doesn't need to be localized since it is a system value.
		 * @type {'global' | string}
		 */
		appScope: {},

		appName: {},

		/**
		 * The name of the currently selected page.				default: {}
		 * This property is set by the user and it needn't be localized when assigned.			},
		 * @type {string}
		 */
		pageName: {},

		pageSysId: {},
		/**
		 * The object containing runtime url chunks
		 */
		runtimeUrl: {
			default: {}
		},
		/**
		 * The UIB action that handles
		 * changes to page properties in the url
		 */
		urlTestValueChanged: {
			default: ''
		},
		parentInfo: {
			default: {}
		},
		pageStatus: {},

		menuItems: {
			default: []
		},
		openedMenu: {
			default: null
		},
		headerActions: {
			default: []
		},
		/**
		 * The type of application (either UI, Dashboard)
		 * @type {string}
		 */
		builderType: {
			default: 'UI'
		},

		isEmptyState: {
			default: false
		},

		displayScopePicker: {
			default: false
		},
		applications: {
			default: []
		},

		userScope: {
			default: {}
		},

		pageScope: {
			default: {
				id: '',
				label: ''
			}
		},

		isPagesLoading: {
			default: false
		},

		domainTree: {
			default: []
		},

		currentDomain: {
			default: null
		},
		isVariantSelected: {
			default: false
		},
		displayDomainPicker: {
			default: false
		},
		displayExpandDomain: {
			default: false
		},
		isDomainExpanded: {
			default: false
		}
	},
	initialState: {
		mediaSize: {
			isSM: false,
			isMD: false,
			isLG: false,
			isXL: true
		}
	},

	actions: {
		[SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE]: { private: true }
	},

	actionHandlers: {
		'NOW_BUTTON#CLICKED': nowButtonClickedHandler,
		'NOW_DROPDOWN#ITEM_CLICKED': nowDropdownItemClickedHandler,
		'SN_UIB_CONTENT_TREE_PICKER#ITEM_SELECTED': uibContentTreeItemSelectedHandler,
		'SN_UIB_CONTENT_TREE_PICKER#SECONDARY_ACTION_CLICKED': uibContentTreeSecondaryActionHandler,
		[SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE]: snUIBPageHeaderUpdateMediaSizeHandler,
		[SN_UIB_PAGE_CLOSE_MENU]: snUIBCloseMenuHandler,
		'NOW_POPOVER#OPENED_SET': nowPopoverOpenedSetHandler
	},

	dispatches: {
		/**
		 * Dispatched when the save button is clicked in the header toolbar.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	appType: 'Agent Workspace',
		 * 	appScope: 'global',
		 * 	pageName: 'Landing Page',
		 * 	pageStatus: 'inactive',
		 * 	pageIsSelected: true,
		 * 	pageIsReadOnly: false
		 * }
		 */
		'SN_UIB_PAGE_HEADER#SAVE_CLICKED': {},

		/**
		 * Dispatched when the activate button is clicked in the header toolbar.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	appType: 'Agent Workspace',
		 * 	appScope: 'global',
		 * 	pageName: 'Landing Page',
		 * 	pageStatus: 'inactive',
		 * 	pageIsSelected: true,
		 * 	pageIsReadOnly: false
		 * }
		 */
		'SN_UIB_PAGE_HEADER#ACTIVATE_CLICKED': {},

		/**
		 * Dispatched when the Create New Page item is selected for the menu dropdown.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	appType: 'Agent Workspace',
		 * 	appScope: 'global',
		 * 	pageName: 'Landing Page',
		 * 	pageStatus: 'inactive',
		 * 	pageIsSelected: true,
		 * 	pageIsReadOnly: false
		 * }
		 */
		'SN_UIB_PAGE_HEADER#NEW_PAGE_CLICKED': {},

		/**
		 * Dispatched when the Edit Page Properties item is selected for the menu dropdown.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	appType: 'Agent Workspace',
		 * 	appScope: 'global',
		 * 	pageName: 'Landing Page',
		 * 	pageStatus: 'inactive',
		 * 	pageIsSelected: true,
		 * 	pageIsReadOnly: false
		 * }
		 */
		'SN_UIB_PAGE_HEADER#EDIT_PAGE_PROPERTIES_CLICKED': {},

		/**
		 * Dispatched when the Clone Page item is selected for the menu dropdown.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	appType: 'Agent Workspace',
		 * 	appScope: 'global',
		 * 	pageName: 'Landing Page',
		 * 	pageStatus: 'inactive',
		 * 	pageIsSelected: true,
		 * 	pageIsReadOnly: false
		 * }
		 */
		'SN_UIB_PAGE_HEADER#CLONE_PAGE_CLICKED': {},

		/**
		 * Dispatched when the application is selected on the dropdown.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	id: 'global',
		 * 	label: 'Global'
		 * }
		 */
		'SN_UIB_PAGE_HEADER#APPLICATION_SELECTED': {},

		/**
		 * Dispatched when the domain is selected on the dropdown.
		 * Payload contains the header properties.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	id: 'global',
		 * 	label: 'Global'
		 * }
		 */
		'SN_UIB_PAGE_HEADER#DOMAIN_SELECTED': {}
	},

	behaviors: [
		{
			behavior: SnUIBMatchMediaBehavior,
			options: {
				queries: [
					{
						media: '(max-width: 1100px)',
						actionType: SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE,
						appendToPayload: {
							mediaSize: 'sm'
						}
					},
					{
						media: '(min-width: 1100px) and (max-width: 1400px)',
						actionType: SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE,
						appendToPayload: {
							mediaSize: 'md'
						}
					},
					{
						media: '(min-width: 1400px) and (max-width: 1750px)',
						actionType: SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE,
						appendToPayload: {
							mediaSize: 'lg'
						}
					},
					{
						media: '(min-width: 1750px)',
						actionType: SN_UIB_PAGE_HEADER_UPDATE_MEDIA_SIZE,
						appendToPayload: {
							mediaSize: 'xl'
						}
					}
				]
			}
		},
		focusBehavior
	],

	styles
});
