import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-layout-context-menu.scss';
import { t } from 'sn-translate';

export const SN_UIB_LAYOUT_CONTEXT_MENU_EDIT_LAYOUT =
	'SN_UIB_LAYOUT_CONTEXT_MENU#EDIT_LAYOUT';
export const SN_UIB_LAYOUT_CONTEXT_MENU_OPENED_SET =
	'SN_UIB_LAYOUT_CONTEXT_MENU#OPENED_SET';
export const SN_UIB_LAYOUT_CONTEXT_MENU_RESET_LAYOUT =
	'SN_UIB_LAYOUT_CONTEXT_MENU#RESET_LAYOUT';
export const SN_UIB_LAYOUT_CONTEXT_MENU_OPEN_IN_PLATFORM =
	'SN_UIB_LAYOUT_CONTEXT_MENU#OPEN_IN_PLATFORM';

const divider = () => <div className="context-menu-divider" />;

//TODO: Link to documentaiton when it is ready
const layoutContextMenuPanel = (state, dispatch) => {
	const { context } = state.properties;

	return (
		<div className="context-menu-panel">
			<button
				on-click={() => {
					dispatch(SN_UIB_LAYOUT_CONTEXT_MENU_EDIT_LAYOUT, { context });
					dispatch(SN_UIB_LAYOUT_CONTEXT_MENU_OPENED_SET, { value: false });
				}}
				className="context-menu-item">
				{t('Edit layout code')}
			</button>
			<button
				on-click={() => {
					dispatch(SN_UIB_LAYOUT_CONTEXT_MENU_RESET_LAYOUT, { context });
				}}
				className="context-menu-item">
				{t('Reset to original')}
			</button>
			{divider()}
			<button
				className="context-menu-item"
				on-click={() => {
					dispatch(SN_UIB_LAYOUT_CONTEXT_MENU_OPEN_IN_PLATFORM);
				}}>
				{t('Open Builder Template table')}
			</button>
			{/* <button className="context-menu-item">Layout documentation</button> */}
		</div>
	);
};
const view = (state) => {
	const { opened } = state;
	const { context, disabled } = state.properties;

	return (
		<now-popover
			constrain={{ width: 256 }}
			manageOpened
			opened={opened}
			hideTail
			positions={[
				{ target: 'bottom-end', content: 'top-end' },
				{ target: 'bottom-start', content: 'top-start' }
			]}
			className="layout-tab-section"
			disabed={disabled}>
			<now-button-iconic
				slot="trigger"
				className="layout-tab-item"
				variant="tertiary"
				icon="ellipsis-v-fill"
				size="sm"
				tooltipContent={t('Edit layout')}
				disabled={disabled}
			/>
			<sn-uib-layout-context-menu-panel context={context} slot="content" />
		</now-popover>
	);
};
createCustomElement('sn-uib-layout-context-menu-panel', {
	view: layoutContextMenuPanel,
	properties: {
		context: {
			default: null
		},
		disabled: {
			default: false
		}
	},
	initialState: {},
	styles
});
createCustomElement('sn-uib-layout-context-menu', {
	view,
	properties: {
		context: {
			default: null
		},
		disabled: {
			default: false
		}
	},
	initialState: {
		opened: false
	},
	actionHandlers: {
		'NOW_POPOVER#OPENED_SET': ({ action, updateState, properties }) => {
			if (properties.disabled) {
				return;
			}

			updateState({
				opened: action.payload.value
			});
		},
		'SN_UIB_LAYOUT_CONTEXT_MENU#OPENED_SET': ({ action, updateState }) => {
			updateState({
				opened: action.payload.value
			});
		},
		'SN_UIB_LAYOUT_CONTEXT_MENU#RESET_LAYOUT': ({ updateState }) => {
			updateState({
				opened: false
			});
		}
	},
	styles
});
