import '@servicenow/now-heading';
import '@servicenow/now-icon';
import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { onNextFrame, Transition } from '../helpers/Transition'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-collapsible-panel.scss';
export const PANEL_TOGGLED = 'COLLAPSIBLE_PANEL#PANEL_TOGGLED';
export const PANEL_TRANSITION_END = 'COLLAPSIBLE_PANEL#TRANSITION_END';
export const COLLAPSE_TOOLTIP = t('Collapse Panel');
export const EXPAND_TOOLTIP = t('Expand Panel');
export const chevronIcon = {
	open: {
		left: 'chevron-left-fill',
		right: 'chevron-right-fill'
	},
	closed: {
		left: 'chevron-right-fill',
		right: 'chevron-left-fill'
	}
};

const focusQ = new Map();

const expandedView = (
	{
		heading,
		direction,
		tooltip,
		componentId,
		expanded,
		id,
		headingRenderer = {}
	},
	dispatch
) => {
	const { render = false, tagName: TagName, ...props } = headingRenderer; //eslint-disable-line no-unused-vars
	return (
		<Fragment>
			<Transition className="header" enter={100} exit={200}>
				{render === true ? (
					<TagName className="collapsible-panel-heading" {...props} />
				) : (
					<h2
						id={`heading-${id}`}
						className="p2-panel-header-text collapsible-panel-heading">
						{heading}
					</h2>
				)}
			</Transition>

			<now-button-iconic
				bare
				icon={chevronIcon.open[direction]}
				className="expander"
				size="sm"
				tooltip-content={tooltip}
				variant="tertiary"
				hook-insert={({ elm }) => {
					if (focusQ.get(componentId) === 'open') {
						focusQ.set(componentId, null);
						onNextFrame(() => {
							elm.focus();
						}, 125);
					}
				}}
				on-click={() => {
					focusQ.set(componentId, 'closed');
					dispatch(PANEL_TOGGLED, { value: !expanded });
				}}
				config-aria={{
					'aria-label': tooltip,
					'aria-expanded': `${expanded}`,
					'aria-controls': `${id}`
				}}
			/>
		</Fragment>
	);
};
const expandedSlot = () => {
	return (
		<Transition className="slotted-content" enter={100} exit={200}>
			<div className="slotted-content">
				<slot />
			</div>
		</Transition>
	);
};

const collapsedView = (
	{ headingIcon, tooltip, componentId, expanded, id, isEmptyState },
	dispatch
) => {
	return (
		<now-button-iconic
			bare
			icon={headingIcon}
			className="collapse-toggle"
			size="sm"
			tooltip-content={tooltip}
			hook-insert={({ elm }) => {
				if (focusQ.get(componentId) === 'closed') {
					focusQ.set(componentId, null);
					onNextFrame(() => {
						elm.focus();
					}, 125);
				}
			}}
			config-aria={{
				'aria-label': tooltip,
				'aria-expanded': `${expanded}`,
				'aria-controls': id
			}}
			variant="tertiary"
			on-click={() => {
				focusQ.set(componentId, 'open');

				dispatch(PANEL_TOGGLED, { value: true });
			}}
			disabled={isEmptyState}
		/>
	);
};
const iconView = (
	{ headingIcon, tooltipContent, componentId, expanded, id, isEmptyState },
	dispatch
) => {
	return (
		<span>
			{expanded ? (
				<now-icon
					className="collapsible-panel-icon"
					icon={headingIcon}
					size="sm"
					on-click={() => {
						const status = expanded ? 'closed' : 'open';
						focusQ.set(componentId, status);

						dispatch(PANEL_TOGGLED, { value: !expanded });
					}}
					disabled={isEmptyState}
				/>
			) : (
				collapsedView(
					{
						headingIcon,
						componentId,
						tooltip: tooltipContent.collapsed,
						expanded,
						id,
						isEmptyState
					},
					dispatch
				)
			)}
		</span>
	);
};
const view = (state, { dispatch }) => {
	const {
		collapseTooltip,
		direction,
		expanded,
		heading,
		headingIcon,
		id,
		headingRenderer,
		isEmptyState
	} = state.properties;

	const { componentId } = state;

	const tooltipContent = {
		...{
			expanded: COLLAPSE_TOOLTIP,
			collapsed: EXPAND_TOOLTIP
		},
		...collapseTooltip
	};
	const Wrapper = expanded ? 'div' : Fragment; //eslint-disable-line no-unused-vars
	return (
		<aside
			class={{
				'collapsible-panel-container': true,
				expanded: expanded,
				collapsed: !expanded,
				[direction]: true
			}}
			aria-labelledby={`heading-${id}`}
			on-transitionend={(event) => {
				// This is looking for a specific transition state where the
				// collaapsible panel's width style is finished transitioning
				const eventPath = event.path || event.composedPath();
				if (
					event.propertyName === 'width' &&
					eventPath[0].classList.contains('collapsible-panel-container')
				) {
					dispatch(PANEL_TRANSITION_END);
				}
			}}>
			<Wrapper className="overflow-wrapper" key="overflow-wrapper">
				<div className="heading">
					{iconView(
						{
							headingIcon,
							componentId,
							heading,
							direction,
							tooltipContent,
							expanded,
							id,
							isEmptyState
						},
						dispatch
					)}
					{expanded
						? expandedView(
								{
									headingIcon,
									componentId,
									heading,
									direction,
									tooltip: tooltipContent.expanded,
									expanded,
									id,
									headingRenderer
								},
								dispatch
						  )
						: null}
				</div>

				{expanded
					? expandedSlot(
							{
								headingIcon,
								componentId,
								heading,
								direction,
								tooltip: tooltipContent.expanded
							},
							dispatch
					  )
					: null}
			</Wrapper>
			{expanded ? <div className="shadow-box"></div> : null}
		</aside>
	);
};

/**
 * A helper element for expandable / collapsible panels in UI Builder.
 *
 */
export default createCustomElement('sn-uib-collapsible-panel', {
	view,
	properties: {
		/**
		 * The Id of the element being controlled.
		 * @type {string}
		 */
		id: {
			default: null
		},
		/**
		 * Whether the collapsible section is currently expanded.
		 * @type {boolean}
		 */
		expanded: {
			default: true
		},
		/**
		 * The icon value to use for the heading.
		 * @type {string}
		 */
		headingIcon: {
			default: 'document-outline'
		},
		/**
		 * The text to render for the header.
		 * @type {string}
		 */
		heading: {
			default: ''
		},
		/**
		 * Which direction the panel collapses to.
		 * @type {'left'|'right'}
		 *
		 */
		direction: {
			default: 'left'
		},
		/**
		 * The text content of the collapse icon states.
		 * @type {{expanded: string, collapsed: string}}
		 *
		 */
		collapseTooltip: {
			default: {
				expanded: COLLAPSE_TOOLTIP,
				collapsed: EXPAND_TOOLTIP
			}
		},
		/**
		 * A config object for the collapsible panel header renderer
		 */
		headingRenderer: {
			schema: { type: 'object' },
			default: {}
		},
		isEmptyState: {
			default: false
		}
	},
	slots: {
		/**
		 * Content placed in the default slot will appear inside of the collapsible section.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	styles
});
