import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-content-tree';
import styles from './sn-uib-data-broker-definitions.scss';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars

createCustomElement('sn-uib-db-applications', {
	view: ({ properties: { applications = [], selectedItems } }) => {
		return applications.length === 0 ? (
			<EmptyState
				size="md"
				description={t('No applications with matching data resources.')}
			/>
		) : (
			<div className="sn-uib-db-applications">
				<now-content-tree
					size="sm"
					items={applications}
					selectedItems={selectedItems}
				/>
			</div>
		);
	},
	styles,
	properties: {
		applications: {
			schema: { type: 'array' },
			default: []
		},
		selectedItems: {
			schema: { type: 'array' },
			default: []
		}
	},
	actionHandlers: {
		'NOW_CONTENT_TREE#ITEM_CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch('SN_UIB_DB_APPLICATIONS#APPLICATION_SELECTED', {
				application: action.payload.item.id
			});
		}
	}
});
