import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './_sn-uib-element-controls-placement.scss';
import {
	elementRefMap,
	ElementUpdater,
	ACTIVE_WITH_BAD_DIMENSIONS
} from './utils';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

const overlapMap = new Map();
const overlapBounds = {
	x: 225,
	y: 26
};
const typeClassnames = {
	slot: '--slot',
	container: '--container',
	leaf: '--leaf'
};
const contextClassnames = {
	hovered: '--hovered',
	selected: '--selected'
};
const observerConfig = { attributes: true, childList: false, subtree: false };
const getTranslateValues = (str) => {
	const regex = /translate3d\(([\d.]+)px, ([\d.]+)px, [\d.]+px\)/;
	const results = regex.exec(str);
	if (!results) {
		return results;
	}
	return [
		['x', results[1]],
		['y', results[2]]
	].reduce((acc, result) => {
		const [axis, value] = result;
		return {
			...acc,
			[axis]: parseFloat(value, 10)
		};
	}, {});
};
const getTypeClasses = (type, context, disabled) => {
	return {
		[typeClassnames[type]]: true,
		[contextClassnames[context]]: true,
		'--disabled': disabled
	};
};
const getObserver = (name, namesToCheck = []) => {
	return new MutationObserver((mutationsList) => {
		for (const mutation of mutationsList) {
			if (
				mutation.type !== 'attributes' ||
				mutation.attributeName !== 'style'
			) {
				return;
			}
			const transformValues = getTranslateValues(
				mutation.target.style.transform
			);
			overlapMap.set(
				name,
				getTranslateValues(mutation.target.style.transform),
				transformValues
			);
			const overlap =
				transformValues &&
				namesToCheck
					.map((key) => overlapMap.get(key))
					.filter(Boolean)
					.some((coords) => {
						const xDistance = Math.abs(coords.x - transformValues.x);
						const yDistance = Math.abs(coords.y - transformValues.y);
						return yDistance < overlapBounds.y && xDistance < overlapBounds.x;
					});
			if (overlap) {
				mutation.target.classList.add('overlap');
			} else if (mutation.target.classList.contains('overlap')) {
				mutation.target.classList.remove('overlap');
			}
		}
	});
};
const view = ({
	placementModel,
	observer,
	properties: { elementId, type, context, disabled }
}) => {
	const typeClasses = getTypeClasses(type, context, disabled);
	return (
		<div
			class={{ placer: true }}
			hook={{
				insert({ elm }) {
					if (placementModel) {
						placementModel.setElement(elm);
					}
					if (observer) {
						observer.observe(elm, observerConfig);
					}
				},
				update(_, { elm }) {
					if (placementModel) {
						placementModel.setElement(elm);
					}
					if (observer) {
						observer.observe(elm, observerConfig);
					}
				}
			}}>
			<div className="controls">
				<slot name={`${elementId}_controls`} />
			</div>
			<div className="insert-buttons">
				<slot name={`${elementId}_insert-buttons`} />
			</div>
			<div
				class={{
					highlight: true,
					...typeClasses
				}}>
				<slot name={`${elementId}_highlight`} />
			</div>
		</div>
	);
};
export default createCustomElement('sn-uib-element-controls-placement', {
	view,
	initialState: {
		placementModel: null,
		observer: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			state,
			action: { payload }
		}) => {
			const { name, value, previousValue } = payload;
			const { placementModel } = state;
			if (name === 'elementId' && !value !== previousValue) {
				const stageRoot = elementRefMap.get('stageRootRef') || {};
				const fallbackElement = (stageRoot.ref || {}).current;
				if (fallbackElement) {
					placementModel.setFallback(fallbackElement);
				}
				placementModel.setId(value);
			}
		},
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			updateState,
			updateProperties,
			properties
		}) => {
			const model = new ElementUpdater({
				map: elementRefMap,
				notifyStateChange: ({ state }) => {
					updateProperties({ isVisible: state !== ACTIVE_WITH_BAD_DIMENSIONS });
				}
			});
			const name = properties.name;
			const namesToCheck = properties.namesToCheck || [];
			const observer = getObserver(name, namesToCheck);

			batchShorthandUpdateState(
				{
					placementModel: model,
					observer
				},
				updateState
			);
		},
		[actionTypes.COMPONENT_DISCONNECTED]: ({ state }) => {
			const { observer } = state;
			if (observer) {
				observer.disconnect();
			}
		}
	},
	properties: {
		elementId: {
			default: null
		},
		name: {
			default: 'default'
		},
		isVisible: { default: true, selectable: true },
		namesToCheck: {
			default: []
		},
		type: {
			default: null
		},
		context: {
			default: null
		},
		disabled: {
			default: false
		}
	},
	styles
});
