import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-dropdown';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import '../../sn-uib-menu-elements';
import '../../sn-uib-menu-header-button';
import SnUIBPageHeaderBackButton from './SnUIBPageHeaderBackButton'; // eslint-disable-line no-unused-vars
import logo from '../assets/now-logo.svg';
import { t } from 'sn-translate';

export default function SnUIBPageHeaderMenu({
	menuItems,
	builderType,
	openedMenu,
	appName,
	isEmptyState,
	parentInfo,
	classes,
	updateState,
	state
}) {
	const setTargetRef = (ref) =>
		setTimeout(() => updateState({ targetRef: ref }));
	return (
		<div
			class={{
				'sn-uib-page-header--menu': true,
				...classes
			}}>
			{builderType === 'Dashboard' ? null : (
				<img src={logo} alt="UI builder logo" className="logo" />
			)}
			<div className="ui-builder-text">
				&nbsp;{t('{0} Builder', builderType)}&nbsp;
			</div>
			{appName && (
				<Fragment>
					<div
						className="ui-builder-experience"
						hook-insert={({ elm }) => setTargetRef(elm)}
						hook-destroy={() => setTargetRef(null)}
						aria-describedby="tooltip1">
						{appName}
					</div>
					<now-tooltip
						id="tooltip1"
						target-ref={state.targetRef}
						content={appName}
						delay={500}
					/>
				</Fragment>
			)}
			{menuItems.map((menuItem) => {
				const [menu, items] = Object.entries(menuItem)[0];
				const opened = openedMenu === menu;
				return (
					<div>
						<now-popover
							hideTail
							manageOpened
							opened={opened}
							append-to-payload={{ menu: menu }}>
							<sn-uib-menu-header-button
								slot="trigger"
								menu={menu}
								className="sn-uib-menu-header-button"
							/>
							<sn-uib-menu-elements slot="content" items={items} />
						</now-popover>
					</div>
				);
			})}
			<SnUIBPageHeaderBackButton
				parentInfo={parentInfo}
				isEmptyState={isEmptyState}
			/>
		</div>
	);
}
