/**
 * WARNING: This file is public API!
 *
 * This file defines the recommended rules as a standalone object to be used
 * when configuring eslint in the browser to support UIB. 2 goals here:
 *
 * - UIB shouldn't have to care about every single rule defined by this plugin.
 *   We want to be able to update the plugin independently and have new rules
 *   enabled by default in UIB. This is similar to CLI usage of eslint, where
 *   plugins can define a shared config, and consumers can extend that config
 *   without configuring individual rules. Just like CLI usage, consumers still
 *   have the ability to override these rules if they don't like the defaults.
 *
 * - We don't want to export the normal plugin configuration (lib/index.js),
 *   because that includes the implementation of all the rules + a lot of
 *   additional fields that we don't need in production. The only thing UIB
 *   needs is a map of rule name to severity. The rules implementations are
 *   handled by a custom eslint build in library-uxf. See ./build/README.md.
 *
 *
 * Also note that when using the CLI configuration, the `extends` property is
 * _not_ importing this file directly:
 *
 *   "extends": "@devsnc/uxf-client-scripts/recommended"
 *
 * will use eslint's configuration resolution magic to read the
 * `config.recommended` object from the main package module (lib/index.js).
 */
const RULES = require("./lib/rules");
const mapKeys = require("./lib/utils/map-keys");
const { PLUGIN_NAME } = require("./lib/constants");

module.exports = mapKeys(RULES, (_, ruleName) => `${PLUGIN_NAME}/${ruleName}`);
