// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import isNil from 'lodash/isNil';
import { validateRepeatedTabData } from '../utils';
import {
	PILLAR_DYNAMIC_ROUTE_MODAL,
	PILLAR_STATIC_ROUTE_MODAL
} from '../constants';

// A static config that drives this component
const COMPONENT_CONFIG = {
	selectable: true // whether or not pillar items are selectable
};

const renderPanelItems = (
	viewportItems,
	appConfig,
	properties,
	staticViewportPanel
) => {
	if (!isEmpty(viewportItems)) {
		return (
			<div className="draggable-list">
				<sn-uib-draggable-list
					size="sm"
					items={viewportItems}
					selectable={COMPONENT_CONFIG.selectable}
					is-editable={false}
				/>
			</div>
		);
	}

	if (staticViewportPanel) {
		return <div className="viewports-no-routes">{t('No tabs, yet')}</div>;
	} else {
		return (
			<div className="validation-container viewports-no-routes">
				{t('No data driven tabs, yet')}
			</div>
		);
	}
};

/**
 * Renders the dynamic data Add+ button
 *
 * @param {Array} repeatedTabData an array of repeated tabs
 * @param {Boolean} disableRouteConfiguration disabled routes
 * @param {Object} properties properties passed to this component
 */
const renderDataDrivenTabAdd = (
	repeatedTabData,
	disableRouteConfiguration,
	properties
) => {
	const { appConfig, viewportDataSource, disabled } = properties || {};

	const dataDrivenViewportError = validateRepeatedTabData(
		repeatedTabData,
		viewportDataSource
	);
	if (!isNil(dataDrivenViewportError) || disableRouteConfiguration) {
		return null;
	}
	return (
		<now-button-bare
			disabled={disabled}
			size="sm"
			variant="tertiary"
			label={t('+ Add')}
			className="insert-route"
			append-to-payload={{
				elementId: appConfig.elementId,
				elementProperties: get(properties, 'viewportElementProperties', {}),
				type: PILLAR_DYNAMIC_ROUTE_MODAL
			}}
		/>
	);
};

const renderDataDrivenErrorState = (repeatedTabData, viewportDataSource) => {
	const dataDrivenViewportError = validateRepeatedTabData(
		repeatedTabData,
		viewportDataSource
	);
	if (!isEmpty(dataDrivenViewportError)) {
		return (
			<div className="validation-container">{dataDrivenViewportError}</div>
		);
	}
};

export default (
	state,
	viewportItems,
	repeatedTabData = [],
	staticViewportPanel = true
) => {
	const { properties } = state;
	const {
		appConfig,
		viewportDataSource,
		viewportPropertiesConfiguration,
		disabled
	} = properties;
	const disableRouteConfiguration = get(
		viewportPropertiesConfiguration,
		'disableRouteConfiguration',
		false
	);
	return (
		<Fragment>
			{!disableRouteConfiguration ? (
				<div className="heading">
					<span>{staticViewportPanel ? t('Tabs') : t('Data driven tabs')}</span>
					{staticViewportPanel ? (
						<now-button-bare
							disabled={disabled}
							size="sm"
							variant="tertiary"
							label={t('+ Add')}
							className="insert-route"
							append-to-payload={{
								elementId: appConfig.elementId,
								elementProperties: get(
									properties,
									'viewportElementProperties',
									{}
								),
								type: PILLAR_STATIC_ROUTE_MODAL
							}}
						/>
					) : (
						renderDataDrivenTabAdd(
							repeatedTabData,
							disableRouteConfiguration,
							properties
						)
					)}
				</div>
			) : null}
			{!staticViewportPanel ? (
				<Fragment>
					<sn-uib-dynamic-property-input
						bindings={appConfig.bindings}
						value={viewportDataSource}
						label={t('Select a data source')}
						/* prettier-ignore */
						description={t('You can create a data driven tab by binding a data resource to the tab.')}
						append-to-payload={{ propertyName: 'dataSourceValue' }}
						isStatic={false}
						disabled={disabled}
					/>
					{renderDataDrivenErrorState(repeatedTabData, viewportDataSource)}
				</Fragment>
			) : null}
			{!disableRouteConfiguration
				? renderPanelItems(
						viewportItems,
						appConfig,
						properties,
						staticViewportPanel
				  )
				: null}
		</Fragment>
	);
};
