import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-uib-layout-template.scss';

const toggleActiveClass = (e) => {
	const getAllInputLabels = Array.from(e.target.parentNode.parentNode.children);
	const activeElement = getAllInputLabels.find((e) =>
		e.className.includes('active')
	);
	if (activeElement) {
		activeElement.classList.toggle('active');
	}
	e.target.parentNode.classList.toggle('active');
};

const labelTemplate = (element, dispatch) => {
	return (
		<label htmlFor={element.id} className="layout-label">
			<input
				id={element.id}
				key={element.id}
				type="radio"
				name="layout"
				value={element.id}
				on-click={(e) => {
					dispatch('SN_UIB_LAYOUT_TEMPLATE#CLICKED', {
						value: e.target.value
					});
					toggleActiveClass(e);
				}}
			/>
			<div className="layout-content">
				<img src={`/${element.image}`} alt={element.name} />
				{t('{0}', element.name)}
			</div>
		</label>
	);
};

const view = (state, dispatch) => {
	const { templates } = state.properties;
	return (
		<div className="layout-template-container">
			<form className="layout-form">
				{templates.map((element) => labelTemplate(element, dispatch))}
			</form>
		</div>
	);
};

export default createCustomElement('sn-uib-layout-templates', {
	view,
	properties: {
		templates: {
			default: []
		}
	},
	styles
});
