import { createCustomElement } from '@servicenow/ui-core';

// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-uib-boolean-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import '../sn-uib-property-description-tooltip';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';

import '@servicenow/now-toggle';

// Dispatched action types
export const BOOLEAN_PROPERTY_VALUE_CHANGED =
	'BOOLEAN_PROPERTY_INPUT#VALUE_CHANGED';

const view = ({
	properties: { label, value, disabled, description, selectedComponentTag }
}) => {
	const booleanizedValue = typeof value === 'string' ? value === 'true' : value;

	return (
		<div className="sn-ui-boolean-property-input">
			<section className="sn-ui-boolean-property-input-toggle-section">
				<now-toggle
					size="sm"
					props-checked={booleanizedValue}
					configAria={{ 'aria-label': label }}
					disabled={disabled}
				/>
			</section>
			<section className="sn-ui-boolean-property-input-label-section">
				<label className="sn-uib-boolean-property-input-label">{label}</label>
				{!description ? null : (
					<sn-uib-property-description-tooltip
						description={description}
						selectedComponentTag={selectedComponentTag}
					/>
				)}
			</section>
		</div>
	);
};

/**
 * An input component to be used for boolean properties configured on the
 * UI Builder properties editor panel
 */
export default createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		editingScript: false
	},
	actionHandlers: {
		'NOW_TOGGLE#CHECKED_SET': ({ dispatch, action }) => {
			/**
			 * The new value of the boolean property is dispatched
			 * @type {boolean}
			 */
			action.stopPropagation();

			dispatch(BOOLEAN_PROPERTY_VALUE_CHANGED, {
				value: action.payload.value
			});
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	},
	styles
});
