import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-content-tree';
import '@servicenow/now-button';
import SearchBox from '../partials/search-box'; // eslint-disable-line no-unused-vars
import {
	componentPropertyChangedHandler,
	nowButtonClickedHandler,
	nowContentTreeClickedHandler,
	nowInputInputHandler,
	nowPopoverOpenedSetHandler,
	nowContentTreeExpandedItemsSetHandler,
	nowContentTreeLoadingRequestedHandler,
	APPLY_BUTTON,
	OPEN_POPOVER_BUTTON,
	SECONDARY_ACTION_CLICKED
} from './actions';

const applyButtonPayload = {
	actionName: APPLY_BUTTON
};

const secondaryActionPayload = {
	actionName: SECONDARY_ACTION_CLICKED
};

const openPopoverButtonPayload = {
	actionName: OPEN_POPOVER_BUTTON
};

import styles from './sn-uib-content-tree-picker.scss';

function SnUIBContentTreePickerPopover(state, { dispatch }) {
	const {
		search,
		items,
		expandedItems,
		selectedItemPath,
		searchLabel,
		searchPlaceholder,
		size,
		displaySecondaryAction,
		secondaryActionLabel,
		hideApplyButton,
		bare
	} = state.properties;

	return (
		<div class={{ 'sn-uib-content-tree-picker-popover': true, bare }}>
			<div class={{ bare, 'search-box': true }}>
				<SearchBox
					label={searchLabel}
					placeholder={searchPlaceholder}
					value={search}
					dispatch={dispatch}
				/>
			</div>
			<div class={{ 'content-tree-container': true, bare }}>
				{items.length ? (
					<now-content-tree
						items={items}
						manage-expanded-items
						overflow="wrap"
						expanded-items={expandedItems}
						manage-selected-items
						selected-items={selectedItemPath}
						search-term={search}
						size={size}
						select="single"></now-content-tree>
				) : (
					<div className="no-items">
						{search
							? t('Your search for "{0}" didn\'t return any results.', search)
							: t('No items available.')}
					</div>
				)}
			</div>
			<div class={{ 'footer-actions': true, 'no-apply': hideApplyButton }}>
				<now-button
					variant="primary"
					label={t('Apply')}
					size={size}
					append-to-payload={applyButtonPayload}
				/>
				{displaySecondaryAction ? (
					<now-button-bare
						variant="primary"
						label={secondaryActionLabel}
						size={size}
						append-to-payload={secondaryActionPayload}
					/>
				) : null}
			</div>
		</div>
	);
}

createCustomElement('sn-uib-content-tree-picker-popover', {
	view: SnUIBContentTreePickerPopover,
	styles,
	properties: {
		items: {
			default: []
		},
		selectedItem: {
			default: undefined
		},
		selectedItemPath: {
			default: []
		},
		search: {
			default: ''
		},
		expandedItems: {
			default: []
		},
		searchLabel: {
			default: ''
		},
		searchPlaceholder: {
			default: ''
		},
		size: {
			default: 'sm'
		},
		displaySecondaryAction: {
			default: false
		},
		secondaryActionLabel: {
			default: ''
		},
		hideApplyButton: {
			default: true
		},
		bare: {
			default: false
		}
	}
});

function SnUIBContentTreePicker(state) {
	const {
		popoverOpened,
		search,
		selectedItem,
		filteredItems,
		expandedItems,
		selectedItemPath
	} = state;

	const {
		currentItem,
		disabled,
		icon,
		size,
		searchLabel,
		searchPlaceholder,
		displaySecondaryAction,
		secondaryActionLabel,
		hideApplyButton,
		bare
	} = state.properties;
	const ButtonCtrl = bare ? 'now-button-bare' : 'now-button'; // eslint-disable-line no-unused-vars
	return (
		<div className="sn-uib-content-tree-picker">
			<now-popover hideTail manageOpened opened={popoverOpened}>
				<ButtonCtrl
					slot="trigger"
					disabled={disabled}
					size={size}
					variant="tertiary"
					configAria={{
						'aria-haspopup': 'listbox',
						'aria-expanded': String(popoverOpened),
						'aria-disabled': String(disabled)
					}}
					append-to-payload={openPopoverButtonPayload}>
					<div class={{ 'sn-uib-content-tree-picker-label': true, bare }}>
						{icon ? <now-icon icon={icon} size={size}></now-icon> : null}
						<span>{currentItem && currentItem.label}</span>
						<now-icon icon="caret-down-fill" size={size}></now-icon>
					</div>
				</ButtonCtrl>
				<sn-uib-content-tree-picker-popover
					slot="content"
					items={filteredItems}
					search={search}
					selectedItem={selectedItem}
					expandedItems={expandedItems}
					searchLabel={searchLabel}
					searchPlaceholder={searchPlaceholder}
					selectedItemPath={selectedItemPath}
					displaySecondaryAction={displaySecondaryAction}
					secondaryActionLabel={secondaryActionLabel}
					hideApplyButton={hideApplyButton}
					bare={bare}
				/>
			</now-popover>
		</div>
	);
}
createCustomElement('sn-uib-content-tree-picker', {
	view: SnUIBContentTreePicker,

	styles,

	properties: {
		/**
		 * List with all the items in a tree format
		 * @type {object}
		 */
		items: {
			default: []
		},

		/**
		 * Current item
		 * @type {object}
		 */
		currentItem: {
			default: {}
		},

		/**
		 * If set, the picker item will be visible, but not selectable by the user.
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},

		/**
		 * Icon to be displayed on the trigger
		 * @type {boolean}
		 */
		icon: {
			default: ''
		},

		/**
		 * Size of inner elements
		 * @type {boolean}
		 */
		size: {
			default: 'sm'
		},

		/**
		 * Label for search input
		 * @type {string}
		 */
		searchLabel: {
			default: ''
		},

		/**
		 * Placeholder for search input
		 * @type {string}
		 */
		searchPlaceholder: {
			default: ''
		},

		/**
		 * Display a secondary action on the right bottom of popover
		 * @type {boolean}
		 */
		displaySecondaryAction: {
			default: false
		},

		/**
		 * Label for secondary action
		 * @type {string}
		 */
		secondaryActionLabel: {
			default: ''
		},
		/**
		 * Hide Apply button and make selection when element selected in the tree
		 * This will only dispatch SN_UIB_CONTENT_TREE_PICKER#TREE_ITEM_CLICKED
		 */
		hideApplyButton: {
			default: false
		},
		bare: {
			default: false
		}
	},

	initialState: {
		popoverOpened: false,
		search: '',
		selectedItem: undefined,
		selectedItemPath: [],
		filteredItems: [],
		expandedItems: []
	},

	dispatches: {
		/**
		 * Dispatched when the apply button is clicked.
		 * Payload contains the item selected.
		 * @type {object}
		 * @public
		 * @example
		 * {
		 * 	id: 'global',
		 * 	label: 'Global'
		 * }
		 */
		'SN_UIB_CONTENT_TREE_PICKER#ITEM_SELECTED': {},
		'SN_UIB_CONTENT_TREE_PICKER#TREE_ITEM_CLICKED': {}
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: componentPropertyChangedHandler,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: componentPropertyChangedHandler,
		'NOW_BUTTON#CLICKED': nowButtonClickedHandler,
		'NOW_BUTTON_BARE#CLICKED': nowButtonClickedHandler,
		'NOW_CONTENT_TREE#ITEM_CLICKED': nowContentTreeClickedHandler,
		'NOW_INPUT#INPUT': nowInputInputHandler,
		'NOW_POPOVER#OPENED_SET': nowPopoverOpenedSetHandler,
		'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET': nowContentTreeExpandedItemsSetHandler,
		'NOW_CONTENT_TREE#LOADING_REQUESTED': nowContentTreeLoadingRequestedHandler
	}
});
