export const config = {
	name: 'sn-uib-c3-property-input',
	properties: {
		/**
		 * The property id sent down from UXF.  It is needed to match against the
		 * current C3 config object to see if rich text changes need to be dispatched
		 * @type {string}
		 * @example 'label'
		 */
		id: { default: null },
		/**
		 * The label of the rich text property
		 * @type {string}
		 * @example 'Widget Description'
		 */
		label: { default: null },
		/**
		 * The current value of the rich text property to be dispatched up
		 * when the value changes in the c3Property config
		 * @type {string}
		 * @example '<p>This is some text for the <strong>value</strong></p>'
		 */
		value: { default: null },
		/**
		 * The config object from the element config data provider that contains
		 * The current state of the c3 modal.
		 * @type {object}
		 * @example {srcUrl: '/$thing.do', initialValue: '<p>Stuff</p>', value: '<p>MOre Stuff</p>', label: 'Description'}
		 */
		c3Config: { default: null },
		/**
		 * The icon used on the c3 launch button
		 * @type {string}
		 * @example 'pencil-outline'
		 */
		icon: { default: null },
		/**
		 * The url that will open in the iframed modal
		 * @type {string}
		 * @example '/@ui_builder_c3_page.do'
		 */
		url: { default: null },
		/**
		 * Optional metadata to be used for C3 properties that require additional config data
		 * @type {object}
		 * @example { table: 'incident' }
		 */
		typeMeta: { default: null },
		/**
		 * Optional badge to display on the c3 launch button
		 * @type {string}
		 * @example '3'
		 */
		badge: { default: null },
		/**
		 * Whether the input should be disabled
		 */
		disabled: { default: false },
		/**
		 * Whether the modal that displays the property config
		 * UX is scrollable
		 * @type {boolean}
		 */
		scrollable: { default: true },
		/**
		 * Optional CSS selector identifying an element in the iframe to focus if
		 * the iframe is focused.
		 * @type {string}
		 */
		redirectFocusTo: { default: null },
		/**
		 * Whether dynamic configuration is enabled.
		 * @type {boolean}
		 */
		dataBindingEnabled: {
			default: true
		},
		/**
		 * the value to default to when the property is in an empty state, eg. input has ""
		 * @type {*}
		 */
		emptyStateBehavior: {
			default: { value: '' }
		},
		/**
		 * Whether the property value is scripted.
		 * @type {boolean}
		 */
		isScripted: {
			default: false
		},
		/**
		 * Template for client transform scripts
		 */
		scriptTemplate: {
			default: ''
		},
		/**
		 * Whether client transform script is enabled.
		 * @type {boolean}
		 */
		scriptingEnabled: {
			default: true
		},
		/**
		 * A short description of what the property does or how it is used
		 * @type {string}
		 */
		description: {},
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {
			default: ''
		}
	}
};
