import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import get from 'lodash/get';
import styles from './sn-uib-menu-elements.scss';
import { SN_UIB_PAGE_CLOSE_MENU } from '../sn-uib-page-header/actions';
import '@servicenow/now-popover';
import './sn-uib-menu-element-section';

const renderNestedChild = (
	childClassName,
	icon,
	label,
	children,
	isHovered,
	updateState
) => {
	return (
		<now-popover
			opened={isHovered}
			manage-opened
			hide-tail
			positions={[
				{
					target: 'top-end',
					content: 'top-start'
				}
			]}>
			<span
				slot="trigger"
				class={childClassName}
				on-click={(e) => e.stopPropagation()}
				on-mouseenter={() => updateState({ isHovered: true })}
				on-mouseleave={(e) => {
					const exitToContent =
						get(e, 'relatedTarget.slot', null) === 'content';
					!exitToContent && updateState({ isHovered: false });
				}}>
				{icon ? <now-icon icon={icon} size="sm" /> : null}
				<span className={`text-label ${icon ? '' : 'no-icon'}`}> {label} </span>
				<now-icon
					icon="caret-right-fill"
					size="md"
					className="carat-right-icon"
				/>
			</span>
			<sn-uib-menu-element-section
				slot="content"
				sectionProps={{ items: children }}
				index={0}
				add-padding={true}
			/>
		</now-popover>
	);
};

const view = ({ properties, isHovered }, { dispatch, updateState }) => {
	const { icon, label, headerAction, children, disabled } = properties;
	const childClassName = {
		['menu-child']: true,
		disabled: disabled
	};
	return children ? (
		renderNestedChild(
			childClassName,
			icon,
			label,
			children,
			isHovered,
			updateState
		)
	) : (
		<Fragment>
			<span
				class={childClassName}
				on-click={(e) => {
					if (!disabled) {
						dispatch(headerAction, { ...e, itemLabel: label });
						dispatch(SN_UIB_PAGE_CLOSE_MENU);
					}
				}}>
				{icon ? <now-icon icon={icon} size="sm" /> : null}
				<span className={`text-label ${icon ? '' : 'no-icon'}`}>{label}</span>
			</span>
		</Fragment>
	);
};

export default createCustomElement('sn-uib-menu-element-child', {
	view,
	initialState: {
		isHovered: false
	},
	properties: {
		icon: {
			default: ''
		},
		label: {
			default: ''
		},
		headerAction: {
			default: ''
		},
		disabled: {
			default: false
		},
		children: {
			default: null
		}
	},
	styles
});
