import '@servicenow/now-progress-bar';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';

const { COMPONENT_DOM_READY } = actionTypes;

export default createCustomElement('sn-uib-progress-bar-wrapper', {
	view: ({ progress, properties: { size } }) => {
		return (
			<now-progress-bar
				pathType="initial"
				size={size}
				value={progress}></now-progress-bar>
		);
	},
	properties: {
		size: {
			default: 'xs'
		}
	},
	initialState: {
		progress: 0.01
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ state, updateState }) => {
			let { progress } = state;
			let rate = 0.1;
			setInterval(() => {
				if (progress >= 0.5) {
					rate = 0.05;
				}
				if (progress >= 0.75) {
					rate = rate / 4;
				}
				if (progress + rate < 1) {
					progress = progress + rate;
					updateState({
						progress
					});
				}
			}, 200);
		}
	}
});
