import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { onNextFrame } from '../helpers/Transition';
import get from 'lodash/get';
import { t } from 'sn-translate';
import styles from './sn-uib-element-controls.scss';
import { filterTemplates } from '../helpers/filterTemplates';

// Actions dispatched
export const ELEMENT_CONFIG_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#CONFIG_BUTTON_CLICKED';
export const ELEMENT_TRASH_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#TRASH_BUTTON_CLICKED';
export const ELEMENT_TRASH_CONFIRM_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#TRASH_CONFIRM_BUTTON_CLICKED';
export const ELEMENT_TRASH_CANCEL_BUTTON_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#TRASH_CANCEL_BUTTON_CLICKED';
export const ELEMENT_FIT_DETECTED = 'SN_UIB_ELEMENT_CONTROLS#FIT_DETECTED';
export const ELEMENT_CONTEXT_TRIGGER_CLICKED =
	'SN_UIB_ELEMENT_CONTROLS#CONTEXT_TRIGGER_CLICKED';

// Button icons
const CONFIG_ICON = 'sliders-outline';
const TRASH_ICON = 'trash-outline';
const CONFIRM_ICON = 'check-outline';
const CANCEL_ICON = 'close-outline';
const COMPONENT_TREE_ICON = 'tree-view-short-outline';

// CSS Classes
const ROOT_CONTAINER_BASE_CLASS = 'uib-element-container';
const CONTROLS_CONTAINER_BASE_CLASS = 'uib-element-controls-container';
const ELEMENT_SLOT_CONTAINER_BASE_CLASS = 'uib-element-slot-container';

const contextMenuPositions = [
	{ target: 'bottom-start', content: 'top-start' },
	{ target: 'bottom-end', content: 'top-end' }
];
const { createFocusRef } = DevxKeyFocusBehavior;
const flexDirectionLabels = {
	column: 'Column',
	row: 'Row',
	'row-reverse': 'Row-reverse',
	'column-reverse': 'Column-reverse',
	inherit: 'Inherit',
	initial: 'Initial',
	unset: 'Unset'
};

const iconFlexDirection = {
	column: 'move-item-down-fill',
	'column-reverse': 'move-item-up-fill',
	row: 'move-item-right-fill',
	'row-reverse': 'move-item-left-fill'
};

const checkContextMenuInfo = (currentContext, currentHovered, info, opened) => {
	const isCorrectOrigin =
		get(info, 'origin', null) === 'sn-uib-element-controls';
	const context = get(info, 'context', null);
	const isCorrectHoveredState = get(info, 'hovered', false) === currentHovered;
	const menuOpened =
		opened &&
		isCorrectHoveredState &&
		isCorrectOrigin &&
		currentContext === context;
	return menuOpened;
};

const getFlexDirectionIcon = (isFlex, flexDirection) => {
	if (!flexDirection && isFlex) {
		return 'move-item-right-fill';
	} else if (flexDirection) {
		return iconFlexDirection[flexDirection];
	} else {
		return null;
	}
};

const getFlexDirection = (isFlex, flexDirection) => {
	if (!flexDirection && isFlex) {
		return 'Row';
	} else if (flexDirection) {
		return flexDirectionLabels[flexDirection];
	} else {
		return null;
	}
};

const getLabel = ({ type, element, selectedSlotName }) => {
	if (type === 'slot') {
		return selectedSlotName;
	} else {
		return element.displayName;
	}
};
export const elementClassFactory = function ({
	properties: { focused, hovered, disabled, confirm, isWorkspace }
}) {
	const classes = {
		container: {
			[ROOT_CONTAINER_BASE_CLASS]: true
		},
		controls: {
			[CONTROLS_CONTAINER_BASE_CLASS]: true
		},
		slot: {
			[ELEMENT_SLOT_CONTAINER_BASE_CLASS]: true
		}
	};
	Object.keys(classes).forEach((key) => {
		classes[key] = {
			...classes[key],
			'--focused': focused,
			'--hovered': !focused && hovered,
			'--confirm': confirm,
			'--disabled': disabled,
			'--legacy': isWorkspace
		};
	});
	return classes;
};

const typeClassFactory = (type, isRoot) => {
	if (isRoot) {
		return {
			'--body': true
		};
	} else {
		return {
			'--slot': type === 'slot',
			'--component': type === 'leaf' || type === 'container'
		};
	}
};
// Renderers

const renderConfigButton = (properties, dispatch) => {
	return (
		<button
			aria-label={t('Open element configuration')}
			class={{
				'uib-element-controls-button': true,
				'uib-element-controls-button__open-config-button': true
			}}
			tabindex="-1"
			ref={createFocusRef(`${properties.element.id}_CONFIG`, {
				Escape: `${properties.element.id}_ROOT`,
				Tab: `${properties.element.id}_TRASH`,
				ShiftTab: `${properties.element.id}_MOVE`
			})}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(ELEMENT_CONFIG_BUTTON_CLICKED, {
					element: properties.element
				});
			}}>
			<now-icon icon={CONFIG_ICON} />
		</button>
	);
};

const renderTrashButton = (properties, dispatch) => {
	return (
		<button
			aria-label={t('Delete component')}
			class={{
				'uib-element-controls-button': true,
				'uib-element-controls-button__delete-button': true
			}}
			disabled={properties.disabled}
			tabindex="-1"
			ref={createFocusRef(`${properties.element.id}_TRASH`, {
				ShiftTab: `${properties.element.id}_CONFIG`,
				Escape: `${properties.element.id}_ROOT`,
				Tab: tabId(
					properties.resizeInfo,
					properties.keymaps,
					properties.element.id
				)
			})}
			on-click={(e) => {
				e.stopPropagation();
				if (properties.disabled) {
					return;
				}

				dispatch(ELEMENT_TRASH_BUTTON_CLICKED, {
					elementId: properties.element.id
				});
			}}>
			<now-icon icon={TRASH_ICON} />
		</button>
	);
};

const renderConfirmButton = (properties, dispatch) => {
	return (
		<button
			aria-label={t('Confirm delete component')}
			class={{
				'uib-element-controls-button': true,
				'uib-element-controls-button__confirm-delete-buttonn': true
			}}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(ELEMENT_TRASH_CONFIRM_BUTTON_CLICKED, {
					elementId: properties.element.id
				});
				dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
					ariaLiveText: t('Element deleted from the workspace')
				});
			}}>
			<now-icon icon={CONFIRM_ICON} />
		</button>
	);
};

const renderCancelButton = (dispatch) => {
	return (
		<button
			aria-label={t('Cancel delete component')}
			class={{
				'uib-element-controls-button': true,
				'uib-element-controls-button__cancel-delete-button': true
			}}
			on-click={(e) => {
				e.stopPropagation();
				dispatch(ELEMENT_TRASH_CANCEL_BUTTON_CLICKED);
			}}>
			<now-icon icon={CANCEL_ICON} />
		</button>
	);
};

const renderSelectParentButton = (properties, dispatch) => {
	const { element, selectedSlotParentComponent, rootElementId } = properties;
	// the parent of the current element
	const elementToSelectId = get(
		element,
		'slot.name',
		properties.selectedSlotParent
	);

	if (elementToSelectId) {
		return (
			<button
				aria-label={t('Select parent component')}
				class={{
					'uib-element-controls-button': true,
					'uib-element-controls-button__select-parent-button': true
				}}
				on-click={(e) => {
					'';
					e.stopPropagation();
					// select the parent element
					const parentElement = get(element, 'slot.parentElement');
					if (element.component && element.component.tag) {
						dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
							type: 'slot',
							id:
								parentElement && parentElement.id
									? `${parentElement.id}-${element.slot.name}`
									: `${rootElementId}-${element.slot.name}`,
							selectedSlotName: elementToSelectId,
							selectedSlotParent: parentElement.id || rootElementId,
							selectedSlotParentComponent: parentElement.component,
							unselectElement: true
						});
					} else {
						dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
							type: 'element',
							elementId: elementToSelectId,
							elementInstanceName: getLabel(properties),
							component: selectedSlotParentComponent
						});
					}
				}}>
				<now-icon icon={COMPONENT_TREE_ICON} />
			</button>
		);
	}
	return null;
};

const renderButton = (type, properties, dispatch) => {
	switch (type) {
		case 'config':
			return renderConfigButton(properties, dispatch);
		case 'trash':
			return renderTrashButton(properties, dispatch);
		case 'confirm':
			return renderConfirmButton(properties, dispatch);
		case 'cancel':
			return renderCancelButton(dispatch);
		case 'parent':
			return renderSelectParentButton(properties, dispatch);
		default:
			return null;
	}
};

const renderGrip = (properties, dispatch) => {
	const dragProps = {
		id: properties.element.id,
		key: properties.element.id,
		element: {
			displayName: properties.element.displayName,
			icon: properties.element.icon || 'building-fill'
		},
		disabled: properties.disabled
	};

	const DragWrapper = properties.disabled // eslint-disable-line no-unused-vars
		? Fragment
		: 'sn-uib-layout-drag-leaf';

	return (
		<DragWrapper {...dragProps}>
			<span
				className="uib-element-controls-drag-grip"
				tabindex="-1"
				ref={createFocusRef(`${properties.element.id}_MOVE`, {
					Escape: `${properties.element.id}_ROOT`,
					Tab: `${properties.element.id}_LAYOUT`
				})}
				on-keydown={(e) => {
					if (properties.disabled) {
						return;
					}

					const { key, defaultPrevented } = e;
					if (key !== 'Enter' || defaultPrevented) {
						return;
					}
					e.preventDefault();
					dispatch('DRAG_LEAF#START_KEYBOARD_DRAG', {
						id: properties.element.id
					});
					onNextFrame(() => {
						dispatch('UIB_PAGE_MANAGEMENT#FOCUS_DND_ENTRYPOINT');
					});
				}}
				aria-label={t('Move Element Button')}
				className="uib-element-controls-drag-grip"
			/>
		</DragWrapper>
	);
};
const renderElementContextTrigger = (state, dispatch) => {
	const { contextMenuOpened, properties } = state;
	const { type, isRoot, element, hovered, disabled } = properties;
	const typeClasses = typeClassFactory(type, isRoot);
	return (
		<button
			aria-label={t('Open element configuration')}
			class={{
				...typeClasses,
				'--active': contextMenuOpened,
				'--hovered': hovered,
				'uib-element-controls-button': true,
				'uib-element-controls-button__element_context_trigger': true,
				'uib-element-controls-menu': true
			}}
			tabindex="-1"
			ref={createFocusRef(`${element.id}_CONFIG`, {
				Escape: `${element.id}_ROOT`,
				Tab: `${element.id}_TRASH`,
				ShiftTab: `${element.id}_MOVE`
			})}
			on-click={(e) => {
				e.stopPropagation();
				if (disabled) {
					return null;
				}

				dispatch(ELEMENT_CONTEXT_TRIGGER_CLICKED, {
					element,
					positionTarget: e.target,
					context: 'default'
				});
			}}
			disabled={disabled}>
			<now-icon icon={'ellipsis-v-fill'} />
		</button>
	);
};

const renderLayoutTrigger = (state) => {
	const { customizedTemplates, standardTemplates, properties } = state;
	const {
		element,
		disabled,
		isRoot,
		type,
		lastAppliedTemplateId,
		hovered,
		isLayoutModified
	} = properties;
	const typeClasses = typeClassFactory(type, isRoot);
	if (type === 'container' && !disabled) {
		return (
			<Fragment>
				<now-popover
					positions={[
						{ target: 'bottom-start', content: 'top-start' },
						{ target: 'bottom-end', content: 'top-end' }
					]}
					class={{
						'uib-element-controls-menu': true,
						...typeClasses,
						'--active': state.layoutPopoverOpened,
						'--hovered': hovered
					}}
					hideTail="true"
					manageOpened="true"
					opened={state.layoutPopoverOpened}>
					<button
						slot={!disabled && 'trigger'}
						aria-label={t('Open layout configuration')}
						class={{
							'uib-element-controls-button': true,
							'uib-element-controls-button__layout_trigger': true
						}}
						tabindex="-1"
						ref={createFocusRef(`${element.id}_LAYOUT`, {
							Escape: `${element.id}_ROOT`,
							Tab: `${element.id}_CONFIG`,
							ShiftTab: `${element.id}_MOVE`
						})}
						disabled={disabled}>
						<now-icon
							className="uib-element-controls-icon"
							icon={'chart-treemap-fill'}
						/>
						<span>{t('Layout')}</span>
						<now-icon icon={'caret-down-fill'} />
					</button>
					<sn-uib-layout-template-panel
						slot="content"
						elementId={element.id}
						templateType={isRoot ? 'page' : 'layout'}
						standardTemplates={standardTemplates}
						customizedTemplates={customizedTemplates}
						disabled={disabled}
						append-to-payload={{ context: 'layoutPopover' }}
						lastAppliedTemplateId={lastAppliedTemplateId}
						isLayoutModified={isLayoutModified}
					/>
				</now-popover>
				<span className="uib-element-controls-divider" />
			</Fragment>
		);
	}
};

const renderFlexTrigger = (state, dispatch) => {
	const {
		flexDirection,
		selectedSlotName,
		element,
		type,
		isFlex,
		isRoot,
		hovered,
		disabled,
		flexDirectionIcon
	} = state.properties;
	const { flexMenuOpened } = state;
	const typeClasses = typeClassFactory(type, isRoot);
	if (type === 'slot' && isFlex) {
		return (
			<Fragment>
				<button
					slot="trigger"
					aria-label={t('Open layout configuration')}
					class={{
						'uib-element-controls-button': true,
						'uib-element-controls-button__flex_trigger': true,
						'uib-element-controls-menu': true,
						...typeClasses,
						'--active': flexMenuOpened,
						'--hovered': hovered
					}}
					tabindex="-1"
					on-click={(e) => {
						dispatch(ELEMENT_CONTEXT_TRIGGER_CLICKED, {
							element: {
								slotName: selectedSlotName,
								elementId: element.elementId
							},
							positionTarget: e.target,
							type: 'flexDirection',
							context: 'flex'
						});
					}}
					ref={createFocusRef(`${element.id}_LAYOUT`, {
						Escape: `${element.id}_ROOT`,
						Tab: `${element.id}_CONFIG`
					})}
					disabled={disabled}>
					{flexDirectionIcon ? (
						<now-icon
							className="uib-element-controls-icon"
							icon={flexDirectionIcon}
						/>
					) : null}
					<span className="uib-element-controls-flex-direction">
						{flexDirection}
					</span>
					<now-icon icon={'caret-down-fill'} />
				</button>
				<span className="uib-element-controls-divider" />
			</Fragment>
		);
	}
};
const tabId = (resizeInfo, keymaps, id) => {
	let target = id;
	const { nextElement } = keymaps || {};
	const left = get(resizeInfo, 'left', null);
	const right = get(resizeInfo, 'right', null);
	if (left) {
		target += '_RESIZE_LEFT';
	} else if (right) {
		target += '_RESIZE_RIGHT';
	} else {
		target = nextElement
			? `${nextElement}_ROOT`
			: 'quick-link-element-configuration-panel';
	}
	return target;
};
const renderNotVisibleWarning = () => {
	return (
		<Fragment>
			<span className="uib-element-controls-divider" />
			<now-icon
				className="uib-element-controls-hidden-icon"
				icon={'eye-slash-fill'}
			/>
		</Fragment>
	);
};
const view = (state, { dispatch }) => {
	const classes = elementClassFactory(state);
	const { properties, fitLastElement } = state;
	const { type, isRoot, element } = properties;
	const isModal = get(element, 'isModal', false);
	const typeClasses = typeClassFactory(type, isRoot);
	const label = getLabel(properties);

	return (
		<article
			id="uib-element-controls"
			on-mouseover={(e) => e.stopPropagation()}
			class={{
				...classes.controls,
				...typeClasses,
				'--fit': fitLastElement
			}}>
			{type !== 'slot' && !isRoot && !isModal
				? renderGrip(properties, dispatch)
				: null}
			<span title={label} className="uib-element-controls-instance-name">
				{label}
			</span>
			<span className="uib-element-controls-buttons">
				<span className="uib-element-controls-divider" />
				{renderFlexTrigger(state, dispatch)}
				{renderLayoutTrigger(state)}
				{renderButton('config', properties, dispatch)}
				{renderButton('parent', properties, dispatch) && properties.focused ? (
					<Fragment>
						<span className="uib-element-controls-divider" />
						{renderButton('parent', properties, dispatch)}
					</Fragment>
				) : null}
				{<span className="uib-element-controls-divider" />}
				{renderElementContextTrigger(state, dispatch)}
				{properties.isVisible ? null : renderNotVisibleWarning()}
			</span>
		</article>
	);
};

export default createCustomElement('sn-uib-element-controls', {
	view,
	properties: {
		elementContextMenuOpened: {
			default: false
		},
		elementContextMenuInfo: {
			default: {}
		},
		selectedSlotStyles: {
			default: {}
		},
		rootElementId: {
			default: null
		},
		selectedSlotName: {
			default: null
		},
		selectedSlotParent: {
			default: null
		},
		selectedSlotParentComponent: {
			default: null
		},
		isRoot: {
			computed: ({ properties: { rootElementId, element } }) => {
				const elementId = element && element.id;
				return rootElementId === elementId;
			}
		},
		isFlex: {
			computed: ({ properties: { selectedSlotStyles } }) => {
				return selectedSlotStyles['display'] === 'flex';
			}
		},
		isLayoutModified: {
			default: false
		},
		isVisible: {
			default: true
		},
		flexDirection: {
			computed: ({ properties: { selectedSlotStyles, isFlex } }) => {
				const flexDirection = getFlexDirection(
					isFlex,
					selectedSlotStyles['flex-direction']
				);
				return flexDirection;
			}
		},
		flexDirectionIcon: {
			computed: ({ properties: { selectedSlotStyles, isFlex } }) => {
				const flexDirectionIcon = getFlexDirectionIcon(
					isFlex,
					selectedSlotStyles['flex-direction']
				);
				return flexDirectionIcon;
			}
		},
		type: {
			default: null
		},
		layoutTemplates: {
			default: []
		},
		pageTemplates: {
			default: []
		},
		lastAppliedTemplateId: {
			default: {}
		},
		focusRefresh: {
			default: {}
		},
		/**
		 * The element object that describes the currently
		 * mounted element
		 * @type {object}
		 * @example - {id: '12345', displayValue: 'Form component'}
		 */
		element: { default: null },
		/**
		 * The focused flag used to denote that the mounted
		 * element is the currently selected element on UIB
		 * stage and is in focus
		 * @type {boolean}
		 */
		focused: { default: false },

		/**
		 * The hovered flag used to denote that the mounted
		 * element is currently being moused over on UIB
		 * stage
		 */
		hovered: { default: false },
		keymaps: { default: {} },

		/**
		 * Whether the element is disabled
		 * @type {boolean}
		 */
		disabled: { default: false },

		/**
		 * Whether or not the element is being delete confirmed
		 * @type {boolean}
		 */
		confirm: { default: false },
		/**
		 * The current state of the resize handles on the related element
		 * @type {object}
		 */
		resizeInfo: { default: null }
	},
	initialState: {
		fitLastElement: false,
		customizedTemplates: [],
		standardTemplates: [],
		layoutPopoverOpened: false,
		flexMenuOpened: false,
		contextMenuOpened: false
	},
	transformState: (state) => {
		const {
			layoutTemplates,
			pageTemplates,
			isRoot,
			elementContextMenuOpened,
			elementContextMenuInfo,
			hovered
		} = state.properties;
		const templatesToFilter = isRoot ? pageTemplates : layoutTemplates;
		const { standardTemplates, customizedTemplates } = filterTemplates(
			templatesToFilter
		);

		const flexMenuOpened = checkContextMenuInfo(
			'flex',
			hovered,
			elementContextMenuInfo,
			elementContextMenuOpened
		);
		const contextMenuOpened = checkContextMenuInfo(
			'default',
			hovered,
			elementContextMenuInfo,
			elementContextMenuOpened
		);

		return {
			...state,
			customizedTemplates,
			standardTemplates,
			flexMenuOpened,
			contextMenuOpened
		};
	},
	actionHandlers: {
		[ELEMENT_TRASH_BUTTON_CLICKED]: ({ properties, action, updateState }) => {
			if (action.payload.elementId === properties.element.id) {
				updateState({ confirm: true });
			}
		},
		[ELEMENT_TRASH_CONFIRM_BUTTON_CLICKED]: ({ updateState }) => {
			updateState({ confirm: false });
		},
		[ELEMENT_TRASH_CANCEL_BUTTON_CLICKED]: ({ updateState }) => {
			updateState({ confirm: false });
		},
		[ELEMENT_CONTEXT_TRIGGER_CLICKED]: ({ dispatch, action, properties }) => {
			const { hovered } = properties;
			const { element, positionTarget, type, context } = action.payload;
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: true });
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_PROPERTIES', {
				actionTarget: positionTarget,
				item: element,
				positions: contextMenuPositions,
				type,
				info: {
					origin: 'sn-uib-element-controls',
					hovered,
					context
				}
			});
		},
		'NOW_POPOVER#OPENED_SET': ({ action, updateState }) => {
			updateState({
				layoutPopoverOpened: action.payload.value
			});
		},
		'TEMPLATE_POPOVER#SET_OPENED': ({ action, updateState }) => {
			updateState({
				layoutPopoverOpened: action.payload.value
			});
		}
	},
	styles
});
