import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import defaultImage from './default-image';
import orderBy from 'lodash/orderBy';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-collapse';
import styles from './sn-uib-layout-template-panel.scss';
import { t } from 'sn-translate';
import { filterElements } from '../helpers/treeFilterUtils.js';
import SearchBox from '../partials/search-box'; // eslint-disable-line no-unused-vars

// eslint-disable-next-line no-unused-vars
const TemplateItem = ({ template, showName }) => {
	const icon = template.isLayoutModified ? 'circle-fill' : 'circle-check-fill';
	return (
		<Fragment>
			{template.image ? (
				<img
					className="sn-uib-template-image"
					alt={template.name || t('Template Icon')}
					src={`/${template.image}`}
				/>
			) : (
				defaultImage()
			)}

			{showName ? (
				<div className="sn-uib-template-name">{template.name}</div>
			) : null}
			{template.isLastAppliedTemplate ? (
				<Fragment>
					<now-icon className="last-applied-template" icon={icon} size="lg" />
					{template.isLayoutModified ? (
						<span className="layout-modified">*</span>
					) : null}
				</Fragment>
			) : null}
		</Fragment>
	);
};

const templateListView = (
	{
		properties: {
			templates,
			title,
			showName,
			templateType,
			elementId,
			disabled,
			isLayoutModified
		},
		expanded
	},
	dispatch
) => {
	return (
		<Fragment>
			<div className="template-section-header">
				<span className="template-section-header-text">{title}</span>

				<now-button-iconic
					icon={expanded ? 'chevron-down-outline' : 'chevron-right-outline'}
					bare={true}
					tooltipContent={expanded ? t('Collapse') : t('Expand')}
					size="sm"
					variant="primary"
					disabled={disabled}
					configAria={{
						button: { 'aria-label': expanded ? t('Collapse') : t('Expand') }
					}}
				/>
			</div>

			<now-collapse expanded={expanded} id="sn-uib-layout-template-standard">
				<div
					className={showName ? '' : 'sn-uib-layout-template-standard-items'}>
					{templates.map((template) => {
						const buttonTitle = template.isLayoutModified
							? t('{0} - code has been modified', template.name)
							: template.name;

						return (
							<button
								class={{
									'sn-uib-layout-template-item': showName === true,
									'sn-uib-layout-standard-item': showName === false,
									selected: template.isLastAppliedTemplate,
									disabled: disabled
								}}
								tabIndex="0"
								title={buttonTitle}
								disabled={disabled}
								on-click={(e) => {
									e.preventDefault();
									if (disabled) {
										return;
									}
									if (templateType === 'layout') {
										dispatch(
											'SN_UIB_ELEMENT_CONFIGURATION_PANEL#LAYOUT_TEMPLATE_SELECTED',
											{
												elementId,
												layoutTemplateId: template.id,
												isLayoutModified: isLayoutModified
											}
										);
									} else if (templateType === 'page') {
										dispatch(
											'SN_UIB_PAGE_CONFIGURATION_PANEL#PAGE_TEMPLATE_SELECTED',
											{
												pageTemplateId: template.id,
												isLayoutModified: isLayoutModified
											}
										);
									}
									dispatch('TEMPLATE_POPOVER#SET_OPENED', { value: false });
								}}>
								<TemplateItem template={template} showName={showName} />
							</button>
						);
					})}
				</div>
			</now-collapse>
		</Fragment>
	);
};

createCustomElement('sn-uib-layout-template-list', {
	view: templateListView,
	initialState: {
		expanded: true
	},
	properties: {
		templates: { default: [] },
		title: { default: '' },
		showName: { default: false },
		elementId: { default: null },
		templateType: { default: null },
		lastAppliedTemplateId: { default: null },
		disabled: { default: false },
		isLayoutModified: { default: false }
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({
			action,
			updateState,
			state: { expanded }
		}) => {
			action.stopPropagation();
			updateState({
				expanded: !expanded
			});
		}
	},
	styles
});

export const layoutTemplatePanelView = (
	{
		properties: {
			elementId,
			filteredStandardTemplates,
			groupedTemplates,
			templateType,
			lastAppliedTemplateId,
			isLayoutModified,
			disabled
		},
		searchTerm
	},
	{ dispatch }
) => {
	return (
		<form className="sn-uib-layout-template-panel">
			<div>
				<div className="template-search">
					<SearchBox
						value={searchTerm}
						placeholder={t('Search')}
						disabled={disabled}
						dispatch={dispatch}
						appendToPayload={{ id: 'template-search' }}>
						{!disabled ? (
							<sn-uib-layout-context-menu slot="end" context={templateType} />
						) : null}
					</SearchBox>
				</div>
			</div>
			<sn-uib-layout-template-list
				templates={filteredStandardTemplates}
				title={t('Standard')}
				showName={false}
				elementId={elementId}
				templateType={templateType}
				lastAppliedTemplateId={lastAppliedTemplateId}
				disabled={disabled}
				className="template-list"
				isLayoutModified={isLayoutModified}
			/>

			{Object.keys(groupedTemplates).map((scopeName) => {
				return (
					<sn-uib-layout-template-list
						templates={groupedTemplates[scopeName]}
						title={scopeName}
						showName={true}
						elementId={elementId}
						templateType={templateType}
						lastAppliedTemplateId={lastAppliedTemplateId}
						disabled={disabled}
						className="template-list"
						isLayoutModified={isLayoutModified}
					/>
				);
			})}
		</form>
	);
};

export default createCustomElement('sn-uib-layout-template-panel', {
	view: layoutTemplatePanelView,
	initialState: {
		searchTerm: ''
	},
	properties: {
		elementId: { default: null },
		layoutTemplates: { default: [] },
		templateType: { default: null },
		standardTemplates: { default: [] },
		customizedTemplates: { default: [] },
		lastAppliedTemplateId: { default: null },
		isLayoutModified: { default: false },
		filteredStandardTemplates: {
			computed({
				searchTerm,
				properties: {
					standardTemplates,
					lastAppliedTemplateId,
					isLayoutModified
				}
			}) {
				const filteredStandardTemplates = filterElements(
					standardTemplates,
					searchTerm,
					'name'
				);

				filteredStandardTemplates.forEach((template) => {
					if (template.id === lastAppliedTemplateId) {
						template.isLastAppliedTemplate = true;
						template.isLayoutModified = isLayoutModified;
					} else {
						template.isLastAppliedTemplate = false;
					}
				});
				return filteredStandardTemplates;
			}
		},
		groupedTemplates: {
			computed({
				searchTerm,
				properties: {
					customizedTemplates,
					lastAppliedTemplateId,
					isLayoutModified
				}
			}) {
				let groupedTemplates = orderBy(customizedTemplates, [
					'scopeName',
					'name'
				]);
				groupedTemplates = filterElements(groupedTemplates, searchTerm, 'name');

				groupedTemplates.forEach((template) => {
					if (template.id === lastAppliedTemplateId) {
						template.isLastAppliedTemplate = true;
						template.isLayoutModified = isLayoutModified;
					} else {
						template.isLastAppliedTemplate = false;
					}
				});

				return groupedTemplates.reduce((acc, template) => {
					if (!acc[template.scopeName]) {
						acc[template.scopeName] = [];
					}
					acc[template.scopeName].push(template);
					return acc;
				}, {});
			}
		},
		disabled: {
			default: false
		}
	},
	actionHandlers: {
		'NOW_INPUT#INPUT': ({ action, updateState }) => {
			action.stopPropagation();
			const { fieldValue, id } = action.payload;

			if (id === 'template-search') {
				updateState({
					searchTerm: fieldValue
				});
			}
		},
		'NOW_BUTTON_BARE#CLICKED': {
			effect: ({ properties, dispatch }) => {
				const { templateType, disabled } = properties;

				if (disabled) {
					return;
				}

				dispatch('SN_UIB_ELEMENT_LAYOUT_BUTTON_OPEN#CLICKED', { templateType });
				dispatch('TEMPLATE_POPOVER#SET_OPENED', { value: false });
			},
			stopPropagation: true
		}
	},
	styles
});
