// TODO: Change the href link of the now-text-link whenever it is published publicly
import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-heading';
import '@servicenow/now-text-link';
import '@servicenow/now-button';
import styles from './sn-uib-splash-page.scss';
import pushingPageImage from './assets/pushing-page-illustration.svg';

export const CHILD_DATA_SET = 'SN_UIB_CHILD#CHILD_DATA_SET';

export const CREATE_PAGE_BUTTON_CLICKED =
	'SN_UIB_SPLASH_PAGE#CREATE_PAGE_BUTTON_CLICKED';

const view = ({
	properties: {
		application,
		showLandingPageInfoLink,
		customTitleText,
		customButtonText,
		isDashboardBuilder,
		selectedPageIsTab
	}
}) => {
	let type = 'page';
	if (selectedPageIsTab) {
		type = 'tab';
	} else if (isDashboardBuilder) {
		type = 'dashboard';
	}
	return (
		<div className="splash-page">
			<div className="splash-page--container">
				<img
					src={pushingPageImage}
					alt={t('{0} being pushed into frame', type)}
					className="splash-page--image"
				/>
				<now-heading
					className="splash-page-heading"
					label={
						customTitleText ||
						t("Let's create a {1} for [{0}]", application, type)
					}
					level="1"
					variant="title-primary"
				/>
				<div className="splash-page--cta">
					<now-button
						label={customButtonText || t('Create a {0}', type)}
						variant={'primary'}
					/>
				</div>
				{showLandingPageInfoLink ? (
					<now-text-link
						label={t('Learn more about landing pages')}
						href="https://hi.service-now.com/kb_view.do?sysparm_article=KB0781943"
						variant="primary"
					/>
				) : null}
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-splash-page', {
	view,
	properties: {
		/**
		 * The name of the current application
		 * @type {string}
		 */
		application: {
			default: 'application'
		},
		/**
		 * Whether the current application type is 'workspace'
		 * @type {boolean}
		 */
		showLandingPageInfoLink: {
			default: false
		},
		customTitleText: {
			default: null
		},
		customButtonAction: {
			default: null
		},
		customButtonText: {
			default: null
		},
		isDashboardBuilder: {
			default: false
		},
		selectedPageIsTab: {
			default: false
		}
	},
	styles,
	actionHandlers: {
		['NOW_BUTTON#CLICKED']: ({ action, dispatch, properties }) => {
			action.stopPropagation();
			if (properties.customButtonAction) {
				dispatch(properties.customButtonAction);
			} else {
				dispatch(CREATE_PAGE_BUTTON_CLICKED);
			}
		},
		[CREATE_PAGE_BUTTON_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(CHILD_DATA_SET, {
				key: 'pageType',
				value: 'landingPage'
			});
		}
	}
});
