import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
import styles from './sn-uib-page-configuration-panel.module.scss';
import isEmpty from 'lodash/isEmpty';
import capitalize from 'lodash/capitalize';
import '@servicenow/now-tabs';
import '../sn-uib-layout-template-panel';
import '../sn-uib-page-properties-configuration-pane';
import '../sn-uib-page-style-configuration-pane';
import '../sn-uib-page-events-configuration-pane';
import {
	extractReferencesFromProperties,
	getValuesFromEventMappingHandlers
} from '../sn-uib-properties-configuration-pane/utils';

const PAGE_CONFIGURATION_PANEL_REFERENCE_DISPLAY_VALUES_REQUESTED =
	'SN_UIB_PAGE_CONFIGURATION_PANEL#REFERENCE_DISPLAY_VALUES_REQUESTED';

const renderTabContent = (state) => {
	const {
		pageProperties,
		disabled,
		dataBindingEnabled,
		bindings,
		pageTemplates,
		screenEventMappings,
		dispatchedEvents,
		eventMappingHandlers,
		eventHandlers,
		interfaceConfigurationComponent,
		appConfig,
		container,
		lastAppliedTemplateId,
		isPageLayoutModified,
		currentTab,
		outOfSyncFields,
		usingSharedPageDefinition,
		isPageDefinitionEventsDisabled,
		isPageDefinitionLayoutDisabled,
		internalEventMappings,
		internalEventMappingsSourceEvents,
		handledEvents
	} = state.properties;

	switch (currentTab) {
		case 'properties':
			return (
				<section className="page-properties-config-pane">
					<sn-uib-page-properties-configuration-pane
						pageProperties={pageProperties}
						dataBindingEnabled={dataBindingEnabled}
						bindings={bindings}
						disabled={disabled}
						out-of-sync-fields={outOfSyncFields}
						using-shared-page-definition={usingSharedPageDefinition}
					/>
				</section>
			);
		case 'style':
			return (
				<section className="page-style-config-pane">
					<sn-uib-page-style-configuration-pane
						pageTemplates={pageTemplates}
						disabled={isPageDefinitionLayoutDisabled}
						lastAppliedTemplateId={lastAppliedTemplateId}
						isPageLayoutModified={isPageLayoutModified}
					/>
				</section>
			);
		case 'events':
			return (
				<section className="page-events-config-pane">
					<sn-uib-page-events-configuration-pane
						screen-event-mappings={screenEventMappings}
						dispatched-events={dispatchedEvents}
						event-mapping-handlers={eventMappingHandlers}
						event-handlers={eventHandlers}
						bindings={bindings}
						interface-configuration-component={interfaceConfigurationComponent}
						app-config={appConfig}
						container={container}
						disabled={disabled}
						is-page-definition-events-disabled={isPageDefinitionEventsDisabled}
						internalEventMappings={internalEventMappings}
						internalEventMappingsSourceEvents={
							internalEventMappingsSourceEvents
						}
						handledEvents={handledEvents}
					/>
				</section>
			);
	}
};

const panelAppendToPayload = {
	panel: 'page-configuration'
};

const panelCollapseToolTip = {
	expanded: t('Hide configuration panel'),
	collapsed: t('Show configuration panel')
};

const tabs = (pagePropertiesEnabled, eventMappingsEnabled) => {
	const propertiesTab = pagePropertiesEnabled
		? {
				id: 'properties',
				icon: 'gear-outline',
				label: t('Config')
		  }
		: {};
	const layoutTab = {
		id: 'style',
		icon: 'chart-treemap-outline',
		label: t('Layout')
	};
	const eventsTab = eventMappingsEnabled
		? {
				id: 'events',
				icon: 'lightning-outline',
				label: t('Events')
		  }
		: {};
	return [propertiesTab, layoutTab, eventsTab].filter((tab) => !isEmpty(tab));
};

const view = (state, { dispatch }) => {
	const {
		pageConfigurationPanelExpanded,
		currentTab,
		pagePropertiesEnabled,
		eventMappingsEnabled,
		itemType
	} = state.properties;
	return (
		<sn-uib-collapsible-panel
			id="collapsible-page-configuration-panel"
			expanded={pageConfigurationPanelExpanded}
			append-to-payload={panelAppendToPayload}
			direction="right"
			heading={t('{0} configuration', capitalize(itemType))}
			heading-icon="sliders-outline"
			collapseTooltip={panelCollapseToolTip}>
			<div
				className="page-config-pane-container"
				hook-insert={({ elm }) =>
					dispatch('SN_UIB_PAGE_CONFIGURATION_PANEL#ELEMENT_INSERTED', elm)
				}>
				<now-tabs
					fixed-width
					manage-selected-item
					selected-item={currentTab}
					items={tabs(pagePropertiesEnabled, eventMappingsEnabled)}
				/>
				<div className="page-config-tab-content-wrapper">
					{renderTabContent(state)}
				</div>
			</div>
		</sn-uib-collapsible-panel>
	);
};

export default createCustomElement('sn-uib-page-configuration-panel', {
	styles,
	view,
	behaviors: [focusBehavior],
	initialState: {
		currentTab: 'properties'
	},
	properties: {
		pageConfigurationPanelExpanded: {
			default: true,
			selectable: true
		},
		pageProperties: {
			default: []
		},
		disabled: {
			default: false
		},
		pageTemplates: {
			default: []
		},
		lastAppliedTemplateId: {
			default: null
		},
		isPageLayoutModified: {
			default: false
		},
		currentTab: {
			default: 'style'
		},
		dataBindingEnabled: {
			default: true,
			selectable: true
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		screenEventMappings: {
			default: []
		},
		dispatchedEvents: {
			default: []
		},
		eventMappingHandlers: {
			default: {}
		},
		eventHandlers: {
			default: []
		},
		interfaceConfigurationComponent: {
			default: {}
		},
		tables: {
			default: [],
			selectable: true
		},
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * The container that properties editor panel property inputs should be constrained within.
		 * @type {HTMLElement}
		 */
		container: {
			default: null
		},
		outOfSyncFields: {
			default: null
		},
		usingSharedPageDefinition: {
			default: false
		},
		isPageDefinitionEventsDisabled: {
			default: false
		},
		isPageDefinitionLayoutDisabled: {
			default: false
		},
		internalEventMappings: {
			default: []
		},
		internalEventMappingsSourceEvents: {
			default: []
		},
		handledEvents: {
			default: []
		},
		isMacroponentReadOnly: {
			default: false
		},
		pagePropertiesEnabled: {
			default: true
		},
		eventMappingsEnabled: {
			default: true
		},
		itemType: {
			default: ''
		},
		referenceDisplayValues: {
			default: {},
			selectable: true
		},
		referenceResults: {
			default: {},
			selectable: true
		},
		referenceSearching: {
			default: {},
			selectable: true
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ dispatch, properties }) => {
			const { eventMappingHandlers = {} } = properties;
			const currentEventMappingHandlerValues =
				getValuesFromEventMappingHandlers(eventMappingHandlers) || [];
			const references =
				extractReferencesFromProperties(currentEventMappingHandlerValues) || [];
			references.length &&
				dispatch(PAGE_CONFIGURATION_PANEL_REFERENCE_DISPLAY_VALUES_REQUESTED, {
					references
				});
		},
		'SN_UIB_LAYOUT_CONTEXT_MENU#RESET_LAYOUT': ({ action, dispatch }) => {
			const { context } = action.payload;
			if (context === 'page') {
				dispatch('SN_UIB_PAGE_CONFIGURATION_PANEL#RESET_LAYOUT');
			}
		},
		'NOW_TABS#SELECTED_ITEM_SET': ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch('SN_UIB_CONFIG_PANEL#TAB_SELECTED', {
				name: action.payload.value
			});
		},
		'SN_UIB_PAGE_CONFIGURATION_PANEL#ELEMENT_INSERTED': ({
			action,
			updateProperties
		}) => {
			action.stopPropagation();
			updateProperties({ container: action.payload });
		}
	}
});
