export const UNSAVED_DIALOG_SAVE_AND_CONTINUE =
	'UNSAVED_DIALOG#SAVE_AND_CONTINUE';
export const UNSAVED_DIALOG_CANCEL = 'UNSAVED_DIALOG#CANCEL';
export const UNSAVED_DIALOG_DISCARD_CHANGES = 'UNSAVED_DIALOG#DISCARD_CHANGES';

const actionMap = {
	primary: UNSAVED_DIALOG_SAVE_AND_CONTINUE,
	secondary: UNSAVED_DIALOG_DISCARD_CHANGES,
	tertiary: UNSAVED_DIALOG_CANCEL
};

export function footerActionHandler({ action, dispatch }) {
	action.stopPropagation();
	const { action: payloadAction } = action.payload;
	dispatch(actionMap[payloadAction.variant]);
}
