import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { onNextFrame } from '../helpers/Transition';
import 'sn-ui-builder-icons';
import styles from './sn-uib-component-item.scss';

const initializeDragAndDrop = (component, dispatch) => {
	dispatch('COMPONENT_ITEM#START_KEYBOARD_NAV', { id: component.id });
	dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
		ariaLiveText: t('{0} selected', component.name)
	});
	onNextFrame(() => {
		dispatch('UIB_PAGE_MANAGEMENT#FOCUS_DND_ENTRYPOINT');
	});
};

export const view = (state, { dispatch }) => {
	const { component, disabled, focusRef, firstItem } = state.properties;

	return (
		<div
			aria-label={`${component.name} component`}
			class={{
				'sn-uib-component-item': true,
				disabled: state.properties.disabled
			}}
			attr-data-interactable-disabled={state.properties.disabled}
			tabindex={firstItem ? 0 : -1}
			role="button"
			aria-describedby="component-item-description"
			on-keydown={(e) => {
				const { key } = e;
				if (!disabled && key === 'Enter') {
					dispatch('COMPONENT_ITEM#CLICKED', { id: component.id });
					initializeDragAndDrop(component, dispatch);
				}
			}}
			// As we have added the role="button" so screen reader would be able to treat it as a button by asking user to use their specific keybinding to click the button. When using screen reader the default action to click a button is treated as a mouse event rather than a keyboard event. Hence the use of on-click event handler.
			// Todo: Move the dispatches in another function
			on-click={(e) => {
				dispatch('COMPONENT_ITEM#CLICKED', { id: component.id });
				if (!disabled && e.detail === 0) {
					dispatch('COMPONENT_ITEM#START_KEYBOARD_NAV', { id: component.id });
					dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
						ariaLiveText: t('{0} selected', component.name)
					});
					onNextFrame(() => {
						dispatch('UIB_PAGE_MANAGEMENT#FOCUS_DND_ENTRYPOINT');
					});
				}
			}}
			onmouseenter={({ target }) => {
				dispatch('SN_UIB_COMPONENT_ITEM#ACTIVE_ITEM_SET', {
					ref: target,
					component
				});
			}}
			onmouseleave={() => {
				dispatch('SN_UIB_COMPONENT_ITEM#ACTIVE_ITEM_SET', {
					ref: null,
					component: null
				});
			}}
			ref={focusRef}>
			<sn-uib-icon
				icon={component.icon}
				style={{ height: '44px', width: '44px' }}
			/>
			<div
				id="component-item-description"
				aria-hidden="true"
				className="component-item-describedby">
				{
					/* prettier-ignore */
					t('Press Enter to select the component. Use your keyboard arrow keys to navigate all components.')
				}
			</div>
			<div className="component-name" aria-label={component.name}>
				{component.name}
			</div>
		</div>
	);
};

// This custom element only exists for use by the drag-and-drop code to render the preview image.
// sn-uib-component-section calls the view function directly.  This greatly decreases the number
// of web components shown in the toolbox and results in faster rendering.
createCustomElement('sn-uib-component-item', {
	view,
	properties: {
		component: {
			default: {}
		},
		focusRef: {},
		disabled: {
			default: false
		},
		firstItem: {
			default: false
		}
	},
	styles
});
