import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { componentList } from '../sn-uib-instance-name-editor/componentList';
import '@servicenow/now-text-link';
import styles from './sn-uib-property-description-tooltip-popover.scss';

export const RELEASE_VERSION = 'quebec';

// Content inside of popover, used by sn-uib-property-description-tooltip
const popoverContent = (
	{ properties: { description, selectedComponentTag, hideTextLink } },
	{ dispatch }
) => {
	const API_URL = `https://developer.servicenow.com/dev.do#!/reference/now-experience/${RELEASE_VERSION}/now-components/${selectedComponentTag}/overview`;
	return (
		<Fragment>
			<div
				className="tooltip-content"
				on-mouseover={() => {
					dispatch('POPOVER_CONTENT_MOUSE_OVER');
				}}
				on-mouseout={() => {
					dispatch('POPOVER_CONTENT_MOUSE_OUT');
				}}
				role="textbox"
				aria-label={description}>
				<span className="tooltip-description">{description}</span>

				{hideTextLink ||
				!selectedComponentTag ||
				!componentList[selectedComponentTag] ? null : (
					<now-text-link
						opensWindow={true}
						label="Learn more"
						href={API_URL}
						variant="secondary"
					/>
				)}
			</div>
		</Fragment>
	);
};

createCustomElement('sn-uib-property-tooltip-content', {
	view: popoverContent,
	properties: {
		/**
		 * description from metadata that goes into the tooltip
		 */
		description: { default: '' },
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {
			default: ''
		},
		hideTextLink: {
			default: false
		}
	},
	actionHandlers: {
		'NOW_TEXT_LINK#CLICKED': {
			stopPropagation: true
		}
	},
	styles
});
