import get from 'lodash/get';

export const dragBehavior = {
	name: 'dragBehavior',
	properties: { draggable: { default: 'true', reflect: true } },
	eventHandlers: [
		{
			events: ['dragstart'],
			effect(coeffects) {
				// isDraggable is set in src/sn-uib-viewport-config-panel/utils.js
				const isDraggable = get(coeffects, 'properties.isDraggable', true);
				if (!isDraggable) {
					return;
				}
				const {
					action: {
						payload: { options, event },
						meta: { host }
					}
				} = coeffects;
				const data = options.getData(coeffects);
				event.dataTransfer.setData('application/json', JSON.stringify(data));
				const draggingElement =
					host.shadowRoot.querySelector('.child-pillar-wrapper') ||
					host.shadowRoot.querySelector('.pillar-wrapper');
				draggingElement.classList.add('drag-pillar-wrapper');
				coeffects.updateState({
					editMode: false
				});
			}
		},
		{
			events: ['dragend'],
			effect({ dispatch, action, properties }) {
				// isDraggable is set in src/sn-uib-viewport-config-panel/utils.js
				const isDraggable = get(properties, 'isDraggable', true);
				if (!isDraggable) {
					return;
				}
				const {
					payload: { options },
					meta: { host }
				} = action;
				const draggingElement =
					host.shadowRoot.querySelector('.child-pillar-wrapper') ||
					host.shadowRoot.querySelector('.pillar-wrapper');
				// Safe check to prevent breaking of draggable list when the draggingElement is null
				if (draggingElement) {
					draggingElement.classList.remove('drag-pillar-wrapper');
				}
				dispatch(options.dragEndAction);
			}
		}
	]
};

export const dropBehavior = {
	name: 'dropBehavior',
	eventHandlers: [
		{
			events: ['dragover'],
			effect(coeffects) {
				const {
					action: {
						payload: { event, options }
					}
				} = coeffects;
				options.onDraggedOver && options.onDraggedOver(coeffects);
				event.preventDefault();
			},
			capture: true
		},
		{
			events: ['drop'],
			effect(coeffects) {
				// isDraggable is set in src/sn-uib-viewport-config-panel/utils.js
				const isDraggable = get(coeffects, 'properties.isDraggable', true);
				if (!isDraggable) {
					return;
				}
				const {
					action: {
						payload: { options, event }
					}
				} = coeffects;

				const data = JSON.parse(event.dataTransfer.getData('application/json'));
				options.onDrop(data, coeffects);
			}
		}
	]
};
