import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-component-information-popover.scss';
import { t } from 'sn-translate';

export const COMPONENT_INFORMATION_POPOVER_CLOSED =
	'SN_UIB_COMPONENT_INFORMATION_POPOVER#POPOVER_CLOSED';
export const RELEASE_VERSION = 'quebec';

const view = ({
	properties: {
		componentTitle,
		selectedComponentTag,
		componentDescription,
		hasLink
	}
}) => {
	return (
		<div className="instance-name-popover-container">
			<div className="header">
				<span className="header-text">{t('{0}', componentTitle)}</span>
				<now-button-iconic bare variant="tertiary" icon="close-fill" />
			</div>
			<div className="body">
				<span>{t('{0}', componentDescription)}</span>
				{hasLink && (
					<now-text-link
						label={t('Learn More')}
						opensWindow={true}
						href={`https://developer.servicenow.com/dev.do#!/reference/now-experience/${RELEASE_VERSION}/now-components/${selectedComponentTag}/overview`}
						download=""
						variant="primary"></now-text-link>
				)}
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-component-information-popover', {
	view,
	styles,
	properties: {
		componentTitle: {
			default: ''
		},
		selectedComponentTag: {
			default: ''
		},
		componentDescription: {
			default: ''
		},
		hasLink: {
			default: ''
		}
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(COMPONENT_INFORMATION_POPOVER_CLOSED);
		},
		'NOW_TEXT_LINK#CLICKED': {
			stopPropagation: true
		}
	}
});
