import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-icon-property-input.scss';
import { config } from './component-config';
import '@servicenow/now-dropdown';
import '../sn-uib-property-input-data-link-toggle';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import '../sn-uib-property-description-tooltip';

export const ICON_PROPERTY_INPUT_PROPERTY_CHANGED =
	'ICON_PROPERTY_INPUT#PROPERTY_CHANGED';
const EMPTY_VALUE = 'none';
const EMPTY_LABEL = 'No icon';

const view = (state) => {
	const {
		internalValue,
		properties: { icons, label, disabled, helperContent, required }
	} = state;
	const items = icons.map(({ name: id, label: lbl }) => ({
		id,
		name: lbl,
		label: lbl,
		icon: lbl
	}));
	items.unshift({ id: EMPTY_VALUE, name: EMPTY_VALUE, label: EMPTY_LABEL });
	return (
		<Fragment>
			<div className="sn-uib-icon-property-input">
				<now-select
					manage-selected-item
					variant="tertiary"
					search="contains"
					items={items || []}
					required={required}
					label={label}
					helper-content={helperContent}
					selected-item={internalValue}
					configAria={{ 'aria-label': label }}
					disabled={disabled}
				/>
			</div>
		</Fragment>
	);
};

createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		internalValue: '',
		editingScript: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ updateState, properties }) => {
			updateState({
				internalValue: properties.value || EMPTY_VALUE
			});
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action,
			properties,
			dispatch,
			updateState
		}) => {
			action.stopPropagation();
			const value = action.payload.value || EMPTY_VALUE;

			if (value && value !== EMPTY_VALUE) {
				dispatch(ICON_PROPERTY_INPUT_PROPERTY_CHANGED, { value });
			} else if (value === EMPTY_VALUE) {
				dispatch(ICON_PROPERTY_INPUT_PROPERTY_CHANGED, {
					value: properties.emptyStateBehavior.value
				});
			}
			updateState({ internalValue: value || EMPTY_LABEL });
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	},
	styles
});
