import { createCustomElement } from '@servicenow/ui-core';
import loaderIcon from './assets/loader.svg';
import styles from './sn-uib-loader.scss';

function generateLoaderIconClasses(size) {
	return {
		'loader-icon': true,
		[size]: true
	};
}

const view = ({ properties: { label, size } }) => {
	return (
		<div className="loader">
			<img
				src={loaderIcon}
				alt="loading icon"
				class={generateLoaderIconClasses(size)}
			/>
			<p className="loader-label">{label}</p>
		</div>
	);
};

export default createCustomElement('sn-uib-loader', {
	view,
	properties: {
		label: {
			default: ''
		},
		size: {
			default: 'lg',
			reflect: true
		}
	},
	styles
});
