import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-content-tree';
import styles from './sn-uib-data-broker-definitions.scss';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars

createCustomElement('sn-uib-db-definitions', {
	view: ({ properties: { dataBrokerDefinitions = [], searchTerm } }) => {
		return !dataBrokerDefinitions || dataBrokerDefinitions.length === 0 ? (
			<EmptyState
				size="md"
				description={t('Select an application to view definitions.')}
			/>
		) : (
			<div className="sn-uib-db-definitions">
				<now-content-tree
					size="sm"
					overflow="wrap"
					trigger-icon="caret"
					expandedItems={dataBrokerDefinitions.map(({ id }) => [id])}
					items={dataBrokerDefinitions}
					searchTerm={searchTerm}
				/>
			</div>
		);
	},
	styles,
	properties: {
		dataBrokerDefinitions: {
			schema: { type: 'array' },
			default: []
		},
		searchTerm: {
			default: ''
		}
	},
	actionHandlers: {
		'NOW_CONTENT_TREE#ITEM_CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch('SN_UIB_DB_DEFINITIONS#DEFINITION_SELECTED', {
				definition: action.payload.item.id
			});
		}
	}
});
