// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import get from 'lodash/get';
import getStyleClassNames from '../helpers/layoutDirection';

export const configureComponent = (dispatch, properties) => {
	const { type } = properties;
	const label =
		type === 'modal' ? t('Configure modal') : t('Configure component');
	return (
		<section>
			<div
				// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
				tabIndex={0}
				className={'button'}
				on-click={() => {
					dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#CONFIG_BUTTON_CLICKED');
				}}>
				<now-icon size="sm" icon={'sliders-outline'} />
				{label}
			</div>
		</section>
	);
};

export const insertBeforeAndAfter = (dispatch, properties) => {
	const { elementSlotStyle = {} } = properties;
	const syleObj = getStyleClassNames(elementSlotStyle);
	const flexDirection =
		(elementSlotStyle && elementSlotStyle['flex-direction']) || 'row';
	return (
		<section>
			<div
				// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
				tabIndex={0}
				className="button"
				on-click={(event) => {
					dispatch(
						'SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENT_BEFORE_OR_AFTER',
						{ positionTarget: event.target, direction: 'left' }
					);
				}}
				on-keydown={(event) => {
					if (event.key === 'Enter') {
						dispatch(
							'SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENT_BEFORE_OR_AFTER',
							{ positionTarget: event.target, direction: 'left' }
						);
					}
				}}>
				<now-icon
					size="sm"
					icon={insertIcon['before'][flexDirection]}
					class={syleObj}
				/>
				<span className="menu-text">{t('Add component before')}</span>
			</div>
			<div
				// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
				tabIndex={0}
				className="button"
				on-click={(event) => {
					dispatch(
						'SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENT_BEFORE_OR_AFTER',
						{ positionTarget: event.target, direction: 'right' }
					);
				}}
				on-keydown={(event) => {
					if (event.key === 'Enter') {
						dispatch(
							'SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENT_BEFORE_OR_AFTER',
							{ positionTarget: event.target, direction: 'right' }
						);
					}
				}}>
				<now-icon
					size="sm"
					icon={insertIcon['after'][flexDirection]}
					class={syleObj}
				/>
				<span className="menu-text">{t('Add component after')}</span>
			</div>
		</section>
	);
};
export const insertSlot = (dispatch) => {
	return (
		<div
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabIndex={0}
			className="button"
			on-click={(event) => {
				dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENET_SLOT', {
					positionTarget: event.target
				});
			}}
			on-keydown={(event) => {
				if (event.key === 'Enter') {
					dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENET_SLOT', {
						positionTarget: event.target
					});
				}
			}}>
			<now-icon size="sm" icon={'circle-plus-outline'} />
			{t('Add component')}
		</div>
	);
};

const iconFlexDirection = {
	column: 'move-item-down-fill',
	'column-reverse': 'move-item-up-fill',
	row: 'move-item-right-fill',
	'row-reverse': 'move-item-left-fill'
};

const insertIcon = {
	before: {
		column: 'add-item-above-outline',
		'column-reverse': 'add-item-below-outline',
		row: 'add-item-left-outline',
		'row-reverse': 'add-item-right-outline'
	},
	after: {
		column: 'add-item-below-outline',
		'column-reverse': 'add-item-above-outline',
		row: 'add-item-right-outline',
		'row-reverse': 'add-item-left-outline'
	}
};

export const flexDirectionButton = (dispatch, label, direction) => (
	<div
		// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
		tabIndex={0}
		className="button"
		on-click={() => {
			dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#CHANGE_FLEX_DIRECTION', {
				flexDirection: direction
			});
		}}
		on-keydown={(event) => {
			if (event.key === 'Enter') {
				dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#CHANGE_FLEX_DIRECTION', {
					flexDirection: direction
				});
			}
		}}>
		<now-icon size="sm" icon={iconFlexDirection[direction]} />
		{label}
	</div>
);

export const changeFlexDirection = (dispatch, properties) => {
	const { isFlex } = properties;
	if (isFlex) {
		return (
			<section>
				<div className="header-flex-direction">
					{t('Change slot flex direction')}
				</div>
				{flexDirectionButton(dispatch, t('Column'), 'column')}
				{flexDirectionButton(dispatch, t('Column reverse'), 'column-reverse')}
				{flexDirectionButton(dispatch, t('Row'), 'row')}
				{flexDirectionButton(dispatch, t('Row reverse'), 'row-reverse')}
			</section>
		);
	} else {
		return null;
	}
};

export const deleteComponent = (dispatch) => {
	return (
		<div
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabIndex={0}
			className="button"
			on-click={() => {
				dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#DELETE_COMPONENT_CLICKED');
			}}>
			<now-icon size="sm" icon={'trash-outline'} />
			{t('Delete component')}
		</div>
	);
};

export const deleteModal = (dispatch, properties) => {
	const elementId = get(properties, 'element.id', null);
	return (
		<div
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabIndex={0}
			className="button"
			on-click={() => {
				dispatch('SN_UIB_ELEMENT_NAVIGATOR#DELETE_MODAL', {
					elementId
				});
			}}>
			<now-icon size="sm" icon={'trash-outline'} />
			{t('Delete modal')}
		</div>
	);
};

export const deleteSlot = () => {
	//TODO: Delete slot
};

export const divider = () => <div className={'element-context-menu-divider'} />;

export const explodeOption = (dispatch, properties) => {
	const isComponentExplodable = get(
		properties,
		'element.component.explodable',
		false
	);
	return isComponentExplodable ? (
		<div
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabIndex={0}
			className="button"
			on-click={() => {
				dispatch('SN_UIB_ELEMENT_CONTROLS#EXPLODE_BUTTON_CLICKED', {
					elementId: properties.element.id
				});
				dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
			}}>
			<now-icon size="sm" icon={'separate-fill'} />
			{t('Break apart component')}
		</div>
	) : null;
};

export const editableOption = (dispatch, properties) => {
	const isComponentEditable = get(
		properties,
		'element.component.isEditable',
		false
	);
	return isComponentEditable ? (
		<div
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabIndex={0}
			className="button"
			on-click={() => {
				dispatch('SN_UIB_ELEMENT_CONTROLS#COMPONENT_EDIT_BUTTON_CLICKED', {
					componentId: get(properties.element, 'component.id', null),
					elementId: get(properties, 'element.id', null)
				});
				dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
			}}>
			<now-icon size="sm" icon={'pencil-page-outline'} />
			{
				/* prettier-ignore */
				t('Edit [{0}]', get(properties, 'element.instanceName', t('Selected element')))
			}
		</div>
	) : null;
};

export const renderPanelContents = (composition, dispatch, properties) => {
	const lastIndex = composition.length - 1;
	return composition.map((itemRenderer, index) => {
		const item = itemRenderer(dispatch, properties, index);
		return (
			<Fragment>
				{item}
				{index !== lastIndex && item ? divider() : null}
			</Fragment>
		);
	});
};

export const defaultComposition = {
	slot: [insertSlot, changeFlexDirection],
	namedSlot: [insertSlot],
	container: [configureComponent, insertBeforeAndAfter, deleteComponent],
	leaf: [
		configureComponent,
		insertBeforeAndAfter,
		explodeOption,
		editableOption,
		deleteComponent
	],
	flexDirection: [changeFlexDirection],
	body: [configureComponent],
	modal: [configureComponent, deleteModal]
};
