import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from './sn-uib-region-empty-slot.scss';
import { droppableBehavior } from '../behaviors/droppable';
import strategyManager from '../helpers/strategyManager';
import { keyboardNavBehavior } from '../behaviors/droppable/utils';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { MOUSEOVER } from '../sn-uib-slot-wrapper';
import ResizeObserver from 'resize-observer-polyfill';
const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	createFocusRef
} = DevxKeyFocusBehavior;
import '../sn-uib-no-interact-wrapper';

const resizeObserver = new ResizeObserver((entries) => {
	for (const entry of entries) {
		const { width, height } = entry.contentRect || entry.contentBoxSize;
		if (entry.target && entry.target.classList) {
			if (width < 200 || height < 200) {
				entry.target.classList.remove('xl');
			}
			if (width >= 200 && height >= 200) {
				entry.target.classList.add('xl');
			}
		}
	}
});

const view = (
	{
		properties: {
			elementId,
			isDragging,
			keymaps,
			activeId,
			slotName,
			isModalDescendant,
			disabled
		},
		behaviors: {
			droppable: { active }
		}
	},
	{ dispatch }
) => {
	const listeners = isDragging
		? keyboardNavBehavior({
				id: elementId,
				dispatch,
				hoverAction: 'UIB_DND_DROPZONE#DRAG_ENTER',
				blurAction: 'UIB_DND_DROPZONE#CLEAR_REGIONS'
		  })
		: {};

	const parent = keymaps ? keymaps.parent : 'quick-link-stage';
	const itemId = `${elementId}-${slotName}`;
	const isActive = active || activeId === itemId;
	const componentAddAltText = !disabled
		? t('Add component')
		: t('Adding components disabled');
	return (
		<div
			hook={{
				insert: ({ elm }) => {
					resizeObserver.observe(elm);
					const { width, height } = elm.getBoundingClientRect();
					if (width >= 200 && height >= 200) {
						elm.classList.add('xl');
					}
				},
				remove: ({ elm }, removeCallback) => {
					resizeObserver.unobserve(elm);
					removeCallback();
				}
			}}
			class={{
				'padding-wrapper': true,
				active: isActive,
				disabled
			}}>
			<div
				class={{
					'region-empty-slot': true,
					'region-drop-zone-active': isActive
				}}
				ref={createFocusRef(`${elementId}_EMPTY`, {
					Escape: `${parent}`
				})}
				hook={{
					insert: ({ elm }) => {
						dispatch('UIB_DND_DROPZONE#MOUNTED', {
							el: elm
						});

						strategyManager.set(elementId, 'emptySlot', elm);
					}
				}}
				{...listeners}
				on-click={(e) => {
					e.stopPropagation();
					dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
						type: 'slot',
						id: itemId,
						selectedSlotParent: elementId,
						selectedSlotName: slotName,

						unselectElement: !isModalDescendant
					});
					dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
						selectedSlotParent: elementId,
						selectedSlotName: slotName,
						toolboxAnchor: {
							// triggers uib-toolbox-anchor's update hook; sets toolboxPositionTarget
							top: e.clientY,
							left: e.clientX
						}
					});
					!disabled && dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
				}}
				title={componentAddAltText}
				on-mouseover={(event) => {
					event.stopPropagation();
					dispatch(MOUSEOVER, { slotName, slotParent: elementId });
				}}>
				{!disabled ? (
					<Fragment>
						<now-icon
							class={{ 'add-button': true }}
							icon={`circle-plus-${isActive ? 'fill' : 'outline'}`}
							size="lg"
						/>
						<div class={{ 'insert-tooltip': true }}>{t('Add component')}</div>
					</Fragment>
				) : null}
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-region-empty-slot', {
	actionHandlers: {
		'UIB_DND_DROPZONE#DROPPED': {
			effect: ({
				action,
				dispatch,
				state: {
					properties: { elementId, slotName }
				}
			}) => {
				const {
					payload: { dataTransfer }
				} = action;

				dispatch('EMPTY_SLOT#DROPPED', {
					dataTransfer,
					parentId: elementId,
					slotName
				});
			}
		}
	},
	actions: {
		'UIB_DND_DROPZONE#DROPPED': {
			stopPropagation: true,
			private: true
		}
	},
	behaviors: [droppableBehavior, keyFocusBehavior],
	properties: {
		elementId: {
			default: null
		},
		activeId: {
			default: null
		},
		pageType: {
			default: null
		},
		slotName: {
			default: null
		},
		isDragging: {
			default: false
		},
		isModalDescendant: {
			default: false
		},
		keymaps: { default: {} },
		disabled: { default: false }
	},
	view,
	styles
});
