import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-uib-client-state-params-pane.scss';
import '../sn-uib-param-row';
import EmptyState from '../partials/empty-state'; //eslint-disable-line no-unused-vars

const view = (state) => {
	const {
		properties: { parameters, bindings, autofocusId, disabled }
	} = state;

	if (!parameters || parameters.length === 0) {
		return (
			<EmptyState
				/* prettier-ignore */
				description={t('No client state parameters have been added to this page yet')}
			/>
		);
	}
	return (
		<div className="params-container">
			{parameters.map(
				({ id = '', name = '', type = '', initialValue = '' }) => {
					let supportedInputs = ['name', 'type', 'value'];
					if (
						typeof initialValue === 'string' &&
						initialValue.startsWith('@')
					) {
						// if the param value is a binding expression, hide the type dropdown
						supportedInputs = ['name', 'value'];
					}
					return (
						<sn-uib-param-row
							key={`param-row-${id}`}
							supported-inputs={supportedInputs}
							supported-param-types={['string', 'number', 'boolean', 'json']}
							id={id}
							name={name}
							type={type}
							initialValue={initialValue}
							bindings={bindings}
							is-focused={autofocusId === id}
							disabled={disabled}
						/>
					);
				}
			)}
		</div>
	);
};

export default createCustomElement('sn-uib-client-state-params-pane', {
	view,
	styles,
	properties: {
		parameters: {
			schema: {
				type: 'array'
			},
			default: []
		},
		bindings: {
			schema: {
				type: 'array'
			},
			default: []
		},
		autofocusId: {
			schema: {
				anyOf: [{ type: 'string' }, { type: 'null' }]
			},
			default: ''
		},
		disabled: { default: false }
	}
});
