import { createCustomElement } from '@servicenow/ui-core';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { t } from 'sn-translate';
import styles from './sn-uib-overrides-tree-popover.scss';
import { getExpandedItems } from '../sn-uib-content-tree-picker/utils';
import '@servicenow/now-loader';
import { FLAT_LIST_ITEM_POPOVER_CLOSED } from '../sn-uib-flat-list-item/actions';

export const OVERRIDES_TREE_OVERRIDE_SELECTED =
	'SN_UIB_OVERRIDES_TREE#OVERRIDE_SELECTED';
export const overridesTreeSearchPayload = { context: 'overrides-tree-search' };

createCustomElement('sn-uib-overrides-tree-popover', {
	view: (state) => {
		const { overridesTreeExpandedItems } = state;
		const {
			currentOverridesTree,
			overridesTreeSearch,
			overridesFetching,
			selectedOverride
		} = state.properties;
		return !isEmpty(currentOverridesTree) &&
			currentOverridesTree &&
			!overridesFetching ? (
			<div className="overrides-tree-container">
				<h3 className="overrides-tree-heading">{t('Domain overrides')}</h3>
				<div className="divider" />
				<now-input
					autofocus
					placeholder={t('Search overrides')}
					append-to-payload={overridesTreeSearchPayload}>
					<now-icon
						slot="start"
						icon="magnifying-glass-outline"
						className="slotted-search-icon"
					/>
				</now-input>
				<div className="divider" />
				<now-content-tree
					items={currentOverridesTree}
					manage-selected-items={true}
					selected-items={[[selectedOverride]]}
					size="sm"
					show-dividers={false}
					expandedItems={
						overridesTreeExpandedItems ||
						getExpandedItems([currentOverridesTree])
					}
					manage-expanded-items
					search-term={overridesTreeSearch}
				/>
				<now-button-iconic
					size="sm"
					icon="close-outline"
					bare
					append-to-payload={{ context: 'overrides-content-close' }}
				/>
			</div>
		) : (
			<div className="overrides-tree-container loader">
				<now-loader label={t('Loading overrides...')} />
			</div>
		);
	},
	initialState: {
		overridesTreeExpandedItems: null
	},
	properties: {
		currentOverridesTree: {
			default: {}
		},
		overridesTreeSearch: {
			default: ''
		},
		overridesFetching: {
			default: false
		},
		dashboardId: {
			default: ''
		},
		selectedOverride: {
			default: null
		}
	},
	actionHandlers: {
		['NOW_BUTTON_ICONIC#CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(FLAT_LIST_ITEM_POPOVER_CLOSED);
		},
		['NOW_CONTENT_TREE#EXPANDED_ITEMS_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ overridesTreeExpandedItems: action.payload.value });
		},
		['NOW_CONTENT_TREE#ITEM_CLICKED']: ({ action, dispatch, properties }) => {
			const { dashboardId, selectedOverride } = properties;
			action.stopPropagation();
			const variantId = get(action.payload, 'item.id', null);
			if (variantId !== selectedOverride) {
				dispatch(OVERRIDES_TREE_OVERRIDE_SELECTED, {
					variantId,
					dashboardId
				});
			}
			dispatch(FLAT_LIST_ITEM_POPOVER_CLOSED);
		}
	},
	styles
});
