import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import debounce from 'lodash/debounce';
import { t } from 'sn-translate';
import styles from './sn-uib-event-mapping-handler-config.scss';
import '@servicenow/now-button';
import '@servicenow/now-content-tree';
import '@servicenow/now-dropdown';
import '@servicenow/now-icon';
import { sortHandlersByType } from '../sn-uib-event-mapping-configuration/utils';
import {
	HANDLER_VALUE_CHANGED,
	HANDLER_VALUE_REFERENCE_SEARCHED,
	HANDLER_VALUE_SCRIPT_CHANGED
} from '../sn-uib-event-mapping-handler-values-config';
import {
	getFilteredHandlers,
	getIconByCategory,
	provideScriptTemplate
} from './utils';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';
import SearchBox from '../partials/search-box'; // eslint-disable-line no-unused-vars

export const EVENT_MAPPING_HANDLER_CHANGED =
	'EVENT_MAPPING_HANDLER_CONFIG#HANDLER_CHANGED';
export const EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED =
	'EVENT_MAPPING_HANDLER_CONFIG#SCRIPTED_VALUE_CHANGED';
export const EVENT_MAPPING_HANDLER_CONFIG_CANCEL_CLICKED =
	'EVENT_MAPPING_HANDLER_CONFIG#CANCEL_CLICKED';
export const EVENT_MAPPING_HANDLER_CONFIG_ADD_CLICKED =
	'EVENT_MAPPING_HANDLER_CONFIG#ADD_CLICKED';
export const EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED =
	'EVENT_MAPPING_HANDLER_CONFIG#REFERENCE_PROPERTY_SEARCHED';

const MODES = {
	SCRIPT: 'script',
	FORM: 'form'
};
const debouncedUpdateState = debounce(
	(updateState, state) => updateState(state),
	250
);
const renderHeading = (event = {}, handlerTypeId = null, selectedHandler) => {
	const unselected = { unselected: !handlerTypeId };
	return (
		<section className="heading">
			<span className="text">
				<p className="main">{t('Event handler preview:')}</p>
				<p>{t('On, {0}', event.label)}</p>
				<now-icon size="sm" icon="arrow-right-fill" class={unselected} />
				<p class={unselected}>
					{handlerTypeId
						? selectedHandler.label
						: t('Select event handler below...')}
				</p>
				{handlerTypeId ? (
					<now-icon size="sm" icon="arrow-right-fill" className="unselected" />
				) : null}
				{handlerTypeId ? (
					<p className="unselected">{t('Enter values below...')}</p>
				) : null}
			</span>
		</section>
	);
};

const view = (state, { updateState, dispatch }) => {
	const { container, searchTerm, selectedMode } = state;
	const {
		appConfig,
		bindings,
		elementId,
		event,
		eventHandlers = [],
		handlerTypeId,
		handlerValues,
		interfaceConfigurationComponent,
		mode,
		scriptTemplate,
		clientTransformScript,
		enableScripting
	} = state.properties;
	const allHandlers = sortHandlersByType(eventHandlers) || [];
	const selectedHandler =
		eventHandlers.find((handler) => handler.id === handlerTypeId) || {};

	return (
		<div className="sn-uib-event-mapping-handler-configuration-container">
			{renderHeading(event, handlerTypeId, selectedHandler)}
			<div className="content">
				<div className="sn-uib-event-mapping-configuration-handler-list-wrapper">
					<SearchBox
						placeholder={t('Search')}
						value={searchTerm}
						manageValue={true}
						appendToPayload={{ search_context: 'eventHandlerSearch' }}
						dispatch={dispatch}
					/>
					<div className="handler-trees">
						{allHandlers.map((category) => {
							return Object.entries(category).map(([id, handlers]) => {
								const icon = getIconByCategory(id);
								const filteredHandlers = getFilteredHandlers(
									searchTerm,
									handlers
								);
								return filteredHandlers.length ? (
									<div>
										<section className="header">
											<now-icon icon={icon} />
											{id}
										</section>
										<now-content-tree
											key={id}
											size="sm"
											overflow="wrap"
											items={filteredHandlers}
											searchTerm={searchTerm}
											selectedItems={handlerTypeId ? [[handlerTypeId]] : []}
											appendToPayload={{ context: 'handlerType' }}
										/>
									</div>
								) : null;
							});
						})}
					</div>
				</div>
				{handlerTypeId ? (
					<div
						className="sn-uib-event-mapping-configuration-handler-details-wrapper"
						hook-insert={({ elm }) => updateState({ container: elm })}>
						<div className="event-mapping-handler-header-section">
							{selectedHandler.label || ''}
							{handlerValues.length && enableScripting ? (
								<section className="event-mapping-handler-mode-section">
									<label>{t('Mode')}</label>
									<now-dropdown
										size="sm"
										variant="tertiary"
										items={[
											{ label: t('Form'), id: MODES.FORM },
											{ label: t('Script'), id: MODES.SCRIPT }
										]}
										selectedItems={[selectedMode]}
									/>
								</section>
							) : (
								''
							)}
						</div>
						<sn-uib-event-mapping-handler-values-config
							key={'sn-uib-event-mapping-handler-values-config'}
							appConfig={appConfig}
							bindings={bindings}
							container={container}
							elementId={elementId}
							handlerValues={handlerValues}
							interfaceConfigurationComponent={interfaceConfigurationComponent}
							enableScripting={enableScripting && selectedMode === MODES.SCRIPT}
							clientTransformScript={
								clientTransformScript ||
								provideScriptTemplate(scriptTemplate, handlerValues)
							}
							clientScriptApiCompletions="@uxf/clientScriptApiCompletions"
						/>
						<div className="event-mapping-handler-apply-button-section">
							<now-button
								variant="tertiary"
								label={t('Cancel')}
								append-to-payload={{ context: 'cancel' }}
							/>
							<now-button
								variant="primary"
								label={mode === 'edit' ? t('Apply') : t('Add')}
								append-to-payload={{ context: 'submit' }}
							/>
						</div>
					</div>
				) : (
					<div className="sn-uib-event-mapping-configuration-empty-state-wrapper">
						<figure>
							<div className="icon-container">
								<now-icon icon="lightning-outline" size="lg" />
							</div>
							<figcaption>
								{t('Select an event handler to configure')}
							</figcaption>
						</figure>
						<div className="event-mapping-handler-apply-button-section">
							<now-button
								variant="tertiary"
								label={t('Cancel')}
								append-to-payload={{ context: 'cancel' }}
							/>
						</div>
					</div>
				)}
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-event-mapping-handler-config', {
	view,
	initialState: {
		container: null,
		searchTerm: '',
		initialHandlerTypeId: null,
		initialHandlerValues: [],
		selectedMode: 'form',
		currentScript: null,
		compatibilityMode: false
	},
	properties: {
		bindings: {
			default: []
		},
		eventHandlers: {
			default: []
		},
		handlerTypeId: {
			default: null
		},
		handlerValues: {
			default: []
		},
		interfaceConfigurationComponent: {
			default: {}
		},
		/**
		 * elementId the element id
		 * @type {string}
		 */
		elementId: {
			default: null
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		mode: {
			computed(state) {
				return state.initialHandlerTypeId ? 'edit' : 'create';
			}
		},
		event: {
			default: null
		},
		clientTransformScript: {
			default: ''
		},
		scriptTemplate: {
			default: ''
		},
		enableScripting: {
			default: true
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ updateState, properties }) => {
			batchShorthandUpdateState(
				{
					initialHandlerTypeId: properties.handlerTypeId,
					initialHandlerValues: properties.handlerValues,
					selectedMode: properties.clientTransformScript
						? MODES.SCRIPT
						: MODES.FORM,
					currentScript: properties.clientTransformScript
				},
				updateState
			);
		},
		[HANDLER_VALUE_CHANGED]: ({ action, dispatch }) => {
			dispatch(EVENT_MAPPING_HANDLER_CHANGED, action.payload);
		},
		[HANDLER_VALUE_SCRIPT_CHANGED]: ({ action, updateState }) => {
			action.stopPropagation();
			batchShorthandUpdateState(
				{
					currentScript: action.payload.script,
					compatibilityMode: action.payload.compatibilityMode
				},
				updateState
			);
		},
		[HANDLER_VALUE_REFERENCE_SEARCHED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(
				EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED,
				action.payload
			);
		},
		['NOW_BUTTON#CLICKED']: ({ action, dispatch, state }) => {
			action.stopPropagation();
			if (action.payload.context === 'submit') {
				if (state.selectedMode === MODES.SCRIPT) {
					for (const { id: initialId } of state.initialHandlerValues) {
						dispatch(EVENT_MAPPING_HANDLER_CHANGED, {
							handlerValueId: initialId,
							value: null, //Reset all form values
							immediate: true
						});
					}
					dispatch(EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED, {
						script: state.currentScript,
						compatibilityMode: state.compatibilityMode
					});
				} else {
					dispatch(EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED, {
						script: null,
						compatibilityMode: false
					});
				}
				dispatch(EVENT_MAPPING_HANDLER_CONFIG_ADD_CLICKED);
			} else if (action.payload.context === 'cancel') {
				if (!state.initialHandlerTypeId) {
					dispatch(EVENT_MAPPING_HANDLER_CONFIG_CANCEL_CLICKED, {
						delete: true
					});
				} else {
					dispatch(EVENT_MAPPING_HANDLER_CONFIG_CANCEL_CLICKED, {
						delete: false
					});
					dispatch(EVENT_MAPPING_HANDLER_CHANGED, {
						handlerTypeId: state.initialHandlerTypeId,
						immediate: true
					});
					for (const {
						id: initialId,
						value: initialValue
					} of state.initialHandlerValues) {
						dispatch(EVENT_MAPPING_HANDLER_CHANGED, {
							handlerValueId: initialId,
							value: initialValue,
							immediate: true
						});
					}
				}
			}
		},
		['NOW_CONTENT_TREE#ITEM_CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();
			action.payload.context === 'handlerType' &&
				dispatch(EVENT_MAPPING_HANDLER_CHANGED, {
					handlerTypeId: action.payload.item.id
				});
		},
		['NOW_INPUT#INPUT']: {
			effect: debounce(({ action, updateState }) => {
				const { search_context, fieldValue: searchTerm } = action.payload;
				search_context === 'eventHandlerSearch' && updateState({ searchTerm });
			}, 250),
			stopPropagation: true
		},
		['NOW_DROPDOWN#SELECTED_ITEMS_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			const {
				payload: { value }
			} = action;
			updateState({ selectedMode: value[0] });
		}
	},
	styles
});
