import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-uib-aria-live.scss';

export const ARIA_LIVE_UPDATE = 'SN_UIB_ARIA_LIVE#CONTENT_UPDATE';

const view = ({
	properties: { ariaLiveText, ariaBusy, ariaLiveType, ariaAtomic, ariaRelevant }
}) => {
	return (
		<Fragment>
			<div
				aria-live={ariaLiveType}
				aria-busy={ariaBusy}
				className="aria-live--hidden"
				aria-atomic={ariaAtomic}
				aria-relevant={ariaRelevant}>
				{ariaLiveText}
			</div>
			<slot />
		</Fragment>
	);
};

createCustomElement('sn-uib-aria-live', {
	view,
	styles,
	properties: {
		ariaLiveText: { default: '' },
		ariaBusy: { default: false },
		ariaLiveType: { default: 'polite' },
		ariaAtomic: { default: false },
		ariaRelevant: { default: 'additions text' }
	},
	actionHandlers: {
		[ARIA_LIVE_UPDATE]: ({ updateProperties, action }) => {
			action.preventDefault();
			action.stopPropagation();
			updateProperties({
				...action.payload
			});
		}
	}
});
