import get from 'lodash/get';
import isEqual from 'lodash/isEqual';

const sanitizeInputValue = (value) => {
	const removeExtraDotsFromStartAndEnd = /^\.|\.$/;
	return value.replace(removeExtraDotsFromStartAndEnd, '');
};

export const getItemListDropdownSize = (
	fakeSpanRef,
	updateState,
	typeaheadDropdownSize
) => {
	//we need extra offset width as we want the dropdown to grow before the items starts displaying
	const extraOffsetToExpand = 80;
	const fakeSpanWidth =
		get(fakeSpanRef, 'current.offsetWidth', 0) + extraOffsetToExpand;
	const currentDropdownSize = { width: fakeSpanWidth };
	if (!isEqual(typeaheadDropdownSize, currentDropdownSize)) {
		updateState({
			typeaheadDropdownSize: currentDropdownSize
		});
	}
};

export const getValuesFromResolvedInputDataSource = ({
	value,
	baseDataSourceItem,
	resolvedInputDataSource
}) => {
	if (value === baseDataSourceItem.label) {
		return resolvedInputDataSource;
	}
	// we are removing base label (ex: @state.something, @data.something) as we would use the substring
	// to get the new object keys from the resolved object.
	const inputValueWithoutBaseDBLabel = value.replace(
		baseDataSourceItem.label,
		''
	);
	const sanitizedInput = sanitizeInputValue(inputValueWithoutBaseDBLabel);
	return get(resolvedInputDataSource, sanitizedInput, null);
};

export const isValueAlreadyPresentInSuggestedBindings = (
	value,
	suggestedDataBindings,
	resolvedInputValue
) => {
	// We are checking if the first property in the resolvedInputValue already exist
	// then it means that we have added the value in the data bindings.
	// Todo: Find a better way to do
	let firstBindingLabel;
	if (!resolvedInputValue) {
		return true;
	}
	if (Array.isArray(resolvedInputValue)) {
		firstBindingLabel = getDataBindingLabel(true, value, 0, null);
	} else if (
		typeof resolvedInputValue === 'object' &&
		resolvedInputValue !== null
	) {
		const resolvedInputKeys = Object.keys(resolvedInputValue);
		firstBindingLabel = getDataBindingLabel(
			false,
			value,
			0,
			resolvedInputKeys[0]
		);
	}

	return !!suggestedDataBindings.find(
		(elem) => elem.label === firstBindingLabel
	);
};

const getDataBindingLabel = (isArray, value, index, property) => {
	const sanitizedValue = sanitizeInputValue(value);
	if (isArray) {
		return `${sanitizedValue}.${index}`;
	}
	return `${sanitizedValue}.${property}`;
};

export const updateDataBindingWithResolvedValues = (
	resolvedInputValue,
	value,
	baseDataSourceItem,
	isTypeArray = false
) => {
	return resolvedInputValue.map((elem, index) => {
		const newBindingLabel = getDataBindingLabel(
			isTypeArray,
			value,
			index,
			elem
		);
		return {
			bindingType: baseDataSourceItem.bindingType,
			label: newBindingLabel,
			id: newBindingLabel
		};
	});
};
