import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-flat-list.scss';
import '../sn-uib-flat-list-item';

const renderItem = (
	item,
	items,
	selectedItems,
	actions,
	disabled,
	actionsDisabled,
	listType,
	displayPopover,
	popoverContent,
	popoverButtonLabel,
	popoverButtonIcon
) => {
	const selected = selectedItems
		? selectedItems.some((s) => s === item.id)
		: false;
	return (
		<sn-uib-flat-list-item
			id={item.id}
			label={item.label}
			secondary-label={item.secondaryLabel}
			actions={actions}
			disabled={item.disabled || disabled}
			actions-disabled={actionsDisabled}
			selected={selected}
			append-to-payload={{ item, items }}
			item={item}
			listType={listType}
			display-popover={displayPopover}
			popover-content={popoverContent}
			popover-button-label={popoverButtonLabel}
			popover-button-icon={popoverButtonIcon}
		/>
	);
};
const view = (state) => {
	const { properties } = state;
	const {
		items,
		selectedItems,
		actions,
		listType,
		itemPopover,
		disabled,
		actionsDisabled
	} = properties;
	return (
		<div className="now-align -column">
			{items.map((item) =>
				renderItem(
					item,
					items,
					selectedItems,
					actions,
					disabled,
					actionsDisabled,
					listType,
					itemPopover && itemPopover.condition(item),
					itemPopover ? itemPopover.renderer(item) : null,
					itemPopover ? itemPopover.buttonLabel : '',
					itemPopover ? itemPopover.buttonIcon : ''
				)
			)}
		</div>
	);
};

createCustomElement('sn-uib-flat-list', {
	view,
	properties: {
		items: {
			default: null
		},
		selectedItems: {
			default: []
		},
		listType: {
			default: ''
		},
		actions: {
			default: []
		},
		itemPopover: {
			schema: {
				type: 'object'
			},
			default: null
		},
		actionsDisabled: {
			default: false
		},
		disabled: {
			default: false
		}
	},
	styles
});
