import { createCustomElement } from '@servicenow/ui-core';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import styles from './sn-uib-db-details.scss';

const getPartials = (selectedDefinition) => {
	if (!selectedDefinition) {
		return {};
	}

	const {
		description: desc,
		inputParams: ip,
		outputSchema: os
	} = selectedDefinition;
	const description = (
		<div className="item">
			<h3>{t('Description')}</h3>
			<p>{desc || t('No description available')}</p>
		</div>
	);
	const inputParams =
		ip && ip.length ? (
			<div className="item">
				<h3>{t('Configuration')}</h3>
				<ul>
					{ip.map(({ label, description: ipd }) => (
						<li>
							<h4>{label}</h4>
							<p>{ipd}</p>
						</li>
					))}
				</ul>
			</div>
		) : null;
	const outputSchema =
		os && os.length ? (
			<div className="item">
				<h3>{t('Output')}</h3>
				<ul>
					{os.map(({ label, fieldType }) => (
						<li>
							<h4>
								{label} <span>({fieldType})</span>
							</h4>
						</li>
					))}
				</ul>
			</div>
		) : null;

	return { description, inputParams, outputSchema };
};

createCustomElement('sn-uib-db-details', {
	view: ({ properties: { selectedDefinition } }) => {
		const { description, inputParams, outputSchema } = getPartials(
			selectedDefinition
		);

		return !selectedDefinition ? (
			<EmptyState size="md" description={t('No data resource selected.')} />
		) : (
			<div className="sn-uib-db-details">
				<div className="items">
					{description}
					{inputParams}
					{outputSchema}
				</div>
			</div>
		);
	},
	properties: {
		selectedDefinition: {
			schema: { type: ['object', 'null'] },
			default: null
		}
	},
	styles
});
