import { createCustomElement } from '@servicenow/ui-core';
import get from 'lodash/get';
import '../sn-uib-dnd-node-provider';
import { pageTreeFlatten } from '../helpers/pageTreeFlatten';
import { simpleLastMemoize } from '../helpers/pageMapNavigator';
import strategyManager from '../helpers/strategyManager';

const clearDragInfo = (coeffects) => {
	const { updateProperties } = coeffects;
	updateProperties({
		isDragging: false,
		keyboardDrag: false,
		activeId: '',
		activeDirection: ''
	});
};
const resetLayout = (coeffects) => {
	const {
		updateProperties,
		dispatch,
		state: {
			properties: { detachedElement, actionNames }
		}
	} = coeffects;
	if (detachedElement) {
		const RESTORE_ACTION = actionNames.RESTORE || 'UIB_DND_PROVIDER#RESTORE';
		dispatch(RESTORE_ACTION);
	}
	updateProperties({
		isDragging: false,
		keyboardDrag: false,
		activeId: '',
		activeDirection: '',
		detachedElement: null
	});
};

const memoizedFlatten = simpleLastMemoize((tree) => {
	const flattened = pageTreeFlatten(tree);
	const ids = Object.keys(flattened)
		.map((key) => {
			return flattened[key];
		})
		.filter((element) => {
			return get(element, 'nodeType', '') === 'layout';
		});
	return ids;
});

const clearActiveDropzones = (coeffects) => {
	const { updateProperties } = coeffects;

	updateProperties({
		activeId: '',
		activeDirection: ''
	});
};
const view = (state, { dispatch }) => {
	const { activeId, activeDirection, layoutNodes } = state.properties;
	strategyManager.set('dnd', 'dispatchFn', dispatch);
	return (
		<div>
			{layoutNodes.map((node) => {
				return (
					<sn-uib-dnd-node-provider
						component-id={`${node.id}_dnd_node`}
						active={node.id === activeId}
						// setting this to only populate if active so activeDirection changes don't cause a rerender
						activeDirection={node.id === activeId ? activeDirection : ''}
					/>
				);
			})}
			<slot />
		</div>
	);
};

export default createCustomElement('sn-uib-dnd-provider', {
	view,
	properties: {
		activeId: {
			default: '',
			selectable: true
		},
		activeDirection: {
			default: '',
			selectable: true
		},
		isDragging: {
			default: false,
			selectable: true
		},
		detachedElement: {
			default: null,
			selectable: true
		},
		draggedElement: {
			default: null,
			selectable: true
		},
		actionNames: {
			default: {}
		},
		keyboardDrag: {
			default: false,
			selectable: true
		},
		layoutNodes: {
			computed({ properties: { pageTree } }) {
				if (!pageTree || !pageTree.root) {
					return [];
				}
				const root = pageTree ? pageTree.root : [];
				const nodes = memoizedFlatten(root);
				return nodes;
			}
		},
		pageTree: {
			default: {}
		}
	},
	actionHandlers: {
		// TODO: move this, and other toolbox, tree/slot/element handling to a new provider element in uib-app
		'UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED': ({ action, dispatch }) => {
			const { id, type, unselectElement } = action.payload;
			if (type === 'slot') {
				dispatch('UIB_DND_DROPZONE#HOVER', { id });
				if (unselectElement) {
					dispatch('UIB_ELEMENT#ELEMENT_SELECTED', { payload: {} });
				}
			} else {
				dispatch('UIB_DND_DROPZONE#HOVER', { id: null });
				dispatch('UIB_ELEMENT#ELEMENT_SELECTED', action.payload);
			}
		},
		'UIB_DND_DROPZONE#HOVER': (coeffects) => {
			const { updateProperties, action } = coeffects;
			const { id, direction } = action.payload;
			updateProperties({
				activeId: id,
				activeDirection: direction
			});
		},
		'UIB_DND_DROPLEAF#HOVER': (coeffects) => {
			const { updateProperties, action } = coeffects;
			const { id, direction } = action.payload;

			updateProperties({
				activeId: id,
				activeDirection: direction
			});
		},
		'EMPTY_SLOT#DROPPED': (coeffects) => {
			const {
				updateProperties,
				dispatch,
				action: {
					payload: { dataTransfer, parentId, slotName }
				},
				state: {
					properties: { detachedElement, draggedElement, actionNames }
				}
			} = coeffects;
			const REATTACH_ACTION =
				actionNames.REATTACH || 'UIB_DND_PROVIDER#REATTACH';
			const PLACE_ACTION = actionNames.PLACED || 'UIB_DND_PROVIDER#PLACE';

			const id = draggedElement || dataTransfer.id || dataTransfer;

			if (detachedElement && detachedElement === id) {
				dispatch(REATTACH_ACTION, {
					parentId,
					slotName
				});
			} else {
				dispatch(PLACE_ACTION, {
					componentId: id,
					parentId,
					slotName
				});
			}
			updateProperties({
				isDragging: false,
				keyboardDrag: false,

				activeId: '',
				activeDirection: ''
			});
		},
		'UIB_DND_DROPZONE#DROPPED': (coeffects) => {
			const {
				updateProperties,
				dispatch,
				action: {
					payload: {
						dataTransfer,
						direction,
						adjacentLayoutElementId,
						elementId,
						parentId
					}
				},
				state: {
					properties: { actionNames, detachedElement }
				}
			} = coeffects;
			const REATTACH_ACTION =
				actionNames.REATTACH || 'UIB_DND_PROVIDER#REATTACH';
			const PLACE_ACTION = actionNames.PLACED || 'UIB_DND_PROVIDER#PLACE';

			if (detachedElement && detachedElement === dataTransfer.id) {
				dispatch(REATTACH_ACTION, {
					parentId,
					adjacentLayoutElementId,
					elementId,
					direction
				});
			} else {
				dispatch(PLACE_ACTION, {
					componentId: dataTransfer,
					parentId,
					adjacentLayoutElementId,
					elementId,
					direction
				});
			}
			updateProperties({
				isDragging: false,
				keyboardDrag: false,
				activeId: '',
				activeDirection: ''
			});
		},
		'UIB_DRAGGABLE_BEHAVIOR#DRAG_START': (coeffects) => {
			const { updateProperties } = coeffects;
			updateProperties({
				isDragging: true,
				keyboardDrag: false
			});
		},
		'COMPONENT_ITEM#START_KEYBOARD_NAV': (coeffects) => {
			const { updateProperties, action } = coeffects;
			const { id } = action.payload;

			updateProperties({
				isDragging: true,
				draggedElement: id,
				keyboardDrag: true
			});
		},
		'UIB_DROPZONE#DROPPED': (coeffects) => {
			const {
				updateProperties,
				dispatch,
				action: {
					payload: { direction, adjacentLayoutElementId, parentId }
				},
				state: {
					properties: { actionNames, detachedElement, draggedElement }
				}
			} = coeffects;
			const REATTACH_ACTION =
				actionNames.REATTACH || 'UIB_DND_PROVIDER#REATTACH';
			const PLACE_ACTION = actionNames.PLACED || 'UIB_DND_PROVIDER#PLACE';

			if (detachedElement && detachedElement === draggedElement) {
				dispatch(REATTACH_ACTION, {
					parentId,
					adjacentLayoutElementId,
					direction
				});
			} else {
				dispatch(PLACE_ACTION, {
					componentId: draggedElement,
					parentId,
					adjacentLayoutElementId,
					direction
				});
			}
			updateProperties({
				isDragging: false,
				activeId: '',
				activeDirection: '',
				keyboardDrag: false
			});
		},
		'UIB_DRAGGABLE_BEHAVIOR#DETACH': (coeffects) => {
			const {
				updateProperties,
				dispatch,
				action,
				state: {
					properties: { actionNames }
				}
			} = coeffects;
			const { id } = action.payload;
			const DETACH_ACTION = actionNames.DETACH || 'UIB_DND_PROVIDER#DETACH';
			dispatch(DETACH_ACTION, { elementId: id });
			updateProperties({
				detachedElement: id
			});
		},
		'DRAG_LEAF#START_KEYBOARD_DRAG': (coeffects) => {
			const {
				updateProperties,
				dispatch,
				action,
				state: {
					properties: { actionNames }
				}
			} = coeffects;
			const { id } = action.payload;
			const DETACH_ACTION = actionNames.DETACH || 'UIB_DND_PROVIDER#DETACH';
			dispatch(DETACH_ACTION, { elementId: id });

			updateProperties({
				isDragging: true,
				detachedElement: id,
				draggedElement: id,
				keyboardDrag: true
			});
		},
		'UIB_DRAGGABLE_BEHAVIOR#DRAG_END': clearDragInfo,
		'UIB_DND_DROPZONE#CLEAR_ACTIVE_DROPZONE': clearActiveDropzones,
		'UIB_DND_DROPZONE#CLEAR_REGIONS': clearActiveDropzones,
		'UIB_DND_DROPZONE#INVALID_DROP': resetLayout,
		'KB_NAV_PROVIDER#INVALID_DROP': resetLayout,
		'UIB_DRAGGABLE_BEHAVIOR#INVALID_DROP': resetLayout
	}
});
