import { createCustomElement } from '@servicenow/ui-core';
import { keyIsHandledCreator, cancel } from './utils';

const view = ({ properties: { keyMap } }) => {
	const keyIsHandled = keyIsHandledCreator(keyMap);
	const keyLookup = ({ key, event }) => {
		if (keyIsHandled(key)) {
			cancel(event);
			keyMap[key].current.focus();
		}
	};

	return (
		<div
			on-keydown={(e) => {
				const { key } = e;
				keyLookup({ key, event: e });
			}}>
			<slot />
		</div>
	);
};

createCustomElement('sn-uib-component-item-focuser', {
	view,
	properties: {
		keyMap: {
			default: {}
		}
	}
});
