import { createCustomElement } from '@servicenow/ui-core';
import { get } from 'lodash';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-popover';

import styles from './sn-uib-element-context-menu.scss';
import {
	renderPanelContents,
	defaultComposition
} from '../partials/sn-uib-element-context-menu';

/* eslint-disable jsx-a11y/no-autofocus */
const panelView = (state, { dispatch }) => {
	const { properties } = state;
	const { composition } = properties;
	return (
		<div className="container">
			{renderPanelContents(composition, dispatch, properties)}
		</div>
	);
};
const view = (state) => {
	const { properties } = state;
	const { opened, positionTarget, element, type, positions } = properties;

	return (
		<now-popover
			manage-opened
			hide-tail
			opened={opened}
			positionTarget={positionTarget}
			positions={positions}
			key={'action-pop-over-elemet-navigator'}
			append-to-payload={{ context: 'context-menu' }}
			constrain={{ width: 256 }}>
			{element ? (
				<element-context-panel
					slot="content"
					composition={
						defaultComposition[element.isNamedSlot ? 'namedSlot' : type]
					}
					{...properties}
				/>
			) : null}
		</now-popover>
	);
};

createCustomElement('element-context-panel', {
	view: panelView,
	styles,
	properties: {
		composition: { default: [] },
		element: { default: null },
		elementSlotStyle: { default: null },
		isFlex: { default: false },
		type: { default: null }
	}
});
createCustomElement('sn-uib-element-context-menu', {
	view,
	styles,
	properties: {
		header: { default: '' },
		elementSlotStyle: { default: null },
		actions: { default: [] },
		opened: { default: false },
		element: { default: null },
		positionTarget: { default: null },
		positions: { default: null },
		type: { default: null },
		isFlex: { default: false }
	},
	actionHandlers: {
		'NOW_POPOVER#OPENED_SET': ({ action, dispatch }) => {
			const { context } = action.payload;
			switch (context) {
				case 'context-menu':
					return dispatch(
						'UIB_ELEMENT_CONTEXT_MENU#SET_OPENED',
						action.payload
					);
				default:
					break;
			}
		},
		'SN_UIB_ELEMENT_CONTEXT_MENU#CONFIG_BUTTON_CLICKED': ({
			dispatch,
			state
		}) => {
			const { element, type } = state.properties;
			const {
				elementId,
				selectedSlotParentComponent,
				slotName,
				component,
				id
			} = element;
			if (type === 'slot') {
				dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#CONFIGURE_COMPONENT', {
					type: 'slot',
					selectedSlotName: slotName,
					selectedSlotParent: elementId || id,
					selectedSlotParentComponent,
					unselectElement: true
				});
			} else {
				dispatch('SN_UIB_ELEMENT_CONTEXT_MENU#CONFIGURE_COMPONENT', {
					type: 'element',
					elementId: elementId || id,
					component
				});
			}
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
		},
		'SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENET_SLOT': ({
			action,
			dispatch,
			state
		}) => {
			const { positionTarget } = action.payload;
			const { element } = state.properties;
			const selectedSlotParent = element.elementId;
			const selectedSlotName = element.slotName;
			dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
				selectedSlotParent,
				selectedSlotName,
				hideTail: true,
				onSelect: ({ dispatch, component }) => {
					if (selectedSlotName && selectedSlotParent) {
						dispatch('EMPTY_SLOT#DROPPED', {
							slotName: selectedSlotName,
							parentId: selectedSlotParent,
							dataTransfer: component
						});
					}
					dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
				}
			});
			dispatch('UIB_TOOLBOX#SET_POSITION', {
				toolboxPositionTarget: positionTarget
			});
			dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
		},
		'SN_UIB_ELEMENT_CONTEXT_MENU#INSERT_COMPONENT_BEFORE_OR_AFTER': ({
			action,
			dispatch,
			state
		}) => {
			const { direction, positionTarget } = action.payload;
			const { element } = state.properties;
			dispatch('SN_UIB_ELEMENT_PLACE_CONTROL#ADD_REQUESTED', {
				elementId: element.id,
				direction
			});
			dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
				elementId: element.id,
				elementInstanceName: element.instanceName
			});
			dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
				selectedSlotParent: null,
				selectedSlotName: null,
				hideTail: true,
				onClose: ({ dispatch }) => {
					dispatch('SN_UIB_ELEMENT_PLACE_CONTROL#CANCEL_ADD_REQUESTED');
				},
				onSelect: ({ dispatch, component }) => {
					dispatch('UIB_DND_DROPZONE#DROPPED', {
						dataTransfer: component.id,
						elementId: element.id,
						direction
					});
				}
			});
			dispatch('UIB_TOOLBOX#SET_POSITION', {
				toolboxPositionTarget: positionTarget
			});
			dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
		},
		'SN_UIB_ELEMENT_CONTEXT_MENU#DELETE_COMPONENT_CLICKED': ({
			dispatch,
			state
		}) => {
			const { element } = state.properties;
			const parentModalId = get(element, 'parentModalId', null);
			//if there is a modal parent select the empty container in the modal after deletion
			dispatch('SN_UIB_COMPONENT_TREE_PANEL#ELEMENT_DELETED', {
				elementId: element.id,
				elementToSelect: parentModalId,
				name: element.displayName || element.label
			});
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
		},
		'SN_UIB_ELEMENT_CONTEXT_MENU#CHANGE_FLEX_DIRECTION': ({
			action,
			dispatch,
			state
		}) => {
			const { element } = state.properties;
			const { flexDirection } = action.payload;
			dispatch('UIB_ELEMENT_CONTEXT_MENU#CHANGE_FLEX_DIRECTION', {
				element,
				flexDirection
			});
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
		}
	}
});
