import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom, { createRef } from '@servicenow/ui-renderer-snabbdom';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { t } from 'sn-translate';
import get from 'lodash/get';
import styles from './_sn-uib-resize-handle.scss';
import { attachDrag } from './listeners';
import { TOP, BOTTOM, LEFT, RIGHT, EXPAND, SHRINK } from './helpers';

import '@servicenow/now-icon';

const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	createFocusRef
} = DevxKeyFocusBehavior;

const { COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED } = actionTypes;
const RESIZE_ELEMENT = 'UIB_RESIZE_HANDLE#RESIZE';
const drags = new Map();

const convertResizeUnit = {
	roundOffPercentage(size) {
		return Math.round((size / 12) * 100);
	},
	resizeTypePercentage(payload) {
		const {
			resizeType,
			layoutInfo: { size }
		} = payload;
		if (resizeType === SHRINK) {
			return this.roundOffPercentage(parseInt(size) - 1);
		} else {
			return this.roundOffPercentage(parseInt(size) + 1);
		}
	}
};

const validDirectionsForSide = {
	top: {
		[TOP]: EXPAND,
		[BOTTOM]: SHRINK
	},
	bottom: {
		[TOP]: SHRINK,
		[BOTTOM]: EXPAND
	},
	left: {
		[LEFT]: EXPAND,
		[RIGHT]: SHRINK
	},
	right: {
		[LEFT]: SHRINK,
		[RIGHT]: EXPAND
	}
};
const keyResize = ({
	dispatch,
	direction,
	state: {
		properties: { side }
	}
}) => {
	if (validDirectionsForSide[side] && validDirectionsForSide[side][direction]) {
		const resizeType = validDirectionsForSide[side][direction];
		dispatch('UIB_RESIZE_HANDLE#RESIZE_KEYPRESS', {
			resizeType
		});
	}
};

const keyLookup = {
	ArrowLeft: ({ ...args }) => keyResize({ ...args, direction: LEFT }),
	ArrowRight: ({ ...args }) => keyResize({ ...args, direction: RIGHT }),
	ArrowUp: ({ ...args }) => keyResize({ ...args, direction: TOP }),
	ArrowDown: ({ ...args }) => keyResize({ ...args, direction: BOTTOM })
};

const positionLookup = {
	[TOP]: {
		width: '100%',
		position: 'absolute',
		top: 0
	},
	[BOTTOM]: {
		width: '100%',
		position: 'absolute',
		bottom: 0
	},
	[LEFT]: {
		height: '100%',
		position: 'absolute',
		left: 0
	},
	[RIGHT]: {
		height: '100%',
		position: 'absolute',
		right: 0
	}
};
const syncedProps = [
	'side',
	'id',
	'keymaps',
	'layoutInfo',
	'resizeActionName',
	'dragId'
];
const syncProp = (id, name, value) => {
	let update = { [name]: value };
	const existingInfo = drags.get(id) || {};

	if (name === 'layoutInfo') {
		const { size, maxSize } = value;
		update = { size, maxSize, [name]: value };
		if (
			name === 'layoutInfo' &&
			get(value, 'size', null) !== get(existingInfo, 'size', null)
		) {
			update.awaitingUpdate = false;
		}
	}
	drags.set(id, { ...existingInfo, ...update });
};
const view = (state, dispatch) => {
	const {
		properties: { side, elementId, keymaps, layoutInfo, dragId },
		componentId
	} = state;

	const { size } = layoutInfo;

	const existingDragInfo = drags.get(dragId) || {};
	const dragRef = createRef();

	existingDragInfo.ref = dragRef;
	/**
	 * This map is being used because when this was being developed there seemed to be some issues
	 * where however the elements would be rerendered after a successful "resize" it would invalidate all the previous refs/dispatch, and all that.
	 * This only really happened with elements nested in another container. But without some of these hacks I would run into refs/elements not being connected.
	 * Especially when dragging quickly.
	 */
	drags.set(dragId, existingDragInfo);

	const ref = createFocusRef(dragId, {
		...keymaps
	});

	return (
		<div
			className={`wrapper ${side}`}
			tabindex="-1"
			ref={dragRef}
			on-focus={(e) => {
				e.preventDefault();
				ref.current && ref.current.focus();
			}}>
			<div
				data-size={size}
				className={`resizer ${side}`}
				hook-insert={({ elm }) => {
					const existing = drags.get(dragId) || {};
					drags.set(dragId, { ...existing, el: elm });

					attachDrag({
						edge: side,
						elementId,
						dragId,
						componentId,
						drags
					});
				}}>
				<div
					className="handle-wrapper"
					tabindex="0"
					ref={ref}
					on-keydown={(event) => {
						const { key, defaultPrevented } = event;
						const lookup = keyLookup[key];
						if (!lookup || defaultPrevented) {
							return;
						}
						event.preventDefault();
						lookup({ event, dispatch, state });
					}}>
					<div className="handle" />
				</div>
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-resize-handle', {
	properties: {
		side: { default: null },
		elementId: { default: null },
		keymaps: { default: {} },
		dragId: {
			computed({ properties: { elementId, side } }) {
				return `${elementId}_RESIZE_${side.toUpperCase()}`;
			}
		},
		wrapperRef: {},
		element: { default: null },
		resizeActionName: { default: RESIZE_ELEMENT },
		layoutInfo: {
			default: {}
		},
		size: {
			default: 0
		}
	},
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [keyFocusBehavior],
	actions: {
		'UIB_RESIZE_HANDLE#RESIZE_KEYPRESS': {
			private: true
		}
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: (coeffects) => {
			const {
				action: {
					payload: { name, value }
				},
				properties: { dragId }
			} = coeffects;

			if (!dragId) {
				return;
			}
			if (syncedProps.includes(name)) {
				syncProp(dragId, name, value);
			}
		},
		[COMPONENT_DOM_READY]: (coeffects) => {
			const {
				properties: {
					side,
					elementId,
					keymaps,
					layoutInfo,
					resizeActionName,
					dragId
				},
				action: {
					payload: { host }
				},
				dispatch
			} = coeffects;
			const style = positionLookup[side] || {};
			Object.keys(style).forEach((key) => {
				host.style[key] = style[key];
			});
			const existingDragInfo = drags.get(dragId) || {};
			drags.set(dragId, {
				...existingDragInfo,
				side,
				elementId,
				keymaps,
				size: layoutInfo.size,
				maxSize: layoutInfo.maxSize,
				layoutInfo,
				resizeActionName,
				dragId,
				dispatch
			});
		},

		'UIB_RESIZE_HANDLE#RESIZE_KEYPRESS': (coeffects) => {
			const {
				state: {
					properties: { side, layoutInfo, resizeActionName, elementId }
				},
				action: {
					payload: { resizeType }
				},
				dispatch
			} = coeffects;

			dispatch(resizeActionName, {
				side,
				direction: resizeType,
				id: layoutInfo.id,
				elementId: layoutInfo.owner
			});
			// Below logic prevents dispatching of aria live to reach the max and min.
			// Screenreader wouldn't read "Component Resized" to "100% or Component Resized to 0%"
			if (
				(layoutInfo.size < layoutInfo.maxSize && resizeType === EXPAND) ||
				(layoutInfo.size > layoutInfo.minSize && resizeType === SHRINK)
			) {
				dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
					/* prettier-ignore */
					ariaLiveText: t('Component resized to {0}%', convertResizeUnit.resizeTypePercentage({ resizeType, layoutInfo }))
				});
			}
			dispatch('UIB_RESIZE_HANDLE#RESIZED', {
				resizer: `${elementId}_RESIZE_${side.toUpperCase()}`
			});
		}
	},
	styles
});
