import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { flattenLayout } from './layout-utils';
import memoize from 'lodash/memoize';
const flattenMem = memoize(flattenLayout);

// TODO: Refactor this out using pageTree
function isLeaf(layout, id) {
	if (!layout) {
		return false;
	}
	const flattened = flattenMem(layout.root);

	const layoutElement = flattened[id];
	return !!(layoutElement && layoutElement.slotName);
}

export const columnRenderer = ({
	children,
	id,
	layout,
	elementId,
	className
}) => {
	if (isLeaf(layout, id)) {
		return (
			<div className={className} style={{}}>
				<sn-uib-element-dnd-wrapper
					id={id}
					key={id}
					parentId={elementId}
					activeDirection={'@dnd-provider/activeDirection'}
					isDragging={'@dnd-provider/isDragging'}
					detachedElement={'@dnd-provider/detachedElement'}
					keyboardDrag={'@dnd-provider/keyboardDrag'}
					type="leaf"
					keymaps={`@${id}_information/drag`}
					activeId={'@dnd-provider/activeId'}>
					{children}
				</sn-uib-element-dnd-wrapper>
			</div>
		);
	} else {
		/* eslint-disable-next-line no-unused-vars */
		const Wrapper = id ? 'sn-uib-element-dnd-wrapper' : Fragment;

		return (
			<div className={className} style={{}}>
				<Wrapper
					id={id}
					key={id}
					activeDirection={'@dnd-provider/activeDirection'}
					is-dragging={'@dnd-provider/isDragging'}
					parentId={elementId}
					keyboardDrag={'@dnd-provider/keyboardDrag'}
					type="col"
					activeId={'@dnd-provider/activeId'}
					keymaps={`@${id}_information/drag`}>
					{children}
				</Wrapper>
			</div>
		);
	}
};

export const rowRenderer = ({ children, id, layout, elementId, className }) => {
	if (!isLeaf(layout, id) && id) {
		return (
			<sn-uib-element-dnd-wrapper
				id={id}
				key={id}
				activeDirection={'@dnd-provider/activeDirection'}
				is-dragging={'@dnd-provider/isDragging'}
				type="row"
				parentId={elementId}
				keyboardDrag={'@dnd-provider/keyboardDrag'}
				style={{ width: '100%' }}
				activeId={'@dnd-provider/activeId'}
				keymaps={`@${id}_information/drag`}>
				<div className={className} style={{}}>
					{children}
				</div>
			</sn-uib-element-dnd-wrapper>
		);
	}

	return (
		<div className={className} style={{}}>
			{children}
		</div>
	);
};
export const rowColRenderers = { row: rowRenderer, column: columnRenderer };
