import { NAVIGATE_TO_VIEWPORT_SCREEN } from '../actions.js';

import { generateDataDrivenTabPayload, updateRootContainerId } from '../utils';
import {
	SN_UIB_PILLAR_WRAPPER_EXPANDED,
	SN_UIB_PILLAR_WRAPPER_ACTION_CLICKED,
	SN_UIB_PILLAR_WRAPPER_CLICKED
} from '../../sn-uib-draggable-list/sn-uib-pillar-wrapper';
import { batchShorthandUpdateState } from '../../helpers/batchShorthandUpdateState';

export default {
	[SN_UIB_PILLAR_WRAPPER_ACTION_CLICKED]: ({
		action: { stopPropagation, payload },
		properties: { elementRef },
		dispatch
	}) => {
		stopPropagation();
		const { itemId, rootContainerId, itemProperties } = payload;
		const updatedRootContainerId = updateRootContainerId(
			rootContainerId,
			itemProperties
		);
		if (payload.actionId === NAVIGATE_TO_VIEWPORT_SCREEN) {
			const { width } = elementRef
				? elementRef.current.getBoundingClientRect()
				: {};
			dispatch('SN_UIB_VIEWPORT#ISOLATED_EDIT', {
				routeId: updatedRootContainerId,
				screenId: itemId,
				width
			});
		} else if (payload.actionId === 'shared-route') {
			dispatch('UIB_APP#SHOW_SHARED_ROUTE_ALERT');
		}
	},
	[SN_UIB_PILLAR_WRAPPER_EXPANDED]: ({
		action: {
			stopPropagation,
			payload: { id, isExpanded }
		},
		state,
		updateState
	}) => {
		stopPropagation();
		const { collapsedItems } = state;
		collapsedItems[id] = isExpanded;
		batchShorthandUpdateState(
			{
				...state,
				collapsedItems
			},
			updateState
		);
	},
	[SN_UIB_PILLAR_WRAPPER_CLICKED]: ({ action, properties, dispatch }) => {
		action.stopPropagation();
		const { id, rootContainerId, item, itemProperties } = action.payload;
		const updatedRootContainerId = updateRootContainerId(
			rootContainerId,
			itemProperties
		);
		const { viewportRoutes, repeatedTabData } = properties;
		const dataDrivenTabInfo = generateDataDrivenTabPayload(
			item,
			repeatedTabData
		);
		dispatch('UIB_VIEWPORT#RENDER_REQUESTED', {
			id,
			parentId: updatedRootContainerId,
			viewportRoutes,
			dataDrivenTabInfo
		});
	}
};
