import focusBehavior from '@servicenow/behavior-focus';
import { createCustomElement } from '@servicenow/ui-core';
import 'sn-ui-builder-icons';
import '../';
import styles from './sn-uib-data-configuration-shelf.scss';
import '../sn-uib-scripts-configuration-panel';

const renderTargetDataConfigPanel = (state) => {
	const {
		selectedDataTab,
		clientTransformScriptTemplates = {},
		clientStateParameters,
		clientStateData,
		bindings,
		scripts,
		disabled,
		isScriptsBusy,
		eventHandlerElementMap,
		isMacroponentReadOnly
	} = state.properties;

	switch (selectedDataTab) {
		case 'data-brokers':
			return (
				<sn-uib-data-broker-configuration-panel
					{...state.properties}
					clientTransformScriptTemplate={clientTransformScriptTemplates.EVENT}
				/>
			);
		case 'client-state':
			return (
				<sn-uib-client-state-configuration-panel
					disabled={disabled}
					is-macroponent-read-only={isMacroponentReadOnly}
					clientStateParameters={clientStateParameters}
					clientStateData={clientStateData}
					bindings={bindings}
				/>
			);
		case 'client-scripts':
			return (
				<sn-uib-scripts-configuration-panel
					scripts={scripts}
					disabled={disabled}
					is-macroponent-read-only={isMacroponentReadOnly}
					clientScriptApiCompletions="@uxf/clientScriptApiCompletions"
					isScriptsBusy={isScriptsBusy}
					event-handler-element-map={eventHandlerElementMap}
				/>
			);
		default:
			return null;
	}
};

const view = (state) => {
	return (
		<div className="data-config-shelf">
			{renderTargetDataConfigPanel(state)}
		</div>
	);
};

export default createCustomElement('sn-uib-data-configuration-shelf', {
	initialState: {
		currentTab: 'broker'
	},
	properties: {
		/**
		 * the list of data broker definitions
		 * @type {array}
		 */
		dataBrokerDefinitions: {
			default: null
		},
		/**
		 * the list of data broker instances and their properties
		 * @type {array}
		 */
		dataBrokerInstances: {
			default: null
		},
		/**
		 * the ID of the data broker instance that is currently being configured
		 * @type {string}
		 */
		selectedDataBrokerId: {
			default: null
		},
		/** the data broker instance that is currently being configured
		 * @type {object}
		 */
		selectedDataBroker: {
			computed({
				properties: {
					dataBrokerInstances,
					parentDataBrokerInstances,
					selectedDataBrokerId
				}
			}) {
				return [...dataBrokerInstances, ...parentDataBrokerInstances].find(
					(dataBroker) => dataBroker.id === selectedDataBrokerId
				);
			}
		},
		/**
		 * Whether the component is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * The array of properties for the currently selected data broker instance
		 * @type {array}
		 */
		currentSelectedProperties: {
			default: []
		},
		currentSelectablePropPreview: { default: '' },
		currentSelectablePropExecutionTime: { default: '' },
		referenceDisplayValues: { default: null, selectable: true },
		referenceResults: { default: null, selectable: true },
		referenceSearching: { default: null, selectable: true },
		tables: { default: null, selectable: true },
		tableFieldValues: { default: null, selectable: true },
		c3PropertyConfig: { default: null, selectable: true },
		/**
		 * the list of parent data broker instances and their properties
		 * @type {array}
		 */
		parentDataBrokerInstances: {
			default: []
		},
		/**
		 * whether the currently selected data broker instance is an ancestor broker
		 * @type {boolean}
		 */
		selectedDataBrokerIsAncestor: {
			default: false
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * An array of current client scripts
		 */
		scripts: {
			schema: {
				type: 'array'
			},
			default: []
		},
		/**
		 * client scripts can be manipulated
		 */
		isScriptsBusy: {
			schema: {
				type: 'boolean'
			},
			default: false
		},
		/**
		 * Event handler element map
		 */
		eventHandlerElementMap: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		/**
		 * An array of current client state parameters
		 */
		clientStateParameters: {
			schema: {
				type: 'array'
			},
			default: []
		},
		/**
		 * The current client state data in object form
		 */
		clientStateData: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		/**
		 * A map of data broker instance IDs to their instance names
		 */
		dataBrokerInstanceNameMap: {
			schema: { type: 'object' },
			default: {}
		},
		selectedDataTab: {
			default: null
		},
		eventHandlers: {
			default: []
		},
		eventMappings: {
			default: []
		},
		eventMappingHandlers: {
			default: null
		},
		appConfig: {
			default: null
		},
		interfaceConfigurationComponent: {
			default: null
		},
		clientTransformScriptTemplates: {
			default: null
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	styles,
	view,
	behaviors: [focusBehavior]
});
