import { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import {
	getActions,
	getTransactionProperties,
	getMessage,
	stringValuesFromProperties
} from './utils';
import '@servicenow/now-button';
import messageActions from './messageConditions'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import properties from './properties';

const getMessageActions = (properties, options, isEditing) => {
	const actions = getActions(properties, options, isEditing) || [];

	return (
		<div className="sn-uib-transaction-alert--actions">
			{actions.map(({ text, context }) => (
				<now-button
					label={text}
					size="sm"
					variant="tertiary"
					append-to-payload={{ context }}
				/>
			))}
		</div>
	);
};

/**
 * Turns a message template into properly styled JSX span
 *
 * @param {string} msg The original message
 * @returns {object} The JSX element for the message
 */
const processMessage = (msg) => {
	if (!msg) {
		return null;
	}
	const segments = msg
		.replace('<b></b>', '')
		.split('</b>')
		.flatMap((segment) => {
			if (segment.indexOf('<b>') === -1) {
				return [{ type: 'plain', msg: segment }];
			} else {
				const pieces = segment.split('<b>').filter((p) => p && p.length > 0);
				const boldPiece = pieces[pieces.length - 1];
				return [
					...pieces.slice(0, -1).map((m) => ({
						type: 'plain',
						msg: m.replace('([scope name]', '')
					})),
					{
						type: 'bold',
						msg: boldPiece
					}
				];
			}
		});

	return (
		<Fragment>
			{segments.map((s) =>
				s.type === 'bold' && s.msg.length > 0 ? (
					<b>{s.msg}</b>
				) : (
					<span>{s.msg}</span>
				)
			)}
		</Fragment>
	);
};

export default (state) => {
	const { isEditing, message } = state;
	const { size } = state.properties;

	if (message === null) {
		return (
			<div>
				<div className={`sn-uib-transaction-alert--content -mode-critical`}>
					<div className="sn-uib-transaction-alert--message">
						<now-icon icon={`lock-outline`} size="sm" />
						<span className="sn-uib-transaction-alert--text">
							{t('Cant edit. There is an unknown issue.')}
						</span>
					</div>
				</div>
			</div>
		);
	} else if (message === undefined) {
		return null;
	}

	const transactionText = isEditing
		? processMessage(
				getMessage(
					state.properties,
					stringValuesFromProperties(state.properties),
					false
				).message['inTransactionMessage']
		  )
		: processMessage(message.message['transactionMessage']);
	const ppTitle = message.message.ppMessageTitle ? (
		<b>{message.message.ppMessageTitle}</b>
	) : null;
	const ppBody = processMessage(message.message.ppMessageBody);

	if (!transactionText && !ppTitle && !ppBody) {
		return <div />;
	}

	const messageActions = getMessageActions(
		state.properties,
		message.options,
		isEditing
	);

	const conditions = message.conditions;

	let alertIcon;
	if (
		conditions.userInSameDomain === false &&
		conditions.recordPrivateScope === false
	) {
		alertIcon = 'building-outline';
	} else if (conditions.userInSameScope === false) {
		alertIcon = 'tree-view-long-outline';
	} else {
		alertIcon = 'lock-outline';
	}

	return (
		<div>
			{ppBody || ppTitle ? (
				<div className={`sn-uib-transaction-alert--content -mode-info`}>
					<div className="sn-uib-transaction-alert--message">
						<now-icon icon={`lock-outline`} size="sm" />
						{ppTitle}
						<span className="sn-uib-transaction-alert--text">{ppBody}</span>
					</div>
				</div>
			) : null}
			{transactionText ? (
				<div
					className={`sn-uib-transaction-alert--content -mode-${
						isEditing ? 'warning' : 'info'
					} -size-${size}`}>
					<div className="sn-uib-transaction-alert--message">
						{isEditing === false ? (
							<now-icon icon={`${alertIcon}`} size="sm" />
						) : null}
						<span className="sn-uib-transaction-alert--text">
							{transactionText}
						</span>
					</div>
					{messageActions}
				</div>
			) : null}
		</div>
	);
};
