import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import { t } from 'sn-translate';
import styles from './sn-uib-properties-configuration-pane.scss';
import '../sn-uib-error';
import '../sn-uib-viewport-config-panel';
import '@servicenow/now-button';
import '@servicenow/now-input';
import '../sn-uib-text-link';
import '../sn-uib-is-hidden-property-input';
import '../sn-uib-layout-template-popover';
import { PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED } from '../sn-uib-properties-editor-panel';
import focusBehavior from '@servicenow/behavior-focus';
import {
	componentProperties,
	getHiddenProperty,
	isInstanceNameProperty,
	isViewportComponent,
	findPropertyValue,
	isDeclarativeActionComponent,
	massageViewportElementProperties
} from './utils';
import {
	ELEMENT_INSTANCE_NAME,
	DEFAULT_ROUTE,
	VIEWPORT_ROUTES,
	ACTIVE_SCREEN,
	ACTIVE_ROUTE,
	VIEWPORT_CONFIGURATION,
	DATA_DRIVEN_VIEWPORT_ITEMS,
	VIEWPORT_DATA_SOURCE
} from './constants';
import { REFERENCE_PROPERTY_VALUE_CHANGED } from '../sn-uib-reference-property-input';
import { FIELD_LIST_PROPERTY_TABLE_SELECTED } from '../sn-uib-field-list-property-input';
import { FIELD_PROPERTY_TABLE_SELECTED } from '../sn-uib-field-property-input';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import { PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED } from '../sn-uib-property-input-script-link-toggle';
import { OBJECT_TYPE, STRING_TYPE } from '../constants';
// Dispatched action types
export const PROPERTIES_CONFIG_PROPERTY_CHANGED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#PROPERTY_CHANGED';
export const PROPERTIES_CONFIG_REFERENCE_PROPERTY_SEARCHED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#REFERENCE_SEARCHED';
export const PROPERTIES_CONFIG_ELEMENT_CHANGED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#ELEMENT_CHANGED';
export const PROPERTIES_CONFIG_FIELD_PROPERTY_TABLE_SELECTED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#FIELD_PROPERTY_TABLE_SELECTED';
export const PROPERTIES_CONFIG_PROPERTY_BINDING_TOGGLED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#PROPERTY_BINDING_TOGGLED';
export const PROPERTIES_CONFIG_PROPERTY_SCRIPT_CHANGED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#PROPERTY_SCRIPT_CHANGED';
export const PROPERTIES_CONFIG_PROPERTY_EXTERNAL_CONFIG_BUTTON_CLICKED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#EXTERNAL_CONFIG_BUTTON_CLICKED';
export const PROPERTIES_CONFIG_DA_MAPPING_BUTTON_CLICKED =
	'SN_UIB_PROPERTIES_CONFIGURATION_PANE#DA_MAPPING_BUTTON_CLICKED';

export const C3_PROPERTY_OPENED_SET = 'SN_UIB_C3_PROPERTY_INPUT#OPENED_SET';
import { componentInterfaceSchema } from '../sn-uib-element-configuration-panel/schemas';

const actions = {
	NOW_INPUT_CHANGED: 'NOW_INPUT#INPUT',
	NOW_BUTTON_BARE_CLICKED: 'NOW_BUTTON_BARE#CLICKED',
	NOW_BUTTON_CLICKED: 'NOW_BUTTON#CLICKED'
};

const renderIsHiddenPanel = (
	instanceNameEditorEnabled,
	isHiddenInputOpen,
	isMacroponentReadOnly,
	disabled,
	isHiddenConfigured,
	bindings,
	element,
	properties
) => {
	if (!instanceNameEditorEnabled) {
		return null;
	}

	return isHiddenInputOpen ||
		((isMacroponentReadOnly || disabled) && isHiddenConfigured) ? (
		<section className="is-hidden-property-input-wrapper">
			<sn-uib-is-hidden-property-input
				bindings={bindings}
				elementId={element.id}
				hidden-property={getHiddenProperty(properties)}
				script-template={`@properties-configuration-pane-${element.id}/scriptTemplate`}
			/>
		</section>
	) : null;
};

const view = ({
	properties: {
		element,
		properties,
		disabled,
		externalConfigurationButtons,
		error,
		container,
		dataBindingEnabled,
		interfaceConfigurationComponent,
		bindings,
		appConfig,
		selectedComponentTag,
		instanceNameEditorEnabled,
		viewportPropertiesConfiguration,
		isHiddenInputOpen,
		dynamicTabsEnabled,
		isMacroponentReadOnly
	}
}) => {
	const { value: instanceValue } =
		(properties.length !== 0 && properties.find(isInstanceNameProperty)) || '';
	const elementProperties = componentProperties(properties, element);
	const hidePanel =
		!elementProperties.length && !isViewportComponent(properties, element);
	const isHiddenPropValue = get(getHiddenProperty(properties), 'value', null);
	const isHiddenConfigured = isHiddenPropValue && isHiddenPropValue !== '@';
	return (
		<Fragment>
			{element && !hidePanel ? (
				<div className="sn-uib-properties-configuration-pane">
					{renderIsHiddenPanel(
						instanceNameEditorEnabled,
						isHiddenInputOpen,
						isMacroponentReadOnly,
						disabled,
						isHiddenConfigured,
						bindings,
						element,
						properties
					)}
					{!instanceNameEditorEnabled ? (
						<div className="sn-uib-element-instance-name-editor">
							<now-input
								label={t('Component name')}
								value={instanceValue || element.displayName}
								disabled={disabled}
								append-to-payload={{ context: 'instance-name' }}
							/>
						</div>
					) : null}
					<section className="sn-uib-element-properties-editor">
						{error ? (
							<sn-uib-error
								message={t('There was a problem loading this component.')}
							/>
						) : null}
						{isViewportComponent(properties, element) ? (
							<sn-uib-viewport-config-panel
								activeRoute={findPropertyValue(properties, ACTIVE_ROUTE)}
								defaultRoute={findPropertyValue(properties, DEFAULT_ROUTE)}
								viewportRoutes={findPropertyValue(properties, VIEWPORT_ROUTES)}
								viewportPropertiesConfiguration={
									viewportPropertiesConfiguration
								}
								is-page-state-dirty="@uxf/isPageStateDirty"
								activeScreen={findPropertyValue(properties, ACTIVE_SCREEN)}
								append-to-payload={{ elementId: element.id }}
								appConfig={appConfig}
								disabled={disabled}
								elementRef={`@${element.id}_information/elementRef`}
								repeatedTabData={findPropertyValue(
									properties,
									DATA_DRIVEN_VIEWPORT_ITEMS
								)}
								viewportDataSource={findPropertyValue(
									properties,
									VIEWPORT_DATA_SOURCE
								)}
								dynamicTabsEnabled={dynamicTabsEnabled}
								viewportElementProperties={massageViewportElementProperties(
									properties
								)}
							/>
						) : null}
						<sn-uib-properties-editor-panel
							key={element.id}
							elementId={element.id}
							properties={elementProperties}
							append-to-payload={{
								context: 'element'
							}}
							container={container}
							dataBindingEnabled={dataBindingEnabled}
							interfaceConfigurationComponent={interfaceConfigurationComponent}
							bindings={bindings}
							app-config={appConfig}
							scripting-enabled="@sn-uib-element-configuration-panel/scriptedPropertiesEnabled"
							script-template={`@properties-configuration-pane-${element.id}/scriptTemplate`}
							selectedComponentTag={selectedComponentTag}
						/>
					</section>
					{isDeclarativeActionComponent(properties) ? (
						<section className="sn-uib-element-configure-mapping">
							<now-button-bare
								label={t('Configure declarative action event mappings')}
								size="sm"
								append-to-payload={{ id: 'da-mapping' }}
							/>
							<sn-uib-property-description-tooltip
								/* prettier-ignore */
								description={t('Declarative action event mappings may require data resource and component property configuration prior to use.')}
							/>
						</section>
					) : null}
					{externalConfigurationButtons &&
					externalConfigurationButtons.length > 0 ? (
						<section className="sn-uib-element-properties-editor center-buttons">
							{externalConfigurationButtons.map((button) => (
								<sn-uib-text-link
									href={button.url}
									manageClicked={true}
									label={button.label}
									disabled={button.disabled}
									icon="open-link-fill"
								/>
							))}
						</section>
					) : null}
				</div>
			) : (
				<div className="sn-uib-properties-config-empty-state-wrapper">
					{renderIsHiddenPanel(
						instanceNameEditorEnabled,
						isHiddenInputOpen,
						isMacroponentReadOnly,
						disabled,
						isHiddenConfigured,
						bindings,
						element,
						properties
					)}
					<div className="sn-uib-element-configuration-panel --empty-state">
						<figure>
							<div className="icon-container">
								<now-icon icon="sliders-fill" size="lg" />
							</div>
							<figcaption>
								<span>{t('No configuration options.')}</span>
								{!properties.length ? (
									<span>
										{t('Select a component to view configuration options.')}
									</span>
								) : null}
							</figcaption>
						</figure>
					</div>
				</div>
			)}
		</Fragment>
	);
};

/**
 * The panel for configuring an element's instance name and properties in the UI Builder.
 */
export default createCustomElement('sn-uib-properties-configuration-pane', {
	view,
	styles,
	behaviors: [focusBehavior],
	properties: {
		/**
		 * The element whose instance name and properties are currently being configured.
		 * @type {object}
		 */
		element: {
			default: null
		},
		/**
		 * The layout and type of the element whose instance name and properties are currently being configured.
		 * @type {object}
		 */
		elementLayout: {
			default: {}
		},
		/**
		 * The properties belonging to the currently selected element.
		 * @type {object[]}
		 */
		layoutTemplates: {
			default: []
		},
		/**
		 * The properties belonging to the currently selected element.
		 * @type {object[]}
		 */
		properties: {
			default: []
		},
		/**
		 * The properties editor container that individual property inputs should be constrained within.
		 * @type {HTMLElement}
		 */
		container: {
			default: null
		},
		/**
		 * The list of tables passed in from the data provider.
		 * @type {object[]}
		 * @example [{ id: 'incident', label: 'Incident' }, { ... }]
		 */
		tables: {
			default: null,
			selectable: true
		},
		/**
		 * The reference results passed in from the data provider, associated with a table.
		 * @type {object}
		 * @example { incident: [{ id: '98hq34f98hq3g98hq3g', label: 'INC00002' }, { ... }] }
		 */
		referenceResults: {
			default: null,
			selectable: true
		},
		/**
		 * The reference picker displayValues to be used in the input search box of typeahead
		 * @type {object}
		 * @example { 'p9q3745fgp9unaefv': 'My email doesn't work!' } { sysId: displayValue, ... }
		 */
		referenceDisplayValues: {
			default: null,
			selectable: true
		},
		/**
		 * A hash of booleans denoting which reference input is currently searching for results
		 * @type {object}
		 * @example { '123abc': true, '234cde': false }
		 */
		referenceSearching: {
			default: null,
			selectable: true
		},
		/**
		 * The current value of the C3Modal content to be sent up to UXF as a property change
		 * Selectable by the property type inputs that are C3 configured
		 * @type {object}
		 */
		c3PropertyConfig: {
			default: null,
			selectable: true
		},
		/**
		 * The list of fields passed in from the data provider, associated with a table.
		 * @type {object}
		 * @example { incident: [{ id: 'short_descr', label: 'Short Description' }, { ... }] }
		 */
		tableFieldValues: {
			default: null,
			selectable: true
		},
		/**
		 * Whether the element is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * the list of configuration buttons to be rendered for the currently selected element, eg. [{label: 'Configure list', url: '/sys_aw_list_list.do'}]
		 * @type {array}
		 */
		externalConfigurationButtons: {
			default: null
		},
		error: {
			default: false
		},
		dataBindingEnabled: {
			default: true,
			selectable: true
		},
		interfaceConfigurationComponent: {
			/**
			 *  the interface name and its corresponding component tag
			 * @typedef componentInterface
			 * @property {string} interfaceName
			 * @property {string}  componentId
			 */

			/**
			 *  a map containing component interfaces
			 * @typedef {Object.<string, componentInterface>} componentInterfaceMap
			 */

			/**
			 * @type {componentInterfaceMap}
			 */
			default: {},
			schema: { type: 'object', schema: componentInterfaceSchema }
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * Used to determine whether the instance name editor should display
		 */
		instanceNameEditorEnabled: {
			schema: { type: 'boolean' },
			default: true
		},
		/**
		 * The template for client transform scripts
		 */
		scriptTemplate: {
			default: '',
			selectable: true
		},
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {
			default: ''
		},
		/**
		 * Whether the hidden config input is toggled open
		 */
		isHiddenInputOpen: {
			default: false
		},
		dynamicTabsEnabled: {
			default: true
		},
		isMacroponentReadOnly: {
			default: false
		},
		/**
		 * Viewport config properties computed from now-ui.json
		 */
		viewportPropertiesConfiguration: {
			default: null,
			selectable: true,
			computed({ properties: { properties } }) {
				const viewportConfig = findPropertyValue(
					properties,
					VIEWPORT_CONFIGURATION
				);
				const configType = typeof viewportConfig;
				try {
					if (configType === STRING_TYPE) {
						return JSON.parse(viewportConfig);
					} else if (configType === OBJECT_TYPE) {
						return viewportConfig;
					}
				} catch (e) {
					console.warn(
						'Cannot parse viewportConfiguration for viewport component, using default viewport config properties'
					);
					return null;
				}
				return null;
			}
		}
	},
	actionHandlers: {
		[actions.NOW_BUTTON_BARE_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			if (action.payload.id === 'da-mapping') {
				dispatch(PROPERTIES_CONFIG_DA_MAPPING_BUTTON_CLICKED);
			}
		},
		['SN_UIB_TEXT_LINK#CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(PROPERTIES_CONFIG_PROPERTY_EXTERNAL_CONFIG_BUTTON_CLICKED, {
				url: action.payload.href
			});
		},
		[actions.NOW_INPUT_CHANGED]: ({
			dispatch,
			action,
			state: { properties }
		}) => {
			action.stopPropagation();
			action.payload.context === 'instance-name' &&
				dispatch(PROPERTIES_CONFIG_PROPERTY_CHANGED, {
					propertyId: ELEMENT_INSTANCE_NAME,
					value: action.payload.fieldValue,
					elementId: properties.element.id
				});
		},
		[PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED]: ({
			action,
			dispatch,
			state: { properties }
		}) => {
			action.stopPropagation();

			const context = get(action, 'payload.context', null);
			context === 'element' &&
				dispatch(PROPERTIES_CONFIG_PROPERTY_CHANGED, {
					propertyId: action.payload.id,
					value: action.payload.value,
					elementId: properties.element.id,
					noDebounce: action.payload.noDebounce
				});
		},
		[REFERENCE_PROPERTY_VALUE_CHANGED]: ({ action, dispatch }) => {
			action.stopPropagation();
			const referenceId = action.payload.referenceId;

			dispatch(PROPERTIES_CONFIG_REFERENCE_PROPERTY_SEARCHED, {
				...action.payload,
				referenceId
			});
		},
		[FIELD_LIST_PROPERTY_TABLE_SELECTED]: ({
			action,
			dispatch,
			state: { properties }
		}) => {
			action.stopPropagation();

			dispatch(PROPERTIES_CONFIG_FIELD_PROPERTY_TABLE_SELECTED, {
				table: action.payload.value,
				propertyId: action.payload.id,
				elementId: properties.element.id
			});
		},
		[FIELD_PROPERTY_TABLE_SELECTED]: ({
			action,
			dispatch,
			state: { properties }
		}) => {
			action.stopPropagation();

			dispatch(PROPERTIES_CONFIG_FIELD_PROPERTY_TABLE_SELECTED, {
				table: action.payload.value,
				propertyId: action.payload.id,
				elementId: properties.element.id
			});
		},
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: ({
			action,
			properties,
			dispatch
		}) => {
			action.stopPropagation();

			dispatch(PROPERTIES_CONFIG_PROPERTY_BINDING_TOGGLED, {
				value: !action.payload.isStatic,
				propertyId: action.payload.propertyId,
				elementId: properties.element.id
			});
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			dispatch(PROPERTIES_CONFIG_PROPERTY_SCRIPT_CHANGED, {
				value: action.payload.script,
				propertyId: action.payload.propertyId,
				elementId: properties.element.id,
				compatibilityMode: action.payload.compatibilityMode
			});
		}
	}
});
