import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import { t } from 'sn-translate';
import styles from './sn-uib-element-navigator-popover.scss';
import {
	INSERT_COMPONENT_SLOT,
	INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER
} from '../sn-uib-element-navigator';

const handleClickOrKeydown = (event, type, dispatch, payload) => {
	if (type === INSERT_COMPONENT_SLOT) {
		dispatch(type, {
			positionTarget: event.target
		});
	} else if (type === INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER) {
		dispatch(type, {
			positionTarget: event.target,
			...payload
		});
	} else {
		dispatch(type, payload);
	}
};

/* eslint-disable jsx-a11y/no-autofocus */
export const view = (
	{ properties: { actions, showModalsContent, modalsList } },
	{ dispatch }
) => {
	const modalsContent = showModalsContent ? modalsList : null;
	const actionContent = !showModalsContent
		? actions
				.filter((action) => !action.disabled)
				.map(({ type, icon, label, payload = {} }) => (
					<div
						// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
						tabIndex={0}
						className="element-navigator-popover-button"
						on-click={(event) =>
							handleClickOrKeydown(event, type, dispatch, payload)
						}
						on-keydown={(event) =>
							handleClickOrKeydown(event, type, dispatch, payload)
						}>
						<now-icon size="sm" icon={icon} />
						{label}
					</div>
				))
		: null;
	return (
		<div className="container">
			{modalsContent}
			{actionContent}
		</div>
	);
};

createCustomElement('sn-uib-element-navigator-popover', {
	view,
	styles,
	properties: {
		header: { default: '' },
		actions: { default: [] },
		showModalsContent: {
			default: false
		},
		modalsList: {
			default: {}
		}
	}
});
