import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-error.scss';
import { t } from 'sn-translate';

const view = (state) => {
	const { message, href } = state.properties;
	return (
		<section className="unregistered-error">
			<now-icon className="icon" size="md" icon="triangle-exclamation-fill" />

			<section role="alert" className="message">
				{message}
				{href ? (
					<section className="link">
						<a target="_blank" href={href}>
							{t('Learn more about this error')}
						</a>
					</section>
				) : null}
			</section>
		</section>
	);
};

createCustomElement('sn-uib-error', {
	view,
	properties: {
		message: {
			default: null
		},
		href: {
			default: null
		}
	},
	styles
});
