import {
	move,
	dragmove,
	dragend,
	getAutoScrollOptions,
	setBodyToNotScroll,
	stageScrollModifier,
	removeListenersIfFound,
	stageDragMap
} from './utils';
import interact from 'interactjs';
import get from 'lodash/get';

const checkForRefresh = (
	test = () => true,
	fn = () => {},
	interval = 0,
	timeout = 10000
) => {
	let intervalId;
	const run = () => {
		const passed = test();
		if (passed) {
			fn();
			clearTimeout(intervalId);
		} else {
			intervalId = setTimeout(run, interval);
		}
	};
	setTimeout(() => {
		clearTimeout(intervalId);
	}, timeout);
	run();
};
function remount(el, getScrollContainer, args) {
	return function remountFn() {
		const fn = () => {
			removeListenersIfFound(el);
			if (!el || !el.isConnected) {
				return;
			}
			attachDrag({ ...args, observe: true });
		};
		const testFn = () => {
			return getScrollContainer().isConnected;
		};
		checkForRefresh(testFn, fn, 50);
	};
}

const notScrollingBody = ({ autoScroll }) => {
	const bodyAutoScroll = autoScroll === true;
	const containerAutoScrollIsBody =
		get(autoScroll, 'container') === document.body;
	return !(bodyAutoScroll || containerAutoScrollIsBody);
};

const stopDragstart = (e) => {
	e.preventDefault();
};

const attachDrag = (args) => {
	const {
		el,
		draggableProps,
		dataTransfer,
		dispatch,
		previewData,
		previewTag
	} = args;

	const scrollContainer = stageDragMap.get('stageRef').current;
	const options = {
		...draggableProps,
		...getAutoScrollOptions(scrollContainer)
	};
	const callbacks = notScrollingBody(options)
		? setBodyToNotScroll()
		: {
				move: () => {},
				dragend: () => {}
		  };

	/**
	 * Context for `stateScrollManager`:
	 * Currently in our app when we start `dragging` the entire stage renders the dropzone wrappers.
	 * In some browsers, Firefox (69.0) and Safari (12.1.1) at the time of this writing, react to this by
	 * resetting `scrollTop` of the stage to 0. Not remounting/unmmounting the drop web components seems to
	 * fix this but that increases the amount of work the stage has to do when updating the layout
	 * and we then have to manage a longer lifescycle of the drop components. With some refactoring this is possible
	 * But this is a lighter touch and only runs once.
	 */
	let stateScrollManager = null;

	const dragendHandler = dragend(dataTransfer);
	const dragMoveHandler = dragmove(dataTransfer);
	// Prevents dragging of draggable elements inside the element. Such as images.
	el.addEventListener('dragstart', stopDragstart);
	stageDragMap.addNotifier('stageRef', () => {
		remount(el, () => stageDragMap.get('stageRef').current, args)();
	});
	interact(el)
		.draggable(options)
		.on('mousedown', () => {
			callbacks.move();
			if (scrollContainer) {
				stateScrollManager = stageScrollModifier();
				stateScrollManager.initializeStage(scrollContainer);
			}
		})
		.on('mouseup', () => {
			callbacks.dragend();
			stateScrollManager = null;
		})
		.on('move', move(dataTransfer, dispatch, previewData, previewTag))
		.on('down', (event) => {
			const interaction = event.interaction;

			// Storing buttons so in `move` we can not activate on right clicks.
			if (interaction.pointerIsDown && !interaction.interacting()) {
				interaction.buttonId = event.buttons;
			}
		})
		.on('dragmove', (e) => {
			dragMoveHandler(e);
		})
		.on('dragstart', (e) => {
			/**
			 * This sets a datatransfer on the event so any dropzone can see the event has data.
			 * Otherwise the dropzone will reject/not respond to the drag event.
			 */

			e.transfer = dataTransfer;
		})
		.on('dragend', (e) => {
			dragendHandler(e);
		});
};

const removeDrag = ({ el }) => {
	const interactable = interact(el);
	const draggable = interact(el);
	el.removeEventListener('dragstart', stopDragstart);
	const listeners = ['move', 'dragmove', 'dragstart', 'dragend'];
	listeners.forEach((listener) => {
		draggable[`on${listener}`] = null;
		draggable.off(listener);
	});

	interactable.unset();
};

const setupListenersEffect = (coeffects) => {
	const { action, state, dispatch, properties, updateState } = coeffects;
	const { draggableProps } = properties;

	const {
		el,
		getDataTransferObjFn,
		getPreviewTag = () => {},
		getPreviewData = () => {},
		getScrollContainer = () => {},
		isDisabled = null
	} = action.payload;
	const dataTransfer = getDataTransferObjFn.call(null, state);

	const disabled = isDisabled ? !!isDisabled(state) : false;
	const interactData = (data) => {
		return {
			el,
			draggableProps,
			dataTransfer,
			dispatch,
			previewData: getPreviewData(data),
			previewTag: getPreviewTag(),
			getScrollContainer
		};
	};
	if (!disabled) {
		attachDrag(interactData(state));
	}
	if (isDisabled) {
		updateState([
			{
				path: 'disabled',
				value: disabled,
				operation: 'set'
			},
			{
				path: 'interactData',
				value: interactData,
				operation: 'set'
			},
			{
				path: 'isDisabled',
				value: isDisabled,
				operation: 'set'
			}
		]);
	}
};

export const checkIfDisabled = ({ state, updateState }) => {
	const { disabled, interactData, isDisabled = () => {} } = state;
	if (!interactData) {
		return;
	}
	const update = !!isDisabled(state);
	if (update === disabled) {
		return;
	}
	if (update !== true) {
		attachDrag(interactData(state));
	} else {
		removeDrag(interactData(state));
	}
	updateState({
		path: 'disabled',
		value: update,
		operation: 'set'
	});
};

const setupListenersHandler = {
	effect: setupListenersEffect
};
export { setupListenersHandler, attachDrag as attachDragListeners };
