import throttle from 'lodash/throttle';
import interact from 'interactjs';
import { t } from 'sn-translate';

export const LEFT = 'LEFT';
export const TOP = 'TOP';
export const BOTTOM = 'BOTTOM';
export const RIGHT = 'RIGHT';
export const NONE = 'NONE';
export const EMPTY = 'EMPTY';

export const cancel = function (e) {
	if (e.preventDefault) {
		e.preventDefault();
	}
	if (e.stopPropagation) {
		e.stopPropagation();
	}
	return false;
};
const dispatchActionsWhenDropped = (id, direction, parentId, dispatch) => {
	if (direction) {
		dispatch('UIB_DROPZONE#DROPPED', {
			direction,
			adjacentLayoutElementId: id,
			parentId
		});
		dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
			ariaLiveText: t('Component Dropped at {0} position', direction)
		});
	} else {
		dispatch('EMPTY_SLOT#DROPPED', {
			parentId: id
		});
		dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
			ariaLiveText: t('Component Dropped inside a Container')
		});
	}
};
export const keyboardNavBehavior = ({
	id,
	dispatch,
	direction,
	parentId,
	hoverAction = 'UIB_DND_DROPLEAF#HOVER',
	blurAction = 'UIB_DND_DROPZONE#CLEAR_ACTIVE_DROPZONE',
	cancelKeyFn = function () {}
}) => ({
	tabindex: '0',
	'on-focus': () => {
		dispatch(hoverAction, {
			id,
			direction
		});
		if (direction) {
			dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
				ariaLiveText: t('{0} drop zone', direction)
			});
		} else {
			dispatch('SN_UIB_ARIA_LIVE#CONTENT_UPDATE', {
				ariaLiveText: t('Press enter to drop the component')
			});
		}
	},
	'on-blur': () => {
		dispatch(blurAction);
	},
	'on-keydown': (e) => {
		const { key } = e;
		cancelKeyFn(e);
		if (key !== 'Enter') {
			return;
		}
		dispatchActionsWhenDropped(id, direction, parentId, dispatch);
	},
	'on-click': (e) => {
		if (e.detail === 0) {
			dispatchActionsWhenDropped(id, direction, parentId, dispatch);
		}
	}
});

export const isInRectangle = ({ x, y, rect }) => {
	const newX = x - rect.x;
	const newY = y - rect.y;

	const { width, height } = rect;
	if (newX < 0 || newY < 0) {
		return false;
	}
	if (newX >= width || newY >= height) {
		return false;
	}
	return true;
};

const getToDocument = (node) => {
	let curr = node.getRootNode();

	if (curr === node.ownerDocument) {
		return [curr];
	} else {
		curr = curr.host;
	}

	const path = [curr];

	while (curr !== node.ownerDocument) {
		curr = curr.getRootNode().host
			? curr.getRootNode().host
			: curr.getRootNode();
		path.push(curr);
	}
	return path;
};

/**
 * Finds the of quadrant of where an existing x,y coordinate.
 * @param {Object} eventData - The location data of the event.
 * @param {Object} eventData.rect - The location and size information of the rectangle.
 * @param {number} eventData.rect.width - Width of the rectangle.
 * @param {number} eventData.rect.height - Height of the rectangle.
 * @param {number} eventData.rect.x - The x offset of the rectangle.
 * @param {number} eventData.rect.y -The y offset of the rectangle .
 * @param {number} eventData.x - The x offset of the point.
 * @param {number} eventData.y - The y offset of the point.
 * @param {Array} sides - A list of sides that are valid.
 * @returns {string} - the side of the point (LEFT, TOP, RIGHT, BOTTOM) or NONE if invalid side or no intersection.
 */

export const getQuadrant = ({ rect, x, y }, sides) => {
	if (!isInRectangle({ x, y, rect })) {
		return NONE;
	}
	const newX = x - rect.x;
	const newY = y - rect.y;

	const { width, height } = rect;
	return getSide(sides, { x: newX, y: newY, width, height }) || EMPTY;
};
export const findDiagonalQuadrant = (
	{ x, y, width, height },
	sides = [LEFT, TOP, BOTTOM, RIGHT]
) => {
	const diagQuadrants = [
		[LEFT, BOTTOM],
		[TOP, RIGHT]
	];
	let corner = [];
	const slope = height / width;
	const edgeHeight = slope * x;
	const validateSide = (side) => (sides.includes(side) ? side : NONE);

	if (y > edgeHeight) {
		corner = diagQuadrants[0]; // bottomLeft
	} else {
		corner = diagQuadrants[1]; // topright
	}

	if (y < -edgeHeight + height) {
		return validateSide(corner[0]); // left or top
	} else {
		return validateSide(corner[1]); // bottom or right
	}
};

const sideNames = {
	left: LEFT,
	right: RIGHT,
	top: TOP,
	bottom: BOTTOM
};

const getValidSideNames = (sides) => {
	return Object.keys(sides)
		.filter((key) => sides[key])
		.map((key) => sideNames[key]);
};

const orientations = [
	{
		// row
		is(sides) {
			return sides.includes(LEFT) && sides.includes(RIGHT);
		},
		getSide({ x, width }) {
			if (x < width / 2) {
				return LEFT;
			}
			return RIGHT;
		}
	},
	{
		// column
		is(sides) {
			return sides.includes(TOP) && sides.includes(BOTTOM);
		},
		getSide({ y, height }) {
			if (y < height / 2) {
				return TOP;
			}
			return BOTTOM;
		}
	},
	{
		//diagonal, fallback
		is() {
			return true;
		},
		getSide(positioning, sides) {
			return findDiagonalQuadrant(positioning, sides);
		}
	}
];
const rowColOrDiagonal = (sides, positioning) => {
	return orientations
		.reduce((acc, fn) => {
			if (acc) {
				return acc;
			}
			if (fn.is(sides)) {
				return fn;
			}
			return acc;
		}, null)
		.getSide(positioning, sides);
};

export const getSide = ({ left, right, top, bottom }, positioning) => {
	const validSides = getValidSideNames({ left, right, top, bottom });
	switch (validSides.length) {
		case 1:
			return validSides[0];
		case 2:
			return rowColOrDiagonal(validSides, positioning);
		case 3:
		case 4:
			return findDiagonalQuadrant(positioning, validSides);
		default:
			return NONE;
	}
};

export const dropmove = (event, cb) => {
	const { currentTarget, target } = event;
	const el = currentTarget || target;

	cb({
		x: event.x,
		y: event.y,
		el,
		rect: el.getBoundingClientRect()
	});
};

export const dragleave = function (event) {
	event.stopPropagation();
	event.stopImmediatePropagation();
};

export const dragenter = (event) => {
	event.stopPropagation();
	event.stopImmediatePropagation();
};

export const ondrop = (dispatch) => (event) => {
	const { transfer } = event.dragEvent;

	if (!transfer) {
		event.stopPropagation();
		event.stopImmediatePropagation();
		event.preventDefault();
		event.reject();
		return;
	}
	event.dragEvent.wasDropped = true;
	dispatch('UIB_DND_DROPZONE#DROP', {
		data: transfer
	});
	dispatch('UIB_DND_DROPZONE#CLEAR_REGIONS');
};

export const checkActive = (id, isActive) =>
	isActive
		? (side, activeDir, name) => (side === activeDir ? name : '')
		: () => '';

const dragOverLeaf = ({ id, width, height, x, y }, dispatch) => {
	const direction = findDiagonalQuadrant({
		height,
		width,
		x,
		y
	});

	dispatch('UIB_DND_DROPZONE#HOVER', {
		id,
		direction
	});
};

/**
 * Sort function for ordering html elements in relation to each other depending on shadow root depth and position.
 * @param {Object} a - The first dropzone being compared.
 * @param {Array} a.path - An array of the enclosing web components of the dropzone up to `window`.
 * @param {string} a.id - The cuid of the dropzone.
 * @param {Object} b - The second dropzone being compared.
 * @param {Array} b.path - An array of the enclosing web components of the dropzone up to `window`.
 * @param {string} b.id - The cuid of the dropzone.

 * @returns {integer} - Whether `a` is larger than `b`. negative means smaller, 0 means equal, positive means bigger.
 */
const sortByDepth = (a, b) => {
	/*
	 * The paths being compared are the amount of shadow roots the dropzone is enclosed in.
	 * This is relatively simple way of comparing what is deeper in our dom and thus higher priority for
	 * dropping.
	 */
	const differenceInDepth = b.path.length - a.path.length;

	// if the difference is 0 we want to compare to see if one gets priority over another.
	if (differenceInDepth === 0) {
		const positionBitmask = a.path[0].compareDocumentPosition(b.path[0]);

		/*
		 * https://developer.mozilla.org/en-US/docs/Web/API/Node/compareDocumentPosition
		 * This method returns a integer bitmask from which you can divine some information of the two nodes being compared.
		 * If the bitwise AND operator returns an integer > 0 that comparison is true.
		 * We're using it here to tell if "a" is following or is contained by "b"
		 */
		if (
			positionBitmask & Node.DOCUMENT_POSITION_FOLLOWING ||
			positionBitmask & Node.DOCUMENT_POSITION_CONTAINED_BY
		) {
			return 1;
		} else {
			return -1;
		}
	}
	return differenceInDepth;
};

export const getPreferredDropzone = (arr, sortFn = sortByDepth) => {
	/*
	 * Because the layout divs both precede and don't contain the leaf node to have the activation occur over the leaf
	 * we prefer any dropzones that are tagged with layout
	 */
	const layout = arr.filter(({ label }) => label === 'layout').sort(sortFn)[0];
	if (layout) {
		return layout.id;
	}

	return (
		arr
			.filter(({ label }) => label === 'leaf')
			.sort(sortFn)
			.map(({ id }) => id)[0] || null
	);
};
const activities = new Map();

/**
 * Overrides default drop checking. The first dropzone to return true will be the dropzone to have the drop event fire.
 * @param {DragEvent} dragEvent - The related drag event from the object being dragged.
 * @param {PointerEvent} event - Touch, Pointer or Mouse Event related to the action.
 * @param {boolean} dropped - The default result for the element.
 * @param {Interactable} dropzone - The dropzone Interactable data from interactjs.
 * @param {Element} dropElement - The actual dropzone Element being dragged over.
 * @param {number} draggable -The draggabblr Interactable data from interactjs .
 * @param {number} draggableElement - The actual draggable Element being dragged over.
 * @returns {boolean} - Whether to this drop should be active.
 */
const checker = (label) => (
	dragEvent,
	event,
	dropped,
	dropzone,
	dropElement,
	draggable,
	draggableElement
) => {
	/**
	 * This exists to overwrite the default drop behavior. This is fired by interact for each dropzone on each dragmove.
	 * Because we need to see every zone before we return true the actual "active" dropzone is set by the results of the previous round of
	 * checker events which are stored in `draggableElement.process`. Each checker firing that is valid is stored on `draggableElement.currentRound`.
	 * Then when the `dragmove` event is fired (in the draggable behavior) it sets those events to be processed.
	 */

	// If it isn't actively being dragged over we don't care
	if (!dropped) {
		return dropped;
	}

	if (!draggableElement.currentRound) {
		draggableElement.currentRound = [];
	}

	draggableElement.currentRound.push({
		label,
		id: dropElement.dndTag,
		path: getToDocument(dropElement)
	});

	if (draggableElement.activeDropzone) {
		if (draggableElement.activeDropzone === dropElement.dndTag) {
			return true;
		} else {
			return false;
		}
	}
	return dropped;
};

const layoutMoveThrottle = throttle(
	({ adjacentLayoutElementId, direction, dispatch }) => {
		dispatch('UIB_DND_DROPZONE#HOVER', {
			id: adjacentLayoutElementId,
			direction
		});
	},
	50,
	{ leading: true, trailing: false }
);

export const layoutOptions = ({
	adjacentLayoutElementId,
	direction,
	dispatch,
	additionalData = {},
	el
}) => ({
	ondrop: (event) => {
		event.stopPropagation();
		dispatch('UIB_DND_DROPZONE#DROPPED', {
			dataTransfer: event.dragEvent.transfer,
			direction,
			adjacentLayoutElementId,
			...additionalData
		});
	},
	ondragenter: (e) => {
		activities.set(el, true);
		dragenter(e, dispatch);
	},
	ondropmove: (event) => {
		event.stopPropagation();

		if (!activities.get(el)) {
			return;
		}

		layoutMoveThrottle({ direction, adjacentLayoutElementId, dispatch });
	},
	ondragleave: (event) => {
		activities.set(el, false);
		dragleave(event);
		dispatch('UIB_DND_DROPZONE#CLEAR_ACTIVE_DROPZONE');
	},
	checker: checker('layout')
});

const dropMoveThrottle = throttle(
	({ id, width, height, insideX, insideY, dispatch }) => {
		dragOverLeaf(
			{
				id,
				width,
				height,
				x: insideX,
				y: insideY
			},
			dispatch
		);
	},
	50,
	{ leading: true, trailing: false }
);

export const leafOptions = ({ id, dispatch, el, additionalData = {} }) => ({
	ondrop: (event) => {
		const { target } = event;
		let direction;

		if (!target.isConnected) {
			direction = event.dropzone.quadrant;
		} else {
			const { x, y } = event.dragEvent.client;
			const {
				x: rectX,
				y: rectY,
				width,
				height
			} = event.target.getBoundingClientRect();
			const insideX = x - rectX;
			const insideY = y - rectY;

			direction = findDiagonalQuadrant({
				height,
				width,
				x: insideX,
				y: insideY
			});
		}
		event.stopPropagation();
		event.stopImmediatePropagation();

		if (!direction) {
			// something went wrong, probably it was dropped so fast the target wasn't mounted/dropmoves weren't fired.
			dispatch('UIB_DND_DROPZONE#INVALID_DROP');
		} else {
			dispatch('UIB_DND_DROPZONE#DROPPED', {
				dataTransfer: event.dragEvent.transfer,
				direction,
				adjacentLayoutElementId: id,
				...additionalData
			});
		}
	},
	ondragenter: (e) => {
		activities.set(el, true);
		dragenter(e);
	},
	ondropmove: (event) => {
		if (!activities.get(el)) {
			return;
		}
		event.stopPropagation();
		event.stopImmediatePropagation();

		const { x, y } = event.dragmove.client;
		const {
			x: rectX,
			y: rectY,
			width,
			height
		} = event.target.getBoundingClientRect();
		const insideX = x - rectX;
		const insideY = y - rectY;
		const dir = findDiagonalQuadrant({
			height,
			width,
			x: insideX,
			y: insideY
		});
		event.dropzone.quadrant = dir;
		dropMoveThrottle({
			id,
			width,
			height,
			insideX,
			insideY,
			dispatch
		});
	},
	ondragleave: (event) => {
		activities.set(el, false);
		dragleave(event);
		dispatch('UIB_DND_DROPZONE#CLEAR_ACTIVE_DROPZONE');
	},
	checker: checker('leaf')
});
const attachDrop = ({ el, options }) => {
	interact(el).dropzone(options);
};

export const handleInsert = ({ el, tag, options, ...rest }) => {
	el.dndTag = tag;
	attachDrop({ el, options: options({ el, ...rest }) });
};

export const handleUpdate = ({ el, tag, options, ...rest }) => {
	if (tag !== el.dndTag) {
		el.dndTag = tag;
		attachDrop({ el, options: options({ el, ...rest }) });
	}
};
