import { createCustomElement } from '@servicenow/ui-core';

const view = (state) => {
	const { properties } = state;
	const { isRoot } = properties;
	const providerId = `@${properties.id}_information`;

	if (isRoot) {
		return (
			<sn-uib-read-only-element-wrapper {...properties}>
				<slot />
			</sn-uib-read-only-element-wrapper>
		);
	}
	const leafProperties = {
		storeRef: true,
		slotName: `${providerId}/slotName`,
		parentNodeId: `${providerId}/parent-node-id`,

		...properties
	};

	return (
		<sn-uib-dnd-element-droppable
			{...properties}
			slotStyles={`${providerId}/slotStyles`}
			slotName={`${providerId}/slotName`}
			parentElement={`${providerId}/parentElement`}
			add-next-to-element-id={'@uib-app/addNextToElementId'}
			direction-to-add={'@uib-app/directionToAdd'}>
			<sn-uib-layout-drag-leaf {...leafProperties}>
				<slot />
			</sn-uib-layout-drag-leaf>
		</sn-uib-dnd-element-droppable>
	);
};

export default createCustomElement('sn-uib-dnd-element-wrapper', {
	view,
	properties: {
		id: {
			default: null
		},
		elementInstanceName: {
			default: ''
		},
		elementComponentIcon: {
			default: ''
		},
		elementStyles: {
			default: []
		},
		isRoot: {
			default: false
		},
		disabled: {
			default: false
		},
		isHovered: {
			default: false
		},
		selected: {
			default: false
		},

		activeDirection: {
			default: null
		},
		keyboardDrag: {
			default: null
		},
		isDragging: {
			default: false
		},
		activeId: {
			default: null
		},
		parentId: {
			default: null
		}
	}
});
