import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-content-tree';
import '@servicenow/now-input';
import '@servicenow/now-toggle';
import styles from './sn-uib-app-page-route-config.scss';
import { t } from 'sn-translate';
import get from 'lodash/get';
import { STRING_PROPERTY_VALUE_CHANGED } from '../sn-uib-string-property-input';
import { JSON_PROPERTY_VALUE_CHANGED } from '../sn-uib-json-property-input';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import { PROPERTY_BINDING_BINDING_SET } from '../sn-uib-dynamic-property-input';
import { getPathForId } from '../sn-uib-page-management-panel/utils';
import { wrapPropertyContainer } from '../sn-uib-properties-editor-panel/utils';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

const getTagName = (value, fallbackTag) => {
	const valueIsDynamic =
		value && typeof value === 'string' && value.startsWith('@');
	return valueIsDynamic ? 'sn-uib-dynamic-property-input' : fallbackTag;
};

const view = ({
	properties: {
		pages,
		currentRoute,
		routeProperties,
		advancedRouteProperties,
		bindings,
		external
	},
	isAdvancedMode,
	externalSelected
}) => {
	const contentTreeItems = pages.reduce(
		(acc, page) => {
			acc[0].children.push({
				label: page.name,
				id: page.route
			});
			return acc;
		},
		[
			{ label: t('App routes'), id: 'internal', children: [] },
			{ label: t('External URL'), id: 'external_url' }
		]
	);
	// eslint-disable-next-line no-unused-vars
	const RouteInputTagName = getTagName(
		currentRoute,
		'sn-uib-string-property-input'
	);
	const url = get(external, 'url', '');
	const UrlInputTagName = getTagName(url, 'sn-uib-string-property-input'); // eslint-disable-line no-unused-vars
	const currentSelectedItem = externalSelected
		? ['external_url']
		: getPathForId(contentTreeItems, currentRoute);

	return (
		<div className="app-page-route-config-container">
			<div className="app-page-route-config-toggle-section">
				{t('Advanced mode')}
				<now-toggle
					size="sm"
					disabled={currentSelectedItem[0] === 'external_url'}
					checked={isAdvancedMode}
					manage-checked={true}
					configAria={{ 'aria-label': t('Advanced mode') }}
				/>
			</div>
			{isAdvancedMode ? (
				<div className="app-page-route-config-config-section">
					<section className="app-page-route-config-props-pane --advanced">
						{wrapPropertyContainer(
							<RouteInputTagName
								key={'route-advanced'}
								id={'route'}
								label={t('route')}
								value={currentRoute || null}
								isStatic={
									!(
										typeof currentRoute === 'string' &&
										currentRoute.startsWith('@')
									)
								}
								bindings={bindings}
								dataBindingEnabled={true}
								scriptingEnabled={false}
								append-to-payload={{
									id: 'route',
									context: 'advancedRouteConfig'
								}}
								slot="content"
							/>
						)}
						{advancedRouteProperties.map((property) => {
							const { name, value, isStatic } = property;
							//eslint-disable-next-line no-unused-vars
							const TagName = isStatic
								? 'sn-uib-json-property-input'
								: 'sn-uib-dynamic-property-input';
							return wrapPropertyContainer(
								<TagName
									key={`${name}-advanced`}
									id={name}
									label={t('{0}', name)}
									value={value || null}
									isStatic={isStatic}
									bindings={bindings}
									dataBindingEnabled={true}
									scriptingEnabled={false}
									append-to-payload={{
										id: name,
										context: 'advancedRouteConfig'
									}}
									slot="content"
								/>
							);
						})}
					</section>
				</div>
			) : (
				<div className="app-page-route-config-config-section">
					<section className="app-page-route-config-route-pane">
						<div className="app-page-route-config-header">
							{t('Choose a destination')}
						</div>
						<now-content-tree
							items={contentTreeItems}
							selected-items={[currentSelectedItem]}
							manage-selected-items={true}
							trigger-icon="caret"
							highlight-selected-parent={true}
						/>
					</section>
					<section className="app-page-route-config-props-pane">
						<div className="app-page-route-config-header">
							{t('Chosen route')}
						</div>
						{externalSelected ? (
							wrapPropertyContainer(
								<UrlInputTagName
									id="external-url"
									label={t('External URL')}
									value={url || null}
									isStatic={!(typeof url === 'string' && url.startsWith('@'))}
									bindings={bindings}
									dataBindingEnabled={true}
									scriptingEnabled={false}
									append-to-payload={{
										id: 'external-url',
										context: 'externalRouteConfig'
									}}
									slot="content"
								/>
							)
						) : (
							<div>
								<now-input
									className="app-page-route-config-route-input"
									disabled={true}
									value={
										currentRoute
											? `/${currentRoute}`
											: t('Please select a page')
									}
								/>
								{routeProperties.map((property) => {
									const { name, value, isStatic, type } = property;
									// eslint-disable-next-line no-unused-vars
									const TagName = isStatic
										? 'sn-uib-string-property-input'
										: 'sn-uib-dynamic-property-input';
									const label =
										type === 'field' ? t('{0} *', name) : t('{0}', name);
									return wrapPropertyContainer(
										<TagName
											key={`${currentRoute}-${name}`}
											id={name}
											label={label}
											value={value}
											isStatic={isStatic}
											bindings={bindings}
											dataBindingEnabled={true}
											scriptingEnabled={false}
											append-to-payload={{ id: name, context: 'routeConfig' }}
											slot="content"
										/>
									);
								})}
							</div>
						)}
					</section>
				</div>
			)}
		</div>
	);
};

createCustomElement('sn-uib-app-page-route-config', {
	view,
	styles,
	initialState: {
		isAdvancedMode: false,
		externalSelected: false
	},
	properties: {
		pages: {
			default: []
		},
		currentRoute: {
			default: ''
		},
		routeProperties: {
			default: []
		},
		bindings: {
			default: []
		},
		advancedRouteProperties: {
			default: []
		},
		external: {
			default: {}
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ properties, updateState }) => {
			const { currentRoute, external, pages } = properties;
			const targetRoute =
				pages.find((page) => page.route === currentRoute) || null;
			const url = get(external, 'url', '');
			batchShorthandUpdateState(
				{
					isAdvancedMode: currentRoute && !targetRoute && !url,
					externalSelected: !!url
				},
				updateState
			);
		},
		'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET': ({ action }) => {
			action.stopPropagation();
		},
		'NOW_CONTENT_TREE#ITEM_CLICKED': ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			const {
				payload: { item }
			} = action;
			if (action.payload.item.id === 'external_url') {
				updateState({ externalSelected: true });
			} else if (action.payload.item.id === 'internal') {
				return;
			} else {
				updateState({ externalSelected: false });
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_CHANGED', {
					route: item.id
				});
			}
		},
		'NOW_TOGGLE#CHECKED_SET': ({ action, state, updateState, dispatch }) => {
			action.stopPropagation();
			updateState({ isAdvancedMode: !state.isAdvancedMode });
			dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ADVANCED_MODE_TOGGLED');
		},
		[STRING_PROPERTY_VALUE_CHANGED]: ({ action, dispatch, properties }) => {
			action.stopPropagation();
			if (action.payload.context === 'routeConfig') {
				const { routeProperties } = properties;
				const targetRouteProperty = routeProperties.find(
					(routeProp) => routeProp.name === action.payload.id
				);
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_PROPERTY_CHANGED', {
					...action.payload,
					name: targetRouteProperty.name,
					type: targetRouteProperty.type
				});
			} else if (action.payload.context === 'advancedRouteConfig') {
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_CHANGED', {
					route: action.payload.value
				});
			} else if (action.payload.context === 'externalRouteConfig') {
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#URL_PROPERTY_CHANGED', {
					value: action.payload.value
				});
			}
		},
		[JSON_PROPERTY_VALUE_CHANGED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ADVANCED_ROUTE_PROPERTY_CHANGED', {
				name: action.payload.id,
				value: action.payload.value
			});
		},
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: ({
			action,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const { propertyId, isStatic } = action.payload;

			if (propertyId === 'route') {
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_CHANGED', {
					route: isStatic ? '@' : ''
				});
			} else if (propertyId === 'fields' || propertyId === 'params') {
				dispatch(
					'SN_UIB_APP_PAGE_ROUTE_CONFIG#ADVANCED_ROUTE_PROPERTY_CHANGED',
					{
						name: action.payload.propertyId,
						value: isStatic ? '@' : ''
					}
				);
			} else if (propertyId === 'external-url') {
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#URL_PROPERTY_CHANGED', {
					value: isStatic ? '@' : ''
				});
			} else {
				const { routeProperties } = properties;
				const targetRouteProperty = routeProperties.find(
					(routeProp) => routeProp.name === propertyId
				);
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_PROPERTY_CHANGED', {
					name: targetRouteProperty.name,
					type: targetRouteProperty.type,
					value: isStatic ? '@' : ''
				});
			}
		},
		[PROPERTY_BINDING_BINDING_SET]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			if (action.payload.context === 'routeConfig') {
				const { routeProperties } = properties;
				const targetRouteProperty = routeProperties.find(
					(routeProp) => routeProp.name === action.payload.id
				);
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_PROPERTY_CHANGED', {
					name: targetRouteProperty.name,
					type: targetRouteProperty.type,
					value: action.payload.value
				});
			} else if (action.payload.context === 'advancedRouteConfig') {
				if (action.payload.id === 'route') {
					dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_CHANGED', {
						route: action.payload.value
					});
				} else if (
					action.payload.id === 'fields' ||
					action.payload.id === 'params'
				) {
					dispatch(
						'SN_UIB_APP_PAGE_ROUTE_CONFIG#ADVANCED_ROUTE_PROPERTY_CHANGED',
						{
							name: action.payload.id,
							value: action.payload.value
						}
					);
				}
			} else if (action.payload.context === 'externalRouteConfig') {
				dispatch('SN_UIB_APP_PAGE_ROUTE_CONFIG#URL_PROPERTY_CHANGED', {
					value: action.payload.value
				});
			}
		}
	}
});
