export const filterPages = (pages, search) => {
	const searchExpression = new RegExp(search.trim(), 'i');
	return pages
		.map((page) => filterPagesHelper(page, searchExpression))
		.filter((page) => Object.keys(page).length > 0);
};

const filterPagesHelper = (child, search) => {
	let newChildren;
	const { label, children } = child;
	if (label.search(search) > -1) {
		return child;
	}

	if (children) {
		newChildren = children
			.map((child) => filterPagesHelper(child, search))
			.filter((child) => Object.keys(child).length > 0);
		if (newChildren.length > 0) {
			return { ...child, children: newChildren };
		}
	}

	return {};
};

export const expandAllItems = (pages, paths = [], currentPath = []) => {
	pages.forEach(({ children, id }) => {
		if (children) {
			paths.push([...currentPath, id]);
			expandAllItems(children, paths, [...currentPath, id]);
		}
	});
	return paths;
};
