import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import {
	getQuadrant,
	isInRectangle,
	dropmove,
	dragleave,
	dragenter,
	ondrop
} from './utils';
import throttle from 'lodash/throttle';
import interact from 'interactjs';
import cuid from 'cuid';

const droppedEffect = ({ state, action, dispatch }) => {
	const { droppable } = state.behaviors;
	const { data } = action.payload;
	dispatch('UIB_DND_DROPZONE#DROPPED', {
		dataTransfer: data,
		direction: droppable.activeRegion
	});

	dispatch('UIB_DND_DROPZONE#CLEAR_REGIONS');
};

const dragLeaveEffect = ({ action, dispatch }) => {
	if (action.payload && isInRectangle(action.payload)) {
		return;
	}
	dispatch('UIB_DND_DROPZONE#CLEAR_REGIONS');
};
const clearEffect = (state) => {
	const { droppable } = state.behaviors;
	return {
		...state,
		behaviors: {
			droppable: { ...droppable, activeRegion: null, active: false }
		},
		active: false
	};
};
const dragEnterEffect = (state) => {
	const { droppable } = state.behaviors;
	return {
		...state,
		behaviors: {
			droppable: { ...droppable, active: true }
		},
		active: true
	};
};

const dragOverEffect = ({
	state,
	dispatch,
	action: {
		payload: { rect, x, y }
	}
}) => {
	const {
		properties: { isEmpty, left, right, top, bottom },
		active
	} = state;

	if (active && isEmpty && isInRectangle({ rect, x, y })) {
		dispatch('UIB_DND_DROPZONE#DRAG_ENTER');
	}
	if (!active || isEmpty) {
		// This catch exists to prevent the dragover actions to fire when the zone is deactivated.
		// Which sometimes happens when dragging out of the drag area.
		return;
	}

	const quadrant = getQuadrant({ rect, x, y }, { left, right, top, bottom });

	dispatch('UIB_DND_DROPZONE#ACTIVE_OVER_REGION', { activeRegion: quadrant });
};

const updateRegion = (state, { payload }) => {
	const { droppable } = state.behaviors;

	// This is a guard to make sure we don't set an active region after drops occur
	// It's here and not in a dispatched active because I wanted a synchronous check
	const activeRegion = state.active ? payload.activeRegion : null;

	return {
		...state,
		behaviors: {
			droppable: { ...droppable, activeRegion }
		}
	};
};

const mountedEffect = ({ dispatch, action: { payload } }) => {
	const { el } = payload;

	const cb = throttle((data) => {
		dispatch('UIB_DND_DROPZONE#DRAG_OVER', data);
	}, 100);

	const id = `BHV_${cuid()}`;
	el.dndTag = id;
	interact(el)
		.dropzone({
			ondrop: ondrop(dispatch)
		})
		.on('dragenter', (e) => {
			if (!e.dragEvent.transfer) {
				e.stopPropagation();
				e.stopImmediatePropagation();
				e.preventDefault();
				e.reject();
				return;
			}
			dragenter(e, dispatch, id);
			dispatch('UIB_DND_DROPZONE#DRAG_ENTER');
		})
		.on('dropmove', (e) => {
			dispatch('UIB_DND_DROPZONE#HOVER', {
				id
			});
			dropmove(e, cb);
		})
		.on('dragleave', (e) => {
			dragleave(e);
			dispatch('UIB_DND_DROPZONE#CLEAR_REGIONS');
			dispatch('UIB_DND_DROPZONE#CLEAR_ACTIVE_DROPZONE');
		});
};

const unmountedEffect = ({
	action: {
		payload: { el }
	}
}) => interact(el).unset();

const updatePropsEffect = (state, { payload = {} }) => ({
	...state,
	properties: {
		...state.properties,
		...payload
	}
});

export const droppedHandler = { effect: droppedEffect };
export const dragOverHandler = { effect: dragOverEffect };
export const mountedHandler = { effect: mountedEffect };
export const unmountedHandler = { effect: unmountedEffect };
export const dragLeaveHandler = { effect: dragLeaveEffect };
export const updateRegionHandler = createUpdateStateEffect(updateRegion);
export const dragEnterHandler = createUpdateStateEffect(dragEnterEffect);
export const clearRegionsHandler = createUpdateStateEffect(clearEffect);
export const updatePropsHandler = createUpdateStateEffect(updatePropsEffect);
