import { actionTypes } from '@servicenow/ui-core';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';

import {
	NOW_SELECT_SELECTED_ITEM_SET,
	SN_UIB_VIEWPORT_DEFAULT_CHANGED,
	SN_UIB_DYNAMIC_INPUT_CHANGED,
	SN_UIB_VIEWPORT_DATA_SOURCE_CHANGED,
	NOW_BUTTON_CLICKED,
	NOW_BUTTON_BARE_CLICKED,
	SN_UIB_CREATE_PAGE_REQUESTED
} from '../actions.js';
import {
	DEFAULT_ROUTE_DROPDOWN,
	PILLAR_STATIC_ROUTE_MODAL,
	PILLAR_DYNAMIC_ROUTE_MODAL,
	ISOLATED_EDIT_MODE
} from '../constants';

/**
 * Analyze whether the page state is dirty
 * and either trigger a page save / discard modal after which new tab creation modal
 * or directly trigger the new tab creation modal
 *
 * @param {Object} args
 * @param {function} args.dispatch Dispatch function for seismic component framwork
 * @param {Boolean} args.isPageStateDirty to determine if page state is dirty or clean (page save needed?)
 * @param {Object} args.metadata information required by the new tab creation modal
 * @returns {null}
 */
const createNewTabWithOrWithoutSave = ({
	dispatch,
	metadata: { elementId, elementProperties, payloadType }
}) => {
	const payload = {
		elementId,
		elementProperties,
		createAsTab: true,
		type:
			payloadType === PILLAR_DYNAMIC_ROUTE_MODAL ? 'dynamicTab' : 'staticTab'
	};

	dispatch(SN_UIB_CREATE_PAGE_REQUESTED, payload);
};

export default {
	[NOW_BUTTON_CLICKED]: ({
		action,
		dispatch,
		properties: { viewportRoutes, elementRef }
	}) => {
		if (get(action, 'payload.type', '') === ISOLATED_EDIT_MODE) {
			action.stopPropagation();
			const updatedPayload = {
				...action.payload
			};

			if (!isEmpty(viewportRoutes)) {
				const { width } = elementRef
					? elementRef.current.getBoundingClientRect()
					: {};

				if (width) {
					updatedPayload.width = width;
				}
			} else {
				updatedPayload.isolatedEditMode = true;
			}

			dispatch('SN_UIB_VIEWPORT#ISOLATED_EDIT', updatedPayload);
		}
	},
	[NOW_SELECT_SELECTED_ITEM_SET]: ({ action, dispatch }) => {
		if (get(action, 'payload.type', '') === DEFAULT_ROUTE_DROPDOWN) {
			action.stopPropagation();
			dispatch(SN_UIB_VIEWPORT_DEFAULT_CHANGED, action.payload);
		}
	},
	[NOW_BUTTON_BARE_CLICKED]: ({ action, dispatch }) => {
		const payloadType = get(action, 'payload.type', '');
		const elementId = get(action, 'payload.elementId', null);
		const elementProperties = get(action, 'payload.elementProperties', {});

		if (!elementId) {
			console.warn(
				'[CREATE_TAB]',
				'Add button clicked, but no elementId found. Properties:',
				elementProperties
			);
			return;
		}

		if (
			payloadType === PILLAR_STATIC_ROUTE_MODAL ||
			payloadType === PILLAR_DYNAMIC_ROUTE_MODAL
		) {
			// Create a new tab
			// A page save modal maybe triggered if the page is in a dirty state
			createNewTabWithOrWithoutSave({
				dispatch,
				metadata: { elementId, elementProperties, payloadType }
			});
		}
	},
	[SN_UIB_DYNAMIC_INPUT_CHANGED]: ({
		action: { payload },
		dispatch,
		properties
	}) => {
		const { value, propertyName } = payload;
		const {
			appConfig: { elementId }
		} = properties;
		if (propertyName === 'dataSourceValue') {
			dispatch(SN_UIB_VIEWPORT_DATA_SOURCE_CHANGED, {
				value,
				elementId
			});
		}
	},
	[actionTypes.COMPONENT_DOM_READY]: ({ action, dispatch, properties }) => {
		// For recalculating the latest repeatedTabdata from the data source when a component is clicked
		const { elementId } = action.payload;
		const { viewportDataSource } = properties;
		if (!isEmpty(viewportDataSource)) {
			dispatch(SN_UIB_VIEWPORT_DATA_SOURCE_CHANGED, {
				value: viewportDataSource,
				elementId
			});
		}
	}
};
