import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-uib-client-state-configuration-panel.scss';
import '../sn-uib-shelf-pane';
import '../sn-uib-client-state-params-pane';
import '../sn-uib-client-state-preview-pane';
import * as snUIBDynamicPropertyInput from '../sn-uib-dynamic-property-input';
import {
	PARAM_ROW_DELETE_REQUESTED,
	PARAM_ROW_PARAMETER_CHANGED,
	PARAM_ROW_AUTOFOCUS_RESET
} from '../sn-uib-param-row/sn-uib-param-row';

export const STATE_CONFIG_PANEL_PARAMETER_CREATED =
	'SN_UIB_STATE_CONFIGURATION_PANEL#PARAMETER_CREATED';
export const STATE_CONFIG_PANEL_PARAMETER_DELETED =
	'SN_UIB_STATE_CONFIGURATION_PANEL#PARAMETER_DELETED';
export const STATE_CONFIG_PANEL_PARAMETER_CHANGED =
	'SN_UIB_STATE_CONFIGURATION_PANEL#PARAMETER_CHANGED';
const STATE_CONFIG_PANEL_ADD_REQUESTED =
	'SN_UIB_STATE_CONFIGURATION_PANEL#ADD_PARAMETER_REQUESTED';
export const STATE_CONFIG_PANEL_PARAMETER_CONTEXT_BINDINGS_REQUESTED =
	'STATE_CONFIG_PANEL#PARAMETER_CONTEXT_BINDINGS_REQUESTED';
const ADD = 'add';
const view = (state) => {
	const { autofocusId, properties } = state;
	const {
		clientStateParameters,
		clientStateData,
		bindings,
		disabled
	} = properties;
	return (
		<div className="client-state-config-panel-container">
			<sn-uib-shelf-pane
				id="client-state-params"
				headerLabel={t('Client state parameters')}
				headerButtons={[
					{
						label: t('Add'),
						id: ADD,
						icon: 'plus-fill',
						clickActionType: STATE_CONFIG_PANEL_ADD_REQUESTED
					}
				]}
				disabledButtons={disabled ? [ADD] : []}>
				<sn-uib-client-state-params-pane
					slot="content"
					parameters={clientStateParameters}
					bindings={bindings}
					autofocus-id={autofocusId}
					disabled={disabled}
				/>
			</sn-uib-shelf-pane>
			<sn-uib-shelf-pane
				id="client-state-preview"
				headerLabel={t('Client state preview')}>
				<sn-uib-client-state-preview-pane
					slot="content"
					preview={clientStateData}
				/>
			</sn-uib-shelf-pane>
		</div>
	);
};

export default createCustomElement('sn-uib-client-state-configuration-panel', {
	view,
	styles,
	initialState: {
		autofocusId: null
	},
	properties: {
		clientStateParameters: {
			schema: {
				type: 'array'
			},
			default: []
		},
		clientStateData: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		bindings: {
			schema: {
				type: 'array'
			},
			default: []
		},
		disabled: {
			schema: {
				type: 'boolean'
			},
			default: false
		}
	},
	actionHandlers: {
		[STATE_CONFIG_PANEL_ADD_REQUESTED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(STATE_CONFIG_PANEL_PARAMETER_CREATED);
		},
		[PARAM_ROW_DELETE_REQUESTED]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const { clientStateParameters } = properties;
			const paramToDelete = clientStateParameters.find(
				(param) => param.id === action.payload.id
			);
			paramToDelete &&
				dispatch(STATE_CONFIG_PANEL_PARAMETER_DELETED, {
					id: paramToDelete.id,
					name: paramToDelete.name || t('parameter')
				});
		},
		[PARAM_ROW_PARAMETER_CHANGED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(STATE_CONFIG_PANEL_PARAMETER_CHANGED, action.payload);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const { name, value = [], previousValue = [] } = action.payload;
			// when a new param is added, autofocus its name input
			if (
				name === 'clientStateParameters' &&
				value.length === previousValue.length + 1
			) {
				const newParam = value[value.length - 1];
				updateState({ autofocusId: newParam.id });
			}
		},
		[PARAM_ROW_AUTOFOCUS_RESET]: ({ updateState }) => {
			updateState({ autofocusId: null });
		},
		[snUIBDynamicPropertyInput.DYNAMIC_PROPERTY_INPUT_FOCUSED]: ({
			action,
			dispatch
		}) => {
			const { id } = action.payload;
			dispatch(STATE_CONFIG_PANEL_PARAMETER_CONTEXT_BINDINGS_REQUESTED, {
				propertyId: id
			});
		}
	}
});
