import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import _startCase from 'lodash/startCase';
import styles from './sn-uib-param-row.scss';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import { PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED } from '../sn-uib-properties-editor-panel';

export const PARAM_ROW_PARAMETER_CHANGED = 'SN_UIB_PARAM_ROW#PARAMETER_CHANGED';
export const PARAM_ROW_DELETE_REQUESTED =
	'SN_UIB_PARAM_ROW#DELETE_PARAMETER_REQUESTED';
export const PARAM_ROW_AUTOFOCUS_RESET =
	'SN_UIB_PARAM_ROW#NAME_INPUT_AUTOFOCUS_RESET';

const SUPPORTED_PROPERTY_TYPES = [
	'string',
	'number',
	'boolean',
	'json',
	'reference',
	'condition_string',
	'table_name'
];
const getParamTypes = (propTypes = []) => {
	return propTypes
		.filter((type) => SUPPORTED_PROPERTY_TYPES.includes(type))
		.map((type) => ({
			id: type,
			label: type === 'json' ? 'JSON' : _startCase(type)
		}));
};

const parameterUpdatedHandler = ({ action, dispatch }) => {
	action.stopPropagation();
	action.payload.context === 'param-value' &&
		dispatch(PARAM_ROW_PARAMETER_CHANGED, action.payload);
};

const renderValueInput = (
	type,
	value = null,
	id,
	bindings,
	disabled,
	isStatic
) => {
	const properties = [
		{ id, label: t('Initial value'), value, disabled, type, isStatic }
	];
	return (
		<sn-uib-properties-editor-panel
			properties={properties}
			dataBindingEnabled={true}
			bindings={bindings}
			scriptingEnabled={false}
			append-to-payload={{ context: 'param-value', id }}
		/>
	);
};

export default createCustomElement('sn-uib-param-row', {
	view: (state, { dispatch }) => {
		const {
			name,
			label,
			id,
			type,
			interfaceApi,
			propInputIsStatic,
			initialValue,
			bindings,
			isFocused,
			supportedInputs,
			supportedParamTypes,
			showCardOutline,
			interfaceApiNames,
			disabled
		} = state.properties;
		const paramRowClasses = {
			'sn-uib-param-row': true,
			'show-card-outline': !!showCardOutline,
			'no-card-outline': !showCardOutline
		};
		const paramTypes = getParamTypes(supportedParamTypes);

		return (
			<div class={paramRowClasses}>
				{supportedInputs.includes('name') ? (
					<div className="name-input" id={type}>
						<now-input
							key={`param-${id}-name--${isFocused}`}
							label={t('Name')}
							placeholder={t('Enter a name')}
							value={name || ''}
							append-to-payload={{ context: 'param-name', id }}
							maxlength={255}
							autofocus={isFocused}
							on-blur={() => dispatch(PARAM_ROW_AUTOFOCUS_RESET)}
							disabled={disabled}
						/>
					</div>
				) : null}
				{supportedInputs.includes('label') ? (
					<div className="label-input">
						<now-input
							key={`param-${id}-label`}
							label={t('Label')}
							placeholder={t('Enter a label')}
							value={label || ''}
							append-to-payload={{ context: 'param-label', id }}
							maxlength={255}
							disabled={!name || disabled}
						/>
					</div>
				) : null}
				{supportedInputs.includes('type') ? (
					<div className="dropdown-wrapper --type" id={type}>
						<now-select
							manage-selected-item
							id={`param-${id}-type`}
							aria-labelledby={`param-${id}-type-label`}
							disabled={!name || disabled}
							items={paramTypes}
							label={t('Type')}
							selected-item={type ? type : 'string'}
							append-to-payload={{ context: 'param-type', id }}
						/>
					</div>
				) : null}
				{supportedInputs.includes('interfaceApi') ? (
					<div className="dropdown-wrapper --interface-api">
						<now-select
							manage-selected-item
							id={`param-${id}-interface`}
							aria-labelledby={`param-${id}-interface-label`}
							disabled={!name || disabled}
							label={t('Interface API name')}
							bare={true}
							items={interfaceApiNames}
							selected-item={interfaceApi ? interfaceApi : ''}
							append-to-payload={{ context: 'param-interfaceApi', id }}
						/>
					</div>
				) : null}
				{supportedInputs.includes('value') ? (
					<div className="value-input" id={type}>
						{renderValueInput(
							type,
							initialValue,
							id,
							bindings,
							!name || disabled,
							propInputIsStatic
						)}
					</div>
				) : null}
				<now-button-iconic
					className="delete-button"
					tooltip-content={t('Delete {0}', name || '')}
					variant="tertiary"
					icon="trash-outline"
					append-to-payload={{ context: 'param-delete', id }}
					disabled={disabled}
				/>
			</div>
		);
	},
	properties: {
		name: {
			schema: { type: 'string' },
			default: ''
		},
		id: {
			schema: { type: 'string' },
			default: ''
		},
		type: {
			schema: { type: 'string' },
			default: ''
		},
		label: {
			schema: { type: 'string' },
			default: ''
		},
		interfaceApi: {
			schema: { type: 'string' },
			default: ''
		},
		initialValue: {
			schema: {
				anyOf: [
					{ type: 'string' },
					{ type: 'number' },
					{ type: 'boolean' },
					{ type: 'object' },
					{ type: 'array' },
					{ type: 'null' }
				]
			},
			default: null
		},
		propInputIsStatic: {
			computed({ properties: { initialValue } }) {
				if (
					initialValue &&
					typeof initialValue === 'string' &&
					initialValue.startsWith('@')
				) {
					return false;
				} else {
					return true;
				}
			}
		},
		disabled: {
			schema: { type: 'boolean' },
			default: false
		},
		bindings: {
			schema: { type: 'array' },
			default: []
		},
		isFocused: {
			schema: { type: 'boolean' },
			default: false
		},
		supportedInputs: {
			schema: { type: 'array' },
			default: ['name', 'type']
		},
		supportedParamTypes: {
			schema: { type: 'array' },
			default: []
		},
		showCardOutline: {
			schema: { type: 'boolean' },
			default: false
		},
		interfaceApiNames: {
			schema: { type: 'array' },
			default: []
		}
	},
	styles,
	actionHandlers: {
		'NOW_INPUT#VALUE_SET': ({ action, dispatch }) => {
			action.stopPropagation();
			const {
				payload: { id, value, context }
			} = action;
			if (context === 'param-name' || context === 'param-label') {
				dispatch(PARAM_ROW_PARAMETER_CHANGED, {
					id,
					value,
					context,
					noDebounce: true
				});
			}
		},
		'NOW_SELECT#SELECTED_ITEM_SET': ({ action, dispatch }) => {
			action.stopPropagation();
			const {
				payload: { id, item, context }
			} = action;
			if (context === 'param-type' || context === 'param-interfaceApi') {
				dispatch(PARAM_ROW_PARAMETER_CHANGED, { id, value: item.id, context });
			}
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			const { context, id } = action.payload;
			context === 'param-delete' &&
				dispatch(PARAM_ROW_DELETE_REQUESTED, { id });
		},
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			const { propertyId, isStatic } = action.payload;

			dispatch(PARAM_ROW_PARAMETER_CHANGED, {
				id: propertyId,
				value: isStatic ? '@' : null,
				context: 'param-value'
			});
		},
		[PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED]: parameterUpdatedHandler
	}
});
