export const ROOT_NODE = 'ROOT_NODE';
export const ROOT_NODE_KEY = 'ROOT_NODE_KEY';
const stubChildren = (arr = []) => {
	return arr.map(({ id }) => id);
};
const mapNodes = (tree, parent = ROOT_NODE, index = 0, accumulatedMap = {}) => {
	const { id, children = [] } = tree;
	const currentNode = {
		...tree,
		parent,
		index,
		children: stubChildren(children)
	};
	const rootNode = parent === ROOT_NODE ? { ROOT_NODE_KEY: currentNode } : {};
	return tree.children.reduce(
		(acc, node, i) => {
			return mapNodes(node, id, i, acc);
		},
		{ ...accumulatedMap, [id]: currentNode, ...rootNode }
	);
};

export const pageTreeFlatten = (tree = { children: [] }) => {
	return mapNodes(tree);
};
