import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import _capitalize from 'lodash/capitalize';
import isNull from 'lodash/isNull';
import isUndefined from 'lodash/isUndefined';
import get from 'lodash/get';
import styles from './sn-uib-route-config.scss';
import '../sn-uib-app-page-route-config';
import '@servicenow/now-button';
import '@servicenow/now-input';
import '@servicenow/now-modal';
import '@servicenow/now-textarea';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

import { HANDLER_VALUE_CHANGED } from '../sn-uib-event-mapping-handler-values-config';
const ROUTE_OK_CLICKED = 'SN_UIB_ROUTE_CONFIG#MODAL_OK_CLICKED';
const ROUTE_CANCEL_CLICKED = 'SN_UIB_ROUTE_CONFIG#MODAL_CANCEL_CLICKED';

const FIELD_PROPERTY_TYPE = 'field';
const PARAM_PROPERTY_TYPE = 'param';

const renderFieldValueInputs = (obj, messages = []) =>
	Object.entries(obj).map(([field, value]) => (
		<section className="route-config-current-route-item">
			<now-input
				id={`route-property-${field}`}
				disabled={true}
				label={_capitalize(t('{0}', field))}
				value={value}
				className="route-config-current-route-item-input"
				messages={messages}
			/>
		</section>
	));

const renderFieldValueTextAreas = (label, value) => (
	<section className="route-config-current-route-item">
		<now-textarea
			id={`route-property-${label}`}
			disabled={true}
			label={t('{0}', label)}
			value={value}
			resize="none"
			rows="1"
			className="route-config-current-route-item-textarea"
		/>
	</section>
);

// eslint-disable-next-line no-unused-vars
const CurrentRouteConfig = function ({
	route,
	fields,
	params,
	appConfig: { appRoutes },
	external
}) {
	const targetRoute =
		appRoutes.find((appRoute) => appRoute.routeType === route) || null;
	const parsedFields = resolveJSON(fields);
	const parsedParams = resolveJSON(params);
	const parsedExternal = resolveJSON(external);

	if (
		!isNull(parsedExternal) &&
		!isUndefined(parsedExternal) &&
		parsedExternal !== {}
	) {
		return renderFieldValueInputs({ 'External URL': parsedExternal.url });
	} else if (route && !targetRoute) {
		return (
			<div>
				{renderFieldValueInputs({ Route: `/${route}` })}
				{fields ? renderFieldValueTextAreas('Fields', fields) : null}
				{params ? renderFieldValueTextAreas('Parameters', params) : null}
			</div>
		);
	} else {
		let messages = [];
		const targetFields = targetRoute ? targetRoute.fields || {} : {};
		for (const [_, field] of Object.entries(targetFields)) {
			if (!get(parsedFields, `${field.name}`)) {
				messages = [
					{
						status: 'warning',
						/* prettier-ignore */
						content: t('This destination is missing values for required parameters')
					}
				];
			}
		}
		return (
			<div>
				{route
					? renderFieldValueInputs({ Route: `/${route}` }, messages)
					: null}
				{fields ? renderFieldValueInputs(parsedFields) : null}
				{params ? renderFieldValueInputs(parsedParams) : null}
			</div>
		);
	}
};

const propertiesHaveValues = function ({ route, fields, params, external }) {
	const routeApplied = !isNull(route) && !isUndefined(route) && route !== '';
	const fieldsApplied =
		!isNull(fields) && !isUndefined(fields) && fields !== '';
	const paramsApplied =
		!isNull(params) && !isUndefined(params) && params !== '';
	const externalApplied =
		!isNull(external) && !isUndefined(external) && external !== {};
	return routeApplied || fieldsApplied || paramsApplied || externalApplied;
};

const view = ({
	pages,
	currentRoute,
	routeProperties,
	modalOpened,
	properties,
	advancedRouteProperties,
	currentExternalUrl
}) => {
	const isNew = !propertiesHaveValues(properties);
	const {
		appConfig: { bindings = [], disabled }
	} = properties;

	return (
		<Fragment>
			{modalOpened ? (
				<now-modal
					opened={true}
					manage-opened={true}
					size="lg"
					header-label={t('Configure navigation')}
					footer-actions={[
						{
							label: t('OK'),
							clickActionType: ROUTE_OK_CLICKED,
							variant: 'primary'
						},
						{
							label: t('Cancel'),
							clickActionType: ROUTE_CANCEL_CLICKED,
							variant: 'secondary'
						}
					]}>
					<slot name="defaultSlot">
						<div className="route-config-modal-content">
							<sn-uib-app-page-route-config
								pages={pages}
								route-properties={routeProperties}
								current-route={currentRoute}
								bindings={bindings}
								advancedRouteProperties={advancedRouteProperties}
								external={currentExternalUrl}
							/>
						</div>
					</slot>
				</now-modal>
			) : null}
			{propertiesHaveValues(properties) ? (
				<div className="route-config-current-route-container">
					<section className="route-config-current-route-heading-wrapper">
						<h3 className="route-config-current-route-heading">
							{t('Current route')}
						</h3>
					</section>
					<CurrentRouteConfig {...properties} />
				</div>
			) : null}
			<section className="route-config-current-route-edit-button">
				<now-button
					label={t('{0} destination', isNew ? 'Select' : 'Edit')}
					disabled={disabled}
				/>
			</section>
		</Fragment>
	);
};

const mapPages = function (appRoutes) {
	return appRoutes.map(({ page: { name }, routeType }) => {
		return {
			name,
			route: routeType
		};
	});
};

const mapPropertiesByType = function (type, obj) {
	return Object.entries(obj).map(([key, value]) => {
		return {
			name: key,
			value,
			type,
			isStatic: value && value.startsWith('@') ? false : true
		};
	});
};

const resolveRoutePropertyValues = function (targetRouteProperties, props) {
	if (!targetRouteProperties) {
		return {};
	}
	return Object.values(targetRouteProperties).reduce((acc, { name }) => {
		return {
			...acc,
			[name]: props ? props[name] || null : null
		};
	}, {});
};

const getProperties = function (appRoutes, currentProperties, route, type) {
	const targetRoute =
		appRoutes.find((appRoute) => appRoute.routeType === route) || null;
	if (!targetRoute) {
		return {};
	}
	switch (type) {
		case FIELD_PROPERTY_TYPE:
			return {
				...resolveRoutePropertyValues(targetRoute.fields, currentProperties)
			};
		case PARAM_PROPERTY_TYPE:
			return {
				...resolveRoutePropertyValues(
					targetRoute.optionalParameters,
					currentProperties
				)
			};
	}
};

const transformRouteProperties = function (appRoutes, route, fields, params) {
	const currentFields = getProperties(
		appRoutes,
		fields,
		route,
		FIELD_PROPERTY_TYPE
	);
	const currentParams = getProperties(
		appRoutes,
		params,
		route,
		PARAM_PROPERTY_TYPE
	);
	return [
		...mapPropertiesByType(FIELD_PROPERTY_TYPE, currentFields),
		...mapPropertiesByType(PARAM_PROPERTY_TYPE, currentParams)
	];
};

const transformAdvancedRouteProperties = function (
	appRoutes,
	route,
	fields,
	params
) {
	const targetRoute =
		appRoutes.find((appRoute) => appRoute.routeType === route) || null;
	const fieldsValue = targetRoute ? null : fields;
	const paramsValue = targetRoute ? null : params;
	const isBoundValue = (value) =>
		value && typeof value === 'string' && value.startsWith('@');

	return [
		{
			name: 'fields',
			value: fieldsValue,
			isStatic: !isBoundValue(fieldsValue)
		},
		{ name: 'params', value: paramsValue, isStatic: !isBoundValue(paramsValue) }
	];
};

const resolveJSON = function (value) {
	if (typeof value === 'string') {
		try {
			return JSON.parse(value);
		} catch (e) {
			return value;
		}
	}
	return value;
};

createCustomElement('sn-uib-route-config', {
	view,
	styles,
	initialState: {
		modalOpened: false,
		pages: [],
		routeProperties: [],
		currentRoute: null,
		pendingHandlerValues: {},
		advancedRouteProperties: [],
		currentExternalUrl: null
	},
	transformState(state) {
		const {
			properties: { fields, params, route, appConfig, external },
			pendingHandlerValues
		} = state;
		const { appRoutes } = appConfig;

		const routeToUse =
			'route' in pendingHandlerValues ? pendingHandlerValues.route : route;
		let fieldsToUse, paramsToUse;
		if (
			pendingHandlerValues.route &&
			pendingHandlerValues.fields === null &&
			pendingHandlerValues.params === null
		) {
			fieldsToUse = null;
			paramsToUse = null;
		} else if (
			pendingHandlerValues.route &&
			pendingHandlerValues.route === route
		) {
			fieldsToUse = fields;
			paramsToUse = params;
		} else {
			fieldsToUse =
				'fields' in pendingHandlerValues ? pendingHandlerValues.fields : fields;
			paramsToUse =
				'params' in pendingHandlerValues ? pendingHandlerValues.params : params;
		}

		const parsedFields = resolveJSON(fieldsToUse);
		const parsedParams = resolveJSON(paramsToUse);
		const parsedExternal =
			'external' in pendingHandlerValues
				? resolveJSON(pendingHandlerValues.external)
				: resolveJSON(external);

		return {
			...state,
			currentRoute: routeToUse,
			pages: mapPages(appRoutes),
			routeProperties: transformRouteProperties(
				appRoutes,
				routeToUse,
				parsedFields,
				parsedParams
			),
			advancedRouteProperties: transformAdvancedRouteProperties(
				appRoutes,
				routeToUse,
				fieldsToUse,
				paramsToUse
			),
			currentExternalUrl: parsedExternal
		};
	},
	properties: {
		route: {
			default: null
		},
		fields: {
			default: null
		},
		params: {
			default: null
		},
		external: {
			default: null
		},
		appConfig: {
			default: null
		}
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({ action }) => {
			action.stopPropagation();
		},
		'NOW_BUTTON#CLICKED': ({ action, state, updateState }) => {
			action.stopPropagation();
			updateState({ modalOpened: !state.modalOpened });
		},
		'NOW_MODAL#OPENED_SET': ({ action, state, updateState }) => {
			action.stopPropagation();
			batchShorthandUpdateState(
				{
					modalOpened: !state.modalOpened,
					pendingHandlerValues: {}
				},
				updateState
			);
		},
		'SN_UIB_APP_PAGE_ROUTE_CONFIG#ADVANCED_MODE_TOGGLED': ({
			action,
			updateState
		}) => {
			action.stopPropagation();
			batchShorthandUpdateState(
				{
					pendingHandlerValues: { route: null, fields: null, params: null }
				},
				updateState
			);
		},
		'SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_PROPERTY_CHANGED': ({
			action,
			state,
			properties,
			updateState
		}) => {
			const { name, value, type } = action.payload;
			const { pendingHandlerValues } = state;
			const {
				fields,
				params,
				route,
				appConfig: { appRoutes }
			} = properties;

			// If the currently saved route is dynamically configured and the user is now adding pending changes
			// which are statically configured, do not spread the saved fields and params onto the pending values
			const targetRoute =
				appRoutes.find((appRoute) => appRoute.routeType === route) || null;
			let fieldsToUse = resolveJSON(fields);
			let paramsToUse = resolveJSON(params);
			if (route && !targetRoute) {
				fieldsToUse = {};
				paramsToUse = {};
			}

			// Transform into fields/params object and dispatch
			switch (type) {
				case FIELD_PROPERTY_TYPE:
					updateState({
						pendingHandlerValues: {
							...pendingHandlerValues,
							fields: {
								...fieldsToUse,
								...pendingHandlerValues.fields,
								[name]: value
							}
						}
					});
					break;
				case PARAM_PROPERTY_TYPE:
					updateState({
						pendingHandlerValues: {
							...pendingHandlerValues,
							params: {
								...paramsToUse,
								...pendingHandlerValues.params,
								[name]: value
							}
						}
					});
					break;
			}
		},
		'SN_UIB_APP_PAGE_ROUTE_CONFIG#ADVANCED_ROUTE_PROPERTY_CHANGED': ({
			action,
			state,
			updateState
		}) => {
			action.stopPropagation();
			const { name, value } = action.payload;
			updateState({
				pendingHandlerValues: {
					...state.pendingHandlerValues,
					[name]: value
				}
			});
		},
		[ROUTE_OK_CLICKED]: ({ state, dispatch, updateState }) => {
			for (const [propName, propValue] of Object.entries(
				state.pendingHandlerValues
			)) {
				dispatch(HANDLER_VALUE_CHANGED, {
					handlerValueId: propName,
					value: propValue,
					immediate: true
				});
			}
			updateState({ pendingHandlerValues: {} });
		},
		[ROUTE_CANCEL_CLICKED]: ({ updateState }) => {
			batchShorthandUpdateState(
				{ pendingHandlerValues: {}, modalOpened: false },
				updateState
			);
		},
		'SN_UIB_APP_PAGE_ROUTE_CONFIG#ROUTE_CHANGED': ({
			action,
			updateState,
			properties
		}) => {
			const { route } = action.payload;
			const parsedFields = resolveJSON(properties.fields);
			const parsedParams = resolveJSON(properties.params);
			const newPendingHandlerValues = {
				route,
				fields: null,
				params: null,
				external: null
			};
			if (route === properties.route) {
				newPendingHandlerValues.fields = parsedFields;
				newPendingHandlerValues.params = parsedParams;
			}
			updateState({ pendingHandlerValues: newPendingHandlerValues });
		},
		['SN_UIB_APP_PAGE_ROUTE_CONFIG#URL_PROPERTY_CHANGED']: ({
			action,
			state,
			updateState
		}) => {
			action.stopPropagation();
			updateState({
				pendingHandlerValues: {
					...state.pendingHandlerValues,
					external: { url: action.payload.value }
				}
			});
		}
	}
});
