import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-shelf-pane.scss';
import '@servicenow/now-button';
import '@servicenow/now-popover';

const getButtonConfig = (button, disabled, triggerPopover = false) => {
	const { label, id, icon, clickActionType } = button;
	let buttonProps = { size: 'sm', key: id, disabled, variant: 'tertiary' };
	if (label) {
		buttonProps = {
			ButtonTagName: 'now-button-bare',
			props: { ...buttonProps, iconStart: icon, label }
		};
	} else {
		buttonProps = {
			ButtonTagName: 'now-button-iconic',
			props: { ...buttonProps, bare: true, icon }
		};
	}
	return triggerPopover === true
		? buttonProps
		: {
				...buttonProps,
				props: {
					...buttonProps.props,
					appendToPayload: {
						id,
						actionType: clickActionType,
						context: 'shelf-pane'
					}
				}
		  };
};

export default createCustomElement('sn-uib-shelf-pane', {
	view: ({
		properties: { headerLabel, headerButtons, disabledButtons, collapsed }
	}) => {
		return (
			<div className="shelf-pane">
				<div
					class={{
						'shelf-pane-header': true,
						'no-border': collapsed === true
					}}>
					<span className="header-text">{headerLabel}</span>
					<div className="button-section">
						{headerButtons.map((headerButton) => {
							const {
								id,
								triggerPopover,
								popoverOpened = false,
								TagName,
								popoverActionType
							} = headerButton; // eslint-disable-line no-unused-vars
							const disabled = disabledButtons.some(
								(disabledButtonId) => id === disabledButtonId
							);
							const { ButtonTagName, props } = getButtonConfig(
								headerButton,
								disabled,
								triggerPopover
							); // eslint-disable-line no-unused-vars
							return triggerPopover === true ? (
								<now-popover
									manageOpened
									hideTail
									opened={popoverOpened}
									positions={[{ target: 'bottom-start', content: 'top-start' }]}
									append-to-payload={{
										popoverActionType,
										context: 'shelf-pane'
									}}>
									<ButtonTagName {...props} slot="trigger" />
									<TagName slot="content" />
								</now-popover>
							) : (
								<ButtonTagName {...props} />
							);
						})}
					</div>
				</div>
				<slot name="content" />
				<slot name="error" />
			</div>
		);
	},
	properties: {
		/**
		 * The label to display in header
		 */
		headerLabel: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		/**
		 * The array of buttons to display in header
		 */
		headerButtons: {
			schema: {
				type: 'array'
			},
			default: []
		},
		/**
		 * The array of button ids to disable
		 */
		disabledButtons: {
			schema: {
				type: 'array'
			},
			default: []
		},
		collapsed: {
			schema: {
				type: 'boolean'
			},
			default: false
		}
	},
	actionHandlers: {
		['NOW_BUTTON_ICONIC#CLICKED']: ({ action, dispatch }) => {
			if (action.payload.context === 'shelf-pane') {
				action.stopPropagation();
				const {
					payload: { actionType, id }
				} = action;
				dispatch(actionType, { id });
			}
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ action, dispatch }) => {
			if (action.payload.context === 'shelf-pane') {
				action.stopPropagation();
				const {
					payload: { actionType, id }
				} = action;
				dispatch(actionType, { id });
			}
		},
		['NOW_POPOVER#OPENED_SET']: ({ action, dispatch }) => {
			if (action.payload.context === 'shelf-pane') {
				action.stopPropagation();
				const {
					payload: { popoverActionType, value }
				} = action;
				dispatch(popoverActionType, { opened: value });
			}
		}
	},
	styles
});
