import { createCustomElement } from '@servicenow/ui-core';

import '@servicenow/now-tabs';
import { t } from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';

import '../sn-uib-collapsible-panel';
import '../sn-uib-element-navigator';
import styles from './sn-uib-generic-page-management-panel.scss';
import {
	CREATE_PAGE_BUTTON_CLICKED,
	CREATE_VARIANT_BUTTON_CLICKED,
	PAGE_SELECTED as NAVIGATOR_PAGE_SELECTED,
	COPY_PAGE_CLICKED as NAVIGATOR_COPY_PAGE_CLICKED,
	EDIT_PAGE_CLICKED as NAVIGATOR_EDIT_PAGE_CLICKED,
	EDIT_AUDIENCES_CLICKED as NAVIGATOR_EDIT_AUDIENCES_CLICKED,
	EDIT_CONDITIONS_CLICKED as NAVIGATOR_EDIT_CONDITIONS_CLICKED,
	EDIT_ROUTE_CLICKED as NAVIGATOR_EDIT_ROUTE_CLICKED,
	EDIT_FIELDS_CLICKED as NAVIGATOR_EDIT_FIELDS_CLICKED,
	EDIT_PARAMETERS_CLICKED as NAVIGATOR_EDIT_PARAMETERS_CLICKED,
	SCREEN_TYPE_SELECTED as NAVIGATOR_SCREEN_TYPE_SELECTED,
	ROUTE_SELECTED as NAVIGATOR_ROUTE_SELECTED
} from '../sn-uib-generic-page-navigator';
import {
	PAGE_CREATE_CLICKED,
	VARIANT_CREATED_CLICKED,
	PAGE_SELECTED,
	COPY_PAGE_CLICKED,
	EDIT_PAGE_CLICKED,
	EDIT_AUDIENCES_CLICKED,
	EDIT_CONDITIONS_CLICKED,
	EDIT_ROUTE_CLICKED,
	EDIT_FIELDS_CLICKED,
	EDIT_PARAMETERS_CLICKED,
	SCREEN_TYPE_SELECTED,
	ROUTE_SELECTED
} from './actions';
import isEmpty from 'lodash/isEmpty';
import { addSlotPlaceholders } from './utils.js';

const DASHBOARD_PAGE_NAVIGATOR_STYLE = {
	maxHeight: '21%',
	height: 'initial'
};
const DASHBOARD_ELEMENT_NAVIGATOR_STYLE = {
	height: '100%'
};

const TAB_ROUTES = {
	pages: 'pages',
	components: 'components'
};

/**
 * This function allows us to conditionally show experiences
 * within the PMP to prevent a complete tab from unmounting, which causes
 * destruction of state and user preferences (ex: component tree expansion)
 * are lost
 *
 * @param {String} selectedTab current tab selected in PMP
 * @param {String} route routeId associated with the view
 * @returns {Boolean} based on the matching return a truthy falsey value
 */
const hideIfRouteNotMatch = (selectedTab, route) => selectedTab !== route;

const view = (state) => {
	const { properties, elementNavSearchTerm } = state;
	const {
		isPagesLoading,
		pagesRequestError,
		pages,
		selectedPage,
		selectedVariant,
		components,
		elements,
		selectedElementId,
		pageVariantsEnabled,
		componentTreeEnabled,
		selectedPageIsTab,
		dropdownHeaderLabels,
		listHeaderLabel,
		createDropdownActions,
		listHeaderActions,
		runtimeUrl,
		selectedTab,
		disabled,
		navigationDisabled,
		variantActionsDisabled,
		elementTreeDisabled,
		isComponentsLoading,
		componentsRequestError,
		isDashboardBuilder,
		pageManagementPanelExpanded,
		currentOverridesTree,
		isDomainExpanded,
		overridesFetching
	} = properties;
	let pageNavigatorStyle = {};
	let elementNavigatorStyle = {};

	if (isDashboardBuilder) {
		pageNavigatorStyle = DASHBOARD_PAGE_NAVIGATOR_STYLE;
		elementNavigatorStyle = DASHBOARD_ELEMENT_NAVIGATOR_STYLE;
	}

	const isComponentsHidden = hideIfRouteNotMatch(
		selectedTab,
		TAB_ROUTES.components
	);
	const isPageNavigatorHidden =
		hideIfRouteNotMatch(selectedTab, TAB_ROUTES.pages) ||
		!pageManagementPanelExpanded;

	const elementsWithPlaceholders = !isEmpty(elements)
		? addSlotPlaceholders(elements, disabled)
		: null;

	return (
		<div className="wrapper now-align">
			<div
				class={{
					hidden: isComponentsHidden
				}}>
				<sn-uib-component-navigator
					disabled={disabled}
					is-components-loading={isComponentsLoading}
					error={componentsRequestError}
					components={components}
					now-mode={isComponentsHidden ? 'suspend' : 'active'}
				/>
			</div>

			<div
				class={{
					'pages-column': true,
					'now-align': true,
					'-column': true,
					hidden: isPageNavigatorHidden
				}}>
				<sn-uib-generic-page-navigator
					className="generic-page-navigator"
					selected-page={selectedPage}
					selected-variant={selectedVariant}
					pages={pages}
					isPagesLoading={isPagesLoading}
					error={pagesRequestError}
					pageVariantsEnabled={pageVariantsEnabled}
					selected-page-is-tab={selectedPageIsTab}
					dropdownHeaderLabels={dropdownHeaderLabels}
					createDropdownActions={createDropdownActions}
					listHeaderLabel={listHeaderLabel}
					listHeaderActions={listHeaderActions}
					runtimeUrl={runtimeUrl}
					is-dashboard-builder={isDashboardBuilder}
					style={pageNavigatorStyle}
					currentOverridesTree={currentOverridesTree}
					is-domain-expanded={isDomainExpanded}
					overrides-fetching={overridesFetching}
					disabled={disabled && navigationDisabled}
					variant-actions-disabled={variantActionsDisabled}
					now-mode={isPageNavigatorHidden ? 'suspend' : 'active'}
				/>
				{!isEmpty(elements) && componentTreeEnabled ? (
					<sn-uib-element-navigator
						className="generic-element-navigator"
						selected-item={selectedElementId}
						elements={elementsWithPlaceholders}
						components={components}
						search-term={elementNavSearchTerm}
						is-dashboard-builder={isDashboardBuilder}
						style={elementNavigatorStyle}
						disabled={elementTreeDisabled}
						now-mode={isPageNavigatorHidden ? 'suspend' : 'active'}
						selectedSlotParent={'@ui-state/selectedSlotParent'}
						selectedSlotName={'@ui-state/selectedSlotName'}
					/>
				) : null}
			</div>
		</div>
	);
};

/**
 * @typedef {object} Page
 * @property {string} id The id of the page
 * @property {string} label The human-readable display value
 * @property {string} icon Icon value to display with the label
 * @property {boolean} [actionable] Whether or not the page is selectable
 * @property {Page[]} children The descendent Page items under this Page.
 */
/**
 * The top level component for page and component management in UI Builder.
 * Responsible for displaying the available pages and components and communicating
 * any chosen pages to the rest of the app.
 * This is a function.
 * @exports sn-uib-page-management-panel
 * @namespace snUibPageManagementPanel
 */
createCustomElement('sn-uib-generic-page-management-panel', {
	view,
	styles,
	behaviors: [focusBehavior],
	/**
	 * Properties of  sn-uib-page-management-panel
	 * @memberof snUibPageManagementPanel
	 * @type {object}
	 * @property {object} pages A child method as property defination
	 */
	properties: {
		isDashboardBuilder: {
			default: false
		},
		isPagesLoading: {
			default: false
		},
		pagesRequestError: {
			default: false
		},
		pages: {
			default: []
		},
		selectedPage: {
			default: ''
		},
		selectedVariant: {
			default: ''
		},
		isComponentsLoading: {
			default: false
		},
		componentsRequestError: {
			default: false
		},
		components: {
			default: [
				{
					id: 'componentsId',
					icon: 'grid-three-outline',
					label: t('Components'),
					children: []
				}
			]
		},
		elements: {
			default: {
				children: []
			}
		},
		selectedElementId: {
			default: null
		},
		/**
		 * Whether the element is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		elementTreeDisabled: {
			default: false
		},
		variantActionsDisabled: {
			default: false
		},
		navigationDisabled: {
			default: false
		},
		pageVariantsEnabled: {
			default: false
		},
		componentTreeEnabled: {
			default: false
		},
		selectedPageIsTab: {
			default: false
		},
		dropdownHeaderLabels: {
			default: {}
		},
		createDropdownActions: {
			default: () => {}
		},
		listHeaderLabel: {
			default: ''
		},
		listHeaderActions: {
			default: []
		},
		runtimeUrl: {
			default: {}
		},
		selectedTab: {
			default: 'pages'
		},
		pageManagementPanelExpanded: {
			default: true
		},
		currentOverridesTree: {
			default: {}
		},
		isDomainExpanded: {
			default: false
		},
		overridesFetching: {
			default: false
		}
	},
	initialState: {
		elementNavSearchTerm: ''
	},
	actionHandlers: {
		'NOW_INPUT#INPUT': {
			effect: ({ action, updateState }) => {
				const { fieldValue, id } = action.payload;
				if (id === 'element-search') {
					updateState({
						elementNavSearchTerm: fieldValue
					});
				}
			},
			stopPropagation: true
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, updateState, state, dispatch }) => {
			const selectedTab = action.payload.tab;

			updateState({
				path: 'selectedTab',
				value: state.selectedTab === selectedTab ? null : selectedTab,
				operation: 'set'
			});
			dispatch('COLLAPSIBLE_PANEL#PANEL_TOGGLED', {
				panel: 'page-management',
				value: state.selectedTab !== selectedTab
			});
		},
		[CREATE_PAGE_BUTTON_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(PAGE_CREATE_CLICKED, action.payload);
		},
		[CREATE_VARIANT_BUTTON_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(VARIANT_CREATED_CLICKED, action.payload);
		},
		[NAVIGATOR_PAGE_SELECTED]: ({ action, dispatch }) => {
			dispatch(PAGE_SELECTED, {
				sysId: action.payload.item.id,
				pageSysId: action.payload.item.pageSysId,
				name: action.payload.item.label
			});
		},
		[NAVIGATOR_COPY_PAGE_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(COPY_PAGE_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_EDIT_PAGE_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(EDIT_PAGE_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_EDIT_AUDIENCES_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(EDIT_AUDIENCES_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_EDIT_CONDITIONS_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(EDIT_CONDITIONS_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_EDIT_ROUTE_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(EDIT_ROUTE_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_EDIT_FIELDS_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(EDIT_FIELDS_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_EDIT_PARAMETERS_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(EDIT_PARAMETERS_CLICKED, { id: action.payload.item.id });
		},
		[NAVIGATOR_SCREEN_TYPE_SELECTED]: ({ action, dispatch }) => {
			dispatch(SCREEN_TYPE_SELECTED, {
				pageSysId: action.payload.item.pageSysId,
				name: action.payload.item.label
			});
		},
		[NAVIGATOR_ROUTE_SELECTED]: ({ action, dispatch }) => {
			dispatch(ROUTE_SELECTED, { id: action.payload.item.id });
		}
	},
	dispatches: {
		/**
		 * Dispatched when a create page button is clicked.
		 * @type {{type: null|'landingPage'}}
		 */
		'SN_UIB_PAGE_MANAGEMENT_PANEL#PAGE_CREATE_CLICKED': {},
		/**
		 * Dispatched when a page is selected. Payload will contain the
		 * sysId of the selected Page
		 * @type {{sysId: <sysId>}}
		 */
		[PAGE_SELECTED]: {},
		/**
		 * Dispatched when copy option from the
		 * configuration dropdown for a page variant is clicked.
		 * Payload contains the page item
		 * @type {{routeType: Object}}
		 */
		[COPY_PAGE_CLICKED]: {},
		/**
		 * Dispatched when edit option from the
		 * configuration dropdown for a page variant is clicked.
		 * Payload contains the page sys id
		 * @type {{routeType: <sysId>}}
		 */
		[EDIT_PAGE_CLICKED]: {},
		/**
		 * Dipsatched when edit option from the
		 * configuration dropdown for a route is selected.
		 * Payload  contains the routeType of the route to be configured
		 * @type {{id: string}}
		 */
		[EDIT_ROUTE_CLICKED]: {},
		[SCREEN_TYPE_SELECTED]: {}
	}
});
