const strategies = new Map();

const strategyManager = {
	set(id, key, value) {
		let existing = strategies.get(id);
		if (!existing) {
			existing = {};
		}
		existing[key] = value;

		strategies.set(id, existing);
	},
	get(id, key) {
		const existing = strategies.get(id);
		if (!existing) {
			return null;
		}

		return existing[key];
	}
};

export default strategyManager;
