import '../../sn-uib-drag-wrapper';
import { getPreferredDropzone } from '../droppable/utils';
import interact from 'interactjs';
import get from 'lodash/get';
import strategyManager from '../../helpers/strategyManager';

class ContainerRefMap {
	notifiers = {};
	constructor() {
		this.map = new Map();
	}

	get(id) {
		return this.map.get(id);
	}

	set(id, ref) {
		const existing = this.map.get(id);
		this.map.set(id, ref);
		if (
			existing &&
			get(this.notifiers, id, []).length > 0 &&
			get(ref, 'current') !== get(existing, 'current')
		) {
			const oldNotifiers = this.notifiers[id];
			this.notifiers[id] = [];
			oldNotifiers.forEach((fn) => fn());
		}
	}
	addNotifier(id, fn) {
		if (!this.notifiers[id]) {
			this.notifiers[id] = [];
		}
		this.notifiers[id].push(fn);
	}
}

export const stageDragMap = new ContainerRefMap();

/**
 *
 * @param HTMLElement container
 * Convenience function for Interact.js which takes an HTMLElement and sets it as the boundary for dragging.
 * This is useful for when you either want to prevent a drag from leaving a container or prevent a drag that allows scrolling from
 * allowing the user to scroll endlessly.
 */
export const getModifierRestrictions = (container) => {
	const LEFT_EDGE = 0;
	const RIGHT_EDGE = 1;
	const BOTTOM_EDGE = 1;
	const BUFFER = 1;
	return {
		modifiers: [
			interact.modifiers.restrict({
				restriction: container,
				elementRect: {
					left: LEFT_EDGE,
					right: RIGHT_EDGE,
					top: BOTTOM_EDGE,
					bottom: BOTTOM_EDGE * BUFFER
				}
			})
		]
	};
};
/**
 *
 * @param HTMLElement container
 * Convenience function for Interact.js which takes an HTMLElement and sets it as the container that can be scrolled .
 * This is useful for when you either want to scroll a specific container as opposed to the entire body.
 */
export const getAutoScrollOptions = (container = window.document.body) => {
	return {
		autoScroll: {
			container,
			margin: 10,
			distance: 5,
			interval: 15
		}
	};
};
const setToScrollTop = (height, container) => (e) => {
	requestAnimationFrame(() => {
		if (height > 0 && container.scrollHeight >= height) {
			container.scrollTop = height;
		}
	});
};
/**
 * Prepares our container to preserve the scroll position when dragging starts.
 */
export const stageScrollModifier = () => {
	let stageContainer;
	let initialStageScrollTop;
	return {
		initializeStage: (container) => {
			stageContainer = container;
			initialStageScrollTop = stageContainer.scrollTop;
			container.addEventListener(
				'scroll',
				setToScrollTop(initialStageScrollTop, container),
				{
					once: true
				}
			);
		}
	};
};

export const setBodyToNotScroll = () => {
	const previousStyles = {};
	const body = document.body;
	const move = () => {
		previousStyles.height = body.style.height;
		previousStyles.overflow = body.style.overflow;
		const rect = body.getBoundingClientRect();
		body.style.height = rect.height;
		body.style.overflow = 'hidden';
	};
	const dragend = () => {
		// restore old height preferences
		body.style.height = previousStyles.height;
		body.style.overflow = previousStyles.overflow;
	};
	return { move, dragend };
};

const DRAG_IMAGE_HEIGHT = 100;
const DRAG_IMAGE_WIDTH = 100;

interact.dynamicDrop(true);
let dragElement = null;

export const isInteracting = (el) => {
	return el === dragElement;
};

export const removeListenersIfFound = (el) => {
	interact(el).unset();
};
export const move = (data, dispatch, previewData, tag, cb = function () {}) => (
	event
) => {
	const interaction = event.interaction;

	// if the pointer was moved while being held down
	// and an interaction hasn't started yet
	if (
		interaction.pointerIsDown &&
		!interaction.interacting() &&
		!(get(interaction, 'buttonId', 0) > 1)
	) {
		dragElement = event.interactable.target;
		const clone = document.createElement('sn-uib-drag-wrapper');
		clone.setAttribute('tag', tag);
		clone.setAttribute('data', JSON.stringify(previewData));

		clone.setAttribute('active-id', '@dnd-provider/activeId');

		const [scrollX, scrollY] = [
			get(window, 'scrollX', 0),
			get(window, 'scrollY', 0)
		];
		const { offsetX, offsetY } = event;

		const centerY = scrollY + offsetY - 10;
		const centerX = scrollX + offsetX - 10;

		// update the position attributes
		clone.setAttribute('data-x', centerX);
		clone.setAttribute('data-y', centerY);

		// insert the clone to the page
		clone.setAttribute(
			'style',
			`
			transform:  translate(${centerX}px, ${centerY}px);
			user-select: none;
			touch-action: none;
			position: absolute;
			cursor: grabbing;
			top: 0;
			left: 0;
			z-index: 12;
			width: ${DRAG_IMAGE_WIDTH}px;
			height: ${DRAG_IMAGE_HEIGHT}px;
			-webkit-user-select: none;
		`
		);

		document.body.appendChild(clone);
		dispatch('UIB_DRAGGABLE_BEHAVIOR#DRAG_START', data);
		interaction.start({ name: 'drag' }, event.interactable, clone);
		cb(data);
		event.transfer = data;
	}
};

export const dragmove = (data, interactId) => (event) => {
	const target = event.target;
	const { clientX, clientY } = event;
	const [scrollX, scrollY] = [
		get(window, 'scrollX', 0),
		get(window, 'scrollY', 0)
	];

	const centerY = scrollY + clientY - 10;
	const centerX = scrollX + clientX - 10;

	// keep the dragged position in the data-x/data-y attribute;
	const x = centerX;
	const y = centerY;
	if (target.currentRound && target.currentRound.length > 0) {
		target.activeDropzone = getPreferredDropzone(target.currentRound);
	}
	target.currentRound = [];

	// translate the element
	requestAnimationFrame(() => {
		target.style.webkitTransform = target.style.transform = ` translate(${x}px, ${y}px)`;
		target.setAttribute('data-x', x);
		target.setAttribute('data-y', y);
	});
	event.transfer = data;
	event.interactId = interactId;
};

export const dragend = (data, interactId) => (event) => {
	/* 
		With Seismic 18 dispatch functions were invalidated when the component unmounted.
		So a draggable element that unmounted on drag start would not be able to fire an action to notify dragend. 
		The current hacky workaround is to use this singleton manager to fetch a valid dispatch.
	*/
	dragElement = null;

	const dispatch = strategyManager.get('dnd', 'dispatchFn') || null;
	const parent = event.target.parentNode;

	if (parent) {
		event.target.parentNode.removeChild(event.target);
	}
	event.transfer = data;
	event.interactId = interactId;
	if (!dispatch) {
		return;
	}

	if (!event.dropzone) {
		dispatch('UIB_DRAGGABLE_BEHAVIOR#INVALID_DROP');
	}
	dispatch('UIB_DRAGGABLE_BEHAVIOR#DRAG_END');
};

function getDragPreviewData(element) {
	return {
		component: { icon: element.icon, name: element.displayName }
	};
}
const dataMap = new Map();

const attachDrag = ({
	el,
	dataTransfer,
	dispatch,
	tag,
	id,
	scrollContainer
}) => {
	interact(el)
		.draggable({
			manualStart: true,
			inertia: true,
			hold: 200,
			...getAutoScrollOptions(scrollContainer)
		})
		.on('move', (event) => {
			const element = dataMap.get(tag);
			move(
				dataTransfer,
				dispatch,
				element ? getDragPreviewData(element) : {},
				'sn-uib-component-item',
				() =>
					dispatch('UIB_DRAGGABLE_BEHAVIOR#DETACH', {
						id: id
					})
			)(event);
		})
		.on('dragmove', dragmove(dataTransfer, id))
		.on('dragend', dragend(dataTransfer, id));
};

export const handleDragInsert = ({ el, tag, element, ...rest }) => {
	el.dndTag = tag;
	dataMap.set(tag, element);
	attachDrag({ el, tag, element, ...rest });
};

export const handleDragUpdate = ({ el, tag, element, ...rest }) => {
	dataMap.set(tag, element);

	if (tag !== el.dndTag) {
		el.dndTag = tag;
		interact(el).unset();
		attachDrag({ el, tag, element, ...rest });
	}
};
