import '@servicenow/now-input';
import '@servicenow/now-icon';
export default function searchbox(
	{
		value,
		manageValue,
		placeholder,
		appendToPayload,
		dispatch,
		disabled,
		label
	},
	children
) {
	return (
		<div class={{ 'pure-search-box': true }}>
			<now-input
				label={label}
				disabled={disabled}
				placeholder={placeholder}
				manageValue={manageValue}
				value={value}
				append-to-payload={appendToPayload}>
				<now-icon slot="start" size="sm" icon="magnifying-glass-fill" />
				<div
					slot="end"
					class={{ clear: true, blank: !value }}
					on-click={(e) => {
						e.stopPropagation();

						dispatch('NOW_INPUT#INPUT', {
							fieldValue: '',
							...appendToPayload
						});
					}}>
					<now-icon icon="close-fill" size="sm" />
				</div>
				{children}
			</now-input>
		</div>
	);
}
