import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import cuid from 'cuid';
import cloneDeep from 'lodash/cloneDeep';
import reject from 'lodash/reject';
import { t } from 'sn-translate';
import '@servicenow/now-typeahead';
import '@servicenow/now-button';
import '@servicenow/now-input';
import '@servicenow/now-toggle';
import styles from './sn-uib-dispatched-events-modal.scss';
import {
	PARAM_ROW_DELETE_REQUESTED,
	PARAM_ROW_PARAMETER_CHANGED
} from '../sn-uib-param-row/sn-uib-param-row';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

// Dispatched events
const DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED =
	'SN_UIB_DISPATCHED_EVENTS_MODAL#PENDING_VALUES_UPDATED';

export const DISPATCHED_EVENTS_MODAL_FOOTER_ACTIONS = (type) => [
	{
		label: type === 'create' ? t('Add') : t('Apply'),
		clickActionType: 'SN_UIB_DISPATCHED_EVENTS_MODAL#OK_CLICKED',
		variant: 'primary'
	},
	{
		label: t('Cancel'),
		clickActionType: 'SN_UIB_DISPATCHED_EVENTS_MODAL#CANCEL_CLICKED',
		variant: 'secondary'
	}
];

const convertLabelToEventName = (label) => {
	if (!label) {
		return '';
	}
	return label.replace(/\s/g, '_').toUpperCase();
};

const findMatchingEventHandler = (eventHandlers, currentPayloadFields) => {
	return eventHandlers.reduce((acc, eventHandler) => {
		if (
			currentPayloadFields.length &&
			eventHandler.ancestor &&
			eventHandler.payloadFields.length === currentPayloadFields.length &&
			eventHandler.payloadFields.every((payloadField) => {
				return !!currentPayloadFields.find(
					(field) => field.name === payloadField.name
				);
			}, currentPayloadFields)
		) {
			acc = eventHandler;
		}
		return acc;
	}, null);
};

const updatePendingValues = (
	properties,
	templates,
	selectedTemplate,
	dispatch,
	sendParentHandler = false
) => {
	const templateToUse = templates.find(
		(template) => template.id === selectedTemplate
	);
	const payloadFields = (templateToUse && templateToUse.payloadFields) || [];
	const newProps = payloadFields.map((prop) => ({
		id: cuid(),
		name: prop.name,
		label: prop.label,
		type: prop.type,
		interfaceApi: prop.interfaceApiName,
		typeMetadata: prop.typeMetadata
	}));

	dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
		pendingValues: {
			...properties.pendingValues,
			props: newProps,
			parentHandler: sendParentHandler ? templateToUse : null
		}
	});
};

const view = (state) => {
	const {
		templates = [],
		selectedTemplate,
		selectedTemplateLabel,
		properties,
		isAdvancedMode,
		forceAdvancedMode
	} = state;
	const {
		interfaceConfigurationComponent = {},
		pendingValues,
		disabled
	} = properties;
	const { name, label, props = [] } = pendingValues;
	const supportedParamTypes = [
		'string',
		'number',
		'boolean',
		'json',
		'condition_string',
		'table_name'
	];
	const interfaceApiNames = Object.keys(
		interfaceConfigurationComponent
	).map((interfaceApi) => ({ id: interfaceApi, label: interfaceApi }));
	const ancestorTemplates = templates.filter(
		(template = {}) => template.ancestor === true
	);

	return (
		<div className="dispatched-events-modal-content">
			<div className="dispatched-events-modal-inputs-container">
				<now-input
					label={t('Event label')}
					value={label || ''}
					placeholder={t('Enter a label')}
					append-to-payload={{ context: 'event-label' }}
				/>
				<now-input
					disabled
					label={t('Event name')}
					value={properties.name || convertLabelToEventName(label)}
					append-to-payload={{ context: 'event-name' }}
				/>
			</div>
			{forceAdvancedMode ? null : (
				<div className="dispatched-events-modal-target-selection">
					<now-typeahead
						key={`target-typeahead--${isAdvancedMode.toString()}`}
						disabled={isAdvancedMode || !name}
						label={t('Target parent event handler')}
						items={ancestorTemplates}
						selected-item={isAdvancedMode ? 'none' : selectedTemplate}
						/* prettier-ignore */
						helper-content={t('Select a parent event handler as a relay target')}
						append-to-payload={{ context: 'event-target-handler' }}
						value={isAdvancedMode ? t('None') : selectedTemplateLabel}
					/>
					<section className="advanced-mode-section">
						<div className="advanced-mode-toggle-label">
							{t('Advanced mode')}
						</div>
						<now-toggle
							managed-checked={true}
							checked={isAdvancedMode}
							append-to-payload={{ context: 'event-advanced-mode' }}
							size="sm"
						/>
					</section>
				</div>
			)}
			{isAdvancedMode || forceAdvancedMode ? (
				<div className="dispatched-events-modal-params-container">
					<div className="dispatched-events-modal-params-header">
						{t('Payload fields')}
						<now-button-bare
							label={t('Add')}
							icon-start="plus-fill"
							disabled={!name}
							append-to-payload={{ context: 'param-add' }}
						/>
					</div>
					<div className="dispatched-events-modal-params-template-section">
						<now-typeahead
							key={`template-typeahead--${!name}`}
							label={t('Template')}
							disabled={!name}
							items={templates}
							selected-item={selectedTemplate}
							/* prettier-ignore */
							helper-content={t('Select an existing event as a template for your payload fields')}
							append-to-payload={{ context: 'event-template' }}
							value={isAdvancedMode ? t('None') : selectedTemplateLabel}
						/>
						<now-button
							label={t('Apply')}
							disabled={!name}
							append-to-payload={{ context: 'event-template' }}
						/>
					</div>
					{props.map(
						({
							id = '',
							name = '',
							label = '',
							type = '',
							interfaceApi = ''
						}) => {
							return (
								<sn-uib-param-row
									key={`param-row`}
									id={id}
									name={name}
									label={label}
									type={type}
									interfaceApi={interfaceApi}
									supported-inputs={['name', 'label', 'interfaceApi', 'type']}
									supported-param-types={supportedParamTypes}
									interface-api-names={interfaceApiNames}
									show-card-outline={true}
									disabled={disabled}
								/>
							);
						}
					)}
				</div>
			) : null}
		</div>
	);
};

export default createCustomElement('sn-uib-dispatched-events-modal', {
	view,
	styles,
	initialState: {
		templates: [{ id: 'none', label: t('None'), payloadFields: [] }],
		selectedTemplate: 'none',
		selectedTemplateLabel: t('None'),
		isAdvancedMode: false,
		forceAdvancedMode: false
	},
	transformState(state) {
		const {
			properties: { context }
		} = state;
		return {
			...state,
			forceAdvancedMode: !!(context === 'handledEvents')
		};
	},
	properties: {
		context: {
			default: 'dispatchedEvents',
			schema: { type: 'string' }
		},
		name: {
			default: '',
			schema: { type: 'string' }
		},
		label: {
			default: '',
			schema: { type: 'string' }
		},
		payloadFields: {
			default: [],
			schema: { type: 'array' }
		},
		interfaceConfigurationComponent: {
			default: {},
			schema: { type: 'object' }
		},
		pendingValues: {
			default: {},
			schema: { type: 'object' }
		},
		eventHandlers: {
			default: [],
			schema: { type: 'array' }
		},
		disabled: {
			default: false,
			schema: { type: 'boolean' }
		},
		mode: {
			default: 'create',
			schema: {
				type: 'string',
				enum: ['create', 'edit']
			}
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({
			properties,
			dispatch,
			state,
			updateState
		}) => {
			const { eventHandlers = [], payloadFields = [] } = properties;
			updateState({ templates: state.templates.concat(eventHandlers) });

			const templateToUse = findMatchingEventHandler(
				eventHandlers,
				payloadFields
			);
			if (templateToUse) {
				batchShorthandUpdateState(
					{
						selectedTemplate: templateToUse.id,
						selectedTemplateLabel: templateToUse.label
					},
					updateState
				);
			} else if (payloadFields.length && !templateToUse) {
				updateState({ isAdvancedMode: true });
			}

			dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
				pendingValues: {
					name: properties.name,
					label: properties.label,
					props: properties.payloadFields
				}
			});
		},
		['NOW_INPUT#INPUT']: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const { context, fieldValue } = action.payload;
			if (context === 'event-label') {
				const changes =
					properties.mode === 'edit'
						? { label: fieldValue }
						: { label: fieldValue, name: convertLabelToEventName(fieldValue) };
				dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
					pendingValues: {
						...properties.pendingValues,
						...changes
					}
				});
			}
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			if (action.payload.context === 'param-add') {
				const currentProps = properties.pendingValues.props || [];
				dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
					pendingValues: {
						...properties.pendingValues,
						props: [
							...currentProps,
							{ id: cuid(), name: '', label: '', type: '' }
						]
					}
				});
			}
		},
		[PARAM_ROW_DELETE_REQUESTED]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const currentProps = properties.pendingValues.props || [];
			const updatedProps = reject(currentProps, { id: action.payload.id });
			dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
				pendingValues: {
					...properties.pendingValues,
					props: updatedProps
				}
			});
		},
		[PARAM_ROW_PARAMETER_CHANGED]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const { id, value, context } = action.payload;
			const propName = context.split('-')[1];
			const newProps = cloneDeep(properties.pendingValues.props) || [];
			const propToUpdate = newProps.find((prop) => prop.id === id);
			propToUpdate[propName] = value;

			dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
				pendingValues: {
					...properties.pendingValues,
					props: newProps
				}
			});
		},
		['NOW_TYPEAHEAD#SELECTED_ITEM_SET']: ({
			state,
			action,
			updateState,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			(action.payload.context === 'event-template' ||
				action.payload.context === 'event-target-handler') &&
				updateState({
					selectedTemplate: action.payload.value
				});
			action.payload.context === 'event-target-handler' &&
				updatePendingValues(
					properties,
					state.templates,
					action.payload.value,
					dispatch,
					true
				);
		},
		['NOW_BUTTON#CLICKED']: ({ action, state, properties, dispatch }) => {
			action.stopPropagation();
			const { templates, selectedTemplate } = state;

			if (action.payload.context === 'event-template') {
				updatePendingValues(
					properties,
					templates,
					selectedTemplate,
					dispatch,
					false
				);
			}
		},
		['NOW_TOGGLE#CHECKED_SET']: ({
			action,
			state,
			properties,
			updateState,
			dispatch
		}) => {
			action.stopPropagation();
			if (action.payload.context === 'event-advanced-mode') {
				batchShorthandUpdateState(
					{
						isAdvancedMode: !state.isAdvancedMode,
						selectedTemplate: 'none'
					},
					updateState
				);
				dispatch(DISPATCHED_EVENTS_MODAL_PENDING_VALUES_UPDATED, {
					pendingValues: {
						...properties.pendingValues,
						parentHandler: null,
						props: []
					}
				});
			}
		}
	}
});
