import styles from './sn-uib-scripts-includes.scss';
import { t } from 'sn-translate';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-typeahead';
import '@servicenow/now-button';

import { scriptIncludesRequestBehavior } from '../behaviors/scriptsIncludesRequestBehavior';
import { SCRIPT_INCLUDES_REQUESTED } from '../behaviors/scriptsIncludesRequestBehavior';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const SN_UIB_CLIENT_SCRIPT_INCLUDES_CHANGED =
	'SN_UIB_CLIENT_SCRIPT_INCLUDES#CHANGED';

const dictionary = {
	add: t('Add'),
	addAScriptInclude: t('Add a script include'),
	loadingClientScripts: t('Loading client scripts'),
	/* prettier-ignore */
	yourSelectedScriptWillAppearHere: t('Your selected script includes will appear here')
};

const renderPillars = (items, disabled) => {
	return (
		<div className="includes-list">
			{items.map(({ id, label }) => {
				return (
					<div className="wrapper">
						<sn-par-pillar size="md">
							<div
								style={{
									display: 'flex',
									'align-items': 'center',
									'justify-content': 'space-between'
								}}>
								<div>{label}</div>
								<div>
									{!disabled ? (
										<now-button
											icon="close-fill"
											variant="inherit"
											size="md"
											append-to-payload={{
												type: 'delete',
												id
											}}
										/>
									) : null}
								</div>
							</div>
						</sn-par-pillar>
					</div>
				);
			})}
		</div>
	);
};

createCustomElement('sn-uib-scripts-includes', {
	behaviors: [scriptIncludesRequestBehavior],
	view: ({
		inputValue,
		availableScriptIncludes = [],
		isLoading = true,
		properties: { clientScriptIncludeIds = [], disabled }
	}) => {
		const clientScriptIncludes = clientScriptIncludeIds.map((id) =>
			availableScriptIncludes.find((include) => include.id === id)
		);

		return (
			<div>
				<div className="sn-uib-scripts-includes-add-script-form">
					<now-typeahead
						className="sn-uib-scripts-includes--add-script-input"
						items={availableScriptIncludes}
						label={dictionary.addAScriptInclude}
						manage-value={true}
						manage-selected-item={true}
						value={inputValue}
						disabled={disabled}
					/>
					<now-button
						slot="end"
						label={dictionary.add}
						tooltipContent={dictionary.add}
						variant="secondary"
						size="md"
						disabled={disabled}
						append-to-payload={{
							type: 'add'
						}}
						configAria={{
							button: {
								'aria-label': dictionary.addAScriptInclude
							}
						}}
					/>
				</div>
				<div className="scripts-includes-list">
					{isLoading ? (
						<sn-uib-loader label={dictionary.loadingClientScripts} />
					) : clientScriptIncludes.length > 0 ? (
						renderPillars(clientScriptIncludes, disabled)
					) : (
						<span className="no-elements">
							<now-icon
								size="md"
								icon="exclamation-circle-outline"
								className="no-elements-icon"
							/>
							{dictionary.yourSelectedScriptWillAppearHere}
						</span>
					)}
				</div>
			</div>
		);
	},
	initialState: {
		selectedInclude: null,
		inputValue: ''
	},
	properties: {
		clientScriptIncludeIds: {
			default: []
		},
		scriptId: {
			default: null
		},
		scriptScope: {
			default: null
		},
		disabled: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties }) => {
			const { scriptScope } = properties;

			dispatch(SCRIPT_INCLUDES_REQUESTED, {
				scriptScope: scriptScope ? scriptScope : 'global'
			});
		},
		['NOW_TYPEAHEAD#VALUE_SET']: ({ updateState, action }) => {
			updateState({
				inputValue: action.payload.value
			});
		},
		['NOW_BUTTON#CLICKED']: ({
			dispatch,
			state,
			updateProperties,
			action,
			updateState
		}) => {
			const { selectedInclude = {}, availableScriptIncludes = [] } = state;
			const { clientScriptIncludeIds = [] } = state.properties;
			const { type } = action.payload;

			if (type === 'add') {
				if (
					!clientScriptIncludeIds.includes(selectedInclude.id) &&
					availableScriptIncludes.some(({ id }) => id === selectedInclude.id)
				) {
					const newClientScriptIncludeIds = [
						...clientScriptIncludeIds,
						selectedInclude.id
					];

					dispatch(
						SN_UIB_CLIENT_SCRIPT_INCLUDES_CHANGED,
						newClientScriptIncludeIds
					);
					updateProperties({
						clientScriptIncludeIds: newClientScriptIncludeIds
					});
					batchShorthandUpdateState(
						{
							selectedInclude: null,
							inputValue: ''
						},
						updateState
					);
				}
			}

			if (type === 'delete') {
				const newClientScriptIncludeIds = clientScriptIncludeIds.filter(
					(id) => id !== action.payload.id
				);

				dispatch(
					SN_UIB_CLIENT_SCRIPT_INCLUDES_CHANGED,
					newClientScriptIncludeIds
				);
				updateProperties({
					clientScriptIncludeIds: newClientScriptIncludeIds
				});
			}
		},
		['NOW_TYPEAHEAD#SELECTED_ITEM_SET']: ({ updateState, action }) => {
			updateState({
				selectedInclude: action.payload.item
			});
		}
	},
	styles
});
