import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './_sn-uib-layout-drop-leaf.scss';
import { t } from 'sn-translate';
import get from 'lodash/get';

import {
	leafOptions,
	handleInsert,
	handleUpdate,
	checkActive,
	keyboardNavBehavior
} from '../behaviors/droppable/utils';
import strategyManager from '../helpers/strategyManager';

import { handleEscapeKeyPressed } from '../helpers/keyboardNavShared';

import { DevxKeyFocusBehavior } from 'sn-devx-components';
import SnUIBLayoutTooltip from '../partials/sn-uib-layout-tooltip';

const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	createFocusRef
} = DevxKeyFocusBehavior;

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
/* prettier-ignore */
const containerMessage = t('Press enter to navigate inside this container. Use arrow keys to select a relative dropzone.');
const leafMessage = t('Use arrow keys to select a relative dropzone.');
const tooltipContents = {
	main: { container: containerMessage, leaf: leafMessage },
	top: t('Press enter to drop component. Use arrow keys to navigate dropzones'),
	/* prettier-ignore */
	bottom: t('Press enter to drop component. Use arrow keys to navigate dropzones'),
	/* prettier-ignore */
	left: t('Press enter to drop component. Use arrow keys to navigate dropzones'),
	/* prettier-ignore */
	right: t('Press enter to drop component. Use arrow keys to navigate dropzones')
};
const generateDropzoneRefs = (properties) => {
	const { id, keymaps } = properties;
	const { previousSibling, nextSibling, parent, firstChild } = keymaps || {};

	const leafRef = createFocusRef(`${id}`, {
		Enter: `${firstChild}`,
		Space: `${firstChild}`,
		Tab: `${nextSibling}`,
		ShiftTab: `${previousSibling}`,
		ArrowRight: `${id}_RIGHT`,
		ArrowLeft: `${id}_LEFT`,
		ArrowDown: `${id}_BOTTOM`,
		ArrowUp: `${id}_TOP`,
		Escape: parent
	});
	const bottomRef = createFocusRef(`${id}_BOTTOM`, {
		Tab: `${nextSibling}`,
		ShiftTab: `${id}`,
		Escape: `${id}`,
		ArrowLeft: `${id}_LEFT`,
		ArrowUp: `${id}_TOP`,
		ArrowRight: `${id}_RIGHT`
	});
	const topRef = createFocusRef(`${id}_TOP`, {
		Tab: `${nextSibling}`,
		ShiftTab: `${id}`,
		Escape: `${id}`,
		ArrowRight: `${id}_RIGHT`,
		ArrowLeft: `${id}_LEFT`,
		ArrowDown: `${id}_BOTTOM`
	});
	const leftRef = createFocusRef(`${id}_LEFT`, {
		Tab: `${nextSibling}`,
		ShiftTab: `${id}`,
		Escape: `${id}`,
		ArrowRight: `${id}_RIGHT`,
		ArrowUp: `${id}_TOP`,
		ArrowDown: `${id}_BOTTOM`
	});
	const rightRef = createFocusRef(`${id}_RIGHT`, {
		Tab: `${nextSibling}`,
		ShiftTab: `${id}`,
		Escape: `${id}`,
		ArrowLeft: `${id}_LEFT`,
		ArrowUp: `${id}_TOP`,
		ArrowDown: `${id}_BOTTOM`
	});
	return {
		leafRef,
		bottomRef,
		topRef,
		leftRef,
		rightRef
	};
};

const getContainerTooltip = (id, type) => {
	let key = type;

	const container = strategyManager.get(id, 'emptySlot');
	if (type === 'leaf' && container && container.offsetHeight > 0) {
		key = 'container';
	}
	return tooltipContents.main[key];
};

const view = (state, { dispatch, updateState }) => {
	const {
		isShowingLeafTooltip,
		leafRef,
		topRef,
		bottomRef,
		leftRef,
		rightRef
	} = state;

	const {
		id,
		additionalData,
		isActive,
		activeDirection,
		keyboardDrag,
		entrypoint,
		keymaps,
		callback
	} = state.properties;

	const mainTooltip = getContainerTooltip(
		get(keymaps, 'childInformation.id'),
		get(keymaps, 'childInformation.type')
	);
	const dropzoneClasses = ['dropzone'];

	const getActive = checkActive(id, isActive);

	const isShowingBottomTooltip =
		isActive && activeDirection === 'BOTTOM' && keyboardDrag;
	const isShowingTopTooltip =
		isActive && activeDirection === 'TOP' && keyboardDrag;
	const isShowingRightTooltip =
		isActive && activeDirection === 'RIGHT' && keyboardDrag;
	const isShowingLeftTooltip =
		isActive && activeDirection === 'LEFT' && keyboardDrag;
	return (
		<div
			className={dropzoneClasses.join(' ')}
			ref={leafRef}
			tabindex="0"
			role="button"
			title="dropzone"
			aria-describedby="drop-leaf-tooltip"
			on-focus={() => {
				updateState({ isShowingLeafTooltip: true });
			}}
			on-focusout={() => {
				updateState({ isShowingLeafTooltip: false });
			}}
			hook-update={(newNode) => {
				handleUpdate({
					el: newNode.elm,
					tag: `${id}__LEAF`,
					id,
					callback,
					additionalData,
					dispatch,
					options: leafOptions
				});
			}}
			hook-insert={({ elm }) => {
				setTimeout(() => {
					handleInsert({
						el: elm,
						id,
						additionalData,
						callback,
						tag: `${id}__LEAF`,
						dispatch,
						options: leafOptions
					});
				}, 25);
				if (entrypoint) {
					dispatch('UIB_STAGE#DND_ENTRYPOINT_CREATED', { id });
				}
			}}
			on-keydown={(e) => {
				const { target } = e;
				if (target !== leafRef.current) {
					return;
				}
				handleEscapeKeyPressed(e, dispatch, entrypoint);
			}}>
			<div
				className={`top ${getActive('TOP', activeDirection, 'active')}`}
				ref={topRef}
				aria-describedby="drop-leaf-top-tooltip"
				role="button"
				title="dropzone"
				{...keyboardNavBehavior({
					id,
					dispatch,
					direction: 'TOP',
					...additionalData
				})}
			/>
			<div className="middle">
				<div
					{...keyboardNavBehavior({
						id,
						dispatch,
						direction: 'LEFT',
						...additionalData
					})}
					ref={leftRef}
					aria-describedby="drop-leaf-left-tooltip"
					role="button"
					title="dropzone"
					className={`left ${getActive('LEFT', activeDirection, 'active')} `}
				/>
				<div className="content">
					<slot />
				</div>

				<div
					{...keyboardNavBehavior({
						id,
						dispatch,
						direction: 'RIGHT',
						...additionalData
					})}
					ref={rightRef}
					aria-describedby="drop-leaf-right-tooltip"
					role="button"
					title="dropzone"
					className={`right ${getActive('RIGHT', activeDirection, 'active')} `}
				/>
			</div>
			<div
				ref={bottomRef}
				{...keyboardNavBehavior({
					id,
					dispatch,
					direction: 'BOTTOM',
					...additionalData
				})}
				aria-describedby="drop-leaf-bottom-tooltip"
				role="button"
				title="dropzone"
				className={`bottom ${getActive('BOTTOM', activeDirection, 'active')} `}
			/>
			<SnUIBLayoutTooltip
				active={isShowingLeafTooltip}
				id="drop-leaf-tooltip"
				content={mainTooltip}
				ref={leafRef}
			/>
			<SnUIBLayoutTooltip
				active={isShowingTopTooltip}
				id="drop-leaf-top-tooltip"
				content={tooltipContents.top}
				ref={topRef}
			/>
			<SnUIBLayoutTooltip
				active={isShowingBottomTooltip}
				id="drop-leaf-bottom-tooltip"
				content={tooltipContents.bottom}
				ref={bottomRef}
			/>
			<SnUIBLayoutTooltip
				active={isShowingLeftTooltip}
				id="drop-leaf-left-tooltip"
				content={tooltipContents.left}
				ref={leftRef}
			/>
			<SnUIBLayoutTooltip
				active={isShowingRightTooltip}
				id="drop-leaf-right-tooltip"
				content={tooltipContents.right}
				ref={rightRef}
			/>
		</div>
	);
};

export default createCustomElement('sn-uib-layout-drop-leaf', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		id: {
			default: null
		},
		additionalData: {
			default: {}
		},
		callback: {
			default: () => {}
		},
		activeDirection: {
			default: ''
		},
		keyboardDrag: {
			default: false
		},
		keymaps: {
			default: {}
		},
		entrypoint: {
			default: false
		},
		activeId: {
			default: ''
		},
		isActive: {
			default: false
		}
	},
	behaviors: [keyFocusBehavior],
	actions: {
		'UIB_DND_DROPZONE#HOVER': { private: true }
	},
	initialState: {
		isShowingTooltip: false,
		targetRef: null,
		leafRef: {},
		rightRef: {},
		leftRef: {},
		topRef: {},
		bottomRef: {}
	},
	actionHandlers: {
		'UIB_DND_DROPZONE#HOVER': (coeffects) => {
			const {
				dispatch,
				action,
				state: {
					properties: { id }
				}
			} = coeffects;
			const { direction } = action.payload;
			dispatch('UIB_DND_DROPLEAF#HOVER', {
				id,
				direction
			});
		},
		[COMPONENT_CONNECTED]: ({ properties, updateState }) => {
			const dropzoneRefs = generateDropzoneRefs(properties);
			updateState(dropzoneRefs);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ properties, updateState }) => {
			const dropzoneRefs = generateDropzoneRefs(properties);
			updateState(dropzoneRefs);
		}
	},
	styles
});
