import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
// eslint-disable-next-line no-unused-vars
import {
	NoOptions,
	StyleConfigOptions,
	SlotStyleConfigOptions
} from './partials';
import '@servicenow/now-icon';
import styles from './_sn-uib-style-configuration-pane.scss';
import '../sn-uib-layout-template-popover';
import { CSS_INPUT_UPDATED } from '../sn-uib-css-input';

const INDIVIDUAL_FIELD_UPDATED =
	'SN_UIB_STYLE_CONFIGURATION_PANE#INDIVIDUAL_FIELD_UPDATED';

const view = ({
	properties: {
		elementStyles,
		error,
		element,
		elementLayout,
		layoutTemplates,
		standardTemplates,
		customizedTemplates,
		selectedSlotName,
		selectedSlotParent,
		selectedSlotStyles,
		disabled,
		lastAppliedTemplateId
	}
}) => {
	if (element && !selectedSlotName) {
		const empty = Object.keys(elementStyles).length === 0;
		return (
			<StyleConfigOptions
				element={element}
				elementStyles={empty ? {} : elementStyles}
				elementLayout={elementLayout}
				layoutTemplates={layoutTemplates}
				standardTemplates={standardTemplates}
				customizedTemplates={customizedTemplates}
				error={error}
				disabled={disabled}
				lastAppliedTemplateId={lastAppliedTemplateId}
			/>
		);
	} else if (selectedSlotName && selectedSlotParent && selectedSlotStyles) {
		return (
			<SlotStyleConfigOptions
				slotStyles={selectedSlotStyles}
				selectedSlotName={selectedSlotName}
				selectedSlotParent={selectedSlotParent}
				error={error}
				disabled={disabled}
			/>
		);
	} else {
		return <NoOptions />;
	}
};

/**
 * The panel for configuring an element's instance name and properties in the UI Builder.
 */
export default createCustomElement('sn-uib-style-configuration-pane', {
	view,
	styles,
	properties: {
		elementLayout: { default: {} },
		layoutTemplates: { default: [] },
		standardTemplates: { default: [] },
		customizedTemplates: { default: [] },
		element: {},
		elementStyles: { default: null },
		selectedSlotName: { default: null },
		selectedSlotParent: { default: null },
		selectedSlotStyles: { default: {} },
		lastAppliedTemplateId: { default: null },
		disabled: { default: false }
	},
	actionHandlers: {
		['NOW_SELECT#SELECTED_ITEM_SET']: ({ dispatch, action }) => {
			dispatch(INDIVIDUAL_FIELD_UPDATED, action.payload);
		},
		['NOW_INPUT#VALUE_SET']: ({ dispatch, action }) => {
			// debouncedCssUpdate(coeffects);
			dispatch(INDIVIDUAL_FIELD_UPDATED, action.payload);
		},
		[INDIVIDUAL_FIELD_UPDATED]: ({ action, state, dispatch }) => {
			action.stopPropagation();
			const {
				selectedSlotName,
				selectedSlotParent,
				selectedSlotStyles
			} = state.properties;
			if (selectedSlotName && selectedSlotParent) {
				const { property, value } = action.payload;
				const styles = {
					...selectedSlotStyles,
					[property]: value
				};
				if (!styles[property]) {
					delete styles[property];
				}
				dispatch('SN_UIB_ELEMENT_CONFIGURATION_PANEL#SLOT_STYLE_CHANGED', {
					styles,
					elementId: selectedSlotParent,
					selectedSlotName
				});
			}
		},
		[CSS_INPUT_UPDATED]: ({
			state,
			dispatch,
			action: {
				payload: { value, elementId }
			}
		}) => {
			const { selectedSlotName, selectedSlotParent } = state.properties;
			if (selectedSlotName && selectedSlotParent) {
				dispatch('SN_UIB_ELEMENT_CONFIGURATION_PANEL#SLOT_STYLE_CHANGED', {
					styles: value,
					elementId,
					selectedSlotName
				});
			} else {
				dispatch('SN_UIB_ELEMENT_CONFIGURATION_PANEL#STYLE_CHANGED', {
					styles: value,
					elementId
				});
			}
		}
	}
});
