import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-choice-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import '../sn-uib-property-description-tooltip';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';

import '@servicenow/now-dropdown';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-button';

const BUTTON_GROUP_CHOICE_CLICKED =
	'SN_UIB_CHOICE_PROPERTY_INPUT#BUTTON_GROUP_CHOICE_CLICKED';
// Dispatched action types
export const CHOICE_PROPERTY_VALUE_CHANGED =
	'SN_UIB_CHOICE_PROPERTY_INPUT#VALUE_CHANGED';

const setValueForNowCheckbox = (value) => {
	switch (value) {
		case 'true':
			return true;
		case 'false':
			return false;
		case 'indeterminite':
		default:
			return value;
	}
};

const getSelectedItem = (value, defaultValue, selectedComponentTag) => {
	if (selectedComponentTag === 'now-checkbox') {
		return value === true ? 'true' : value === false ? 'false' : value;
	}
	return value ? value : defaultValue ? defaultValue : '';
};

const renderRadioGroup = (properties) => {
	const { value, defaultValue, choices, disabled } = properties;
	const radioValue = value || defaultValue;
	const radioChoices = choices.map((choice) => {
		if (choice.id === radioValue) {
			choice.checked = true;
		}
		return choice;
	});

	return (
		<now-radio-buttons
			value={radioValue}
			options={radioChoices}
			disabled={disabled}
			layout="vertical"
		/>
	);
};

const renderSelect = (properties) => {
	const {
		choices,
		value,
		defaultValue,
		container,
		label,
		disabled,
		selectedComponentTag
	} = properties;
	const selectedItem = getSelectedItem(
		value,
		defaultValue,
		selectedComponentTag
	);

	return (
		<now-select
			variant="tertiary"
			items={choices}
			selected-item={selectedItem}
			panel-fit-props={{ container }}
			configAria={{ 'aria-label': label }}
			disabled={disabled}
		/>
	);
};

const renderButtonGroup = (properties, dispatch) => {
	const { value, defaultValue, choices, disabled } = properties;
	const selectedValue = value || defaultValue;
	return (
		<div className="choice-button-group">
			{choices.map((c, index) => {
				const classes = {
					'first-button': index === 0,
					'last-button': index === choices.length - 1,
					selected: c.id === selectedValue,
					disabled: disabled
				};
				return (
					<button
						class={classes}
						on-click={() => {
							dispatch(BUTTON_GROUP_CHOICE_CLICKED, { value: c.id });
						}}>
						{c.label}
					</button>
				);
			})}
		</div>
	);
};

const renderChoiceSelection = (properties, dispatch) => {
	const { variant } = properties;

	switch (variant) {
		case 'radio-group':
			return renderRadioGroup(properties);
		case 'button-group':
			return renderButtonGroup(properties, dispatch);
		case 'select':
		default:
			return renderSelect(properties);
	}
};

const view = ({ properties }, { dispatch }) => {
	const { label, variant, description, selectedComponentTag } = properties;

	const isSelect = variant === 'select';
	const inputLabelClasses = {
		['sn-uib-property-input-label']: true,
		['select-variant']: isSelect
	};

	return (
		<Fragment>
			<div className="sn-uib-property-input">
				<label class={inputLabelClasses}>
					{label}
					{!description ? null : (
						<sn-uib-property-description-tooltip
							description={description}
							selectedComponentTag={selectedComponentTag}
						/>
					)}
				</label>
				{renderChoiceSelection(properties, dispatch)}
			</div>
		</Fragment>
	);
};

/**
 * An input component to be used for choice properties
 * configured on the UI Builder properties editor panel
 */
createCustomElement(config.name, {
	view,
	styles,
	properties: config.properties,
	initialState: {
		editingScript: false
	},
	actionHandlers: {
		'NOW_SELECT#SELECTED_ITEM_SET': ({ action, dispatch, properties }) => {
			const {
				payload: { value }
			} = action;

			action.stopPropagation();
			let valueToSet = value;

			if (properties.selectedComponentTag === 'now-checkbox') {
				valueToSet = setValueForNowCheckbox(value);
			}

			dispatch(CHOICE_PROPERTY_VALUE_CHANGED, {
				value: valueToSet
			});
		},
		['NOW_RADIO_BUTTONS#VALUE_SET']: ({ action, dispatch }) => {
			const {
				payload: { value }
			} = action;

			action.stopPropagation();

			dispatch(CHOICE_PROPERTY_VALUE_CHANGED, {
				value
			});
		},
		[BUTTON_GROUP_CHOICE_CLICKED]: ({ action, dispatch }) => {
			const {
				payload: { value }
			} = action;

			action.stopPropagation();
			dispatch(CHOICE_PROPERTY_VALUE_CHANGED, {
				value
			});
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	}
});
