import { createCustomElement, actionTypes } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { memoizeDeepN } from 'seismic-memo-utils'; //eslint-disable-line no-unused-vars
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-popover';
import { t } from 'sn-translate';
import '../sn-uib-collapsible-panel';
import '../sn-uib-error';
import '../sn-uib-loader';
import '../sn-uib-flat-list';
import { overridesTreeSearchPayload } from './sn-uib-overrides-tree-popover';
import { SN_UIB_CONTENT_TREE_PICKER_TREE_ITEM_CLICKED } from '../sn-uib-content-tree-picker';
import { SN_UIB_PAGE_MANAGEMENT_POPOVER_ACTION_CLICKED } from '../sn-uib-page-management-popover';

import styles from './sn-uib-generic-page-navigator.scss';
import isArray from 'lodash/isArray';
import isEqual from 'lodash/isEqual';
// eslint-disable-next-line no-unused-vars
import SearchBox from '../partials/search-box';

import {
	FLAT_LIST_ITEM_CLICKED,
	FLAT_LIST_ITEM_POPOVER_TRIGGERED,
	FLAT_LIST_ITEM_POPOVER_CLOSED
} from '../sn-uib-flat-list-item/actions';
import {
	CREATE_PAGE_BUTTON_CLICKED,
	CREATE_VARIANT_BUTTON_CLICKED,
	PAGE_SELECTED,
	CREATE_PAGE_CLICKED,
	CREATE_VARIANT_CLICKED,
	SCREEN_TYPE_SELECTED,
	OVERRIDES_LIST_REQUESTED
} from './actions';

import { findSelectedItem } from './utils';
import memoize from 'lodash/memoize';
import get from 'lodash/get';

const findSelectedItemMemoized = memoize(findSelectedItem);

const Content = {
	dropdown: 'dropdown',
	list: 'list'
};

const variantSearchPayload = { category: 'variant-search' };

const renderError = () => {
	return (
		<div className={'error-message'}>
			<sn-uib-error message={t('There was an issue loading your pages')} />
		</div>
	);
};

const renderOverridesContent = (
	_,
	currentOverridesTree,
	overridesTreeSearch,
	overridesFetching,
	selectedVariant
) => {
	return (
		<sn-uib-overrides-tree-popover
			currentOverridesTree={currentOverridesTree}
			overridesTreeSearch={overridesTreeSearch}
			overridesFetching={overridesFetching}
			selected-override={selectedVariant}
		/>
	);
};

const renderOverridesHeaderPopover = (
	currentOverridesTree,
	overridesTreeSearch,
	overridesFetching,
	dashboardId,
	headerPopoverOpened
) => {
	return (
		<now-popover
			manage-opened
			append-to-payload={{ context: 'header-popover' }}
			opened={headerPopoverOpened}>
			<now-button-iconic
				append-to-payload={{ context: 'header-overrides' }}
				icon="building-outline"
				size="sm"
				slot="trigger"
			/>
			<sn-uib-overrides-tree-popover
				slot="content"
				currentOverridesTree={currentOverridesTree}
				overridesTreeSearch={overridesTreeSearch}
				overridesFetching={overridesFetching}
				dashboard-id={dashboardId}
			/>
		</now-popover>
	);
};

const getVariants = (currentPages, selectedItem) => {
	if (!currentPages || !selectedItem) {
		return null;
	}

	return currentPages.find((page) => page.id === selectedItem);
};

const mapPagesToItems = (pages) =>
	pages.map((page) => ({
		id: page.id,
		label: page.label
	}));

const renderFlatList = memoizeDeepN(function (
	isDomainExpanded,
	variantItems,
	selectedVariant,
	listHeaderLabel,
	listHeaderActions,
	disabled,
	variantActionsDisabled,
	currentOverridesTree,
	overridesTreeSearch,
	overridesFetching
) {
	const items = isDomainExpanded
		? variantItems.filter((variantItem) => !variantItem.overrides)
		: variantItems;
	const condition = (item) =>
		!!item.overrides ||
		(isDomainExpanded &&
			item.domainName === 'global' &&
			!item.overrides &&
			variantItems.some((itm) => itm.overrides === item.id));
	const renderer = (item) =>
		renderOverridesContent(
			item,
			currentOverridesTree,
			overridesTreeSearch,
			overridesFetching,
			selectedVariant
		);
	return (
		<div className="flat-list">
			<sn-uib-flat-list
				items={items}
				selectedItems={[selectedVariant]}
				listType={listHeaderLabel}
				actions={listHeaderActions}
				disabled={disabled}
				actions-disabled={variantActionsDisabled}
				item-popover={{
					condition: condition,
					renderer: renderer,
					buttonLabel: t('Overrides'),
					buttonIcon: 'building-outline'
				}}
			/>
		</div>
	);
});

const renderContent = (
	error,
	isPagesLoading,
	currentPages,
	pageVariantsEnabled,
	selectedPage,
	selectedVariant,
	selectedPageIsTab,
	type,
	listHeaderLabel,
	listHeaderActions,
	isDashboardBuilder,
	variantSearch,
	currentOverridesTree,
	overridesTreeSearch,
	isDomainExpanded,
	overridesFetching,
	headerPopoverOpened,
	disabled,
	variantActionsDisabled,
	dispatch
) => {
	const currentPageObj =
		currentPages.find((pg) => pg.id === selectedPage) || {};

	if (error) {
		return renderError();
	} else if (isPagesLoading) {
		return (
			<div className="loader">
				<now-loader />
				<p className="loader-label">{t('Loading...')}</p>
			</div>
		);
	} else if (type === Content.dropdown) {
		return (
			<Fragment>
				{currentPages && currentPages.length > 0 ? (
					<sn-uib-content-tree-picker
						className="item-selection-dropdown"
						items={mapPagesToItems(currentPages)}
						currentItem={currentPageObj}
						disabled={disabled}
						icon={false}
						searchLabel={''}
						searchPlaceholder={t('Select page')}
						hideApplyButton={true}
						bare={true}
					/>
				) : (
					<div className="loader">
						<now-loader />
						<p className="loader-label">{t('Loading...')}</p>
					</div>
				)}
				{(isDashboardBuilder === 'true' || isDashboardBuilder === true) &&
				(isDomainExpanded || currentPageObj.overrides) ? (
					<div className="header-overrides-flyout-container">
						{renderOverridesHeaderPopover(
							currentOverridesTree,
							overridesTreeSearch,
							overridesFetching,
							currentPageObj.id,
							headerPopoverOpened
						)}
					</div>
				) : null}
			</Fragment>
		);
	} else if (pageVariantsEnabled) {
		const view = getVariants(currentPages, selectedPage);
		if (view && view.children) {
			const variantItems = view.children.filter(
				({ label }) =>
					label.toLowerCase().indexOf(variantSearch.toLowerCase()) > -1
			);
			return (
				<Fragment>
					<div className="variant-search">
						<SearchBox
							placeholder={t('Search')}
							value={variantSearch}
							appendToPayload={variantSearchPayload}
							dispatch={dispatch}
						/>
					</div>
					{renderFlatList(
						isDomainExpanded,
						variantItems,
						selectedVariant,
						listHeaderLabel,
						listHeaderActions,
						disabled,
						variantActionsDisabled,
						currentOverridesTree,
						overridesTreeSearch,
						overridesFetching
					)}
				</Fragment>
			);
		}
	}
};

const renderDropdownActions = (
	error,
	isPagesLoading,
	selectedDropdownItem,
	currentPages,
	dropdownActionsPopoverOpened,
	createDropdownActions,
	dropdownHeaderText
) => {
	if (
		!error &&
		!isPagesLoading &&
		currentPages &&
		currentPages.length > 0 &&
		selectedDropdownItem
	) {
		return (
			<Fragment>
				<div className="dropdown-actions">
					<now-popover
						hide-tail
						manage-opened
						append-to-payload={{ button: 'dropdownActions' }}
						opened={dropdownActionsPopoverOpened}>
						<now-button-iconic
							bare
							className="header-button"
							icon="ellipsis-v-outline"
							size="sm"
							slot="trigger"
							append-to-payload={{ button: 'dropdownActions' }}
							tooltip-content={t('{0} options', dropdownHeaderText)}
							variant="tertiary"
							config-aria={{
								'aria-label': t('{0} options', dropdownHeaderText)
							}}
						/>
						<sn-uib-page-management-popover
							actions={createDropdownActions(
								selectedDropdownItem,
								dropdownHeaderText
							)}
							slot="content"
						/>
					</now-popover>
				</div>
			</Fragment>
		);
	}
};

const renderCreateButton = (
	currentPages,
	pageVariantsEnabled,
	selectedPage
) => {
	if (
		currentPages &&
		currentPages.length > 0 &&
		pageVariantsEnabled &&
		selectedPage
	) {
		return (
			<Fragment>
				<now-button-bare
					bare
					className="header-button header-create-button"
					iconStart="plus-outline"
					label={t('Create')}
					size="sm"
					append-to-payload={{ button: 'createVariant' }}
					tooltip-content={t('Create new variant')}
					variant="tertiary"
					config-aria={{
						'aria-label': t('Create new variant')
					}}
				/>
			</Fragment>
		);
	}
};

const getDropdownHeaderText = (
	dropdownHeaderLabels,
	selectedPageIsTab,
	isDashboardBuilder
) => {
	return selectedPageIsTab
		? dropdownHeaderLabels.TAB
		: isDashboardBuilder
		? dropdownHeaderLabels.DASHBOARD
		: dropdownHeaderLabels.PAGE;
};

const getSelectedDropdownItem = (currentPages, selectedPage) => {
	if (!currentPages) {
		return null;
	}
	return currentPages.find((item) => item.id === selectedPage);
};

const createMemoHash = (
	currentPages,
	selectedPage,
	selectedVariant,
	parentInfo
) => {
	const pageHash = (pages) =>
		pages.reduce((acc, page) => {
			// listen to children's attributes as well
			// to see if they've changed and make them part of our
			// memo cache
			const childHash =
				isArray(page.children) && page.children.length > 0
					? pageHash(page.children)
					: '';
			return (
				acc +
				parentInfo.compositionElementId +
				page.pageSysId +
				page.id +
				page.disabled +
				page.label +
				page.routeType +
				page.icon +
				childHash
			);
		}, '');
	return pageHash(currentPages) + selectedPage + selectedVariant;
};

const getDropdownItems = (
	currentPages,
	selectedPage,
	selectedVariant,
	parentInfo
) => {
	if (!currentPages || (currentPages && currentPages.length === 0)) {
		return [];
	}
	const memoHash = createMemoHash(
		currentPages,
		selectedPage,
		selectedVariant,
		parentInfo
	);
	return findSelectedItemMemoized(
		memoHash,
		currentPages,
		selectedPage,
		parentInfo
	);
};

const renderVariantList = ({
	isDashboardBuilder,
	listHeaderLabel,
	currentPages,
	pageVariantsEnabled,
	selectedPageIsTab,
	error,
	isPagesLoading,
	selectedPage,
	selectedVariant,
	listHeaderActions,
	variantSearch,
	currentOverridesTree,
	overridesTreeSearch,
	isDomainExpanded,
	overridesFetching,
	headerPopoverOpened,
	disabled,
	variantActionsDisabled,
	dispatch
}) => {
	if (isDashboardBuilder) {
		return null;
	}

	return (
		<Fragment>
			<div className="header list">
				<span className="p1-panel-header-text">{listHeaderLabel}</span>
				{renderCreateButton(currentPages, pageVariantsEnabled, selectedPage)}
			</div>
			<div className="variant-section">
				{renderContent(
					error,
					isPagesLoading,
					currentPages,
					pageVariantsEnabled,
					selectedPage,
					selectedVariant,
					selectedPageIsTab,
					Content.list,
					listHeaderLabel,
					listHeaderActions,
					isDashboardBuilder,
					variantSearch,
					currentOverridesTree,
					overridesTreeSearch,
					isDomainExpanded,
					overridesFetching,
					headerPopoverOpened,
					disabled,
					variantActionsDisabled,
					dispatch
				)}
			</div>
		</Fragment>
	);
};

const view = (state, { dispatch }) => {
	const { properties, variantSearch, overridesTreeSearch } = state;
	const {
		currentPages,
		selectedPage: propSelectedPage,
		selectedVariant,
		isPagesLoading,
		error,
		pageVariantsEnabled,
		selectedPageIsTab,
		dropdownHeaderLabels,
		listHeaderLabel,
		createDropdownActions,
		listHeaderActions,
		isDashboardBuilder,
		currentOverridesTree,
		isDomainExpanded,
		overridesFetching,
		disabled,
		variantActionsDisabled
	} = properties;
	const { dropdownActionsPopoverOpened, headerPopoverOpened } = state;
	const dropdownHeaderText = getDropdownHeaderText(
		dropdownHeaderLabels,
		selectedPageIsTab,
		isDashboardBuilder
	);

	const validSelectedPage =
		currentPages !== undefined && currentPages !== null
			? currentPages.find(({ id }) => propSelectedPage === id)
			: false;

	const selectedPage = validSelectedPage ? validSelectedPage.id : '';

	return (
		<div className="page-navigator-content">
			<div className="page-navigator">
				<div className="header dropdown ellipsis-header-row page-selector-header">
					<div className="dropdown-selection">
						<span className="p1-panel-header-text header-text">
							{`${dropdownHeaderText}:`}
						</span>
						{renderContent(
							error,
							isPagesLoading,
							currentPages,
							pageVariantsEnabled,
							selectedPage,
							selectedVariant,
							selectedPageIsTab,
							Content.dropdown,
							listHeaderLabel,
							listHeaderActions,
							isDashboardBuilder,
							{},
							currentOverridesTree,
							overridesTreeSearch,
							isDomainExpanded,
							overridesFetching,
							headerPopoverOpened,
							disabled,
							variantActionsDisabled,
							dispatch
						)}
					</div>
					{renderDropdownActions(
						error,
						isPagesLoading,
						getSelectedDropdownItem(currentPages, selectedPage),
						currentPages,
						dropdownActionsPopoverOpened,
						createDropdownActions,
						dropdownHeaderText
					)}
				</div>
				{renderVariantList({
					isDashboardBuilder,
					listHeaderLabel,
					currentPages,
					pageVariantsEnabled,
					selectedPageIsTab,
					error,
					isPagesLoading,
					selectedPage,
					selectedVariant,
					listHeaderActions,
					variantSearch,
					currentOverridesTree,
					overridesTreeSearch,
					isDomainExpanded,
					overridesFetching,
					headerPopoverOpened,
					disabled,
					variantActionsDisabled,
					dispatch
				})}
			</div>
		</div>
	);
};

createCustomElement('sn-uib-generic-page-navigator', {
	view,
	styles,
	initialState: {
		dropdownActionsPopoverOpened: false,
		newPopoverOpened: false,
		popoverActions: [],
		variantSearch: '',
		overridesTreeSearch: '',
		headerPopoverOpened: false
	},
	properties: {
		pages: {
			default: []
		},
		currentPages: {
			default: []
		},
		isDashboardBuilder: {
			default: false
		},
		isPagesLoading: {
			default: false
		},
		selectedPage: {
			default: ''
		},
		selectedVariant: {
			default: ''
		},
		error: {
			default: false
		},
		pageVariantsEnabled: {
			default: false
		},
		selectedPageIsTab: {
			default: false
		},
		dropdownHeaderLabels: {
			default: {}
		},
		createDropdownActions: {
			default: () => {}
		},
		listHeaderLabel: {
			default: ''
		},
		listHeaderActions: {
			default: []
		},
		parentInfo: {
			default: '@uxf/parentInfo'
		},
		currentOverridesTree: {
			default: {}
		},
		isDomainExpanded: {
			default: false
		},
		overridesFetching: {
			default: false
		},
		disabled: {
			default: false
		},
		variantActionsDisabled: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			updateProperties,
			dispatch,
			properties,
			action
		}) => {
			const { name, value, previousValue } = action.payload;
			const { currentPages } = properties;
			if (
				name === 'selectedPageIsTab' ||
				// Need to prevent an infinite loop.  Since prev and curr
				// could be equal on initial load the currentPages length
				// check is needed to work around a Seismic issue
				(name === 'pages' &&
					(!isEqual(value, previousValue) || !currentPages.length)) ||
				name === 'selectedPage' ||
				name === 'selectedVariant' ||
				name === 'parentInfo'
			) {
				const {
					selectedPageIsTab,
					pages,
					selectedPage,
					selectedVariant,
					parentInfo
				} = properties;
				let currentPages = [];

				currentPages = selectedPageIsTab
					? getDropdownItems(pages, selectedPage, selectedVariant, parentInfo)
					: pages;

				updateProperties({
					currentPages
				});

				dispatch('UIB_APP#NAVIGATOR_CURRENT_PAGES_CHANGED', {
					currentPages
				});
			}
		},
		[CREATE_PAGE_CLICKED]: ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			const { isCreateDynamicTab } = action.payload;
			dispatch(CREATE_PAGE_BUTTON_CLICKED, {
				type: isCreateDynamicTab ? 'dynamicTab' : null
			});

			updateState({
				dropdownActionsPopoverOpened: false
			});
		},
		'NOW_BUTTON#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();

			dispatch(CREATE_PAGE_BUTTON_CLICKED, action.payload);
		},
		'NOW_INPUT#INPUT': ({ action, updateState }) => {
			action.stopPropagation();
			const { fieldValue: variantSearch, category, context } = action.payload;
			if (category === variantSearchPayload.category) {
				updateState({
					variantSearch
				});
			} else if (context === overridesTreeSearchPayload.category) {
				updateState({ overridesTreeSearch: action.payload.fieldValue });
			}
		},
		[CREATE_VARIANT_CLICKED]: ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			dispatch(CREATE_VARIANT_BUTTON_CLICKED);

			updateState({
				dropdownActionsPopoverOpened: false
			});
		},
		[SN_UIB_PAGE_MANAGEMENT_POPOVER_ACTION_CLICKED]: ({ updateState }) => {
			updateState({
				dropdownActionsPopoverOpened: false
			});
		},
		'NOW_BUTTON_BARE#CLICKED': ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			if (action.payload.button === 'createVariant') {
				dispatch(CREATE_VARIANT_BUTTON_CLICKED);

				updateState({
					dropdownActionsPopoverOpened: false
				});
			} else if (action.payload.button !== 'create') {
				return;
			} else {
				dispatch(CREATE_PAGE_BUTTON_CLICKED, {
					type: null
				});
			}
		},
		[SN_UIB_CONTENT_TREE_PICKER_TREE_ITEM_CLICKED]: {
			effect: (coeffect) => {
				const {
					action: {
						payload: { id }
					},
					dispatch,
					properties: { currentPages, isDashboardBuilder, selectedPage }
				} = coeffect;
				if (id !== selectedPage) {
					const currentPage = currentPages.find((page) => id === page.id);

					if (isDashboardBuilder) {
						dispatch(SCREEN_TYPE_SELECTED, {
							item: { ...currentPage, pageSysId: currentPage.id }
						});
						return;
					}

					// currentPage.children are the variants of the page
					const currentPageVariants = get(currentPage, 'children');

					// when the currentPage has variants, find the variant with the lowest order to default to
					if (currentPageVariants && currentPageVariants.length > 0) {
						const lowestOrderVariant = currentPageVariants.reduce(
							(minOrderVariant, currentVariant) =>
								currentVariant.order < minOrderVariant
									? currentVariant
									: minOrderVariant
						);
						dispatch(PAGE_SELECTED, { item: lowestOrderVariant });
					} else {
						// if there were no defaults, then load the page and show the splash page for no variants
						dispatch(SCREEN_TYPE_SELECTED, {
							item: currentPage
						});
					}
				}
			},
			stopPropagation: true
		},
		[FLAT_LIST_ITEM_CLICKED]: ({
			action: {
				payload: { item, type },
				stopPropagation
			},
			properties: { selectedVariant },
			dispatch
		}) => {
			stopPropagation();
			if (type !== 'itemAction' && selectedVariant !== item.id) {
				dispatch(PAGE_SELECTED, { item });
			}
		},
		[FLAT_LIST_ITEM_POPOVER_TRIGGERED]: ({ action, dispatch, properties }) => {
			const { item } = action.payload;
			const { selectedPage, currentPages } = properties;
			const view = getVariants(currentPages, selectedPage);
			dispatch(OVERRIDES_LIST_REQUESTED, {
				variant: item,
				variants: view.children
			});
		},
		[FLAT_LIST_ITEM_POPOVER_CLOSED]: ({ updateState }) => {
			updateState({ headerPopoverOpened: false });
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({
			action,
			properties,
			dispatch,
			updateState
		}) => {
			if (action.payload.button === 'dropdownActions') {
				action.stopPropagation();
			}

			if (action.payload.context === 'header-overrides') {
				action.stopPropagation();
				updateState({ headerPopoverOpened: true });
				dispatch(OVERRIDES_LIST_REQUESTED, {
					dashboard: properties.currentPages.find(
						(pg) => pg.id === properties.selectedPage
					)
				});
			}

			if (action.payload.context === 'overrides-content-close') {
				action.stopPropagation();
				updateState({ headerPopoverOpened: false });
			}
		},
		'NOW_POPOVER#OPENED_SET': ({ action, updateState }) => {
			action.stopPropagation();
			if (action.payload.button === 'dropdownActions') {
				updateState({
					dropdownActionsPopoverOpened: action.payload.value
				});
			}
			if (action.payload.context === 'header-popover') {
				updateState({
					headerPopoverOpened: action.payload.value
				});
			}
		}
	}
});
