import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';
import get from 'lodash/get';
import isNil from 'lodash/isNil';
import isEqual from 'lodash/isEqual';
import difference from 'lodash/difference';

const viewportScreenActions = (viewportPropertiesConfiguration) => {
	const disableIsolationEditing = get(
		viewportPropertiesConfiguration,
		'disableIsolationEditing',
		false
	);
	const baseScreenActions = [];
	if (!disableIsolationEditing) {
		baseScreenActions.push({
			icon: 'isolation-outline',
			label: t('Navigate to tab'),
			id: 'navigate'
		});
	}

	return baseScreenActions;
};

export const createDropdownItems = (properties) => {
	const items = [];
	const { viewportRoutes } = properties;
	const routeKeys = Object.keys(viewportRoutes);
	routeKeys.forEach((key) => {
		viewportRoutes[key].forEach((route) => {
			items.push({
				id: route.routeType,
				label: route.name,
				order: route.order
			});
		});
	});
	return [
		{
			id: '',
			label: '',
			order: '-999999'
		},
		...items.sort((a, b) => a.order - b.order)
	];
};

export const sortByOrder = (a, b) => a.order - b.order;

export const isDataDrivenTab = (route) => !isEmpty(route.fields);

const isDataDrivenSubrouteSelected = (activeRoute, currentSubroute) => {
	const activeRouteFields = get(activeRoute, 'macroponents[0].fields', {});
	const currentSubrouteFields = get(currentSubroute, 'fields', {});
	const isRouteAndSubroutesPathSame =
		activeRoute.routeType === currentSubroute.route;
	return (
		isEqual(activeRouteFields, currentSubrouteFields) &&
		isRouteAndSubroutesPathSame
	);
};

const getResolvedDataDrivenSubroutes = (
	route,
	repeatedTabData,
	activeRoute
) => {
	if (isNil(repeatedTabData)) {
		return [];
	}
	const resolvedDataBinding = Array.isArray(repeatedTabData)
		? repeatedTabData
		: [];

	const dataDrivenSubroutes = [];
	resolvedDataBinding.forEach((elem, i) => {
		if (elem.route === route.routeType) {
			dataDrivenSubroutes.push({
				id: `${route.routeType}-${i}`,
				label: elem.label,
				order: i,
				isDraggable: false,
				isExpanded: true,
				isSelected: isDataDrivenSubrouteSelected(activeRoute, elem),
				actions: null,
				isDataDrivenTab: true,
				isRepeatedTabPanel: true,
				routeType: route.routeType,
				properties: {
					fields: route.fields,
					icon: route.icon,
					name: route.name,
					order: route.order,
					path: route.routeType
				}
			});
		}
	});

	return dataDrivenSubroutes;
};

const getRouteActions = (route, appsSysId, isDashboardBuilder) => {
	if (route.appConfig === appsSysId || isDashboardBuilder) {
		return [];
	} else {
		return [
			{
				icon: 'combine-fill',
				label: t('Shared route'),
				id: 'shared-route'
			}
		];
	}
};

export const createDraggableItems = (
	properties,
	collapsedItems = {},
	appsSysId,
	isDashboardBuilder
) => {
	const staticTabItems = [];
	const dynamicTabItems = [];
	const {
		viewportRoutes,
		activeRoute,
		activeScreen,
		viewportPropertiesConfiguration,
		repeatedTabData,
		viewportDataSource,
		clientScriptApi
	} = properties;
	const routeKeys = Object.keys(viewportRoutes);
	const isScreenActive = (currentMacroponent) =>
		isEmpty(activeScreen) ? false : activeScreen.id === currentMacroponent.id;

	routeKeys.forEach((key) => {
		(viewportRoutes[key] || []).forEach((route) => {
			const subrouteItems = [];
			for (let i = 0; i < route.macroponents.length; i++) {
				let convertToPillarItem = [];
				const curMacroponent = route.macroponents[i];
				if (isDataDrivenTab(route)) {
					convertToPillarItem = getResolvedDataDrivenSubroutes(
						route,
						resolveDataSourceFromClientScriptApi({
							repeatedTabData,
							viewportDataSource,
							clientScriptApi
						}),
						activeRoute
					);
				}
				subrouteItems.push({
					id: curMacroponent.id,
					label: curMacroponent.name,
					order: curMacroponent.order,
					isExpanded: isNil(collapsedItems[curMacroponent.id])
						? true
						: collapsedItems[curMacroponent.id],
					isSelected: isScreenActive(curMacroponent),
					actions: viewportScreenActions(viewportPropertiesConfiguration),
					items: convertToPillarItem,
					isDataDrivenTab: isDataDrivenTab(route),
					isDraggable: false,
					routeType: route.routeType,
					properties: {
						fields: route.fields,
						icon: route.icon,
						name: route.name,
						order: route.order,
						path: route.routeType
					}
				});
			}
			const pillarItem = {
				id: route.routeType,
				label: route.name,
				order: route.order,
				isExpanded: isNil(collapsedItems[route.routeType])
					? true
					: collapsedItems[route.routeType],
				isSelected: route.routeType === activeRoute.routeType,
				actions: getRouteActions(route, appsSysId, isDashboardBuilder),
				items: subrouteItems.sort(sortByOrder),
				isDraggable: false,
				properties: {
					fields: route.fields,
					icon: route.icon,
					name: route.name,
					order: route.order,
					path: route.routeType
				}
			};
			if (isDataDrivenTab(route)) {
				dynamicTabItems.push({
					...pillarItem,
					isDataDrivenTab: true
				});
			} else {
				staticTabItems.push({
					...pillarItem,
					isDataDrivenTab: false
				});
			}
		});
	});
	return {
		staticTabItems: staticTabItems.sort(sortByOrder),
		dynamicTabItems: dynamicTabItems.sort(sortByOrder)
	};
};

export const validateRepeatedTabData = (
	repeatedTabData,
	viewportDataSource = ''
) => {
	const allowedKeys = ['route', 'label', 'fields'];
	let error = null;

	if (!Array.isArray(repeatedTabData)) {
		error = t('Data driven tabs can only be bound to arrays as input source');
	} else {
		if (!viewportDataSource.startsWith('@data.')) {
			return t('Data driven tabs can only be bound to data resources');
		}
		repeatedTabData.forEach((data) => {
			if (!error) {
				if (data && typeof data === 'object') {
					const keys = Object.keys(data);
					const missingKeys = difference(allowedKeys, keys);
					const { route, fields, label } = data;

					if (missingKeys.length) {
						/* prettier-ignore */
						error = t('Missing keys: "{0}". Required keys are "route", "label", and "fields"', missingKeys.join(','));
					} else if (typeof route !== 'string') {
						/* prettier-ignore */
						error = t('Route should be a string, found {0}', JSON.stringify(route));
					} else if (typeof label !== 'string') {
						/* prettier-ignore */
						error = t('Label should be a string, found {0}', JSON.stringify(label));
					} else if (!fields || typeof fields !== 'object') {
						/* prettier-ignore */
						error = t('Fields should be an object, found {0}', JSON.stringify(fields));
					}
				} else {
					/* prettier-ignore */
					error = t('Expected an object with keys "route", "label", and "fields". Found {0} instead.', data);
				}
			}
		});
	}

	return error;
};

export const generateDataDrivenTabPayload = (item, repeatedTabData) => {
	const dataDrivenTabInfo = {};
	if (get(item, 'isDataDrivenTab', false)) {
		const routeOrScreenDefaultIndex = 0;
		const repeatedTabIndex = item.isRepeatedTabPanel
			? item.order
			: routeOrScreenDefaultIndex;
		dataDrivenTabInfo.dataDrivenViewportParentId =
			item.routeType || get(item, 'properties.path', '');

		const selectedRepeatedTabItem = repeatedTabData.find(
			(elem) => elem.route === item.properties.path
		);
		// if repeatedTabIndex is 0 which means the route or screen are selected otherwise the repeated tabs are selected.
		dataDrivenTabInfo.fields = repeatedTabIndex
			? repeatedTabData[repeatedTabIndex].fields
			: selectedRepeatedTabItem.fields;
	}
	return dataDrivenTabInfo;
};

export const updateRootContainerId = (rootContainerId, itemProperties) => {
	if (rootContainerId && isDataDrivenTab(itemProperties)) {
		return itemProperties.path;
	}
	return rootContainerId;
};

/**
 * @param  {Array} {repeatedTabData
 * @param  {String} viewportDataSource
 * @param  {Object} clientScriptApi}
 */
export const resolveDataSourceFromClientScriptApi = ({
	repeatedTabData,
	viewportDataSource,
	clientScriptApi
}) => {
	if (!repeatedTabData && viewportDataSource) {
		const dataSource = viewportDataSource || '';
		const sanitizedDataBinding = dataSource.replace('@', '');
		const resolvedDataBindings = get(
			clientScriptApi,
			sanitizedDataBinding,
			null
		);
		return resolvedDataBindings;
	}
	return repeatedTabData;
};
