import messageConditions, { IGNORE } from './messageConditions';
import { t } from 'sn-translate';

const cancelAction = {
	text: t('Cancel'),
	context: 'cancel'
};

/**
 * Creates a list of actions to display
 *
 * @typedef {object} Transaction_Action
 * @property {string} context What the action should do
 * @property {string} text What the button should say
 *
 * @param {object} properties The component properties
 * @param {object} options The options returned from the message
 * @param {boolean} isEditing Is a transaction in progress
 * @returns {Array<Transaction_Action>} The actions to display
 */
export const getActions = (properties, options, isEditing) => {
	const {
		transactionBannerEditAction,
		transactionBannerOtherActions
	} = options;

	const { recordIsOverridenInCurrentDomain } = properties;
	const actionFilter = ({ context }) =>
		!(context === 'override' && recordIsOverridenInCurrentDomain);

	return !isEditing
		? [
				...(transactionBannerEditAction ? [transactionBannerEditAction] : []),
				...(transactionBannerOtherActions
					? [transactionBannerOtherActions]
					: [])
		  ].filter(actionFilter)
		: [cancelAction];
};

/**
 * Uses the properties and various strings to generate a
 * configuration for the component that contains messaging,
 * actions, and other metadata.
 *
 * @param {object} properties The component's properties
 * @param {messageConditions.Message_StringValues} stringValues The needed string values for message templates
 * @returns {object} The matching row
 */
export const getMessage = (properties, stringValues, isEditing) => {
	const {
		requestScopeId,
		requestDomainId,
		recordProtectionPolicy
	} = properties;

	if (!requestScopeId && !requestDomainId && recordProtectionPolicy) {
		return null;
	}
	const messageValues = getTransactionProperties(properties, isEditing);

	const states = messageConditions(stringValues);

	const messages = states.filter(({ conditions }) => {
		const matchesMessageValue = ([key, value]) => {
			// console.log(key, value, messageValues[key]);
			return value === IGNORE ? true : messageValues[key] === value;
		};
		return Object.entries(conditions).every(matchesMessageValue);
	});

	if (messages && messages.length > 1) {
		const { highest, specificityMessageTuples } = messages.reduce(
			({ highest, specificityMessageTuples }, m) => {
				const nonIgnoredConditions = Object.entries(m.conditions).filter(
					([, v]) => v !== IGNORE
				);
				const nullValueConditions = nonIgnoredConditions.filter(
					([, v]) => v === null
				);
				const specificity =
					nonIgnoredConditions.length - nullValueConditions.length / 2;
				return {
					highest: specificity > highest ? specificity : highest,
					specificityMessageTuples: [
						...specificityMessageTuples,
						[specificity, m]
					]
				};
			},
			{ highest: 0, specificityMessageTuples: [] }
		);

		const matching = specificityMessageTuples
			.filter(([specificity]) => specificity === highest)
			.map(([, m]) => m);

		// eslint-disable-next-line no-undef
		if (matching.length > 1 && process.env.NODE_ENV === 'test') {
			/* eslint-disable no-console */
			console.groupCollapsed(
				`[sn-uib-transaction-alert]: ${
					matching.length
				} states matched: Rows ${matching.map((m) => m.rowNo).join(', ')}`
			);
			console.error('Conditions');
			console.table(matching.map((m) => m.conditions));
			console.error('Messages');
			console.table(matching.map((m) => m.message));
			console.error('Actions');
			console.table(matching.map((m) => m.options));
			console.error(`Selecting first option`);
			console.dir(matching[0]);
			console.groupEnd();
			/* eslint-enable no-console */
		}

		return matching[0];
	}

	return messages && messages[0];
};

export const mixedScopes = (requestScopeId, requestPageDefinitionScopeId) =>
	requestPageDefinitionScopeId === null
		? false
		: requestScopeId !== requestPageDefinitionScopeId;

export const getTransactionProperties = (properties, isEditing = false) => {
	const {
		userScope,
		requestScopeId,
		userDomain,
		requestDomainId,
		requestPageDefinitionScopeId,
		applications = [],
		domains = [],
		supportDomains,
		supportScope,
		isSharedPageDef: screenUsingSharedPageDef,
		isDomainExpanded
	} = properties;

	const isNotRequestScope = ({ id }) => id !== requestScopeId;
	const isRequestDomain = ({ id }) => id === requestDomainId;

	// RECORD SCOPE
	const recordPrivateScope =
		requestScopeId !== null && applications.every(isNotRequestScope);
	const userInSameScope =
		isEditing ||
		userScope === null ||
		requestScopeId === null ||
		(userScope && userScope.id === requestScopeId);
	// The below column is not doing anything in Q, per Jina Li
	// const userCanEditInScope = noScope ? null : screenPrivateScope === false; // TODO: Fix me?

	// RECORD DOMAIN
	// must always check that what is being passed in is null because otherwise the
	// flag doesnt make sense
	const userInSameDomain =
		isEditing ||
		userDomain === null ||
		requestDomainId === null ||
		(userDomain && userDomain.id === requestDomainId);
	const userCanEditInDomain =
		requestDomainId === null || domains.some(isRequestDomain);

	// PAGE DEF
	const screenAndPageDefDifferentScope = mixedScopes(
		requestScopeId,
		requestPageDefinitionScopeId
	);

	const userInGlobalWithExpandedScopeAndRecordInChild =
		userDomain && userDomain.id === 'global' && isDomainExpanded;

	const { recordProtected, pageDefProtected } = applicableProtectionPolicies(
		properties
	);

	return {
		// Domain
		userCanAccessDomain: supportDomains, // User can see domain?
		userCanEditInDomain, // Can user edit the record in the domain?
		userInSameDomain, // User is same domain as screen record?
		// Scope
		userCanAccessScope: supportScope, // User can see scope?
		// userCanEditInScope, // Can user edit existing record in the scope?
		userInSameScope, // User in same scope as screen?
		recordPrivateScope, // Screen record private scope?
		// Page Def
		screenAndPageDefDifferentScope, // Screen and Macroponent in mixed scope
		screenUsingSharedPageDef, // Screen using shared page template
		// Protection Policy
		recordProtected, // Screen record is protected
		pageDefProtected, // Macroponent is protected
		userInGlobalWithExpandedScopeAndRecordInChild
	};
};

export const applicableProtectionPolicies = ({
	userRoles = [],
	applications = [],
	requestScopeId,
	recordProtectionPolicy,
	requestPageDefinitionScopeId,
	pageDefProtectionPolicy
}) => {
	const isScopeStoreApp = (scopeId) => {
		if (scopeId === 'global') {
			return true;
		}
		const scope = applications.find(({ id }) => id === scopeId);
		return scope ? scope.className === 'sys_store_app' : true;
	};
	const isUserMaint = userRoles.includes('maint');
	/*
	 *  Protection policy is only applicable if the scope is a store app(foreign scope),
	 *  and is NOT applicable at any time to a MAINT user.
	 */
	const protectionPolicyIsApplicable = (scopeId) =>
		isUserMaint ? false : isScopeStoreApp(scopeId);
	const recordProtected = protectionPolicyIsApplicable(requestScopeId)
		? recordProtectionPolicy
		: null;
	const pageDefProtected =
		recordProtected === null
			? requestPageDefinitionScopeId &&
			  protectionPolicyIsApplicable(requestPageDefinitionScopeId)
				? recordProtected && !pageDefProtectionPolicy
					? recordProtected
					: pageDefProtectionPolicy
				: null
			: recordProtected;

	return { recordProtected, pageDefProtected };
};

export const stringValuesFromProperties = (properties) => {
	const {
		requestScopeName,
		requestDomainName,
		requestPageDefinitionScopeName,
		userScope = {},
		requestItemType
	} = properties;

	return {
		requestScopeName,
		requestDomainName,
		requestPageDefinitionScopeName,
		userScopeName: userScope && userScope.label,
		requestItemType: requestItemType || 'record'
	};
};
