import { createCustomElement } from '@servicenow/ui-core';
import styles from './_sn-uib-slot-wrapper.scss';
import { elementRefMap } from '../sn-uib-element-controls-placement';

const MOUSEOVER = 'UIB_SLOT_WRAPPER#MOUSE_OVER';
const SELECTED = 'UIB_SLOT_WRAPPER#SELECTED';

export const slotRefMap = new Map();
const view = (state, { dispatch }) => {
	const {
		slotName,
		slotParent,
		selectedSlotName,
		selectedSlotParent,
		selectedSlotParentComponent,
		hoveredSlotName,
		hoveredSlotParent,
		elementRef,
		styles
	} = state.properties;

	elementRefMap.set(`${slotParent}_${slotName}`, elementRef, slotParent);

	return (
		<div
			class={{
				['slot-bounds']: true,
				selected:
					selectedSlotName === slotName && selectedSlotParent === slotParent
			}}
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabindex="0"
			hook-insert={() => {
				slotRefMap.set(`${slotParent}-${slotName}`, {
					ref: elementRef,
					displayName: slotName,
					resizeInfo: {}
				});
			}}
			on-mouseover={(event) => {
				event.stopPropagation();
				dispatch(MOUSEOVER, { slotName, slotParent });
			}}
			on-click={(e) => {
				e.stopPropagation();

				dispatch(SELECTED, {
					slotName,
					slotParent,
					selectedSlotParentComponent
				});
				dispatch('UIB_DND_DROPZONE#HOVER', { id: `${slotParent}-${slotName}` });
			}}
			slot={slotName}>
			<div className="slot-wrapper" style={styles} ref={elementRef}>
				<slot name={slotName} />
			</div>
			<div
				class={{
					lines: true,
					hovered:
						hoveredSlotName === slotName && hoveredSlotParent === slotParent,
					selected:
						selectedSlotName === slotName && selectedSlotParent === slotParent
				}}>
				{['top', 'right', 'bottom', 'left'].map((dir) => {
					return <div class={{ line: true, [dir]: true }} />;
				})}
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-slot-wrapper', {
	view,
	properties: {
		elementRef: {
			default: null
		},
		slotName: {
			default: null
		},
		slotParent: {
			default: null
		},
		styles: {},
		selectedSlotName: {
			default: null,
			selectable: true
		},
		selectedSlotParent: {
			default: null,
			selectable: true
		},
		hoveredSlotName: {
			default: null,
			selectable: true
		},
		hoveredSlotParent: {
			default: null,
			selectable: true
		},
		selectedSlotParentComponent: {
			default: null,
			selectable: true
		}
	},
	styles
});

export { MOUSEOVER };
