import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-text-link.scss';
import '@servicenow/now-icon';

export const SN_UIB_TEXT_LINK_CLICKED = 'SN_UIB_TEXT_LINK#CLICKED';

const view = (state, dispatch) => {
	const { properties } = state;
	const { href, target, disabled, manageClicked, label, icon } = properties;

	const linkClassNames = {
		['uib-text-link']: true,
		disabled
	};

	return (
		<div className="uib-text-link-container">
			<a
				class={linkClassNames}
				href={href}
				target={target}
				aria-disabled={disabled}
				on-click={(e) => {
					(disabled || manageClicked) && e.preventDefault();
					!disabled && dispatch(SN_UIB_TEXT_LINK_CLICKED, { href });
				}}>
				{icon ? (
					<now-icon
						icon={icon || 'open-link-fill'}
						size="sm"
						className="uib-text-link-icon"
					/>
				) : null}
				{label || href}
			</a>
		</div>
	);
};

createCustomElement('sn-uib-text-link', {
	view,
	properties: {
		/**
		 * The url to be used in the text link
		 * @type {string}
		 */
		href: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * The label to be displayed on the text link
		 * @type {string}
		 */
		label: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Whether the link should be disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * Whether the consumer is managing the default
		 * click behavior
		 * @type {boolean}
		 */
		manageClicked: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * The icon to be positioned before the link text
		 * @type {string}
		 */
		icon: {
			default: true,
			schema: {
				type: 'string'
			}
		},
		/**
		 * The anchor target (i.e. _blank)
		 * @type {string}
		 */
		target: {
			default: '_blank',
			schema: {
				type: 'string'
			}
		}
	},
	styles
});
