// TODO: For now, I'm just sending back all of the properties passed to this component as the action payload.
// We may want more fine-tuned control in the future, though.
export function nowButtonClickedHandler({ properties, action, dispatch }) {
	const { headerAction, context } = action.payload;

	if (context === 'backButton') {
		dispatch('SN_UIB_PAGE_HEADER#BACK_BUTTON_CLICKED');
		action.stopPropagation();
	} else {
		action.stopPropagation();
		dispatch(headerAction, properties);
	}
}

export function nowDropdownItemClickedHandler({
	properties,
	action,
	dispatch
}) {
	const { headerAction } = action.payload.item;

	action.stopPropagation();
	dispatch(headerAction, properties);
}

export function uibContentTreeItemSelectedHandler({ action, dispatch }) {
	action.stopPropagation();

	const { headerAction } = action.payload;
	if (headerAction) {
		dispatch(headerAction, action.payload);
	}
}

export function uibContentTreeSecondaryActionHandler({
	action,
	properties,
	dispatch
}) {
	action.stopPropagation();

	const { isDomainExpanded } = properties;
	dispatch(SN_UIB_PAGE_HEADER_SET_DOMAIN_EXPANDED, {
		isDomainExpanded: !isDomainExpanded
	});
}

export function snUIBPageHeaderUpdateMediaSizeHandler({ action, updateState }) {
	const { mediaQuery, mediaSize } = action.payload;

	if (mediaQuery.matches) {
		updateState({
			path: 'mediaSize',
			value: {
				isSM: mediaSize === 'sm',
				isMD: mediaSize === 'md',
				isLG: mediaSize === 'lg',
				isXL: mediaSize === 'xl'
			},
			operation: 'set'
		});
	}
}

export const nowPopoverOpenedSetHandler = ({ action, updateProperties }) => {
	const { menu, value } = action.payload;
	const openedMenu = value ? menu : null;
	updateProperties({ openedMenu });
};

export const snUIBCloseMenuHandler = ({ updateProperties }) =>
	updateProperties({ openedMenu: null });
