import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { columnRenderer, rowRenderer } from './layout-renderers';
import { ElementWrapper } from './partials';
import './sn-uib-row-col-page-info';

const view = ({ properties }) => {
	const { renderFn, pageTree, elementTree } = properties;
	return (
		<div>
			{renderFn(properties)}
			<sn-uib-row-col-page-info elementTree={elementTree} pageTree={pageTree} />
		</div>
	);
};
export default createCustomElement('sn-uib-row-col-adapter', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		renderFn: {
			default() {
				return null;
			}
		},
		pageTree: {
			default: {}
		},
		renderers: {
			default: { row: rowRenderer, column: columnRenderer },
			selectable: true
		},
		elementWrapper: {
			default: ElementWrapper,
			selectable: true
		},
		elementTree: {
			default: {}
		}
	}
});
