import '@servicenow/now-tooltip';

//TODO: There is an error with the now-tooltip in which
//a property ('showTooltip') can't be read. This is most
//likely to do with `now-tooltip`s use of `WeakMaps`.
//Further investigation and followup is needed. Specifically
//it may be worth determining if the `WeakMap` has `lastTarget` already
export default function SnUIBLayoutTooltip({ active, id, content, ref }) {
	if (active && ref.current) {
		return (
			<now-tooltip
				id={id}
				content={content}
				target-ref={ref.current}
				manage-opened
				opened={active}
			/>
		);
	}
}
