			let sib;
			let value = null;

			while (parent.value()) {
				sib = parent[relationalMethod]().value();
				if (parent.value().type === 'column' && sib) {
					value = parent.value();
					break;
				} else if (parent.value().type === 'container') {
					// we have reached a container without a sibling. Cutting it off.
					value = null;
					break;
				}
				parent = parent.getImmediateParent();
			}
			return pageMapNavigator({ map: object, root, previousValue: { value } });
