import { t } from 'sn-translate';
import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-modal';

import { footerActionHandler } from './actions';

/**
 * Function that returns conditionally calculated buttons to display for the modal
 *
 * @param {Object} args
 * @param {Boolean} args.hideUnsavedDialogDiscard a boolean flag to dictate whether the discard button should be shown
 */
const getFooterActions = ({ hideUnsavedDialogDiscard }) => {
	const footerActions = JSON.stringify(
		[
			{
				variant: 'primary',
				label: t('{0}', 'Save and continue')
			},
			!hideUnsavedDialogDiscard && {
				variant: 'secondary',
				label: t('{0}', 'Discard changes')
			},
			{
				variant: 'tertiary',
				label: t('Cancel')
			}
		]
			// trim false conditions
			.filter((e) => e)
	);
	return footerActions;
};

const view = (state) => {
	const { header, message, hideUnsavedDialogDiscard } = state.properties;

	const footerActions = getFooterActions({ hideUnsavedDialogDiscard });

	return (
		<now-modal
			opened={true}
			size="md"
			header-label={t('{0}', header)}
			footerActions={footerActions}>
			<slot name="defaultSlot">
				<div>
					<p>{t('{0}', message)}</p>
				</div>
			</slot>
		</now-modal>
	);
};

export default createCustomElement('sn-uib-component-unsaved-dialog', {
	view,
	properties: {
		header: {
			default: 'This page has unsaved changes'
		},
		message: {
			default: ''
		},
		hideUnsavedDialogDiscard: {
			default: false
		}
	},
	actionHandlers: {
		'NOW_MODAL#FOOTER_ACTION_CLICKED': footerActionHandler
	}
});
