import '@servicenow/now-typeahead';
import '@servicenow/now-input';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-uib-page-info.scss';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const CHILD_DATA_SET = 'SN_UIB_CHILD#CHILD_DATA_SET';
export const HEADER_LABEL = t("OK. Let's get some general info for this page");
export const APPLICATION_SEARCHED = 'SN_UIB_PAGE_INFO#APPLICATION_SEARCHED';
export const VALIDATION_ERROR = 'VALIDATION_BEHAVIOR#VALIDATION_ERROR';
export const UPDATE_VALIDATION_LIST =
	'VALIDATION_BEHAVIOR#UPDATE_VALIDATION_LIST';

const handleTypeaheadChanged = ({ action, dispatch }) => {
	action.stopPropagation();
	const { category, value } = action.payload;

	if (category === 'selectedApplication') {
		return dispatch(APPLICATION_SEARCHED, { searchString: value });
	}
};

const handleInputChanged = ({ action, dispatch }) => {
	action.stopPropagation();
	const { category, name, fieldValue: value } = action.payload;
	const key = category || name;

	dispatch(CHILD_DATA_SET, { key, value });
	dispatch(UPDATE_VALIDATION_LIST, { key, value });
};

const handleTypeaheadItemClicked = ({ action, dispatch, updateState }) => {
	action.stopPropagation();
	const { name, category, item } = action.payload;
	const key = category || name;

	dispatch(CHILD_DATA_SET, { key, value: item });
	dispatch(UPDATE_VALIDATION_LIST, { key, value: item });
	switch (key) {
		case 'selectedApplication':
			updateState({
				applicationSearch: item
			});
			break;
		case 'selectedProtectionChoice':
			updateState({
				protectionSearch: item
			});
			break;
	}
};

const getSelectedItem = (selectedItem, items) => {
	if (selectedItem && selectedItem.id) {
		return items.find(({ id }) => id === selectedItem.id);
	}
};

const view = (state) => {
	const {
		pageName,
		availableApplications,
		availableProtectionChoices,
		isEditMode,
		disabled,
		validationErrors
	} = state.properties;
	const { applicationSearch, protectionSearch } = state;

	return (
		<div className="form">
			<div>
				<now-input
					className="form-name"
					label={t('Page name')}
					placeholder={t('Untitled Page')}
					value={pageName}
					append-to-payload={{ category: 'pageName' }}
					maxlength={255}
					disabled={disabled}
					messages={
						validationErrors['pageName']
							? [
									{
										content: validationErrors['pageName'],
										status: 'critical',
										icon: 'circle-exclamation-outline'
									}
							  ]
							: []
					}
					/* prettier-ignore */
					helperContent={t("This isn't user-facing. Only you will see this name.")}
					required
					autofocus
				/>
			</div>
			<div className="typeahead-row">
				<now-typeahead
					className="form-application"
					label={t('Application')}
					value={applicationSearch ? applicationSearch.label : ''}
					selected-item={applicationSearch ? applicationSearch.id : ''}
					append-to-payload={{ category: 'selectedApplication' }}
					items={availableApplications}
					search={'contains'}
					/* prettier-ignore */
					helperContent={t("Application scope to create your flow in. Global is the default.")}
					disabled={isEditMode || disabled}
					messages={
						validationErrors['selectedApplication']
							? [
									{
										content: validationErrors['selectedApplication'],
										status: 'critical',
										icon: 'circle-exclamation-outline'
									}
							  ]
							: []
					}
					required
				/>
				<now-typeahead
					className="form-protection"
					label={t('Protection')}
					value={protectionSearch ? protectionSearch.label : ''}
					selected-item={protectionSearch ? protectionSearch.id : ''}
					append-to-payload={{ category: 'selectedProtectionChoice' }}
					items={availableProtectionChoices}
					search={'contains'}
					/* prettier-ignore */
					helperContent={t("Specifies if the page is read-only. You can only control this for the pages you own.")}
					disabled={isEditMode || disabled}
					messages={
						validationErrors['selectedProtectionChoice']
							? [
									{
										content: validationErrors['selectedProtectionChoice'],
										status: 'critical',
										icon: 'circle-exclamation-outline'
									}
							  ]
							: []
					}
					required
				/>
			</div>
		</div>
	);
};

createCustomElement('sn-uib-page-info', {
	/**
	 * @typedef {string} inputText User-readable; shown in dropdown
	 * @typedef {any} inputValue internal representation of selection
	 * @typedef {{text:inputText; value:inputValue}} inputOption for select or multiselect
	 * @typedef {object} validationError the field id and it's corresponding error message
	 */
	properties: {
		pageName: {
			default: ''
		},
		/**
		 * The application that was selected for this page
		 * @type {string}
		 */
		selectedApplication: {
			default: { label: 'Global', id: 'global' }
		},
		/**
		 * Provide a list of applications for user selection
		 * @type {inputOption[]}
		 */
		availableApplications: {
			default: []
		},
		/**
		 * Protection levels for user selection
		 * @type {inputOption[]}
		 */
		availableProtectionChoices: {
			default: []
		},
		/**
		 * Provide a list of protection levels for user selection
		 * @type {inputOption[]}
		 */
		selectedProtectionChoice: {
			default: { label: t('None (default)'), id: 'none' }
		},
		/**
		 * specify whether or not the form is in edit mode
		 * @type {boolean}
		 */
		isEditMode: {
			default: false
		},
		/**
		 * specify whether or not the form is in read only mode
		 * @type boolean
		 */
		disabled: {
			default: false
		},
		/**
		 * error messages passed to the inputs
		 * @type {validationError[]}
		 */
		validationErrors: {
			default: []
		}
	},
	initialState: {
		applicationSearch: {},
		protectionSearch: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ updateState, properties }) => {
			const {
				selectedApplication,
				selectedProtectionChoice,
				availableApplications,
				availableProtectionChoices
			} = properties;
			const setApplication = getSelectedItem(
				selectedApplication,
				availableApplications
			);
			const setProtection = getSelectedItem(
				selectedProtectionChoice,
				availableProtectionChoices
			);
			batchShorthandUpdateState(
				{
					applicationSearch: setApplication,
					protectionSearch: setProtection
				},
				updateState
			);
		},
		/**
		 * Externally emitted when the user submits the form
		 * @typedef {{
		 *      name: string;
		 *      protection: inputValue;
		 *      application: inputValue;
		 * }} SUBMIT_CLICKED.payload
		 */
		/** Internally emitted when any text input changes */
		'NOW_INPUT#INPUT': handleInputChanged,
		/** Internally emitted when a typeahead panel option has been selected */
		'NOW_TYPEAHEAD#SELECTED_ITEM_SET': handleTypeaheadItemClicked,

		/** Internally emitted when a typeahead's search field is updated **/
		'NOW_TYPEAHEAD#VALUE_SET': handleTypeaheadChanged
	},
	styles,
	view
});
