import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { createRef } from '@servicenow/ui-renderer-snabbdom';
import './sn-uib-row-col-node-info';
import { getRelativeIds, visitEveryNode } from './utils';
import get from 'lodash/get';

const view = ({ properties: { nodeInfo } }) => {
	return (
		<div>
			{' '}
			{Object.keys(nodeInfo).map((key) => {
				const props = nodeInfo[key];
				return (
					<sn-uib-row-col-node-info
						{...props}
						nodeId={key}
						component-id={`${key}_information`}
					/>
				);
			})}
		</div>
	);
};
export default createCustomElement('sn-uib-row-col-page-info', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		pageTree: {
			default: {}
		},
		elementTree: {
			default: {}
		},
		nodeInfo: {
			computed({ properties: { pageTree, elementTree } }) {
				if (!pageTree || !pageTree.root) {
					return {};
				}
				const root = pageTree ? pageTree.root : {};
				let mappedKeys = visitEveryNode(root, (id) => {
					return getRelativeIds(id, root);
				});

				const modals = get(elementTree, 'children', []).filter(
					(el) => el.isModal
				);

				modals.forEach((modal) => {
					const modalRoot = get(modal, 'layoutTree.root', {});

					const modalKeys = visitEveryNode(modalRoot, (id) => {
						return {
							...getRelativeIds(id, modalRoot),
							elementRef: createRef()
						};
					});
					mappedKeys = {
						...mappedKeys,
						...modalKeys
					};
				});

				return mappedKeys;
			}
		}
	}
});
