const greaterThan = (prev, next) => {
	return next > prev;
};
const lessThan = (prev, next) => {
	return next < prev;
};
const dimensionKeys = [
	{ key: 'top', comparison: greaterThan },
	{ key: 'left', comparison: greaterThan },
	{ key: 'right', comparison: lessThan },
	{ key: 'bottom', comparison: lessThan }
];
const crossDirections = {
	top: 'bottom',
	bottom: 'top',
	left: 'right',
	right: 'left'
};

const OKAY = 'OKAY';
const HIDDEN = 'HIDDEN';
const normalizeCoordinates = ({ top, left, right, bottom }) => {
	const coords = {
		top,
		left,
		right,
		bottom
	};
	if (right < left) {
		coords.right = left;
	}
	if (bottom < top) {
		coords.bottom = top;
	}

	return coords;
};
const calculateIntersection = (elementDimensions) => {
	if (elementDimensions.length === 0) {
		return {
			top: 0,
			bottom: 0,
			left: 0,
			right: 0
		};
	}
	const [first, ...rest] = elementDimensions.filter(Boolean);
	let status = OKAY;
	const coords = normalizeCoordinates(
		rest.reduce((acc, curr) => {
			return dimensionKeys.reduce((coords, { key, comparison }) => {
				const currentValue = acc[key];
				const nextValue = curr[key];
				const value = comparison(currentValue, nextValue)
					? nextValue
					: currentValue;
				const crossValue =
					curr[crossDirections[key]] || acc[crossDirections[key]];
				let resolvedValue = value;

				if (comparison(crossValue, value)) {
					resolvedValue = crossValue;
					status = HIDDEN;
				}

				return {
					...coords,
					[key]: resolvedValue
				};
			}, {});
		}, first)
	);
	return {
		result: coords,
		status
	};
};

export { calculateIntersection };
