import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import { t } from 'sn-translate';
import styles from './sn-uib-page-management-popover.scss';

export const SN_UIB_PAGE_MANAGEMENT_POPOVER_ACTION_CLICKED =
	'SN_UIB_PAGE_MANAGEMENT_POPOVER#ACTION_CLICKED';

/* eslint-disable jsx-a11y/no-autofocus */
export const view = ({ properties: { header, actions } }, { dispatch }) => (
	<div className="container">
		{header ? <div className="header-text"> {t(header)} </div> : null}
		{actions
			.filter((action) => !action.disabled)
			.map(({ type, icon, label, payload = {}, separator }) =>
				separator ? (
					<div className="separator"></div>
				) : (
					<div
						// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
						tabIndex={0}
						className="button"
						on-click={() => {
							dispatch(type, payload);
							dispatch(SN_UIB_PAGE_MANAGEMENT_POPOVER_ACTION_CLICKED);
						}}
						on-keydown={(event) => {
							if (event.key === 'Enter') {
								dispatch(type, payload);
							}
						}}>
						<now-icon size="sm" icon={icon} />
						{t(label)}
					</div>
				)
			)}
	</div>
);

createCustomElement('sn-uib-page-management-popover', {
	view,
	styles,
	properties: {
		header: { default: '' },
		actions: { default: [] }
	}
});
