export const convertToLonghand = (key, value) => ({
	path: key,
	value: value,
	operation: 'set'
});

/**
 * 
 This function is only meant to work for doing a complete overwrite of a parent level state key. Meaning if your state
 looks something like this {
    initialValue1: '',
    intitialValue2: 2 
} then this helper will work just fine for initialValue1 and intiialValue 2.
if you have an object that looks more like this 
{
    initialValue1: {
        nestedValue: ''
    },
    intitialValue2: 2 
}
then this function call will not work if you provide it with the shorthand object of {
    nestedValue: 3,
    intitialValue2: 5
}
 */

export const batchShorthandUpdateState = (shortHandObject, updateState) => {
	const longhandNotation = Object.entries(shortHandObject).map(([key, value]) =>
		convertToLonghand(key, value)
	);
	return updateState(longhandNotation);
};
