import '@servicenow/now-input';
import { t } from 'sn-translate';
import EmptyState from './empty-state'; // eslint-disable-line no-unused-vars
import '@servicenow/now-dropdown';
import '@servicenow/now-typeahead';
import _capitalize from 'lodash/capitalize';

export default function PageConfigurationForm({
	template,
	templates,
	itemType,
	parameters,
	fields,
	itemName,
	editMode,
	routeType = {},
	step,
	scope,
	availableApplications,
	routeConfigurationEnabled,
	showAlert
}) {
	switch (step) {
		default:
		case 'general-info':
			return (
				<div className={routeConfigurationEnabled ? 'setHeight' : ''}>
					<form className="general">
						<now-input
							label={_capitalize(t('{0} name', itemType))}
							value={itemName}
							append-to-payload={{ input: 'page-name' }}
							manageValue
							required
							name="page-name"
						/>
						{templates && Array.isArray(template) ? (
							<div className="dropdown-wrapper">
								<label for="template" htmlFor="template" id="templateLabel">
									{t('Page Template')}
								</label>
								<now-dropdown
									manage-selected-items
									selected-items={template ? [template] : []}
									placeholder={t('Select a template')}
									items={templates.map((t) => ({ id: t.id, label: t.name }))}
									id="template"
									aria-labelledby="templateLabel"
									variant="tertiary"
									append-to-payload={{ dropdown: 'template' }}
								/>
							</div>
						) : null}
						{routeConfigurationEnabled ? (
							<now-input
								label={t('Route')}
								value={routeType.content}
								append-to-payload={{ input: 'route' }}
								manageValue
								required
								manageInvalid
								invalid={
									routeType.messages && Boolean(routeType.messages.length)
								}
								messages={routeType.messages}
								name="route"
							/>
						) : null}
						<now-typeahead
							className="form-application"
							label={t('Application scope')}
							append-to-payload={{ category: 'scope' }}
							selected-items={scope}
							value={scope.label}
							items={availableApplications}
							disabled={
								editMode ||
								!availableApplications ||
								!availableApplications.length
							}
							required
							/* prettier-ignore */
							helper-content={t('Application scope to create your flow in. Global is the default.')}
						/>
					</form>
					{showAlert ? renderAlert() : null}
				</div>
			);
		case 'fields':
			return (
				<div className="setHeight">
					<form className={`setHeight ${showAlert ? 'shrink' : ''}`}>
						<h4 className="parameters-heading">
							<now-button-bare
								variant="secondary"
								append-to-payload={{ button: 'add-field' }}>
								{t('+ New required parameter')}
							</now-button-bare>
						</h4>
						{fields && fields.length ? (
							<sn-uib-draggable-list items={fields} />
						) : (
							<EmptyState
								/* prettier-ignore */
								headerMessage={t('There are no required parameters defined yet.')}
								size="sm"
								hideBorder={true}
							/>
						)}
					</form>
					{showAlert ? renderAlert() : null}
				</div>
			);
		case 'param':
			return (
				<div className="setHeight">
					<form className={`setHeight ${showAlert ? 'shrink' : ''}`}>
						<h4 className="parameters-heading">
							<now-button-bare
								variant="secondary"
								append-to-payload={{ button: 'add-parameter' }}>
								{t('+ New optional parameter')}
							</now-button-bare>
						</h4>
						{parameters && Object.keys(parameters).length ? (
							<div className="parameters">
								{Object.keys(parameters || {}).map((id) => {
									const param = parameters[id];
									return param ? renderParam(param, id) : null;
								})}
							</div>
						) : (
							<EmptyState
								/* prettier-ignore */
								headerMessage={t('There are no optional parameters defined yet.')}
								size="sm"
								hideBorder={true}
							/>
						)}
					</form>
					{showAlert ? renderAlert() : null}
				</div>
			);
	}
}

function renderParam(param, id) {
	return (
		<div className="param-row">
			<div className="param-input">
				<now-input
					value={param.name || ''}
					append-to-payload={{ input: 'param-name', id }}
					placeholder={t('Enter a name')}
					manageValue
					autofocus={param.id && !param.name}
					name="name"
					manageInvalid
					invalid={param.messages && Boolean(param.messages.length)}
					messages={param.messages}
				/>
			</div>
			<div className="center">
				<now-button-iconic
					icon="trash-outline"
					variant="tertiary"
					bare
					size="lg"
					append-to-payload={{ id, button: 'delete-parameter' }}
				/>
			</div>
		</div>
	);
}

export function renderAlert() {
	return (
		<now-alert
			status="critical"
			icon="circle-exclamation-outline"
			/* prettier-ignore */
			content={t('The application has another route with the same name and number of required parameters which is not allowed. Please change the route name or number of required parameters.')}
		/>
	);
}
