import { createCustomElement } from '@servicenow/ui-core';
import 'sn-ui-builder-icons';
import styles from './sn-uib-modals-list.scss';

export const INSERT_MODAL = 'SN_UIB_ELEMENT_NAVIGATOR#INSERT_MODAL';

createCustomElement('sn-uib-modals-list', {
	view: (state, { dispatch }) => {
		const {
			properties: { modals, appendToPayload }
		} = state;

		const sortedModals = modals.sort((a, b) => a.name.localeCompare(b.name));
		return (
			<div className="modal-list">
				{sortedModals.map((modal) => (
					<button
						className="modal-type"
						on-click={() => {
							dispatch(INSERT_MODAL, {
								modal,
								...appendToPayload
							});
							dispatch('NOW_POPOVER#OPENED_SET', {
								value: false,
								...appendToPayload
							});
						}}>
						<sn-uib-icon
							icon={modal.icon}
							style={{ height: '64px', width: '80px' }}
						/>
						<div className="label">{modal.name}</div>
					</button>
				))}
			</div>
		);
	},
	styles,
	properties: {
		modals: {
			default: []
		},
		appendToPayload: {
			default: {}
		}
	}
});
