import {
	ELEMENT_INSTANCE_NAME,
	VIEWPORT_ROUTES,
	ACTIVE_SCREEN,
	VIEWPORT_CONFIGURATION,
	ACTIVE_ROUTE,
	DEFAULT_ROUTE,
	DATA_DRIVEN_VIEWPORT_ITEMS,
	VIEWPORT_DATA_SOURCE,
	IS_HIDDEN,
	DA_CLIENT_ACTION_CONTRACT
} from './constants';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

export function componentProperties(properties, element) {
	let result = [];
	if (isViewportComponent(properties, element)) {
		//generic viewport component, we are also ignoring the properties which are reminiscent from old tabset component
		// which gets added to properties and whose id is empty, so we hide unnecessary properties which are not used.
		result = properties.filter((property) => {
			return (
				!isInstanceNameProperty(property) &&
				!isViewportProperty(property) &&
				!isIsHiddenProperty(property) &&
				!isEmpty(property.id)
			);
		});
	} else {
		//all other components including tab set
		result = properties.filter((property) => {
			return !isInstanceNameProperty(property) && !isIsHiddenProperty(property);
		});
	}
	return result;
}

export function getHiddenProperty(properties) {
	return properties.find((property) => isIsHiddenProperty(property));
}

export function extractReferencesFromProperties(properties) {
	return properties
		.filter(
			(property) =>
				property.type.toLowerCase() === 'reference' &&
				property.value &&
				typeof property.table === 'string' &&
				!property.table.startsWith('@')
		)
		.map((property) => ({
			table: property.table,
			sysId: property.value
		}));
}

export function getValuesFromEventMappingHandlers(eventMappingHandlers = {}) {
	const currentHandlers = [].concat(
		...(Object.values(eventMappingHandlers) || [])
	);
	return currentHandlers.reduce((vals, handler) => {
		return vals.concat(handler.values || []);
	}, []);
}

export function isInstanceNameProperty(property) {
	return property.id === ELEMENT_INSTANCE_NAME;
}

export function isIsHiddenProperty(property) {
	return property.id === IS_HIDDEN;
}

export function isViewportProperty(property) {
	return (
		property.id === VIEWPORT_ROUTES ||
		property.id === DEFAULT_ROUTE ||
		property.id === ACTIVE_ROUTE ||
		property.id === VIEWPORT_CONFIGURATION ||
		property.id === DATA_DRIVEN_VIEWPORT_ITEMS ||
		property.id === VIEWPORT_DATA_SOURCE ||
		property.id === ACTIVE_SCREEN
	);
}

export function isViewportComponent(props, element) {
	return isEmpty(props) ? false : element.componentCategory === 'viewport';
}

export function findPropertyValue(properties, id) {
	return get(
		properties.find((prop) => prop.id === id),
		'value'
	);
}

export function isDeclarativeActionComponent(properties) {
	return (
		properties.filter((prop) => prop.id === DA_CLIENT_ACTION_CONTRACT)
			.length !== 0
	);
}

export function massageViewportElementProperties(properties) {
	const massagedProperties = {};
	properties.forEach((prop) => {
		if (prop.id === VIEWPORT_CONFIGURATION) {
			try {
				massagedProperties[prop.id] = JSON.parse(prop.value);
			} catch (e) {
				console.error(VIEWPORT_CONFIGURATION, e);
			}
		} else {
			massagedProperties[prop.id] = prop.value;
		}
	});
	return massagedProperties;
}
