import '@servicenow/now-alert';
import '@servicenow/now-button';
import '@servicenow/now-code-editor';
import '@servicenow/now-modal';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './_sn-uib-code-editor-modal.scss';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const SN_UIB_CODE_EDITOR_MODAL_CLOSED =
	'SN_UIB_CODE_EDITOR_MODAL#CLOSED';
export const SN_UIB_CODE_EDITOR_MODAL_TEXT_UPDATED =
	'SN_UIB_CODE_EDITOR_MODAL#TEXT_UPDATED';

const EDITOR_MODAL = {
	APPLY: 'SN_UIB_CODE_EDITOR_MODAL_APPLY#CLICKED',
	CANCEL: 'SN_UIB_CODE_EDITOR_MODAL_CANCEL#CLICKED'
};
const codeEditorStlye = {
	height: '100%',
	width: 'calc(100% - 2px)'
};

const inlineHoistBodyStyle = {
	height: '20rem',
	'margin-bottom': '1.35rem'
};

const hoistErrorStyle = {
	fontSize: '0.75rem',
	color: 'red',
	bottom: 0,
	left: 0
};

const codeModalFooterActions = [
	{
		label: t('Apply'),
		variant: 'primary',
		clickActionType: EDITOR_MODAL.APPLY
	},
	{
		label: t('Cancel'),
		variant: 'secondary',
		clickActionType: EDITOR_MODAL.CANCEL
	}
];

const closeHandler = (dispatch, updateState) => {
	updateState({
		codeText: ''
	});
	dispatch(SN_UIB_CODE_EDITOR_MODAL_CLOSED);
};
const view = (
	{
		isValid,
		properties: {
			readOnly,
			internalValue,
			showModal,
			editorHeader,
			editorType,
			inline
		}
	},
	{ dispatch }
) => {
	return showModal ? (
		inline ? (
			<div className="inline-editor">
				<div className="header">{editorHeader}</div>
				<now-code-editor
					language={editorType}
					content={internalValue}
					read-only={readOnly}
					style={codeEditorStlye}
				/>
				{isValid ? null : (
					<div
						className={
							'sn-uib-code-editor-modal-error'
						}>{`Invalid ${editorType} input`}</div>
				)}
				<div className="footer">
					{codeModalFooterActions.map(({ label, variant, clickActionType }) => (
						<now-button
							key={label}
							variant={variant}
							label={label}
							on-click={() => dispatch(clickActionType)}
						/>
					))}
				</div>
			</div>
		) : (
			<now-modal
				className={'sn-uib-code-editor-modal'}
				header-label={editorHeader}
				opened
				size="lg"
				footerActions={codeModalFooterActions}>
				<div
					className={'sn-uib-code-editor-modal-content'}
					style={inlineHoistBodyStyle}>
					<div style={codeEditorStlye}>
						<now-code-editor
							language={editorType}
							content={internalValue}
							read-only={readOnly}
							style={codeEditorStlye}
						/>
					</div>
					{isValid ? null : (
						<div
							style={hoistErrorStyle}
							className={
								'sn-uib-code-editor-modal-error'
							}>{`Invalid ${editorType} input`}</div>
					)}
				</div>
			</now-modal>
		)
	) : null;
};

export default createCustomElement('sn-uib-code-editor-modal', {
	view,
	styles,
	properties: {
		editorType: {
			default: 'css'
		},
		editorHeader: {
			default: t('Edit CSS')
		},
		showModal: {
			default: false
		},
		internalValue: {
			default: ''
		},
		readOnly: {
			default: false
		},
		validator: {
			default: () => true
		},
		inline: {
			default: false
		}
	},
	initialState: {
		editorTextValue: null,
		isValid: true,
		errors: []
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: {
			effect: ({ updateState, properties: { internalValue } }) => {
				batchShorthandUpdateState(
					{
						editorTextValue: internalValue,
						isValid: true
					},
					updateState
				);
			},
			stopPropagation: true
		},
		['NOW_MODAL#OPENED_SET']: {
			effect: ({ action, dispatch, updateState }) => {
				const { value } = action.payload;
				if (!value) {
					closeHandler(dispatch, updateState);
				}
			},
			stopPropagation: true
		},
		[EDITOR_MODAL.APPLY]: {
			effect: ({ dispatch, state, properties: { validator }, updateState }) => {
				const { editorTextValue, errors } = state;
				const isValid =
					(!errors || errors.length < 1) && validator(editorTextValue);
				if (!isValid) {
					updateState({ isValid: false });
					return;
				}
				dispatch(SN_UIB_CODE_EDITOR_MODAL_TEXT_UPDATED, {
					value: editorTextValue
				});

				closeHandler(dispatch, updateState);
			},
			stopPropagation: true
		},
		[EDITOR_MODAL.CANCEL]: {
			effect: ({ dispatch, updateState }) => {
				updateState({ editorTextValue: '' });
				closeHandler(dispatch, updateState);
			},
			stopPropagation: true
		},
		'NOW_CODE_EDITOR#CONTENT_CHANGED': {
			effect: ({ action, updateState }) => {
				const { text, errors } = action.payload;
				batchShorthandUpdateState(
					{ editorTextValue: text, errors, isValid: true },
					updateState
				);
			},
			stopPropagation: true
		}
	}
});
