import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import styles from './sn-uib-property-description-tooltip.scss';
import { config } from './component-config';
import './sn-uib-property-tooltip-content.js';

import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import '@servicenow/now-popover';

const POPOVER_OPENED_SET = 'NOW_POPOVER#OPENED_SET';

const view = (
	{
		opened,
		properties: { description, selectedComponentTag, hideTextLink, icon }
	},
	{ dispatch }
) => {
	return (
		<now-popover
			tabindex={0}
			on-keydown={(e) => {
				if (e.code === 'Space' || e.code === 'Enter') {
					dispatch(POPOVER_OPENED_SET, { value: !opened });
				}
			}}
			manageOpened={true}
			opened={opened}>
			<now-icon className="info-icon" icon={icon} slot="trigger" size="sm" />
			<sn-uib-property-tooltip-content
				slot="content"
				description={description}
				selectedComponentTag={selectedComponentTag}
				hideTextLink={hideTextLink}
			/>
		</now-popover>
	);
};

/**
 * An input component to be used for boolean properties configured on the
 * UI Builder properties editor panel
 */
export default createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		opened: false
	},
	styles,
	actionHandlers: {
		[POPOVER_OPENED_SET]: {
			effect: ({
				action: {
					payload: { value }
				},
				updateState
			}) => {
				updateState({ opened: value });
			},
			stopPropagation: true
		}
	}
});
