import '@servicenow/now-input';
import '@servicenow/now-button';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import isNull from 'lodash/isNull';
import { t } from 'sn-translate';

import '../sn-uib-property-description-tooltip';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import { config } from './component-config';
import styles from './sn-uib-string-property-input.scss';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

// Dispatched action types
export const STRING_PROPERTY_VALUE_CHANGED =
	'STRING_PROPERTY_INPUT#VALUE_CHANGED';
export const STRING_PROPERTY_AUTOFOCUS_SET = 'STRING_PROPERTY_INPUT#BLURRED';
export const STRING_PROPERTY_SCRIPT_ENABLED =
	'STRING_PROPERTY_INPUT#SCRIPT_ENABLED';

export const isValidValue = (value = '', pattern = '') => {
	if (!value) {
		return true;
	}
	const validationMatcher = value.match(new RegExp(pattern));
	return !isNull(validationMatcher);
};

export const getErrorMessage = (isValid, validationMessage, error) => {
	if (!isValid) {
		return validationMessage || t('Invalid input');
	} else if (error && error.type === 'circular') {
		const { elements = [] } = error;
		/* prettier-ignore */
		return t("You've created a circular reference between {0} and {1}", elements[0], elements[1]);
	} else {
		return null;
	}
};

const view = (
	{
		internalValue = '',
		isValid,
		properties: {
			label,
			description,
			disabled,
			id,
			isScripted,
			autofocus,
			error,
			validation = {},
			value,
			mode,
			selectedComponentTag,
			translatable
		}
	},
	{ dispatch }
) => {
	const errorMsg = getErrorMessage(isValid, validation.message, error);
	const isURLInput = mode === 'url';
	// eslint-disable-next-line no-unused-vars
	const TagName = isURLInput ? 'now-input-url' : 'now-input';

	return (
		<Fragment className="sn-uib-property-input">
			<label className="sn-uib-property-input-label">
				{label}
				{!description ? null : (
					<sn-uib-property-description-tooltip
						description={description}
						selectedComponentTag={selectedComponentTag}
					/>
				)}
				{!translatable ? null : (
					<sn-uib-property-description-tooltip
						description={t('Translatable turned on')}
						icon={'globe-outline'}
					/>
				)}
			</label>
			{isScripted ? (
				<div
					className="scripted-value-input-container"
					on-click={(e) => {
						e.preventDefault();
						!e
							.composedPath()
							.some((el) => el.tagName === 'NOW-BUTTON-ICONIC') &&
							dispatch(PROPERTY_INPUT_SCRIPT_LINK_TOGGLED, {
								propertyId: id,
								editing: true
							});
					}}>
					<section className="scripted-value-input-button-combo-wrapper">
						<input className="scripted-value-input" value={value} />
						<div className="scripted-value-clear-button-wrapper">
							<now-button-iconic
								bare
								variant="tertiary"
								icon="close-fill"
								size="sm"
								append-to-payload={{ context: 'clear-script' }}
							/>
						</div>
					</section>
				</div>
			) : (
				<div className="input-container">
					<TagName
						value={internalValue}
						manageValue={true}
						maxlength={255}
						disabled={disabled}
						messages={
							errorMsg
								? [
										{
											status: 'critical',
											content: errorMsg,
											icon: 'circle-exclamation-outline'
										}
								  ]
								: []
						}
						autofocus={autofocus}
						manage-invalid={isURLInput}
						invalid={!isValid}
						readonly={isScripted}
					/>
				</div>
			)}
		</Fragment>
	);
};

const inputValueSetHelper = (
	dispatch,
	updateState,
	value,
	state,
	properties
) => {
	const { emptyStateBehavior, validation = {} } = properties;
	const isValid = isValidValue(value, validation.pattern);
	const valueToEmit =
		value === '' || value === '@' ? emptyStateBehavior.value : value;

	batchShorthandUpdateState(
		{
			isValid,
			internalValue: isValid ? valueToEmit : state.internalValue
		},
		updateState
	);

	if (isValid) {
		dispatch(STRING_PROPERTY_VALUE_CHANGED, {
			value: valueToEmit,
			noDebounce: true
		});
	}
};

const inputKeyInputHandler = ({
	dispatch,
	updateState,
	action,
	properties
}) => {
	action.stopPropagation();
	const { fieldValue: value } = action.payload;
	const { id: propertyId, dataBindingEnabled, validation = {} } = properties;
	const isValid = isValidValue(value, validation.pattern);
	updateState({ isValid });

	if (dataBindingEnabled && value === '@') {
		dispatch(PROPERTY_INPUT_BINDING_BUTTON_CLICKED, {
			propertyId,
			isStatic: true
		});
	}
};
/**
 * An input property component to be used for input properties
 * configured on the UI Builder properties editor panel
 */
createCustomElement(config.name, {
	properties: config.properties,
	view,
	initialState: {
		internalValue: '',
		isValid: true,
		hasFocus: false,
		editingScript: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: ({
			dispatch,
			updateState,
			properties
		}) => {
			const { emptyStateBehavior, autofocus, id, validation = {} } = properties;
			const internalValue = properties.value || emptyStateBehavior.value;
			const isValid = isValidValue(internalValue, validation.pattern);
			batchShorthandUpdateState(
				{
					internalValue,
					isValid
				},
				updateState
			);

			if (autofocus) {
				dispatch(STRING_PROPERTY_AUTOFOCUS_SET, { propertyId: id });
			}
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			updateState,
			action,
			state
		}) => {
			const { value, name } = action.payload;
			const { hasFocus } = state;
			if (name === 'value' && !hasFocus) {
				updateState({
					internalValue: value
				});
			}
		},
		['NOW_INPUT#INPUT']: inputKeyInputHandler,
		['NOW_INPUT_URL#INPUT']: inputKeyInputHandler,
		['NOW_INPUT#VALUE_SET']: ({
			dispatch,
			updateState,
			action,
			state,
			properties
		}) => {
			action.stopPropagation();
			inputValueSetHelper(
				dispatch,
				updateState,
				action.payload.value,
				state,
				properties
			);
		},
		['NOW_INPUT_URL#INVALID_SET']: ({
			dispatch,
			updateState,
			action,
			state,
			properties
		}) => {
			action.stopPropagation();
			inputValueSetHelper(
				dispatch,
				updateState,
				action.payload.fieldValue,
				state,
				properties
			);
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		},
		['NOW_BUTTON_ICONIC#CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();
			action.payload.context === 'clear-script' &&
				dispatch(STRING_PROPERTY_VALUE_CHANGED, { value: '' });
		}
	},
	styles
});
