import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import layoutAdapterSelector, {
	layoutType
} from '../helpers/layoutAdapterSelector';
import { gridRenderers } from '../sn-uib-grid-adapter';
import { rowColRenderers } from '../sn-uib-row-col-adapter';
import { defaultRenderers } from '../sn-uib-default-adapter';
import { memoizeDeepN } from 'seismic-memo-utils';

const visitEveryNode = (tree, cb, result = {}) => {
	const value = cb(tree);
	return {
		...tree.children.reduce(
			(acc, node) => {
				return visitEveryNode(node, cb, acc);
			},
			{ ...value, ...result }
		)
	};
};

const view = ({ properties }) => {
	const {
		providerId,
		layoutAdapter: LayoutAdapter, //eslint-disable-line no-unused-vars
		pageTree,
		combinedRenderers,
		actionNameFn,
		pageState,
		elementTree
	} = properties;

	return (
		<LayoutAdapter
			renderFn={(props) => (
				<sn-uib-layout-data-source
					{...props}
					component-id={providerId}
					action-names={actionNameFn(
						layoutType(get(pageState, 'rootElement.layout', {}))
					)}
					renderers={combinedRenderers}
				/>
			)}
			pageTree={pageTree}
			elementTree={elementTree}
		/>
	);
};
const rendererLookup = {
	grid: gridRenderers,
	rowColumn: rowColRenderers
};
createCustomElement('sn-uib-layout-data-source', {
	renderer: {
		type: snabbdom,
		view: () => {
			return <div />;
		}
	},
	properties: {
		renderers: {
			default: {},
			selectable: true
		},
		elementWrapper: {
			default: () => null,
			selectable: true
		},
		actionNames: {
			default: {},
			selectable: true
		}
	}
});

const getLayoutAdapter = memoizeDeepN(function (layout) {
	return layoutAdapterSelector(layout);
});

const getCombinedRenderers = memoizeDeepN(function (pageTree) {
	if (!pageTree || !pageTree.root) {
		return {};
	}
	const renderers = visitEveryNode(pageTree.root, (node) => {
		const { layoutType } = node;
		const renderer = rendererLookup[layoutType];
		return renderer ? renderer : {};
	});

	return { ...renderers, ...defaultRenderers };
});

export default createCustomElement('sn-uib-layout-adapter', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		pageState: {
			default: {}
		},
		actionNameFn: {
			default: () => ({})
		},
		pageTree: {
			default: {}
		},
		elementTree: {
			default: {}
		},
		combinedRenderers: {
			computed({ properties: { pageTree } }) {
				return getCombinedRenderers(pageTree);
			}
		},
		providerId: {
			default: 'uib-layout-adapter'
		},
		layoutAdapter: {
			computed({ properties: { pageState } }) {
				return getLayoutAdapter(get(pageState, 'rootElement.layout'));
			}
		}
	}
});
