import { createCustomElement, actionTypes } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import { BOOLEAN_PROPERTY_VALUE_CHANGED } from '../sn-uib-boolean-property-input';
import { C3_PROPERTY_VALUE_CHANGED } from '../sn-uib-c3-property-input';
import { CHOICE_PROPERTY_VALUE_CHANGED } from '../sn-uib-choice-property-input';
import { componentInterfaceSchema } from '../sn-uib-element-configuration-panel/schemas';
import { FIELD_LIST_PROPERTY_FIELD_SELECTED } from '../sn-uib-field-list-property-input';
import { FIELD_PROPERTY_FIELD_SELECTED } from '../sn-uib-field-property-input';
import { GLIDE_LIST_PROPERTY_VALUE_CHANGED } from '../sn-uib-glide-list-property-input';
import { JSON_PROPERTY_VALUE_CHANGED } from '../sn-uib-json-property-input';
import { NUMBER_PROPERTY_VALUE_CHANGED } from '../sn-uib-number-property-input';
import { REFERENCE_PROPERTY_RECORD_SELECTED } from '../sn-uib-reference-property-input';
import { ICON_PROPERTY_INPUT_PROPERTY_CHANGED } from '../sn-uib-icon-property-input';
import { PROPERTY_WRAPPER_STATIC_MODE_TOGGLED } from './sn-uib-property-config-wrapper';
import {
	STRING_PROPERTY_VALUE_CHANGED,
	STRING_PROPERTY_AUTOFOCUS_SET
} from '../sn-uib-string-property-input';
import { CSS_PROPERTY_VALUE_CHANGED } from '../sn-uib-css-property-input';
import {
	TEXT_PROPERTY_VALUE_CHANGED,
	TEXT_PROPERTY_AUTOFOCUS_SET
} from '../sn-uib-text-property-input';
import { TABLE_PROPERTY_INPUT_TABLE_SELECTED } from '../sn-uib-table-property-input';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import styles from './sn-uib-properties-editor-panel.scss';
import { PROPERTY_BINDING_BINDING_SET } from '../sn-uib-dynamic-property-input';
import {
	SCRIPT_SHORTHAND_PATTERN,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import { snPropertiesComponentLoader } from './utils';

// Dispatched action types
export const PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED =
	'PROPERTIES_EDITOR_PANEL#PROPERTY_CHANGED';

const primeValuesCache = ({ properties, updateState }) => {
	const { properties: currentProperties } = properties;
	for (const prop of currentProperties) {
		updateState({
			path: `valuesCache.${prop.id}`,
			value: {
				static: prop.defaultValue,
				binding: undefined,
				script: undefined
			},
			operation: 'set',
			shouldRender: false
		});
	}
};

const populateValuesCache = (currentProperties, propertyId, updateState) => {
	const oldProperty =
		currentProperties.find((prop) => prop.id === propertyId) || {};
	const { id: oldId, value: oldValue } = oldProperty;

	let propertyDisposition; // Either binding, script or static
	if (typeof oldValue !== 'string') {
		propertyDisposition = 'static';
	} else if (oldValue.startsWith('@')) {
		propertyDisposition = 'binding';
	} else if (oldValue.match(SCRIPT_SHORTHAND_PATTERN)) {
		propertyDisposition = 'script';
	} else {
		propertyDisposition = 'static';
	}

	oldProperty &&
		updateState({
			path: `valuesCache.${oldId}.${propertyDisposition}`,
			value: oldValue,
			operation: 'set',
			shouldRender: false
		});
};

const dispatchCachedPropertyValue = (
	cache,
	disposition,
	propertyId,
	dispatch,
	action
) => {
	const defaultValue = disposition === 'static' ? '' : undefined;
	const cachedValue = get(cache, [propertyId, disposition], defaultValue);

	if (
		cachedValue ||
		cachedValue === '' ||
		cachedValue === null ||
		cachedValue === false
	) {
		action.stopPropagation();
		dispatch(PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED, {
			value: cachedValue,
			id: propertyId
		});
	}
};

const propertyUpdatedHandler = ({
	action,
	dispatch,
	properties,
	updateState
}) => {
	action.stopPropagation();

	const { properties: currentProperties } = properties;
	const { id, value, noDebounce } = action.payload;

	populateValuesCache(currentProperties, id, updateState);

	/**
	 * The new value of the child property that just changed is dispatched
	 * @type {}
	 */
	dispatch(PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED, { value, id, noDebounce });
};

const toggleStaticIdReset = ({ action, updateState, state }) => {
	action.stopPropagation();
	const { toggleStaticId } = state;
	const { propertyId } = action.payload;
	updateState({
		toggleStaticId: toggleStaticId === propertyId ? null : propertyId
	});
};

const handleDispositionToggle = (dispositionType) => ({
	state,
	action,
	dispatch,
	updateState
}) => {
	const { propertyId } = action.payload;
	const {
		valuesCache,
		properties: { properties: currentProperties }
	} = state;

	if (dispositionType === 'binding') {
		updateState({
			toggleStaticId: propertyId
		});
	}

	const currentPropertyValue = get(
		currentProperties.find((prop) => prop.id === propertyId) || {},
		'value',
		null
	);

	let dispatchedRequired = true;
	if (
		currentPropertyValue &&
		typeof currentPropertyValue === 'string' &&
		currentPropertyValue.startsWith('javascript') &&
		dispositionType === 'script'
	) {
		dispatchedRequired = false;
	}

	populateValuesCache(currentProperties, propertyId, updateState);
	dispatchedRequired &&
		dispatchCachedPropertyValue(
			valuesCache,
			dispositionType,
			propertyId,
			dispatch,
			action
		);
};

const view = ({ properties, toggleStaticId }) => {
	return (
		<Fragment>
			{snPropertiesComponentLoader(
				properties.properties,
				properties.dataBindingEnabled,
				properties.container,
				properties.interfaceConfigurationComponent,
				properties.elementId,
				properties.bindings,
				properties.appConfig,
				toggleStaticId,
				properties.scriptTemplate,
				properties.scriptingEnabled,
				properties.selectedComponentTag,
				properties.defaultedFieldTypes
			)}
		</Fragment>
	);
};

export default createCustomElement('sn-uib-properties-editor-panel', {
	view,
	initialState: {
		toggleStaticId: null,
		valuesCache: {}
	},
	properties: {
		/**
		 * An array of uib element properties to pipe down into individual
		 * property type controls for the user to modify
		 * @type {object[]}
		 */
		properties: {
			default: []
		},
		dataBindingEnabled: {
			default: true
		},
		/**
		 * The properties editor container that the now-dropdown-panel should be constrained within
		 * @type {HTMLElement}
		 */
		container: {
			default: null
		},
		/**
		 * the element ID
		 * @type {string}
		 */
		elementId: {
			default: null
		},
		interfaceConfigurationComponent: {
			/**
			 *  the interface name and its corresponding component tag
			 * @typedef componentInterface
			 * @property {string} interfaceName
			 * @property {string}  componentId
			 */

			/**
			 *  a map containing component interfaces
			 * @typedef {Object.<string, componentInterface>} componentInterfaceMap
			 */

			/**
			 * @type {componentInterfaceMap}
			 */
			default: {},
			schema: { type: 'object', schema: componentInterfaceSchema }
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * The template for client transform scripts
		 */
		scriptTemplate: {
			default: ''
		},
		/**
		 * Whether client transform scripting is enabled
		 */
		scriptingEnabled: {
			default: true
		},
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {
			default: ''
		},
		/**
		 * List of fieldTypes that shoiuld default to string
		 */
		defaultedFieldTypes: {
			schema: {
				type: 'array'
			},
			default: []
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: primeValuesCache,
		[BOOLEAN_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[C3_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[CHOICE_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[FIELD_LIST_PROPERTY_FIELD_SELECTED]: propertyUpdatedHandler,
		[FIELD_PROPERTY_FIELD_SELECTED]: propertyUpdatedHandler,
		[GLIDE_LIST_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[JSON_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[NUMBER_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[REFERENCE_PROPERTY_RECORD_SELECTED]: propertyUpdatedHandler,
		[STRING_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[CSS_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[TEXT_PROPERTY_VALUE_CHANGED]: propertyUpdatedHandler,
		[TABLE_PROPERTY_INPUT_TABLE_SELECTED]: propertyUpdatedHandler,
		[PROPERTY_BINDING_BINDING_SET]: propertyUpdatedHandler,
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: handleDispositionToggle('binding'),
		[PROPERTY_WRAPPER_STATIC_MODE_TOGGLED]: handleDispositionToggle('static'),
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: handleDispositionToggle('script'),
		[STRING_PROPERTY_AUTOFOCUS_SET]: toggleStaticIdReset,
		[TEXT_PROPERTY_AUTOFOCUS_SET]: toggleStaticIdReset,
		[ICON_PROPERTY_INPUT_PROPERTY_CHANGED]: propertyUpdatedHandler
	},
	styles
});
