import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-popover';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import './url-test-value';
import { t } from 'sn-translate';
import styles from './sn-uib-address-bar.scss';
import snakeCase from 'lodash/snakeCase';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
export const FIELDS = 'fields';
export const PARAMS = 'params';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';
import { SN_UIB_TRANSACTION_ALERT_TRANSACTION_IN_SCOPE } from '../sn-uib-transaction-alert';

export const validationRegex = /^[ .~a-zA-Z0-9_-]*$/;

const strongFn = (asFormattedText) => (value) =>
	asFormattedText ? <span className="url-test-value">{value}</span> : value;

const trimSlashes = (str) => (str ? str.replace(/^\/?(.*)\/?$/, '$1') : '');

const runtimeUrlArray = (
	runtimeUrl,
	asFormattedText = false,
	encodeURI = false
) => {
	if (isEmpty(runtimeUrl) || !runtimeUrl.absolute) {
		return [];
	}

	const { root, appsURL, page, fields, params } = runtimeUrl;

	const url = [root, appsURL, page].map(trimSlashes);
	const strong = strongFn(asFormattedText);
	fields.forEach((field) =>
		url.push(
			field.value ? (
				strong(encodeURI ? encodeURIComponent(field.value) : field.value)
			) : (
				<span className="url-test-value placeholder">{`{{ ${field.name} }}`}</span>
			)
		)
	);
	const urlParams = params.reduce((acc, curr) => {
		if (curr.value) {
			return [
				...acc,
				snakeCase(curr.name).replace(/_/g, '-'),
				strong(encodeURI ? encodeURIComponent(curr.value) : curr.value)
			];
		}
		return acc;
	}, []);
	if (urlParams.length) {
		url.push('params');
	}
	return url.concat(urlParams);
};

const isOpenDisabled = (fields = [], isVariantSelected) =>
	!!fields.find((fp) => !fp.value || !fp.value.match(validationRegex)) ||
	!isVariantSelected;

const renderUrl = (currentFormattedUrl) => (hasFieldsOrParams) => (
	<div slot="trigger" class={{ url: true, inactive: !hasFieldsOrParams }}>
		{currentFormattedUrl.map((path, idx) => {
			if (idx > 0) {
				const slash = idx === 1 ? '/' : <span className="slash">/</span>;
				return [slash, path];
			}
		})}
	</div>
);

const view = ({
	properties: {
		runtimeUrl: { absolute, fields = [], params = [] },
		pageName,
		pageSysPolicy,
		applications,
		userScope,
		supportScope,
		pageScope
	},
	editingDisabled,
	currentFormattedUrl,
	popoverOpened,
	openDisabled
}) => {
	if (!pageName && !absolute) {
		return null;
	}

	const hasFieldsOrParams = !!(fields.length || params.length);
	if (!absolute) {
		return (
			<div className="container">
				{hasFieldsOrParams ? (
					<now-popover
						manage-opened
						opened={popoverOpened}
						hide-tail
						positions={{ trigger: 'bottom-start', content: 'top-start' }}>
						<div slot="trigger" className="popover-trigger">
							<span className="page-name"> {pageName} </span>
							{t('Edit test values')}
							<now-icon size="sm" icon="caret-down-fill" />
						</div>
						<url-test-values
							slot="content"
							fields={fields}
							params={params}
							page-sys-policy={pageSysPolicy}
							applications={applications}
							user-scope={userScope}
							support-scope={supportScope}
							page-scope={pageScope}
							disabled={editingDisabled}
						/>
					</now-popover>
				) : (
					<span className="page-name"> {pageName} </span>
				)}
			</div>
		);
	}

	const url = renderUrl(currentFormattedUrl)(hasFieldsOrParams);
	return (
		<div className="sn-uib-address-bar">
			<span className="label">URL</span>
			{hasFieldsOrParams ? (
				<now-popover
					manage-opened
					opened={popoverOpened}
					hide-tail
					positions={{ trigger: 'bottom-start', content: 'top-start' }}>
					{url}
					<url-test-values
						slot="content"
						fields={fields}
						params={params}
						page-sys-policy={pageSysPolicy}
						applications={applications}
						user-scope={userScope}
						support-scope={supportScope}
						page-scope={pageScope}
						disabled={editingDisabled}
					/>
				</now-popover>
			) : (
				url
			)}
			<now-button-bare
				label={t('Open')}
				icon-start="open-link-fill"
				size="sm"
				variant="tertiary"
				disabled={openDisabled}
				append-to-payload={{ button: 'open' }}
			/>
		</div>
	);
};

export default createCustomElement('sn-uib-address-bar', {
	view,
	properties: {
		runtimeUrl: {
			default: {}
		},
		isVariantSelected: {
			default: false
		},
		urlTestValueChanged: {
			default: ''
		},
		pageName: {
			default: ''
		},
		pageSysPolicy: {
			default: ''
		},
		applications: {
			default: []
		},
		userScope: {
			default: null
		},
		supportScope: {
			default: false
		},
		pageScope: {
			default: null
		}
	},
	initialState: {
		currentAbsoluteUrl: '',
		currentFormattedUrl: [],
		popoverOpened: false,
		openDisabled: true,
		isNecessaryTransactionInProgress: false
	},
	actionHandlers: {
		[SN_UIB_TRANSACTION_ALERT_TRANSACTION_IN_SCOPE]: ({
			updateState,
			action
		}) => {
			action.stopPropagation();
			const { isNecessaryTransactionInProgress } = action.payload;
			updateState({
				loaded: true,
				editingDisabled: !isNecessaryTransactionInProgress
			});
		},
		[actionTypes.COMPONENT_DOM_READY]: ({ properties, updateState }) => {
			const { absolute, fields } = properties.runtimeUrl;
			const currentFormattedUrl = runtimeUrlArray(properties.runtimeUrl, true);
			const openDisabled = isOpenDisabled(fields);
			batchShorthandUpdateState(
				{
					currentFormattedUrl,
					currentAbsoluteUrl: absolute,
					openDisabled
				},
				updateState
			);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			properties,
			action,
			updateState
		}) => {
			const { name, value, previousValue } = action.payload;
			const {
				runtimeUrl: { absolute, fields },
				isVariantSelected
			} = properties;

			if (name === 'runtimeUrl' && value.absolute !== previousValue.absolute) {
				updateState({
					currentFormattedUrl: runtimeUrlArray(value, true)
				});
			}
			const openDisabled = isOpenDisabled(fields, isVariantSelected);

			batchShorthandUpdateState(
				{
					currentAbsoluteUrl: absolute,
					openDisabled
				},
				updateState
			);
		},
		['NOW_POPOVER#OPENED_SET']: ({ action, updateState }) => {
			updateState({ popoverOpened: action.payload.value });
		},
		['URL_TEST_VALUE#CLOSE_ICON_CLICKED']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ popoverOpened: false });
		},
		['URL_TEST_VALUE#INPUT']: ({ action, properties, updateState }) => {
			const {
				payload: { testValue, type, pagePropertyId }
			} = action;
			const { runtimeUrl, isVariantSelected } = properties;

			const fieldsOrParams = cloneDeep(runtimeUrl[type]);
			const changed = fieldsOrParams.find(
				({ pageProperty: { id } }) => id === pagePropertyId
			);
			changed.value = testValue || changed.pageProperty.value;

			const newRuntimeUrl = {
				...runtimeUrl,
				[type]: fieldsOrParams
			};
			const currentAbsoluteUrl = runtimeUrlArray(newRuntimeUrl).join('/');
			const currentFormattedUrl = runtimeUrlArray(newRuntimeUrl, true);
			const fields = type === FIELDS ? fieldsOrParams : runtimeUrl.fields;
			const openDisabled = isOpenDisabled(fields, isVariantSelected);

			batchShorthandUpdateState(
				{
					currentAbsoluteUrl,
					currentFormattedUrl,
					openDisabled
				},
				updateState
			);
		},
		['URL_TEST_VALUE#CHANGE_COMPLETE']: ({ dispatch, properties, action }) => {
			const headerAction = properties.urlTestValueChanged;
			dispatch(headerAction, action.payload);
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ action, properties }) => {
			action.stopPropagation();
			const currentAbsoluteUrl = runtimeUrlArray(
				properties.runtimeUrl,
				false,
				true
			).join('/');
			if (action.payload.button === 'open') {
				window.open(currentAbsoluteUrl, '_blank');
			}
		}
	},
	styles
});
