import '@servicenow/now-button';
import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from './sn-uib-property-input-data-link-toggle.scss';

export const PROPERTY_INPUT_BINDING_BUTTON_CLICKED =
	'SN_UIB_PROPERTY_INPUT#PROPERTY_BINDING_BUTTON_CLICKED';

const view = (state) => {
	const {
		properties: { disabled, selected }
	} = state;

	const statefulButtonClasses = {
		selected: selected
	};

	return (
		<Fragment>
			<now-button-stateful
				manageSelected
				selected={selected}
				class={statefulButtonClasses}
				icon="database-outline"
				tooltipContent={t('Dynamic data binding')}
				variant="tertiary"
				disabled={disabled}
				bare
			/>
		</Fragment>
	);
};

export default createCustomElement('sn-uib-property-input-data-link-toggle', {
	view,
	properties: {
		isStatic: {
			default: true
		},
		propertyId: {
			default: null
		},
		disabled: {
			default: false
		},
		selected: {
			default: false
		}
	},
	actionHandlers: {
		'NOW_BUTTON_STATEFUL#SELECTED_SET': ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const { isStatic, propertyId } = properties;

			dispatch(PROPERTY_INPUT_BINDING_BUTTON_CLICKED, {
				isStatic,
				propertyId
			});
		}
	},
	styles
});
