export const expandAllItems = (treeArray) => {
	const createOpenableItems = (array = [], ids = [], path = []) => {
		const getOpenableItems = (acc, tree) => {
			const keys = Object.keys(tree);
			if (!keys.includes('children')) {
				return acc;
			}
			const updated = {
				ids: [...acc.ids, [...acc.path, tree.id]],
				path: [].concat(path, tree.id)
			};
			return {
				ids: [
					...updated.ids,
					...createOpenableItems(tree.children, [], updated.path).ids
				],
				path: path
			};
		};
		return array.reduce(getOpenableItems, { ids, path });
	};
	return createOpenableItems(treeArray).ids;
};

export const getPathForId = (treeArray, id, basePath = []) => {
	const last = (arr) => arr[arr.length - 1];
	let path = [];
	const toFind = [...treeArray];

	while (last(path) !== id && toFind.length > 0) {
		const current = toFind.shift();
		if (current.id === id) {
			path = [...basePath, current.id];
		} else if (current.children && current.children.length > 0) {
			path = getPathForId(current.children, id, [...basePath, current.id]);
		}
	}

	return path;
};
