import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom, { createRef } from '@servicenow/ui-renderer-snabbdom';
import styles from './_sn-uib-layout-drag-leaf.scss';
import {
	handleDragUpdate,
	handleDragInsert
} from '../behaviors/draggable/utils';
import strategyManager from '../helpers/strategyManager';
import interact from 'interactjs';
import get from 'lodash/get';
import { elementRefMap } from '../sn-uib-element-controls-placement';

interact.dynamicDrop(true);

const { COMPONENT_DISCONNECTED } = actionTypes;

const dragOverlayStateMapper = (() => {
	const overlayElementMap = new Map();
	const interactions = new Map();
	let mousedowns = {};
	const OFF = 'OFF';
	const DOWN = 'DOWN';
	const MOVE = 'MOVE';

	return {
		mouseDowned(id, e) {
			const isRegistered = mousedowns[e.timeStamp];
			if (isRegistered) {
				return;
			}
			if (!isRegistered) {
				mousedowns[e.timeStamp] = true;
			}

			interactions.set(id, DOWN);
		},

		mouseUpped(id) {
			const el = overlayElementMap.get(id);

			if (el && el.classList.contains('activated')) {
				el.classList.remove('activated');
			}
			interactions.set(id, OFF);
		},
		mouseMoved(id) {
			const el = overlayElementMap.get(id);
			if (el && interactions.get(id) === DOWN) {
				el.classList.add('activated');
				interactions.set(id, MOVE);
			}
			mousedowns = {};
		},
		mouseLeft(id) {
			const el = overlayElementMap.get(id);

			if (el && el.classList.contains('activated')) {
				el.classList.remove('activated');
			}
			interactions.set(id, OFF);
			mousedowns = {};
		},
		newOverlayElm(id, el) {
			overlayElementMap.set(id, el);
		},
		elementUnmounted(id) {
			interactions.delete(id);
			overlayElementMap.delete(id);
		}
	};
})();
const view = (state, dispatch) => {
	const { componentId, elmHeightStyle, elmWidthStyle, wrapperRef } = state;
	const {
		id,
		additionalRenders,
		elementInstanceName,
		elementComponentIcon,
		storeRef,
		parentNodeId
	} = state.properties;

	const element = {
		displayName: get(
			state.properties,
			'element.displayName',
			elementInstanceName
		),
		icon: get(state.properties, 'element.icon', elementComponentIcon)
	};

	const dataTransfer = {
		id,
		movedComponent: true
	};

	const dndTag = `${id}__DRAG`;
	const scrollRef = strategyManager.get('stage', 'ref');

	const scrollContainer =
		scrollRef && scrollRef.current ? scrollRef.current : document.body;
	const style = { ...elmHeightStyle, ...elmWidthStyle };

	if (storeRef) {
		elementRefMap.set(id, wrapperRef, parentNodeId);
	}

	return (
		<div
			ref={wrapperRef}
			className="container"
			style={style}
			hook={{
				update: (_, newNode) => {
					const { elm } = newNode;
					handleDragUpdate({
						el: elm,
						id,
						element,
						dispatch,
						tag: dndTag,
						scrollContainer,
						dataTransfer
					});
				},
				insert: ({ elm }) => {
					handleDragInsert({
						el: elm,
						id,
						element,
						dispatch,
						tag: dndTag,
						scrollContainer,
						dataTransfer
					});
				}
			}}
			on-mousedown={(e) => {
				dragOverlayStateMapper.mouseDowned(componentId, e);
			}}
			on-mouseleave={() => {
				dragOverlayStateMapper.mouseLeft(componentId);
			}}
			on-mouseup={() => {
				dragOverlayStateMapper.mouseUpped(componentId);
			}}
			on-mousemove={() => {
				dragOverlayStateMapper.mouseMoved(componentId);
			}}>
			{additionalRenders(wrapperRef)}

			<slot />
			<div
				class={{ 'drag-overlay': true }}
				hook={{
					update: (_, newNode) => {
						dragOverlayStateMapper.newOverlayElm(componentId, newNode.elm);
					},
					insert: ({ elm }) => {
						dragOverlayStateMapper.newOverlayElm(componentId, elm);
					}
				}}
			/>
		</div>
	);
};

export default createCustomElement('sn-uib-layout-drag-leaf', {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		wrapperRef: createRef()
	},

	properties: {
		id: {
			default: null
		},
		elementStyles: {
			default: []
		},
		storeRef: {
			default: false
		},
		parentNodeId: {
			default: null
		},
		slotName: {
			default: null
		},
		additionalRenders: {
			default: () => {}
		},
		element: {
			default: {}
		},
		elementInstanceName: {
			default: ''
		},
		elementComponentIcon: {
			default: ''
		}
	},
	transformState: (state) => {
		const { elementStyles, id } = state.properties;
		const style = get(elementStyles, id, {});
		const height = get(style, 'height');
		if (height) {
			return { ...state, elmHeightStyle: { height, 'overflow-y': 'auto' } };
		}
		const width = get(style, 'width');
		if (width) {
			return { ...state, elmWidthStyle: { width } };
		}
		return state;
	},
	actionHandlers: {
		[COMPONENT_DISCONNECTED]: ({ state: { componentId } }) => {
			dragOverlayStateMapper.elementUnmounted(componentId);
		},
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ updateState }) => {
			updateState({
				wrapperRef: createRef()
			});
		}
	},
	styles
});
