import isEmpty from 'lodash/isEmpty';
import intersection from 'lodash/intersection';

const hasWhiteSpace = (s) => {
	return /\s/g.test(s);
};

/**
 * Function that score calculation is invoked only on multi word search such as
 *	"set tab" or "tab set" etc.
 *
 *	The score is based on 2 things, how many words match exactly (1 point per word match)
 *	and how many words partially match (0.1 point per partial match)
 *
 * @param {String} s1 the string to match in
 * @param {String} s2 the search text
 * @returns {number} the score of the match between the two strings
 */

const matchScore = (s1 = '', s2 = '') => {
	const wordMatchScore = intersection(
		s1.toLowerCase().split(' '),
		s2.toLowerCase().split(' ')
	).length;

	const partialMatchScore = s2
		.toLowerCase()
		.split(' ')
		.reduce((acc, s2part) => {
			if (s1.indexOf(s2part) > -1) {
				return acc + 0.1;
			}

			return acc;
		}, 0);

	return wordMatchScore + partialMatchScore;
};

/**
 * Function that filters and returns the components based on the matching search term
 *
 * @param {Array} components the master list of components
 * @param {String} search the search text
 * @returns {Array} the filtered list of components based on the search term
 */

export const filterComponents = (components, search) => {
	if (isEmpty(search)) {
		return components;
	}

	const searchText = search.trim();

	if (!hasWhiteSpace(searchText)) {
		const searchExpression = new RegExp(searchText, 'i');
		return components.filter(
			({ name, category }) =>
				name.search(searchExpression) > -1 ||
				category.search(searchExpression) > -1
		);
	} else {
		const ranked = components.map((component) => {
			const { name, category } = component;

			return {
				component,
				score: Math.max(
					matchScore(name, searchText),
					matchScore(category, searchText)
				)
			};
		});

		return ranked
			.filter((r) => r.score > 0)
			.sort((a, b) => b.score - a.score)
			.map((r) => r.component);
	}
};
