import { actionTypes } from '@servicenow/ui-core';

const { COMPONENT_CONNECTED, COMPONENT_DISCONNECTED } = actionTypes;

export const MEDIA_HOST_MAP = new WeakMap();
export const MEDIA_QUERY_MAP = new WeakMap();

export const SnUIBMatchMediaBehavior = {
	name: 'SnUIBMatchMediaBehavior',

	actionHandlers: {
		[COMPONENT_CONNECTED]: ({ action, dispatch }) => {
			const { matchMedia } = window;
			const {
				host,
				options: { dispatchOnInit = true, queries = [] }
			} = action.payload;

			/** @type {MediaQueryList[]} */
			const queryLists = queries

				// Ignore queries that misconfigure the `media` property
				.filter(({ media }) => typeof media === 'string')

				// Given that `media` is a string, the rest of the configuration is the responsibility of the consumer.
				// Here we create an array of MediaQueryList instances
				.map(({ media, actionType, appendToPayload = {} }) => {
					const mql = matchMedia(media);
					const handler = (mediaQuery) => {
						dispatch(actionType, { mediaQuery, ...appendToPayload });
					};
					mql.addListener(handler);

					// If the current MediaQueryList applies to the current window, dispatch the configured handler
					if (dispatchOnInit && mql.matches) {
						handler(mql);
					}

					// Save the handler for this MediaQueryList to remove it on disconnect
					MEDIA_QUERY_MAP.set(mql, handler);
					return mql;
				});

			MEDIA_HOST_MAP.set(host, queryLists);
		},

		[COMPONENT_DISCONNECTED]: ({ action }) => {
			const { host } = action.payload;
			const queryLists = MEDIA_HOST_MAP.get(host);

			queryLists.forEach((mq) => {
				const handler = MEDIA_QUERY_MAP.get(mq);
				mq.removeListener(handler);
				MEDIA_QUERY_MAP.delete(mq);
			});

			MEDIA_HOST_MAP.delete(host);
		}
	}
};

export default SnUIBMatchMediaBehavior;
