import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import { t } from 'sn-translate';
import styles from './sn-uib-instance-name-editor.scss';
import {
	INSTANCE_NAME_POPOVER_CLOSED,
	INSTANCE_NAME_POPOVER_VALUES_SAVED
} from '../sn-uib-instance-name-popover';
import { componentList } from './componentList';
import { COMPONENT_INFORMATION_POPOVER_CLOSED } from '../sn-uib-component-information-popover';

export const INSTANCE_NAME_EDITOR_IS_HIDDEN_TOGGLED =
	'SN_UIB_INSTANCE_NAME_EDITOR#IS_HIDDEN_TOGGLED';

const INSTANCE_NAME_EDITOR_VALUES_CHANGED =
	'SN_UIB_INSTANCE_NAME_EDITOR#VALUES_CHANGED';

const view = ({ properties, popoverOpened, informationOpened }) => {
	const {
		context,
		disabled,
		id,
		instanceName,
		currentIsHiddenValue,
		selectedComponentTag,
		componentDescription,
		isHiddenInputOpen,
		hideComponentPropertyBindingsEnabled,
		isMacroponentReadOnly
	} = properties;
	const isHiddenConfigured =
		currentIsHiddenValue && currentIsHiddenValue !== '@';
	const hasLink = componentList[selectedComponentTag];
	return (
		<div className="sn-uib-instance-name-editor-header">
			<div className="p2-panel-header-text header-text">
				<span>{instanceName}</span>
				<span>
					<strong>ID: </strong>
					{id}
				</span>
			</div>
			{hasLink || componentDescription ? (
				<now-popover
					key={id}
					opened={informationOpened}
					manage-opened={true}
					constrain={{ width: 300 }}
					positions={[{ target: 'bottom-end', content: 'top-end' }]}>
					<now-button-iconic
						className="popover-trigger-button"
						slot="trigger"
						bare
						size="sm"
						variant="tertiary"
						icon={
							informationOpened ? 'circle-info-fill' : 'circle-info-outline'
						}
						append-to-payload={{ context: 'info' }}
					/>
					<sn-uib-component-information-popover
						slot="content"
						key={id}
						{...properties}
						append-to-payload={{ context }}
						hasLink={hasLink}
					/>
				</now-popover>
			) : null}
			{context === 'component' ? (
				<div
					class={{
						['is-hidden-toggle-bg']: true,
						configured: isHiddenConfigured
					}}>
					{isHiddenConfigured ? (
						<div className="is-hidden-toggle-indicator" />
					) : null}
					{hideComponentPropertyBindingsEnabled ? (
						<now-button-iconic
							className="is-hidden-toggle"
							bare
							size="sm"
							variant="tertiary"
							icon={isHiddenInputOpen ? 'eye-fill' : 'eye-outline'}
							disabled={isMacroponentReadOnly || disabled}
							tooltip-content={t('Edit component visibility')}
							append-to-payload={{ context: 'hidden' }}
						/>
					) : null}
				</div>
			) : null}
			<now-popover
				key={id}
				hide-tail={true}
				opened={popoverOpened}
				manage-opened={true}
				constrain={{ width: 300 }}
				positions={[{ target: 'bottom-end', content: 'top-end' }]}>
				<now-button-iconic
					className="popover-trigger-button"
					slot="trigger"
					bare
					size="sm"
					variant="tertiary"
					icon={popoverOpened ? 'pencil-fill' : 'pencil-outline'}
					tooltip-content={
						context === 'component'
							? t('Edit component label and ID')
							: t('Edit label and ID')
					}
					disabled={isMacroponentReadOnly || disabled}
					append-to-payload={{ context: 'name' }}
				/>
				<sn-uib-instance-name-popover
					slot="content"
					key={id}
					{...properties}
					append-to-payload={{ context }}
				/>
			</now-popover>
		</div>
	);
};

export default createCustomElement('sn-uib-instance-name-editor', {
	view,
	styles,
	properties: {
		context: {
			schema: { type: 'string' },
			default: ''
		},
		disabled: {
			schema: { type: 'boolean' },
			default: false
		},
		id: {
			schema: { type: 'string' },
			default: ''
		},
		instanceName: {
			schema: { type: 'string' },
			default: ''
		},
		instanceNameMap: {
			schema: { type: 'object' },
			default: {}
		},
		category: {
			schema: { type: 'string' },
			default: ''
		},
		currentIsHiddenValue: {
			default: ''
		},
		selectedComponentTag: {
			default: ''
		},
		componentDescription: {
			default: ''
		},
		isHiddenInputOpen: {
			default: ''
		},
		componentTitle: {
			computed({ properties: { instanceName } }) {
				return instanceName.slice(0, instanceName.length - 2);
			}
		},
		hideComponentPropertyBindingsEnabled: {
			default: false
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	initialState: {
		popoverOpened: false,
		informationOpened: false
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({
			action,
			state,
			properties,
			updateState,
			dispatch
		}) => {
			action.stopPropagation();
			switch (action.payload.context) {
				case 'name':
					updateState({ popoverOpened: !state.popoverOpened });
					break;
				case 'info':
					updateState({ informationOpened: !state.informationOpened });
					break;
				case 'hidden':
					dispatch(INSTANCE_NAME_EDITOR_IS_HIDDEN_TOGGLED, {
						elementId: properties.id
					});
			}
		},
		[INSTANCE_NAME_POPOVER_CLOSED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ popoverOpened: false });
		},
		[COMPONENT_INFORMATION_POPOVER_CLOSED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ informationOpened: false });
		},
		[INSTANCE_NAME_POPOVER_VALUES_SAVED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(INSTANCE_NAME_EDITOR_VALUES_CHANGED, action.payload);
		}
	}
});
