import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-menu-header-button.scss';

const view = ({ properties }) => {
	const { menu } = properties;
	return (
		<div className="menu-header-button" role="button" tabIndex="0">
			{menu}
		</div>
	);
};

export default createCustomElement('sn-uib-menu-header-button', {
	view,
	properties: {
		menu: {
			default: ''
		}
	},
	styles
});
