import trim from 'lodash/trim';

// TODO: make this shared with UXF middleware.
// We have the same constants and function defined there.
const TRANSLATION_PREFIXES = [
	/TRF:(.*)/,
	/MSG:(.*)/,
	/GMLD:(.*)/,
	/TRT:(.*)/,
	/CHC:(.*)/
];

export const removePrefixes = (str) => {
	for (const prefix of TRANSLATION_PREFIXES) {
		const match = str.match(prefix);
		if (match) {
			return trim(match[1]);
		}
	}
	return str;
};
