import { createCustomElement } from '@servicenow/ui-core';
import get from 'lodash/get';
import styles from './sn-uib-menu-elements.scss';
import './sn-uib-menu-element-child';

const view = ({ properties }) => {
	const { sectionProps, index, addPadding } = properties;
	const children = get(sectionProps, ['items', index, 'children']);
	const sectionLabel = get(sectionProps, ['items', index, 'sectionLabel']);
	return (
		<div className={`menu-section ${addPadding ? 'padded' : ''}`}>
			{sectionLabel ? (
				<span className="menu-section-label">{sectionLabel}</span>
			) : null}
			{children ? (
				<div className="menu-children">
					{children.map((child) => (
						<sn-uib-menu-element-child
							icon={child.icon}
							label={child.label}
							headerAction={child.headerAction}
							children={child.children}
							disabled={child.disabled}
						/>
					))}
				</div>
			) : null}
		</div>
	);
};

export default createCustomElement('sn-uib-menu-element-section', {
	view,
	properties: {
		sectionProps: {
			default: {}
		},
		index: {
			default: 0
		},
		addPadding: {
			default: false
		}
	},
	styles
});
