import {
	pageMapNavigator,
	simpleLastMemoize
} from '../helpers/pageMapNavigator';
import get from 'lodash/get';
import { DevxKeyFocusBehavior } from 'sn-devx-components';

const { createFocusRef } = DevxKeyFocusBehavior;

const getId = (node) => {
	return node ? node.id : node;
};
const getElementIds = (navigator) => {
	const nextElement = getId(navigator.nextElement().value() || null);
	const previousElement = getId(navigator.previousElement().value() || null);
	const firstChildElement = getId(
		navigator.firstChildElement(navigator).value()
	);

	return {
		previousElement,
		nextElement,
		parent: getId(navigator.ownerElement().value()),
		firstChild: firstChildElement
	};
};

const getDragIds = (navigator) => {
	const parent = getId(navigator.parent().value());
	let firstChild = navigator.firstDragChild().value();

	const nextSibling = getId(navigator.nextDrag().value());
	const previousSibling = getId(navigator.previousDrag().value());

	if (
		firstChild &&
		firstChild.nodeType === 'element' &&
		firstChild.children.length === 0
	) {
		firstChild = `${firstChild.id}_EMPTY`;
	} else {
		firstChild = getId(firstChild);
	}

	const immediateFirstChild = navigator.immediateFirstChild().value();
	const type = get(immediateFirstChild, 'type');
	const id = getId(immediateFirstChild);
	return {
		previousSibling,
		nextSibling,
		parent,
		entrypoint: navigator.isEntrypoint(),
		firstChild,
		childInformation: {
			type,
			id
		}
	};
};

const getMaxSize = (node) => {
	const minimumSizes = node.parent().children().value().length - 1;
	return 12 - minimumSizes;
};
const getResizeInformation = (navigator) => {
	let info = {
		left: null,
		right: null
	};
	const nextSiblingParent = navigator.nextColumnParent('nextSibling');
	const previousSiblingParent = navigator.nextColumnParent('previousSibling');
	if (!nextSiblingParent.value() && !previousSiblingParent.value()) {
		return info;
	}

	const left = previousSiblingParent.value()
		? {
				size: get(previousSiblingParent.value(), 'size', -1),
				id: getId(previousSiblingParent.value()),
				minSize: 1,
				maxSize: getMaxSize(previousSiblingParent),
				owner: getId(previousSiblingParent.ownerElement().value())
		  }
		: null;
	const right = nextSiblingParent.value()
		? {
				size: get(nextSiblingParent.value(), 'size', -1),
				id: getId(nextSiblingParent.value()),
				minSize: 1,
				maxSize: getMaxSize(nextSiblingParent),
				owner: getId(nextSiblingParent.ownerElement().value())
		  }
		: null;

	info = {
		left,
		right
	};
	return info;
};
const getEmptySlotIds = (navigator) => {
	const parent = getId(navigator.directParent().value());
	return {
		parent
	};
};

const getElementRef = ({ id, element }) => {
	const elementId = id;
	const { nextElement, previousElement, parent } = element;
	return createFocusRef(`${elementId}_ROOT`, {
		Enter: `${elementId}_MOVE`,
		Tab: nextElement
			? `${nextElement}_ROOT`
			: 'quick-link-element-configuration-panel',
		ShiftTab: previousElement ? `${previousElement}_ROOT` : null,
		Escape: parent ? `${parent}_ROOT` : 'quick-link-page-management-panel'
	});
};

const getRank = (keymaps) => {
	if (!keymaps.previousElement) {
		return 'ENTRYPOINT';
	}
	return 'BASIC';
};
const getIds = (id, tree) => {
	const navigator = pageMapNavigator({ root: tree }).findNode(id);

	const node = navigator.value() || {};
	const isElement = node.nodeType === 'element';
	const element = isElement ? getElementIds(navigator) : {};
	const resize = getResizeInformation(navigator);

	const elementInfo = isElement
		? {
				elementRef: getElementRef({ id, element }),
				rank: getRank(element),
				wrapperProps: {
					resizeInfo: resize,
					nextElement: element.nextElement
				}
		  }
		: {};
	return {
		element,
		drag: isElement ? {} : getDragIds(navigator),
		emptySlot: isElement ? getEmptySlotIds(navigator) : {},
		resize,
		type: node.type,
		...elementInfo
	};
};
/**
 * Creates a very simple lookup store with the tree as the key. It is structured this way so when the tree changes we wipe out previous
 * caches so they can be garbage-collected.
 */

const mapStore = () => {
	let store = {};
	return (id, tree) => {
		let existingStore = store[tree];
		if (!existingStore) {
			existingStore = {};
			store = { [tree]: existingStore };
		}
		if (!existingStore[id]) {
			const memoizedGet = simpleLastMemoize(getIds);
			existingStore[id] = memoizedGet;
		}
		const fn = existingStore[id];
		return fn(id, tree);
	};
};
const store = mapStore();

/**
 * All this memoization is being done because even though the keyboard provider is a high level component it is rerendered alot.
 * This causes alot of unneccessary recomputation of the same information
 */

export const getRelativeIds = (id, tree) => {
	if (!tree) {
		return {};
	}

	return store(id, tree);
};
export const visitEveryNode = (tree, cb, result = {}) => {
	const value = cb(tree.id);
	return {
		...tree.children.reduce(
			(acc, node) => {
				return visitEveryNode(node, cb, acc);
			},
			{ [tree.id]: value, ...result }
		)
	};
};
