import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import styles from './sn-uib-draggable-list.scss';
import isNil from 'lodash/isNil';
import {
	SN_UIB_PILLAR_WRAPPER_DRAGGED_OVER,
	SN_UIB_PILLAR_WRAPPER_DRAG_STARTED,
	SN_UIB_PILLAR_WRAPPER_DRAG_ENDED
} from './sn-uib-pillar-wrapper';
import { dropBehavior } from './behaviors';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const SN_UIB_DRAGGABLE_LIST_ITEM_MOVED =
	'SN_UIB_DRAGGABLE_LIST#ITEM_MOVED';

export const DragPosition = {
	top: 'top',
	bottom: 'bottom'
};

// A static config that drives this component
const COMPONENT_CONFIG = {
	noSelectTextAndImages: true // prevent text selection, image drags
};

const renderList = (
	items,
	selectable,
	size,
	dragTarget,
	draggingItem,
	dragPosition,
	allowDrop,
	isChild,
	isEditable,
	rootContainerId
) =>
	items &&
	items.map((item) => {
		if (isNil(item.order) || isNil(item.id)) {
			console.error('order & id are required for each item of draggable list');
		}

		const hasChildren = Array.isArray(item.items) && item.items.length > 0;
		const itemIsDragTarget = dragTarget ? item.id === dragTarget.id : false;
		const itemIsDraggingItem = item.id === (draggingItem && draggingItem.id);

		return allowDrop && itemIsDragTarget && !itemIsDraggingItem ? (
			<Fragment>
				{dragPosition === DragPosition.top ? (
					<div
						class={{
							placeholder: true,
							'child-placeholder': isChild
						}}></div>
				) : null}
				<sn-uib-pillar-wrapper
					item={item}
					append-to-payload={{
						id: item.id,
						order: item.order,
						label: item.label,
						rootContainerId,
						itemProperties: item.properties
					}}
					isDraggable={item.isDraggable}
					hasChildren={hasChildren}
					draggable={item.isDraggable}
					is-editable={isEditable}
					isExpanded={item.isExpanded}
					size={size}
					selectable={selectable}
					isSelected={item.isSelected}
					isChild={isChild}
					rootContainerId={rootContainerId}>
					{renderList(
						item.items,
						selectable,
						size,
						dragTarget,
						draggingItem,
						dragPosition,
						allowDrop,
						true,
						isEditable,
						item.id
					)}
				</sn-uib-pillar-wrapper>
				{dragPosition === DragPosition.bottom ? (
					<div
						class={{
							placeholder: true,
							'child-placeholder': isChild
						}}></div>
				) : null}
			</Fragment>
		) : (
			<sn-uib-pillar-wrapper
				item={item}
				append-to-payload={{
					id: item.id,
					order: item.order,
					label: item.label,
					rootContainerId,
					itemProperties: item.properties
				}}
				being-dragged={draggingItem ? item.id === draggingItem.id : false}
				selectable={selectable}
				isDraggable={item.isDraggable}
				draggable={item.isDraggable}
				is-editable={isEditable} // prevent double click name edits of tabs
				hasChildren={hasChildren}
				isExpanded={item.isExpanded}
				isSelected={item.isSelected}
				size={size}
				isChild={isChild}
				rootContainerId={rootContainerId}>
				{!itemIsDraggingItem
					? renderList(
							item.items,
							selectable,
							size,
							dragTarget,
							draggingItem,
							dragPosition,
							allowDrop,
							true,
							isEditable,
							item.id
					  )
					: null}
			</sn-uib-pillar-wrapper>
		);
	});

const view = ({
	properties: { items, selectable, size, disabled, isEditable },
	dragTarget,
	draggingItem,
	dragPosition,
	allowDrop
}) => (
	<div
		class={{
			'draggable-list': true,
			'-is-disabled': disabled,
			'no-select': COMPONENT_CONFIG.noSelectTextAndImages
		}}
		style={{
			cursor: draggingItem && draggingItem.order > -1 ? 'move' : 'default'
		}}>
		{disabled ? <div className="disabled-overlay" /> : null}
		{renderList(
			items,
			selectable,
			size,
			dragTarget,
			draggingItem,
			dragPosition,
			allowDrop,
			false,
			isEditable,
			null
		)}
	</div>
);

createCustomElement('sn-uib-draggable-list', {
	renderer: { type: snabbdom },
	view,
	styles,
	properties: {
		/**
		 * @typedef Item
		 * @property {string} id [required]
		 * @property {number} order [required]
		 * @property {string} label [required]
		 * @property {boolean} isExpanded
		 * @property {boolean} isSelected
		 * @property {boolean} isDraggable
		 * @property {object[]} actions
		 */
		/**
		 * @type {Item[]}
		 */
		items: {
			default: []
		},
		/**
		 * @type {boolean} whether or not pillar items are selectable
		 */
		selectable: {
			default: false
		},
		/**
		 * @type {boolean} whether double click name edits of tabs
		 */
		isEditable: {
			default: true
		},
		/**
		 * sets the font & icon sizes, eg. sm, md, lg
		 * @type {string}
		 */
		size: {
			default: 'md'
		},
		/**
		 * @type {boolean} whether the entire draggable list is disabled
		 */
		disabled: {
			default: false
		}
	},
	initialState: {
		dragTarget: null,
		dragPosition: null,
		draggingItem: null,
		allowDrop: false,
		dragItemRootContainerId: null
	},
	actionHandlers: {
		[SN_UIB_PILLAR_WRAPPER_DRAGGED_OVER]: ({
			action: {
				payload: { dragTarget, event, el, dragTargetRoot }
			},
			updateState,
			state: { draggingItem, dragItemRootContainerId }
		}) => {
			if (dragTarget.id === draggingItem.id) {
				return;
			}

			const box = el.getBoundingClientRect();
			const draggedOverTopHalf =
				event.clientY < box.top + Math.floor(box.height / 2);

			batchShorthandUpdateState(
				{
					allowDrop: dragItemRootContainerId === dragTargetRoot,
					dragTarget,
					dragPosition: draggedOverTopHalf
						? DragPosition.top
						: DragPosition.bottom
				},
				updateState
			);
		},
		[SN_UIB_PILLAR_WRAPPER_DRAG_STARTED]: ({
			action: {
				payload: { item, rootContainerId }
			},
			updateState
		}) => {
			batchShorthandUpdateState(
				{
					draggingItem: item,
					dragItemRootContainerId: rootContainerId
				},
				updateState
			);
		},
		[SN_UIB_PILLAR_WRAPPER_DRAG_ENDED]: ({ updateState }) => {
			batchShorthandUpdateState(
				{
					dragTarget: null,
					dragPosition: null,
					draggingItem: null,
					dragItemRootContainerId: null
				},
				updateState
			);
		}
	},
	behaviors: [
		{
			behavior: dropBehavior,
			options: {
				onDrop(
					item,
					{
						dispatch,
						state: {
							dragPosition,
							dragTarget,
							draggingItem,
							dragItemRootContainerId
						},
						updateState
					}
				) {
					let dragTargetIndex = dragTarget.order;

					// Below is a fix to an edge case where the dragTarget is the wrong value (When a user drags an item from the top,
					// passes through any number of items, goes back to the top and drops anywhere in the list. The dragTarget is expectedIndex + 1 rather than the expectedIndex)
					const draggedItemInitialPosition = draggingItem.order;
					const draggedItemDroppedPosition = dragTarget.order;
					const isDragPositionTop = dragPosition === DragPosition.top;
					const isDroppedPositionNextToInitialPosition =
						Math.abs(
							draggedItemInitialPosition - draggedItemDroppedPosition
						) === 1;
					const isItemDroppedBelowInitialPos =
						draggedItemInitialPosition < draggedItemDroppedPosition;
					if (
						isItemDroppedBelowInitialPos &&
						isDroppedPositionNextToInitialPosition &&
						isDragPositionTop
					) {
						dragTargetIndex = dragTargetIndex - 1;
					}

					dispatch(SN_UIB_DRAGGABLE_LIST_ITEM_MOVED, {
						item,
						dragIndex: dragTargetIndex,
						dragItemRootContainerId
					});

					batchShorthandUpdateState(
						{
							dragTarget: null,
							dragPosition: null,
							draggingItem: null
						},
						updateState
					);
				}
			}
		}
	]
});
