import get from 'lodash/get';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
const BASIC = 'BASIC';
const { createFocusRef } = DevxKeyFocusBehavior;
const isRoot = (node) => node && node.parent === ROOT_NODE;
const last = (arr) => arr[arr.length - 1];
const ENTRYPOINT = 'ENTRYPOINT_NODE';

import { ROOT_NODE, ROOT_NODE_KEY } from '../helpers/pageTreeFlatten';
import { memoizedFlatten } from '../helpers/pageMapNavigator';

const whileHelper = (data, test, next) => {
	let current = next(data);
	let count = 0;
	while (test(current, data)) {
		if (count > 10) {
			break;
		}
		current = next(current, data);
		count++;
	}
	return current;
};
const pageMapNavigator = ({ map, root, previousValue }) => {
	const object = (map ? map : memoizedFlatten(root)) || { children: [] };
	const current = previousValue ? previousValue : { value: object };
	const chainer = {
		value() {
			return current.value;
		},
		self() {
			return pageMapNavigator({ map: object, root, previousValue });
		},
		findNode(id) {
			const value = object[id] || null;
			return pageMapNavigator({ map: object, root, previousValue: { value } });
		},
		parent() {
			let { value } = current;

			if (value) {
				if (isRoot(value)) {
					value = null;
				} else {
					value = map[value.parent];
				}
			}
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		lastElement() {
			let node = this.rootNode().lastChild();
			let child = node.lastChild();
			while (child.value()) {
				node = child;
				child = child.lastChild();
			}

			const value = child.value() || node.value();
			return pageMapNavigator({ map: object, root, previousValue: { value } });
		},
		firstElement() {
			const node = this.rootNode().firstChild();
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value: node }
			});
		},
		previousSibling() {
			const children = this.parent().children().value();
			const value =
				children[
					children.findIndex((id) => id === get(current, 'value.id')) - 1
				];

			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		nextSibling() {
			const children = this.parent().children().value();
			const value =
				children[
					children.findIndex((id) => id === get(current, 'value.id')) + 1
				];

			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		nextAncestorSibling() {
			let parent = this.parent().nextSibling();
			let value = parent.nextSibling().value();
			while (!value && parent.value()) {
				parent = parent.parent();
				value = parent.nextSibling().value();
			}
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		previousAncestorSibling() {
			let parent = this.parent().previousSibling();
			let value = parent.previousSibling().value();
			while (!value && parent.value()) {
				parent = parent.parent();
				value = parent.previousSibling().value();
			}
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		nextElement() {
			const node = this.nextSibling();
			let value = node.value();
			while (!value) {
				const firstChild = this.firstChild();
				if (firstChild.value()) {
					value = firstChild.value();
					break;
				}
				const nextAncestorSibling = this.nextAncestorSibling();
				if (nextAncestorSibling.value()) {
					value = nextAncestorSibling.value();
					break;
				}
			}
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		previousElement() {
			const node = this.previousSibling();
			let value = node.value();
			while (!value) {
				const firstChild = this.lastChild();
				if (firstChild.value()) {
					value = firstChild.value();
					break;
				}
				const nextAncestorSibling = this.previousAncestorSibling();
				if (nextAncestorSibling.value()) {
					value = nextAncestorSibling.value();
					break;
				}
			}
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value: current }
			});
		},
		children() {
			const value = get(current, 'value.children', []);
			return pageMapNavigator({ map: object, root, previousValue: { value } });
		},
		ownerElement() {
			const test = (curr) =>
				curr.value() && curr.value().nodeType !== 'element';
			const next = (curr) => curr.parent();
			const value = whileHelper(this, test, next).value();

			return pageMapNavigator({ map: object, root, previousValue: { value } });
		},
		rootNode() {
			const id = get(map, ROOT_NODE_KEY, {}).id;
			const value = get(map, id, null);

			return pageMapNavigator({ map: object, root, previousValue: { value } });
		},
		firstChild() {
			const value = get(current, 'value.children[0]', null);
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		lastChild() {
			const value = last(get(current, 'value.children', [])) || null;
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		},
		firstChildElement() {
			const value = this.firstChild();
			return pageMapNavigator({
				map: object,
				root,
				previousValue: { value }
			});
		}
	};
	return chainer;
};
const getId = (node) => {
	if (node === ROOT_NODE) {
		return node;
	}
	return node ? node.id : node;
};
const getElementRef = ({ id, element }) => {
	const elementId = id;
	const { nextElement, previousElement, parent } = element;

	return createFocusRef(`${elementId}_ROOT`, {
		Enter: `${elementId}_MOVE`,
		Tab: nextElement
			? `${nextElement}_ROOT`
			: 'quick-link-element-configuration-panel',
		ShiftTab: previousElement ? `${previousElement}_ROOT` : null,
		Escape: parent ? `${parent}_ROOT` : 'quick-link-page-management-panel'
	});
};

const getSlotRef = ({ id, element }) => {
	const elementId = id;
	const { nextElement, previousElement, parent } = element;

	return createFocusRef(`${elementId}_ROOT`, {
		Enter: `${elementId}_MOVE`,
		Tab: nextElement
			? `${nextElement}_ROOT`
			: 'quick-link-element-configuration-panel',
		ShiftTab: previousElement ? `${previousElement}_ROOT` : null,
		Escape: parent ? `${parent}_ROOT` : 'quick-link-page-management-panel'
	});
};

const visitEveryNode = (tree, cb, result = {}) => {
	const value = cb(tree);
	return {
		...tree.children.reduce(
			(acc, node) => {
				return visitEveryNode(node, cb, acc);
			},
			{ ...value, ...result }
		)
	};
};

const getElementIds = (navigator) => {
	// const nextElement = getId(navigator.nextElement().value() || null);
	// const previousElement = getId(navigator.previousElement().value() || null);
	// const firstChildElement = getId(
	// 	navigator.firstChildElement(navigator).value()
	// );

	// TODO: We need to test build this up with tests
	return {
		previousElement: null,
		nextElement: null,
		parent: getId(navigator.parent().value()),
		firstChild: null
	};
};
const getEmptySlotIds = (navigator) => {
	const parent = getId(navigator.parent().value());
	return {
		parent
	};
};

const getRelativeIds = (id, map) => {
	const navigator = pageMapNavigator({ root: map }).findNode(id);

	const node = navigator.value() || {};
	const isElement = true;
	const element = getElementIds(navigator);

	const elementInfo = isElement
		? {
				elementRef: getElementRef({ id, element }),
				wrapperProps: {
					resizeInfo: {},
					nextElement: element.nextElement
				},
				parentElement: getId(navigator.ownerElement().value()),
				slotName: get(navigator.value(), 'slotName', null),
				slotStyles: get(navigator.parent().value(), 'slotStyles', {})
		  }
		: {
				elementRef: getSlotRef({ id, element })
		  };
	return {
		element,
		drag: {},
		emptySlot: getEmptySlotIds(navigator),
		parentNodeId: element.parent,
		type: node.type,
		...elementInfo
	};
};
export { visitEveryNode, getRelativeIds, ROOT_NODE, ENTRYPOINT, BASIC };
