import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import debounce from 'lodash/debounce';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import '@servicenow/now-alert';
import '@servicenow/now-icon';
import '@servicenow/now-input';
import '@servicenow/now-select';
import '@servicenow/now-tabs';
import styles from './sn-uib-data-broker-editor.scss';
import {
	dataBrokerInstancePropertyUpdated,
	dataBrokerPropertyBindingToggled,
	dataBrokerReferencePropertySearched,
	dataBrokerEditorComponentPropertyChanged,
	dataBrokerFieldPropertyTableSelected,
	dataBrokerInstanceEvaluationModeChanged,
	dataBrokerEventMappingAdded,
	dataBrokerEventMappingHandlerAdded,
	dataBrokerEventMappingHandlerDeleteRequested,
	dataBrokerEventMappingHandlerDeleted,
	dataBrokerEventMappingHandlerChanged,
	dataBrokerEventMappingHandlerReferencePropertySearched
} from './actionHandlers';
import { PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED } from '../sn-uib-properties-editor-panel';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import { REFERENCE_PROPERTY_VALUE_CHANGED } from '../sn-uib-reference-property-input';
import { FIELD_PROPERTY_TABLE_SELECTED } from '../sn-uib-field-property-input';
import { FIELD_LIST_PROPERTY_TABLE_SELECTED } from '../sn-uib-field-list-property-input';
import {
	EVENT_MAPPING_CONFIG_MAPPING_ADDED,
	EVENT_MAPPING_CONFIG_HANDLER_ADDED,
	EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED,
	EVENT_MAPPING_CONFIG_HANDLER_DELETED
} from '../sn-uib-event-mapping-configuration';
import {
	EVENT_MAPPING_HANDLER_CHANGED,
	EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED,
	EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED
} from '../sn-uib-event-mapping-handler-config';
import '../sn-uib-instance-name-editor';
import '../sn-uib-shelf-pane';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';
import SearchBox from '../partials/search-box'; // eslint-disable-line no-unused-vars

const SN_UIB_DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED =
	'SN_UIB_DATA_BROKER_EDITOR#EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED';

const hasNoDefinition = (definition) =>
	!definition || isEmpty(definition) || definition.invalid;

const renderError = (message) => (
	<div slot="error" className="data-broker-editor-error-message">
		<sn-uib-error
			message={
				message || t('There was a problem loading this data resource instance')
			}
		/>
	</div>
);

const renderContent = ({
	properties,
	container,
	filteredEvents,
	searchTerm,
	selectedTab,
	dispatch
}) => {
	if (!properties.selectedDataBroker || !properties.selectedDataBroker.id) {
		return (
			<EmptyState
				headerMessage={t('No configuration options.')}
				/* prettier-ignore */
				description={t('Select a data resource instance to view configuration options.')}
			/>
		);
	} else {
		return selectedTab === 'events'
			? renderEventsPane(properties, filteredEvents, searchTerm, dispatch)
			: renderConfigPane(properties, container);
	}
};

const renderEventsPane = (properties, filteredEvents, searchTerm, dispatch) => {
	const {
		selectedDataBroker: dataBroker,
		selectedDataBrokerIsAncestor: ancestor
	} = properties;

	const noDefinitionFound = hasNoDefinition(dataBroker.definition);

	return (
		<div className="data-broker-editor-content --events">
			{ancestor ? (
				<now-alert
					status="info"
					icon="lock-fill"
					header={t('Read only:')}
					/* prettier-ignore */
					content={t('This data resource instance is inherited from the parent page')}
				/>
			) : null}
			{noDefinitionFound
				? renderError(
						/* prettier-ignore */
						t('The definition for this data resource instance could not be found')
				  )
				: renderEventsTree(properties, filteredEvents, searchTerm, dispatch)}
		</div>
	);
};

const renderEventsTree = (properties, filteredEvents, searchTerm, dispatch) => {
	const {
		selectedDataBroker: dataBroker,
		disabled,
		selectedDataBrokerIsAncestor: ancestor,
		bindings,
		appConfig,
		eventHandlers,
		eventMappings,
		eventMappingHandlers,
		interfaceConfigurationComponent,
		clientTransformScriptTemplate
	} = properties;
	const { dispatchedEvents = [] } = dataBroker;

	return dispatchedEvents.length > 0 ? (
		<div>
			<div className="search-input">
				<SearchBox
					placeholder={t('Search')}
					value={searchTerm}
					appendToPayload={{ context: 'eventSearch' }}
					dispatch={dispatch}
				/>
			</div>
			{filteredEvents.map((event = {}) => {
				const mappingForCurrentEvent =
					eventMappings.find((mapping) => mapping.eventId === event.apiName) ||
					{};
				return (
					<sn-uib-event-mapping-configuration
						key={event.apiName}
						appConfig={appConfig}
						bindings={bindings}
						context="data-broker"
						event={event}
						eventHandlers={eventHandlers}
						eventMapping={mappingForCurrentEvent}
						eventMappingHandlers={
							eventMappingHandlers[mappingForCurrentEvent.id]
						}
						interfaceConfigurationComponent={interfaceConfigurationComponent}
						disabled={disabled || ancestor ? true : false}
						searchTerm={searchTerm}
						slot="content"
						appendToPayload={{
							context: 'data-broker',
							eventApiName: event.apiName,
							eventMappingId: mappingForCurrentEvent.id
						}}
						scriptTemplate={clientTransformScriptTemplate}
					/>
				);
			})}
		</div>
	) : (
		<EmptyState
			headerMessage={t('No events available.')}
			/* prettier-ignore */
			description={t('The selected data resource instance has no configurable events.')}
		/>
	);
};

const renderDataBrokerExecutionAlert = (output) => {
	const isAclViolation = output.errors && output.aclViolation;
	const headerText = isAclViolation
		? t('You dont have permission to select this data resource.')
		: t('Something went wrong.');
	const contentText = isAclViolation
		? t('Contact your admin for more information')
		: /* prettier-ignore */
		  t('Data resource execution failed.  Please check your input properties and try again.');
	return isAclViolation || output.errors ? (
		<now-alert
			status="high"
			icon="circle-exclamation-outline"
			header={headerText}
			content={contentText}
		/>
	) : null;
};

const renderConfigPane = (properties, container) => {
	const {
		selectedDataBroker: dataBroker,
		disabled,
		currentSelectedProperties,
		selectedDataBrokerIsAncestor: ancestor,
		bindings,
		dataBrokerInstanceNameMap,
		output
	} = properties;

	const noDefinitionFound = hasNoDefinition(dataBroker.definition);

	return (
		<div className="data-broker-editor-content --config">
			{ancestor ? (
				<now-alert
					status="info"
					icon="lock-fill"
					header={t('Read only:')}
					/* prettier-ignore */
					content={t('This data resource instance is inherited from the parent page')}
				/>
			) : null}
			{renderDataBrokerExecutionAlert(output)}
			<div className="instance-name-editor">
				<sn-uib-instance-name-editor
					context="data broker"
					disabled={disabled || ancestor ? true : false}
					id={dataBroker.id || ''}
					instance-name={dataBroker.instanceName || ''}
					instance-name-map={dataBrokerInstanceNameMap}
				/>
			</div>
			{noDefinitionFound ? (
				renderError(
					t('The definition for this data resource instance could not be found')
				)
			) : (
				<div>
					<div className="top-inputs">
						<now-input
							className="data-broker-type-input"
							disabled={true}
							value={dataBroker.definition.type}
							label={t('Type')}
						/>
						<now-select
							className="data-broker-evaluation-mode-dropdown"
							disabled={disabled || ancestor ? true : false}
							/* prettier-ignore */
							helperContent={t('You can choose to have this data resource instance evaluated immediately on page load (eager) or only when explicitly invoked through an event handler (explicit).')}
							items={[
								{ id: 'EAGER', label: t('Immediately (eager)') },
								{ id: 'EXPLICIT', label: t('Only when invoked (explicit)') }
							]}
							label={t('When to evaluate this data resource')}
							manageSelectedItem
							selectedItem={dataBroker.readEvaluationMode || 'EAGER'}
						/>
					</div>
					{!dataBroker.simpleMutationBroker ? (
						<sn-uib-properties-editor-panel
							key={dataBroker.id}
							properties={currentSelectedProperties}
							dataBindingEnabled={ancestor ? false : true}
							bindings={bindings}
							container={container}
							append-to-payload={{ context: 'dataBrokers' }}
							scriptingEnabled={false}
						/>
					) : (
						<EmptyState
							size="sm"
							headerMessage={t('Server update data resource.')}
							/* prettier-ignore */
							description={t('Server update data resource inputs are configured as part of event mappings only.')}
						/>
					)}
				</div>
			)}
		</div>
	);
};

const getTabItems = (error = null, selectedDataBroker = null) => {
	let tabsDisabled = false;
	if (error || !selectedDataBroker || !selectedDataBroker.id) {
		tabsDisabled = true;
	}
	return [
		{
			id: 'config',
			label: t('Configuration'),
			icon: 'gear-outline',
			disabled: tabsDisabled
		},
		{
			id: 'events',
			label: t('Events'),
			icon: 'lightning-outline',
			disabled: tabsDisabled
		}
	];
};

const view = (
	{ properties, container, filteredEvents, searchTerm, selectedTab },
	{ dispatch }
) => {
	return (
		<div
			className="data-broker-editor-container"
			hook-insert={({ elm }) =>
				dispatch('SN_UIB_DATA_BROKER_EDITOR#ELEMENT_INSERTED', elm)
			}>
			<div className="data-broker-editor-header">
				<now-tabs
					items={getTabItems(properties.error, properties.selectedDataBroker)}
					selectedItem={selectedTab}
					manageSelectedItem
				/>
			</div>
			{properties.error
				? renderError()
				: renderContent({
						properties,
						container,
						filteredEvents,
						searchTerm,
						selectedTab,
						dispatch
				  })}
		</div>
	);
};

export default createCustomElement('sn-uib-data-broker-editor', {
	view,
	styles,
	initialState: {
		container: null,
		filteredEvents: [],
		searchTerm: '',
		selectedTab: 'config'
	},
	transformState(state) {
		const {
			searchTerm = '',
			properties: { selectedDataBroker = {} }
		} = state;
		let filteredEvents = selectedDataBroker
			? selectedDataBroker.dispatchedEvents || []
			: [];
		if (searchTerm) {
			const search = searchTerm.toUpperCase().trim();
			filteredEvents = filteredEvents.filter(({ label }) =>
				label.toUpperCase().includes(search)
			);
		}

		return { ...state, filteredEvents };
	},
	properties: {
		/**
		 * The currently selected data broker instance being configured
		 * @type {object}
		 * @example { name: 'Data broker 1', id: '123', properties: [], type: 'GraphQL' }
		 */
		selectedDataBroker: {
			default: null
		},
		/**
		 * Whether to render an error
		 * @type {boolean}
		 */
		error: {
			default: false
		},
		/**
		 * Whether to disable the component
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * The array of properties for the currently selected data broker instance
		 * @type {array}
		 */
		currentSelectedProperties: {
			default: []
		},
		/**
		 * whether the currently selected data broker instance is an ancestor broker
		 * @type {boolean}
		 */
		selectedDataBrokerIsAncestor: {
			default: false
		},

		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * A map of data broker instance IDs to their instance names
		 */
		dataBrokerInstanceNameMap: {
			schema: { type: 'object' },
			default: {}
		},
		eventHandlers: {
			default: []
		},
		eventMappings: {
			default: []
		},
		eventMappingHandlers: {
			default: {}
		},
		appConfig: {
			default: null
		},
		interfaceConfigurationComponent: {
			default: null
		},
		clientTransformScriptTemplate: {
			default: null
		},
		output: {
			default: {}
		}
	},
	actionHandlers: {
		['SN_UIB_DATA_BROKER_EDITOR#ELEMENT_INSERTED']: ({
			action,
			updateState
		}) => {
			action.stopPropagation();
			updateState({ container: action.payload });
		},
		['NOW_TABS#SELECTED_ITEM_SET']: ({ action, updateState, state }) => {
			action.stopPropagation();
			const filteredEvents = get(
				state,
				'properties.selectedDataBroker.dispatchedEvents',
				[]
			);
			batchShorthandUpdateState(
				{
					selectedTab: action.payload.value,
					filteredEvents: filteredEvents.map(
						(event, idx) => (event.expanded = idx === 0)
					)
				},
				updateState
			);
		},
		['NOW_INPUT#INPUT']: debounce(({ action, updateState }) => {
			action.stopPropagation();
			const { context, fieldValue: searchTerm } = action.payload;
			context === 'eventSearch' && updateState({ searchTerm });
		}, 250),
		['NOW_SELECT#SELECTED_ITEM_SET']: dataBrokerInstanceEvaluationModeChanged,
		[PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED]: dataBrokerInstancePropertyUpdated,
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: dataBrokerPropertyBindingToggled,
		[REFERENCE_PROPERTY_VALUE_CHANGED]: dataBrokerReferencePropertySearched,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: dataBrokerEditorComponentPropertyChanged,
		[FIELD_LIST_PROPERTY_TABLE_SELECTED]: dataBrokerFieldPropertyTableSelected,
		[FIELD_PROPERTY_TABLE_SELECTED]: dataBrokerFieldPropertyTableSelected,
		[EVENT_MAPPING_CONFIG_MAPPING_ADDED]: dataBrokerEventMappingAdded,
		[EVENT_MAPPING_CONFIG_HANDLER_ADDED]: dataBrokerEventMappingHandlerAdded,
		[EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED]: dataBrokerEventMappingHandlerDeleteRequested,
		[EVENT_MAPPING_CONFIG_HANDLER_DELETED]: dataBrokerEventMappingHandlerDeleted,
		[EVENT_MAPPING_HANDLER_CHANGED]: dataBrokerEventMappingHandlerChanged,
		[EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED]: dataBrokerEventMappingHandlerReferencePropertySearched,
		[EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED]: ({ action, dispatch }) => {
			dispatch(
				SN_UIB_DATA_BROKER_EDITOR_EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED,
				action.payload
			);
		}
	}
});
