import { t } from 'sn-translate';
import { individualFields } from './config';

const NoOptions = () => {
	return (
		<div className="sn-uib-style-configuration-panel --empty-state">
			<figure>
				<div className="icon-container">
					<now-icon icon="sliders-fill" size="lg" />
				</div>
				<figcaption>
					<span>{t('No style options.')}</span>
					<span>{t('Select a component to view style options.')}</span>
				</figcaption>
			</figure>
		</div>
	);
};
const SlotStyleConfigOptions = (props) => {
	const {
		slotStyles,
		selectedSlotName,
		selectedSlotParent,
		error = false,
		disabled = false
	} = props;

	return (
		<div className="sn-uib-style-configuration-pane">
			<section className="sn-uib-style-individual-properties">
				{individualFields.map((field) => {
					const { property, component, props: fieldProps, valueKey } = field;
					return !field.showIf || field.showIf(slotStyles)
						? component({
								...fieldProps,
								[valueKey]: slotStyles[property] || '',
								appendToPayload: { property, valueKey },
								disabled
						  })
						: null;
				})}
			</section>
			<section className="sn-uib-style-properties-editor">
				{error ? <sn-uib-error message={t('Style issue.')} /> : null}
				<sn-uib-css-input
					key={`${selectedSlotParent}-${selectedSlotName}`}
					label={t('CSS styles')}
					name={'style-properties-editor'}
					element-id={selectedSlotParent}
					css-obj={slotStyles}
					disabled={disabled}
				/>
			</section>
		</div>
	);
};
const StyleConfigOptions = (props) => {
	const {
		element,
		elementStyles,
		elementLayout,
		layoutTemplates,
		standardTemplates,
		customizedTemplates,
		error = false,
		disabled = false,
		lastAppliedTemplateId
	} = props;
	const { isLayoutModified } = elementLayout;
	return (
		<div className="sn-uib-style-configuration-pane">
			{elementLayout.type === 'container' ? (
				<div className="sn-uib-element-layout-configuration-section">
					<sn-uib-layout-template-panel
						templateType={'layout'}
						elementId={element.id}
						layoutTemplates={layoutTemplates}
						standardTemplates={standardTemplates}
						customizedTemplates={customizedTemplates}
						disabled={disabled}
						lastAppliedTemplateId={lastAppliedTemplateId}
						isLayoutModified={isLayoutModified}
					/>
				</div>
			) : null}
			<section className="sn-uib-style-properties-editor">
				{error ? <sn-uib-error message={t('Style issue.')} /> : null}
				<sn-uib-css-input
					key={element.id}
					label={t('CSS styles')}
					name={'style-properties-editor'}
					element-id={element.id}
					css-obj={elementStyles}
					disabled={disabled}
				/>
			</section>
		</div>
	);
};

export { NoOptions, StyleConfigOptions, SlotStyleConfigOptions };
