export const SN_UIB_CONTENT_TREE_PICKER_ITEM_SELECTED =
	'SN_UIB_CONTENT_TREE_PICKER#ITEM_SELECTED';
export const SN_UIB_CONTENT_TREE_PICKER_TREE_ITEM_CLICKED =
	'SN_UIB_CONTENT_TREE_PICKER#TREE_ITEM_CLICKED';

export const SN_UIB_CONTENT_TREE_PICKER_SECONDARY_ACTION_CLICKED =
	'SN_UIB_CONTENT_TREE_PICKER#SECONDARY_ACTION_CLICKED';

export const APPLY_BUTTON = 'APPLY_BUTTON';
export const OPEN_POPOVER_BUTTON = 'OPEN_POPOVER_BUTTON';
export const SECONDARY_ACTION_CLICKED = 'SECONDARY_ACTION_CLICKED';
export const NUMBER_ITEMS_TO_DISPLAY = 10;

import {
	filterAndHideItems,
	getExpandedItems,
	getSelectedItemPath,
	findChildrenPath
} from './utils';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export function componentPropertyChangedHandler({ state, updateState }) {
	const { items, search } = state.properties;

	const filteredItems = filterAndHideItems(
		items,
		search,
		NUMBER_ITEMS_TO_DISPLAY
	);
	const expandedItems = getExpandedItems(filteredItems);

	batchShorthandUpdateState(
		{
			filteredItems,
			expandedItems
		},
		updateState
	);
}

export function nowButtonClickedHandler({
	state,
	action,
	dispatch,
	updateState
}) {
	action.stopPropagation();

	const { actionName } = action.payload;

	if (actionName === APPLY_BUTTON) {
		const {
			selectedItem,
			properties: { currentItem }
		} = state;

		const currentItemId = currentItem && currentItem.id;
		const selectedItemId = selectedItem && selectedItem.id;

		batchShorthandUpdateState(
			{
				popoverOpened: false,
				search: '',
				selectedItem: undefined
			},
			updateState
		);

		if (selectedItemId && currentItemId !== selectedItemId) {
			dispatch(SN_UIB_CONTENT_TREE_PICKER_ITEM_SELECTED, selectedItem);
		}
	} else if (actionName === OPEN_POPOVER_BUTTON) {
		const { popoverOpened } = state;

		updateState({
			popoverOpened: !popoverOpened
		});
	} else if (actionName === SECONDARY_ACTION_CLICKED) {
		updateState({
			popoverOpened: false
		});
		dispatch(SN_UIB_CONTENT_TREE_PICKER_SECONDARY_ACTION_CLICKED);
	}
}

export function nowContentTreeClickedHandler({
	state,
	updateState,
	action,
	dispatch
}) {
	action.stopPropagation();

	const { items, hideApplyButton } = state.properties;

	const { item } = action.payload;
	const selectedItemPath = getSelectedItemPath(items, item);
	if (item && item.id) {
		dispatch(SN_UIB_CONTENT_TREE_PICKER_TREE_ITEM_CLICKED, item);
		if (hideApplyButton) {
			batchShorthandUpdateState(
				{
					popoverOpened: false,
					search: '',
					selectedItem: undefined
				},
				updateState
			);
		}
	}
	batchShorthandUpdateState(
		{
			selectedItem: item,
			selectedItemPath
		},
		updateState
	);
}

export function nowInputInputHandler({ updateState, state, action }) {
	action.stopPropagation();

	const { items } = state.properties;
	const { fieldValue: search } = action.payload;
	const filteredItems = filterAndHideItems(
		items,
		search,
		NUMBER_ITEMS_TO_DISPLAY
	);
	const expandedItems = getExpandedItems(filteredItems, search);

	batchShorthandUpdateState(
		{
			search,
			filteredItems,
			expandedItems
		},
		updateState
	);
}

export function nowPopoverOpenedSetHandler({ updateState, action, state }) {
	action.stopPropagation();

	const { disabled, currentItem, items } = state.properties;
	if (disabled) {
		return;
	}

	const { value } = action.payload;
	const selectedItemPath = getSelectedItemPath(items, currentItem);

	batchShorthandUpdateState(
		{
			popoverOpened: value,
			selectedItem: currentItem,
			selectedItemPath
		},
		updateState
	);
}

export function nowContentTreeExpandedItemsSetHandler({ action, updateState }) {
	action.stopPropagation();

	updateState({
		expandedItems: action.payload.value
	});
}

export function nowContentTreeLoadingRequestedHandler({
	state,
	action,
	updateState
}) {
	action.stopPropagation();

	const { filteredItems } = state;
	const { item, path } = action.payload;

	const childrenPath = findChildrenPath(filteredItems, path);
	if (childrenPath) {
		const children = item.totalChildren.slice(
			0,
			item.children.length + NUMBER_ITEMS_TO_DISPLAY
		);

		updateState({
			path: `filteredItems${childrenPath}`,
			operation: 'set',
			value: children
		});
	}
}
