import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from '../sn-uib-page-management-popover/sn-uib-page-management-popover.scss';

export const CREATE_DB_POPOVER_TYPE_SELECTED =
	'SN_UIB_CREATE_DB_POPOVER#TYPE_SELECTED';
const dataBrokerTypes = [
	{ label: t('GraphQL'), id: 'graphql' },
	{ label: t('Transform'), id: 'transform' },
	{ label: t('Client state'), id: 'proxy' },
	{ label: t('Composite'), id: 'composite' },
	{ label: t('REST'), id: 'rest' }
];

createCustomElement('sn-uib-create-db-popover', {
	view: (state, { dispatch }) => (
		<div className="container" style={{ width: '80px' }}>
			{dataBrokerTypes.map(({ label, id }) => (
				<div
					// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
					tabIndex={0}
					className="button"
					on-click={() =>
						dispatch(CREATE_DB_POPOVER_TYPE_SELECTED, { selectedType: id })
					}
					on-keydown={(event) => {
						if (event.key === 'Enter') {
							dispatch(CREATE_DB_POPOVER_TYPE_SELECTED, { selectedType: id });
						}
					}}>
					{label}
				</div>
			))}
		</div>
	),
	styles
});
