import '../sn-uib-properties-editor-panel';
import get from 'lodash/get';

export default function PropertyFormView({
	id: pagePropertyId,
	name,
	description,
	value,
	fieldType = 'string',
	bindings,
	typeMetadata,
	dataBindingEnabled,
	disabled,
	label
}) {
	const isStatic =
		value && typeof value === 'string' && value.startsWith('@') ? false : true;

	const choices = get(typeMetadata, 'choices', []).map((choice) => ({
		label: choice.label,
		id: choice.value
	}));

	const properties = [
		{
			name,
			label,
			value: value,
			id: `${pagePropertyId}-value`,
			type: fieldType || 'string',
			isStatic,
			description,
			disabled,
			dispatchOnBlur: true,
			translatable: get(typeMetadata, 'translatable'),
			choices
		}
	];

	return (
		<sn-uib-properties-editor-panel
			properties={properties}
			dataBindingEnabled={dataBindingEnabled}
			append-to-payload={{ context: 'page-property-value' }}
			bindings={bindings}
			scriptingEnabled={false}
			defaulted-field-types={['condition_string', 'table_name', 'reference']}
		/>
	);
}
