export const config = {
	name: 'sn-uib-field-list-property-input',
	properties: {
		/**
		 * The label to be displayed above the field picker.
		 * @type {string}
		 */
		label: {
			default: ''
		},
		/**
		 * The currently selected table.
		 * @type {string}
		 */
		table: {
			default: null
		},
		/**
		 * The field results passed in from the data provider, associated with a table.
		 * @type {object}
		 * @example {incident: [{id: 'name', label: 'Name'}, {id: 'sys_updated_on', label: 'Updated On'}]}
		 */
		tableFieldValues: {
			default: null
		},
		/**
		 * The items to be passed in to the multiselect field picker, after checking that
		 * the field values correspond to the current property's table reference.
		 * @type {object[]}
		 * @example [{value: 'name', text: 'Name'}, {value: 'sys_updated_on', text: 'Updated On'}]
		 */
		fields: {
			computed({ properties: { table, tableFieldValues } }) {
				if (!table || !tableFieldValues) {
					return [];
				} else {
					return tableFieldValues[table] instanceof Array
						? tableFieldValues[table]
						: [];
				}
			}
		},
		/**
		 * The comma-delimited string of value(s) assigned to the field property.
		 * @type {string}
		 */
		value: {
			default: null
		},
		/**
		 * The selected items to be passed in to the multiselect field picker.
		 * @type {object[]}
		 * @example [{value: 'name', text: 'Name'}]
		 */
		selectedFields: {
			computed({ properties: { value, fields } }) {
				const vals = value && typeof value === 'string' ? value.split(',') : [];

				return vals.reduce((acc, val) => {
					const matchingField =
						fields.find((field) => field.id === val) || null;
					if (matchingField) {
						return [...acc, matchingField];
					} else {
						return [...acc];
					}
				}, []);
			}
		},
		/**
		 * Whether the input should be disabled
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * The ID of the property
		 * @type {string}
		 */
		id: { default: '' },
		/**
		 * Whether dynamic configuration is enabled.
		 * @type {boolean}
		 */
		dataBindingEnabled: {
			default: true
		},
		/**
		 * the value to default to when the property is in an empty state, eg. input has ""
		 * @type {*}
		 */
		emptyStateBehavior: {
			default: { value: '' }
		},
		/**
		 * Whether the value is scripted
		 */
		isScripted: {
			default: false
		},
		/**
		 * Template for client transform scripts
		 */
		scriptTemplate: {
			default: ''
		},
		/**
		 * Whether client transform script is enabled.
		 * @type {boolean}
		 */
		scriptingEnabled: {
			default: true
		},
		/**
		 * A short description of what the property does or how it is used
		 * @type {string}
		 */
		description: {},
		/**
		 * The tagname of the current selected component
		 */
		selectedComponentTag: {
			default: ''
		},
		/**
		 * The variant used for the dropdown, either ordered or unordered
		 * @type {string}
		 */
		variant: {
			default: 'unordered'
		}
	}
};
