import strategyManager from './strategyManager';
import { onNextFrame } from './Transition';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
const { focusTargetId } = DevxKeyFocusBehavior;

function init(dispatch, id) {
	strategyManager.set(`awaitingFocus`, 'func', () => {
		/**
		 * Even with the structure of this strategy, which works on Chrome/FF Safari was giving me issues with focusing successfully.
		 * In searching for solutions I came upon this explanation.
		 * https://medium.com/@owencm/one-weird-trick-to-performant-touch-response-animations-with-react-9fe4a0838116
		 * https://medium.com/@owencm/well-there-are-definitely-issues-here-ordered-least-significant-to-most-1f0931b03024
		 * https://bugs.chromium.org/p/chromium/issues/detail?id=675795
		 *
		 * The short summary seems to be that raf is sometimes called on the previous frame. Nesting it allows it to start on the next frame.
		 * This may or may not be a bug. It sort of seems like Chrome changed its behavior but the actual whatwg spec is unclear on the exact behavior.
		 * So please, if you are coming upon this in the future and want to delete it. Make sure safari does not regress,
		 */
		requestAnimationFrame(() => {
			requestAnimationFrame(() => {
				focusTargetId(dispatch, id);
			});
		});
	});
	strategyManager.set(`awaitingFocus`, 'id', id);

	const idSnapshot = strategyManager.get(`awaitingFocus`, 'id');
	fire(idSnapshot);
}
function awaiting(id) {
	const stored = strategyManager.get(`awaitingFocus`, 'id');
	return stored && stored === id;
}
function fire(idSnapshot) {
	return new Promise((resolve) => {
		const inflight = strategyManager.get(`awaitingFocus`, 'call');
		if (inflight) {
			return resolve(false);
		}
		const call = onNextFrame(() => {
			if (idSnapshot !== strategyManager.get(`awaitingFocus`, 'id')) {
				return resolve(false);
			}
			strategyManager.get(`awaitingFocus`, 'func')();
			deactivate();
			return resolve(true);
		});
		strategyManager.set(`awaitingFocus`, 'call', call);
		call.then(() => {
			strategyManager.set(`awaitingFocus`, 'call', null);
		});
	});
}

function deactivate() {
	strategyManager.set(`awaitingFocus`, 'id', null);

	strategyManager.set(`awaitingFocus`, 'func', () => {});
}

function isActive() {
	return !!strategyManager.get(`awaitingFocus`, 'id');
}

const focusStrategy = {
	init,
	fire,
	deactivate,
	isActive,
	awaiting
};

export default focusStrategy;
