import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-typeahead';
import '../../sn-uib-content-tree-picker';

// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const UNDEFINED_LABEL = '  ';

import {
	SN_UIB_PAGE_HEADER_APPLICATION_SELECTED,
	SN_UIB_PAGE_HEADER_DOMAIN_SELECTED
} from '../actions';

const applicationScopePayload = {
	headerAction: SN_UIB_PAGE_HEADER_APPLICATION_SELECTED
};
const domainScopePayload = { headerAction: SN_UIB_PAGE_HEADER_DOMAIN_SELECTED };

export default function SnUIBPageHeaderActions({
	mediaSize,
	appScope,
	pageStatus,
	appType,
	headerActions,
	displayScopePicker,
	applications,
	userScope,
	isPagesLoading,
	domainTree,
	currentDomain,
	displayDomainPicker,
	displayExpandDomain,
	isDomainExpanded,
	classes
}) {
	const { isSM, isMD } = mediaSize;

	const displayExpandDomainScope =
		displayExpandDomain && currentDomain && currentDomain.id === 'global';

	const currentDomainItem = currentDomain
		? {
				id: currentDomain.id,
				label:
					currentDomain && currentDomain.label
						? currentDomain.label.split('/').pop()
						: ''
		  }
		: {};

	return (
		<div
			class={{
				'sn-uib-page-header--actions': true,
				...classes
			}}>
			{appType === 'workspace' ? (
				<Fragment>
					<span className="status">
						<span className="status-label">
							{isSM || isMD ? null : `${t('Status')}:`}
						</span>
						<now-highlighted-value
							className="status-value"
							show-icon
							label={pageStatus || UNDEFINED_LABEL}
							status={pageStatus === 'Active' ? 'positive' : 'low'}
						/>
					</span>
					<span className="scope">
						<span className="scope-label">
							{isSM || isMD ? null : `${t('Application')}:`}
						</span>
						<now-highlighted-value
							className="scope-value"
							show-icon
							label={appScope || UNDEFINED_LABEL}
							status="moderate"
						/>
					</span>
				</Fragment>
			) : null}
			{displayDomainPicker ? (
				<sn-uib-content-tree-picker
					className="content-tree-picker-domain"
					append-to-payload={domainScopePayload}
					items={domainTree}
					currentItem={currentDomainItem}
					disabled={isPagesLoading}
					icon="building-outline"
					searchLabel={t('Select a domain')}
					searchPlaceholder={t('Search domain')}
					displaySecondaryAction={displayExpandDomainScope}
					secondaryActionLabel={
						isDomainExpanded
							? t('Collapse domain scope')
							: t('Expand domain scope')
					}
				/>
			) : null}
			{displayScopePicker ? (
				<sn-uib-content-tree-picker
					className="content-tree-picker-scope"
					append-to-payload={applicationScopePayload}
					items={applications}
					currentItem={userScope}
					disabled={isPagesLoading}
					icon="tree-view-long-outline"
					searchLabel={t('Select an application scope')}
					searchPlaceholder={t('Search application scope')}
				/>
			) : null}
			{headerActions.map((action) => (
				<now-button size="sm" {...action} />
			))}
		</div>
	);
}
