import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-page-style-configuration-pane.scss';
import { filterTemplates } from '../helpers/filterTemplates';

const view = (state) => {
	const {
		standardTemplates,
		customizedTemplates,
		properties: { disabled, lastAppliedTemplateId, isPageLayoutModified }
	} = state;

	return (
		<div className="layout-tab-section">
			<sn-uib-layout-template-panel
				className="layout-tab-item"
				templateType={'page'}
				standardTemplates={standardTemplates}
				customizedTemplates={customizedTemplates}
				disabled={disabled}
				lastAppliedTemplateId={lastAppliedTemplateId}
				isLayoutModified={isPageLayoutModified}
			/>
		</div>
	);
};

export default createCustomElement('sn-uib-page-style-configuration-pane', {
	view,
	properties: {
		pageTemplates: {
			default: []
		},
		lastAppliedTemplateId: {
			default: null
		},
		disabled: {
			default: false
		},
		isPageLayoutModified: {
			default: false
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	initialState: {
		standardTemplates: {
			default: []
		},
		customizedTemplates: {
			default: []
		}
	},
	transformState: (state) => {
		const { pageTemplates } = state.properties;
		const { standardTemplates, customizedTemplates } = filterTemplates(
			pageTemplates
		);
		return {
			...state,
			standardTemplates,
			customizedTemplates
		};
	},
	styles
});
