import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import styles from './_sn-uib-layout-drop-branch.scss';
import {
	layoutOptions,
	handleInsert,
	checkActive,
	handleUpdate,
	keyboardNavBehavior
} from '../behaviors/droppable/utils';
import { handleEscapeKeyPressed } from '../helpers/keyboardNavShared';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { t } from 'sn-translate';
import SnUIBLayoutTooltip from '../partials/sn-uib-layout-tooltip';

const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	createFocusRef,
	focusTargetId
} = DevxKeyFocusBehavior;

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const generateDropzoneRefs = (properties) => {
	const { id, keymaps } = properties;

	const { previousSibling, nextSibling, parent } = keymaps || {};
	const topRef = createFocusRef(`${id}_TOP`, {
		ArrowDown: `${id}_BOTTOM`,
		Escape: parent,
		Space: `${nextSibling}`,
		Tab: `${nextSibling}`,
		ShiftTab: `${previousSibling}`
	});
	const bottomRef = createFocusRef(`${id}_BOTTOM`, {
		ArrowUp: `${id}_TOP`,
		Escape: parent,
		Space: `${nextSibling}`,
		Tab: `${nextSibling}`,
		ShiftTab: `${previousSibling}`
	});
	const leftRef = createFocusRef(`${id}_LEFT`, {
		ArrowRight: `${id}_RIGHT`,
		Escape: parent,
		Space: `${nextSibling}`,
		Tab: `${nextSibling}`,
		ShiftTab: `${previousSibling}`
	});
	const rightRef = createFocusRef(`${id}_RIGHT`, {
		ArrowLeft: `${id}_LEFT`,
		Escape: parent,
		Space: `${nextSibling}`,
		Tab: `${nextSibling}`,
		ShiftTab: `${previousSibling}`
	});

	return {
		topRef,
		bottomRef,
		leftRef,
		rightRef
	};
};
/* prettier-ignore */
const tooltipContent = t('Press enter to drop component, Press Space to navigate within the component. Use arrow keys to navigate dropzones.');

const getClassesWrapper = ({ activeDirection, getActive }) => (
	defaultName = '',
	side
) => {
	return [defaultName, getActive(side, activeDirection, 'active')].join(' ');
};

const blankRender = (state) => {
	const { showZones } = state.properties;
	const dropzoneClasses = ['dropzone', 'dropzone--blank'];
	if (showZones) {
		dropzoneClasses.push('show-zones');
	}

	return (
		<div className={`${dropzoneClasses.join(' ')}`}>
			<div className={dropzoneClasses.join(' ')}>
				<div className="visible" />
			</div>
			<slot />
			<div className={dropzoneClasses.join(' ')} />
		</div>
	);
};

const rowRender = (state, dispatch) => {
	const { topRef, bottomRef } = state;
	const {
		isActive,
		activeDirection,
		id,
		additionalData,
		entrypoint,
		keyboardDrag,
		callback,
		showZones
	} = state.properties;
	const dropzoneClasses = ['dropzone', 'dropzone--row'];
	const getActive = checkActive(id, isActive);
	const getClasses = getClassesWrapper({ activeDirection, getActive });

	if (showZones) {
		dropzoneClasses.push('show-zones');
	}
	const isShowingTopTooltip =
		isActive && activeDirection === 'TOP' && keyboardDrag;
	const isShowingBottomTooltip =
		isActive && activeDirection === 'BOTTOM' && keyboardDrag;
	return (
		<div
			className={`${dropzoneClasses.join(' ')}`}
			ref={createFocusRef(id)}
			tabindex="-1"
			on-focus={() => {
				focusTargetId(dispatch, `${id}_TOP`);
			}}>
			<div
				className={getClasses('top', 'TOP')}
				hook-update={(newNode) => {
					handleUpdate({
						el: newNode.elm,
						id,
						adjacentLayoutElementId: id,
						tag: `${id}__TOP`,
						callback,
						additionalData,

						dispatch,
						direction: 'TOP',
						options: layoutOptions
					});
				}}
				hook-insert={({ elm }) => {
					handleInsert({
						el: elm,
						id,
						callback,
						additionalData,

						adjacentLayoutElementId: id,
						tag: `${id}__TOP`,
						dispatch,
						direction: 'TOP',
						options: layoutOptions
					});
					if (entrypoint) {
						dispatch('UIB_STAGE#DND_ENTRYPOINT_CREATED', { id: `${id}_TOP` });
					}
				}}>
				<div
					className="visible"
					ref={topRef}
					aria-describedby="drop-branch-top-tooltip"
					role="button"
					title="dropzone"
					{...keyboardNavBehavior({
						id,
						dispatch,
						direction: 'TOP',
						...additionalData,
						cancelKeyFn: (e) => {
							handleEscapeKeyPressed(e, dispatch, entrypoint);
						}
					})}
				/>
			</div>
			<slot />
			<div
				className={getClasses('bottom', 'BOTTOM')}
				hook-update={(newNode) => {
					handleUpdate({
						el: newNode.elm,
						id,
						adjacentLayoutElementId: id,
						callback,
						additionalData,

						dispatch,
						direction: 'BOTTOM',
						tag: `${id}__BOTTOM`,
						options: layoutOptions
					});
				}}
				hook-insert={({ elm }) => {
					handleInsert({
						el: elm,
						id,
						additionalData,

						adjacentLayoutElementId: id,
						callback,
						dispatch,
						direction: 'BOTTOM',
						tag: `${id}__BOTTOM`,
						options: layoutOptions
					});
				}}>
				<div
					className="visible"
					ref={bottomRef}
					aria-describedby="drop-branch-bottom-tooltip"
					role="button"
					title="dropzone"
					{...keyboardNavBehavior({
						id,
						dispatch,
						direction: 'BOTTOM',
						...additionalData,
						cancelKeyFn: (e) => {
							handleEscapeKeyPressed(e, dispatch, entrypoint);
						}
					})}
				/>
			</div>
			<SnUIBLayoutTooltip
				active={isShowingBottomTooltip}
				id="drop-branch-bottom-tooltip"
				content={tooltipContent}
				ref={bottomRef}
			/>
			<SnUIBLayoutTooltip
				active={isShowingTopTooltip}
				id="drop-branch-top-tooltip"
				content={tooltipContent}
				ref={topRef}
			/>
		</div>
	);
};

const columnRender = (state, { dispatch }) => {
	const { rightRef, leftRef } = state;

	const {
		isActive,
		activeDirection,
		callback,
		additionalData,
		entrypoint,
		keyboardDrag,
		showZones,
		id
	} = state.properties;

	const dropzoneClasses = ['dropzone', 'dropzone--column'];
	const getActive = checkActive(id, isActive);
	if (showZones) {
		dropzoneClasses.push('show-zones');
	}
	const getClasses = getClassesWrapper({ activeDirection, getActive });

	const isShowingLeftTooltip =
		isActive && activeDirection === 'LEFT' && keyboardDrag;
	const isShowingRightTooltip =
		isActive && activeDirection === 'RIGHT' && keyboardDrag;

	return (
		<div
			className={`container ${dropzoneClasses.join(' ')}`}
			ref={createFocusRef(id)}
			tabindex="-1"
			on-focus={() => {
				focusTargetId(dispatch, `${id}_LEFT`);
			}}>
			<div
				className={getClasses('left', 'LEFT')}
				hook-update={(newNode) => {
					handleUpdate({
						el: newNode.elm,
						id,
						adjacentLayoutElementId: id,
						dispatch,
						additionalData,
						callback,
						direction: 'LEFT',
						tag: `${id}__LEFT`,
						options: layoutOptions
					});
				}}
				hook-insert={({ elm }) => {
					handleInsert({
						el: elm,
						id,
						adjacentLayoutElementId: id,
						dispatch,
						callback,
						additionalData,

						direction: 'LEFT',
						tag: `${id}__LEFT`,
						options: layoutOptions
					});
					if (entrypoint) {
						dispatch('UIB_STAGE#DND_ENTRYPOINT_CREATED', { id: `${id}_LEFT` });
					}
				}}>
				<div
					className="visible"
					ref={leftRef}
					role="button"
					title="dropzone"
					aria-describedby="drop-branch-left-tooltip"
					aria-hidden="true"
					{...keyboardNavBehavior({
						id,
						dispatch,
						direction: 'LEFT',
						...additionalData,
						cancelKeyFn: (e) => {
							handleEscapeKeyPressed(e, dispatch, entrypoint);
						}
					})}
				/>
			</div>
			<slot />
			<div
				className={getClasses('right', 'RIGHT')}
				hook-update={(newNode) => {
					handleUpdate({
						el: newNode.elm,
						adjacentLayoutElementId: id,
						id,
						callback,
						additionalData,

						dispatch,
						direction: 'RIGHT',
						tag: `${id}__RIGHT`,

						options: layoutOptions
					});
				}}
				hook-insert={({ elm }) => {
					handleInsert({
						el: elm,
						id,
						callback,
						additionalData,

						adjacentLayoutElementId: id,
						dispatch,
						direction: 'RIGHT',
						tag: `${id}__RIGHT`,
						options: layoutOptions
					});
				}}>
				<div
					className="visible"
					ref={rightRef}
					aria-describedby="drop-branch-right-tooltip"
					role="button"
					title="dropzone"
					aria-hidden="true"
					{...keyboardNavBehavior({
						id,
						dispatch,
						direction: 'RIGHT',
						...additionalData,
						cancelKeyFn: (e) => {
							handleEscapeKeyPressed(e, dispatch, entrypoint);
						}
					})}
				/>
			</div>
			<SnUIBLayoutTooltip
				active={isShowingLeftTooltip}
				id="drop-branch-left-tooltip"
				content={tooltipContent}
				ref={leftRef}
			/>
			<SnUIBLayoutTooltip
				active={isShowingRightTooltip}
				id="drop-branch-right-tooltip"
				content={tooltipContent}
				ref={rightRef}
			/>
		</div>
	);
};

const view = (state, dispatch) => {
	const { type } = state.properties;
	switch (type) {
		case 'col':
			return columnRender(state, dispatch);
		case 'row':
			return rowRender(state, dispatch);
		default:
			return blankRender(state, dispatch);
	}
};

export default createCustomElement('sn-uib-layout-drop-branch', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		topRef: {},
		bottomRef: {},
		leftRef: {},
		rightRef: {}
	},
	properties: {
		id: {
			default: ''
		},
		callback: {
			default: () => {}
		},
		isActive: {
			default: false
		},
		additionalData: {
			default: {}
		},
		showZones: {
			default: false
		},
		keyboardDrag: {
			default: false
		},
		entrypoint: {
			default: false
		},
		keymaps: {
			default: {}
		},

		activeDirection: {
			default: ''
		},
		type: {
			default: 'blank'
		}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({ properties, updateState }) => {
			const dropzoneRefs = generateDropzoneRefs(properties);
			updateState(dropzoneRefs);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ properties, updateState }) => {
			const dropzoneRefs = generateDropzoneRefs(properties);
			updateState(dropzoneRefs);
		}
	},
	behaviors: [keyFocusBehavior],
	styles,
	onConnect(host, dispatch) {
		setTimeout(() => {
			dispatch('PROPERTIES_SET', { showZones: true });
		}, 10);
	}
});
