import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-uib-client-state-preview-pane.scss';
import EmptyState from '../partials/empty-state'; //eslint-disable-line no-unused-vars
import '@servicenow/now-code-editor';

const view = ({ properties: { stringified } }) => {
	if (!stringified) {
		return <EmptyState description={t('No preview available.')} />;
	}

	return (
		<now-code-editor
			language="json"
			read-only
			hide-toolbar
			disable-minimap
			disable-full-screen
			disable-debugging
			disable-context-menu
			content={stringified}
			height="100%"
			width="100%"
		/>
	);
};

export default createCustomElement('sn-uib-client-state-preview-pane', {
	view,
	styles,
	properties: {
		preview: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		stringified: {
			computed: ({ properties: { preview } }) => {
				let str = '';
				if (typeof preview === 'object') {
					try {
						str = JSON.stringify(preview, null, 2);
					} catch (e) {
						(() => null)();
					}
				}
				return str;
			}
		}
	}
});
