import { createCustomElement } from '@servicenow/ui-core';
import dragHandleSrc from '../sn-uib-element-controls/assets/drag_grip_dark.svg';
import styles from './sn-uib-draggable-list-item.scss';
import { t } from 'sn-translate';
import '@servicenow/now-button';

const supportedButtonTypes = {
	SETTINGS: 'settings',
	OPEN: 'open',
	TRASH: 'trash',
	CLOSE: 'close'
};

const supportedIcons = {
	[supportedButtonTypes.SETTINGS]: 'sliders-outline',
	[supportedButtonTypes.OPEN]: 'open-link-fill',
	[supportedButtonTypes.TRASH]: 'trash-outline',
	[supportedButtonTypes.CLOSE]: 'close-outline'
};

const view = ({ labelRef, properties }, { updateState }) => {
	const { actions, label, draggable } = properties;
	const setLabelRef = (elm) => setTimeout(() => updateState({ labelRef: elm }));
	return (
		<li className="sn-uib-draggable-list-item-container">
			{draggable ? (
				<section className="draggable-list-item-drag-grip">
					<img src={dragHandleSrc} alt="dragHandle" />
				</section>
			) : null}
			<section
				className="draggable-list-item-label"
				hook-insert={({ elm }) => setLabelRef(elm)}
				hook-destroy={() => setLabelRef(null)}>
				<now-tooltip content={label} targetRef={labelRef} />
				{label}
			</section>
			<section className="draggable-list-item-buttons">
				{actions.map(({ type, clickActionType, renderer, slot, disabled }) => {
					const actionButton = (
						<now-button-iconic
							className="item-icon"
							slot={slot || null}
							size="md"
							icon={supportedIcons[type]}
							bare={true}
							variant="tertiary"
							append-to-payload={{ clickActionType }}
							hide-padding={true}
							disabled={disabled}
						/>
					);
					return renderer ? renderer(actionButton) : actionButton;
				})}
			</section>
		</li>
	);
};

export default createCustomElement('sn-uib-draggable-list-item', {
	view,
	properties: {
		/**
		 * A collection of actions to be applied to this list item
		 * @example [{type: 'settings', slot: 'trigger', clickActionType: 'LIST_ITEM#CONFIG_CLICKED', renderer: (button) => <component />{button}</component>}]
		 */
		actions: {
			default: [],
			schema: { type: 'array' }
		},
		label: {
			default: '',
			schema: { type: 'string' }
		},
		draggable: {
			default: true,
			schema: {
				type: 'boolean'
			}
		}
	},
	initialState: {
		labelRef: null
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			const { clickActionType } = action.payload;
			dispatch(clickActionType);
		}
	},
	styles
});
