import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import debounce from 'lodash/debounce';
import styles from './sn-uib-page-events-configuration-pane.scss';
import { t } from 'sn-translate';
import '@servicenow/now-accordion';
import '@servicenow/now-button';
import '../sn-uib-draggable-list-item';

// eslint-disable-next-line no-unused-vars
import SearchBox from '../partials/search-box';
import {
	EVENT_MAPPING_CONFIG_MAPPING_ADDED,
	EVENT_MAPPING_CONFIG_HANDLER_ADDED,
	EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED,
	EVENT_MAPPING_CONFIG_HANDLER_DELETED
} from '../sn-uib-event-mapping-configuration';

import {
	EVENT_MAPPING_HANDLER_CHANGED,
	EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED
} from '../sn-uib-event-mapping-handler-config';

import {
	EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED,
	EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE,
	EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETE_REQUESTED
} from '../sn-uib-event-configuration-pane';

import { DISPATCHED_EVENTS_MODAL_FOOTER_ACTIONS } from '../sn-uib-dispatched-events-modal';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars

// Dispatched action types
const PAGE_EVENT_CONFIG_PANE_EVENT_MAPPING_ADDED =
	'PAGE_EVENT_CONFIG_PANE#EVENT_MAPPING_ADDED';
const PAGE_EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_ADDED =
	'PAGE_EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_ADDED';
const PAGE_EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETED =
	'PAGE_EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_DELETED';
const PAGE_EVENT_CONFIG_PANE_MACROPONENT_CONTRACT_EVENT_CREATED =
	'PAGE_EVENT_CONFIG_PANE#MACROPONENT_CONTRACT_EVENT_CREATED';
const PAGE_EVENT_CONFIG_PANE_MACROPONENT_CONTRACT_EVENT_EDITED =
	'PAGE_EVENT_CONFIG_PANE#MACROPONENT_CONTRACT_EVENT_EDITED';
const PAGE_EVENT_CONFIG_PANE_MACROPONENT_CONTRACT_EVENT_DELETE_REQUESTED =
	'PAGE_EVENT_CONFIG_PANE#MACROPONENT_CONTRACT_EVENT_DELETE_REQUESTED';
const PAGE_EVENT_CONFIG_PANE_HANDLER_VALUE_REFERENCE_SEARCHED =
	'PAGE_EVENT_CONFIG_PANE#HANDLER_VALUE_REFERENCE_PROPERTY_SEARCHED';

const PAGE_EVENT_CONFIG_PANE_MCP_CONTRACT_EVENT_EDIT_CLICKED =
	'PAGE_EVENT_CONFIG_PANE#EDIT_MACROPONENT_CONTRACT_EVENT_CLICKED';
const PAGE_EVENT_CONFIG_PANE_MCP_CONTRACT_EVENT_DELETE_CLICKED =
	'PAGE_EVENT_CONFIG_PANE#DELETE_MACROPONENT_CONTRACT_EVENT_CLICKED';

const renderEventMappingsSection = ({
	event = {},
	eventHandlers = [],
	eventMappings = [],
	properties = {},
	searchTerm = '',
	context = 'screen'
}) => {
	const {
		appConfig,
		bindings,
		disabled,
		isPageDefinitionEventsDisabled,
		eventMappingHandlers,
		interfaceConfigurationComponent
	} = properties;
	const mappingForCurrentEvent =
		eventMappings.find((mapping) => mapping.eventId === event.id) || {};

	return (
		<sn-uib-event-mapping-configuration
			key={event.id}
			appConfig={appConfig}
			bindings={bindings}
			context={context}
			disabled={
				context === 'screen' ? disabled : isPageDefinitionEventsDisabled
			}
			enableScripting={false}
			event={event}
			eventHandlers={eventHandlers}
			eventMapping={mappingForCurrentEvent}
			eventMappingHandlers={
				eventMappingHandlers[mappingForCurrentEvent.id] || []
			}
			interfaceConfigurationComponent={interfaceConfigurationComponent}
			searchTerm={searchTerm}
			slot="content"
			appendToPayload={{
				context,
				eventId: event.id,
				eventApiName: event.apiName,
				eventMappingId: mappingForCurrentEvent.id
			}}
		/>
	);
};

const renderEventsSection = ({
	context,
	properties,
	emptyStateMessage,
	events
}) => (
	<Fragment>
		<section className="event-crud-list-button-wrapper">
			<now-button-bare
				label={t('Add')}
				icon-start="plus-outline"
				size="sm"
				append-to-payload={{ context }}
				disabled={properties.isPageDefinitionEventsDisabled}
			/>
		</section>
		<ul className="event-crud-list">
			{events && events.length ? (
				events.map((event) => (
					<sn-uib-draggable-list-item
						key={event.id}
						label={event.label}
						append-to-payload={{ context, event }}
						actions={[
							{
								type: 'open',
								clickActionType: PAGE_EVENT_CONFIG_PANE_MCP_CONTRACT_EVENT_EDIT_CLICKED,
								disabled: properties.isPageDefinitionEventsDisabled
							},
							{
								type: 'trash',
								clickActionType: PAGE_EVENT_CONFIG_PANE_MCP_CONTRACT_EVENT_DELETE_CLICKED,
								disabled: properties.isPageDefinitionEventsDisabled
							}
						]}
						draggable={false}
					/>
				))
			) : (
				<div>{emptyStateMessage}</div>
			)}
		</ul>
	</Fragment>
);

const view = (state, { dispatch }) => {
	const {
		filteredPageEvents = [],
		filteredScreenEvents = [],
		openedSections,
		pageSearchTerm,
		properties,
		screenSearchTerm
	} = state;
	const {
		disabled,
		dispatchedEvents = [],
		eventHandlers = [],
		handledEvents = [],
		internalEventMappings = [],
		internalEventMappingsSourceEvents = [],
		screenEventMappings
	} = properties;
	const ancestorEventHandlers = eventHandlers.filter(
		(handler) => handler.ancestor === true
	);

	return (
		<div className="page-events-pane-container">
			<now-accordion headingLevel="3">
				<now-accordion-item
					header={{
						label: t('Page event mappings'),
						size: 'sm'
					}}
					expanded={!!openedSections.pageMappings}
					appendToPayload={{ pane: 'pageMappings' }}>
					<div slot="content">
						{internalEventMappingsSourceEvents.length > 0 ? (
							<Fragment>
								<div className="search-input">
									<SearchBox
										placeholder={t('Search')}
										value={pageSearchTerm}
										className="component-search"
										appendToPayload={{ context: 'page' }}
										dispatch={dispatch}
									/>
								</div>
								<section className="scrollable-events-section">
									{filteredPageEvents.map((event = {}) => {
										return renderEventMappingsSection({
											event,
											eventHandlers,
											eventMappings: internalEventMappings,
											properties: state.properties,
											searchTerm: pageSearchTerm,
											context: 'page'
										});
									})}
								</section>
							</Fragment>
						) : (
							<EmptyState
								size="sm"
								headerMessage={t('No events available.')}
								description={t('This page has no configurable events.')}
							/>
						)}
					</div>
				</now-accordion-item>
				<now-accordion-item
					header={{
						label: t('Variant event mappings'),
						size: 'sm'
					}}
					expanded={!!openedSections.screenMappings}
					appendToPayload={{ pane: 'screenMappings' }}>
					<div slot="content">
						{dispatchedEvents.length > 0 ? (
							<Fragment>
								<div className="search-input">
									<SearchBox
										placeholder={t('Search')}
										value={screenSearchTerm}
										className="component-search"
										appendToPayload={{ context: 'screen' }}
										dispatch={dispatch}
									/>
								</div>
								<div className="scrollable-events-section">
									{filteredScreenEvents.map((event = {}) => {
										return renderEventMappingsSection({
											event,
											eventHandlers: ancestorEventHandlers,
											eventMappings: screenEventMappings,
											properties: state.properties,
											searchTerm: screenSearchTerm,
											context: 'screen'
										});
									})}
								</div>
							</Fragment>
						) : (
							<EmptyState
								size="sm"
								headerMessage={t('No events available.')}
								description={t('This variant has no configurable events.')}
							/>
						)}
					</div>
				</now-accordion-item>
				<now-accordion-item
					id="events-list"
					header={{ label: t('Dispatched events'), size: 'sm' }}
					expanded={!!openedSections.dispatchedEvents}
					appendToPayload={{ pane: 'dispatchedEvents' }}>
					<div slot="content" className="events-list-wrapper">
						{renderEventsSection({
							context: 'dispatchedEvents',
							properties: state.properties,
							/* prettier-ignore */
							emptyStateMessage: t('There are no dispatched events for this variant'),
							events: dispatchedEvents
						})}
					</div>
				</now-accordion-item>
				<now-accordion-item
					id="events-list"
					header={{ label: t('Handled events'), size: 'sm' }}
					expanded={!!openedSections.handledEvents}
					appendToPayload={{ pane: 'handledEvents' }}>
					<div slot="content" className="events-list-wrapper">
						{renderEventsSection({
							context: 'handledEvents',
							properties: state.properties,
							/* prettier-ignore */
							emptyStateMessage: t('There are no handled events for this variant'),
							events: handledEvents
						})}
					</div>
				</now-accordion-item>
			</now-accordion>
		</div>
	);
};

export default createCustomElement('sn-uib-page-events-configuration-pane', {
	view,
	initialState: {
		openedSections: {
			pageMappings: true,
			screenMappings: true,
			dispatchedEvents: true,
			handledEvents: true
		},
		screenSearchTerm: '',
		filteredScreenEvents: [],
		pageSearchTerm: '',
		filteredPageEvents: []
	},
	transformState(state) {
		const {
			pageSearchTerm = '',
			screenSearchTerm = '',
			properties: {
				dispatchedEvents = [],
				internalEventMappingsSourceEvents = []
			}
		} = state;

		const filterEvents = (events, searchTerm) => {
			let filteredEvents = events || [];
			if (searchTerm) {
				const search = searchTerm.toUpperCase().trim();
				filteredEvents = filteredEvents.filter(({ label }) =>
					label.toUpperCase().includes(search)
				);
			}
			filteredEvents.forEach((event, idx) => (event.expanded = idx === 0));
			return filteredEvents;
		};

		const filteredPageEvents =
			filterEvents(internalEventMappingsSourceEvents, pageSearchTerm) || [];
		const filteredScreenEvents =
			filterEvents(dispatchedEvents, screenSearchTerm) || [];

		return { ...state, filteredPageEvents, filteredScreenEvents };
	},
	properties: {
		screenEventMappings: {
			schema: {
				type: 'array'
			},
			default: []
		},
		dispatchedEvents: {
			schema: {
				type: 'array'
			},
			default: []
		},
		disabled: {
			default: false
		},
		eventHandlers: {
			schema: {
				type: 'array'
			},
			default: []
		},
		eventMappingHandlers: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		interfaceConfigurationComponent: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		internalEventMappings: {
			default: []
		},
		internalEventMappingsSourceEvents: {
			default: []
		},
		handledEvents: {
			default: [],
			schema: { type: 'array' }
		},
		isPageDefinitionEventsDisabled: {
			default: false
		}
	},
	actionHandlers: {
		['NOW_INPUT#INPUT']: {
			effect: debounce(({ action, updateState }) => {
				const { context, fieldValue } = action.payload;
				if (context === 'page') {
					updateState({ pageSearchTerm: fieldValue });
				} else if (context === 'screen') {
					updateState({ screenSearchTerm: fieldValue });
				}
			}, 250),
			stopPropagation: true
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ action, dispatch, properties }) => {
			action.stopPropagation();
			const { interfaceConfigurationComponent, eventHandlers } = properties;
			dispatch(PAGE_EVENT_CONFIG_PANE_MACROPONENT_CONTRACT_EVENT_CREATED, {
				headerLabel: t('Create an event'),
				footerActions: DISPATCHED_EVENTS_MODAL_FOOTER_ACTIONS('create'),
				componentProps: {
					interfaceConfigurationComponent,
					eventHandlers,
					mode: 'create',
					context: action.payload.context
				}
			});
		},
		[EVENT_MAPPING_CONFIG_MAPPING_ADDED]: ({ action, dispatch }) => {
			dispatch(PAGE_EVENT_CONFIG_PANE_EVENT_MAPPING_ADDED, action.payload);
		},
		[EVENT_MAPPING_CONFIG_HANDLER_ADDED]: ({ action, dispatch }) => {
			dispatch(
				PAGE_EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_ADDED,
				action.payload
			);
		},
		[EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED]: ({ action, dispatch }) => {
			dispatch(
				EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETE_REQUESTED,
				action.payload
			);
		},
		[EVENT_MAPPING_CONFIG_HANDLER_DELETED]: ({ action, dispatch }) => {
			if (
				action.payload.context === 'page' ||
				action.payload.context === 'screen'
			) {
				dispatch(
					PAGE_EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETED,
					action.payload
				);
			}
		},
		[EVENT_MAPPING_HANDLER_CHANGED]: ({ action, dispatch }) => {
			if (action.payload.immediate) {
				dispatch(
					EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE,
					action.payload
				);
			} else {
				dispatch(
					EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED,
					action.payload
				);
			}
		},
		[EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			(action.payload.context === 'page' ||
				action.payload.context === 'screen') &&
				dispatch(
					PAGE_EVENT_CONFIG_PANE_HANDLER_VALUE_REFERENCE_SEARCHED,
					action.payload
				);
		},
		['NOW_ACCORDION_ITEM#EXPANDED_SET']: ({ action, state, updateState }) => {
			action.stopPropagation();
			updateState({
				openedSections: {
					...state.openedSections,
					[action.payload.pane]: !state.openedSections[action.payload.pane]
				}
			});
		},
		[PAGE_EVENT_CONFIG_PANE_MCP_CONTRACT_EVENT_EDIT_CLICKED]: ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			const { interfaceConfigurationComponent, eventHandlers } = properties;
			const {
				context,
				event: { eventName, id, label, payloadShape }
			} = action.payload;
			dispatch(PAGE_EVENT_CONFIG_PANE_MACROPONENT_CONTRACT_EVENT_EDITED, {
				headerLabel: t('Edit event'),
				footerActions: DISPATCHED_EVENTS_MODAL_FOOTER_ACTIONS('edit'),
				componentProps: {
					context,
					interfaceConfigurationComponent,
					eventHandlers,
					mode: 'edit',
					eventId: id,
					name: eventName,
					label,
					payloadFields: payloadShape
				}
			});
		},
		[PAGE_EVENT_CONFIG_PANE_MCP_CONTRACT_EVENT_DELETE_CLICKED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			dispatch(
				PAGE_EVENT_CONFIG_PANE_MACROPONENT_CONTRACT_EVENT_DELETE_REQUESTED,
				action.payload
			);
		}
	},
	styles
});
