import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createRef, Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import debounce from 'lodash/debounce';
import get from 'lodash/get';
import { t } from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
import styles from './sn-uib-event-configuration-pane.scss';
import {
	EVENT_MAPPING_CONFIG_MAPPING_ADDED,
	EVENT_MAPPING_CONFIG_HANDLER_ADDED,
	EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED,
	EVENT_MAPPING_CONFIG_HANDLER_DELETED
} from '../sn-uib-event-mapping-configuration';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars
import {
	EVENT_MAPPING_HANDLER_CHANGED,
	EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED,
	EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED
} from '../sn-uib-event-mapping-handler-config';
import SearchBox from '../partials/search-box'; // eslint-disable-line no-unused-vars

// Dispatched action types
export const EVENT_CONFIG_PANE_EVENT_MAPPING_ADDED =
	'EVENT_CONFIG_PANE#EVENT_MAPPING_ADDED';
export const EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_ADDED =
	'EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_ADDED';
export const EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETE_REQUESTED =
	'EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_DELETE_REQUESTED';
export const EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETED =
	'EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_DELETED';
export const EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED =
	'EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_CHANGED';
export const EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE =
	'EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE';
export const EVENT_CONFIG_PANE_TRIGGERING_ELEMENT_SELECTED =
	'EVENT_CONFIG_PANE#TRIGGERING_ELEMENT_SELECTED';
export const EVENT_CONFIG_PANE_HANDLER_SCRIPTED_VALUE_CHANGED =
	'EVENT_CONFIG_PANE#HANDLER_SCRIPTED_VALUE_CHANGED';
export const EVENT_CONFIG_PANE_HANDLER_VALUE_REFERENCE_SEARCHED =
	'SN_UIB_EVENT_CONFIG_PANE#HANDLER_VALUE_REFERENCE_SEARCHED';

const renderTriggeredBySection = (currentTriggeringElements) => {
	const ref = createRef();
	if (!currentTriggeringElements || currentTriggeringElements.length === 0) {
		return null;
	} else {
		return (
			<sn-uib-shelf-pane
				className="element-event-pane-triggering-elements"
				header-label={t('Triggered by')}>
				<div
					slot="content"
					className="element-event-pane-triggering-elements-section">
					<ul className="event-pane-triggering-elements-list">
						{currentTriggeringElements.map(({ element }) => (
							<sn-uib-draggable-list-item
								key={element.id}
								ref={ref}
								label={element.instanceName}
								actions={[
									{
										type: 'open',
										clickActionType: EVENT_CONFIG_PANE_TRIGGERING_ELEMENT_SELECTED
									}
								]}
								draggable={false}
								append-to-payload={{ element }}
							/>
						))}
					</ul>
				</div>
			</sn-uib-shelf-pane>
		);
	}
};

const view = (
	{
		properties: {
			eventMappings,
			events = [],
			eventHandlers,
			disabled,
			eventMappingHandlers,
			interfaceConfigurationComponent,
			bindings,
			elementId,
			appConfig,
			currentTriggeringElements,
			scriptTemplate
		},
		searchTerm,
		filteredEvents = []
	},
	{ dispatch }
) => {
	const eventMappingsSectionExpanded =
		!currentTriggeringElements || currentTriggeringElements.length === 0
			? '--expanded'
			: '';
	return (
		<div className="sn-uib-event-configuration-pane">
			{renderTriggeredBySection(currentTriggeringElements)}
			<div
				className={`element-event-pane-event-mappings-section ${eventMappingsSectionExpanded}`}>
				{events.length > 0 ? (
					<Fragment>
						<div className="search-input">
							<SearchBox
								placeholder={t('Search')}
								value={searchTerm}
								manageValue
								appendToPayload={{ context: 'eventSearch' }}
							/>
						</div>
						{filteredEvents.map((event = {}) => {
							const mappingForCurrentEvent =
								eventMappings.find((mapping) => mapping.eventId === event.id) ||
								{};
							return (
								<sn-uib-event-mapping-configuration
									key={event.id}
									appConfig={appConfig}
									bindings={bindings}
									elementId={elementId}
									event={event}
									eventHandlers={eventHandlers}
									eventMapping={mappingForCurrentEvent}
									eventMappingHandlers={
										eventMappingHandlers[mappingForCurrentEvent.id]
									}
									interfaceConfigurationComponent={
										interfaceConfigurationComponent
									}
									disabled={disabled}
									searchTerm={searchTerm}
									slot="content"
									appendToPayload={{
										context: 'element.hidden-prop-input2',
										eventId: event.id,
										eventApiName: event.apiName,
										eventMappingId: mappingForCurrentEvent.id
									}}
									scriptTemplate={scriptTemplate}
								/>
							);
						})}
					</Fragment>
				) : (
					<EmptyState
						headerMessage={t('No events available.')}
						/* prettier-ignore */
						description={t('The selected component has no configurable events.')}
					/>
				)}
			</div>
		</div>
	);
};

/**
 * The panel for configuring an element's instance name and properties in the UI Builder.
 */
export default createCustomElement('sn-uib-event-configuration-pane', {
	view,
	styles,
	behaviors: [focusBehavior],
	initialState: {
		searchTerm: '',
		filteredEvents: []
	},
	transformState(state) {
		const {
			searchTerm = '',
			properties: { events = [] }
		} = state;
		let filteredEvents = events;
		if (searchTerm) {
			const search = searchTerm.toUpperCase().trim();
			filteredEvents = filteredEvents.filter(({ label }) =>
				label.toUpperCase().includes(search)
			);
		}

		return { ...state, filteredEvents };
	},
	properties: {
		/**
		 * The list of event mappings for the currently selected element.
		 * @type {object[]}
		 */
		eventMappings: {
			default: []
		},
		/**
		 * A list of events that the element may emit that are mappable.
		 * @type {object[]}
		 */
		events: {
			default: []
		},
		/**
		 * A list of all UIB-appropriate event handlers that are available.
		 * @type {object[]}
		 */
		eventHandlers: {
			default: []
		},
		/**
		 * A map of event mapping ids to a list of field objects for the current handler(s)
		 * for that event mapping.
		 * @type {object}
		 */
		eventMappingHandlers: {
			default: {}
		},
		/**
		 * Whether the element is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},

		interfaceConfigurationComponent: {
			default: {}
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * elementId the element id
		 * @type {string}
		 */
		elementId: {
			default: null
		},
		/**
		 * Ancestor event handlers to be used for payload templates in the dispatched events modal
		 */
		ancestorEventHandlers: {
			default: [],
			schema: { type: 'array' }
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * Array of elements with event mapping handlers that trigger the currently selected element
		 */
		currentTriggeringElements: {
			default: [],
			schema: { type: 'array' }
		},
		/**
		 * Client transform script template
		 */
		scriptTemplate: {
			default: '',
			selectable: true
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ state, updateState }) => {
			const filteredEvents = get(state, 'properties.events', []);
			updateState({
				filteredEvents: filteredEvents.map(
					(event, idx) => (event.expanded = idx === 0)
				)
			});
		},
		['NOW_INPUT#INPUT']: debounce(({ action, updateState }) => {
			action.stopPropagation();
			const { context, fieldValue: searchTerm } = action.payload;
			context === 'eventSearch' && updateState({ searchTerm });
		}, 250),
		[EVENT_MAPPING_CONFIG_MAPPING_ADDED]: ({ action, dispatch }) => {
			dispatch(EVENT_CONFIG_PANE_EVENT_MAPPING_ADDED, action.payload);
		},
		[EVENT_MAPPING_CONFIG_HANDLER_ADDED]: ({ action, dispatch }) => {
			dispatch(EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_ADDED, action.payload);
		},
		[EVENT_MAPPING_CONFIG_HANDLER_DELETE_REQUESTED]: ({ action, dispatch }) => {
			dispatch(
				EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETE_REQUESTED,
				action.payload
			);
		},
		[EVENT_MAPPING_CONFIG_HANDLER_DELETED]: ({ action, dispatch }) => {
			action.payload.context === 'element' &&
				dispatch(
					EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_DELETED,
					action.payload
				);
		},
		[EVENT_MAPPING_HANDLER_CHANGED]: ({ action, dispatch }) => {
			if (action.payload.immediate) {
				dispatch(
					EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE,
					action.payload
				);
			} else {
				dispatch(
					EVENT_CONFIG_PANE_EVENT_MAPPING_HANDLER_CHANGED,
					action.payload
				);
			}
		},
		[EVENT_MAPPING_HANDLER_REFERENCE_PROPERTY_SEARCHED]: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();
			action.payload.context === 'element' &&
				dispatch(
					EVENT_CONFIG_PANE_HANDLER_VALUE_REFERENCE_SEARCHED,
					action.payload
				);
		},
		[EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED]: ({ action, dispatch }) => {
			dispatch(
				EVENT_CONFIG_PANE_HANDLER_SCRIPTED_VALUE_CHANGED,
				action.payload
			);
		}
	}
});
