import { createCustomElement } from '@servicenow/ui-core';
import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-button';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import styles from './sn-uib-quick-link.scss';
import { onNextFrame } from '../helpers/Transition';

const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	focusTargetId
} = DevxKeyFocusBehavior;

function SnUIBQuickLink({ properties: { links } }, dispatch) {
	return links.map(({ label, ariaLabel, target, actionProps = {} }) => {
		return (
			<now-button
				className="sn-uib-quick-link"
				config-aria={{
					'aria-label': ariaLabel || label
				}}
				on-click={() => {
					if (actionProps.name && actionProps.value) {
						dispatch(actionProps.name, actionProps.value);
					}
					onNextFrame(() => {
						focusTargetId(dispatch, target);
					});
				}}
				label={label}
				variant="primary"
			/>
		);
	});
}

createCustomElement('sn-uib-quick-link', {
	view: SnUIBQuickLink,
	styles,

	properties: {
		links: {
			default: []
		}
	},

	behaviors: [keyFocusBehavior, focusBehavior]
});
