import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import _get from 'lodash/get';
import {
	SCRIPT_INCLUDES_REQUESTED,
	SCRIPT_INCLUDES_GRAPHQL_REQUEST_SENT,
	SCRIPT_INCLUDES_REQUEST_SUCCEEDED,
	SCRIPT_INCLUDES_GRAPHQL_REQUEST_FAILED
} from './constants';
import { batchShorthandUpdateState } from '../../helpers/batchShorthandUpdateState';

const scriptIncludesQuery = `
	query {
		GlideRecord_Query {
			sys_ux_client_script_include(queryConditions: "access=public^ORsys_scope={{scriptScope}}", pagination: {limit: 1000}) {
				_results {
					id: sys_id {
						value
					},
					label: sys_name {
						value
					}
				}
			}
		}
	}
`.trimStart();

export const scriptIncludesRequestedHandler = ({ dispatch, action }) => {
	const { scriptScope } = action.payload;

	dispatch(SCRIPT_INCLUDES_GRAPHQL_REQUEST_SENT, { scriptScope });
};

export const fetchScriptIncludesByScopeEffect = createGraphQLEffect(
	scriptIncludesQuery,
	{
		successActionType: SCRIPT_INCLUDES_REQUEST_SUCCEEDED,
		errorActionType: SCRIPT_INCLUDES_GRAPHQL_REQUEST_FAILED,
		templateVarList: ['scriptScope'],
		batch: true
	}
);

export const scriptIncludesRequestSucceededHandler = ({
	action,
	updateState
}) => {
	const availableScriptIncludes = _get(
		action,
		'payload.data.GlideRecord_Query.sys_ux_client_script_include._results',
		[]
	).map(({ id, label }) => ({ id: id.value, label: label.value }));

	batchShorthandUpdateState(
		{
			isLoading: false,
			availableScriptIncludes
		},
		updateState
	);
};

export const scriptIncludesGraphqlRequestFailed = ({ updateState }) => {
	updateState({
		isLoading: false
	});
};

export default {
	[SCRIPT_INCLUDES_REQUESTED]: scriptIncludesRequestedHandler,
	[SCRIPT_INCLUDES_GRAPHQL_REQUEST_SENT]: fetchScriptIncludesByScopeEffect,
	[SCRIPT_INCLUDES_REQUEST_SUCCEEDED]: scriptIncludesRequestSucceededHandler,
	[SCRIPT_INCLUDES_GRAPHQL_REQUEST_FAILED]: scriptIncludesGraphqlRequestFailed
};
