import { createCustomElement } from '@servicenow/ui-core';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import get from 'lodash/get';
import styles from './sn-uib-component-section.scss';
import { view as snUibComponentItem } from '../sn-uib-component-item';
import '../sn-uib-component-item-focuser';
import { t } from 'sn-translate';
import '@servicenow/now-tooltip';
import {
	removeListenersIfFound,
	isInteracting
} from '../behaviors/draggable/utils';
import { attachDragListeners } from '../behaviors/draggable';

export const SN_UIB_COMPONENT_SECTION_COMPONENT_SELECTED =
	'SN_UIB_COMPONENT_SECTION#COMPONENT_SELECTED';

const { createFocusRef } = DevxKeyFocusBehavior;

const getNextValid = (arr, index, interval, fallback) => {
	let nextIndex = index + interval;

	if (nextIndex < 0) {
		nextIndex = arr.length - Math.abs(nextIndex);
	} else if (nextIndex > arr.length - 1) {
		nextIndex = nextIndex % arr.length;
	}

	const next = arr[nextIndex];
	return next ? next : fallback;
};

const view = (state, { dispatch }) => {
	const { components, disabled, activeItem, inPopover } = state.properties;
	const { componentId } = state;

	const childrenRefs = components.map(({ id }) => {
		// We are using componentId because the panel gets rendered in multiple places at the same time so the ids will overwrite each other
		return createFocusRef(`TOOLBOX_COMPONENT_${componentId}_${id}`);
	});
	return (
		<section className={inPopover ? 'in-popover' : ''}>
			<div
				className="children"
				// eslint-disable-next-line jsx-a11y/aria-proptypes
				role="region"
				aria-label={t('All components')}>
				{components
					.filter((elem) => get(elem, 'category', '') !== 'modal')
					.map((child, i) => {
						const ref = childrenRefs[i];
						const previousEl = getNextValid(childrenRefs, -1, i, {});
						const nextEl = getNextValid(childrenRefs, 1, i, {});

						const aboveEl = getNextValid(childrenRefs, -3, i, previousEl);
						const belowEl = getNextValid(childrenRefs, 3, i, nextEl);
						const keyMap = {
							ArrowLeft: previousEl,
							ArrowRight: nextEl,
							ArrowUp: aboveEl,
							ArrowDown: belowEl
						};
						return (
							<sn-uib-component-item-focuser keyMap={keyMap} key={child.id}>
								{snUibComponentItem(
									{
										...state,
										properties: {
											firstItem: i === 0 ? true : false,
											component: child,
											disabled,
											focusRef: ref
										}
									},
									{ dispatch }
								)}
							</sn-uib-component-item-focuser>
						);
					})}
			</div>
			<now-tooltip
				id={`tooltip--${get(activeItem, ['component', 'id'], '')}`}
				target-ref={activeItem.ref}
				content={get(activeItem, ['component', 'name'], 'NO CONTENT!')}
				manage-opened
				opened={state.tooltipOpened}
			/>
		</section>
	);
};

createCustomElement('sn-uib-component-section', {
	view,
	initialState: {
		tooltipTimerId: undefined,
		tooltipOpened: false
	},
	properties: {
		/**
		 * Whether the element is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * The components available to drag onto the stage, passed down from UXF.
		 * @type {object[]}
		 */
		components: {
			default: []
		},
		/**
		 * Whether its displayed in popover or in panel
		 * @type {boolean}
		 */
		inPopover: {
			default: false
		},
		activeItem: {
			default: {}
		},
		draggableProps: {
			default: {
				manualStart: true,
				inertia: true
			}
		}
	},
	styles,
	actionHandlers: {
		'COMPONENT_ITEM#CLICKED': ({ dispatch, action }) => {
			dispatch(SN_UIB_COMPONENT_SECTION_COMPONENT_SELECTED, action.payload);
		},
		['SN_UIB_COMPONENT_ITEM#ACTIVE_ITEM_SET']: ({
			action,
			updateProperties,
			updateState,
			dispatch,
			state
		}) => {
			const activeItem = action.payload;
			action.stopPropagation();
			const { properties } = state;

			// if the element is disabled, don't configure drag and drop.
			if (properties.disabled) {
				return;
			}

			// Use the existing activeItem in properties to perform this check,
			// not the activeItem we just received in the payload.
			if (
				properties.activeItem.ref &&
				!isInteracting(properties.activeItem.ref)
			) {
				removeListenersIfFound(properties.activeItem.ref);
			}

			let newTooltipTimerId = undefined;

			// Cancel the old tooltip timeout, if any
			if (state.tooltipTimerId) {
				clearTimeout(state.tooltipTimerId);
			}

			// Configure drag and drop to reference the new activeItem if given
			if (activeItem.ref) {
				const dataTransfer = activeItem.component.id;

				attachDragListeners({
					el: activeItem.ref,
					id: activeItem.component.id,
					draggableProps: {
						manualStart: true,
						inertia: true
					},
					previewData: {
						component: {
							name: activeItem.component.name,
							icon: activeItem.component.icon
						}
					},
					dispatch,
					previewTag: 'sn-uib-component-item',
					dataTransfer
				});

				// Setup the tooltip timer again
				newTooltipTimerId = setTimeout(
					() => updateState({ tooltipOpened: true }),
					1000
				);
			}

			updateState({ tooltipTimerId: newTooltipTimerId, tooltipOpened: false });

			// Update the properties so the tooltip can re-render
			updateProperties({ activeItem });
		}
	}
});
