import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import get from 'lodash/get';
import styles from './sn-uib-event-mapping-handler-values-config.module.scss';
import { PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED } from '../sn-uib-properties-editor-panel';
import { PROPERTY_INPUT_BINDING_BUTTON_CLICKED } from '../sn-uib-property-input-data-link-toggle';
import { PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED } from '../sn-uib-property-input-script-link-toggle';
import EmptyState from '../partials/empty-state'; // eslint-disable-line no-unused-vars
import { REFERENCE_PROPERTY_VALUE_CHANGED } from '../sn-uib-reference-property-input';
import { LINTING_CONFIG } from '../constants';

export const HANDLER_VALUE_CHANGED =
	'EVENT_MAPPING_HANDLER_VALUE_CONFIG#HANDLER_VALUE_CHANGED';
export const HANDLER_VALUE_SCRIPT_CHANGED =
	'EVENT_MAPPING_HANDLER_VALUE_CONFIG#HANDLER_VALUE_SCRIPT_CHANGED';
export const HANDLER_VALUE_REFERENCE_SEARCHED =
	'EVENT_MAPPING_HANDLER_VALUE_CONFIG#HANDLER_VALUE_REFERENCE_SEARCHED';

const SCRIPT_SHORTHAND_PATTERN = new RegExp(
	`^javascript\\(([\\n\\S\\d\\W\\w]*)\\)(\\!)?$`
);

const extractScriptFromPropertyValue = (script) => {
	const scriptMatcher = script && script.match(SCRIPT_SHORTHAND_PATTERN);
	return {
		script: scriptMatcher ? scriptMatcher[1] : script || '',
		compatibilityMode: scriptMatcher && !scriptMatcher[2]
	};
};

const view = ({
	properties: {
		handlerValues,
		interfaceConfigurationComponent,
		bindings,
		container,
		elementId,
		appConfig,
		enableScripting,
		clientTransformScript,
		clientScriptApiCompletions
	}
}) => {
	if (handlerValues.length === 0) {
		return (
			<EmptyState
				size="md"
				description={t('No configurable payload fields for this handler.')}
			/>
		);
	}

	const { script } = extractScriptFromPropertyValue(clientTransformScript);

	return (
		<div className="handler-values-config">
			<now-code-editor
				className={enableScripting ? 'code-editor' : 'code-editor-hidden'}
				autocomplete-suggestions={{
					variant: 'replace',
					disabled: false,
					suggestions: [clientScriptApiCompletions.clientTransformScript]
				}}
				disable-debugging
				disable-context-menu
				language="javascript"
				disable-full-screen
				disable-minimap
				readOnly={'false'}
				content={script}
				linting={LINTING_CONFIG}
			/>
			{enableScripting ? null : (
				<sn-uib-properties-editor-panel
					properties={handlerValues}
					interfaceConfigurationComponent={interfaceConfigurationComponent}
					dataBindingEnabled={true}
					bindings={bindings}
					append-to-payload={{ context: 'eventMappings' }}
					container={container}
					elementId={elementId}
					app-config={appConfig}
					script-template={`@event-configuration-pane-${elementId}/scriptTemplate`}
					scriptingEnabled={false}
				/>
			)}
		</div>
	);
};

createCustomElement('sn-uib-event-mapping-handler-values-config', {
	properties: {
		/**
		 * A list of payload fields for the selected event handler.
		 * @type {object[]}
		 */
		handlerValues: {
			default: []
		},
		interfaceConfigurationComponent: {
			default: {}
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * The properties editor container that the now-dropdown-panel should be constrained within
		 * @type {HTMLElement}
		 */
		container: {
			default: null
		},
		/**
		 * elementId the element id
		 * @type {string}
		 */
		elementId: {
			default: null
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		enableScripting: {
			default: false
		},
		clientTransformScript: {
			default: null
		},
		clientScriptApiCompletions: {
			default: {
				clientScript: '',
				clientTransformScript: ''
			}
		}
	},
	initialState: {
		compatibilityMode: true
	},
	styles,
	view,
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ updateState, properties }) => {
			if (properties.clientTransformScript) {
				const { compatibilityMode } = extractScriptFromPropertyValue(
					properties.clientTransformScript
				);
				updateState({
					compatibilityMode
				});
			}
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			if (action.payload.name === 'clientTransformScript') {
				const { compatibilityMode } = extractScriptFromPropertyValue(
					action.payload.value
				);
				updateState({
					compatibilityMode
				});
			}
		},
		[PROPERTIES_EDITOR_PANEL_PROPERTY_CHANGED]: ({ dispatch, action }) => {
			action.stopPropagation();

			const context = get(action, 'payload.context', null);
			context === 'eventMappings' &&
				dispatch(HANDLER_VALUE_CHANGED, {
					handlerValueId: action.payload.id,
					value: action.payload.value,
					immediate: action.payload.noDebounce
				});
		},
		[REFERENCE_PROPERTY_VALUE_CHANGED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(HANDLER_VALUE_REFERENCE_SEARCHED, action.payload);
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(HANDLER_VALUE_SCRIPT_CHANGED, action.payload);
		},
		[PROPERTY_INPUT_BINDING_BUTTON_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(HANDLER_VALUE_CHANGED, {
				handlerValueId: action.payload.propertyId,
				value: action.payload.isStatic ? '@' : null
			});
		},
		'NOW_CODE_EDITOR#CONTENT_CHANGED': ({ action, state, dispatch }) => {
			action.stopPropagation();
			const { text: script } = action.payload;
			dispatch(HANDLER_VALUE_SCRIPT_CHANGED, {
				script,
				compatibilityMode: state.compatibilityMode
			});
		}
	}
});
