const refStore = new Map();

class RefWrapper {
	constructor({
		id,
		ref,
		parent = null,
		children = [],
		notifyUpdate = () => {}
	}) {
		this.id = id;
		this.ref = ref;
		this.notifyUpdate = notifyUpdate;
		this.parent = parent;
		this.children = children;
	}
}

class ElementRefMap {
	set(id, ref, parent, children) {
		if (!id) {
			return false;
		}
		const existing = refStore.get(id);
		if (!existing) {
			const model = new RefWrapper({ id, ref, parent, children });
			refStore.set(id, model);
			return model;
		} else if (existing.ref !== ref) {
			existing.notifyUpdate();
			const model = new RefWrapper({ id, ref, parent, children });
			refStore.set(id, model);
			return model;
		}
		return existing;
	}
	get(id) {
		const model = refStore.get(id) || null;
		return model;
	}
	stubNotify(id, callback) {
		if (!id) {
			return false;
		}
		const existing = refStore.get(id);
		if (!existing) {
			const model = new RefWrapper({ id, ref: {} });
			model.notifyUpdate = callback;
			refStore.set(id, model);
		}
	}
}
const elementRefMap = new ElementRefMap();

export { elementRefMap };
