import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';
import '@servicenow/now-dropdown';
import '../sn-uib-property-description-tooltip';
import '../sn-uib-draggable-list';
import {
	createDraggableItems,
	createDropdownItems,
	resolveDataSourceFromClientScriptApi
} from './utils';
import { DEFAULT_ROUTE_DROPDOWN, ISOLATED_EDIT_MODE } from './constants';
import actionHandlers from './actionHandlers/';
import styles from './sn-uib-viewport-config-panel.scss';
import renderTabsPanelSection from './partials/renderTabsPanelSection';

const view = (state) => {
	const { properties, collapsedItems } = state;
	const {
		disabled,
		repeatedTabData,
		viewportDataSource,
		clientScriptApi,
		viewportPropertiesConfiguration,
		viewportRoutes,
		dynamicTabsEnabled,
		defaultRoute = '',
		appsSysId,
		isDashboardBuilder
	} = properties;
	const { staticTabItems, dynamicTabItems } = createDraggableItems(
		properties,
		collapsedItems,
		appsSysId,
		isDashboardBuilder
	);
	const dropdownItems = createDropdownItems(properties);
	const disableIsolationEditing = get(
		viewportPropertiesConfiguration,
		'disableIsolationEditing',
		false
	);
	return (
		<Fragment>
			<div className="viewport-container">
				<div className="viewport-static-container">
					{renderTabsPanelSection(state, staticTabItems)}
				</div>
				{dynamicTabsEnabled &&
				get(viewportPropertiesConfiguration, 'enableDataDrivenTabs', false) ? (
					<div className="viewport-dynamic-container">
						{renderTabsPanelSection(
							state,
							dynamicTabItems,
							resolveDataSourceFromClientScriptApi({
								repeatedTabData,
								viewportDataSource,
								clientScriptApi
							}),
							false
						)}
					</div>
				) : null}
				{!disableIsolationEditing ? (
					<div className="viewports-isolation-mode-button-container">
						<now-button
							className="isolation-mode-button"
							label={t('Edit content')}
							icon="modal-outline"
							variant="secondary"
							size="sm"
							append-to-payload={{
								type: ISOLATED_EDIT_MODE
							}}
						/>
					</div>
				) : null}
				{!isEmpty(viewportRoutes) ? (
					<div className="default-route-dropdown">
						<label for="path" className="dropdown-label">
							{t('Default item')}
							<sn-uib-property-description-tooltip
								description={t("Route to your tab's path")}
							/>
						</label>
						<now-select
							disabled={disabled}
							size="sm"
							select="single"
							selectedItem={defaultRoute}
							name="defaultRouteDropdown"
							items={dropdownItems}
							className="nds-dropdown"
							append-to-payload={{ type: DEFAULT_ROUTE_DROPDOWN }}
							panel-fit-props={{
								constrain: { maxWidth: 270, minWidth: 'target' }
							}}
						/>
					</div>
				) : null}
			</div>
		</Fragment>
	);
};

createCustomElement('sn-uib-viewport-config-panel', {
	properties: {
		activeScreen: {
			default: {}
		},
		dynamicTabsEnabled: {
			default: true
		},
		viewportRoutes: {
			default: {}
		},
		defaultRoute: {
			default: ''
		},
		activeRoute: {
			default: {}
		},
		disabled: {
			default: false
		},
		appConfig: {
			default: {}
		},
		repeatedTabData: {
			default: null
		},
		viewportDataSource: {
			default: null
		},
		viewportPropertiesConfiguration: {
			default: {
				validation: {
					name: {
						required: true
					},
					order: {
						required: false
					},
					icon: {
						required: false
					}
				},
				disableIsolationEditing: false,
				disableRouteConfiguration: false,
				enableDataDrivenTabs: false
			}
		},
		viewportElementProperties: {
			default: {}
		},
		elementRef: {
			default: {}
		},
		clientScriptApi: {
			default: '@uxf/clientScriptApi'
		},
		appsSysId: {
			default: '@uib-app/appsSysId'
		},
		isDashboardBuilder: {
			default: '@uib-app/isDashboardBuilder'
		}
	},
	view,
	styles,
	initialState: {
		dataSourceValue: null,
		collapsedItems: {},
		pillarAction: null
	},
	actionHandlers: {
		...actionHandlers
	}
});
