export const getIconByCategory = (id) => {
	switch (id) {
		case 'Inherited event handlers':
			return 'window-outline';
		case 'Page-level event handlers':
		case 'Declarative actions':
			return 'document-blank-outline';
		case 'Scripts':
			return 'code-outline';
		default:
			return 'database-outline';
	}
};

export const getFilteredHandlers = (searchTerm = '', handlers = []) => {
	if (searchTerm) {
		const search = searchTerm.toUpperCase().trim();
		return handlers.filter(({ label }) => label.toUpperCase().includes(search));
	} else {
		return handlers;
	}
};

export const serializeReturnStatementString = (handlerValues) => {
	return handlerValues.reduce((acc, value, i) => {
		return `${acc}\n\t\t${value.name}: null${
			i < handlerValues.length - 1 ? ',' : ''
		}`;
	}, '');
};

export const provideScriptTemplate = (scriptTemplate, handlerValues) => {
	const defaultReturnStatementPattern = new RegExp('\\treturn\\s\\{\\};');
	let scriptTemplateToUse = scriptTemplate;
	if (
		scriptTemplate.match(defaultReturnStatementPattern) &&
		handlerValues.length
	) {
		scriptTemplateToUse = scriptTemplate.replace(
			defaultReturnStatementPattern,
			`\treturn {${serializeReturnStatementString(handlerValues)}\n\t};`
		);
	}
	return `javascript(${scriptTemplateToUse})`;
};
