import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import 'sn-devx-components';
import styles from './sn-uib-field-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import '../sn-uib-property-description-tooltip';

// Dispatched actions
export const FIELD_PROPERTY_TABLE_SELECTED =
	'FIELD_PROPERTY_INPUT#TABLE_SELECTED';
export const FIELD_PROPERTY_FIELD_SELECTED =
	'FIELD_PROPERTY_INPUT#FIELD_SELECTED';

const view = (state, dispatch) => {
	const {
		label,
		table,
		tableFieldValues,
		fields,
		value,
		disabled,
		errorMsg,
		description,
		selectedComponentTag
	} = state.properties;

	if (table && (!tableFieldValues || !tableFieldValues[table])) {
		dispatch(FIELD_PROPERTY_TABLE_SELECTED, { value: table });
	}

	return (
		<Fragment className="sn-uib-property-input">
			<label className="sn-uib-property-input-label">
				{label}
				{!description ? null : (
					<sn-uib-property-description-tooltip
						description={description}
						selectedComponentTag={selectedComponentTag}
					/>
				)}
			</label>
			<devx-typeahead
				component-id={`${state.componentId}_${table}`}
				transparent
				disabled={disabled}
				errorMsg={errorMsg}
				placeholder={!table ? t('Please make a table selection') : ''}
				items={fields}
				selected-item={value || {}}
			/>
		</Fragment>
	);
};

createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		editingScript: false
	},
	actionHandlers: {
		'DEVX_TYPEAHEAD#ITEM_CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(FIELD_PROPERTY_FIELD_SELECTED, {
				value: action.payload.option.id
			});
		},
		'DEVX_TYPEAHEAD#INPUT_CHANGED': ({ action, properties, dispatch }) => {
			const { emptyStateBehavior } = properties;
			action.stopPropagation();
			if (action.payload.value === '' && properties.value) {
				// When there is a currently stored value for the field property and the
				// user clears out the input field, reset the value in UXF.
				dispatch(FIELD_PROPERTY_FIELD_SELECTED, {
					value: emptyStateBehavior.value
				});
			}
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	},
	styles
});
