import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-menu-elements.scss';
import { renderPanelContents } from '../partials/sn-uib-element-context-menu';
import './sn-uib-menu-element-child';
import './sn-uib-menu-element-section';

const renderMenuSection = (dispatch, properties, index) => {
	return (
		<sn-uib-menu-element-section sectionProps={properties} index={index} />
	);
};

const view = ({ properties }, { dispatch }) => {
	const { items } = properties;
	const comp = items.map(() => renderMenuSection);
	return (
		<div className="menu-container">
			{renderPanelContents(comp, dispatch, properties)}
		</div>
	);
};

export default createCustomElement('sn-uib-menu-elements', {
	view,
	properties: {
		items: {
			default: {}
		}
	},
	styles
});
