import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './_sn-uib-missing-component.scss';
const view = ({ properties }) => {
	const { elementId } = properties;
	return (
		<div className="missing-component" attr-data-element-id={elementId}>
			<now-icon size="xl" icon="triangle-exclamation-fill" />
		</div>
	);
};

export default createCustomElement('sn-uib-missing-component', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		elementId: {
			default: ''
		}
	},
	styles
});
