import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import get from 'lodash/get';
import debounce from 'lodash/debounce';
import '@servicenow/now-typeahead';
import { t } from 'sn-translate';
import '../sn-uib-property-description-tooltip';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import { config } from './component-config';
import styles from './sn-uib-reference-property-input.scss';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

// Dispatched actions
export const REFERENCE_PROPERTY_VALUE_CHANGED =
	'REFERENCE_PROPERTY_INPUT#VALUE_CHANGED';
export const REFERENCE_PROPERTY_RECORD_SELECTED =
	'REFERENCE_PROPERTY_INPUT#RECORD_SELECTED';

const view = (state, { dispatch }) => {
	const {
		id,
		label,
		items,
		value,
		disabled,
		referenceSearching,
		referenceId,
		errorMsg,
		emptyStateBehavior,
		description,
		selectedComponentTag
	} = state.properties;

	const { internalValue } = state;
	const showPARMessage = id === 'reportSysId' && label === 'Report';
	const placeholderText = t('Search for a {0}', label);
	const isSearching = referenceSearching && referenceSearching[referenceId];

	return (
		<Fragment>
			<div className="sn-uib-property-input">
				{showPARMessage ? (
					<span className="par-message">
						{t('Select either a Report or a PA Widget')}
					</span>
				) : null}
				<label className="sn-uib-property-input-label">
					{label}
					<sn-uib-property-description-tooltip
						description={
							description
								? description
								: /* prettier-ignore */
								  t('Search for a record and click or use your keyboard arrow keys to select')
						}
						selectedComponentTag={selectedComponentTag}
						hideTextLink={!description}
					/>
				</label>
				<now-typeahead
					on-focus={() =>
						dispatch('SN_UIB_REFERENCE_PROPERTY_INPUT#TYPEAHEAD_FOCUSED')
					}
					items={items || []}
					search="managed"
					selectedItem={value || emptyStateBehavior.value}
					manageSelectedItem
					messages={errorMsg ? [{ status: 'critical', content: errorMsg }] : []}
					value={internalValue || emptyStateBehavior.value}
					manageValue
					disabled={disabled}
					placeholder={placeholderText}>
					<now-button-iconic
						slot="end"
						icon={isSearching ? 'change-fill' : 'magnifying-glass-outline'}
						disabled
						bare
					/>
				</now-typeahead>
			</div>
		</Fragment>
	);
};

/**
 * A typeahead input component to be used for properties of type
 * reference configured in the UI Builder element configuration panel
 */
createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		internalValue: '',
		lastSelectedItem: null,
		initialSelectedValueLabel: '',
		editingScript: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_RENDERED]: ({ state, updateState, properties }) => {
			// This should be DOM READY but since we have to
			// wait for the reference display values to resolve
			// we have to do this
			const { items, referenceDisplayValues, value } = properties;
			const { initialSelectedValueLabel } = state;
			const selectedItem =
				items.find((item) => item.id === properties.value) || {};
			if (!initialSelectedValueLabel) {
				if (selectedItem.label) {
					batchShorthandUpdateState(
						{
							internalValue: selectedItem.label,
							initialSelectedValueLabel: selectedItem.label,
							lastSelectedItem: selectedItem
						},
						updateState
					);
				} else if (
					value &&
					referenceDisplayValues &&
					referenceDisplayValues[value]
				) {
					// in event mapping handler values context: if we change the value in the reference
					// typehead and then close the whole popover by pressing cancel, we have to reset the
					// display value using the value (sysId) when we come back into the popover.
					batchShorthandUpdateState(
						{
							internalValue: referenceDisplayValues[value],
							initialSelectedValueLabel: referenceDisplayValues[value],
							lastSelectedItem: {
								id: value,
								label: referenceDisplayValues[value]
							}
						},
						updateState
					);
				}
			}
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			updateState
		}) => {
			const { name, previousValue, value } = action.payload || {};
			const emptyStateValue = get(properties, 'emptyStateBehavior.value', '');
			// if the value is reset in middleware, we want to clear the display value in the input.
			if (name === 'value' && previousValue && !value) {
				updateState({ internalValue: emptyStateValue });
			}
		},
		'NOW_TYPEAHEAD#VALUE_SET': debounce(
			({
				action: {
					payload: { value },
					stopPropagation
				},
				properties: { table, referenceId, emptyStateBehavior },
				dispatch,
				updateState
			}) => {
				stopPropagation();

				const nextValue = value === '' ? emptyStateBehavior.value : value;
				/**
				 * The new value of the input field is dispatched up to the
				 * data provider, along with the table it is referencing
				 * @example {value: 'user typed value'}
				 */
				(value || value === '') &&
					dispatch(REFERENCE_PROPERTY_VALUE_CHANGED, {
						table,
						referenceId,
						value: nextValue
					});

				updateState({ internalValue: value });
			},
			250,
			{ trailing: true }
		),

		'NOW_TYPEAHEAD#SELECTED_ITEM_SET': ({
			dispatch,
			action,
			updateState,
			properties
		}) => {
			action.stopPropagation();
			const { value } = action.payload;
			const emptyStateValue = get(properties, 'emptyStateBehavior.value', '');
			/**
			 * The new selected record is dispatched when a reference is selected
			 * @example { value: 'p98jq34pf98hq3rpgfi9uh3g' } {sysId}
			 */

			if (value || value === emptyStateValue) {
				dispatch(REFERENCE_PROPERTY_RECORD_SELECTED, {
					value
				});
				const targetItem = properties.items.find((item) => item.id === value);
				targetItem &&
					updateState({
						lastSelectedItem: targetItem
					});
			}
		},
		['NOW_TYPEAHEAD#INTERACTION_FINISHED']: ({
			action,
			state,
			updateState,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			const {
				payload: { selectedItem: selectedItemId, value }
			} = action;
			const { lastSelectedItem } = state;
			if (lastSelectedItem && selectedItemId === lastSelectedItem.id && value) {
				updateState({ internalValue: lastSelectedItem.label });
			} else if (value === '') {
				const nextValue = get(properties, 'emptyStateBehavior.value', null);
				updateState([
					{
						path: 'internalValue',
						value: '',
						operation: 'set'
					},
					{
						path: 'lastSelectedItem',
						value: null,
						operation: 'set'
					}
				]);
				dispatch('NOW_TYPEAHEAD#SELECTED_ITEM_SET', { value: nextValue });
			}
		},
		['SN_UIB_REFERENCE_PROPERTY_INPUT#TYPEAHEAD_FOCUSED']: ({
			state,
			properties,
			dispatch
		}) => {
			const { table, referenceId } = properties;
			// populates the reference picker when the typeahead is focused, before the user starts typing.
			dispatch(REFERENCE_PROPERTY_VALUE_CHANGED, {
				table,
				referenceId,
				value: state.internalValue
			});
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	},
	styles
});
