import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import '@servicenow/now-button';
import '@servicenow/now-content-tree';
import '@servicenow/now-icon';
import '@servicenow/now-loader';
import '@servicenow/now-input';
import '@servicenow/now-popover';
import { t } from 'sn-translate';
import '../sn-uib-collapsible-panel';
import '../sn-uib-error';
import '../sn-uib-loader';
import '../sn-uib-page-management-popover';
import styles from './sn-uib-page-navigator.scss';
import { getPathForId } from '../sn-uib-page-management-panel/utils';
import { filterPages, expandAllItems } from './utils';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

export const CREATE_PAGE_BUTTON_CLICKED =
	'SN_UIB_PAGE_NAVIGATOR#CREATE_PAGE_BUTTON_CLICKED';
export const CREATE_VARIANT_BUTTON_CLICKED =
	'SN_UIB_PAGE_NAVIGATOR#CREATE_VARIANT_BUTTON_CLICKED';
export const OPEN_PAGES_UPDATED = 'SN_UIB_PAGE_NAVIGATOR#OPEN_PAGES_UPDATED';
export const PAGE_SELECTED = 'SN_UIB_PAGE_NAVIGATOR#PAGE_SELECTED';
export const COPY_PAGE_CLICKED = 'SN_UIB_PAGE_NAVIGATOR#COPY_PAGE_CLICKED';
export const EDIT_PAGE_CLICKED = 'SN_UIB_PAGE_NAVIGATOR#EDIT_PAGE_CLICKED';
export const EDIT_ROUTE_CLICKED = 'SN_UIB_PAGE_NAVIGATOR#EDIT_ROUTE_CLICKED';
export const CREATE_PAGE_CLICKED =
	'INTERNAL_CREATE_POPOVER#CREATE_PAGE_CLICKED';
export const CREATE_VARIANT_CLICKED =
	'INTERNAL_CREATE_POPOVER#CREATE_VARIANT_CLICKED';
export const SCREEN_TYPE_SELECTED =
	'SN_UIB_PAGE_NAVIGATOR#SCREEN_TYPE_SELECTED';
export const ROUTE_SELECTED = 'SN_UIB_PAGE_NAVIGATOR#ROUTE_SELECTED';

const { COMPONENT_CONNECTED } = actionTypes;

const getRouteConfigurationActions = (item) => [
	{
		label: t('Edit'),
		icon: 'pencil-page-fill',
		type: EDIT_ROUTE_CLICKED,
		payload: { item }
	}
];

// TODO: This is disabling the copy option until
// copying workspace pages works as expected
// Once that is supported, the copy option should
// always be included
const getPageConfigurationActions = (item, pageVariantsEnabled) =>
	pageVariantsEnabled
		? [
				{
					label: t('Edit'),
					icon: 'pencil-page-fill',
					type: EDIT_PAGE_CLICKED,
					payload: { item }
				},
				{
					label: t('Copy'),
					icon: 'documents-outline',
					type: COPY_PAGE_CLICKED,
					payload: { item }
				}
		  ]
		: [
				{
					label: t('Edit'),
					icon: 'pencil-page-fill',
					type: EDIT_PAGE_CLICKED,
					payload: { item }
				}
		  ];

const emptyState = (pageVariantsEnabled) => (
	<Fragment>
		<div className="empty-state empty-state">
			<figure>
				<div className="icon-container">
					<now-icon
						className="page-icon"
						icon={pageVariantsEnabled ? 'document-fill' : 'home-fill'}
						size="lg"
					/>
				</div>
				<figcaption>
					{pageVariantsEnabled
						? t('No pages exist in this application.')
						: t('No landing pages exist in this workspace')}
				</figcaption>
			</figure>
			<now-button
				label={
					pageVariantsEnabled ? t('Create page') : t('Create landing page')
				}
				size="sm"
				variant="tertiary"
			/>
		</div>
	</Fragment>
);

const renderContentTree = ({ pages, openPages, selectedItemPath }) => {
	return (
		<Fragment>
			<now-input
				placeholder={t('Search')}
				append-to-payload={{ id: 'tree-search' }}>
				<now-icon
					slot="start"
					size="sm"
					icon="magnifying-glass-fill"
					className="slotted-search-icon"
				/>
			</now-input>
			<div className="tree-scroll">
				<now-content-tree
					className="now-content-tree--populated"
					expanded-items={openPages}
					items={pages}
					selected-items={[selectedItemPath]}
					manage-selected-items
					clickActionType={PAGE_SELECTED}
					manage-expanded-items={true}
					trigger-icon="caret"
					overflow="truncate"
					size="sm"
					highlight-selected-parent={true}
					show-actions-on-hover
				/>
			</div>
		</Fragment>
	);
};
const renderError = () => {
	return (
		<div className={'error-message'}>
			<sn-uib-error message={t('There was an issue loading your pages')} />
		</div>
	);
};
const view = (state) => {
	const {
		pages,
		selectedItem,
		openPages,
		isPagesLoading,
		error,
		pageVariantsEnabled,
		selectedPageIsTab
	} = state.properties;
	const { filteredPages, searching, newPopoverOpened } = state;
	const selectedItemPath = getPathForId(pages, selectedItem);
	const treeProps = {
		pages: searching ? filteredPages : pages,
		openPages,
		selectedItemPath
	};
	const renderContent = () => {
		if (error) {
			return renderError();
		} else if (isPagesLoading) {
			return (
				<div className={'loader'}>
					<now-loader />
					<p className={'loader-label'}>{t('Loading...')}</p>
				</div>
			);
		} else {
			return (
				<section className="page-trees">
					{pages && pages.length > 0
						? renderContentTree(treeProps)
						: emptyState(pageVariantsEnabled)}
				</section>
			);
		}
	};

	return (
		<div className="page-navigator-content">
			<div className="page-navigator">
				<div className="header">
					<span className="p1-panel-header-text">
						{pageVariantsEnabled ? t('Pages') : t('Landing pages')}
					</span>
					{pages &&
					pages.length > 0 &&
					pageVariantsEnabled &&
					!selectedPageIsTab ? (
						<now-popover
							hide-tail
							constrain={{
								width: 98
							}}
							manage-opened
							opened={newPopoverOpened}>
							<now-button-bare
								bare
								className="header-button header-create-page-button"
								iconEnd="plus-outline"
								label={t('New')}
								size="sm"
								slot="trigger"
								append-to-payload={{ button: 'dropdown' }}
								tooltip-content={t('Create new page')}
								variant="tertiary"
								config-aria={{
									'aria-label': t('Create new page')
								}}
							/>
							<sn-uib-page-management-popover
								header="Create New"
								actions={[
									{
										label: t('Page'),
										type: CREATE_PAGE_CLICKED,
										icon: 'document-outline'
									},
									{
										label: t('Variant'),
										type: CREATE_VARIANT_CLICKED,
										icon: 'minus-outline'
									}
								]}
								slot="content"
							/>
						</now-popover>
					) : (
						<now-button-bare
							bare
							className="header-button header-create-page-button"
							iconEnd="plus-outline"
							label={t('New')}
							size="sm"
							append-to-payload={{ button: 'create' }}
							tooltip-content={t('Create new page')}
							variant="tertiary"
							config-aria={{
								'aria-label': t('Create new page')
							}}
						/>
					)}
				</div>
				{renderContent()}
			</div>
			<now-popover
				manage-opened
				hide-tail
				opened={state.popoverOpen}
				positionTarget={state.popoverTarget}
				positions={[{ target: 'top-end', content: 'top-start' }]}
				constrain={{
					width: 150
				}}
				key={state.popoverKey ? state.popoverKey : undefined}
				append-to-payload={{ target: state.popoverTarget, type: 'Configure' }}>
				{state.popoverOpen ? (
					<sn-uib-page-management-popover
						actions={state.popoverActions}
						slot="content"
					/>
				) : null}
			</now-popover>
		</div>
	);
};

createCustomElement('sn-uib-page-navigator', {
	view,
	styles,
	initialState: {
		filteredPages: [],
		searching: false,
		newPopoverOpened: false,
		popoverActions: []
	},
	properties: {
		openPages: {
			default: []
		},
		pages: {
			default: []
		},
		isPagesLoading: {
			default: false
		},
		selectedItem: {
			default: ''
		},
		error: {
			default: false
		},
		pageVariantsEnabled: {
			default: false
		},
		selectedPageIsTab: {
			default: false
		}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({ updateState, properties }) => {
			const { pages } = properties;
			updateState({
				filteredPages: pages
			});
		},
		[CREATE_PAGE_CLICKED]: ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			dispatch(CREATE_PAGE_BUTTON_CLICKED, {
				type: null
			});

			updateState({
				newPopoverOpened: false
			});
		},
		'NOW_BUTTON#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();

			dispatch(CREATE_PAGE_BUTTON_CLICKED, action.payload);
		},
		[CREATE_VARIANT_CLICKED]: ({ action, dispatch, updateState }) => {
			action.stopPropagation();
			dispatch(CREATE_VARIANT_BUTTON_CLICKED);

			updateState({
				newPopoverOpened: false
			});
		},
		[COPY_PAGE_CLICKED]: ({ updateState }) => {
			updateState({
				popoverOpen: false
			});
		},
		[EDIT_PAGE_CLICKED]: ({ updateState }) => {
			updateState({
				popoverOpen: false
			});
		},
		[EDIT_ROUTE_CLICKED]: ({ updateState }) => {
			updateState({
				popoverOpen: false
			});
		},
		'NOW_BUTTON_BARE#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			if (action.payload.button !== 'create') {
				return;
			}
			dispatch(CREATE_PAGE_BUTTON_CLICKED, {
				type: null
			});
		},
		'NOW_CONTENT_TREE#ITEM_CLICKED': ({ action, dispatch }) => {
			if (action.payload.item.type !== 'route') {
				action.stopPropagation();
				const {
					payload: { item }
				} = action;

				if (item.actionable === true) {
					dispatch(PAGE_SELECTED, action.payload);
				}
			} else {
				action.stopPropagation();
				dispatch(SCREEN_TYPE_SELECTED, action.payload);
			}
		},
		'NOW_CONTENT_TREE#ACTION_CLICKED': ({
			action,
			properties,
			updateState,
			dispatch
		}) => {
			action.stopPropagation();

			const { actionTarget, item } = action.payload;
			const popoverActions =
				item.type === 'route'
					? getRouteConfigurationActions(item)
					: getPageConfigurationActions(item, properties.pageVariantsEnabled);

			batchShorthandUpdateState(
				{
					popoverOpen: true,
					popoverTarget: actionTarget,
					popoverKey: actionTarget.getRootNode().host.getAttribute('now-id'),
					popoverActions
				},
				updateState
			);

			dispatch(ROUTE_SELECTED, action.payload);
		},
		'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET': ({ action, dispatch }) => {
			action.stopPropagation();

			dispatch(OPEN_PAGES_UPDATED, action.payload);
		},
		'NOW_INPUT#INPUT': ({ action, properties, updateState, dispatch }) => {
			action.stopPropagation();
			const { fieldValue, id } = action.payload;

			if (id === 'tree-search') {
				const { pages } = properties;
				const filteredPages = filterPages(pages, fieldValue);

				batchShorthandUpdateState(
					{
						filteredPages,
						searching: fieldValue.length > 0
					},
					updateState
				);

				dispatch(OPEN_PAGES_UPDATED, { value: expandAllItems(filteredPages) });
			}
		},
		'NOW_POPOVER#OPENED_SET': ({ action, state, updateState }) => {
			action.stopPropagation();
			if (action.payload.type === 'Configure') {
				if (
					!action.payload.value &&
					action.payload.target === state.popoverTarget
				) {
					batchShorthandUpdateState(
						{
							popoverOpen: false,
							popoverTarget: null,
							popoverKey: undefined,
							popoverActions: []
						},
						updateState
					);
				}
			} else {
				updateState({
					newPopoverOpened: action.payload.value
				});
			}
		}
	}
});
