import { createCustomElement, actionTypes } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import cuid from 'cuid';
import styles from './sn-uib-property-input-script-link-toggle.scss';
import '@servicenow/now-button';
import '@servicenow/now-modal';

import '@servicenow/now-code-editor';
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';
import { LINTING_CONFIG } from '../constants';

export const PROPERTY_INPUT_SCRIPT_LINK_TOGGLED =
	'SN_UIB_PROPERTY_INPUT_SCRIPT_LINK#TOGGLED';
export const PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED =
	'SN_UIB_PROPERTY_INPUT_SCRIPT_LINK#SCRIPT_UPDATED';
export const PROPERTY_INPUT_SCRIPT_BUTTON_OK_CLICKED =
	'SN_UIB_PROPERTY_INPUT_SCRIPT_LINK#OK_CLICKED';
export const PROPERTY_INPUT_SCRIPT_BUTTON_CANCEL_CLICKED =
	'SN_UIB_PROPERTY_INPUT_SCRIPT_LINK#CANCEL_CLICKED';
export const PROPERTY_INPUT_SCRIPT_BUTTON_HELP_CLICKED =
	'SN_UIB_PROPERTY_INPUT_SCRIPT_LINK#HELP_CLICKED';

export const SCRIPT_SHORTHAND_PATTERN = new RegExp(
	`^javascript\\(([\\n\\S\\d\\W\\w]*)\\)(\\!)?$`
);

const extractScriptFromPropertyValue = (script) => {
	const scriptMatcher = script.match(SCRIPT_SHORTHAND_PATTERN);
	return {
		script: scriptMatcher ? scriptMatcher[1] : script || '',
		compatibilityMode: scriptMatcher && !scriptMatcher[2]
	};
};

const view = (state) => {
	const {
		currentScript,
		properties: {
			disabled,
			editingScript,
			selected,
			clientScriptApiCompletions
		}
	} = state;

	const statfulButtonClasses = {
		'sn-uib-property-input-script-link-button': true,
		selected: selected
	};

	return (
		<Fragment>
			<now-button-stateful
				manageSelected
				selected={selected}
				class={statfulButtonClasses}
				variant="tertiary"
				icon="code-outline"
				tooltipContext={t('Script input')}
				disabled={disabled}
				bare
			/>
			<now-modal
				hoist
				headerLabel={t('Edit scripted property value')}
				opened={editingScript}
				size="lg"
				footerActions={
					!disabled
						? [
								{
									label: t('Apply'),
									clickActionType: PROPERTY_INPUT_SCRIPT_BUTTON_OK_CLICKED,
									variant: 'primary'
								},
								{
									label: t('Cancel'),
									clickActionType: PROPERTY_INPUT_SCRIPT_BUTTON_CANCEL_CLICKED,
									variant: 'secondary'
								},
								{
									label: t('Learn more'),
									clickActionType: PROPERTY_INPUT_SCRIPT_BUTTON_HELP_CLICKED,
									variant: 'tertiary',
									icon: 'circle-question-outline'
								}
						  ]
						: [
								{
									label: t('Done'),
									clickActionType: PROPERTY_INPUT_SCRIPT_BUTTON_CANCEL_CLICKED,
									variant: 'primary'
								},
								{
									label: t('Learn more'),
									clickActionType: PROPERTY_INPUT_SCRIPT_BUTTON_HELP_CLICKED,
									variant: 'tertiary',
									icon: 'circle-question-outline'
								}
						  ]
				}>
				<div
					className="script-link-button-editor-container"
					// We have to inline the styles because the modal is hoisted
					style={{ height: '20rem', marginBottom: '1.29rem' }}>
					{editingScript ? (
						<now-code-editor
							autocomplete-suggestions={{
								variant: 'replace',
								disabled: false,
								suggestions: [clientScriptApiCompletions.clientTransformScript]
							}}
							disable-debugging
							disable-context-menu
							language="javascript"
							content={currentScript}
							readOnly={disabled}
							linting={LINTING_CONFIG}
						/>
					) : null}
				</div>
			</now-modal>
		</Fragment>
	);
};

createCustomElement('sn-uib-property-input-script-link-toggle', {
	view,
	properties: {
		disabled: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		propertyId: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		editingScript: {
			default: false
		},
		currentScript: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		selected: {
			default: false
		},
		clientScriptApiCompletions: {
			default: {
				clientScript: '',
				clientTransformScript: ''
			}
		}
	},
	initialState: {
		currentScript: '',
		scriptEditorId: cuid(),
		compatibilityMode: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({
			action,
			updateState,
			properties
		}) => {
			action.stopPropagation();
			if (properties.currentScript) {
				const { script, compatibilityMode } = extractScriptFromPropertyValue(
					properties.currentScript
				);
				batchShorthandUpdateState(
					{
						currentScript: script,
						compatibilityMode
					},
					updateState
				);
			}
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			action.stopPropagation();
			const { name, value } = action.payload;
			if (name === 'editingScript' && value) {
				updateState({ scriptEditorId: cuid() });
			}

			if (name === 'currentScript') {
				const { script, compatibilityMode } = extractScriptFromPropertyValue(
					value
				);
				batchShorthandUpdateState(
					{ currentScript: script, compatibilityMode },
					updateState
				);
			}
		},
		['NOW_BUTTON_STATEFUL#SELECTED_SET']: ({
			action,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const { propertyId } = properties;

			dispatch(PROPERTY_INPUT_SCRIPT_LINK_TOGGLED, {
				propertyId,
				editing: true
			});
		},
		['NOW_BUTTON#CLICKED']: ({ action, dispatch }) => {
			action.stopPropagation();
			const { context } = action.payload;
			if (context === 'help') {
				dispatch(PROPERTY_INPUT_SCRIPT_BUTTON_HELP_CLICKED);
			}
		},
		['NOW_MODAL#OPENED_SET']: ({
			action,
			properties,
			dispatch,
			updateState
		}) => {
			action.stopPropagation();
			const { propertyId } = properties;
			dispatch(PROPERTY_INPUT_SCRIPT_LINK_TOGGLED, {
				propertyId,
				editing: action.payload.value
			});

			const { script, compatibilityMode } = extractScriptFromPropertyValue(
				properties.currentScript
			);
			!action.payload.value &&
				batchShorthandUpdateState(
					{
						currentScript: script,
						compatibilityMode
					},
					updateState
				);
		},
		'NOW_CODE_EDITOR#CONTENT_CHANGED': ({ action, updateState }) => {
			const script = action.payload.text;

			action.stopPropagation();
			updateState({
				path: 'currentScript',
				value: script,
				operation: 'set',
				shouldRender: false
			});
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_OK_CLICKED]: ({
			action,
			properties,
			state,
			dispatch
		}) => {
			action.stopPropagation();
			const { propertyId } = properties;

			dispatch(PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED, {
				script: state.currentScript,
				compatibilityMode: state.compatibilityMode
			});
			dispatch(PROPERTY_INPUT_SCRIPT_LINK_TOGGLED, {
				propertyId,
				editing: false
			});
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_CANCEL_CLICKED]: ({
			action,
			properties,
			updateState,
			dispatch
		}) => {
			action.stopPropagation();
			const { propertyId } = properties;
			dispatch(PROPERTY_INPUT_SCRIPT_LINK_TOGGLED, {
				propertyId,
				editing: false
			});
			const { script, compatibilityMode } = extractScriptFromPropertyValue(
				properties.currentScript
			);
			batchShorthandUpdateState(
				{
					currentScript: script,
					compatibilityMode
				},
				updateState
			);
		}
	},
	styles
});
