import get from 'lodash/get';
const ROW = {
	'fd-row': true,
	'fd-row-reverse': false,
	'fd-col-reverse': false,
	'fd-column': false
};
const COL = {
	'fd-row': false,
	'fd-row-reverse': false,
	'fd-col-reverse': false,
	'fd-column': true
};
const COL_REV = {
	'fd-row': false,
	'fd-row-reverse': false,
	'fd-col-reverse': true,
	'fd-column': false
};
const ROW_REV = {
	'fd-row': false,
	'fd-row-reverse': true,
	'fd-col-reverse': false,
	'fd-column': false
};

const lookup = {
	display: {
		flex: { 'd-flex': true, 'd-grid': false },
		'inline-flex': { 'd-flex': true, 'd-grid': false },
		grid: { 'd-flex': false, 'd-grid': true },
		fallback: { 'd-flex': true, 'd-grid': false }
	},
	'flex-direction': {
		column: COL,
		row: ROW,
		'row-reverse': ROW_REV,
		'column-reverse': COL_REV,
		fallback: ROW
	},
	'flex-flow': {
		format(str) {
			return str.split(' ')[0].trim();
		},
		column: COL,
		row: ROW,
		'row-reverse': ROW_REV,
		'column-reverse': COL_REV,
		fallback: ROW
	}
};

const getStyleClassNames = (styles = {}) => {
	const defRet = {
		'd-flex': true,
		'fd-row': true
	};
	const defGridRet = {
		'd-flex': true,
		'fd-column': true
	};
	if (!styles) {
		return defRet;
	}
	const classNames = Object.entries(styles)
		.map((kv) => kv.map((str) => str.trim()))
		.filter(([rule]) => lookup[rule])
		.reduce((acc, [rule, value]) => {
			const format = get(lookup, `${rule}.format`, (str) => str);
			const val = get(
				lookup,
				`${rule}.${format(value)}`,
				lookup[rule].fallback
			);
			return { ...acc, ...val };
		}, defRet);
	if (classNames['d-grid']) {
		return defGridRet;
	}
	return classNames;
};
export default getStyleClassNames;
