import { removeListenersIfFound } from './utils';
import { setupListenersHandler, checkIfDisabled } from './handlers';

import { actionTypes } from '@servicenow/ui-core';

const privateActionConfig = {
	private: true,
	stopPropagation: true
};
export default {
	name: 'draggable',

	actions: {
		'UIB_DRAGGABLE_BEHAVIOR#SETUP_LISTENERS': privateActionConfig
	},
	actionHandlers: {
		'UIB_DRAGGABLE_BEHAVIOR#SETUP_LISTENERS': setupListenersHandler,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: checkIfDisabled
	},

	onConnect(el, { dispatch }, options) {
		const {
			getDataTransferObjFn,
			getPreviewData,
			getPreviewTag,
			getScrollContainer,
			isDisabled
		} = options;

		// There's a weakness here where if the expression value changes it won't
		// get updated in the handler because events have to be synchronous so
		// we have to set the value in the action. This is a candidate for
		// refactoring to use some sort of seismic asynchronous dataTransfer
		// provider.

		dispatch('UIB_DRAGGABLE_BEHAVIOR#SETUP_LISTENERS', {
			getDataTransferObjFn,
			getPreviewData,
			getPreviewTag,
			getScrollContainer,
			isDisabled,
			el
		});
	},

	onDisconnect(el) {
		removeListenersIfFound(el);
	}
};
