import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import '@servicenow/now-input';
import '@servicenow/now-highlighted-value';
import '@now-par-components/sn-par-pillar';
import { dropBehavior, dragBehavior } from './behaviors';
import '@servicenow/now-button';
import styles from './sn-uib-draggable-list.scss';

export const SN_UIB_PILLAR_WRAPPER_DRAGGED_OVER =
	'SN_UIB_PILLAR_WRAPPER#DRAGGED_OVER';
export const SN_UIB_PILLAR_WRAPPER_DRAG_STARTED =
	'SN_UIB_PILLAR_WRAPPER#DRAG_STARTED';
export const SN_UIB_PILLAR_WRAPPER_DRAG_ENDED =
	'SN_UIB_PILLAR_WRAPPER#DRAG_ENDED';
export const SN_UIB_PILLAR_WRAPPER_EXPANDED =
	'SN_UIB_PILLAR_WRAPPER#EXPAND_CLICKED';
export const SN_UIB_PILLAR_WRAPPER_CLICKED = 'SN_UIB_PILLAR_WRAPPER#CLICKED';
export const SN_UIB_PILLAR_WRAPPER_ACTION_CLICKED =
	'SN_UIB_PILLAR_WRAPPER#ACTION_CLICKED';
export const SN_UIB_PILLAR_WRAPPER_INPUT_BLUR =
	'SN_UIB_PILLAR_WRAPPER#INPUT_BLUR';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

/**
 * seismic actions don't give the correct event target for event delegation
 * so we're using a native event
 * @param {*} event
 */
const handleClick = (
	event,
	{ dispatch, updateState },
	item,
	isExpanded,
	isEditable,
	isSelected,
	selectable,
	editMode,
	isChild,
	rootContainerId
) => {
	switch (event.target.className) {
		case 'child-toggle':
			dispatch(SN_UIB_PILLAR_WRAPPER_EXPANDED, {
				item,
				isExpanded: !isExpanded
			});
			return;
		case 'label child':
		case 'label':
		case 'container':
			if (event.detail === 2 && !isChild && !editMode) {
				isEditable &&
					updateState({
						editMode: true
					});
				return;
			}
			if (event.detail === 1 && selectable) {
				if (isChild) {
					event.stopPropagation(); //block parent click event from triggering
					dispatch(SN_UIB_PILLAR_WRAPPER_CLICKED, {
						item,
						id: item.id,
						isChild,
						rootContainerId
					});
					return;
				} else {
					dispatch(SN_UIB_PILLAR_WRAPPER_CLICKED, {
						item,
						id: item.id,
						isSelected: !isSelected,
						isChild,
						rootContainerId
					});
					return;
				}
			}
			return;
		default:
			return;
	}
};

const view = (
	{
		properties: {
			item,
			beingDragged,
			isChild,
			isDraggable,
			hasChildren,
			isSelected,
			selectable,
			isExpanded,
			isEditable,
			size,
			rootContainerId
		},
		editMode
	},
	dispatch
) => (
	<div
		// This needs to be a string HTML attribute for DnD
		draggable={isDraggable ? 'true' : null}
		class={{
			'pillar-wrapper': !beingDragged && !isChild,
			'child-pillar-wrapper': isChild
		}}
		on-click={(e) =>
			handleClick(
				e,
				dispatch,
				item,
				isExpanded,
				isEditable,
				isSelected,
				selectable,
				editMode,
				isChild,
				rootContainerId
			)
		}>
		<sn-par-pillar
			actions={
				!editMode
					? item.actions
					: item.actions.filter(({ alwaysVisible }) => alwaysVisible)
			}
			selected={selectable && isSelected}
			contextMenuAction={item.contextMenuActions}
			draggable={isDraggable}
			append-to-payload={{ id: item.id }}
			size={size}
			style={{
				display: beingDragged ? 'none' : 'block'
			}}>
			<div className="container">
				{hasChildren ? (
					<now-button-iconic
						icon={isExpanded ? 'caret-down-outline' : 'caret-right-outline'}
						bare={true}
						hidePadding={true}
						variant="tertiary"
						className="child-toggle"
						size={size}
					/>
				) : null}
				{editMode ? (
					<now-input
						placeholder="Enter a name"
						autofocus
						value={item.label}
						manageInvalid
						invalid={item.messages && Boolean(item.messages.length)}
						messages={item.messages}
						append-to-payload={{ id: item.id, input: item.type }}
						on-blur={() => {
							dispatch(SN_UIB_PILLAR_WRAPPER_INPUT_BLUR);
						}}
					/>
				) : (
					<span class={{ label: true, child: isChild && !isDraggable }}>
						{item.label}
					</span>
				)}
			</div>
		</sn-par-pillar>
		{isExpanded ? <slot /> : null}
		{isChild ? <div className="dropzone-end"></div> : null}
	</div>
);

createCustomElement('sn-uib-pillar-wrapper', {
	renderer: { type: snabbdom },
	view,
	styles,
	initialState: {
		editMode: false
	},
	properties: {
		/**
		 * @typedef Item
		 * @property {string} id [required]
		 * @property {number} order [required]
		 * @property {string} label [required]
		 * @property {boolean} isExpanded
		 * @property {boolean} isSelected
		 * @property {boolean} isDraggable
		 * @property {boolean} isEditable
		 * @property {object[]} actions
		 */
		/**
		 * @type {Item}
		 */
		item: {
			default: {}
		},
		beingDragged: {
			default: false
		},
		isChild: {
			default: false
		},
		hasChildren: {
			default: false
		},
		isDraggable: {
			default: true
		},
		/**
		 * if tab label name can be edited
		 */
		isEditable: {
			default: true
		},
		isSelected: {
			default: false
		},
		/**
		 * @type {boolean} whether the pillar items are selectable
		 */
		selectable: {
			default: false
		},
		/**
		 * @type {boolean} whether the pillar item's children are expanded
		 */
		isExpanded: {
			default: false
		},
		/**
		 * sets the font & icon sizes, eg. sm, md, lg
		 * @type {string}
		 */
		size: {
			default: 'md'
		},
		/**
		 *  the id of the item's root container (this will be null if it is a top level item)
		 */
		rootContainerId: {
			default: null
		}
	},
	behaviors: [
		{
			behavior: dragBehavior,
			options: {
				getData({
					properties: { item, isDraggable, rootContainerId },
					dispatch,
					action: {
						payload: { event }
					}
				}) {
					if (!isDraggable) {
						event.preventDefault();
						event.stopImmediatePropagation();
						return;
					}
					event.stopImmediatePropagation();

					dispatch(SN_UIB_PILLAR_WRAPPER_DRAG_STARTED, {
						id: item.id,
						order: item.order,
						item,
						rootContainerId
					});
					return item;
				},
				dragEndAction: SN_UIB_PILLAR_WRAPPER_DRAG_ENDED
			}
		},
		{
			behavior: dropBehavior,
			options: {
				onDrop() {},
				onDraggedOver({
					properties: { item, isDraggable, rootContainerId },
					dispatch,
					action: {
						payload: { event }
					},
					host
				}) {
					if (!isDraggable) {
						event.preventDefault();
						event.stopImmediatePropagation();
						return;
					}

					let dragTargetRoot = rootContainerId;
					let dragTarget = item;
					const dropZoneEnd =
						event.composedPath()[0] &&
						event.composedPath()[0].className === 'dropzone-end';

					const targetId = event.target && event.target.appendToPayload.id;
					const currentTargetId =
						event.currentTarget && event.currentTarget.appendToPayload.id;

					//handle a special edge case where dropping the last child item is janky
					if (!dropZoneEnd && targetId !== currentTargetId) {
						dragTargetRoot = event.target.parentNode.appendToPayload.id;
						dragTarget = event.target.appendToPayload;
					}

					dispatch(SN_UIB_PILLAR_WRAPPER_DRAGGED_OVER, {
						event,
						dragTarget,
						dragTargetRoot,
						el: host
					});
				}
			}
		}
	],
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ properties, updateState }) => {
			const { item } = properties;
			const isEditable = get(properties, 'isEditable', true);
			if (!item.label || (item.messages && Boolean(item.messages.length))) {
				isEditable &&
					updateState({
						editMode: true
					});
			}
		},
		[SN_UIB_PILLAR_WRAPPER_INPUT_BLUR]: ({
			updateState,
			properties,
			action
		}) => {
			action.stopPropagation();
			const { item } = properties;
			const error = item.messages && Boolean(item.messages.length);
			const empty = !item.label;
			updateState({
				editMode: error || empty
			});
		},
		'NOW_BUTTON#CLICKED': ({
			action,
			updateState,
			state,
			dispatch,
			properties
		}) => {
			const isEditable = get(properties, 'isEditable', true);

			action.stopPropagation();
			const { actionId } = action.payload;
			if (actionId.indexOf('edit') > -1) {
				isEditable &&
					updateState({
						editMode: true
					});
			}

			if (actionId.indexOf('delete') > -1) {
				isEditable &&
					updateState({
						editMode: false
					});
			}

			const { item } = state.properties;
			dispatch(SN_UIB_PILLAR_WRAPPER_ACTION_CLICKED, {
				itemId: item.id,
				actionId,
				order: item.order
			});
		}
	}
});
