import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './sn-uib-element-placement-controls.scss';
import get from 'lodash/get';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import getStyleClassNames from '../helpers/layoutDirection';

// eslint-disable-next-line no-unused-vars
const InsertButton = ({
	slotStyles,
	focused,
	dispatch,
	direction,
	updateState,
	element
}) => {
	const tooltip = `Add ${direction === 'left' ? 'before' : 'after'}`;
	const elmHeight = get(element, 'elm_ref.clientHeight') < 100;
	const elmWidth = get(element, 'elm_ref.clientWidth') < 100;
	return (
		<div
			class={{
				'insert-button-wrapper': true,
				[direction]: true,
				...slotStyles
			}}>
			<div
				class={{ 'insert-button': true, focused }}
				on-mouseenter={() => updateState({ hoveredDirection: direction })} //element.elm_ref.classList.add(elmDir)}
				on-mouseleave={() => updateState({ hoveredDirection: 'none' })} //element.elm_ref.classList.remove(elmDir)}
				on-click={(e) => {
					const centerTarget = e.target.getBoundingClientRect();
					dispatch('INSERT_COMPONENT#CLICKED', {
						target: e,
						direction,
						toolboxAnchor: {
							top: centerTarget.top + centerTarget.height / 2,
							left: centerTarget.left + centerTarget.width / 2
						}
					});
				}}
				title={tooltip}>
				{elmHeight && elmWidth ? (
					<now-icon icon="plus-outline" bare="true" size="sm" />
				) : (
					<Fragment>
						<now-icon
							className="withText"
							icon="plus-outline"
							bare="true"
							size="sm"
						/>
						<span className="insert-text">{t('Add')}</span>
					</Fragment>
				)}
			</div>
		</div>
	);
};

const view = (
	{
		properties: { element, slotStyles, focused, focusedDirecion },
		hoveredDirection
	},
	{ dispatch, updateState }
) => {
	const classHelper = getStyleClassNames(slotStyles);
	const elmHeight = get(element, 'elm_ref.clientHeight') > 20;
	const elmWidth = get(element, 'elm_ref.clientWidth') > 20;
	if (elmHeight || elmWidth) {
		return (
			<div class={{ 'insert-buttons-container': true }}>
				<div
					class={{
						'insert-boundaries': true,
						[hoveredDirection]: true,
						[focusedDirecion]: true,
						...classHelper
					}}
				/>
				<div id="uib-element-placement-controls" class={classHelper}>
					<InsertButton
						focused={focused && focusedDirecion === 'left'}
						slotStyles={classHelper}
						element={element}
						dispatch={dispatch}
						direction="left"
						updateState={updateState}
					/>
					<InsertButton
						focused={focused && focusedDirecion === 'right'}
						slotStyles={classHelper}
						element={element}
						dispatch={dispatch}
						direction="right"
						updateState={updateState}
					/>
				</div>
			</div>
		);
	} else {
		return null;
	}
};

export default createCustomElement('sn-uib-element-placement-controls', {
	view,
	properties: {
		focusRefresh: {
			default: {}
		},
		/**
		 * direction describes location of insert button
		 * @type {string}
		 * @example - {left, right}
		 */
		direction: {
			default: 'left'
		},
		/**
		 * The element object that describes the currently
		 * mounted element
		 * @type {object}
		 * @example - {id: '12345', displayValue: 'Form component'}
		 */
		element: { default: null },
		/**
		 * The focused flag used to denote that the mounted
		 * element is the currently selected element on UIB
		 * stage and is in focus
		 * @type {boolean}
		 */
		focused: { default: false },
		focusedDirecion: { default: 'none' },
		/**
		 * The hovered flag used to denote that the mounted
		 * element is currently being moused over on UIB
		 * stage
		 */
		hovered: { default: false },
		keymaps: { default: {} },
		slotStyles: { default: null },
		/**
		 * Whether the element disabled
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * The current state of the resize handles on the related element
		 * @type {object}
		 */
		resizeInfo: { default: null }
	},
	initialState: {
		hoveredDirection: 'none',
		focused: false
	},
	actionHandlers: {
		['INSERT_COMPONENT#CLICKED']: {
			effect: (coeffects) => {
				const {
					dispatch,
					action,
					properties: { element }
				} = coeffects;

				const { direction, toolboxAnchor } = action.payload;
				dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
					elementId: element.id,
					elementInstanceName: element.instanceName
				});
				dispatch('SN_UIB_ELEMENT_PLACE_CONTROL#ADD_REQUESTED', {
					elementId: element.id,
					direction
				});
				dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
					toolboxAnchor,
					onSelect: ({ dispatch, component }) => {
						dispatch('UIB_DND_DROPZONE#DROPPED', {
							dataTransfer: component.id,
							elementId: element.id,
							direction
						});
					},
					onClose: ({ dispatch }) => {
						dispatch('SN_UIB_ELEMENT_PLACE_CONTROL#CANCEL_ADD_REQUESTED');
					}
				});
				dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
			},
			stopPropagation: false
		}
	},
	styles
});
