import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-content-tree';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import '../sn-uib-error';
import '../sn-uib-loader';
import styles from './sn-uib-data-broker-navigator.scss';
import { getPathForId } from '../sn-uib-page-management-panel/utils';
import EmptyState from '../partials/empty-state';
import '../sn-uib-property-description-tooltip';

export const DATA_BROKER_NAVIGATOR_DELETE_DATA_BROKER_BUTTON_CLICKED =
	'SN_UIB_DATA_BROKER_NAVIGATOR#DELETE_DATA_BROKER_BUTTON_CLICKED';
export const DATA_BROKER_NAVIGATOR_ADD_DATA_BROKER_BUTTON_CLICKED =
	'SN_UIB_DATA_BROKER_NAVIGATOR#ADD_DATA_BROKER_BUTTON_CLICKED';
export const DATA_BROKER_NAVIGATOR_DATA_BROKER_SELECTED =
	'SN_UIB_DATA_BROKER_NAVIGATOR#DATA_BROKER_SELECTED';
export const DATA_BROKER_NAVIGATOR_OPEN_DATA_BROKER_DEF_CLICKED =
	'SN_UIB_DATA_BROKER_NAVIGATOR#OPEN_DATA_BROKER_DEF_CLICKED';

const renderError = () => (
	<div className="data-broker-error-message">
		<sn-uib-error
			/* prettier-ignore */
			message={t('There was a problem loading the data resource instances for this page')}
		/>
	</div>
);

const renderTree = (dataBrokers, loading, selectedItemPath, ancestors) => {
	if (loading) {
		return (
			<div className="data-broker-loading-message">
				<now-loader />
				<p className="loader-label">{t('Loading...')}</p>
			</div>
		);
	}

	if (!dataBrokers || dataBrokers.length === 0) {
		return (
			<EmptyState
				hidePadding={true}
				description={
					ancestors
						? t('There are no inherited data resource instances for this page.')
						: t('No data resource instances have been added to this page yet.')
				}
			/>
		);
	}

	const contentTreeItems = dataBrokers.reduce((acc, dataBroker) => {
		return acc.concat({
			...dataBroker,
			label: dataBroker.instanceName,
			icon: ancestors ? 'lock-fill' : 'configuration-item-outline',
			actionable: true,
			actions: [
				{ icon: 'open-link-fill', label: t('View data resource definition') }
			]
		});
	}, []);

	return (
		<now-content-tree
			className="data-broker-content-tree"
			items={contentTreeItems}
			selected-items={[selectedItemPath]}
			manage-selected-items={true}
			show-actions-on-hover={true}
		/>
	);
};

const view = (state) => {
	const { popoverOpened, properties } = state;
	const {
		dataBrokerInstances,
		disabled,
		error,
		loading,
		selectedDataBroker,
		parentDataBrokerInstances,
		selectedDataBrokerIsAncestor,
		dataBrokerDefinitions,
		isMacroponentReadOnly
	} = properties;

	let selectedItemPath = [];
	if (selectedDataBroker) {
		selectedItemPath = selectedDataBrokerIsAncestor
			? getPathForId(parentDataBrokerInstances, selectedDataBroker.id)
			: getPathForId(dataBrokerInstances, selectedDataBroker.id);
	}

	return (
		<div className="data-broker-navigator">
			<div className="data-broker-navigator-header">
				<div>
					<span className="header-text">{t('Data resource instances')}</span>
					<sn-uib-property-description-tooltip
						description={t('View and add data resources')}
					/>
				</div>
				<div>
					<now-button-iconic
						className="header-button --delete"
						bare
						icon="trash-fill"
						size="sm"
						variant="tertiary"
						tooltip-content={t('Delete selected data resource instance')}
						config-aria={{
							'aria-label': t('Delete selected data resource instance')
						}}
						append-to-payload={{ action: 'delete' }}
						disabled={
							selectedDataBroker && !selectedDataBrokerIsAncestor
								? disabled || isMacroponentReadOnly
								: true
						}
					/>
					<now-popover
						positions={[{ target: 'top-center', content: 'bottom-center' }]}
						manage-opened
						opened={popoverOpened}
						append-to-payload={{ id: 'data-broker-definitions-popover' }}>
						<now-button-bare
							className="header-button --add"
							icon-start="plus-outline"
							label={t('Add')}
							size="sm"
							variant="tertiary"
							tooltip-content={t('Add new data resource instance')}
							config-aria={{
								'aria-label': t('Add new data resource instance')
							}}
							append-to-payload={{ action: 'add' }}
							disabled={disabled || isMacroponentReadOnly}
							slot="trigger"
						/>
						<sn-uib-data-broker-definitions
							dataBrokerDefinitions={dataBrokerDefinitions}
							displayed={popoverOpened}
							slot="content"
						/>
					</now-popover>
				</div>
			</div>
			<div className="data-broker-navigator-content">
				<section className="data-broker-navigator-ancestor-instances">
					<div className="data-broker-navigator-header --subheader">
						<div>
							<span className="header-text">
								{t('Inherited data resource instances')}
							</span>
							<sn-uib-property-description-tooltip
								/* prettier-ignore */
								description={t('Data resources that are inherited from the page')}
							/>
						</div>
					</div>
					<div className="data-broker-navigator-subcontent">
						{error
							? renderError()
							: renderTree(
									parentDataBrokerInstances,
									loading,
									selectedItemPath,
									true
							  )}
					</div>
				</section>
				<section className="data-broker-navigator-local-instances">
					<div className="data-broker-navigator-header --subheader">
						<div>
							<span className="header-text">
								{t('Local data resource instances')}
							</span>
							<sn-uib-property-description-tooltip
								description={t('Data resources that you added')}
							/>
						</div>
					</div>
					<div className="data-broker-navigator-subcontent">
						{error
							? renderError()
							: renderTree(
									dataBrokerInstances,
									loading,
									selectedItemPath,
									false
							  )}
					</div>
				</section>
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-data-broker-navigator', {
	view,
	styles,
	initialState: {
		popoverOpened: false
	},
	properties: {
		/**
		 * The list of data broker instances on the current page
		 * @type {object[]}
		 * @example [{ name: 'Data broker 1', id: '123', properties: [], type: 'GraphQL' }, { ... }]
		 */
		dataBrokerInstances: {
			default: []
		},
		/**
		 * Whether editing on configuation is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * Whether to render an error
		 * @type {boolean}
		 */
		error: {
			default: false
		},
		/**
		 * Whether the data broker instances are currently being loaded
		 * @type {boolean}
		 */
		loading: {
			default: false
		},
		/**
		 * The currently selected data broker instance being configured
		 * @type {object}
		 * @example { name: 'Data broker 1', id: '123', properties: [], type: 'GraphQL' }
		 */
		selectedDataBroker: {
			default: null
		},
		/**
		 * the list of parent data broker instances and their properties
		 * @type {array}
		 */
		parentDataBrokerInstances: {
			default: []
		},
		/**
		 * whether the currently selected data broker instance is an ancestor broker
		 * @type {boolean}
		 */
		selectedDataBrokerIsAncestor: {
			default: false
		},
		/**
		 * the list of data broker definitions
		 * @type {array}
		 */
		dataBrokerDefinitions: {
			default: []
		},
		isMacroponentReadOnly: {
			default: false
		}
	},
	actionHandlers: {
		'SN_UIB_DATA_BROKER_NAVIGATOR#TOGGLE_POPOVER': ({
			action,
			updateState
		}) => {
			updateState({ popoverOpened: action.payload.value });
		},
		'NOW_POPOVER#OPENED_SET': {
			effect: ({ action, properties: { disabled }, updateState }) =>
				action.payload.id === 'data-broker-definitions-popover' &&
				!disabled &&
				updateState({ popoverOpened: action.payload.value }),
			stopPropagation: true
		},
		'NOW_CONTENT_TREE#ITEM_CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			const {
				payload: { item }
			} = action;
			dispatch(DATA_BROKER_NAVIGATOR_DATA_BROKER_SELECTED, {
				dataBrokerId: item.id
			});
		},
		'NOW_CONTENT_TREE#ACTION_CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			const { item = {} } = action.payload;
			dispatch(DATA_BROKER_NAVIGATOR_OPEN_DATA_BROKER_DEF_CLICKED, {
				definition: item.definition
			});
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, dispatch }) => {
			action.stopPropagation();
			if (action.payload.action === 'delete') {
				dispatch(DATA_BROKER_NAVIGATOR_DELETE_DATA_BROKER_BUTTON_CLICKED);
			}
		},
		'NOW_BUTTON_BARE#CLICKED': ({ action, updateState }) => {
			action.stopPropagation();
			if (action.payload.action === 'add') {
				updateState({ popoverOpened: true });
			}
		}
	}
});
