import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-popover';
import '@servicenow/now-button';
import { t } from 'sn-translate';
import styles from './sn-uib-toolbox.scss';
import { SN_UIB_COMPONENT_SECTION_COMPONENT_SELECTED } from '../sn-uib-component-section';
const SN_UIB_TOOLBOX_PRIORITY_COMPONENT_SELECTED =
	'SN_UIB_TOOLBOX#PRIORITY_COMPONENT_SELECTED';
import { filterComponents } from '../sn-uib-component-navigator/utils';
import debounce from 'lodash/debounce';
import isEmpty from 'lodash/isEmpty';
// eslint-disable-next-line no-unused-vars
import SearchBox from '../partials/search-box';
import { sortedComponents } from '../helpers/componentUtils';

const componentSelectedHandler = ({ action, dispatch, properties }) => {
	const { id } = action.payload;
	const component = properties.components.find(
		(component) => component.id === id
	);
	if (component) {
		dispatch('SN_UIB_TOOLBOX#COMPONENT_SELECTED', { component });
		dispatch('UIB_TOOLBOX#SET_OPENED', { value: false });
	}
};

// eslint-disable-next-line no-unused-vars
const PrioritizedComponentsSection = ({ components = [], dispatch }) => {
	return (
		<section className="toolbox-prioritized-components">
			{components.map(({ name, icon, id }) => {
				return (
					<section
						aria-label={`${name} component`}
						className="prioritized-component"
						role="button" // eslint-disable-line  jsx-a11y/no-noninteractive-element-to-interactive-role
						tab-index="0"
						aria-describedby="component-item-description"
						on-keydown={(e) => {
							const { key } = e;
							if (key === 'Enter') {
								dispatch(SN_UIB_TOOLBOX_PRIORITY_COMPONENT_SELECTED, { id });
							}
						}}
						on-click={(e) => {
							e.preventDefault();
							dispatch(SN_UIB_TOOLBOX_PRIORITY_COMPONENT_SELECTED, { id });
						}}>
						<sn-uib-icon icon={icon} className="template-icon" />
						<div className="component-name" aria-label={name}>
							{name}
						</div>
						<now-icon
							className="plus-button"
							icon="circle-plus-outline"
							size="md"
						/>
					</section>
				);
			})}
		</section>
	);
};

const panelView = (
	{
		properties: {
			searchTerm,
			components,
			filteredComponents,
			prioritizedComponents
		}
	},
	dispatch
) => {
	return (
		<div className="toolbox-panel">
			<div className="panel-header">
				<h3 className="heading">{t('Components')}</h3>
				<now-button-iconic
					icon="close-fill"
					size="sm"
					bare
					on-click={() => {
						dispatch('UIB_TOOLBOX#SET_OPENED', { value: false });
					}}
				/>
			</div>
			<div className="panel-search">
				<SearchBox
					placeholder={t('Search')}
					manageValue={true}
					value={searchTerm}
					appendToPayload={{ id: 'component-search' }}
					dispatch={dispatch}
				/>
			</div>
			{prioritizedComponents.length > 0 ? (
				<PrioritizedComponentsSection
					components={prioritizedComponents}
					dispatch={dispatch}
				/>
			) : null}

			<div className="panel-components">
				<sn-uib-component-section
					disabled={false}
					components={sortedComponents(components)}
					in-popover={true}
					style={
						!isEmpty(searchTerm) && {
							visibility: 'hidden',
							position: 'absolute'
						}
					}
				/>
				{!isEmpty(searchTerm) ? (
					<sn-uib-component-section
						disabled={false}
						components={filteredComponents}
					/>
				) : null}
			</div>
		</div>
	);
};

createCustomElement('sn-uib-toolbox-panel', {
	view: panelView,
	styles,
	properties: {
		searchTerm: {
			default: ''
		},
		prioritizedComponents: { default: [] },
		components: {
			default: []
		},
		filteredComponents: {
			default: []
		}
	},
	actionHandlers: {
		// The following is needed because this component is hoisted into now-popover.
		// If this is not done, the action will not be propagated.
		[SN_UIB_COMPONENT_SECTION_COMPONENT_SELECTED]: () => {}
	}
});

const view = ({
	properties: { opened, positionTarget, prioritizedComponents, components },
	searchTerm,
	filteredComponents
}) => {
	if (!opened) {
		return null;
	}

	return (
		<now-popover
			manage-opened={true}
			opened={opened}
			position-target={positionTarget}>
			<sn-uib-toolbox-panel
				slot="content"
				searchTerm={searchTerm}
				prioritizedComponents={prioritizedComponents}
				components={components}
				filteredComponents={filteredComponents}
			/>
		</now-popover>
	);
};

createCustomElement('sn-uib-toolbox', {
	view,
	properties: {
		opened: {
			default: false
		},
		positionTarget: {
			default: null
		},
		prioritizedComponents: {
			default: []
		},
		components: {
			default: []
		},
		hideTail: {
			default: false
		}
	},
	initialState: {
		searchTerm: '',
		filteredComponents: []
	},
	transformState(state) {
		const {
			searchTerm,
			properties: { components }
		} = state;
		const filteredComponents = filterComponents(components, searchTerm);
		return {
			...state,
			filteredComponents
		};
	},
	actionHandlers: {
		'NOW_POPOVER#CONTENT_VISIBLE': ({ action: { payload } }) => {
			// focus and select the search input
			try {
				const inputEl = payload.content[0].nowRootNode
					.querySelector('now-input')
					.nowRootNode.querySelector('input');
				inputEl.focus();
				inputEl.select();
			} catch (er) {
				void 0;
			}
		},
		'NOW_POPOVER#CONTENT_HIDDEN': ({ updateState }) => {
			updateState({ searchTerm: '' });
		},
		'NOW_INPUT#INPUT': debounce(({ action, updateState }) => {
			action.stopPropagation();
			const { fieldValue, id } = action.payload;

			if (id === 'component-search') {
				updateState({
					searchTerm: fieldValue
				});
			}
		}, 300),
		[SN_UIB_TOOLBOX_PRIORITY_COMPONENT_SELECTED]: componentSelectedHandler,
		[SN_UIB_COMPONENT_SECTION_COMPONENT_SELECTED]: componentSelectedHandler,
		'NOW_POPOVER#OPENED_SET': ({ action, dispatch }) => {
			dispatch('UIB_TOOLBOX#SET_OPENED', action.payload);
		}
	}
});
