import flow from 'lodash/flow';
import get from 'lodash/get';
import debounce from 'lodash/debounce';
import { calculateIntersection } from './calculateIntersection';
import { onNextFrame } from '../../helpers/Transition';

const ACTIVE = 'ACTIVE';
const INACTIVE = 'INACTIVE';
const ACTIVE_WITH_BAD_DIMENSIONS = 'ACTIVE_WITH_BAD_DIMENSIONS';
const ACTIVE_AND_OUT_OF_VIEW = 'ACTIVE_AND_OUT_OF_VIEW';

const positionElement = (measurements, element) => {
	if (!element || !element.style) {
		return;
	}
	const results = calculateIntersection(measurements);
	const bounds = results.result;

	/**
	 *  Note: This application of attributes matters here.
	 * The MutationObserver we use for the overlap detection is triggered by both the classList add
	 * and the css changes but it seems that if the styles are applied first we only get the callback while the styles are `opacity: 0`
	 */
	element.style.cssText = ` 
		opacity: 1;
		transform: translate3d(${Math.round(bounds.left)}px, ${Math.round(
		bounds.top
	)}px, 0);
		width: ${Math.round(bounds.right - bounds.left)}px;
		height: ${Math.round(bounds.bottom - bounds.top)}px;
	`;

	if (results.status === 'HIDDEN') {
		element.classList.add('out-of-view');
	} else {
		element.classList.remove('out-of-view');
	}
};
const hideElement = (element) => {
	if (!element || !element.style) {
		return;
	}
	element.style.cssText = `
		opacity: 0;
	`;
};
const hasDefaultCoordinates = (node) => {
	if (node) {
		const rect = node.getBoundingClientRect();
		return [rect.top, rect.left, rect.bottom, rect.right].every((num) => {
			return num === 0;
		});
	}
	return true;
};

const getMeasurements = (refs = [], instance) => {
	return refs
		.map((element) => {
			const node = get(element, 'ref.current', null);
			if (node) {
				instance.elementMap[node] = true;
				return node.getBoundingClientRect();
			}
		})
		.filter((domRect) => {
			if (!domRect) {
				return domRect;
			}
			return domRect.x !== 0 && domRect.y !== 0;
		});
};
function draw(elements, instance) {
	requestAnimationFrame(() => {
		const measurements = getMeasurements(elements, instance);

		if (
			instance.state === ACTIVE_WITH_BAD_DIMENSIONS &&
			!instance.fallbackElement
		) {
			instance.state = INACTIVE;
		}
		switch (instance.state) {
			case ACTIVE:
				positionElement(measurements, instance.element, instance);
				break;

			case INACTIVE:
				hideElement(instance.element);
				break;
			case ACTIVE_WITH_BAD_DIMENSIONS:
				positionElement(
					[
						instance.fallbackElement.getBoundingClientRect(),
						...getMeasurements(getStageRefs(instance.refMap), instance)
					],
					instance.element,
					instance
				);
				break;

			default:
				// Shouldn't be here but if we are assume we should hide it.
				hideElement(instance.element);
		}
	});
}

const isValidId = (id) => {
	return !!id;
};
const isValidRef = (element) => {
	return !!(element && element.ref && element.ref.current);
};
const isActive = (activeFn, inactiveFn) => {
	return (args) => {
		const { instance } = args;
		return instance.isActive() ? activeFn(args) : inactiveFn(args);
	};
};

const isUsingFallback = (trueFn, falseFn) => {
	return (args) => {
		const { instance } = args;
		return instance.state === ACTIVE_WITH_BAD_DIMENSIONS
			? trueFn({ ...args })
			: falseFn(args);
	};
};

const noop = (args) => args;

const updateElement = (args) => {
	const { instance, newElement } = args;

	instance.element = newElement;

	return args;
};
const removeListeners = (args) => {
	const { instance } = args;
	const attachedListeners = instance.attachedListeners;

	Object.keys(attachedListeners).forEach((id) => {
		const removeFn = attachedListeners[id].remove;

		if (!removeFn) {
			return;
		}
		removeFn();
	});
	instance.elementMap = {};
	return args;
};
const attachListenerToElement = (args) => {
	const { instance, element } = args;
	const node = element.ref.current;

	if (!node) {
		return args;
	}
	const onscroll = () => {
		instance.handleScrollFired({ instance });
	};
	const observer = new ResizeObserver(() => {
		instance.handleResizeFired({ instance });
	});
	const remove = () => {
		node.removeEventListener('scroll', onscroll);
		observer.unobserve(node);
		element.notifyUpdate = () => {};
	};
	instance.attachedListeners[element.id] = {
		current: element.ref.current,
		remove
	};

	element.notifyUpdate = () => {
		remove();
		requestAnimationFrame(() =>
			requestAnimationFrame(() => {
				instance.notifyFunction(element.id)();
			})
		);
	};
	node.addEventListener('scroll', onscroll);
	observer.observe(node);
	return args;
};
const getStageRefs = (map) => {
	return ['stageWrapper', 'stageRootRef']
		.map((key) => {
			return map.get(key);
		})
		.filter((element) => {
			return element && get(element, 'ref.current', null);
		});
};
const getAncestors = (currentElement, map, previousElements = []) => {
	if (!currentElement) {
		return previousElements;
	}
	const parent = currentElement.parent;
	if (!isValidRef(currentElement) && !parent) {
		return previousElements;
	}
	if (parent) {
		return getAncestors(map.get(parent), map, [
			...previousElements,
			currentElement
		]);
	}
	return [...previousElements, currentElement];
};
const gatherRefs = (args) => {
	const { instance } = args;
	const activeId = instance.activeId;
	if (!activeId) {
		return args;
	}
	const map = instance.refMap;
	const targetElement = map.get(activeId);

	if (!targetElement || !isValidRef(targetElement)) {
		instance.state = ACTIVE_WITH_BAD_DIMENSIONS;
		map.stubNotify(activeId, () => instance.notifyFunction(activeId)());
		return { ...args, elements: [] };
	} else if (hasDefaultCoordinates(targetElement.ref.current)) {
		attachListenerToElement({ element: targetElement, instance });
		instance.state = ACTIVE_WITH_BAD_DIMENSIONS;
		return {
			...args,
			elements: [targetElement, ...getStageRefs(map)]
		};
	} else {
		instance.state = ACTIVE;
	}

	const elements = [...getAncestors(targetElement, map), ...getStageRefs(map)];

	return {
		...args,
		elements
	};
};
const attachListeners = (args) => {
	const { instance, elements = [] } = args;

	if (!instance.state === INACTIVE) {
		return args;
	}

	elements.forEach((element) => {
		attachListenerToElement({ instance, element });
	});
	return args;
};
const scheduleDraw = (args) => {
	const { instance, elements = [] } = args;
	draw(elements, instance);
};
const setActiveState = (args) => {
	const { instance, id } = args;
	if (isValidId(id)) {
		instance.state = ACTIVE;
	} else {
		instance.state = INACTIVE;
	}
	return args;
};

const handleOldId = removeListeners;
const handleId = flow(
	isActive(handleOldId, noop),
	setActiveState,
	gatherRefs,
	attachListeners,
	scheduleDraw
);
const handleListenerFired = flow(gatherRefs, scheduleDraw);
const updateForRef = flow(attachListenerToElement, gatherRefs, scheduleDraw);
const handleNewElement = flow(
	updateElement,
	gatherRefs,
	isActive(scheduleDraw, noop)
);
const handleNewFallback = (args) => {
	return isUsingFallback(scheduleDraw, noop)(args);
};
const cleanup = flow(setActiveState, removeListeners, scheduleDraw);
class ElementUpdater {
	attachedListeners = {};
	elementMap = {};
	activeElements = {};
	notifyStateChange = () => {};
	fallbackElement = null;

	handleResizeFired = debounce(handleListenerFired, 10, { trailing: true });
	handleScrollFired = debounce(handleListenerFired, 0);
	constructor({ map, element, activeId, fallbackElement, notifyStateChange }) {
		this.state = INACTIVE;
		this.refMap = map;

		if (element) {
			this.setElement(element);
		}
		if (activeId) {
			this.setId(activeId);
		}
		if (fallbackElement) {
			this.fallbackElement = fallbackElement;
		}
		if (notifyStateChange) {
			this.notifyStateChange = notifyStateChange;
		}
	}
	setId(newId) {
		if (this.activeId === newId) {
			return;
		}
		hideElement(this.element);
		this.activeId = newId;
		const action = isValidId(newId) ? handleId : isActive(cleanup, noop);
		action({ instance: this, id: newId });
	}

	setElement(newElement) {
		if (this.element === newElement) {
			return;
		}
		handleNewElement({ instance: this, newElement });
	}

	setFallback(element) {
		if (this.fallbackElement === element) {
			return;
		}
		this.fallbackElement = element;
		handleNewFallback({ instance: this });
	}
	setInvalidNotification(fn) {
		this.notifyStateChange = fn;
	}
	isActive() {
		return this.state !== INACTIVE;
	}
	notifyFunction(id, cleanupFn = () => {}) {
		return () => {
			onNextFrame(() => {
				cleanupFn();
				updateForRef({ instance: this, element: this.refMap.get(id) });
			});
		};
	}

	refExists(id) {
		const element = this.refMap.get(id);
		if (!element) {
			return false;
		}
		const current = element.ref.current;
		if (!current) {
			return false;
		}

		return !!this.elementMap[current];
	}
	set state(val) {
		if (
			this._state === ACTIVE_WITH_BAD_DIMENSIONS ||
			val === ACTIVE_WITH_BAD_DIMENSIONS
		) {
			this.notifyStateChange({ state: val });
		}
		this._state = val;
	}
	get state() {
		return this._state;
	}
}

export {
	ElementUpdater,
	ACTIVE,
	INACTIVE,
	ACTIVE_WITH_BAD_DIMENSIONS,
	ACTIVE_AND_OUT_OF_VIEW
};
