import isArray from 'lodash/isArray';

/**
 * Takes the unsorted array of components and sorts them by name in ascending order
 *
 * @param {Array} components An array of components that populate the toolbox
 * @returns {Array} sorted component list or empty array
 */
export const sortedComponents = (components = []) =>
	isArray(components)
		? components.sort((a, b) => (a.name > b.name ? 1 : -1))
		: [];
