import get from 'lodash/get';
import styles from './sn-uib-scripts-details.scss';
import { t } from 'sn-translate';
import { createCustomElement } from '@servicenow/ui-core';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-input';
import '@servicenow/now-icon';
import '@servicenow/now-tabs';
import '../sn-uib-draggable-list-item';

export const SCRIPTS_DETAILS_SCRIPT_NAME_CHANGED =
	'SN_UIB_SCRIPTS_DETAILS#NAME_CHANGED';
export const SCRIPTS_DETAILS_TRIGGERING_ELEMENT_DELETE =
	'SN_UIB_SCRIPTS_DETAILS#TRIGGERING_ELEMENT_DELETE';
export const SCRIPTS_DETAILS_TRIGGERING_ELEMENT_OPEN =
	'SN_UIB_SCRIPTS_DETAILS#TRIGGERING_ELEMENT_OPEN';
export const SCRIPTS_DETAILS_SCRIPT_INCLUDES_CHANGED =
	'SN_UIB_SCRIPTS_DETAILS#SCRIPT_INCLUDES_CHANGED';

const scriptDetailsTabItems = [
	{
		id: 'script-includes',
		label: t('Script includes')
	},
	{
		id: 'associated-components',
		label: t('Associated components')
	}
];

function createDraggableListView(elements, ref, disabled, scriptId) {
	return elements.map((element) => (
		<sn-uib-draggable-list-item
			key={element.id}
			ref={ref}
			label={element.instanceName}
			actions={[
				{
					type: 'open',
					clickActionType: SCRIPTS_DETAILS_TRIGGERING_ELEMENT_OPEN
				},
				{
					type: 'close',
					clickActionType: SCRIPTS_DETAILS_TRIGGERING_ELEMENT_DELETE,
					disabled
				}
			]}
			draggable={false}
			append-to-payload={{
				element,
				handlerTypeId: scriptId
			}}
		/>
	));
}

const associatedComponentsPartial = ({ elements, disabled, scriptId }) => {
	const ref = createRef();

	return (
		<ul className="scripts-details-triggered-elements-list">
			{!elements.length ? (
				<span className="no-elements">
					<now-icon
						size="md"
						icon="exclamation-circle-outline"
						className="no-elements-icon"
					/>
					{t('There are no associated components using this script.')}
				</span>
			) : (
				createDraggableListView(elements, disabled, ref, scriptId)
			)}
		</ul>
	);
};

const view = (state) => {
	const { properties, currentTab } = state;

	const options = {
		disabled: get(properties, 'disabled', null),
		scriptName: get(properties, 'script.name', null),
		scriptId: get(properties, 'script.id', null),
		scriptScope: get(properties, 'script.scope', null),
		clientScriptIncludeIds: get(properties, 'script.includes', []),
		elements: get(properties, 'triggeringElements', []),
		scriptIncludeElements: get(properties, 'scriptIncludeElements', [])
	};

	return (
		<div className="scripts-details-container">
			<section className="scripts-details-instance-name-section">
				<now-input
					name={options.scriptId}
					label={t('Script name')}
					manageValue
					value={options.scriptName}
					disabled={properties.disabled}
				/>
			</section>
			<section className="scripts-details-triggered-elements-section">
				<now-tabs items={scriptDetailsTabItems} selectedItem={currentTab} />
				<div>
					{currentTab === 'script-includes' ? (
						<sn-uib-scripts-includes
							client-script-include-ids={options.clientScriptIncludeIds}
							script-id={options.scriptId}
							script-scope={options.scriptScope}
							disabled={properties.disabled}
						/>
					) : (
						associatedComponentsPartial(options)
					)}
				</div>
			</section>
		</div>
	);
};

createCustomElement('sn-uib-scripts-details', {
	view,
	initialState: {
		currentTab: 'script-includes',
		stagedScriptInclude: null
	},
	properties: {
		script: {
			schema: {
				type: 'object'
			},
			default: null
		},
		triggeringElements: {
			schema: {
				type: 'array'
			},
			default: []
		},
		disabled: {
			default: false
		}
	},
	actionHandlers: {
		['NOW_INPUT#INPUT']: ({ action, dispatch }) => {
			const {
				payload: { name, fieldValue }
			} = action;

			dispatch(SCRIPTS_DETAILS_SCRIPT_NAME_CHANGED, {
				id: name,
				name: fieldValue
			});
		},
		['NOW_TABS#SELECTED_ITEM_SET']: ({ action, updateState }) => {
			action.stopPropagation();

			const {
				payload: { value }
			} = action;

			updateState({
				operation: 'set',
				path: 'currentTab',
				value
			});
		},
		['SN_UIB_CLIENT_SCRIPT_INCLUDES#CHANGED']: ({
			dispatch,
			properties,
			action
		}) => {
			const includes = action.payload;

			dispatch(SCRIPTS_DETAILS_SCRIPT_INCLUDES_CHANGED, {
				id: get(properties, 'script.id', null),
				includes
			});
		}
	},
	styles
});
