import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { ElementWrapper } from './partials';
import { defaultRenderers } from './renderers';
import './sn-uib-default-page-info';

const view = ({ properties }) => {
	const { renderFn, pageTree, elementTree } = properties;
	return (
		<div>
			{renderFn(properties)}
			<sn-uib-default-page-info
				pageTree={pageTree || {}}
				elementTree={elementTree}
			/>
		</div>
	);
};

export default createCustomElement('sn-uib-default-adapter', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		renderFn: {
			default() {
				return null;
			}
		},
		pageTree: {
			default: {}
		},
		renderers: {
			default: { ...defaultRenderers },
			selectable: true
		},
		elementWrapper: {
			default: ElementWrapper,
			selectable: true
		},
		elementTree: {
			default: {}
		}
	}
});
