import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import debounce from 'lodash/debounce';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-table-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import '@servicenow/now-typeahead';
import '../sn-uib-property-description-tooltip';

export const TABLE_PROPERTY_INPUT_TABLE_SELECTED =
	'TABLE_PROPERTY_INPUT#TABLE_SELECTED';
const EMPTY_VALUE = 'none';
const EMPTY_LABEL = 'No table';

const setInitialSelection = (updateState, tables, value) => {
	const targetTable = tables.find((table) => table.id === value);
	updateState({
		internalValue: targetTable ? targetTable.label : EMPTY_LABEL
	});
};

const view = (state) => {
	const { internalValue, properties } = state;
	const {
		label,
		tables,
		value,
		disabled,
		emptyStateBehavior,
		description,
		selectedComponentTag
	} = properties;
	const items = tables.map(({ id, label }) => ({
		id,
		name: id,
		label,
		sublabel: id
	}));
	items.unshift({ id: EMPTY_VALUE, name: EMPTY_VALUE, label: EMPTY_LABEL });

	return (
		<Fragment className="sn-uib-property-input">
			<label className="sn-uib-property-input-label">
				{label}
				{!description ? null : (
					<sn-uib-property-description-tooltip
						description={description}
						selectedComponentTag={selectedComponentTag}
					/>
				)}
			</label>
			<now-typeahead
				items={items || []}
				search="contains"
				selectedItem={value || emptyStateBehavior.value}
				manageSelectedItem
				value={internalValue}
				manageValue
				disabled={disabled}
			/>
		</Fragment>
	);
};

export default createCustomElement(config.name, {
	view,
	properties: config.properties,
	initialState: {
		internalValue: '',
		editingScript: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ updateState, properties }) => {
			setInitialSelection(updateState, properties.tables, properties.value);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			updateState,
			properties
		}) => {
			const { name, value, previousValue } = action.payload;
			if (name === 'tables' && value.length && previousValue.length === 0) {
				// If the tables are loaded after COMPONENT_DOM_READY fires, set the initial internalValue here
				setInitialSelection(updateState, value, properties.value);
			}
		},
		['NOW_TYPEAHEAD#VALUE_SET']: debounce(({ action, updateState }) => {
			action.stopPropagation();
			updateState({ internalValue: action.payload.value });
		}, 250),
		['NOW_TYPEAHEAD#SELECTED_ITEM_SET']: ({ action, properties, dispatch }) => {
			const { value } = action.payload;
			if (value && value !== EMPTY_VALUE) {
				dispatch(TABLE_PROPERTY_INPUT_TABLE_SELECTED, { value });
			} else if (value === EMPTY_VALUE) {
				dispatch(TABLE_PROPERTY_INPUT_TABLE_SELECTED, {
					value: properties.emptyStateBehavior.value
				});
			}
		},
		['NOW_TYPEAHEAD#INTERACTION_FINISHED']: ({
			action,
			properties,
			updateState
		}) => {
			action.stopPropagation();
			const { selectedItem } = action.payload;
			const targetTable =
				properties.tables.find((table) => table.id === selectedItem) || {};
			updateState({ internalValue: targetTable.label || EMPTY_LABEL });
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	},
	styles
});
