import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { memoizeDeepN, memoizeN } from 'seismic-memo-utils'; //eslint-disable-line no-unused-vars
import '@servicenow/now-content-tree';
import '@servicenow/now-icon';
import '@servicenow/now-loader';
import '@servicenow/now-popover';
import { t } from 'sn-translate';
import '../sn-uib-collapsible-panel';
import '../sn-uib-error';
import '../sn-uib-loader';
import '../sn-uib-page-management-popover';
import styles from './sn-uib-element-navigator.scss';
import { getPathForId } from '../sn-uib-page-management-panel/utils';
import './sn-uib-modals-list';
import strategyManager from '../helpers/strategyManager';
import '../sn-uib-element-navigator-popover';
import { filterElements, expandAllItems } from '../helpers/treeFilterUtils.js';
import last from 'lodash/last';
import get from 'lodash/get';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { batchShorthandUpdateState } from '../helpers/batchShorthandUpdateState';

import SearchBox from '../partials/search-box'; //eslint-disable-line no-unused-vars

export const OPEN_ELEMENTS_UPDATED =
	'SN_UIB_ELEMENT_NAVIGATOR#OPEN_ELEMENTS_UPDATED';
export const ELEMENT_SELECTED = 'SN_UIB_ELEMENT_NAVIGATOR#ELEMENT_SELECTED';
export const LOCK_ELEMENT = 'SN_UIB_ELEMENT_NAVIGATOR#LOCK_ELEMENT';
export const HIDE_ELEMENT = 'SN_UIB_ELEMENT_NAVIGATOR#HIDE_ELEMENT';
export const MODAL_POPOVER_OPENED =
	'SN_UIB_ELEMENT_NAVIGATOR#OPEN_MODAL_POPOVER';
export const INSERT_COMPONENT_SLOT =
	'SN_UIB_ELEMENT_NAVIGATOR#INSERT_COMPONENT_SLOT';
export const INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER =
	'SN_UIB_ELEMENT_NAVIGATOR#INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER';
const POPOVER_TYPES = {
	element: 'ElementActionPopover',
	modal: 'ModalAddPopover',
	slotOrComponent: 'SlotOrComponentActionPopover'
};
const SELECTED_ELEMENT_TYPES = {
	slot: 'Slot',
	element: 'Element',
	body: 'Body'
};

export const OPEN_ACTION_MENU = 'SN_UIB_ELEMENT_NAVIGATOR#OPEN_ACTION_MENU';
const OPEN_ELEMENT_CONTEXT_MENU =
	'SN_UIB_ELEMENT_NAVIGATOR#OPEN_ELEMENT_CONTEXT_MENU';

const MODAL = 'modal';

const cleanupDuplicateEntries = (elements) => {
	const lookup = [...new Set(elements.map((arr) => arr.join('/')))];
	const openElements = lookup.map((path) => path.split('/'));
	return openElements;
};

const configureModalItems = (children = [], parentComponent = {}, disabled) =>
	children.map((child) => {
		const actions = (!disabled
			? [
					{
						icon: 'lock-fill',
						label: t('Lock Editing'),
						clickActionType: LOCK_ELEMENT,
						disabled: true
					},
					{
						icon: 'eye-fill',
						label: t('Hide Element'),
						clickActionType: HIDE_ELEMENT,
						disabled: true
					},
					{
						icon: 'ellipsis-v-fill',
						label: t('Configure'),
						clickActionType: OPEN_ELEMENT_CONTEXT_MENU,
						disabled: get(child, 'variant', null) === 'placeholder'
					}
			  ]
			: []
		).filter((e) => !e.disabled);

		if (child.isRoot) {
			actions.unshift({
				icon: 'lock-fill',
				label: t('Component Locked')
			});
		}

		return {
			...child,
			actions,
			selectedSlotParentComponent: parentComponent,
			children: configureModalItems(child.children, parentComponent, disabled)
		};
	});

const configureNonmodalItems = (
	children = [],
	parentComponent = {},
	disabled
) =>
	children.map((child) => {
		const actions = (!disabled
			? [
					{
						icon: 'lock-fill',
						label: t('Lock Editing'),
						clickActionType: LOCK_ELEMENT,
						disabled: true
					},
					{
						icon: 'eye-fill',
						label: t('Hide Element'),
						clickActionType: HIDE_ELEMENT,
						disabled: true
					},
					{
						icon: 'ellipsis-v-fill',
						label: t('Configure'),
						clickActionType: OPEN_ELEMENT_CONTEXT_MENU,
						disabled: get(child, 'variant', null)
					}
			  ]
			: []
		).filter((e) => !e.disabled);
		const label = child.isRoot ? 'Body' : child.label;
		return {
			...child,
			label,
			actions,
			selectedSlotParentComponent: parentComponent,
			children: configureNonmodalItems(
				child.children,
				child.component,
				disabled
			)
		};
	});

const renderModalElementTree = (
	children,
	openElements,
	selectedItemPath,
	disabled
) => {
	return children && children.length > 0 ? (
		<Fragment>
			<section className="modals">
				<now-content-tree
					className="now-content-tree--populated -sm"
					expanded-items={openElements}
					items={[
						{
							id: 'modals',
							label: t('Modals'),
							isRoot: true,
							icon: 'modal-outline',
							children: configureModalItems(children, null, disabled)
						}
					]}
					selected-items={[selectedItemPath]}
					manage-selected-items
					manage-expanded-items
					size="sm"
					select="single"
					trigger-icon="caret"
					overflow="truncate"
					showActionsOnHover
				/>
			</section>
		</Fragment>
	) : null;
};

const renderNonmodalElementTree = (
	children,
	openElements,
	selectedItemPath,
	disabled
) => {
	return children && children.length > 0 ? (
		<Fragment>
			<section className="element-trees">
				<now-content-tree
					className="now-content-tree--populated -sm"
					expanded-items={openElements}
					items={configureNonmodalItems(children, {}, disabled)}
					selected-items={[selectedItemPath]}
					manage-selected-items
					manage-expanded-items
					size="sm"
					select="single"
					trigger-icon="caret"
					overflow="truncate"
					showActionsOnHover
				/>
			</section>
		</Fragment>
	) : null;
};

const elementActions = [
	{
		label: t('Add component before'),
		icon: 'component-outline',
		type: INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER,
		payload: {
			direction: 'left'
		}
	},
	{
		label: t('Add component after'),
		icon: 'component-outline',
		type: INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER,
		payload: {
			direction: 'right'
		}
	},
	{
		label: t('Modal'),
		icon: 'modal-outline',
		type: MODAL_POPOVER_OPENED
	}
];

const slotActions = [
	{
		label: t('Component'),
		icon: 'component-outline',
		type: INSERT_COMPONENT_SLOT
	},
	{
		label: t('Modal'),
		icon: 'modal-outline',
		type: MODAL_POPOVER_OPENED
	}
];

const renderAddContentMenu = memoizeDeepN(function (
	popOverOpened,
	modalComponents,
	isDashboardBuilder,
	disabled,
	selectedElementType,
	slotPopoverOpened,
	isModalsSelected
) {
	if (isDashboardBuilder) {
		return null;
	}

	const isBody = selectedElementType === SELECTED_ELEMENT_TYPES.body;
	const isElement = selectedElementType === SELECTED_ELEMENT_TYPES.element;
	const opened = isBody ? popOverOpened : slotPopoverOpened;
	const minWidth = isElement ? 175 : 110;
	const type = isBody ? POPOVER_TYPES.modal : POPOVER_TYPES.slotOrComponent;
	const actions = isElement ? elementActions : slotActions;
	const showModalsContent = isBody || isModalsSelected;
	const modalsList = (
		<sn-uib-modals-list
			modals={modalComponents}
			append-to-payload={{ type: POPOVER_TYPES.modal }}
		/>
	);

	return (
		<now-popover
			hide-tail
			opened={opened}
			manageOpened={true}
			constrain={{
				minWidth: minWidth
			}}
			append-to-payload={{
				type: type
			}}>
			<now-button-bare
				slot="trigger"
				className="header-add-button"
				label={t('Add')}
				icon-start="plus-fill"
				size="sm"
				variant="tertiaty"
				disabled={disabled}
				appendToPayload={{
					buttonType: 'add-content-menu-popover-button',
					type: type
				}}
			/>
			<sn-uib-element-navigator-popover
				actions={actions}
				slot={disabled ? null : 'content'}
				showModalsContent={showModalsContent}
				modalsList={modalsList}
			/>
		</now-popover>
	);
});

const view = (state, { dispatch }) => {
	const {
		properties: {
			selectedItem,
			components,
			modalElements,
			nonModalElements,
			searchTerm,
			isDashboardBuilder,
			disabled,
			selectedElementType
		},
		openElements,
		popOverOpened,
		slotPopoverOpened,
		isModalsSelected
	} = state;

	//TODO: When Modal and NonModal are combined into single tree, should be able to do selectedItemPath on elements
	const selectedItemPath = getPathForId(nonModalElements, selectedItem);
	const modalComponents = components.filter(
		(component) => component.category === MODAL
	);

	const addContentActionMenu = renderAddContentMenu(
		popOverOpened,
		modalComponents,
		isDashboardBuilder,
		disabled,
		selectedElementType,
		slotPopoverOpened,
		isModalsSelected
	);

	return (
		<div className="element-navigator-container">
			<div className="element-navigator">
				<div className="header">
					<span className="p1-panel-header-text">{t('Content')}</span>
					{addContentActionMenu}
				</div>
				<div className="element-search">
					<SearchBox
						placeholder={t('Search')}
						value={searchTerm}
						appendToPayload={{ id: 'element-search' }}
						dispatch={dispatch}
					/>
				</div>
				{renderModalElementTree(
					modalElements,
					openElements,
					selectedItemPath,
					disabled
				)}
				{renderNonmodalElementTree(
					nonModalElements,
					openElements,
					selectedItemPath,
					disabled
				)}
			</div>
			<now-popover
				manage-opened
				hide-tail
				opened={state.actionPopoverOpen}
				positionTarget={state.actionPopoverTarget}
				positions={[{ target: 'top-end', content: 'top-start' }]}
				constrain={{
					width: 150
				}}
				key={'action-pop-over-elemet-navigator'}
				append-to-payload={{
					target: state.actionPopoverTarget,
					type: POPOVER_TYPES.element
				}}>
				{state.actionPopoverOpen && state.actionPopoverActions.length ? (
					<sn-uib-page-management-popover
						actions={state.actionPopoverActions}
						slot="content"
					/>
				) : null}
			</now-popover>
		</div>
	);
};
const selectItemActionHandler = ({ action, dispatch, updateState }) => {
	action.stopPropagation();
	batchShorthandUpdateState(
		{
			actionPopoverOpen: false,
			actionPopoverTarget: null,
			actionPopoverActions: []
		},
		updateState
	);
	const {
		id,
		label,
		component,
		elementId,
		selectedSlotParentComponent
	} = action.payload.item;
	if (component && component.tag) {
		dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
			type: 'element',
			elementId: id,
			elementInstanceName: label,
			component
		});
	} else if (get(action, 'payload.item.variant', false)) {
		dispatch('SN_UIB_ELEMENT_NAVIGATOR#ADD_COMPONENT', {
			item: get(action, 'payload.item', {}),
			domTarget: get(action, 'meta.event.target', null)
		});
	} else {
		dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
			type: 'slot',
			id,
			selectedSlotName: label,
			selectedSlotParent: elementId,
			selectedSlotParentComponent,
			unselectElement: true
		});
	}
};

createCustomElement('sn-uib-element-navigator', {
	view,
	styles,
	initialState: {
		popOverOpened: false,
		slotPopoverOpened: false,
		actionPopoverOpen: false,
		actionPopoverTarget: null,
		actionPopoverActions: [],
		openElements: [],
		isModalsSelected: false
	},
	properties: {
		elements: {
			default: {
				children: []
			}
		},
		modalElements: {
			computed({ properties: { elements, searchTerm } }) {
				let modalElements = elements.children.filter((c) => c.isModal);
				modalElements = filterElements(modalElements, searchTerm, 'label');
				return modalElements;
			}
		},
		nonModalElements: {
			computed({ properties: { elements, searchTerm, disabled } }) {
				let nonModalElements = [
					{ ...elements, children: elements.children.filter((c) => !c.isModal) }
				];
				nonModalElements = filterElements(
					nonModalElements,
					searchTerm,
					'label'
				);

				return nonModalElements;
			}
		},

		components: {
			default: []
		},
		selectedItem: {
			default: ''
		},
		searchTerm: {
			default: ''
		},
		isDashboardBuilder: {
			default: false
		},
		disabled: {
			default: false
		},
		selectedSlotName: {
			default: '@ui-state/selectedSlotName'
		},
		selectedSlotParent: {
			default: '@ui-state/selectedSlotParent'
		},
		selectedElementType: {
			computed: memoizeN(function ({
				properties: {
					selectedItem,
					selectedSlotName,
					selectedSlotParent,
					elements,
					nonModalElements
				}
			}) {
				if (!selectedSlotName && !selectedSlotParent) {
					const selectedItemPath = getPathForId(nonModalElements, selectedItem);
					if (
						selectedItem === get(elements, 'elementId', '') ||
						selectedItemPath.length === 0
					) {
						// The body or nothing is selected, render only the add modal modal
						return SELECTED_ELEMENT_TYPES.body;
					} else {
						// Element is selected, render element menu
						return SELECTED_ELEMENT_TYPES.element;
					}
				} else {
					// Slot is selected, render slot menu
					return SELECTED_ELEMENT_TYPES.slot;
				}
			})
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: ({ updateState, properties }) => {
			const { elements } = properties;
			if (!elements || !elements.id) {
				return;
			}
			let openElements = strategyManager.get(
				'ELEMENT_TREE_EXPAND',
				elements.id
			);
			if (!openElements) {
				openElements = expandAllItems([elements]);
			}
			updateState({ openElements });
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			updateState
		}) => {
			const {
				payload: { name }
			} = action;
			if (name === 'elements') {
				const { elements, selectedItem } = properties;
				if (!elements || !elements.id) {
					return;
				}
				let curOpenedElements = strategyManager.get(
					'ELEMENT_TREE_EXPAND',
					elements.id
				);
				const allExpanded = expandAllItems([elements]);
				const newSelection = allExpanded.find(
					(path) => last(path) === selectedItem
				);
				const selectedTree = [];
				if (newSelection) {
					newSelection.forEach(
						(node, i) => (selectedTree[i] = newSelection.slice(0, i + 1))
					);
				}
				if (!curOpenedElements) {
					curOpenedElements = allExpanded;
				}

				const openedElements = [...curOpenedElements, ...selectedTree];
				const value = cleanupDuplicateEntries(openedElements);
				updateState({
					path: 'openElements',
					value,
					operation: 'set'
				});
			}
		},
		[OPEN_ELEMENTS_UPDATED]: ({ action, state, properties, updateState }) => {
			const openElements = cleanupDuplicateEntries(action.payload.value);
			const { elements } = properties;
			if (!elements || !elements.id) {
				console.error('what in the properties?', properties);
				return;
			}
			strategyManager.set('ELEMENT_TREE_EXPAND', elements.id, openElements);
			batchShorthandUpdateState(
				{
					...state,
					openElements
				},
				updateState
			);
		},
		[MODAL_POPOVER_OPENED]: ({ action, updateState, dispatch }) => {
			action.stopPropagation();
			updateState({
				isModalsSelected: true
			});
		},
		'NOW_POPOVER#OPENED_SET': ({ action, updateState, state }) => {
			const {
				payload: { value, type }
			} = action;
			if (type === POPOVER_TYPES.element) {
				batchShorthandUpdateState(
					{
						actionPopoverOpen: value,
						actionPopoverTarget: null,
						actionPopoverActions: []
					},
					updateState
				);
			} else if (type === POPOVER_TYPES.modal) {
				updateState({
					popOverOpened: value,
					slotPopoverOpened: value
				});
			} else if (type === POPOVER_TYPES.slotOrComponent) {
				updateState({
					slotPopoverOpened: value,
					isModalsSelected: !value ? value : state.isModalsSelected
				});
			}
		},
		'NOW_CONTENT_TREE#ITEM_CLICKED': selectItemActionHandler,
		'NOW_CONTENT_TREE#EXPANDED_ITEMS_SET': ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(OPEN_ELEMENTS_UPDATED, action.payload);
		},
		'NOW_BUTTON_BARE#CLICKED': ({ action, dispatch }) => {
			if (
				get(action, 'payload.buttonType', '') ===
				'add-content-menu-popover-button'
			) {
				action.stopPropagation();
				dispatch('NOW_POPOVER#OPENED_SET', {
					type: get(action, 'payload.type', '')
				});
			}
		},
		'NOW_BUTTON_ICONIC#CLICKED': ({ action }) => {
			action.stopPropagation();
		},
		[OPEN_ELEMENT_CONTEXT_MENU]: ({ action, dispatch }) => {
			const { actionTarget, item } = action.payload;
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_PROPERTIES', {
				item,
				actionTarget,
				positions: [
					{ target: 'top-end', content: 'top-start' },
					{ target: 'top-end', content: 'bottom-start' }
				]
			});
			dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: true });
		},
		'SN_UIB_ELEMENT_NAVIGATOR#ADD_COMPONENT': ({ action, dispatch }) => {
			const { domTarget } = action.payload;
			const {
				parentElementId: selectedSlotParent,
				parentSlotName: selectedSlotName
			} = action.payload.item;

			dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
				selectedSlotParent,
				selectedSlotName,
				hideTail: true,
				onSelect: ({ dispatch, component }) => {
					if (selectedSlotName && selectedSlotParent) {
						dispatch('EMPTY_SLOT#DROPPED', {
							slotName: selectedSlotName,
							parentId: selectedSlotParent,
							dataTransfer: component
						});
					}
					dispatch('UIB_ELEMENT_CONTEXT_MENU#SET_OPENED', { value: false });
				}
			});
			dispatch('UIB_TOOLBOX#SET_POSITION', {
				toolboxPositionTarget: domTarget
			});
			dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
		},
		[INSERT_COMPONENT_SLOT]: ({
			action,
			dispatch,
			properties,
			updateState
		}) => {
			const { positionTarget } = action.payload;
			const { selectedSlotName, selectedSlotParent } = properties;
			const updateElementNavStateToClosePopover = () =>
				updateState({ slotPopoverOpened: false });
			dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
				selectedSlotParent,
				selectedSlotName,
				hideTail: true,
				onSelect: ({ dispatch, component }) => {
					if (selectedSlotName && selectedSlotParent) {
						dispatch('EMPTY_SLOT#DROPPED', {
							slotName: selectedSlotName,
							parentId: selectedSlotParent,
							dataTransfer: component
						});
					}
					updateElementNavStateToClosePopover();
				}
			});
			dispatch('UIB_TOOLBOX#SET_POSITION', {
				toolboxPositionTarget: positionTarget
			});
			dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
		},
		[INSERT_COMPONENT_SLOT_BEFORE_OR_AFTER]: ({
			action,
			dispatch,
			properties,
			updateState
		}) => {
			const { direction, positionTarget } = action.payload;
			const { selectedItem } = properties;
			dispatch('SN_UIB_ELEMENT_PLACE_CONTROL#ADD_REQUESTED', {
				elementId: selectedItem,
				direction
			});
			const updateElementNavStateToClosePopover = () =>
				updateState({ slotPopoverOpened: false });
			dispatch('UIB_TOOLBOX#SET_PROPERTIES', {
				selectedSlotParent: null,
				selectedSlotName: null,
				hideTail: true,
				onClose: ({ dispatch }) => {
					dispatch('SN_UIB_ELEMENT_PLACE_CONTROL#CANCEL_ADD_REQUESTED');
				},
				onSelect: ({ dispatch, component }) => {
					dispatch('UIB_DND_DROPZONE#DROPPED', {
						dataTransfer: component.id,
						elementId: selectedItem,
						direction
					});
					updateElementNavStateToClosePopover();
				}
			});
			dispatch('UIB_TOOLBOX#SET_POSITION', {
				toolboxPositionTarget: positionTarget
			});
			dispatch('UIB_TOOLBOX#SET_OPENED', { value: true });
		},
		NONMODAL_ELEMENTS_UPDATED: ({ properties, dispatch }) => {
			const { nonModalElements } = properties;
			dispatch(OPEN_ELEMENTS_UPDATED, {
				value: expandAllItems(nonModalElements)
			});
		}
	}
});
