import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-placeholder.scss';
import beardManSittingGraphic from './svgs/beard-man-sitting.svg';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

const view = (state) => {
	const { dropZoneActive, pageType } = state.properties;

	return (
		<div
			class={{
				'sn-uib-placeholder': true,
				'drop-zone-active': dropZoneActive
			}}>
			{dropZoneActive ? (
				<span className="sn-uib-placeholder-text--active">
					Drop component here
				</span>
			) : (
				<Fragment>
					<img
						src={beardManSittingGraphic}
						className="beard-man-sitting"
						alt="beard man sitting"
						height="200"
					/>
					<span className="sn-uib-placeholder-text">
						{t('Your {0} does not have any components', pageType)}
					</span>
					<span className="sn-uib-placeholder-sub-text">
						{t('Drag and drop a component to start building.')}
					</span>
				</Fragment>
			)}
		</div>
	);
};

export default createCustomElement('sn-uib-placeholder', {
	view,
	properties: {
		dropZoneActive: {
			default: false
		},
		pageType: {
			default: 'page'
		}
	},
	styles
});
