import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import styles from './sn-uib-url-preview.scss';

const DEFAULT_PATH_NAME = t('path-name');

const getPathName = (path, page) => {
	switch (typeof path) {
		case 'boolean':
			return DEFAULT_PATH_NAME;
		case 'string':
			return path ? path : DEFAULT_PATH_NAME;
		default:
			return page ? page.replace(/\//g, '') : '';
	}
};

const renderUrlPart = (
	state,
	updateState,
	pathName,
	value,
	defaultValue,
	tooltipContent,
	index = 0
) => {
	const refKey = `targetRef-${pathName}-${index}`;
	const tooltipId = `tooltip-${pathName}-${index}`;
	const setTargetRef = (ref) =>
		setTimeout(() =>
			updateState({
				[refKey]: ref
			})
		);
	const isDefaultValue = defaultValue === value;
	const urlPartClass = {
		[pathName]: true,
		'has-tooltip': !!tooltipContent,
		'default-value': isDefaultValue
	};
	const isRoot = pathName === 'root';
	return (
		<Fragment>
			{isRoot ? null : <div>/</div>}
			<div
				class={urlPartClass}
				hook-insert={({ elm }) => setTargetRef(elm)}
				hook-destroy={() => setTargetRef(null)}
				aria-describedby={tooltipId}>
				{value}
			</div>
			{tooltipContent ? (
				<now-tooltip
					id={tooltipId}
					target-ref={state[refKey]}
					content={tooltipContent}
				/>
			) : null}
		</Fragment>
	);
};

const renderParams = (state, updateState, parameters) => {
	return (
		<Fragment>
			<div>{`/params`}</div>
			{parameters.map(({ name }, index) => {
				const displayIndex = index + 1;
				const defaultValue = t('optionalParameter{0}', displayIndex);
				const parmaterName = name || defaultValue;
				return (
					<Fragment>
						{renderUrlPart(
							state,
							updateState,
							`parameter`,
							`${parmaterName}`,
							defaultValue,
							t('Optional parameter {0} name', displayIndex),
							displayIndex
						)}
						{renderUrlPart(
							state,
							updateState,
							'parameter-value',
							`{${parmaterName}Value}`,
							`{${defaultValue}Value}`,
							t('Optional parameter {0} value', displayIndex),
							displayIndex
						)}
					</Fragment>
				);
			})}
		</Fragment>
	);
};

const renderFields = (state, updateState, fields) => {
	return (
		<Fragment>
			{fields.map(({ name, label }, index) => {
				const displayIndex = index + 1;
				const defaultValue = t('requiredParameter{0}', displayIndex);
				const displayName = name || label || defaultValue;
				return (
					<Fragment>
						{renderUrlPart(
							state,
							updateState,
							'field-value',
							`{${displayName}}`,
							`{${defaultValue}}`,
							t('Required parameter {0}', displayIndex),
							displayIndex
						)}
					</Fragment>
				);
			})}
		</Fragment>
	);
};

const view = (state, { updateState }) => {
	const {
		properties: { runtimeUrl, path, parameters, fields, isNewPage }
	} = state;
	const { root, appsURL, page } = runtimeUrl;
	const pathName = getPathName(path, page);
	const hasParamsFromProps = !isEmpty(parameters);
	const hasParamsFromRuntime = !isEmpty(runtimeUrl.params);
	const hasParameters =
		hasParamsFromProps || (!parameters && hasParamsFromRuntime);
	const urlParams = hasParamsFromProps
		? Object.values(parameters)
		: Object.values(get(runtimeUrl, 'params', {}));
	const hasFieldsFromProps = !isEmpty(fields);
	const hasFieldsFromRuntime = !isEmpty(runtimeUrl.fields);
	const hasFields = hasFieldsFromProps || (!fields && hasFieldsFromRuntime);
	const urlFields = hasFieldsFromProps ? fields : runtimeUrl.fields;

	return (
		<div className="url-preview-container">
			<div className="label">{t('URL preview')}</div>
			<div className="url-preview">
				{renderUrlPart(state, updateState, 'root', `${root}${appsURL}`, '')}
				{renderUrlPart(
					state,
					updateState,
					'path',
					pathName,
					'path-name',
					'Path'
				)}
				{hasFields && !isNewPage
					? renderFields(state, updateState, urlFields)
					: null}
				{hasParameters && !isNewPage
					? renderParams(state, updateState, urlParams)
					: null}
			</div>
		</div>
	);
};

export default createCustomElement('sn-uib-url-preview', {
	view,
	styles,
	properties: {
		runtimeUrl: {
			default: {}
		},
		path: {
			default: null
		},
		parameters: {
			default: null
		},
		fields: {
			default: null
		},
		isNewPage: {
			default: false
		}
	},
	actionHandlers: {}
});
