import { get, find } from 'lodash';
import { propertiesComponentConfig as supportedTypeComponents } from './supported-type-components';
import './sn-uib-property-config-wrapper';

/**
 * Takes a component property definition object and filters out the unsupported properties
 * @param {string[]} supportedPropertyKeys - String array of supported properties of a property editor component
 * @param {Array.<Object>} componentPropertyDefinition - Original component property definition object to filter
 * @returns {Object} The filtered component definition object
 *
 * @example
 *
 * const supportedKeys = ["label", "value"];
 * const propDefinition = {
 * 	"label": "Color",
 * 	"type": "String",
 * 	"id": "123456789",
 * 	"value": "#4df298",
 * 	"required": false
 *  }
 *
 *  componentPropertyFilter(supportedKeys, propDefinition);
 *  // Returns
 * 	// {
 * 	//   "label": "Color",
 * 	//   "value": "#4df298"
 *  // }
 */
export const componentPropertyFilter = (
	supportedPropertyKeys,
	componentPropertyDefinition
) => {
	return Object.keys(componentPropertyDefinition)
		.filter((key) => supportedPropertyKeys.includes(key))
		.filter((key) => componentPropertyDefinition.hasOwnProperty(key))
		.reduce((obj, key) => {
			// deal with known seismic issue with props named 'value' (DEF0043171/PRB1340498)
			const computedKey = key === 'value' ? 'props-value' : key;
			obj[computedKey] = componentPropertyDefinition[key];
			return obj;
		}, {});
};

/**
 * Wraps the passed in child with the property type input container
 * @param {Object} child - The inner property editor component
 * @return {HTMLElement}
 */
export const wrapPropertyContainer = (child, index) => {
	const { id } = get(child, 'data.props', {});
	return (
		<sn-uib-property-config-wrapper
			key={id}
			child={child}
			isFirstElement={index === 0}>
			{child}
		</sn-uib-property-config-wrapper>
	);
};

/**
 *  An object whose keys are the interfaces and values are the array of property definitions to be passed to the config component tag
 * @typedef interfaceDefinitionsMap
 * @property {Object.<string, []>} interfaceDefinition
 */

/**
 * The interface name and its corresponding component tag
 * @typedef componentInterface
 * @property {string} interfaceName
 * @property {string}  componentId
 */

/**
 *  A map containing component interfaces
 * @typedef {Object.<string, componentInterface>} componentInterfaceMap
 */

/**
 * Groups interfaces and their props into a map to handle 2 different methods of creating components: ones with interfaces and ones without
 * @param {Array} componentPropertyDefinitions
 * @param {componentInterfaceMap} interfaceConfigurationComponent
 * @param {string} elementId
 * @returns {HTMLElement[]}
 */
export const snPropertiesComponentLoader = (
	componentPropertyDefinitions,
	dataBindingEnabled,
	container,
	interfaceConfigurationComponent,
	elementId,
	contextBindings,
	appConfig,
	toggleProp,
	scriptTemplate,
	scriptingEnabled,
	selectedComponentTag,
	defaultedFieldTypes
) => {
	const definitionsWithNoInterfaces = [];
	const interfaceDefinitionsMap = {};

	for (const definition of componentPropertyDefinitions) {
		if (
			definitionHasInterface(
				definition.interfaceApiName,
				interfaceConfigurationComponent
			)
		) {
			const propList =
				interfaceDefinitionsMap[definition.interfaceApiName] || [];
			propList.push(definition);
			interfaceDefinitionsMap[definition.interfaceApiName] = propList;
		} else {
			definitionsWithNoInterfaces.push({
				...definition,
				autofocus: toggleProp === definition.id
			});
		}
	}

	const components = createComponents(
		definitionsWithNoInterfaces,
		dataBindingEnabled,
		container,
		contextBindings,
		scriptTemplate,
		scriptingEnabled,
		selectedComponentTag,
		defaultedFieldTypes
	);
	const {
		interfacedComponents,
		interfaceComponentFirst
	} = createComponentsWithInterfaces(
		interfaceDefinitionsMap,
		interfaceConfigurationComponent,
		elementId,
		container,
		appConfig
	);

	if (interfacedComponents.length === 1 && interfaceComponentFirst) {
		return [...interfacedComponents, ...components];
	}

	return [...components, ...interfacedComponents];
};

/**
 * Takes in an interface name and performs a lookup on the interfaceConfigurationComponent.
 * If there is an interface declared on a property with no match, it will render like a normal property after it fails this test
 * @param {string} interfaceName
 * @param {componentInterfaceMap} interfaceConfigurationComponent
 * @returns {boolean}
 */

const definitionHasInterface = (
	interfaceName,
	interfaceConfigurationComponent
) => {
	if (!!interfaceName && !!interfaceConfigurationComponent) {
		return !!interfaceConfigurationComponent[interfaceName];
	}
	return false;
};

/**
 * Takes a list of component property definitions and returns the matching component from the interface registry, if any exist
 * @param {interfaceDefinitionsMap} interfaceDefinitionsMap
 * @param {componentInterfaceMap} interfaceConfigurationComponent
 * @param {HTMLElement} container the properties editor container that now-dropdown-panel should be constrained within, eg. panel-fit-props
 * @returns {HTMLElement[]}
 */
const createComponentsWithInterfaces = (
	interfaceDefinitionsMap,
	interfaceConfigurationComponent,
	elementId,
	container,
	appConfig
) => {
	const components = [];
	let interfaceComponentFirst = false;
	for (const key of Object.keys(interfaceDefinitionsMap)) {
		const componentInterface = interfaceConfigurationComponent[key];

		if (componentInterface && componentInterface.componentId) {
			// eslint-disable-next-line no-unused-vars
			const TagName = componentInterface.componentId;
			const props = interfaceDefinitionsMap[key];
			const componentProps = props.reduce(
				(acc, prop) => {
					acc[prop.id] = prop.value;
					return acc;
				},
				{ appConfig }
			);

			let description;

			if (TagName === 'sn-uib-modal-picker-config') {
				description = (props.find(({ id }) => id === 'modalId') || {})
					.description;
			}

			if (
				TagName === 'sn-uib-route-config' ||
				TagName === 'sn-uib-modal-picker-config'
			) {
				interfaceComponentFirst = true;
			}

			components.push(
				<article className="interfaced-property-type-input-container">
					<TagName
						description={description}
						{...componentProps}
						append-to-payload={{ elementId }}
						component-props={componentProps}
						container={container}
					/>
				</article>
			);
		}
	}
	return { interfacedComponents: components, interfaceComponentFirst };
};

/**
 * Takes a list of component property definitions, maps each one to the appropriate property editor component and returns the corresponding component
 * @param {Array} componentPropertyDefinitions
 * @returns {HTMLElement[]}
 */
const createComponents = (
	componentPropertyDefinitions,
	dataBindingEnabled,
	container,
	contextBindings,
	scriptTemplate,
	scriptingEnabled,
	selectedComponentTag,
	defaultedFieldTypes
) => {
	return componentPropertyDefinitions
		.filter((property) => !property.readOnly || property.readOnly === 'false')
		.map((componentPropertyDefinition, index) => {
			// If a component property comes in without a mapped configuration
			// or if it is dynamically configured, then default to string
			const isStatic =
				typeof componentPropertyDefinition.isStatic === 'boolean'
					? componentPropertyDefinition.isStatic
					: true;

			const isScripted = componentPropertyDefinition.isScripted;
			const targetPropertyType = componentPropertyDefinition.type.toLowerCase();
			let componentConfig = supportedTypeComponents['string'];
			if (
				isStatic &&
				!isScripted &&
				!defaultedFieldTypes.includes(targetPropertyType)
			) {
				componentConfig =
					supportedTypeComponents[targetPropertyType] ||
					supportedTypeComponents['string'];
			} else if (!isStatic) {
				componentConfig = supportedTypeComponents['dynamic'];
			} else if (isScripted) {
				componentConfig = supportedTypeComponents['string'];
			}

			// Name of property editor component tag to render
			// eslint-disable-next-line no-unused-vars
			const TagName = componentConfig.name;

			// Get all of the currently supported property keys of this component
			const supportedPropertyKeys = Object.keys(componentConfig.properties);

			// Need to make more dynamic to extract all supported properties from given property component
			const filteredProps = componentPropertyFilter(
				supportedPropertyKeys,
				componentPropertyDefinition
			);

			const { id } = componentPropertyDefinition;

			return wrapPropertyContainer(
				<TagName
					key={componentPropertyDefinition.id}
					{...filteredProps}
					container={container}
					dataBindingEnabled={dataBindingEnabled}
					bindings={isStatic ? [] : contextBindings}
					append-to-payload={{ id }}
					scriptTemplate={scriptTemplate}
					scriptingEnabled={scriptingEnabled}
					selectedComponentTag={selectedComponentTag}
					slot="content"
				/>,
				index
			);
		});
};
