import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-uib-component-navigator.scss';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import '../sn-uib-error';
import '@servicenow/now-loader';
import '@servicenow/now-input';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '../sn-uib-component-section';
import '../sn-uib-aria-live';
import debounce from 'lodash/debounce';
import isEmpty from 'lodash/isEmpty';

// eslint-disable-next-line no-unused-vars
import SearchBox from '../partials/search-box';

import { sortedComponents } from '../helpers/componentUtils';

import { t } from 'sn-translate';
import { filterComponents } from './utils';

const { DevxKeyFocusBehavior: keyFocusBehavior } = DevxKeyFocusBehavior;

const renderError = (error) => {
	if (error) {
		return (
			<div className={'error-message'}>
				<sn-uib-error
					message={t('There was an issue loading your components')}
				/>
			</div>
		);
	} else {
		return null;
	}
};
const view = (state, { dispatch }) => {
	const {
		searchTerm,
		properties: {
			components,
			filteredComponents,
			disabled,
			isComponentsLoading,
			error
		}
	} = state;

	if (isComponentsLoading && components.length === 0) {
		return (
			<div className={'loader'}>
				<now-loader />
				<p className={'loader-label'}>{t('Loading...')}</p>
			</div>
		);
	} else {
		return (
			<div className="wrapper">
				<div className="header">
					<span className="p1-panel-header-text">{t('Components')}</span>
				</div>
				<section className="component-list">
					<div className="component-search">
						<SearchBox
							placeholder={t('Search')}
							value={searchTerm}
							appendToPayload={{ id: 'component-search' }}
							dispatch={dispatch}
						/>
					</div>

					{renderError(error)}
					<sn-uib-aria-live>
						<sn-uib-component-section
							disabled={disabled}
							components={sortedComponents(components)}
							style={
								!isEmpty(searchTerm) && {
									visibility: 'hidden',
									position: 'absolute'
								}
							}
						/>
						{!isEmpty(searchTerm) ? (
							<sn-uib-component-section
								disabled={disabled}
								components={filteredComponents}
							/>
						) : null}
					</sn-uib-aria-live>
				</section>
			</div>
		);
	}
};

createCustomElement('sn-uib-component-navigator', {
	view,
	initialState: {
		searchTerm: ''
	},

	properties: {
		components: {
			default: []
		},

		filteredComponents: {
			computed({ searchTerm, properties: { components } }) {
				const filteredComponents = filterComponents(components, searchTerm);
				return filteredComponents;
			}
		},

		/**
		 * Whether the element is disabled
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		isComponentsLoading: {
			default: false
		},
		error: {
			default: true
		}
	},
	behaviors: [keyFocusBehavior],

	actionHandlers: {
		'NOW_INPUT#INPUT': {
			effect: debounce(({ action, updateState }) => {
				const { fieldValue, id } = action.payload;
				if (id === 'component-search') {
					updateState({
						searchTerm: fieldValue
					});
				}
			}, 300),
			stopPropagation: true
		}
	},

	styles
});
