import interact from 'interactjs';
import throttle from 'lodash/throttle';

import {
	DROP_BEHAVIOR_DROPPED,
	DROP_BEHAVIOR_DRAGGED_OVER,
	DROP_BEHAVIOR_DRAG_LEFT,
	DROP_BEHAVIOR_DRAG_ENTERED
} from './uib-drop-behavior';

const dropmove = throttle((e, dispatch, data) => {
	dispatch(DROP_BEHAVIOR_DRAGGED_OVER, { data, event: e });
}, 50);

const attachInteract = ({ el, dispatch, data }) => {
	interact(el)
		.dropzone({
			ondrop: (e) => {
				e.stopPropagation();
				e.stopImmediatePropagation();
				if (e.dragEvent.interactId === data.elementId) {
					e.stopPropagation();
					e.stopImmediatePropagation();
					e.preventDefault();
					e.reject();
					return;
				}

				dispatch(DROP_BEHAVIOR_DROPPED, {
					dataTransfer: e.dragEvent.transfer,
					data
				});
			}
		})
		.on('dragenter', (e) => {
			e.stopPropagation();
			e.stopImmediatePropagation();

			dispatch(DROP_BEHAVIOR_DRAG_ENTERED, e);
		})
		.on('dropmove', (e) => {
			e.stopPropagation();
			e.stopImmediatePropagation();
			if (e.dragEvent.interactId === data.elementId) {
				e.preventDefault();
				e.reject();
				return;
			}

			dropmove(e, dispatch, data);
		})
		.on('dragleave', (e) => {
			e.stopPropagation();
			e.stopImmediatePropagation();

			dispatch(DROP_BEHAVIOR_DRAG_LEFT, e);
		});
};

const removeInteract = ({ el }) => {
	interact(el).unset();
};
export { attachInteract, removeInteract };
