import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-c3-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import { t } from 'sn-translate';

import '@servicenow/now-button';
import {
	PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED,
	PROPERTY_INPUT_SCRIPT_LINK_TOGGLED
} from '../sn-uib-property-input-script-link-toggle';
import '../sn-uib-property-description-tooltip';

export const C3_PROPERTY_VALUE_CHANGED = 'C3_PROPERTY_INPUT#VALUE_CHANGED';
export const C3_PROPERTY_OPENED_SET = 'SN_UIB_C3_PROPERTY_INPUT#OPENED_SET';

const view = (state, dispatch) => {
	const {
		id,
		label,
		value,
		c3Config,
		icon,
		url,
		badge,
		typeMeta,
		disabled,
		scrollable,
		redirectFocusTo,
		emptyStateBehavior,
		description,
		selectedComponentTag
	} = state.properties;

	if (c3Config && id === c3Config.propertyId) {
		dispatch('C3_PROPERTY_INPUT#CHECK_PROP_CHANGE', { value: c3Config.value });
	}

	return (
		<Fragment>
			<div className="sn-uib-c3-property-input-container">
				<label className="sn-uib-property-input-label">
					{label}
					{!description ? null : (
						<sn-uib-property-description-tooltip
							description={description}
							selectedComponentTag={selectedComponentTag}
						/>
					)}
				</label>
				{badge ? <span>{badge}</span> : null}
				<now-button
					icon={icon}
					size="md"
					variant="secondary"
					label={t('Edit {0}', label.toLowerCase())}
					on-click={() => {
						if (disabled) {
							return;
						}
						const valueToEmit = value ? value : emptyStateBehavior.value;

						dispatch(C3_PROPERTY_OPENED_SET, {
							value: valueToEmit,
							label,
							url,
							meta: typeMeta,
							scrollable,
							redirectFocusTo
						});
					}}
					disabled={disabled}
				/>
			</div>
		</Fragment>
	);
};

createCustomElement(config.name, {
	view,
	initialState: {
		previousValue: null,
		editingScript: false
	},
	actionHandlers: {
		// Since you cannot use Seismic PROPS_CHANGED action with selectable properties....
		'C3_PROPERTY_INPUT#CHECK_PROP_CHANGE': {
			effect: ({ action, updateState }) => {
				action.stopPropagation();
				updateState({ previousValue: action.payload.value });
			},
			interceptors: [
				(context) => {
					return {
						...context,
						effects: [
							...context.effects,
							{
								effect: ({ action, state, dispatch }) => {
									if (action.payload.value !== state.previousValue) {
										dispatch(C3_PROPERTY_VALUE_CHANGED, action.payload);

										if (state.previousValue !== null) {
											dispatch('SN_UIB_C3_PROPERTY#CLEAR_C3_CONFIG');
										}
									}
								},
								args: [context.coeffects]
							}
						]
					};
				}
			]
		},
		[PROPERTY_INPUT_SCRIPT_LINK_TOGGLED]: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({ editingScript: action.payload.editing });
		},
		[PROPERTY_INPUT_SCRIPT_BUTTON_SCRIPT_UPDATED]: ({ updateState }) => {
			updateState({ editingScript: false });
		}
	},
	properties: config.properties,
	styles
});
