function filterItem(item, search) {
	const filteredChildren = (item.children || [])
		.map((d) => filterItem(d, search))
		.filter((d) => d !== undefined);

	const itemLabel = item.label && item.label.toLowerCase();

	if (itemLabel.indexOf(search.toLowerCase()) > -1 || filteredChildren.length) {
		return {
			...item,
			children: filteredChildren
		};
	}

	return undefined;
}

function hideChildren(item, numberOfItems) {
	const totalChildren = item.children || [];
	const children = totalChildren
		.map((d) => hideChildren(d, numberOfItems))
		.slice(0, numberOfItems);

	return {
		...item,
		totalChildren,
		childrenAvailable: totalChildren.length,
		children: children
	};
}

export function filterContentTree(items, search) {
	if (!search) {
		return items;
	}

	return items.map((d) => filterItem(d, search)).filter((d) => d !== undefined);
}

export function filterAndHideItems(items, search, numberOfItems) {
	const filteredItems = filterContentTree(items, search);

	return filteredItems.map((i) => hideChildren(i, numberOfItems));
}

function addExpandedItems(expandedItems, item, parentId) {
	const expandedId = parentId.concat(item.id);
	expandedItems.push(expandedId);
	(item.children || []).forEach((d) =>
		addExpandedItems(expandedItems, d, expandedId)
	);
}

export function getExpandedItems(items) {
	const expandedItems = [];
	items.forEach((item) => addExpandedItems(expandedItems, item, []));

	return expandedItems;
}

function findSelectedItemPath(currentItem, selectedItemId) {
	if (currentItem.id === selectedItemId) {
		return [selectedItemId];
	}

	for (const childItem of currentItem.children || []) {
		const itemFound = findSelectedItemPath(childItem, selectedItemId);

		if (itemFound) {
			itemFound.unshift(currentItem.id);
			return itemFound;
		}
	}

	return undefined;
}

export function getSelectedItemPath(items, selectedItem) {
	if (items && selectedItem && selectedItem.id) {
		for (const currenItem of items) {
			const itemFound = findSelectedItemPath(currenItem, selectedItem.id);
			if (itemFound) {
				return [itemFound];
			}
		}
	}

	return [];
}

export function findChildrenPath(items, path, index = 0) {
	const itemIndex = items.findIndex((i) => i.id === path[index]);
	if (itemIndex > -1) {
		if (index === path.length - 1) {
			return `[${itemIndex}].children`;
		}

		const childrenIndexPath = findChildrenPath(
			items[itemIndex].children,
			path,
			index + 1
		);
		if (childrenIndexPath) {
			return `[${itemIndex}].children${childrenIndexPath}`;
		}
	}

	return undefined;
}
