import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom';
import {
	objectToCssElementBlock,
	cssElementBlockToJson,
	inlineCSSWarnings,
	combineErrors
} from './utils';
import '@servicenow/now-textarea';
import styles from './_sn-uib-css-input.scss';
import debounce from 'lodash/debounce';
import isEqual from 'lodash/isEqual';
import '@servicenow/now-code-editor';
import '@servicenow/now-loader';
import { t } from 'sn-translate';
const ERROR_COUNT_TO_REPORT = 3;
const CSS_INPUT_UPDATED = 'CSS_INPUT#UPDATED';
const CSS_INPUT_CHANGED = 'CSS_INPUT#CHANGED';
const CSS_HACK_NCE = ' .suggest-widget { width: 280px !important; }';

const updateErrors = ({ updateState, text, errors, reported_errors }) => {
	const warnings = inlineCSSWarnings(text);
	const edit_problems = combineErrors(warnings, errors);
	if (!isEqual(edit_problems, reported_errors)) {
		updateState({
			edit_problems
		});
	}
};

const debouncedErrorUpdate = debounce(updateErrors, 650, { trailing: true });
const debouncedCssUpdate = debounce(
	({ state, dispatch, action, properties: { elementId }, updateState }) => {
		const { text, errors } = action.payload;
		debouncedErrorUpdate({
			updateState,
			text,
			errors,
			reported_errors: state.edit_problems
		});
		if (
			errors &&
			errors.length > 0 &&
			errors.find((e) => e.severity === 'error')
		) {
			return;
		}

		const cssObj = cssElementBlockToJson(text);
		if (!isEqual(cssObj, state.last.value)) {
			updateState({
				path: 'last.value',
				value: cssObj,
				operation: 'set',
				shouldRender: false
			});
			dispatch(CSS_INPUT_UPDATED, {
				value: cssObj,
				elementId
			});
		}
	},
	100,
	{ leading: true, trailing: true }
);

const view = (state, { updateState }) => {
	const {
		currentValue,
		initFinished,
		codeEditor,
		edit_problems,
		cursorposition,
		properties: { label, disabled }
	} = state;
	return (
		<Fragment>
			<section
				className="sn-uib-css-input-section"
				on-focusin={() =>
					updateState({
						path: 'editing',
						value: true,
						operation: 'set',
						shouldRender: false
					})
				}
				on-focusout={() =>
					updateState({
						path: 'editing',
						value: false,
						operation: 'set',
						shouldRender: false
					})
				}>
				<section className="sn-uib-css-input-label">{label}</section>

				{initFinished ? (
					<now-code-editor
						ref={codeEditor}
						language="css"
						read-only={disabled}
						hide-toolbar
						hide-gutter
						disable-minimap
						disable-full-screen
						disable-debugging
						disable-context-menu
						content={currentValue}
						cursorPosition={cursorposition}
						customCSS={CSS_HACK_NCE}
						height="100%"
					/>
				) : (
					<div className={'css-loader'}>
						<now-loader />
					</div>
				)}
				<div
					class={{ 'css-syntax-warning': true, error: !!edit_problems.length }}>
					{edit_problems.slice(0, ERROR_COUNT_TO_REPORT).map((e, i) => (
						<div
							key={`nce_${i}`}
							class={{ [e.severity]: true, 'more-info': !!e.tooltip }}
							title={e.tooltip}>
							{e.problem}
						</div>
					))}
					{edit_problems.length > ERROR_COUNT_TO_REPORT ? (
						<div
							key={`nce_${edit_problems.length}`}
							class={{ warning: true, 'more-info': true }}
							title={edit_problems
								.slice(ERROR_COUNT_TO_REPORT)
								.map((e) => e.problem)
								.join('\n')}>
							{`${edit_problems.length - ERROR_COUNT_TO_REPORT} more `}
						</div>
					) : null}
				</div>
			</section>
		</Fragment>
	);
};

export default createCustomElement('sn-uib-css-input', {
	view,
	initialState: {
		currentValue: '',
		initFinished: false,
		last: { value: {}, text: '', errors: [] },
		codeEditor: createRef(),
		edit_problems: []
	},
	properties: {
		bodyRef: { default: null },
		label: {
			default: ''
		},
		cssObj: {
			default: {}
		},
		name: {
			default: null
		},
		elementId: {
			default: null
		},
		disabled: {
			default: null
		},
		forced: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: {
			effect: ({ updateState, properties: { cssObj, elementId } }) => {
				const currentValue = objectToCssElementBlock(cssObj, elementId);

				updateState({
					currentValue,
					cursorposition: {
						lineNumber: 2, //currentValue.match(/\n/g).length - 1,
						column: 1
					},
					last: { value: cssObj, text: '', errors: [] },
					initFinished: true
				});
			},
			stopPropagation: true
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
			effect: ({ action, state, properties: { elementId }, updateState }) => {
				const {
					payload: { name, value: newValue }
				} = action;
				if (name === 'cssObj') {
					if (!state.editing && !isEqual(newValue, state.last.value)) {
						updateState({
							currentValue: objectToCssElementBlock(newValue, elementId),
							edit_problems: []
						});
					}
				}
			},
			stopPropagation: true
		},
		'NOW_CODE_EDITOR#TEXT_EDITOR_FOCUS_CHANGED': {
			effect: ({
				updateState,
				state: {
					last: { text, errors },
					edit_problems
				}
			}) => {
				updateErrors({ updateState, text, errors, edit_problems });
			},
			stopPropagation: true
		},
		'NOW_CODE_EDITOR#CONTENT_CHANGED': {
			effect: (coeffects) => {
				const {
					state: { last },
					updateState,
					action: {
						payload: { text, errors }
					}
				} = coeffects;
				updateState({ last: { ...last, text, errors } });
				debouncedCssUpdate(coeffects);
			},
			stopPropagation: true
		}
	},
	styles
});

export { CSS_INPUT_CHANGED, CSS_INPUT_UPDATED };
