import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import styles from './sn-uib-glide-list-property-input.scss';
import { config } from './component-config';
import '../sn-uib-property-input-data-link-toggle';
import '../sn-uib-property-description-tooltip';
import { t } from 'sn-translate';

import '@servicenow/now-dropdown';

// Dispatched action types
export const GLIDE_LIST_PROPERTY_VALUE_CHANGED =
	'SN_UIB_GLIDE_LIST_PROPERTY_INPUT#VALUE_CHANGED';

const view = ({
	properties: {
		label,
		value,
		choices,
		container,
		disabled,
		description,
		selectedComponentTag
	}
}) => {
	const selectedChoices = value ? value.split(',') : [];

	return (
		<Fragment>
			<div className="sn-uib-property-input">
				<label className="sn-uib-property-input-label">
					{label}
					{!description ? null : (
						<sn-uib-property-description-tooltip
							description={description}
							selectedComponentTag={selectedComponentTag}
						/>
					)}
				</label>
				<now-dropdown
					manage-selected-items
					select="multi"
					placeholder={t('Select {0}', label)}
					variant="tertiary"
					items={choices}
					selected-items={selectedChoices}
					panel-fit-props={{ container }}
					configAria={{ 'aria-label': label }}
					disabled={disabled}
				/>
			</div>
		</Fragment>
	);
};

/**
 * An input component to be used for glide_list properties (type 'List')
 * configured on the UI Builder properties editor panel
 */
createCustomElement(config.name, {
	view,
	styles,
	properties: config.properties,
	actionHandlers: {
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({ action, dispatch }) => {
			const {
				payload: { value }
			} = action;

			action.stopPropagation();

			dispatch(GLIDE_LIST_PROPERTY_VALUE_CHANGED, {
				value: value.join(',')
			});
		}
	}
});
